import ListItems, { ListItem } from '@app/components/ListItems2/ListItems';
import React from 'react';
import { useIntl } from 'react-intl';

export default function ListPractical({ items }: { items: ListItem[] }) {
  const { formatMessage } = useIntl();

  return <ListItems title={formatMessage({ id: 'app.practical.title' })} items={items} />;
}
