import React from 'react';
import { NativeStackNavigationProp } from '@react-navigation/native-stack';
import AtTheParkScreen from '@domains/park/at-the-park/screens/AtThePark';
import AtTheParkDetail from '@domains/park/at-the-park/screens/AtTheParkDetail';
import FacilityBooking from '@domains/park/at-the-park/screens/FacilityBooking';
import FacilityBookingSuccessful from '@domains/park/at-the-park/screens/FacilityBookingSuccessful';
import { RouteProp } from '@react-navigation/native';
import { Facility } from 'apis/_generated-park-api/models';
import PaymentConfirmation from '@domains/park/shop/screens/PaymentConfirmation';
import ContactAndInfo from '@domains/park/contact-and-info/screens/ContactAndInfo';

// Types of params passed to route via navigation.navigate('SCREEN_NAME', params)
export type AtTheParkRoutes = {
  AtThePark: { parkCode: string; category: 'onThePark' | 'goodToKnow' };
  AtTheParkDetail: { facility?: Facility };
  FacilityBooking: undefined;
  FacilityBookingSuccessful: undefined;
  ActivitySignup: undefined;
  ActivitiesDetail: undefined;
  ActivityBookingSuccessful: undefined;
};

type RouteNames = keyof AtTheParkRoutes;
interface NavScreenProp<T extends RouteNames> {
  navigation: NativeStackNavigationProp<AtTheParkRoutes, T>;
  route: RouteProp<AtTheParkRoutes, T>;
}

export const useAtTheParkScreens = (Stack: any) => (
  <>
    <Stack.Screen name={'AtThePark'} component={AtTheParkScreen} />
    <Stack.Screen name={'AtTheParkDetail'} component={AtTheParkDetail} />
    <Stack.Screen
      name={'FacilityBooking'}
      component={FacilityBooking}
      options={{
        presentation: 'containedModal',
      }}
    />
    <Stack.Screen
      name={'FacilityBookingSuccessful'}
      component={FacilityBookingSuccessful}
      options={{
        presentation: 'containedModal',
      }}
    />
    <Stack.Screen name="PaymentConfirmation" component={PaymentConfirmation} />
    <Stack.Screen name="ContactAndInfo" component={ContactAndInfo} />
  </>
);
