import useCulture from '@app/hooks/useCulture';
import useSelectedPark from '@app/hooks/useSelectedPark';
import { AxiosError } from 'axios';
import { UseQueryOptions, useQuery } from 'react-query';
import { getAPIDate } from 'utils/date';
import { gql, GraphQLClient } from 'graphql-request';
import Config from 'react-native-config';
import { Activity } from 'apis/_generated-park-api/models';
import useSelectedLanguageAvailableForPark from '@app/hooks/useSelectedLanguageAvailableForPark';

interface ActivityResponse {
  sortedData: Activity[];
  date: string;
  amountOfDays: number;
}

const key = 'getactivities';

type Props = {
  options?: Omit<UseQueryOptions<ActivityResponse, AxiosError>, typeof key>;
  parkCodeOverride?: string;
};

const AMOUNT_OF_DAYS = 14;

export function useGetActivities(props?: Props) {
  const { parkCode: selectedParkCode } = useSelectedPark();
  const parkCode = props?.parkCodeOverride ?? selectedParkCode;

  let { cultureCode, FALLBACK_CULTURE } = useCulture();
  const selectedLanguageAvailableForPark = useSelectedLanguageAvailableForPark();
  if (!selectedLanguageAvailableForPark) {
    cultureCode = FALLBACK_CULTURE;
  }
  const date = getAPIDate(new Date());

  return useQuery(
    [key, { parkCode, cultureCode, date, AMOUNT_OF_DAYS }],
    async () => {
      const graphQLClient = new GraphQLClient(Config.PARK_API_URL);
      const query = gql`
        query activities {
          activities(parkCode: "${parkCode}", cultureCode: "${cultureCode}", date: "${date}", numberOfDays : ${AMOUNT_OF_DAYS}) {
            time {
              id
              date
              displayHint
              startTime
              endTime
              reservationId
            }
            id
            activityId
            name
            description
            locationId
            signUpNeeded
            ageCategory {
              ageFrom
              ageTo
              displayHint
              description
            }
            price {
              amount
              currency
              displayHint
              description
            }
            categories
            media
          }
        }
      `;
      const result = await graphQLClient.rawRequest(query);
      return { sortedData: result?.data?.activities, date, amountOfDays: AMOUNT_OF_DAYS };
    },
    props?.options
  );
}
