import React, { useState } from 'react';
import useMetrics from '@app/metrics/useMetrics';
import { useIntl } from 'react-intl';
import { useFormik } from 'formik';
import { useSendFeedback } from '../queries/useSendFeedback';
import { useBottomSheet } from '@app/navigation/bottom-sheet/BottomSheetProvider';
import Button from '@app/components/Button';
import useTheme from '@app/hooks/useTheme';
import { TouchableOpacity, View } from 'react-native';
import Text from '@app/components/Text';
import RadioList from '@app/components/ListItems2/RadioList';
import FormControl from '@app/components/Form/FormControl';
import { NavigationContainerRef, useNavigation } from '@react-navigation/native';
import { AllRoutes } from '@app/navigation/stacks/Main';

type FormFields = 'rating' | 'remarks';

interface Props {}

export default function FeedbackSheet({}: Props) {
  const navigation = useNavigation<NavigationContainerRef<AllRoutes>>();
  const { formatMessage } = useIntl();
  const [processing, setProcessing] = useState(false);
  const { logEvent } = useMetrics();
  const { sendFeedback } = useSendFeedback();
  const { closeBottomSheet } = useBottomSheet();
  const theme = useTheme();
  const [selectedValue, setSelectedValue] = useState<string>();

  const formik = useFormik<Record<FormFields, any>>({
    initialValues: {
      rating: undefined,
      remarks: undefined,
    },
    validateOnMount: false,
    validateOnChange: true,
    onSubmit: () => {},
    validate: () => {},
  });

  return (
    <View style={{ margin: theme.space.l }}>
      <Text color="dark" style={{ lineHeight: 26, textAlign: 'center' }}>
        {formatMessage({ id: 'app.conversation.feedback' })}
      </Text>
      <RadioList
        items={[
          {
            label: formatMessage({ id: 'app.conversation.feedback.positive' }),
            value: 'positive',
            icon: 'smiley-happy',
            iconColor: 'success',
          },
          {
            label: formatMessage({ id: 'app.conversation.feedback.negative' }),
            value: 'negative',
            icon: 'smiley-unhappy',
            iconColor: 'danger',
          },
          {
            label: formatMessage({ id: 'app.conversation.feedback.neutral' }),
            value: 'neutral',
            icon: 'smiley-neutral',
            iconColor: 'warning',
          },
        ]}
        onSelect={(value) => setSelectedValue(value)}
      />
      {!!selectedValue && (
        <FormControl
          label={formatMessage({ id: 'app.conversation.feedback.remarks' })}
          value={formik.values.remarks}
          isValid={true}
          onChangeText={(text) => {
            formik.setFieldValue('remarks', text);
          }}
          multiline
          optional
        />
      )}
      <Button
        onPress={async () => {
          setProcessing(true);
          try {
            logEvent('FeedbackApp', { rating: selectedValue });
            if (formik.values.remarks) {
              await sendFeedback(formik.values.remarks, 'FeedbackApp', selectedValue ?? '');
            }
            // TODO: closeBottomSheet should not be in a timeout, but without it does not make the bottomsheet close in combination with the refetch...
            const timer = setTimeout(() => {
              closeBottomSheet();
              return () => clearTimeout(timer);
            }, 300);

            navigation.navigate('FeedbackConfirmation');
          } catch {
            const timer = setTimeout(() => {
              closeBottomSheet();
              return () => clearTimeout(timer);
            }, 300);
            navigation.navigate('FeedbackConfirmation');
          }
        }}
        style={{ backgroundColor: theme.colors.secondary, marginTop: theme.space.s }}
        disabled={processing || !formik.isValid}
      >
        {processing ? (
          <Button.ActivityIndicator style={{ height: 22 }} />
        ) : (
          <Button.Text color="white">
            {formatMessage({
              id: 'app.general.send',
            })}
          </Button.Text>
        )}
      </Button>
      <TouchableOpacity
        activeOpacity={0.8}
        style={{ marginTop: theme.space.l, alignItems: 'center' }}
        onPress={closeBottomSheet}
      >
        <Text color="gray" bold>
          {formatMessage({
            id: 'app.general.cancel',
          })}
        </Text>
      </TouchableOpacity>
    </View>
  );
}
