import Icon from '@app/components/Icon';
import Text from '@app/components/Text';
import useTheme from '@app/hooks/useTheme';
import React, { forwardRef, Ref, useImperativeHandle } from 'react';
import { View } from 'react-native';
import Animated, {
  Easing,
  useAnimatedStyle,
  useSharedValue,
  withDelay,
  withSpring,
  withTiming,
} from 'react-native-reanimated';

export interface TooltipRefType {
  show: () => void;
}

interface Props {
  text: string;
  additionalArrowOffset?: number;
  tooltipVerticalOffset?: number;
}

const TRANSITION_CONFIG = { duration: 500, easing: Easing.out(Easing.exp) };

const Tooltip = forwardRef(
  ({ text, additionalArrowOffset = 0, tooltipVerticalOffset = 0 }: Props, ref: Ref<TooltipRefType>) => {
    const theme = useTheme();
    const verticalPadding = theme.space.s;
    const opacity = useSharedValue(0);

    const animatedStyle = useAnimatedStyle(() => {
      return { opacity: withSpring(opacity.value) };
    });

    useImperativeHandle(ref, () => ({
      show() {
        opacity.value = withDelay(60, withTiming(1, TRANSITION_CONFIG));
        opacity.value = withDelay(2000, withTiming(0, TRANSITION_CONFIG));
      },
    }));

    return (
      <Animated.View
        style={[
          {
            position: 'absolute',
            alignSelf: 'center',
            alignItems: 'center',
            zIndex: 99,
            elevation: 10,
            marginBottom: -20,
            marginTop: tooltipVerticalOffset,
            opacity: 0,
          },
          animatedStyle,
        ]}
        pointerEvents="none"
      >
        <View
          style={{
            flexDirection: 'row',
            justifyContent: 'center',
            alignItems: 'center',
            backgroundColor: theme.colors.black,
            borderRadius: 4,
            paddingHorizontal: theme.space.m,
            paddingVertical: verticalPadding,
          }}
        >
          <Icon name="check-mark" size={'xs'} color={'primary'} />
          <Text size="s" style={{ marginLeft: theme.space.s }} type="body" color={'white'}>
            {text}
          </Text>
        </View>
        <View
          style={{
            position: 'absolute',
            width: 0,
            height: 0,
            backgroundColor: 'transparent',
            borderStyle: 'solid',
            borderLeftWidth: 5,
            borderRightWidth: 5,
            borderTopWidth: 4,
            borderLeftColor: 'transparent',
            borderRightColor: 'transparent',
            borderTopColor: theme.colors.black,
            margin: 0,
            marginLeft: -6,
            borderWidth: 0,
            top: theme.fontSizes.s.lineHeight + verticalPadding * 2 + additionalArrowOffset,
          }}
        />
      </Animated.View>
    );
  }
);

export default Tooltip;
