import { UserContext } from '@app/context/usercontext/UserContextProvider';
import useTheme from '@app/hooks/useTheme';
import {
  UpdateDepartureTimeRequest,
  UpdateDepartureTimeResponse,
} from 'apis/_generated-reservation-api/models';
import { AxiosError } from 'axios';
import { gql, GraphQLClient } from 'graphql-request';
import { useContext } from 'react';
import Config from 'react-native-config';
import { useMutation, UseQueryOptions } from 'react-query';
import { getBrand } from 'utils/brand';
import { useGetReservation } from './useGetReservation';

type Props = {
  options?: UseQueryOptions<UpdateDepartureTimeResponse, AxiosError>;
  bookingReference?: string;
};

export function useMutateExpectedCheckout(props?: Props) {
  const theme = useTheme();
  const { token } = useContext(UserContext);
  const { data: reservation } = useGetReservation({
    brand: getBrand(theme),
    token,
    bookingReference: props?.bookingReference,
  });

  const performUpdateDepartureTime = useMutation(async (request: UpdateDepartureTimeRequest) => {
    const graphQLClient = new GraphQLClient(Config.RESERVATION_API_URL);

    if (!reservation?.bookingReference) {
      throw 'no active reservation found';
    }

    const query = gql`
      mutation {
        updateDepartureTime(updateDepartureTimeRequest: { uid: "${token}", bookingReference: "${reservation?.bookingReference}", departureTime: ${request.departureTime} }) {
          success
          message
        }
      }
    `;

    const response = await graphQLClient.rawRequest<{ updateDepartureTime: UpdateDepartureTimeResponse }>(
      query,
      {
        request,
      }
    );
    return response?.data?.updateDepartureTime;
  }, props?.options);

  const updateExpectedCheckout = async (request: UpdateDepartureTimeRequest) => {
    const result = await performUpdateDepartureTime.mutateAsync(request);
    return result;
  };

  return { updateExpectedCheckout };
}
