import { useIntl } from 'react-intl';
import { getFormattedTimeByTime } from 'utils/date';

export default function useOpeningHours() {
  const { formatMessage } = useIntl();

  return (startTime?: string, endTime?: string, displayHint?: string) => {
    if (!displayHint) {
      return '';
    }
    switch (displayHint.toLowerCase()) {
      case 'fromto': {
        if (!startTime || !endTime) {
          return '';
        }
        return `${getFormattedTimeByTime(startTime)} - ${getFormattedTimeByTime(endTime)}`;
      }
      case 'from': {
        if (!startTime) {
          return '';
        }
        return `${formatMessage({ id: 'app.times.from' })} ${getFormattedTimeByTime(startTime)}`;
      }
      case 'to': {
        if (!endTime) {
          return '';
        }
        return `${formatMessage({ id: 'app.times.till' })} ${getFormattedTimeByTime(endTime)}`;
      }
      case 'at': {
        if (!startTime) {
          return '';
        }
        return `${formatMessage({ id: 'app.times.at' })} ${getFormattedTimeByTime(startTime)}`;
      }
      case 'openallday':
        return `${formatMessage({ id: 'app.times.open' }).toLowerCase()}`;
      case 'closedallday':
        return `${formatMessage({ id: 'app.times.closed' }).toLowerCase()}`;
      case 'onrequest':
        return `${formatMessage({ id: 'app.times.request' }).toLowerCase()}`;
      default:
        return '';
    }
  };
}
