import React from 'react';
import useTheme from '@hooks/useTheme';
import CheckBox, { CheckBoxProps } from '@react-native-community/checkbox';

interface Props extends CheckBoxProps {}

export default function FormCheckBox(props: Props) {
  const theme = useTheme();

  return (
    <CheckBox
      disabled={false}
      boxType="square"
      onFillColor={theme.colors.white}
      tintColor={theme.colors.light}
      onCheckColor={theme.colors.black}
      tintColors={{ true: theme.colors.light, false: theme.colors.light }}
      onTintColor={theme.colors.light}
      onAnimationType={'fill'}
      offAnimationType={'fade'}
      {...props}
    />
  );
}
