import React, { useMemo } from 'react';
import { useIntl } from 'react-intl';
import Text from '../../../../app/components/Text';
import { useGetPark } from '@domains/park/contact-and-info/queries/useGetPark';
import useTheme from '@app/hooks/useTheme';
import { Linking, Platform, View } from 'react-native';
import DigitalFunItem from './DigitalFunItem';
import { BASELINE_INSET } from '@app/theme/partials/sizes';
import { useGetPromos } from '@domains/promo/queries/useGetPromos';

interface Props {
  parkCode?: string;
}
export default function DigitalFun({ parkCode }: Props) {
  const theme = useTheme();
  const { formatMessage } = useIntl();
  const { data: park } = useGetPark({ parkCodeOverride: parkCode ?? undefined });
  const { data: promos } = useGetPromos({ parkCodeOverride: parkCode });

  const isHofVanSaksen = park?.id === 'HSN';
  const hasBolloApp = !!park && !isHofVanSaksen;
  const hasAdventureApp = useMemo(() => {
    const foundAdventureItem = promos?.find((p) => p.hoofdCat === 'fe' && p.subCat === 'adventure');
    return !!foundAdventureItem;
  }, [promos]);

  const bolloAppItem = {
    localImage: require('@assets/images/app-bollo.png'),
    title: formatMessage({ id: 'app.promo.bolloapp.title' }),
    subtitle: formatMessage({ id: 'app.promo.bolloapp.sub' }),
    url:
      Platform.OS === 'android'
        ? 'https://play.google.com/store/apps/details?id=nl.landal.bollo'
        : 'itms-apps://apps.apple.com/nl/app/bollo/id1036815131',
  };
  const adventureAppItem = {
    localImage: isHofVanSaksen
      ? require('@assets/images/app-hof-van-saksen-adventure-game.png')
      : require('@assets/images/app-landal-adventure-game.png'),
    title: formatMessage({ id: 'app.promo.adventuregame.title' }),
    subtitle: formatMessage({ id: 'app.promo.adventuregame.sub' }),
    url:
      Platform.OS === 'android'
        ? isHofVanSaksen
          ? 'https://play.google.com/store/apps/details?id=nl.hofvansaksen.adventure'
          : 'https://play.google.com/store/apps/details?id=com.landal.adventure'
        : isHofVanSaksen
        ? 'itms-apps://apps.apple.com/nl/app/hof-van-saksen-adventure/id1502271620'
        : 'itms-apps://apps.apple.com/nl/app/landal-adventure/id1502271658',
  };

  return (
    <View style={{ marginVertical: theme.space.l }}>
      <Text type="title" size="l" bold>
        {formatMessage({ id: 'app.headers.digitalFun' })}
      </Text>
      <View
        style={{
          flex: 1,
          flexDirection: 'row',
          marginTop: theme.space.m,
          marginHorizontal: -BASELINE_INSET - theme.space.s / 2,
          right: -theme.space.s / 2,
        }}
      >
        {hasAdventureApp && (
          <DigitalFunItem
            title={adventureAppItem.title}
            subtitle={adventureAppItem.subtitle}
            localImage={adventureAppItem.localImage}
            onPress={() => {
              Linking.canOpenURL(adventureAppItem.url).then(
                (supported) => {
                  supported && Linking.openURL(adventureAppItem.url);
                },
                (err) => console.log(err)
              );
            }}
          />
        )}
        {hasBolloApp && (
          <DigitalFunItem
            title={bolloAppItem.title}
            subtitle={bolloAppItem.subtitle}
            localImage={bolloAppItem.localImage}
            onPress={() => {
              Linking.canOpenURL(bolloAppItem.url).then(
                (supported) => {
                  supported && Linking.openURL(bolloAppItem.url);
                },
                (err) => console.log(err)
              );
            }}
          />
        )}
      </View>
    </View>
  );
}
