import React, { useContext, useState } from 'react';
import { Alert, TouchableOpacity, View } from 'react-native';
import Button from '@app/components/Button';
import useTheme from '@app/hooks/useTheme';
import Text from '@app/components/Text';
import { useIntl } from 'react-intl';
import useMetrics from '@app/metrics/useMetrics';
import { useGetReservation } from '@domains/reservations/reservation/queries/useGetReservation';
import { getBrand } from 'utils/brand';
import { UserContext } from '@app/context/usercontext/UserContextProvider';
import { useCheckoutNow } from '@domains/reservations/reservation/queries/useCheckoutNow';
import { useBottomSheet } from '@app/navigation/bottom-sheet/BottomSheetProvider';
import { NavigationContainerRef, useNavigation } from '@react-navigation/native';
import { AllRoutes } from '@app/navigation/stacks/Main';
import { useGetPark } from '@domains/park/contact-and-info/queries/useGetPark';

interface Props {
  bookingReference: string;
}

export default function CheckoutNowSheet({ bookingReference }: Props) {
  const theme = useTheme();
  const { formatMessage } = useIntl();
  const navigation = useNavigation<NavigationContainerRef<AllRoutes>>();
  const { closeBottomSheet } = useBottomSheet();
  const { logEvent } = useMetrics();
  const [processing, setProcessing] = useState(false);
  const { checkoutNow } = useCheckoutNow();
  const { token } = useContext(UserContext);
  const { data: reservation } = useGetReservation({
    token,
    brand: getBrand(theme),
    bookingReference,
  });
  const { data: park } = useGetPark({ parkCodeOverride: reservation?.parkCode });

  React.useEffect(() => {
    logEvent('CheckoutBegin', { bookingReference: reservation?.bookingReference });
  }, [logEvent, reservation?.bookingReference]);

  return (
    <View>
      <View
        style={{
          marginTop: theme.space.l,
          alignItems: 'center',
        }}
      >
        <View
          style={{
            paddingHorizontal: theme.space.l,
          }}
        >
          <Text style={{ textAlign: 'center', marginBottom: theme.space.m }} bold>
            {formatMessage({ id: 'app.reservation.checkoutNow.areYouSureTitle' })}
          </Text>
          <Text
            type="body"
            style={{ textAlign: 'center', lineHeight: 26, marginBottom: theme.space.xl }}
            color="dark"
          >
            {formatMessage({ id: 'app.reservation.checkoutNow.areYouSureText' })}
          </Text>
        </View>

        <View
          style={{
            width: '100%',
            paddingHorizontal: theme.space.l,
          }}
        >
          <Button
            onPress={async () => {
              if (reservation?.pendingPayment && !reservation?.isOwner) {
                Alert.alert(
                  formatMessage({ id: 'app.reservation.checkoutNow.pendingPaymentTitle' }),
                  formatMessage({ id: 'app.reservation.checkoutNow.pendingPaymentText' })
                );
                return;
              }

              setProcessing(true);
              try {
                const response = await checkoutNow({
                  uid: token,
                  bookingReference: reservation?.bookingReference,
                });
                if (!response || !response.success) {
                  Alert.alert(formatMessage({ id: 'app.forms.tablebooking.error.generic' }));
                } else {
                  logEvent('CheckoutComplete', { bookingReference: reservation?.bookingReference });
                  closeBottomSheet();
                  navigation.navigate('CheckoutNowConfirmation', { parkName: park?.name });
                }
              } catch {
                Alert.alert(formatMessage({ id: 'app.forms.tablebooking.error.generic' }));
              }
              setProcessing(false);
            }}
            type="primary"
          >
            {processing ? (
              <Button.ActivityIndicator />
            ) : (
              <Button.Text color="white" style={{ lineHeight: 20 }}>
                {formatMessage({ id: 'app.reservation.checkoutNow.areYouSurePositive' })}
              </Button.Text>
            )}
          </Button>
        </View>
        <TouchableOpacity
          activeOpacity={0.8}
          onPress={closeBottomSheet}
          style={{ marginTop: theme.space.m }}
          hitSlop={{ top: 10, bottom: 10, left: 10, right: 10 }}
        >
          <Text color="gray" bold>
            {formatMessage({ id: 'app.general.cancel' })}
          </Text>
        </TouchableOpacity>
      </View>
    </View>
  );
}
