import { UserContext } from '@app/context/usercontext/UserContextProvider';
import useTheme from '@app/hooks/useTheme';
import { useContext } from 'react';
import { getBrand } from 'utils/brand';
import { getActiveReservation } from 'utils/reservation';
import { useGetReservations } from '../queries/useGetReservations';

interface Props {
  bookingReference: string;
}

export function useIsCurrentReservation({ bookingReference }: Props) {
  const theme = useTheme();
  const { token } = useContext(UserContext);

  const { data: reservations } = useGetReservations({
    brand: getBrand(theme),
    token,
  });

  if (!reservations) return false;

  return getActiveReservation(reservations)?.bookingReference === bookingReference;
}
