import React from 'react';
import { View, ViewProps } from 'react-native';
import useTheme from '@hooks/useTheme';
import Button from '../Button';

interface Props extends ViewProps {
  text?: string;
  selected?: boolean;
  onPress?: () => void;
  small?: boolean;
  disabled?: boolean;
}

export default function FormButton({ text, selected, onPress, disabled, small, style, ...props }: Props) {
  const theme = useTheme();

  if (!text) {
    return null;
  }

  return (
    <View style={[{ opacity: disabled ? 0.4 : 1 }, style]} {...props}>
      <Button
        style={{
          backgroundColor: selected ? theme.colors.black : theme.colors.white,
          borderWidth: 1,
          borderColor: theme.colors.white,
          justifyContent: 'center',
          paddingHorizontal: 0,
          paddingVertical: small ? 8 : undefined,
        }}
        disabled={disabled}
        onPress={onPress}
      >
        <Button.Text
          style={{
            color: selected ? theme.colors.white : theme.colors.black,
          }}
          size={'m'}
        >
          {text}
        </Button.Text>
      </Button>
    </View>
  );
}
