import useScreenSize from '@app/hooks/useScreenSize';
import useTheme from '@app/hooks/useTheme';
import { Timeslot } from 'apis/_generated-park-api/models';
import React from 'react';
import { useState } from 'react';
import { useIntl } from 'react-intl';
import { View } from 'react-native';
import { addMinutes, getFormattedTime, minutesBetween } from 'utils/date';
import FormButton from '../Form/FormButton';
import Loader from '../Loader';
import Text from '../Text';

type TimeOfDay = 'morning' | 'afternoon' | 'evening';

interface Props {
  time?: string;
  onSelectTime: (label: string, timestamp: number) => void;
  slotDuration?: number;
  availability?: Timeslot[];
  showDayPart?: boolean;
  dataIsLoading?: boolean;
}

const morningStart = new Date(2000, 1, 1, 8, 0, 0);
const morningEnd = new Date(2000, 1, 1, 12, 0, 0);
const afternoonStart = new Date(2000, 1, 1, 12, 0, 0);
const afternoonEnd = new Date(2000, 1, 1, 17, 0, 0);
const eveningStart = new Date(2000, 1, 1, 17, 0, 0);
const eveningEnd = new Date(2000, 1, 1, 23, 0, 0);

export default function TimeSlotSelector({
  time,
  onSelectTime,
  slotDuration = 15,
  availability,
  showDayPart,
  dataIsLoading,
}: Props) {
  const [timeOfDay, setTimeOfDay] = useState<TimeOfDay>('evening');
  const theme = useTheme();
  const { formatMessage } = useIntl();
  const { width } = useScreenSize();
  const timeBlockPadding = 8;
  const contentWidth = width - theme.space.l * 2;
  const timeblockWidth = (contentWidth - 3 * timeBlockPadding) / 4;

  let amountOfSlotsMorning: number = (morningEnd.getTime() - morningStart.getTime()) / (slotDuration * 60000);
  let amountOfSlotsAfternoon: number =
    (afternoonEnd.getTime() - afternoonStart.getTime()) / (slotDuration * 60000);
  let amountOfSlotsEvening: number = (eveningEnd.getTime() - eveningStart.getTime()) / (slotDuration * 60000);
  let amountOfSlotsAll: number = (eveningEnd.getTime() - morningStart.getTime()) / (slotDuration * 60000);

  const slotsMorning = Array.apply(null, Array(amountOfSlotsMorning))?.map((_, i) =>
    addMinutes(morningStart, i * slotDuration)
  );
  const slotsAfternoon = Array.apply(null, Array(amountOfSlotsAfternoon))?.map((_, i) =>
    addMinutes(afternoonStart, i * slotDuration)
  );
  const slotsEvening = Array.apply(null, Array(amountOfSlotsEvening))?.map((_, i) =>
    addMinutes(eveningStart, i * slotDuration)
  );

  const slotsAll = Array.apply(null, Array(amountOfSlotsAll))?.map((_, i) =>
    addMinutes(morningStart, i * slotDuration)
  );

  const isAvailable = (remoteSlot: number, renderedSlot: Date) => {
    const availableTimestamp = new Date(remoteSlot * 1000);
    const timeBetweenSlots =
      bookableTimesArray?.length > 2 ? minutesBetween(bookableTimesArray[1], bookableTimesArray[0]) : 30;
    // Within the last slot you'll have 30 minutes slack time.
    if (availableTimestamp < addMinutes(new Date(), -timeBetweenSlots)) {
      return false;
    } // we still allow the current timeslot to be booked
    return (
      availableTimestamp.getHours() === renderedSlot.getHours() &&
      availableTimestamp.getMinutes() === renderedSlot.getMinutes()
    );
  };
  const getTimestamp = (renderedSlot: Date) => {
    const remoteItem = availability?.find((a) => {
      const availableTimestamp = a?.timeslot ? new Date(a.timeslot * 1000) : null;
      if (!availableTimestamp) {
        return false;
      }
      return (
        availableTimestamp.getHours() === renderedSlot.getHours() &&
        availableTimestamp.getMinutes() === renderedSlot.getMinutes()
      );
    });
    if (remoteItem) {
      return remoteItem.timeslot;
    }
  };

  const timeSlots = [
    { key: 'morning', text: formatMessage({ id: 'app.general.morning' }) },
    { key: 'afternoon', text: formatMessage({ id: 'app.general.afternoon' }) },
    { key: 'evening', text: formatMessage({ id: 'app.general.evening' }) },
  ] as const;

  let bookableTimesArray = [] as Date[];
  if (!showDayPart) {
    bookableTimesArray = slotsAll;
  } else if (timeOfDay === 'morning') {
    bookableTimesArray = slotsMorning;
  } else if (timeOfDay === 'afternoon') {
    bookableTimesArray = slotsAfternoon;
  } else if (timeOfDay === 'evening') {
    bookableTimesArray = slotsEvening;
  }

  if (dataIsLoading) {
    return <Loader style={{ alignSelf: 'center' }} color="white" />;
  }

  if ((availability?.length || 0) < 1) {
    return (
      <Text bold size="s" style={{ marginBottom: timeBlockPadding }}>
        {formatMessage({ id: 'app.forms.timeslotselector.noslots' })}
      </Text>
    );
  }

  return (
    <View style={{ marginBottom: theme.space.xl }}>
      <Text bold size="s" style={{ marginBottom: timeBlockPadding }}>
        {formatMessage({ id: 'app.forms.labels.time' })}
      </Text>

      {showDayPart ? (
        <View style={{ flexDirection: 'row', justifyContent: 'space-between', marginBottom: 8 }}>
          {timeSlots?.map((item) => (
            <FormButton
              key={item.key}
              style={{ width: contentWidth / 3 - 5 }}
              text={item.text}
              selected={timeOfDay === item.key}
              onPress={() => {
                setTimeOfDay(item.key);
              }}
            />
          ))}
        </View>
      ) : (
        <View />
      )}

      <View style={{ flex: 1, flexDirection: 'row', flexWrap: 'wrap', padding: 0 }}>
        {bookableTimesArray?.map((item, i) => {
          const text = getFormattedTime(item);
          return (
            <FormButton
              key={item.toString()}
              style={{
                marginBottom: timeBlockPadding,
                width: timeblockWidth,
                marginRight: i % 4 !== 3 ? timeBlockPadding : 0,
              }}
              text={text}
              selected={time === text}
              onPress={() => {
                onSelectTime(text, getTimestamp(item) || 0);
              }}
              disabled={!availability?.find((a) => !!a.timeslot && isAvailable(a.timeslot, item))}
              small
            />
          );
        })}
      </View>
    </View>
  );
}
