import React, { forwardRef, useState } from 'react';
import { ListItem } from './ListItems';
import Text from '../Text';
import { AllRoutes } from '@app/navigation/stacks/Main';
import useTheme from '@app/hooks/useTheme';
import { View, ViewStyle, Linking, Platform } from 'react-native';
import Icon from '../Icon';
import { useBottomSheet } from '@app/navigation/bottom-sheet/BottomSheetProvider';
import { NavigationContainerRef, useNavigation } from '@react-navigation/native';
import LandalImage from '../LandalImage';
import { LandalPressable, LandalPressableRefType } from '../LandalPressable';
import Animated, { FadeIn } from 'react-native-reanimated';
import LandalSkiaImage from '../LandalSkiaImage';
import useMetrics from '@app/metrics/useMetrics';

const IMAGE_SIZE = 60;

interface Props {
  item: ListItem;
  usePlaceholderImage: boolean;
  useSkiaImage: boolean;
  useTopRadius: boolean;
  useBottomRadius?: boolean;
  isFirst?: boolean;
  isLast?: boolean;
  subtitleNumberOfLines?: number;
  style?: ViewStyle;
}

export const ListItemSingle = forwardRef(
  (
    {
      item,
      usePlaceholderImage,
      useSkiaImage,
      useTopRadius,
      useBottomRadius,
      isFirst,
      isLast,
      subtitleNumberOfLines,
      style,
    }: Props,
    ref: React.Ref<LandalPressableRefType>
  ) => {
    const {
      title,
      rightTitle,
      titleColor = 'black',
      subtitle,
      subtitleIcon,
      right,
      left,
      rightSubtitle,
      rightSubtitleIcon,
      bottom,
      image,
      isSizableImage = true,
      localImage,
      blurImage,
      icon,
      navigateTo,
      navigateParams,
      linkTo,
      event,
      bottomSheet,
      onPress,
      status,
      external,
      rightIcon,
      disabled,
    } = item;
    let { iconColor = 'primary' } = item;

    const { logEvent } = useMetrics();
    const theme = useTheme();
    const [showSkia, setShowSkia] = useState(false);
    const anyImage = !!image || !!localImage || usePlaceholderImage;

    const { openBottomSheet } = useBottomSheet();
    const navigation = useNavigation<NavigationContainerRef<AllRoutes>>();

    const showImageOrIcon = (!!icon || !!anyImage) && !left;

    let backgroundColor = theme.colors.canvas;

    switch (status) {
      case 'complete':
        backgroundColor = theme.colors.primary + '18'; // TODO: gradient
        iconColor = 'primary';
        break;
      case 'incomplete':
        backgroundColor = theme.colors.attention + '18'; // TODO: gradient
        iconColor = 'attention';
        break;
    }

    const margin = theme.space.s;

    return (
      <LandalPressable
        ref={ref}
        style={[
          {
            backgroundColor: '#FFF',
            flexDirection: 'row',
            minHeight: IMAGE_SIZE + margin * 2,
            paddingVertical: showImageOrIcon ? undefined : 10,
          },
          isFirst && {
            borderTopLeftRadius: useTopRadius ? 12 : 0,
            borderTopRightRadius: useTopRadius ? 12 : 0,
          },
          isLast && {
            borderBottomLeftRadius: useBottomRadius ? 12 : 0,
            borderBottomRightRadius: useBottomRadius ? 12 : 0,
          },
          style,
        ]}
        disabled={disabled || (!onPress && !linkTo && !navigateTo && !bottomSheet)}
        onPress={() => {
          if (event) {
            logEvent(event);
          }
          if (onPress) {
            onPress();
            return;
          }

          if (linkTo) {
            Linking.openURL(linkTo);
          }
          if (navigateTo) {
            navigation.navigate(navigateTo, navigateParams);
            return;
          }
          if (bottomSheet) {
            openBottomSheet(bottomSheet);
            return;
          }
        }}
      >
        <View style={{ opacity: disabled ? 0.4 : 1, flex: 1, flexDirection: 'row' }}>
          {showImageOrIcon && (
            <Animated.View
              style={{
                width: IMAGE_SIZE,
                height: IMAGE_SIZE,
                alignSelf: 'center',
                borderRadius: 8,
                overflow: 'hidden',
                flex: Platform.OS !== 'web' ? 0 : undefined,
                alignItems: 'center',
                justifyContent: 'center',
                backgroundColor: disabled ? theme.colors.canvas : backgroundColor || theme.colors.canvas,
                margin,
              }}
              layout={Platform.OS !== 'web' && anyImage ? FadeIn : undefined}
              onLayout={(event) => {
                var { width: layoutWidth } = event.nativeEvent.layout;
                if (layoutWidth <= 0) {
                  return;
                }
                setShowSkia(true);
              }}
            >
              {!!anyImage &&
                (useSkiaImage ? (
                  <LandalSkiaImage
                    applyBlur={blurImage || false}
                    height={IMAGE_SIZE}
                    width={IMAGE_SIZE}
                    localSource={localImage}
                    remoteSource={image}
                    showIconMask={!!icon}
                    size="m"
                  />
                ) : (
                  <LandalImage
                    remoteSource={image}
                    localSource={localImage}
                    style={{ width: IMAGE_SIZE, height: IMAGE_SIZE }}
                    size={'m'}
                  />
                ))}
              {!!icon && (
                <Icon
                  name={icon}
                  size="xl"
                  color={anyImage ? 'white' : disabled ? 'gray' : iconColor}
                  style={{ alignSelf: 'center' }}
                />
              )}
            </Animated.View>
          )}
          {!!left && left()}
          <View
            style={{
              justifyContent: 'center',
              paddingHorizontal: showImageOrIcon ? theme.space.s : theme.space.l,
              paddingRight: theme.space.s * 2,
              paddingVertical: theme.space.s,
              flex: 1,
            }}
          >
            <View
              style={{
                flexDirection: 'row',
                alignItems: 'center',
                justifyContent: 'space-between',
              }}
            >
              <Text color={titleColor} bold>
                {title}
              </Text>
              {!!rightTitle && <Text color="gray">{rightTitle}</Text>}
            </View>
            {!!subtitle && (
              <View
                style={{
                  flexDirection: 'row',
                  alignItems: 'center',
                  justifyContent: 'space-between',
                }}
              >
                <View style={{ flexDirection: 'row', alignItems: 'center' }}>
                  {!!subtitleIcon && (
                    <Icon name={subtitleIcon} size="s" color="gray" style={{ marginRight: 4 }} />
                  )}
                  <Text color="gray" numberOfLines={subtitleNumberOfLines}>
                    {subtitle}
                  </Text>
                </View>
                {!!rightSubtitle && (
                  <View
                    style={{
                      flexDirection: 'row',
                      alignItems: 'center',
                      flex: 1,
                      flexWrap: 'wrap',
                      justifyContent: 'flex-end',
                      marginLeft: theme.space.s,
                    }}
                  >
                    {!!rightSubtitleIcon && (
                      <Icon name={rightSubtitleIcon} size="s" color="gray" style={{ marginRight: 4 }} />
                    )}
                    <Text color="gray">{rightSubtitle}</Text>
                  </View>
                )}
              </View>
            )}
            {!!bottom && <View style={{ marginVertical: theme.space.s }}>{bottom}</View>}
          </View>
          {(!!linkTo || external || !!rightIcon) && (
            <View
              style={{
                flex: Platform.OS === 'web' ? undefined : 0,
                alignSelf: 'center',
                alignItems: 'flex-end',
                marginRight: theme.space.l,
              }}
            >
              <Icon name={rightIcon || 'exit-app'} size="m" color={rightIcon ? 'black' : 'light'} />
            </View>
          )}
          {!!right && (
            <View style={{ justifyContent: 'center', marginHorizontal: theme.space.l }}>{right()}</View>
          )}
        </View>
      </LandalPressable>
    );
  }
);
