import React, { useContext } from 'react';
import { Platform, TouchableOpacity, View } from 'react-native';
import useTheme from '@app/hooks/useTheme';
import Text from '@app/components/Text';
import { useIntl } from 'react-intl';
import useMetrics from '@app/metrics/useMetrics';
import { useFocusEffect } from '@react-navigation/native';
import { useFormik } from 'formik';
import { useGetCountryItems } from '@domains/reservations/reservation/hooks/useGetCountryItems';
import FormPicker from '@app/components/Form/FormPicker';
import FormControl from '@app/components/Form/FormControl';
import Button from '@app/components/Button';
import { UserContext } from '@app/context/usercontext/UserContextProvider';
import { getBrand } from 'utils/brand';
import { useGetReservation } from '../queries/useGetReservation';
import { NavScreenProp } from '@app/navigation/stacks/Main';
import ScreenScrollContainer from '@app/components/ScreenScrollContainer';
import StickyHeader from '@app/components/StickyHeader';

type FormFields = 'country' | 'postalCode' | 'houseNumber' | 'houseNumberExtension' | 'streetName' | 'city';

type FormikErrors = {
  country?: string;
  postalCode?: string;
  houseNumber?: string;
  houseNumberExtension?: string;
  streetName?: string;
  city?: string;
};

const TravelCompanyAddress = ({ navigation, route }: NavScreenProp<'TravelCompanyAddress'>) => {
  const { formatMessage } = useIntl();
  const { logScreenView } = useMetrics();
  const theme = useTheme();
  const countryItems = useGetCountryItems();
  const COUNTRY_ITEMS = countryItems;
  const { token } = useContext(UserContext);

  const bookingReference = route?.params?.bookingReference;
  const setCustomAddress = route?.params?.setCustomAddress;
  const address = route?.params?.address;

  const { data: reservation } = useGetReservation({
    brand: getBrand(theme),
    token,
    bookingReference,
  });

  useFocusEffect(
    React.useCallback(() => {
      logScreenView('TravelCompanyAddress');
    }, [logScreenView])
  );

  const formik = useFormik<Record<FormFields, any>>({
    initialValues: {
      country:
        address?.country?.code ||
        reservation?.mainTenant?.address?.country?.code ||
        COUNTRY_ITEMS.find((c) => c.value === 'NL')?.value,
      postalCode: address?.postalCode,
      houseNumber: address?.houseNumber?.toString(),
      houseNumberExtension: address?.houseNumberExtension,
      streetName: address?.streetName,
      city: address?.city,
    },
    validateOnMount: false,
    validateOnChange: true,
    onSubmit: () => {},
    validate: (values) => {
      const errors: FormikErrors = {};
      if (!values.country) {
        errors.country = 'no country';
      }
      if (!values.city) {
        errors.country = 'no city';
      }
      if (!values.streetName) {
        errors.country = 'no streetName';
      }
      return errors;
    },
  });

  return (
    <>
      <StickyHeader
        title={formatMessage({ id: 'app.reservation.companions.addAddress' })}
        showClose
        navigation={navigation}
      />
      <ScreenScrollContainer topPadding={false} style={{ backgroundColor: theme.colors.white }}>
        {/* <KeyboardAwareScrollView
        style={{ flex: 1 }}
        enableOnAndroid
        extraScrollHeight={-180}
        keyboardOpeningTime={Number.MAX_SAFE_INTEGER}
      > */}
        <View style={{ flex: 1, marginTop: theme.space.xl }}>
          <View style={{ flex: 1 }}>
            <Text
              bold
              color="black"
              size="s"
              style={{ marginBottom: theme.space.s, lineHeight: theme.fontSizes.s.lineHeight }}
            >
              {formatMessage({ id: 'app.forms.labels.country' })}
            </Text>
            <FormPicker
              value={formik.values.country}
              items={COUNTRY_ITEMS}
              onValueChange={(value) => {
                formik.setFieldValue('country', value);
              }}
              style={{ marginBottom: theme.space.m }}
              border
            />

            <View style={{ flex: 1, flexDirection: 'row' }}>
              <FormControl
                label={formatMessage({ id: 'app.forms.labels.zipcode' })}
                value={formik.values.postalCode}
                isValid={!formik.errors.postalCode}
                placeholder="1234 AB"
                onChangeText={(text) => {
                  formik.setFieldValue('postalCode', text);
                }}
                style={{ marginRight: theme.space.s }}
                flex={4}
              />
              <FormControl
                label={formatMessage({ id: 'app.forms.labels.houseNumber' })}
                value={formik.values.houseNumber}
                isValid={!formik.errors.houseNumber}
                placeholder={'32'}
                onChangeText={(text) => {
                  formik.setFieldValue('houseNumber', text);
                }}
                style={{ marginRight: theme.space.s }}
                flex={3}
                keyboardType="number-pad"
              />
              <FormControl
                label={formatMessage({ id: 'app.forms.labels.houseNumberExtension' })}
                value={formik.values.houseNumberExtension}
                isValid={!formik.errors.houseNumberExtension}
                placeholder="bis"
                onChangeText={(text) => {
                  formik.setFieldValue('houseNumberExtension', text);
                }}
                flex={2}
                optional
              />
            </View>
            <FormControl
              label={formatMessage({ id: 'app.forms.labels.street' })}
              value={formik.values.streetName}
              isValid={!formik.errors.streetName}
              onChangeText={(text) => {
                formik.setFieldValue('streetName', text);
              }}
            />
            <FormControl
              label={formatMessage({ id: 'app.forms.labels.city' })}
              value={formik.values.city}
              isValid={!formik.errors.city}
              onChangeText={(text) => {
                formik.setFieldValue('city', text);
              }}
            />
          </View>
        </View>
        <View style={{ flex: Platform.OS === 'web' ? undefined : 0, marginTop: theme.space.l }}>
          <Button
            onPress={async () => {
              await setCustomAddress({
                city: formik.values.city,
                country: { code: formik.values.country },
                houseNumber: Number(formik.values.houseNumber),
                houseNumberExtension: formik.values.houseNumberExtension,
                postalCode: formik.values.postalCode,
                streetName: formik.values.streetName,
              });
              navigation.goBack();
            }}
            style={{ backgroundColor: theme.colors.secondary }}
            disabled={
              !formik.values.city ||
              !formik.values.streetName ||
              !formik.values.postalCode ||
              !formik.values.houseNumber
            }
          >
            <Button.Text color="white">
              {formatMessage({
                id: 'app.general.add',
              })}
            </Button.Text>
          </Button>
          <TouchableOpacity
            activeOpacity={0.8}
            style={{ marginTop: theme.space.l, alignItems: 'center' }}
            onPress={() => navigation.goBack()}
          >
            <Text color="gray" bold>
              {formatMessage({
                id: 'app.general.cancel',
              })}
            </Text>
          </TouchableOpacity>
        </View>
        {/* </KeyboardAwareScrollView> */}
      </ScreenScrollContainer>
    </>
  );
};
export default TravelCompanyAddress;
