import React from 'react';
import AccountScreen from '@domains/profile/screens/Account';
import CreateAccount from '@domains/profile/screens/CreateAccount';
import SelectLanguageSheet from '@domains/onboarding/screens/SelectLanguageSheet';
import ForgotPassword from '@domains/profile/screens/ForgotPassword';

// Types of params passed to route via navigation.navigate('SCREEN_NAME', params)
export type ProfileRoutes = {
  Profile: undefined;
  LogoutConfirmationSheet: { username: string };
  SelectLanguageSheet: { previousScreenName: string };
  ForgotPassword: { email: string };
  ResetPassword: undefined;
  CreateAccount: undefined;
};

export const useProfileScreens = (Stack: any) => {
  return (
    <>
      <Stack.Screen name="Account" component={AccountScreen} />
      <Stack.Screen name="SelectLanguageSheet" component={SelectLanguageSheet} />
      <Stack.Screen name="CreateAccount" component={CreateAccount} />
      <Stack.Screen name="ForgotPassword" component={ForgotPassword} />
    </>
  );
};
