import useMetrics from '@app/metrics/useMetrics';
import React, { useContext } from 'react';
import { UserContext } from '@app/context/usercontext/UserContextProvider';
import Config from 'react-native-config';
import { WebView } from 'react-native-webview';
import { getParameterByName } from 'utils/url';

const VISIBLE_WEBVIEWS = __DEV__ && false;

interface Props {
  reference: React.RefObject<WebView<{}>>;
  onError: () => void;
  onSuccess: (code: string | null) => void;
  onReady: () => void;
}

const SAPLoginWebview = ({ reference, onError, onSuccess, onReady }: Props) => {
  const { logEvent } = useMetrics();
  const { demoMode } = useContext(UserContext);

  if (demoMode) {
    return <></>;
  }

  const uri = `${Config.SAP_CDC_BASE_URL}/${Config.SAP_CDC_API_KEY}/authorize?client_id=${Config.SAP_CDC_CLIENT_ID}&redirect_uri=${Config.SAP_CDC_REDIRECT_URI}&response_type=${Config.SAP_CDC_RESPONSE_TYPE}&scope=${Config.SAP_CDC_SCOPE}`;

  return (
    <WebView
      source={{ uri }}
      style={{ width: VISIBLE_WEBVIEWS ? 100 : 1, height: VISIBLE_WEBVIEWS ? 100 : 1 }}
      originWhitelist={['https://*', 'git://*', 'http://*']}
      androidLayerType="hardware"
      onNavigationStateChange={({ url, loading }) => {
        if (loading) {
          return;
        }

        if (url.includes('?code=')) {
          const code = getParameterByName('code', url);
          // TODO: this appears to be triggered twice
          onSuccess(code);
          return;
        }

        // Lost request headers on redirect issue (iOS 12)
        if (url.includes('error.html?mode=error')) {
          const message = getParameterByName('message', url);
          if (message) {
            onError();
            // alert('OIDC login error: ' + message.replace(/[+]/g, ' '));
          }
        }
      }}
      ref={reference}
      onMessage={(event: any) => {
        const { data } = event.nativeEvent;
        switch (data) {
          case 'ready':
            onReady();
            break;
          case 'error':
            onError();
            break;
        }
      }}
      onError={() => {
        logEvent('LoginError', { error_type: 'oidc_load_error' });
        onError();
      }}
      onHttpError={() => {
        logEvent('LoginError', { error_type: 'oidc_load_error' });
        onError();
      }}
      domStorageEnabled
      sharedCookiesEnabled
      androidHardwareAccelerationDisabled
    />
  );
};
export default SAPLoginWebview;
