import React, { createContext, useContext } from 'react';
import ReturnSVG from '@app/components/svg/ReturnSVG';
import { ParkContextUpdater } from '@app/context/usercontext/ParkProvider';
import { UserContext } from '@app/context/usercontext/UserContextProvider';
import { useGetReservation } from '@domains/reservations/reservation/queries/useGetReservation';
import Banner from '@domains/start/components/Banner';
import { NavigationContainerRef, useNavigation } from '@react-navigation/native';
import { useIntl } from 'react-intl';
import { getBrand } from 'utils/brand';
import { addDays, fromUnixNumber, isTomorrow } from 'utils/date';
import useChecklist from '../hooks/useChecklist';
import useIsReservationPark from '../hooks/useIsReservationPark';
import useTheme from '../hooks/useTheme';
import ProgressSVG from '@app/components/svg/ProgressSvg';
import { BottomTabsContext } from '@app/navigation/BottomTabsProvider';
import useSelectedLanguageAvailableForPark from '@app/hooks/useSelectedLanguageAvailableForPark';
import useSelectedPark from '@app/hooks/useSelectedPark';
import Animated, {
  Easing,
  SlideInDown,
  SlideOutDown,
  useAnimatedStyle,
  withTiming,
} from 'react-native-reanimated';
import { BookingStatusType } from 'apis/_generated-reservation-api/models';
import useMetrics from '@app/metrics/useMetrics';
import { ChecklistCombiBanner } from '@app/components/ChecklistBanner';
import { CheckoutBanner, CheckoutCombiBanner } from '@app/components/CheckoutBanner';
import useHasOwnApp from '@app/hooks/useHasOwnApp';

enum ActiveBannerType {
  Checklist = 'CHECKLIST',
  Checkout = 'CHECKOUT',
  BackToReservation = 'BACKTORESERVATION',
  LanguageNotAvailable = 'LANGUAGENOTAVAILABLE',
}

const StickyBannerContext = createContext({ bannerShown: false, component: <></> });

export const useStickyBanner = () => {
  const context = useContext(StickyBannerContext);
  return context;
};

// specifiek component voor combibooking percentage en non-combi

const StickyBannerProvider = ({ children }: { children: React.ReactNode }) => {
  const navigation = useNavigation<NavigationContainerRef<any>>();

  const theme = useTheme();
  const { logEvent } = useMetrics();
  const { token } = useContext(UserContext);
  const activeTab = useContext(BottomTabsContext);
  const { formatMessage } = useIntl();
  const { pendingTasks, percentageComplete } = useChecklist({});
  const changePark = useContext(ParkContextUpdater);
  const selectedLanguageAvailableForPark = useSelectedLanguageAvailableForPark();
  const { isReservationPark, parkCode: reservationParkCode } = useIsReservationPark();
  const { data: reservation } = useGetReservation({
    token,
    brand: getBrand(theme),
  });
  const { park } = useSelectedPark();

  const isDayBeforeDepartureDay = !!reservation?.departureDate && isTomorrow(reservation?.departureDate);

  const { hasOwnApp } = useHasOwnApp(reservationParkCode);

  const showChecklistBanner =
    !!token &&
    !!reservation?.seamlessEnabled &&
    !!reservation?.arrivalDate &&
    pendingTasks > 0 &&
    addDays(fromUnixNumber(reservation?.arrivalDate), 1) <= new Date() &&
    activeTab === 0 &&
    !hasOwnApp;
  const showCheckoutBanner =
    !!token &&
    !!reservation &&
    !!reservation?.featuresEnabled?.checkoutNow &&
    reservation.status !== BookingStatusType.Departed &&
    activeTab === 0 &&
    !hasOwnApp;

  const showBackToReservationBanner = activeTab === 1 && !isReservationPark && !hasOwnApp;
  const showLanguageNotAvailableBanner =
    activeTab === 1 && !selectedLanguageAvailableForPark && !showBackToReservationBanner && !!park;

  const bannerShown =
    showChecklistBanner ||
    showCheckoutBanner ||
    showBackToReservationBanner ||
    showLanguageNotAvailableBanner;

  // available banners: checklist, checkout, backToReservation, languageNotAvailable
  const activeBanner = showChecklistBanner
    ? ActiveBannerType.Checklist
    : showCheckoutBanner
    ? ActiveBannerType.Checkout
    : showBackToReservationBanner
    ? ActiveBannerType.BackToReservation
    : showLanguageNotAvailableBanner
    ? ActiveBannerType.LanguageNotAvailable
    : null;

  const bannerPositionStyles = useAnimatedStyle(() => {
    return {
      top: withTiming(bannerShown ? 0 : 90, {
        duration: 400,
        easing: Easing.bezier(0.25, 0.1, 0.25, 1),
      }),
    };
  });

  // additional Animated View wrapper added to move the banners down as a whole
  // note: no banner shown: an empty view will be transitioned up.

  const component = (
    <Animated.View style={[{ position: 'relative', width: '100%' }, bannerPositionStyles]}>
      <Animated.View
        style={{
          width: '100%',
          height: 88,
          elevation: 1,
        }}
        key={
          showChecklistBanner.toString() +
          showCheckoutBanner.toString() +
          showBackToReservationBanner.toString() +
          showLanguageNotAvailableBanner.toString()
        }
        entering={SlideInDown.duration(1400).delay(200).springify().stiffness(40).damping(12)}
        exiting={SlideOutDown.duration(1400)}
      >
        {activeBanner === ActiveBannerType.Checklist &&
          (reservation?.combiReference ? (
            <ChecklistCombiBanner
              combiReference={reservation?.combiReference ?? ''}
              reservation={reservation}
            />
          ) : (
            <Banner
              label={
                pendingTasks
                  ? `${
                      pendingTasks > 1
                        ? formatMessage({ id: 'app.launcher.openTasks' })
                        : formatMessage({ id: 'app.launcher.openTask' })
                    }`.replace('{0}', pendingTasks.toString())
                  : formatMessage({ id: 'app.launcher.noOpenTask' })
              }
              text={
                pendingTasks
                  ? formatMessage({ id: 'app.launcher.openTaskDescription' })
                  : formatMessage({ id: 'app.launcher.noOpenTaskDescription' })
              }
              onPress={() => {
                logEvent('BannerChecklist');
                navigation?.navigate('Reservation', { bookingReference: reservation?.bookingReference });
              }}
              iconSvg={<ProgressSVG progressPercentage={percentageComplete} />}
              sticky
            />
          ))}
        {activeBanner === ActiveBannerType.Checkout &&
          (reservation?.combiReference ? (
            <CheckoutCombiBanner
              combiReference={reservation?.combiReference ?? ''}
              reservation={reservation}
            />
          ) : (
            <CheckoutBanner reservation={reservation} />
          ))}
        {activeBanner === ActiveBannerType.BackToReservation && (
          <Banner
            label={formatMessage({ id: 'app.reservation.backToReservation' })}
            text={formatMessage({ id: 'app.reservation.backToReservation.subtitle' })}
            onPress={() => {
              if (reservationParkCode) {
                logEvent('BannerReturnToMyPark');
                changePark(reservationParkCode);
              }
            }}
            iconSvg={<ReturnSVG />}
            sticky
          />
        )}
        {activeBanner === ActiveBannerType.LanguageNotAvailable && (
          <Banner
            label={formatMessage({ id: 'app.park.languageNotAvailable.title' })}
            text={formatMessage({ id: 'app.park.languageNotAvailable.subtitle' })}
            onPress={() => {}}
            sticky
          />
        )}
      </Animated.View>
    </Animated.View>
  );

  return (
    <StickyBannerContext.Provider value={{ bannerShown, component }}>{children}</StickyBannerContext.Provider>
  );
};

export default StickyBannerProvider;
