import React from 'react';
import LottieView from 'lottie-react-native';
import useTheme from '@app/hooks/useTheme';
import { getBrand } from 'utils/brand';
import { Platform, ViewStyle } from 'react-native';

interface Props {
  color?: 'white' | 'green' | 'blue';
  style?: ViewStyle;
  size?: number;
}

const assets = {
  green: require('@app/assets/lottie/loader.json'),
  white: require('@app/assets/lottie/loader-white.json'),
  blue: require('@app/assets/lottie/loader-blue.json'),
};

const SIZE = 60;
export default function Loader({ color, style, size = SIZE }: Props) {
  const theme = useTheme();
  const brand = getBrand(theme);
  const defColor = color || brand === 'HSN' ? 'blue' : 'green';
  const asset = assets[defColor];
  if (Platform.OS === 'web') {
    return null;
  }
  return (
    <LottieView
      source={asset}
      loop={true}
      autoPlay
      hardwareAccelerationAndroid
      //cacheStrategy="strong"
      style={[
        {
          width: size,
          height: size,
        },
        style,
      ]}
    />
  );
}
