import React, { useState } from 'react';
import useTheme from '@app/hooks/useTheme';
import { View, Linking, Alert } from 'react-native';
import Text from '@app/components/Text';
import { TextInput, TouchableOpacity } from 'react-native-gesture-handler';
import ContentContainer from '@app/components/DetailScreen/ContentContainer';
import DateSelector from '@app/components/Reservation/DateSelector';
import TimeSlotSelector from '@app/components/Reservation/TimeSlotSelector';
import { useFormik } from 'formik';
import useCart from '@app/hooks/useCart';
import { FormattedNumber, useIntl } from 'react-intl';
import FormButton from '@app/components/Form/FormButton';
import FormCheckBox from '@app/components/Form/FormCheckBox';
import Button from '@app/components/Button';
import { useGetShopSettings } from '../queries/useGetShopSettings';
import { useGetShopAvailability } from '../queries/useGetShopAvailability';
import { usePostOrder } from '../queries/usePostOrder';
import useMetrics from '@app/metrics/useMetrics';
import { KeyboardAwareScrollView } from 'react-native-keyboard-aware-scroll-view';
import { CartContextUpdater } from '@app/context/CartProvider';
import { useFocusEffect } from '@react-navigation/native';
import { isValidEmail, isValidPhone } from 'utils/validation';
import { ShopOrderRequest } from 'apis/_generated-park-api/models';
import StickyHeader from '@app/components/StickyHeader';
import FormControl from '@app/components/Form/FormControl';
import { NavScreenProp } from '@app/navigation/stacks/Main';

type FormFields =
  | 'date'
  | 'time'
  | 'timeslot'
  | 'name'
  | 'phone'
  | 'email'
  | 'accommodation_number'
  | 'checkout_type'
  | 'agreed_to_terms';

export default function CompleteOrder({ navigation, route }: NavScreenProp<'CompleteOrder'>) {
  const theme = useTheme();
  const { logScreenView } = useMetrics();
  const { formatNumber, formatMessage } = useIntl();
  const { logEvent } = useMetrics();
  const { activeShop: shopId } = useCart();

  const facilityId = route?.params?.facilityId;

  const formik = useFormik<Record<FormFields, any>>({
    initialValues: {
      date: undefined,
      time: undefined,
      name: '',
      phone: '',
      email: '',
      timeslot: undefined,
      accommodation_number: undefined,
      checkout_type: 'pickup',
      agreed_to_terms: false,
    },
    onSubmit: () => {},
    validate: (values) => {
      if (!values.agreed_to_terms) {
        return { error: 'agreed_to_terms' };
      }

      // TODO: These checks should be based on the callback values
      if (!validAccommodationNumber || !validEmail || !validName || !validPhone) {
        return { error: 'invalid_data_provided' };
      }
      return {};
    },
  });

  const { updateOrderMeta } = React.useContext(CartContextUpdater);
  const { cart } = useCart();
  const { data: settings } = useGetShopSettings({
    options: {
      onSuccess: (newSettings) => {
        const hasPickup = newSettings?.hasPickup;
        const hasDeliver = newSettings?.hasDeliver;

        const hasBothOptions = hasPickup && hasDeliver;

        if (!hasBothOptions) {
          formik.setFieldValue('checkout_type', hasPickup ? 'pickup' : 'deliver');
        }
      },
    },
  });
  const { postOrder } = usePostOrder();
  const scrollViewRef = React.useRef(null);
  const [processing, setProcessing] = useState(false);

  const { data: availability, isLoading: availabilityLoading } = useGetShopAvailability({
    type: formik.values.checkout_type,
  });

  useFocusEffect(
    React.useCallback(() => {
      if (shopId) {
        logScreenView('CompleteOrder', [shopId.toString()]);
      }
    }, [logScreenView, shopId])
  );

  const selectedDateAvailability = availability?.filter(
    (i) =>
      !!i.dateTimeFrom && new Date(i.dateTimeFrom).getUTCDate() === new Date(formik.values.date).getUTCDate()
  );

  const availabilityTimeslots = selectedDateAvailability?.map((a) => {
    return {
      timeslot: a.dateTimeFrom ? new Date(a.dateTimeFrom).getTime() / 1000 : 0,
      id: a.id?.toString(),
    };
  });

  const DELIVERY_PRICE = settings?.deliveryCost ? Number(settings.deliveryCost) : 0;
  const phoneInputRef = React.useRef<TextInput>(null);

  const validName = !!formik.values.name;
  const validEmail = formik.values.email ? isValidEmail(formik.values.email) : false;
  const validPhone = formik.values.phone ? isValidPhone(formik.values.phone) : false;
  const validAccommodationNumber = !isNaN(formik.values.accommodation_number);

  const hasPickup = settings?.hasPickup;
  const hasDeliver = settings?.hasDeliver;
  const showPickupOrDeliver = hasPickup && hasDeliver;

  const totalPrice =
    cart
      ?.map((item) => item.totalPrice * item.quantity)
      .reduce((prev: number, next: number) => Number(prev) + Number(next), 0) +
    (formik.values.checkout_type === 'deliver' ? DELIVERY_PRICE : 0);

  return (
    <View style={{ flex: 1 }}>
      <StickyHeader
        showClose
        navigation={navigation}
        title={formatMessage({ id: 'app.shop.cart.completeorder' })}
      />
      <KeyboardAwareScrollView
        style={{ flex: 1 }}
        ref={scrollViewRef}
        enableOnAndroid
        extraScrollHeight={120}
        keyboardOpeningTime={Number.MAX_SAFE_INTEGER}
        enableResetScrollToCoords={false}
        keyboardShouldPersistTaps="handled"
      >
        <ContentContainer style={{ flex: 1 }}>
          <Text type="title" size="l" style={{ marginBottom: theme.space.l }}>
            {formatMessage({ id: 'app.shop.payment.pickuptime' })}
          </Text>
          <View style={{ marginBottom: 16 }}>
            {showPickupOrDeliver && (
              <Text bold color="white" size="s" style={{ marginBottom: 8 }}>
                {formatMessage({ id: 'app.forms.labels.pickupordelivery' })}
              </Text>
            )}
            <View style={{ flexDirection: 'row', justifyContent: 'space-between', flex: 1 }}>
              {hasPickup && showPickupOrDeliver && (
                <FormButton
                  style={{ flex: 1, marginRight: 8 }}
                  text={formatMessage({ id: 'app.shop.takeaway' })}
                  selected={formik.values.checkout_type === 'pickup'}
                  onPress={() => {
                    formik.setFieldValue('checkout_type', 'pickup');
                  }}
                />
              )}
              {hasDeliver && showPickupOrDeliver && (
                <FormButton
                  style={{ flex: 1 }}
                  text={`${formatMessage({ id: 'app.forms.summary.delivery' })} (+ ${formatNumber(
                    DELIVERY_PRICE,
                    {
                      currency: 'EUR',
                      currencyDisplay: 'symbol',
                      style: 'currency',
                    }
                  )})`}
                  selected={formik.values.checkout_type === 'deliver'}
                  onPress={() => {
                    formik.setFieldValue('checkout_type', 'deliver');
                  }}
                />
              )}
            </View>
          </View>
          <DateSelector
            date={formik.values.date}
            onSelectDate={(newDate) => {
              // TODO: Reset time when another date is selected, as the previous time might not be available this day
              formik.setValues({
                ...formik.values,
                date: newDate,
                time: undefined,
              });
            }}
          />
          {!!formik.values.date && (
            <TimeSlotSelector
              time={formik.values.time}
              onSelectTime={(time, timestamp) => {
                const selectedTimeslot = availabilityTimeslots?.find((c) => c.timeslot === timestamp);
                formik.setValues({
                  ...formik.values,
                  time,
                  timeslot: selectedTimeslot,
                });
              }}
              slotDuration={15}
              availability={availabilityTimeslots}
              showDayPart={true}
              dataIsLoading={availabilityLoading}
            />
          )}
          {!!formik.values.time && (
            <FormControl
              label={formatMessage({ id: 'app.forms.labels.name' })}
              value={formik.values.name}
              isValid={validName}
              onSubmitEditing={() => phoneInputRef.current?.focus()}
              onChangeText={(text) => {
                formik.setFieldValue('name', text);
              }}
            />
          )}
          {!!formik.values.name && (
            <FormControl
              ref={phoneInputRef}
              label={formatMessage({ id: 'app.forms.labels.phone' })}
              value={formik.values.phone}
              isValid={validPhone}
              onChangeText={(text) => {
                formik.setFieldValue('phone', text);
              }}
            />
          )}
          {validPhone && (
            <FormControl
              label={
                formatMessage({ id: 'app.forms.labels.email' }) +
                ' ' +
                formatMessage({ id: 'app.forms.labels.email.explanation' })
              }
              value={formik.values.email}
              isValid={validEmail}
              onChangeText={(text) => {
                formik.setFieldValue('email', text);
              }}
            />
          )}
          {validEmail && (
            <FormControl
              label={formatMessage({ id: 'app.forms.labels.accommodationnumber' })}
              value={formik.values.accommodation_number}
              isValid={validAccommodationNumber}
              onChangeText={(text) => {
                formik.setFieldValue('accommodation_number', text);
              }}
            />
          )}
          {validAccommodationNumber && (
            <>
              <View style={{ borderRadius: 8, backgroundColor: 'white', padding: theme.space.l }}>
                <Text
                  bold
                  style={{
                    marginBottom: theme.space.l,
                  }}
                >
                  {formatMessage({ id: 'app.forms.labels.yourorder' })}
                </Text>
                <View
                  style={{
                    paddingBottom: theme.space.l,
                    marginBottom: theme.space.l,
                    borderBottomWidth: 1,
                  }}
                >
                  {cart?.map((i) => (
                    <View
                      style={{
                        flexDirection: 'row',
                        justifyContent: 'space-between',
                        marginBottom: theme.space.l * 0.5,
                      }}
                      key={i.product?.id + i.options?.join(',')}
                    >
                      <Text size="s" style={{ flexShrink: 1 }}>{`${i.quantity} x ${
                        i.product?.name ?? i.product?.alias
                      }, ${i?.options?.map((o) => o.name)?.join(', ')}`}</Text>
                      <Text size="s">
                        <FormattedNumber value={i.totalPrice} currency={'EUR'} minimumFractionDigits={2} />
                      </Text>
                    </View>
                  ))}
                </View>
                {formik.values.checkout_type === 'deliver' && (
                  <View
                    style={{
                      flexDirection: 'row',
                      justifyContent: 'space-between',
                      marginBottom: theme.space.l * 0.5,
                    }}
                  >
                    <Text size="s">{formatMessage({ id: 'app.forms.summary.delivery' })}</Text>
                    <Text size="s">
                      <FormattedNumber value={DELIVERY_PRICE} currency={'EUR'} minimumFractionDigits={2} />
                    </Text>
                  </View>
                )}
                <View
                  style={{
                    flexDirection: 'row',
                    justifyContent: 'space-between',
                  }}
                >
                  <Text>{formatMessage({ id: 'app.forms.summary.total' })}</Text>
                  <Text type="title" bold>
                    <FormattedNumber
                      value={totalPrice}
                      style="currency"
                      currency={'EUR'}
                      currencyDisplay={'symbol'}
                    />
                  </Text>
                </View>
              </View>
              <View
                style={{
                  marginBottom: theme.space.l,
                  marginTop: theme.space.l,
                  flexDirection: 'row',
                }}
              >
                <FormCheckBox
                  value={formik.values.agreed_to_terms}
                  onValueChange={(newValue) => formik.setFieldValue('agreed_to_terms', newValue)}
                />

                <Text
                  bold
                  color="gray"
                  size="s"
                  style={{ marginLeft: 16, flexShrink: 1 }}
                  onPress={() => {
                    const url = formatMessage({ id: 'app.forms.privacy.terms.url' });
                    Linking.openURL(url);
                  }}
                >
                  {formatMessage({ id: 'app.forms.privacy.terms.part1' })}{' '}
                  <Text bold color="gray" size="s" style={{ textDecorationLine: 'underline' }}>
                    {formatMessage({ id: 'app.forms.privacy.terms.part2' })}
                  </Text>{' '}
                  {formatMessage({ id: 'app.forms.privacy.terms.part3' })}
                </Text>
              </View>
              <Button
                type="primary"
                disabled={!formik.values.agreed_to_terms || processing}
                onPress={async () => {
                  if (!formik.values.agreed_to_terms) {
                    return;
                  }

                  if (!formik.values.timeslot || isNaN(Number(formik.values.timeslot.id))) {
                    return;
                  }

                  setProcessing(true);
                  const order: ShopOrderRequest = {
                    customer: {
                      email: formik.values.email,
                      reference: formik.values.accommodation_number,
                      billingAddress: {
                        firstName: formik.values.name,
                        mobile: formik.values.phone,
                      },
                    },
                    orderRows: cart?.map((p) => {
                      return {
                        count: p.quantity,
                        productId: p.product?.id,
                        attributeCombinationId: p.attributeComboId,
                      };
                    }),
                    takeoutSlot: formik.values.timeslot.id,
                    deliverMethod: formik.values.checkout_type,
                    totalShipping: formik.values.checkout_type === 'deliver' ? DELIVERY_PRICE : 0,
                  };
                  try {
                    const result = await postOrder(order);

                    if (result?.paymentUrl) {
                      const orderMeta = {
                        facilityId: facilityId.toString(),
                        pickupDeliveryDate: new Date(formik.values.timeslot.timeslot * 1000),
                        orderNumber: result.orderNumber ? Number(result.orderNumber) : 0,
                        pickup: formik.values.checkout_type === 'pickup',
                      };

                      updateOrderMeta(orderMeta);

                      logEvent('ExternalLink', {
                        link: result?.paymentUrl,
                        location: 'button',
                      });
                      Linking.openURL(result?.paymentUrl);
                    }
                  } catch (ex) {
                    Alert.alert(formatMessage({ id: 'app.forms.tablebooking.error.generic' }));
                  } finally {
                    setProcessing(false);
                  }
                }}
                style={{
                  marginTop: 16,
                  marginBottom: 16,
                  backgroundColor: formik.isValid ? theme.colors.tertiary : theme.colors.light,
                }}
              >
                {processing ? (
                  <Button.ActivityIndicator />
                ) : (
                  <Button.Text>{formatMessage({ id: 'app.forms.action.order' })}</Button.Text>
                )}
              </Button>
              <TouchableOpacity
                style={{ marginBottom: theme.space.l }}
                onPress={() => {
                  const url = formatMessage({ id: 'app.forms.privacy.statement.url' });
                  Linking.openURL(url);
                }}
              >
                <Text color="gray" style={{ textDecorationLine: 'underline' }}>
                  {formatMessage({ id: 'app.forms.privacy.terms.privacystatement' })}
                </Text>
              </TouchableOpacity>
            </>
          )}
        </ContentContainer>
      </KeyboardAwareScrollView>
    </View>
  );
}
