export function getFullName(firstname?: string, middlename?: string, lastname?: string) {
  if (!middlename && !firstname && !lastname) return '';
  if (!middlename && !lastname) return firstname;
  if (!middlename && !firstname) return lastname;
  if (!middlename) return `${firstname} ${lastname}`;
  return `${firstname} ${middlename} ${lastname}`;
}
