import React, { PropsWithChildren, ReactNode, useEffect } from 'react';
import { TouchableOpacity, View } from 'react-native';
import Button from '@app/components/Button';
import useTheme from '@app/hooks/useTheme';
import Text from '@app/components/Text';
import { useIntl } from 'react-intl';
import useMetrics, { EventName } from '@app/metrics/useMetrics';
import Animated, { FadeIn, FadeOut } from 'react-native-reanimated';
import LockStatusSVG, { LockSheetStatus } from '@app/components/svg/LockStatusSVG';
import { useBottomSheet } from '@app/navigation/bottom-sheet/BottomSheetProvider';
import { useFocusEffect } from '@react-navigation/native';

interface Props {
  onCancel?: () => void;
  onPress?: () => void;
  onMount?: () => void;
  onFocus?: () => void;
  onHelp?: () => void;
  helpComponent?: () => ReactNode;
  eventName?: EventName;
  icon: string;
  status: LockSheetStatus;
  text: string;
  buttonText?: string;
  showSubmitButton?: boolean;
  showCancelButton?: boolean;
  disabled?: boolean;
  processing?: boolean;
}

export default function LockSheetContent({
  eventName,
  icon,
  status,
  text,
  buttonText,
  onCancel,
  onPress,
  onFocus,
  onMount,
  onHelp,
  helpComponent,
  showSubmitButton = true,
  showCancelButton = true,
  disabled,
  processing,
  children,
}: PropsWithChildren<Props>) {
  const theme = useTheme();
  const { formatMessage } = useIntl();
  const { logEvent } = useMetrics();
  const { closeBottomSheet } = useBottomSheet();

  React.useEffect(() => {
    !!eventName && logEvent(eventName);
  }, [eventName, logEvent]);

  useEffect(() => {
    onMount?.();
  }, [onMount]);

  useFocusEffect(
    React.useCallback(() => {
      onFocus?.();
    }, [onFocus])
  );

  return (
    <Animated.View
      style={{
        alignItems: 'center',
        marginHorizontal: theme.space.l,
      }}
      entering={FadeIn.delay(400).duration(400)}
      exiting={FadeOut.duration(400)}
    >
      <View
        style={{
          marginTop: theme.space.xl,
          marginHorizontal: theme.space.m,
          alignItems: 'center',
        }}
      >
        <LockStatusSVG size={90} icon={icon} status={status} />
        <Text
          color="dark"
          style={{
            marginTop: theme.space.xl,
            textAlign: 'center',
            flexWrap: 'wrap',
          }}
        >
          {text}
        </Text>
      </View>
      {children}
      {(!!onHelp || !!helpComponent) && (
        <View style={{ marginTop: theme.space.xl, alignItems: 'center' }}>
          {!!onHelp && (
            <Text type="body" color="tertiary" onPress={onHelp} bold>
              {formatMessage({ id: 'app.key.failed' })}
            </Text>
          )}
          {!!helpComponent && helpComponent()}
        </View>
      )}

      <View style={{ width: '100%', marginTop: theme.space.xxl }}>
        {showSubmitButton && (
          <Button
            onPress={onPress}
            disabled={disabled || processing}
            style={{
              backgroundColor: theme.colors.secondary,
              marginBottom: showCancelButton ? theme.space.m : undefined,
            }}
          >
            {processing ? (
              <Button.ActivityIndicator style={{ height: 22 }} />
            ) : (
              <Button.Text color="white">{buttonText}</Button.Text>
            )}
          </Button>
        )}
        {showCancelButton && (
          <TouchableOpacity style={{ alignItems: 'center' }} onPress={onCancel ?? closeBottomSheet}>
            <Text color="gray" style={{ lineHeight: 26 }} bold>
              {formatMessage({
                id: 'app.general.cancel',
              })}
            </Text>
          </TouchableOpacity>
        )}
      </View>
    </Animated.View>
  );
}
