import { UserContext } from '@app/context/usercontext/UserContextProvider';
import useCulture from '@app/hooks/useCulture';
import useTheme from '@app/hooks/useTheme';
import { useGetReservation } from '@domains/reservations/reservation/queries/useGetReservation';
import { AxiosError } from 'axios';
import { gql, GraphQLClient } from 'graphql-request';
import { useContext } from 'react';
import Config from 'react-native-config';
import { UseQueryOptions, useQuery } from 'react-query';
import { getBrand } from 'utils/brand';

export interface ReservationPark {
  name: string;
  allowAccommodationNavigation: boolean;
}

const key = `getReservationPark`;
type ParkDataProps = {
  options?: Omit<UseQueryOptions<ReservationPark, AxiosError>, typeof key>;
};

export function useGetReservationPark(props?: ParkDataProps) {
  const { cultureCode } = useCulture();
  const theme = useTheme();
  const { token } = useContext(UserContext);
  const { data: reservation } = useGetReservation({
    token,
    brand: getBrand(theme),
  });

  const parkCode = reservation?.parkCode;

  return useQuery(
    [key, { parkCode, cultureCode }],
    async () => {
      if (!parkCode || !cultureCode) {
        throw 'no reservation found';
      }
      const graphQLClient = new GraphQLClient(Config.PARK_API_URL);
      const query = gql`
        query park {
          park(parkCode: "${parkCode}", cultureCode: "${cultureCode}") {
            name
            allowAccommodationNavigation
          }
        } 
      `;
      const result = await graphQLClient.rawRequest<{ park: ReservationPark }>(query);
      return {
        name: result?.data.park.name,
        allowAccommodationNavigation: result?.data.park.allowAccommodationNavigation,
      };
    },
    props?.options
  );
}
