import useTheme from '@app/hooks/useTheme';
import React, { useEffect, useRef, useState } from 'react';
import { Linking, useWindowDimensions, Platform, View } from 'react-native';
import { useIntl } from 'react-intl';
import { SafeAreaView } from 'react-native-safe-area-context';
import { BackHandler } from 'react-native';
import Text from '@app/components/Text';
import LandalImage from '@app/components/LandalImage';
import { KeyRefType } from '@domains/locks/types/KeyRefType';
import useMetrics from '@app/metrics/useMetrics';
import { NavigationContainerRef, useFocusEffect, useNavigation } from '@react-navigation/native';
import { NavScreenProp } from '@app/navigation/stacks/Main';
import Button from '@app/components/Button';

interface Props extends NavScreenProp<'AppDeprecated'> {}

const AppDeprecated = ({}: Props) => {
  const theme = useTheme();
  const { formatMessage } = useIntl();
  const { logEvent } = useMetrics();
  const { width: screenWidth, height: screenHeight } = useWindowDimensions();
  const keyComponentRef = useRef<KeyRefType>(null);
  const [keyAvailable, setKeyAvailable] = useState(false);
  const navigation = useNavigation<NavigationContainerRef<any>>();

  useFocusEffect(
    React.useCallback(() => {
      logEvent('AppDeprecated');
    }, [logEvent])
  );

  useEffect(() => {
    BackHandler.addEventListener('hardwareBackPress', function () {
      return true;
    });
  }, [logEvent]);

  useEffect(() => {
    if (keyComponentRef?.current) {
      if (keyComponentRef?.current?.isKeyAvailable()) {
        setKeyAvailable(true);
      } else {
        setKeyAvailable(false);
      }
    }
  }, [keyComponentRef]);

  const oopsArray = formatMessage({
    id: 'app.deprecated.title',
  })?.split('\n');
  const oops1 = oopsArray[0];
  const oops2 = oopsArray[1];

  return (
    <SafeAreaView style={{ flex: 1, backgroundColor: theme.colors.white }} edges={['bottom']}>
      <View
        style={{
          flex: screenHeight / screenWidth > 2 ? 0 : 1,
          flexShrink: 1,
          overflow: 'hidden',
          justifyContent: 'center',
        }}
      >
        <LandalImage
          style={{
            height: screenWidth * 1,
          }}
          localSource={
            theme.type === 'hofvansaksen'
              ? require('@app/assets/images/hsn/hsn-feedback.png')
              : require('@app/assets/images/bollo-oops.gif')
          }
        />
      </View>

      <View
        style={{
          flex: Platform.OS === 'web' ? undefined : 0,
          flexGrow: screenHeight / screenWidth > 2 ? 1 : 0.4,
          padding: theme.space.xl,
          paddingBottom: theme.space.xl,
        }}
      >
        <View style={{ flex: 1 }}>
          <Text type="title" bold size="xl" style={{ marginTop: theme.space.m }}>
            {oops1}
          </Text>
          <Text type="title" size="xl" style={{ marginBottom: theme.space.m }}>
            {oops2}
          </Text>
          <Text type="body" color="dark" style={{ lineHeight: theme.fontSizes.m.lineHeight }}>
            {formatMessage({
              id: 'app.deprecated.description',
            })}
          </Text>
        </View>
      </View>
      <View
        style={{
          flex: Platform.OS === 'web' ? undefined : 0,
          padding: theme.space.xl,
          paddingBottom: theme.space.xl,
        }}
      >
        <Button
          onPress={() => {
            if (Platform.OS === 'ios') {
              Linking.openURL('https://apps.apple.com/us/app/landal-greenparks/id1290736653');
            } else {
              Linking.openURL('https://play.google.com/store/apps/details?id=com.landal.parkapp');
            }
          }}
          style={{
            backgroundColor: theme.colors.secondary,
          }}
        >
          <Button.Icon name="exit-app" size="s" color="white" />
          <Button.Text color="white">
            {formatMessage({
              id: 'app.deprecated.' + Platform.OS,
            })}
          </Button.Text>
        </Button>
      </View>
    </SafeAreaView>
  );
};
export default AppDeprecated;
