import useTheme from '@app/hooks/useTheme';
import React, { useState } from 'react';
import { useIntl } from 'react-intl';
import {
  KeyboardTypeOptions,
  TextInputProps,
  View,
  ViewStyle,
  TouchableOpacity,
  Platform,
} from 'react-native';
import { fromUnixNumber, getFormattedDate } from 'utils/date';
import CustomDatePicker from '../CustomDatePicker';
import Text from '../Text';
import FormInput from './FormInput';

const MANDATORY_DOT_SIZE = 4;

interface Props extends TextInputProps {
  label?: string;
  sublabel?: string;
  value?: string;
  isValid: boolean;
  onChangeText: (text: string) => void;
  style?: ViewStyle;
  flex?: number;
  date?: boolean;
  minimumDate?: Date;
  maximumDate?: Date;
  placeholder?: string;
  errorMessage?: string;
  keyboardType?: KeyboardTypeOptions;
  optional?: boolean;
}
export default function FormControl({
  label,
  sublabel,
  value,
  isValid,
  onChangeText,
  style,
  flex,
  date,
  minimumDate,
  maximumDate,
  errorMessage,
  placeholder,
  keyboardType,
  optional,
  ...props
}: Props) {
  const theme = useTheme();
  const { formatMessage } = useIntl();
  const [showDatePicker, setShowDatePicker] = useState(false);
  const [blurred, setBlurred] = useState(false);

  return (
    <View
      style={[{ marginBottom: theme.space.xl, flex: flex || (Platform.OS === 'web' ? undefined : 0) }, style]}
    >
      <View style={{ flexDirection: 'row' }}>
        {!!label && (
          <Text bold style={{ marginBottom: theme.space.s }}>
            {label}
          </Text>
        )}
        {!!sublabel && (
          <Text color="gray" style={{ marginBottom: theme.space.s }}>
            {' ' + sublabel}
          </Text>
        )}
        {!optional && (
          <View
            style={{
              top: 4,
              width: MANDATORY_DOT_SIZE,
              height: MANDATORY_DOT_SIZE,
              borderRadius: MANDATORY_DOT_SIZE / 2,
              backgroundColor: theme.colors.attention,
              marginLeft: 2,
            }}
          />
        )}
      </View>
      <TouchableOpacity disabled={!date} onPress={() => date && setShowDatePicker(true)} activeOpacity={1}>
        <FormInput
          value={
            date
              ? value
                ? getFormattedDate(new Date(fromUnixNumber(value ? Number(value) * 1000 : 0).toUTCString()))
                : formatMessage({ id: 'app.forms.labels.fillIn' })
              : value
          }
          isValid={!blurred ? true : isValid}
          showIcon={false}
          onChangeText={onChangeText}
          editable={!date}
          placeholder={placeholder}
          pointerEvents={date ? 'none' : 'auto'}
          onBlur={() => {
            if (value) {
              setBlurred(true);
            } else {
              setBlurred(false);
            }
          }}
          keyboardType={keyboardType}
          {...props}
        />
      </TouchableOpacity>
      {!!errorMessage && !isValid && blurred && (
        <Text color={'attention'} size="s" style={{ marginTop: theme.space.s }}>
          {errorMessage}
        </Text>
      )}
      {date && showDatePicker && (
        <CustomDatePicker
          minimumDate={minimumDate}
          maximumDate={maximumDate}
          initialValue={value ? Number(value) : undefined}
          onChange={(selectedDate) => {
            setShowDatePicker(false);
            if (selectedDate) {
              onChangeText?.(selectedDate);
            }
          }}
          onClose={() => {
            setShowDatePicker(false);
          }}
        />
      )}
    </View>
  );
}
