import { useIntl } from 'react-intl';
import { getTimeSlotOptions } from 'utils/date';

export default function useTimeSlotOptions(accommodationShouldLeaveDateTime?: number) {
  const { formatMessage } = useIntl();

  return getTimeSlotOptions(accommodationShouldLeaveDateTime).map((t) => ({
    ...t,
    label: t.label.replace('{before}', formatMessage({ id: 'app.times.before' })),
  }));
}
