import React, { useContext, useEffect, useMemo, useState } from 'react';
import { Alert, TouchableOpacity, View } from 'react-native';
import useTheme from '@app/hooks/useTheme';
import Text from '@app/components/Text';
import { useIntl } from 'react-intl';
import useMetrics from '@app/metrics/useMetrics';
import { useFocusEffect } from '@react-navigation/native';
import { isValidPhone } from 'utils/validation';
import { useFormik } from 'formik';
import { UserContext } from '@app/context/usercontext/UserContextProvider';
import { getBrand } from 'utils/brand';
import { useGetReservation } from '../queries/useGetReservation';
import FormControl from '@app/components/Form/FormControl';
import GenderSelector from '../components/GenderSelector';
import AddressSelector from '../components/AddressSelector';
import { getFullName } from '../../../../utils/name';
import Button from '@app/components/Button';
import { Address } from 'apis/_generated-reservation-api/models';
import { useMutateTravelCompanion } from '../queries/useMutateTravelCompanion';
import StickyHeader from '@app/components/StickyHeader';
import ScreenScrollContainer from '@app/components/ScreenScrollContainer';
import { NavScreenProp } from '@app/navigation/stacks/Main';
import { addYears } from 'utils/date';
import { getAgeCategoryText } from '../utils/age';

type FormFields =
  | 'email'
  | 'gender'
  | 'firstName'
  | 'middleName'
  | 'lastName'
  | 'phone'
  | 'address'
  | 'customAddress'
  | 'birthDate';

type FormikErrors = {
  gender?: string;
  firstName?: string;
  middleName?: string;
  lastName?: string;
  birthDate?: string;
  address_postalCode?: string;
  address_houseNumber?: string;
  address_city?: string;
  address_streetName?: string;
  address_country?: string;
  email?: string;
  phone?: string;
};

function isSameAddress(a: Address, b: Address) {
  return (
    a.city === b.city &&
    a.houseNumber === b.houseNumber &&
    a.houseNumberExtension === b.houseNumberExtension &&
    a.postalCode === b.postalCode &&
    a.streetName === b.streetName
  );
}

interface Props extends NavScreenProp<'TravelCompanyCompanion'> {
  index: number;
}

const TravelCompanyCompanion = ({ navigation, route }: Props) => {
  const { formatMessage } = useIntl();
  const { logScreenView, logEvent } = useMetrics();
  const theme = useTheme();
  const { updateTravelCompanion } = useMutateTravelCompanion();
  const { token } = useContext(UserContext);

  const bookingReference = route?.params?.bookingReference;
  const index = route?.params?.index;
  const totalCompanions = route?.params?.totalCompanions;
  const { data: reservation, refetch } = useGetReservation({
    brand: getBrand(theme),
    token,
    bookingReference,
  });
  const [processing, setProcessing] = useState(false);

  const companion = reservation?.travelCompanions?.find((companion) => companion?.sequenceNumber === index);
  const fullName = getFullName(companion?.firstName, companion?.middleName, companion?.lastName) ?? '';

  useFocusEffect(
    React.useCallback(() => {
      logScreenView('TravelCompanyCompanion');
      logEvent('TravelCompanionView', { companionId: companion?.sequenceNumber });
    }, [companion?.sequenceNumber, logEvent, logScreenView])
  );

  const formik = useFormik<Record<FormFields, any>>({
    initialValues: {
      address: companion?.address?.streetName ? companion?.address : reservation?.mainTenant?.address,
      customAddress:
        !!companion?.address &&
        reservation?.mainTenant?.address &&
        isSameAddress(companion.address, reservation.mainTenant.address)
          ? undefined
          : companion?.address,
      birthDate: companion?.birthdate,
      email: companion?.email,
      gender: companion?.gender ?? 0,
      firstName: companion?.firstName,
      middleName: companion?.middleName,
      lastName: companion?.lastName,
      phone: companion?.phone,
    },
    enableReinitialize: true,
    validateOnMount: true,
    validateOnChange: true,
    onSubmit: () => {},
    validate: (values) => {
      const errors: FormikErrors = {};

      if (!values.firstName) {
        errors.firstName = 'no first name';
      }
      if (!values.lastName) {
        errors.lastName = 'no last name';
      }
      if (!values.birthDate) {
        errors.birthDate = 'no birthdate';
      }
      /*if (!!values.email && !isValidEmail(values.email)) {
        errors.email = 'incorrect email address';
      }*/
      if (values.phone && !isValidPhone(values.phone)) {
        errors.phone = 'incorrect phone number';
      }
      if (!values.address?.postalCode) {
        errors.address_postalCode = 'no postal code';
      }
      if (!values.address?.streetName) {
        errors.address_streetName = 'no streetName';
      }
      if (!values.address?.houseNumber) {
        errors.address_houseNumber = 'no housenumber';
      }
      if (!values.address?.city) {
        errors.address_city = 'no city';
      }
      if (!values.address?.country) {
        errors.address_country = 'no country';
      }
      return errors;
    },
  });

  const addresses = useMemo(() => {
    const otherAddresses = [
      {
        address: {
          city: reservation?.mainTenant?.address?.city,
          country: reservation?.mainTenant?.address?.country,
          houseNumber: reservation?.mainTenant?.address?.houseNumber,
          houseNumberExtension: reservation?.mainTenant?.address?.houseNumberExtension,
          postalCode: reservation?.mainTenant?.address?.postalCode,
          streetName: reservation?.mainTenant?.address?.streetName,
        },
        fullName:
          getFullName(
            reservation?.mainTenant?.firstName,
            reservation?.mainTenant?.middleName,
            reservation?.mainTenant?.lastName
          ) ?? '',
      },
      ...(reservation?.travelCompanions
        ?.filter((c, mapIndex) => mapIndex !== index && !!c?.address?.streetName) // Remove current companion from list
        .map((c) => ({
          address: {
            city: c?.address?.city,
            country: c?.address?.country,
            houseNumber: c?.address?.houseNumber,
            houseNumberExtension: c?.address?.houseNumberExtension,
            postalCode: c?.address?.postalCode,
            streetName: c?.address?.streetName,
          },
          fullName: getFullName(c?.firstName, c?.middleName, c?.lastName) || '',
        })) || []),
    ];

    const distinctAddresses = otherAddresses?.reduce<typeof otherAddresses>((result, addressRecord) => {
      if (!result.find((r) => isSameAddress(r.address, addressRecord.address))) {
        result.push(addressRecord);
      }
      return result;
    }, []);

    if (
      !!reservation?.mainTenant?.address &&
      !!formik.values.customAddress &&
      !!formik.values.customAddress.streetName &&
      !isSameAddress(formik.values.customAddress, reservation.mainTenant.address)
    ) {
      return [
        ...distinctAddresses,
        {
          address: formik.values.customAddress,
          custom: true,
        },
      ];
    }
    return distinctAddresses;
  }, [formik.values.customAddress, index, reservation?.mainTenant, reservation?.travelCompanions]);

  const [selectedAddress, setSelectedAddress] = useState(
    formik.values.address?.streetName
      ? addresses.findIndex((a) => isSameAddress(a.address, formik.values.address))
      : 0
  );

  useEffect(() => {
    const addressIndex = formik.values.address?.streetName
      ? addresses.findIndex((a) => isSameAddress(a.address, formik.values.address))
      : 0;

    if (addressIndex !== -1) {
      setSelectedAddress(addressIndex);
    }
  }, [addresses, formik.values.address, formik.values.customAddress]);

  return (
    <>
      <StickyHeader
        title={
          fullName
            ? fullName
            : formatMessage({ id: 'app.reservation.companions.data' }) + ' ' + route?.params?.title
        }
        showClose
        beforeClose={(proceedFN) => {
          if (formik.dirty) {
            Alert.alert(
              formatMessage({ id: 'app.conversation.companion.abortTitle' }),
              formatMessage({ id: 'app.conversation.companion.abortText' }),
              [
                {
                  text: formatMessage({ id: 'app.conversation.companion.abortNegative' }),
                  onPress: () => {},
                  style: 'cancel',
                },
                {
                  text: formatMessage({ id: 'app.conversation.companion.abortPositive' }),
                  onPress: () => proceedFN(),
                },
              ]
            );
          } else {
            return true;
          }
        }}
        navigation={navigation}
      />
      <ScreenScrollContainer
        contentContainerStyle={{ backgroundColor: theme.colors.white, paddingTop: theme.space.xl }}
        topPadding={false}
      >
        {/* <KeyboardAwareScrollView
      style={{ flex: 1, paddingBottom: insets.bottom }}
      enableOnAndroid
      keyboardOpeningTime={Number.MAX_SAFE_INTEGER}
    > */}
        <View style={{ flex: 1 }}>
          <GenderSelector
            selected={formik.values.gender}
            onSelect={(value) => {
              formik.setFieldValue('gender', value);
            }}
          />
          <View style={{ flex: 1, flexDirection: 'row' }}>
            <FormControl
              label={formatMessage({ id: 'app.forms.labels.firstName' })}
              value={formik.values.firstName}
              isValid={true}
              onChangeText={(text) => {
                formik.setFieldValue('firstName', text);
              }}
              style={{ marginRight: theme.space.s }}
              flex={5}
            />
            <FormControl
              label={formatMessage({ id: 'app.forms.labels.middleNameShort' })}
              value={formik.values.middleName}
              isValid={true}
              onChangeText={(text) => {
                formik.setFieldValue('middleName', text);
              }}
              flex={2}
              optional
            />
          </View>
          <FormControl
            label={formatMessage({ id: 'app.forms.labels.lastName' })}
            value={formik.values.lastName}
            isValid={true}
            onChangeText={(text) => {
              formik.setFieldValue('lastName', text);
            }}
          />
          <FormControl
            label={formatMessage({ id: 'app.forms.labels.birthDay' })}
            sublabel={`(${getAgeCategoryText(
              formatMessage,
              companion?.ageCategory,
              companion?.minAge,
              companion?.maxAge
            )})`}
            value={formik.values.birthDate}
            isValid={!formik.errors.birthDate}
            onChangeText={(text) => {
              formik.setFieldValue('birthDate', text);
            }}
            date
            minimumDate={companion?.maxAge ? addYears(new Date(), -companion.maxAge) : new Date(1900, 0, 1)}
            maximumDate={companion?.minAge ? addYears(new Date(), -companion?.minAge) : undefined}
          />
          {/*<FormControl
            label={formatMessage({ id: 'app.forms.labels.email' })}
            value={formik.values.email}
            isValid={!formik.errors.email}
            errorMessage={formatMessage({ id: 'app.forms.error.emailaddress' })}
            placeholder={formatMessage({ id: 'app.reservation.companions.emailaddress.placeholder' })}
            onChangeText={(text) => {
              formik.setFieldValue('email', text);
            }}
          />*/}
          <FormControl
            label={formatMessage({ id: 'app.forms.labels.phone' })}
            value={formik.values.phone}
            isValid={!formik.errors.phone}
            errorMessage={formatMessage({ id: 'app.forms.error.phone' })}
            placeholder="+31612345678"
            onChangeText={(text) => {
              formik.setFieldValue('phone', text);
            }}
            optional
          />
          <AddressSelector
            addresses={addresses}
            selected={selectedAddress}
            onSelect={(value) => {
              formik.setFieldValue('address', addresses[value]?.address);
            }}
            setCustomAddress={(address) => {
              formik.setValues({
                ...formik.values,
                address,
                customAddress: address,
              });
            }}
          />
          <Button
            onPress={async () => {
              if (processing) {
                return;
              }
              setProcessing(true);
              logEvent(
                'TravelCompanyBegin',
                {
                  bookingReference: reservation?.bookingReference,
                  totalCompanions,
                },
                true
              );

              try {
                const {
                  firstName,
                  middleName,
                  lastName,
                  birthDate,
                  email,
                  gender,
                  phone,
                  address,
                  customAddress,
                } = formik.values;

                const usedAddress = customAddress?.streetName ? customAddress : address;

                const { city, country, houseNumber, houseNumberExtension, postalCode, streetName }: Address =
                  usedAddress;

                // Check if this is the first person whose details are being provided.
                // If this is the case, we mark this by sending a 'travel company start' event.
                // This code is also present in TravelCompanyMainTenant.tsx!
                const companionsCompleted =
                  (reservation?.travelCompanions?.filter((c) => c?.isComplete)?.length || 0) +
                  (reservation?.mainTenant?.isComplete ? 1 : 0);
                if (companionsCompleted === 0) {
                  logEvent('TravelCompanyBegin');
                }

                await updateTravelCompanion({
                  bookingReference: reservation?.bookingReference,
                  uid: token,
                  guest: {
                    firstName,
                    middleName,
                    lastName,
                    birthDate: Number(birthDate),
                    emailAddress: email,
                    gender,
                    phone,
                    sequenceNumber: companion?.sequenceNumber,
                    address: {
                      city,
                      countryCode: country?.code,
                      houseNumber,
                      houseNumberExtension,
                      postalCode,
                      street: streetName,
                    },
                  },
                });
                logEvent('TravelCompanionSave', { companionId: companion?.sequenceNumber });

                // Check if this is the LAST person whose details are being provided.
                // If this is the case, we mark this by sending a 'travel company complete' event.
                // This code is also present in TravelCompanyMainTenant.tsx!
                // we don't do travelCompanions-1, as maintenant is not included in travelCompanions
                if (companionsCompleted === (reservation?.travelCompanions?.length || 0)) {
                  // This was the last one.
                  logEvent('TravelCompanyComplete');
                }

                await refetch();
                navigation.goBack();
              } catch (e) {
                logEvent('TravelCompanionSaveError', { companionId: companion?.sequenceNumber });
                Alert.alert(formatMessage({ id: 'app.forms.tablebooking.error.generic' }));
              }
              setProcessing(false);
            }}
            style={{ backgroundColor: theme.colors.secondary, marginTop: theme.space.s }}
            disabled={processing || !formik.isValid}
          >
            {processing ? (
              <Button.ActivityIndicator style={{ height: 22 }} />
            ) : (
              <Button.Text color="white">
                {formatMessage({
                  id: 'app.general.save',
                })}
              </Button.Text>
            )}
          </Button>
          <TouchableOpacity
            activeOpacity={0.8}
            style={{ marginVertical: theme.space.l, alignItems: 'center' }}
            onPress={() => {
              logEvent('TravelCompanionCancel', { companionId: companion?.sequenceNumber });
              navigation.goBack();
            }}
          >
            <Text color="gray" bold>
              {formatMessage({
                id: 'app.general.cancel',
              })}
            </Text>
          </TouchableOpacity>
        </View>
        {/* </KeyboardAwareScrollView> */}
      </ScreenScrollContainer>
    </>
  );
};
export default TravelCompanyCompanion;
