import Text from '@app/components/Text';
import useTheme from '@app/hooks/useTheme';
import { AllColors } from '@app/theme/partials/general';
import React, { useMemo } from 'react';
import { useIntl } from 'react-intl';
import { View, ViewStyle } from 'react-native';
import LinearGradient from 'react-native-linear-gradient';
import { capitalizedFirstLetter } from 'utils/string';
import { NotificationMessageLabel } from '../types/NotificationMessage';

interface Props {
  label: NotificationMessageLabel;
  style: ViewStyle;
}

const NotificationLabel = ({ label, style }: Props) => {
  const theme = useTheme();
  const { formatMessage } = useIntl();

  const backgroundColor = useMemo(() => {
    switch (label) {
      case 'reservation':
        return [theme.colors.gradientPrimary1, theme.colors.gradientPrimary2, theme.colors.gradientPrimary3];
    }
  }, [label, theme.colors.gradientPrimary1, theme.colors.gradientPrimary2, theme.colors.gradientPrimary3]);
  const textColor: AllColors = useMemo(() => {
    switch (label) {
      case 'reservation':
        return 'primary';
      case 'calamity':
        return 'attention';
      default:
        return 'dark';
    }
  }, [label]);
  const translatedLabel = useMemo(() => {
    return formatMessage({ id: 'app.notificationcenter.labels.' + label });
  }, [formatMessage, label]);

  return (
    <View
      style={[
        {
          height: 20,
          alignItems: 'center',
          justifyContent: 'center',
          marginLeft: -theme.space.s,
        },
        style,
      ]}
    >
      {!!backgroundColor && (
        <LinearGradient
          colors={backgroundColor}
          style={[
            {
              backgroundColor: 'transparent', // required for gradient
              borderRadius: 8,
              opacity: 0.15,
              position: 'absolute',
              width: '100%',
              height: '100%',
            },
          ]}
          angle={176}
          useAngle
        />
      )}
      <Text style={{ paddingHorizontal: theme.space.s }} size="s" color={textColor} bold>
        {capitalizedFirstLetter(translatedLabel)}
      </Text>
    </View>
  );
};

export default NotificationLabel;
