import React, { forwardRef, Ref, useImperativeHandle, useState } from 'react';
import { View, ViewProps } from 'react-native';
import ListItems, { ListItem } from './ListItems';
import { AllColors } from '@app/theme/partials/general';
import RadioButton from '../Form/RadioButton';
import Text from '../Text';

export interface RadioListRefType {
  setSelected: (selectedIndex: number) => void;
}

export type RadioListItem = {
  label: string;
  labelRight?: string;
  value: string | number;
  icon?: string;
  iconColor?: AllColors;
};

interface Props extends React.PropsWithChildren<ViewProps> {
  title?: string;
  centerTitle?: boolean;
  items: RadioListItem[];
  initialPosition?: number; //counts from 1
  onSelect?: (value: string | number) => void;
}

const RadioList = forwardRef(
  ({ items, onSelect, title, centerTitle, initialPosition }: Props, ref: Ref<RadioListRefType>) => {
    const [selected, setSelected] = useState(initialPosition ? initialPosition - 1 : -1);

    useImperativeHandle(ref, () => ({
      setSelected(selectedIndex: number) {
        setSelected(selectedIndex);
      },
    }));

    const listItems: ListItem[] = items.map((i, index) => ({
      id: i.label,
      title: i.label,
      rightTitle: i.labelRight,
      icon: i.icon,
      iconColor: i.iconColor,
      right: () => <RadioButton selected={index === selected} />,
      onPress: () => {
        setSelected(index);
        onSelect?.(i.value);
      },
    }));

    return (
      <View>
        {!!title && (
          <Text type="title" bold style={{ textAlign: centerTitle ? 'center' : undefined }}>
            {title}
          </Text>
        )}
        <ListItems items={listItems} />
      </View>
    );
  }
);

export default RadioList;
