import useCulture from '@app/hooks/useCulture';
import useSelectedPark from '@app/hooks/useSelectedPark';
import { AxiosError } from 'axios';
import { gql, GraphQLClient } from 'graphql-request';
import Config from 'react-native-config';
import { UseQueryOptions, useQuery } from 'react-query';
import { FacilityMenu } from 'apis/_generated-park-api/models';
import { useIntl } from 'react-intl';

const key = `getmenus`;

type MenuDataProps = {
  options?: Omit<UseQueryOptions<FacilityMenu[], AxiosError>, typeof key>;
  restaurantId?: string;
  parkCodeOverride?: string;
};

export function useGetMenus(props: MenuDataProps) {
  const { parkCode: selectedParkCode } = useSelectedPark();
  const { cultureCode } = useCulture();
  const { formatMessage } = useIntl();

  const { restaurantId, options } = props;
  const parkCode = props?.parkCodeOverride ?? selectedParkCode;

  const extendedOptions: typeof options = {
    ...options,
    enabled: !!props.restaurantId,
  };

  return useQuery(
    [key, { parkCode, cultureCode, restaurantId }],
    async () => {
      const graphQLClient = new GraphQLClient(Config.PARK_API_URL);
      const query = gql`
        query facilityMenus {
          facilityMenus(parkCode: "${parkCode}", restaurantId: "${restaurantId}", cultureCode: "${cultureCode}") {
            facilityId
            type
            mainCategories{
              title
              subCategories{
                title
                courses{
                  title
                  description
                  vega
                  prices{
                    type
                    price
                  }
                }
                footers{
                  type
                  title
                  description
                }
                position
              }
              footers{
                type
                title
                description
              }
              position
            }
            price
            name
          }
        }
      `;
      const result = await graphQLClient.rawRequest<{ facilityMenus: FacilityMenu[] }>(query);

      const menus = result?.data?.facilityMenus;
      if (!menus) {
        return [];
      }

      menus.forEach((m) => {
        m?.mainCategories?.forEach((mc) => {
          if (!mc) return;
          mc.footers = mc?.footers?.map((f) => ({
            ...f,
            title: formatMessage({ id: 'app.menu.vega' }),
          }));
        });
      });

      return menus;
    },
    extendedOptions
  );
}
