import React, { useCallback, useContext, useEffect, useRef, useState } from 'react';
import AsyncStorage from '@react-native-async-storage/async-storage';
import { CardStackType } from '../types/CardStackType';
import { UserContext } from '@app/context/usercontext/UserContextProvider';
import useChecklist from '@app/hooks/useChecklist';
import useMetrics from '@app/metrics/useMetrics';
import { SmartlockAvailability } from 'apis/_generated-reservation-api/models';
import { useIntl } from 'react-intl';
import { Alert, Platform, PermissionsAndroid, Linking, View } from 'react-native';
import { isLocationEnabled } from 'react-native-device-info';
import RNAndroidLocationEnabler from 'react-native-android-location-enabler';
import BluetoothStateManager from 'react-native-bluetooth-state-manager';
import { getBrand } from 'utils/brand';
import { useGetReservation } from '@domains/reservations/reservation/queries/useGetReservation';
import useTheme from '@app/hooks/useTheme';
import { SaltoContext } from '@app/context/SaltoContextProvider';
import SaltoKS from '@domains/locks/native';
import { useBottomSheet } from '@app/navigation/bottom-sheet/BottomSheetProvider';
import LockSheetContent from './LockSheetContent';
import { NavigationContainerRef, useNavigation } from '@react-navigation/native';
import OpenDoorManager from './OpenDoorManager';
import { useGetPark } from '@domains/park/contact-and-info/queries/useGetPark';
import FormControl from '@app/components/Form/FormControl';
import SaltoLoginWebview from '@domains/profile/screens/SaltoLoginWebview';
import WebView from 'react-native-webview';
import { USERNAME_KEY } from '@app/config/StorageKeys';
import { SAPWebviewInject } from 'utils/webviewCommands';
import Text from '@app/components/Text';
import Loader from '@app/components/Loader';
import Animated, { FadeOut } from 'react-native-reanimated';

interface Props {
  bookingReference?: string;
  keyReady: boolean;
  keyExpired: boolean;
  accommodationReady: boolean;
}

const isAndroidBluetoothConnectAuthorized = async () => {
  return await PermissionsAndroid.check(PermissionsAndroid.PERMISSIONS.BLUETOOTH_CONNECT);
};
const isAndroidBluetoothScanAuthorized = async () => {
  return await PermissionsAndroid.check(PermissionsAndroid.PERMISSIONS.BLUETOOTH_SCAN);
};
const isAndroidBluetoothFullyAuthorized = async () => {
  const bluetoothConnect = await isAndroidBluetoothConnectAuthorized();
  const bluetoothScan = await isAndroidBluetoothScanAuthorized();
  if (!bluetoothConnect || !bluetoothScan) {
    return false;
  }
  return true;
};

export default function LockSheet({ bookingReference, keyReady, keyExpired, accommodationReady }: Props) {
  const [doorSuccessfullyOpenedBefore, setDoorSuccessfullyOpenedBefore] = useState(false);
  const { saltoUserToken } = React.useContext(SaltoContext);
  const theme = useTheme();
  const { token, demoMode } = useContext(UserContext);
  const { formatMessage } = useIntl();
  const { logEvent } = useMetrics();
  const { blockingTasks } = useChecklist({ bookingReference });
  const [cardStackOpened, setCardStackOpened] = useState<CardStackType>();
  const brand = getBrand(theme);
  const { closeBottomSheet } = useBottomSheet();
  const navigation = useNavigation<NavigationContainerRef<any>>();

  const loginWebview = React.useRef<WebView>(null);
  const [loginError, setLoginError] = useState<string>();
  const [passwordInput, setPasswordInput] = useState<string>();
  const [lockLoginProcessing, setLockLoginProcessing] = useState(true);
  const [username, setUsername] = useState<string>('');
  const conditionsVerifiedOnMount = useRef(false);

  const { data: reservation } = useGetReservation({
    brand,
    token,
    bookingReference,
  });
  const { data: park } = useGetPark({ parkCodeOverride: reservation?.parkCode });

  useEffect(() => {
    SaltoKS.suppressApplePay();
    return () => {
      // We postpone enabling apple pay a few seconds,
      // to prevent the wallet from appearing while the phone is still close to the lock.
      // Whe do not clear this timeout, because react state is not touched.
      setTimeout(() => {
        SaltoKS.enableApplePay();
      }, 2000);

      SaltoKS.stopListeningForDoorEvents();
    };
  }, []);

  const openStack = (stackName: CardStackType) => {
    setCardStackOpened(stackName);
  };
  const addReservationHasOpenedDoor = async () => {
    if (!reservation?.bookingReference) {
      return;
    }
    AsyncStorage.setItem(reservation?.bookingReference + '_opened', 'true');
    setDoorSuccessfullyOpenedBefore(true);
  };

  const requestAndroidBluetoothConnectPermission = async () => {
    const granted = await PermissionsAndroid.request(PermissionsAndroid.PERMISSIONS.BLUETOOTH_CONNECT, {
      title: formatMessage({ id: 'app.key.bluetoothRightsDescription' }),
      message: '',
      buttonNegative: formatMessage({ id: 'app.general.cancel' }),
      buttonPositive: formatMessage({ id: 'app.key.giveRights' }),
    });
    if (granted === PermissionsAndroid.RESULTS.GRANTED) {
      return true;
    } else {
      return false;
    }
  };
  const requestAndroidBluetoothScanPermission = async () => {
    const granted = await PermissionsAndroid.request(PermissionsAndroid.PERMISSIONS.BLUETOOTH_SCAN, {
      title: formatMessage({ id: 'app.key.bluetoothRightsDescription' }),
      message: '',
      buttonNegative: formatMessage({ id: 'app.general.cancel' }),
      buttonPositive: formatMessage({ id: 'app.key.giveRights' }),
    });
    if (granted === PermissionsAndroid.RESULTS.GRANTED) {
      return true;
    } else {
      return false;
    }
  };

  const verifyKeyConditions = useCallback(async () => {
    !!reservation && logEvent('LockKeyBegin', { lock_type: reservation?.lockType });

    if (!doorSuccessfullyOpenedBefore && blockingTasks > 0) {
      openStack('lockOpenTasks');
      return;
    }

    let bluetoothState = await BluetoothStateManager.getState();

    // We manually check Android bluetooth permissions, because of the new Android 12 permissions
    if (Platform.OS === 'android' && Platform.Version >= 31) {
      if (!(await isAndroidBluetoothFullyAuthorized())) bluetoothState = 'Unauthorized';
    }

    if (bluetoothState === 'Unknown') {
      logEvent('LockKeyReqBluetoothStateDetermined');

      Alert.alert(
        formatMessage({ id: 'app.forms.tablebooking.error.generic' }),
        formatMessage({ id: 'app.key.bluetoothUnsupported' })
      );
      closeBottomSheet();
      return;
    }
    if (bluetoothState === 'Unsupported') {
      logEvent('LockKeyReqBluetoothSupport');

      Alert.alert(
        formatMessage({ id: 'app.forms.tablebooking.error.generic' }),
        formatMessage({ id: 'app.key.bluetoothUnsupported' })
      );
      closeBottomSheet();
      return;
    }
    if (bluetoothState === 'Unauthorized') {
      openStack('lockBluetoothPermissions');
      return;
    }
    if (bluetoothState === 'PoweredOff') {
      openStack('lockBluetoothDisabled');
      return;
    }
    if (bluetoothState === 'Resetting') {
      closeBottomSheet();
      return;
    }
    if (Platform.OS === 'android') {
      const coarsePermission = await PermissionsAndroid.check(
        PermissionsAndroid.PERMISSIONS.ACCESS_COARSE_LOCATION
      );
      const finePermission = await PermissionsAndroid.check(
        PermissionsAndroid.PERMISSIONS.ACCESS_FINE_LOCATION
      );
      if (!coarsePermission || !finePermission) {
        openStack('lockLocationPermissions');
        return;
      }

      const locationEnabled = await isLocationEnabled().then((enabled) => enabled);
      if (!locationEnabled) {
        RNAndroidLocationEnabler.promptForEnableLocationIfNeeded({
          interval: 10000,
          fastInterval: 5000,
        })
          .then(() => {
            verifyKeyConditions();
          })
          .catch((err) => {
            switch (err.code) {
              case 'ERR00': // If the user canceled
                return;
              case 'ERR01': // If the Settings change are unavailable
              case 'ERR02': // If the popup has failed to open
              case 'ERR03': // Internal error
                Alert.alert('', formatMessage({ id: 'app.key.locationDisabled' }));
                closeBottomSheet();
                return;
              default:
                return;
            }
          });
        return;
      }
    }

    if (!demoMode && !saltoUserToken) {
      openStack('lockLogin');
      return;
    }

    if (
      !keyReady ||
      (!!reservation && reservation?.smartlockAvailability !== SmartlockAvailability.Available)
    ) {
      if (keyExpired) {
        logEvent('LockKeyNotAvailable');
        Alert.alert('', formatMessage({ id: 'app.key.keyExpired' }));
        closeBottomSheet();
        return;
      } else if (!accommodationReady) {
        logEvent('LockKeyNotAvailable');
        Alert.alert('', formatMessage({ id: 'app.key.keyReadySoon' }));
        closeBottomSheet();
        return;
      } else if (!doorSuccessfullyOpenedBefore) {
        closeBottomSheet();
        return;
      }
    }

    if (reservation?.bookingReference) {
      const accommodationShownForReservation = await AsyncStorage.getItem(
        reservation?.bookingReference + '_checkAccommodationShown'
      );
      if (!accommodationShownForReservation) {
        openStack('lockCheckAccommodation');
        return;
      }
    }

    openStack('lockOpenDoor');
    if (demoMode) {
      Alert.alert(
        'Alert',
        'The app is currently in demo mode. This functionality will not perform any action, and is for demonstration purposes only.'
      );
    }
  }, [
    accommodationReady,
    closeBottomSheet,
    demoMode,
    doorSuccessfullyOpenedBefore,
    formatMessage,
    keyExpired,
    keyReady,
    logEvent,
    blockingTasks,
    reservation,
    saltoUserToken,
  ]);

  useEffect(() => {
    if (!conditionsVerifiedOnMount.current) {
      conditionsVerifiedOnMount.current = true;
      verifyKeyConditions();
    }
  }, [verifyKeyConditions]);

  const injectLogin = (usrName: string, password: string) => {
    loginWebview?.current?.injectJavaScript(SAPWebviewInject(usrName, password));
  };
  const loginFailed = React.useCallback(
    (
      reason: 'missing_code' | 'missing_token' | 'invalid_credentials' | 'load_error' | 'salto_user_not_found'
    ) => {
      logEvent('LoginError');

      switch (reason) {
        case 'invalid_credentials': {
          setLoginError('app.login.invalidPassword');
          logEvent('LoginInvalidCredentials');
          break;
        }
        case 'missing_code':
        case 'missing_token': {
          logEvent('LoginError', { error_type: reason });
          setLoginError('app.login.serverError');
          break;
        }
        case 'load_error': {
          setLoginError('app.login.serverError');
          break;
        }
        case 'salto_user_not_found': {
          setLoginError('app.login.saltoUserNotFound');
          break;
        }
        default: {
          setLoginError('app.login.serverError');
        }
      }
    },
    [logEvent]
  );

  return (
    <>
      {!cardStackOpened && (
        <Animated.View
          style={{
            alignItems: 'center',
            marginVertical: theme.space.l,
          }}
          exiting={FadeOut.duration(400)}
        >
          <Loader />
        </Animated.View>
      )}
      {cardStackOpened === 'lockOpenTasks' && (
        <LockSheetContent
          text={formatMessage({ id: 'app.key.openStepsCompleteFirst' })}
          buttonText={formatMessage({ id: 'app.key.openStepsTo' })}
          icon="checklist"
          status="attention"
          eventName="LockKeyReqOpenTasks"
          onPress={async () => {
            closeBottomSheet();
            navigation.navigate('Reservation');
            setCardStackOpened('');
          }}
        />
      )}
      {cardStackOpened === 'lockBluetoothPermissions' && (
        <LockSheetContent
          text={formatMessage({ id: 'app.key.bluetoothRightsDescription' })}
          buttonText={formatMessage({ id: 'app.key.giveRights' })}
          icon="bluetooth"
          status="attention"
          eventName="LockKeyReqBluetoothPermission"
          onPress={async () => {
            if (Platform.OS === 'android' && Platform.Version >= 31) {
              const scanAuthorized = await isAndroidBluetoothScanAuthorized();
              const connectAuthorized = await isAndroidBluetoothConnectAuthorized();

              if (!scanAuthorized) {
                if (await requestAndroidBluetoothScanPermission()) {
                  verifyKeyConditions();
                }
              }
              if (!connectAuthorized) {
                if (await requestAndroidBluetoothConnectPermission()) {
                  verifyKeyConditions();
                }
              }
            } else {
              BluetoothStateManager.getState().then(async (bluetoothState: string) => {
                switch (bluetoothState) {
                  case 'Unknown':
                  case 'Resetting':
                  case 'Unsupported':
                  case 'Unauthorized':
                  case 'PoweredOff': {
                    if (Platform.OS === 'android') {
                      BluetoothStateManager.requestToEnable().then((value) => {
                        if (!value) {
                          Alert.alert('Bluetooth', 'Zonder bluetooth kunnen we helaas het slot niet openen.');
                        } else {
                          verifyKeyConditions();
                        }
                      });
                    } else {
                      // iOS
                      BluetoothStateManager.openSettings();
                    }
                    break;
                  }
                  case 'PoweredOn': {
                    verifyKeyConditions();
                    break;
                  }
                  default:
                    break;
                }
              });
            }
          }}
          onFocus={() => {
            BluetoothStateManager.getState().then((bluetoothState: string) => {
              switch (bluetoothState) {
                case 'PoweredOn': {
                  verifyKeyConditions();
                  break;
                }
              }
            });
          }}
        />
      )}
      {cardStackOpened === 'lockBluetoothDisabled' && (
        <LockSheetContent
          text={formatMessage({ id: 'app.key.turnOnBluetooth' })}
          buttonText={formatMessage({ id: 'app.key.turnOnBluetoothImmediately' })}
          icon="bluetooth"
          status="attention"
          eventName="LockKeyReqBluetoothEnabled"
          onPress={async () => {
            BluetoothStateManager.enable();
          }}
          onFocus={() => {
            BluetoothStateManager.getState().then((bluetoothState: string) => {
              switch (bluetoothState) {
                case 'PoweredOn': {
                  verifyKeyConditions();
                  break;
                }
              }
            });
          }}
          showSubmitButton={Platform.OS === 'android'}
        />
      )}
      {cardStackOpened === 'lockLocationPermissions' && (
        <LockSheetContent
          text={formatMessage({ id: 'app.key.locationRightsDescription' })}
          buttonText={formatMessage({ id: 'app.key.giveRights' })}
          icon="distance"
          status="attention"
          eventName="LockKeyReqLocationPermission"
          onPress={async () => {
            try {
              PermissionsAndroid.requestMultiple(
                [
                  PermissionsAndroid.PERMISSIONS.ACCESS_COARSE_LOCATION,
                  PermissionsAndroid.PERMISSIONS.ACCESS_FINE_LOCATION,
                ],
                {
                  title: formatMessage({ id: 'app.key.location' }),
                  message: formatMessage({ id: 'app.key.locationRightsDescription' }),
                  buttonNegative: formatMessage({ id: 'app.general.cancel' }),
                  buttonPositive: formatMessage({ id: 'app.key.giveRights' }),
                }
              ).then((result) => {
                if (
                  result['android.permission.ACCESS_COARSE_LOCATION'] &&
                  result['android.permission.ACCESS_FINE_LOCATION']
                ) {
                  verifyKeyConditions();
                }
              });
            } catch (err) {
              console.warn(err);
            }
          }}
        />
      )}
      {cardStackOpened === 'lockCheckAccommodation' && (
        <LockSheetContent
          text={formatMessage({ id: 'app.key.checkAccommodationText' })}
          buttonText={formatMessage({ id: 'app.general.understood' })}
          icon="accommodation"
          status="attention"
          eventName="LockKeyReqCheckAccommodation"
          onPress={async () => {
            AsyncStorage.setItem(bookingReference + '_checkAccommodationShown', 'true');
            verifyKeyConditions();
          }}
          showCancelButton={false}
        />
      )}
      {cardStackOpened === 'lockOpenDoor' && (
        <>
          <LockSheetContent
            text={formatMessage({ id: 'app.key.keyActivated' })}
            icon="key"
            status="success"
            onCancel={async () => {
              SaltoKS.stopListeningForDoorEvents();
              closeBottomSheet();
            }}
            showSubmitButton={false}
            helpComponent={() => (
              <View style={{ alignItems: 'center' }}>
                <Text
                  type="body"
                  color="tertiary"
                  onPress={() => Linking.openURL(formatMessage({ id: 'app.key.instructionVideoURL' }))}
                  bold
                  style={{ marginBottom: theme.space.m }}
                >
                  {formatMessage({ id: 'app.key.instructionVideoLabel' })}
                </Text>
                <Text type="body" color="tertiary" onPress={() => Linking.openURL('tel:' + park?.phone)} bold>
                  {formatMessage({ id: 'app.contactandinfo.phone' })}
                </Text>
              </View>
            )}
          />
          {!demoMode && (
            <OpenDoorManager
              onSuccess={() => {
                addReservationHasOpenedDoor();
                logEvent('LockKeyOpenEnd', {
                  lock_type: reservation?.lockType,
                  unit_code: reservation?.unitCode,
                });
                logEvent('LockKeyComplete', {
                  lock_type: reservation?.lockType,
                  unit_code: reservation?.unitCode,
                });
                setCardStackOpened('lockOpenDoorSucceeded');
              }}
              onFailure={() => {
                setCardStackOpened('lockOpenDoorFailed');
              }}
            />
          )}
        </>
      )}
      {cardStackOpened === 'lockOpenDoorFailed' && (
        <LockSheetContent
          text={formatMessage({ id: 'app.key.actionFailed' })}
          buttonText={formatMessage({ id: 'app.key.again' })}
          icon="lock"
          status="failed"
          onPress={async () => {
            verifyKeyConditions();
          }}
        />
      )}
      {cardStackOpened === 'lockOpenDoorSucceeded' && (
        <LockSheetContent
          text={formatMessage({ id: 'app.general.success' })}
          buttonText={formatMessage({ id: 'app.general.close' })}
          icon="unlocked"
          status="success"
          onPress={async () => {
            closeBottomSheet();
          }}
          showCancelButton={false}
        />
      )}
      {cardStackOpened === 'lockLogin' && (
        <LockSheetContent
          text={formatMessage({ id: 'app.key.login.explanation' })}
          buttonText={formatMessage({
            id: 'app.login.login',
          })}
          eventName={'LockKeyReqAuthorise'}
          icon="lock"
          status="attention"
          onMount={() => {
            const getUsername = async () => {
              const storedUsername = await AsyncStorage.getItem(USERNAME_KEY);
              if (storedUsername) {
                setUsername(storedUsername);
              }
            };
            getUsername();
          }}
          onPress={async () => {
            if (lockLoginProcessing) {
              return;
            }
            setLockLoginProcessing(true);
            setLoginError(undefined);
            logEvent('Login');
            injectLogin(username, passwordInput ?? '');
          }}
          onHelp={() => {
            Linking.openURL('tel:' + park?.phone);
          }}
          disabled={!passwordInput}
          processing={lockLoginProcessing}
        >
          <FormControl
            label={formatMessage({
              id: 'app.login.passwordPlaceholder',
            })}
            value={passwordInput}
            isValid={!!passwordInput}
            onChangeText={(text) => {
              setPasswordInput(text);
            }}
            autoCorrect={false}
            secureTextEntry
            textContentType="password"
            autoCapitalize="none"
            returnKeyType="send"
            style={{
              marginTop: theme.space.xl,
              width: '100%',
            }}
            //onSubmitEditing={() => }
          />
          {loginError && (
            <Text
              bold
              size="s"
              color="attention"
              style={{
                textAlign: 'center',
              }}
            >
              {loginError &&
                formatMessage({
                  id: loginError,
                })}
            </Text>
          )}
          <SaltoLoginWebview
            reference={loginWebview}
            onSuccess={async () => {
              try {
                verifyKeyConditions();
              } catch (ex) {
                loginFailed('load_error');
                setLockLoginProcessing(false);
              }
            }}
            onReady={() => {
              setLockLoginProcessing(false);
            }}
            onError={(reason) => {
              loginFailed(reason);
            }}
            onLoadStart={() => {
              setLockLoginProcessing(true);
            }}
          />
        </LockSheetContent>
      )}
    </>
  );
}
