import React from 'react';
import { Linking, View, ViewProps } from 'react-native';
import Button from '../Button';
import Text from '../Text';
import { useIntl } from 'react-intl';
import externalNavigateToLocation from 'utils/externalNavigateToLocation';
import useMetrics from '@app/metrics/useMetrics';
import useTheme from '@app/hooks/useTheme';

interface Props extends React.PropsWithChildren<ViewProps> {
  locationName?: string;
  street: string;
  streetNumber?: string;
  zipcode: string;
  city: string;
  website?: string;
  latitude?: number;
  longitude?: number;
  title?: string;
}

export default function MoreInfo({
  locationName,
  street,
  streetNumber,
  zipcode,
  city,
  website,
  latitude,
  longitude,
  title,
}: Props) {
  const { formatMessage } = useIntl();
  const theme = useTheme();
  const { logEvent } = useMetrics();

  let streetWithNumber = street;
  if (streetNumber) {
    streetWithNumber += ` ${streetNumber}`;
  }
  let zipCodeWithCity = city;
  if (zipcode) {
    zipCodeWithCity = `${zipcode} ` + city;
  }

  return (
    <View style={{ marginTop: theme.space.xl }}>
      {!!title && (
        <Text type="title" size="l" bold>
          {title}
        </Text>
      )}
      {!!street && (
        <Text color="dark" style={{ lineHeight: 26 }}>
          {streetWithNumber}
        </Text>
      )}
      {!!city && (
        <Text color="dark" style={{ lineHeight: 26, marginBottom: theme.space.l }}>
          {zipCodeWithCity}
        </Text>
      )}
      <View style={{ flexDirection: 'row' }}>
        <View style={{ flex: 1 }}>
          <Button
            onPress={async () => {
              logEvent('PlanRoute');
              logEvent('ViewRouteToPOI', { name: locationName });

              await externalNavigateToLocation({
                locationName,
                city,
                street,
                streetNumber,
                latitude,
                longitude,
                zipcode,
              });
            }}
            style={{
              flex: 1,
              backgroundColor: 'transparent',
              borderWidth: 1,
              borderColor: theme.colors.light,
            }}
          >
            <Button.Icon name="navigate" color="tertiary" />
            <Button.Text color="tertiary">
              {formatMessage({ id: 'app.components.detail.moreinfo.address' })}
            </Button.Text>
          </Button>
        </View>
        {!!website && (
          <View style={{ flex: 1, marginLeft: theme.space.m }}>
            <Button
              onPress={() => {
                logEvent('ExternalLink', {
                  link: website,
                  location: 'button',
                });
                if (!website?.startsWith('http')) {
                  website = 'https://' + website;
                }
                Linking.openURL(website);
              }}
              style={{
                flex: 1,
                backgroundColor: 'transparent',
                borderWidth: 1,
                borderColor: theme.colors.light,
              }}
            >
              <Button.Icon name="globe" color="tertiary" />
              <Button.Text color="tertiary">
                {formatMessage({ id: 'app.components.detail.moreinfo.website' })}
              </Button.Text>
            </Button>
          </View>
        )}
      </View>
    </View>
  );
}
