import useTheme from '@app/hooks/useTheme';
import React, { Fragment, ReactElement, ReactNode } from 'react';
import { View, ViewStyle } from 'react-native';
import Text from '../Text';
import { BASELINE_INSET } from '@app/theme/partials/sizes';
import { Source } from 'react-native-fast-image';
import { BottomSheetConfigType } from '@app/navigation/bottom-sheet/BottomSheetProvider';
import { AllRoutes } from '@app/navigation/stacks/Main';
import Shadow from '../Shadow';
import { AllColors } from '@app/theme/partials/general';
import { ListItemSingle } from './ListItemSingle';
import ItemDivider from './ItemDivider';
import { EventName } from '@app/metrics/useMetrics';

export interface ListItem {
  id: string | number;
  title?: string | ReactNode; // TODO: should NOT be optional, but necessary to make the usePracticalItems merge possible. Refactor!
  rightTitle?: string;
  titleColor?: AllColors;
  subtitle?: string;
  subtitleIcon?: string;
  left?: () => ReactNode;
  right?: () => ReactNode;
  rightIcon?: string;
  bottom?: ReactNode;
  rightSubtitle?: string;
  rightSubtitleIcon?: string;
  image?: string;
  isSizableImage?: boolean;
  localImage?: Source;
  icon?: string;
  iconColor?: AllColors;
  blurImage?: boolean;
  color?: string;
  navigateTo?: keyof AllRoutes;
  navigateParams?: { [key: string]: any };
  linkTo?: string; // web link
  event?: EventName;
  onPress?: () => void;
  bottomSheet?: BottomSheetConfigType;
  status?: 'complete' | 'incomplete';
  blocking?: boolean;
  bottomContent?: ReactElement;
  external?: boolean;
  disabled?: boolean;
}

interface Props {
  items: ListItem[];
  style?: ViewStyle;
  title?: string;
  usePlaceholderImage?: boolean;
  useSkiaImage?: boolean;
  useBaseLineInset?: boolean;
  useTopRadius?: boolean;
  useBottomRadius?: boolean;
  shadowOffset?: number;
  subtitleNumberOfLines?: number;
}

export const ITEM_HEIGHT = 72;

const ListItems = ({
  items,
  usePlaceholderImage = false,
  useSkiaImage = true,
  useBaseLineInset = true,
  useTopRadius = true,
  useBottomRadius = true,
  subtitleNumberOfLines,
  style,
  title,
  shadowOffset,
}: Props) => {
  const theme = useTheme();
  if ((items?.length || 0) === 0) {
    return null;
  }

  return (
    <View
      style={[
        {
          marginVertical: theme.space.l,
        },
        style,
      ]}
    >
      {!!title && (
        <Text type="title" size="l" bold style={{ marginBottom: theme.space.m }}>
          {title}
        </Text>
      )}
      <Shadow topOffset={shadowOffset ?? 0}>
        <View
          style={{
            borderBottomLeftRadius: useBottomRadius ? 12 : 0,
            borderBottomRightRadius: useBottomRadius ? 12 : 0,
            borderTopLeftRadius: useTopRadius ? 12 : 0,
            borderTopRightRadius: useTopRadius ? 12 : 0,
            overflow: 'hidden',
            marginHorizontal: useBaseLineInset ? -BASELINE_INSET : 0,
          }}
        >
          {items.map((item, index) => (
            <Fragment key={item.id}>
              <ListItemSingle
                item={item}
                usePlaceholderImage={usePlaceholderImage}
                useSkiaImage={useSkiaImage}
                useTopRadius={useTopRadius}
                useBottomRadius={useBottomRadius}
                isFirst={index === 0}
                isLast={index === items.length - 1}
                subtitleNumberOfLines={subtitleNumberOfLines}
              />
              {!!item?.bottomContent && (
                <>
                  <ItemDivider />
                  {item.bottomContent}
                </>
              )}
              {index < items.length - 1 && <ItemDivider />}
            </Fragment>
          ))}
        </View>
      </Shadow>
    </View>
  );
};
export default ListItems;
