import Icon from '@app/components/Icon';
import Text from '@app/components/Text';
import useTheme from '@app/hooks/useTheme';
import React from 'react';
import { useIntl } from 'react-intl';
import { View } from 'react-native';

const PendingTasks = ({ pendingTasks }: { pendingTasks: number }) => {
  const theme = useTheme();
  const { formatMessage } = useIntl();

  return (
    <View style={{ flexDirection: 'row', alignItems: 'center' }}>
      <Icon
        name="exclamation-mark-circular"
        size="s"
        style={{ marginRight: theme.space.s }}
        color="attention"
      />
      <Text color="attention" size="s">
        {pendingTasks > 1
          ? formatMessage({ id: 'app.launcher.openTasks' }).replace('{0}', pendingTasks)
          : formatMessage({ id: 'app.launcher.openTask' })}
      </Text>
    </View>
  );
};

export default PendingTasks;
