import useCulture from './useCulture';
import useCulturesOfSelectedPark from './useCulturesForPark';

export default function useSelectedLanguageAvailableForPark() {
  const culture = useCulture();
  const culturesOfSelectedPark = useCulturesOfSelectedPark();
  const selectedLanguageAvailableForPark = !!culturesOfSelectedPark?.find(
    (c) => c.cultureCode === culture?.cultureCode
  );

  return selectedLanguageAvailableForPark;
}
