import React from 'react';
import useTheme from '@hooks/useTheme';
import { ScrollView, View } from 'react-native';
import { useIntl } from 'react-intl';
import { isTodayUnix, ReadableDayByDate } from 'utils/date';
import Text from '@app/components/Text';
import { NavigationContainerRef, useNavigation } from '@react-navigation/native';
import ListItems, { ListItem } from '@app/components/ListItems2/ListItems';
import { Activity } from 'apis/_generated-park-api/models';
import useScreenSize from '@app/hooks/useScreenSize';
import useOpeningHours from '@app/hooks/useOpeningHours';
import { LandalPressable } from '@app/components/LandalPressable';
import Loader from '@app/components/Loader';
import DigitalFun from '../components/DigitalFun';
import { useSafeAreaInsets } from 'react-native-safe-area-context';

interface Props {
  activities?: Activity[];
  date: Date;
  nextDate: Date | null;
  scrollTo: (date: Date) => void;
  facilityId?: string;
  isLoading: boolean;
  parkCode?: string;
  promoSectionItems?: ListItem[];
  promoSectionTitle?: string;
}

const ActivitiesDate = ({
  isLoading,
  activities,
  date,
  nextDate,
  scrollTo,
  facilityId,
  parkCode,
  promoSectionItems,
  promoSectionTitle,
}: Props) => {
  const theme = useTheme();
  const { formatMessage } = useIntl();
  const navigation = useNavigation<NavigationContainerRef<any>>();
  const facilityBased = !!facilityId;
  const { width: screenWidth } = useScreenSize();
  const insets = useSafeAreaInsets();
  const formattedOpeningHours = useOpeningHours();

  if (!activities || activities.length === 0) {
    return (
      <View
        style={{
          flex: 1,
          marginTop: theme.space.xl,
          width: facilityBased ? screenWidth : undefined,
        }}
      >
        {isLoading ? (
          <Loader />
        ) : (
          <>
            {!!promoSectionItems && (promoSectionItems?.length || 0) > 0 && (
              <ListItems title={promoSectionTitle} items={promoSectionItems} usePlaceholderImage />
            )}
            <Text type="title" color="gray" style={{ textAlign: 'center' }}>
              {formatMessage({ id: 'app.activities.noactivitytoday' })}
            </Text>
            {!!nextDate && (
              <LandalPressable
                style={{ marginTop: theme.space.l }}
                onPress={() => {
                  !!nextDate && scrollTo(nextDate);
                }}
              >
                <Text type="body" color="tertiary" style={{ textAlign: 'center' }}>
                  {`${formatMessage({ id: 'app.activities.nextactivityis' })} ${ReadableDayByDate(
                    nextDate,
                    true
                  )}`}
                </Text>
              </LandalPressable>
            )}
          </>
        )}
      </View>
    );
  }

  return (
    <ScrollView
      style={{ width: screenWidth }}
      contentContainerStyle={{
        paddingHorizontal: theme.space.l,
        paddingBottom: insets.bottom,
      }}
    >
      {!!promoSectionItems && (promoSectionItems?.length || 0) > 0 && (
        <ListItems title={promoSectionTitle} items={promoSectionItems} usePlaceholderImage />
      )}

      <Text type="title" size="l" bold style={{ marginTop: theme.space.xl }}>
        {formatMessage({ id: 'app.park.funAndEntertainment.list.title' }).replace(
          '{0}',
          ReadableDayByDate(date).toLowerCase()
        )}
      </Text>

      <ListItems
        items={
          activities.map((a) => {
            let openingHoursSubtitle = formattedOpeningHours(
              a.time?.startTime,
              a.time?.endTime,
              a.time?.displayHint
            );
            return {
              id: a.id ?? '',
              title: a.name ?? '',
              subtitle: openingHoursSubtitle,
              rightSubtitle: a.signUpNeeded
                ? formatMessage({ id: 'app.activities.signupneeded.short' })
                : undefined,
              image: a.media,
              onPress: () => {
                navigation.navigate('ActivitiesDetail', { id: a.id, parkCode, date });
              },
            };
          }) ?? []
        }
        usePlaceholderImage
      />
      {!facilityId && isTodayUnix(date.getTime() / 1000) && <DigitalFun parkCode={parkCode} />}
    </ScrollView>
  );
};

export default ActivitiesDate;
