import React, { useCallback, useContext, useEffect, useState } from 'react';
import { UserContext } from '@app/context/usercontext/UserContextProvider';
import Banner from '@domains/start/components/Banner';
import { NavigationContainerRef, useNavigation } from '@react-navigation/native';
import { useIntl } from 'react-intl';
import { getBrand } from 'utils/brand';
import useTheme from '../hooks/useTheme';
import ProgressSVG from '@app/components/svg/ProgressSvg';

import { Reservation } from 'apis/_generated-reservation-api/models';
import useMetrics from '@app/metrics/useMetrics';
import { useGetReservations } from '@domains/reservations/reservation/queries/useGetReservations';
import useChecklist from '@app/hooks/useChecklist';

interface StatusItem {
  bookingReference: string;
  pendingTasks?: number;
  percentageComplete?: number;
}

export const ChecklistCombiBanner = ({
  combiReference,
  reservation,
}: {
  combiReference: string;
  reservation: Reservation;
}) => {
  const navigation = useNavigation<NavigationContainerRef<any>>();

  const { token } = useContext(UserContext);
  const { formatMessage } = useIntl();

  const theme = useTheme();
  const { logEvent } = useMetrics();
  const { data: reservations } = useGetReservations({
    brand: getBrand(theme),
    token,
  });

  const [items, setItems] = useState<StatusItem[]>([]);

  useEffect(() => {
    const items = reservations
      ?.filter((res) => res.combiReference === combiReference)
      .map((res) => ({
        bookingReference: res.bookingReference || '',
      }));
    if (items) setItems(items);
  }, [combiReference, reservations]);

  // Get status for every acco, write to state using callback function
  const statusCallback = useCallback((data: StatusItem) => {
    setItems((i) =>
      i.map((item) => (item.bookingReference === data.bookingReference ? { ...item, ...data } : item))
    );
  }, []);

  const percentageComplete = items.reduce(
    (acc, item) => (acc += (item.percentageComplete || 0) / items?.length),
    0
  );

  const pendingTasks = items.reduce((acc, item) => (acc += item.pendingTasks || 0), 0);

  if (!reservation) return null;

  return (
    <>
      {items.map((item) => (
        <BookingChecklistStatus
          key={item.bookingReference}
          bookingReference={item.bookingReference}
          statusCallback={statusCallback}
        />
      ))}
      <Banner
        label={
          pendingTasks
            ? `${
                pendingTasks > 1
                  ? formatMessage({ id: 'app.launcher.openTasks' })
                  : formatMessage({ id: 'app.launcher.openTask' })
              }`.replace('{0}', pendingTasks.toString())
            : formatMessage({ id: 'app.launcher.noOpenTask' })
        }
        text={
          pendingTasks
            ? formatMessage({ id: 'app.launcher.openTaskDescription' })
            : formatMessage({ id: 'app.launcher.noOpenTaskDescription' })
        }
        onPress={() => {
          logEvent('BannerChecklist');
          navigation?.navigate('ReservationsTab', { bookingReference: reservation?.bookingReference });
        }}
        iconSvg={<ProgressSVG progressPercentage={Math.round(percentageComplete)} />}
        sticky
      />
    </>
  );
};

const BookingChecklistStatus = ({
  bookingReference,
  statusCallback,
}: {
  bookingReference: string;
  statusCallback: Function;
}) => {
  const { pendingTasks, percentageComplete } = useChecklist({ bookingReference });

  useEffect(() => {
    statusCallback({
      bookingReference,
      pendingTasks,
      percentageComplete,
    });
  }, [bookingReference, pendingTasks, percentageComplete, statusCallback]);

  return null;
};
