import useTheme from '@app/hooks/useTheme';
import React from 'react';
import { View } from 'react-native';
import Text from '../Text';
import {
  formatDayWithoutTime,
  formatShortDayWithoutTime,
  isToday,
  isTomorrow,
  toUnixNumber,
} from 'utils/date';

import { useIntl } from 'react-intl';
import useOpeningHours from '@app/hooks/useOpeningHours';
import useCulture from '@app/hooks/useCulture';
import { Openinghour } from 'apis/_generated-park-api/models';
import { BASELINE_INSET } from '@app/theme/partials/sizes';
import { capitalizedFirstLetter } from 'utils/string';

interface Props {
  openingHours?: Openinghour[];
  title?: string;
  icon?: string;
  iconColor?: string;
  showWithoutOpeningHours?: boolean;
  hideTitle?: boolean;
}

const MULTIPLE_HOURS_IN_A_DAY_NEGATIVE_MARGIN = -8;

export default function OpeningHours({ openingHours, title, showWithoutOpeningHours, hideTitle }: Props) {
  const theme = useTheme();
  const { formatMessage } = useIntl();
  const formattedOpeningHours = useOpeningHours();
  const { languageCode } = useCulture();

  const LINE_HEIGHT = 28;

  const hasOpeningHours = !!openingHours && openingHours.length > 0;

  if (!showWithoutOpeningHours && !hasOpeningHours) {
    return null;
  }

  const showDate = (date: string) => {
    return !isToday(date) && !isTomorrow(toUnixNumber(date));
  };

  return (
    <View
      style={{
        marginVertical: theme.space.l,
      }}
    >
      {!hideTitle && (
        <Text type="title" size="l" style={{ marginBottom: theme.space.l }} bold>
          {title || formatMessage({ id: 'app.general.openingHours' })}
        </Text>
      )}

      <View
        style={{
          borderRadius: 8,
          backgroundColor: theme.colors.white,
          padding: theme.space.l,
          paddingBottom: theme.space.s,
          marginHorizontal: -BASELINE_INSET,
        }}
      >
        <View style={{ flex: 1 }}>
          {openingHours?.map((o, index) => {
            const newDay = index === 0 || openingHours[index - 1].date !== o.date;
            const dayIsToday = isToday(o.date);
            return (
              <View
                style={{
                  flexDirection: 'row',
                  justifyContent: 'space-between',
                  width: '100%',
                  height: LINE_HEIGHT,
                  marginTop: newDay ? 0 : MULTIPLE_HOURS_IN_A_DAY_NEGATIVE_MARGIN,
                }}
                key={o.id}
              >
                <View>
                  {newDay && o.date && (
                    <Text size="s" color={dayIsToday ? 'black' : 'dark'} bold={dayIsToday}>
                      {isToday(o.date) && formatMessage({ id: 'app.general.today' })}
                      {isTomorrow(toUnixNumber(o.date)) && formatMessage({ id: 'app.general.tomorrow' })}
                      {showDate(o.date) &&
                        languageCode === 'en' &&
                        capitalizedFirstLetter(formatShortDayWithoutTime(o.date, true))}
                      {showDate(o.date) &&
                        languageCode !== 'en' &&
                        capitalizedFirstLetter(formatDayWithoutTime(o.date, true))}
                    </Text>
                  )}
                </View>
                <View>
                  <Text size="s" color={dayIsToday ? 'black' : 'dark'} bold={dayIsToday}>
                    {formattedOpeningHours(o.startTime, o.endTime, o.displayHint)}
                  </Text>
                </View>
              </View>
            );
          })}
        </View>
      </View>
    </View>
  );
}
