import React from 'react';
import { View, ViewProps } from 'react-native';
import Text from '../Text';
import useTheme from '@app/hooks/useTheme';
import TextContainer from './TextContainer';
import NotificationLabel from '@domains/notification/components/NotificationLabel';
import { NotificationMessageLabel } from '@domains/notification/types/NotificationMessage';

interface Props extends React.PropsWithChildren<ViewProps> {
  name?: string;
  description?: string;
  label?: NotificationMessageLabel;
  dateLabel?: string;
}

export default function TitleAndDescription({ name, description, dateLabel, label, children }: Props) {
  const theme = useTheme();

  const hasLabels = !!label || !!dateLabel;

  return (
    <TextContainer>
      {hasLabels && (
        <View
          style={{
            flexDirection: 'row',
            alignItems: 'center',
            marginBottom: theme.space.s / 2,
          }}
        >
          {!!label && (
            <NotificationLabel
              label={label}
              style={{
                marginRight: theme.space.s,
              }}
            />
          )}
          {!!dateLabel && (
            <Text size="s" color="gray">
              {dateLabel}
            </Text>
          )}
        </View>
      )}

      {!!name && (
        <Text
          type="title"
          size="xl"
          style={{ marginBottom: theme.space.s, marginTop: hasLabels ? theme.space.s : undefined }}
          bold
        >
          {name}
        </Text>
      )}
      <View>
        <View>
          {!!description && (
            <View>
              <Text color="dark" useAutoLinking style={{ lineHeight: 26 }}>
                {description}
              </Text>
            </View>
          )}
          {!!children && children}
        </View>
      </View>
    </TextContainer>
  );
}
