import React, { useEffect, useMemo } from 'react';
import useTheme from '@app/hooks/useTheme';
import { View } from 'react-native';
import Text from '@app/components/Text';
import ContentContainer from '@app/components/DetailScreen/ContentContainer';
import { useIntl } from 'react-intl';
import useCart from '@app/hooks/useCart';
import { CartContextUpdater } from '@app/context/CartProvider';
import { useGetFacilities } from '@domains/park/at-the-park/queries/useGetFacilities';
import Button from '@app/components/Button';
import { format } from 'utils/string';
import { getFormattedTime } from 'utils/date';
import { useFocusEffect } from '@react-navigation/native';
import useMetrics from '@app/metrics/useMetrics';
import useSelectedPark from '@app/hooks/useSelectedPark';
import StickyHeader from '@app/components/StickyHeader';
import { LandalPressable } from '@app/components/LandalPressable';
import { NavScreenProp } from '@app/navigation/stacks/Main';
import { NotificationContextUpdater } from '@domains/notification/context/NotificationProvider';
import { NotificationMessage } from '@domains/notification/types/NotificationMessage';

const PaymentConfirmation = ({ route, navigation }: NavScreenProp<'PaymentConfirmation'>) => {
  const theme = useTheme();
  const { logScreenView } = useMetrics();
  const { formatMessage } = useIntl();
  const { setActiveShop, updateCart } = React.useContext(CartContextUpdater);
  const { setNotifications } = React.useContext(NotificationContextUpdater);
  const { parkCode } = useSelectedPark();

  var params = Object.values(route?.params)?.map((part) => part);

  const shopId = Number(params[0].toString());
  const paymentStatus = params[1].toString();

  setActiveShop(shopId);
  const { cart, orderMeta } = useCart();
  const { data: facilities } = useGetFacilities();
  const facility = facilities?.find((f) => f?.id === orderMeta?.facilityId);

  const isSuccess = paymentStatus === 'success';
  const facilityName = facility ? facility.name : '';

  useFocusEffect(
    React.useCallback(() => {
      if (shopId) {
        logScreenView(isSuccess ? 'PaymentConfirmation' : 'PaymentFailed', [shopId.toString()]);
      }
    }, [isSuccess, logScreenView, shopId])
  );

  const time = orderMeta && getFormattedTime(orderMeta?.pickupDeliveryDate);
  const orderNumber = orderMeta?.orderNumber ?? 0;
  const pickup = orderMeta?.pickup;

  const getSuccessFullMessage = useMemo(() => {
    if (pickup) {
      return format(
        formatMessage({ id: 'app.shop.payment.success.text.pickup' }),
        orderNumber.toString(),
        time ?? '',
        facilityName ?? ''
      );
    } else {
      return format(
        formatMessage({ id: 'app.shop.payment.success.text.delivery' }),
        orderNumber.toString(),
        formatMessage({ id: 'app.forms.summary.delivery' }),
        time ?? ''
      );
    }
  }, [facilityName, formatMessage, orderNumber, pickup, time]);

  useEffect(() => {
    if (!cart || cart.length === 0 || !orderMeta) {
      return;
    }
    if (paymentStatus === 'success') {
      if (!cart) {
        return;
      }

      const message: NotificationMessage = {
        createdAt: new Date(),
        title: formatMessage({ id: 'app.messages.order' }) + ' ' + facilityName,
        text: getSuccessFullMessage,
      };

      setNotifications((prev) => [...prev, message]);
      /* 
        reservation: {
          date: orderMeta ? orderMeta?.pickupDeliveryDate : new Date(),
          timespan: time ?? '',
          location: facilityName || '',
          quantity: `${cart.length}`,
          remarks: '-',
          messageOrderRows: cart?.map((c) => ({
            id: `${c.product.id}${c.attributeComboId}`,
            label: `${c.product.name} ${c?.options?.map((o) => o.name)?.join(', ')}`,
            amount: c.quantity,
            price: c.totalPrice,
      */
      updateCart([]);
    }
  }, [
    cart,
    facilityName,
    formatMessage,
    getSuccessFullMessage,
    orderMeta,
    orderNumber,
    parkCode,
    paymentStatus,
    setNotifications,
    time,
    updateCart,
  ]);

  let titleText = formatMessage({ id: 'app.shop.payment.success.title' });
  if (paymentStatus === 'cancelled' || paymentStatus === 'declined') {
    titleText = formatMessage({ id: 'app.shop.payment.cancelled.title' });
  } else if (paymentStatus === 'failed' || paymentStatus === 'exception') {
    titleText = formatMessage({ id: 'app.shop.payment.failed.title' });
  }

  let text = '';
  if (isSuccess) {
    text = getSuccessFullMessage;
  } else if (paymentStatus === 'cancelled' || paymentStatus === 'declined') {
    text = formatMessage({ id: 'app.shop.payment.cancelled.text' });
  } else if (paymentStatus === 'failed' || paymentStatus === 'exception') {
    text = formatMessage({ id: 'app.shop.payment.failed.text' });
  }

  const reset = () => {
    updateCart([]);
    navigation.reset({
      index: 0,
      routes: [
        {
          name: 'Start',
        },
        {
          name: 'FoodAndDrinks',
        },
      ],
    });
  };
  return (
    <>
      <StickyHeader showClose navigation={navigation} onClose={reset} />
      <View style={{ flex: 1, backgroundColor: theme.colors.tertiary }}>
        <ContentContainer style={{ flex: 1, backgroundColor: theme.colors.tertiary }}>
          <Text type="title" size="xl" color="white" style={{ marginBottom: 32 }} bold>
            {titleText}
          </Text>
          <Text type="body" color="white" style={{ marginBottom: 32 }}>
            {text}
          </Text>
          {isSuccess && (
            <Button
              type="primary"
              onPress={() => {
                reset();
              }}
              style={{
                marginBottom: 32,
              }}
            >
              <Button.Text>{formatMessage({ id: 'app.general.ok' })}</Button.Text>
            </Button>
          )}
          {!isSuccess && (
            <View>
              <Button
                type="primary"
                onPress={() => {
                  navigation.goBack();
                }}
                style={{
                  marginBottom: 32,
                }}
              >
                <Button.Text>{formatMessage({ id: 'app.shop.payment.button.tryagain' })}</Button.Text>
              </Button>
              <LandalPressable
                onPress={() => {
                  reset();
                }}
              >
                <Text
                  type="body"
                  color="white"
                  style={{ marginBottom: 32, alignSelf: 'center', textDecorationLine: 'underline' }}
                >
                  {formatMessage({ id: 'app.general.cancel' })}
                </Text>
              </LandalPressable>
            </View>
          )}
        </ContentContainer>
      </View>
    </>
  );
};
export default PaymentConfirmation;
