import React, { useState } from 'react';
import FastImage, { ImageStyle, ResizeMode, Source } from 'react-native-fast-image';
import Animated, { Easing, useAnimatedStyle, useSharedValue, withTiming } from 'react-native-reanimated';
import { getImageUrlWithWidth, ImageSize } from 'utils/imageUrl';

interface Props {
  remoteSource?: string;
  localSource?: Source;
  style?: ImageStyle;
  size?: ImageSize;
  resizeMode?: ResizeMode;
  animateOnLoad?: boolean;
}
interface InnerProps extends Props {
  onLoadOrError?: () => void;
}
const InnerImageComponent = ({
  localSource,
  remoteSource,
  size,
  resizeMode,
  onLoadOrError,
  style,
}: InnerProps) => {
  const [imageError, setImageError] = useState(false);
  const fallBack = require('@assets/images/image-not-available.jpg');

  let source = fallBack;

  if (localSource) {
    source = localSource;
  } else {
    // This toString is to prevent other types from breaking this image.
    // Sometimes we get a 'number' type for an image...
    const error =
      (!!remoteSource &&
        !remoteSource.toString().startsWith('http') &&
        !remoteSource.toString().startsWith('file:')) ||
      imageError;
    source = error
      ? fallBack
      : size
      ? { uri: getImageUrlWithWidth(remoteSource, size) }
      : { uri: remoteSource };
  }
  return (
    <FastImage
      source={source}
      onError={() => {
        setImageError(true);
        onLoadOrError?.();
      }}
      style={style}
      resizeMode={resizeMode}
      onLoadEnd={onLoadOrError ? onLoadOrError : undefined}
    />
  );
};

export default function LandalImage({
  localSource,
  remoteSource,
  size,
  resizeMode,
  animateOnLoad,
  style,
}: Props) {
  const fadeAnimationOpacity = useSharedValue(0);

  const fadeAnimation = useAnimatedStyle(() => {
    if (!animateOnLoad) {
      return {};
    }
    return {
      opacity: withTiming(fadeAnimationOpacity.value, {
        duration: 250,
        easing: Easing.bezier(0.25, 0.1, 0.25, 1),
      }),
    };
  });

  if (animateOnLoad) {
    return (
      <Animated.View style={fadeAnimation}>
        <InnerImageComponent
          localSource={localSource}
          remoteSource={remoteSource}
          size={size}
          resizeMode={resizeMode}
          style={style}
          onLoadOrError={() => {
            fadeAnimationOpacity.value = 1;
          }}
        />
      </Animated.View>
    );
  }
  return (
    <InnerImageComponent
      localSource={localSource}
      remoteSource={remoteSource}
      size={size}
      resizeMode={resizeMode}
      style={style}
    />
  );
}
