import ListItems from '@app/components/ListItems2/ListItems';
import ScreenScrollContainer from '@app/components/ScreenScrollContainer';
import StickyHeader from '@app/components/StickyHeader';
import Text from '@app/components/Text';
import useTheme from '@app/hooks/useTheme';
import { getFullName } from 'utils/name';
import React, { useContext, useEffect } from 'react';
import { useIntl } from 'react-intl';
import { useGetProfile } from '../queries/useGetProfile';
import Icon from '@app/components/Icon';
import { Alert, Linking, TouchableOpacity, View } from 'react-native';
import useMetrics from '@app/metrics/useMetrics';
import { NavScreenProp } from '@app/navigation/stacks/Main';
import { useSafeAreaInsets } from 'react-native-safe-area-context';
import BottomSheetProvider, { BottomSheetContext } from '@app/navigation/bottom-sheet/BottomSheetProvider';
import AccountDeletionConfirmation from '../sheets/AccountDeletionConfirmation';
import useLogout from '../hooks/useLogout';
import { UserContext } from '@app/context/usercontext/UserContextProvider';
import { getBrand } from 'utils/brand';
import { useGetReservation } from '@domains/reservations/reservation/queries/useGetReservation';

const Account = ({ navigation }: NavScreenProp<'Account'>) => {
  const theme = useTheme();
  const { formatMessage } = useIntl();
  const { data: profile } = useGetProfile({});
  const insets = useSafeAreaInsets();
  const { logEvent } = useMetrics();
  const { logout } = useLogout();

  const { token } = useContext(UserContext);
  const { data: reservation } = useGetReservation({
    brand: getBrand(theme),
    token,
  });

  let gender = '';
  switch (profile?.gender) {
    case 'Male':
      gender = formatMessage({ id: 'app.forms.labels.gender.man' });
      break;
    case 'Female':
      gender = formatMessage({ id: 'app.forms.labels.gender.woman' });
      break;
  }

  useEffect(() => {
    logEvent('SettingsDetails');
  }, [logEvent]);

  return (
    <BottomSheetProvider>
      <BottomSheetContext.Consumer>
        {(_bottomsheet) => (
          <>
            <View style={{ flex: 1 }}>
              <StickyHeader
                title={formatMessage({ id: 'app.headers.account' })}
                showClose
                navigation={navigation}
              />
              <ScreenScrollContainer
                topPadding={false}
                contentContainerStyle={{ paddingTop: theme.space.xl }}
              >
                <ListItems
                  title={formatMessage({ id: 'app.profile.account.personalData' })}
                  items={[
                    {
                      id: 'salutation',
                      title: formatMessage({ id: 'app.profile.account.personalData.salutation' }),
                      subtitle: gender,
                    },
                    {
                      id: 'name',
                      title: formatMessage({ id: 'app.profile.account.personalData.name' }),
                      subtitle: getFullName(profile?.firstName, profile?.middleName, profile?.lastName),
                    },
                    /* { id: 'relation-number', title: 'Relation number', subtitle: '00000000' }, */
                    {
                      id: 'email',
                      title: formatMessage({ id: 'app.profile.account.personalData.email' }),
                      subtitle: profile?.emailAddress,
                    },
                  ]}
                />
                <TouchableOpacity
                  activeOpacity={0.8}
                  style={{ flexDirection: 'row', marginBottom: theme.space.xl }}
                  onPress={() => {
                    logEvent('SettingsDetailsUpdate');
                    Linking.openURL(formatMessage({ id: 'app.website.mylandal' }));
                  }}
                >
                  <Icon
                    color="tertiary"
                    name="exit-app"
                    size="s"
                    style={{ marginRight: theme.space.s, top: 1 }}
                  />
                  <Text color="tertiary" bold>
                    {formatMessage({ id: 'app.profile.account.personalData.change' })}
                  </Text>
                </TouchableOpacity>
                <ListItems
                  title={formatMessage({ id: 'app.profile.account.loginData' })}
                  items={[
                    {
                      id: 'password',
                      title: formatMessage({ id: 'app.profile.account.loginData.password' }),
                      subtitle: '***********',
                    },
                  ]}
                />

                <TouchableOpacity
                  activeOpacity={0.8}
                  style={{ flexDirection: 'row', marginBottom: theme.space.xl }}
                  onPress={() => navigation.navigate('ForgotPassword', { isChange: true })}
                >
                  <Icon
                    color="tertiary"
                    name="edit"
                    size="s"
                    style={{ marginRight: theme.space.s, top: 1 }}
                  />
                  <Text color="tertiary" bold>
                    {formatMessage({ id: 'app.profile.account.loginData.change' })}
                  </Text>
                </TouchableOpacity>
                <TouchableOpacity
                  activeOpacity={0.8}
                  onPress={() => {
                    if (reservation?.bookingReference) {
                      Alert.alert(
                        formatMessage({ id: 'app.login.delete.errorActiveReservation.title' }),
                        formatMessage({ id: 'app.login.delete.errorActiveReservation.text' })
                      );
                      return;
                    }
                    _bottomsheet.openBottomSheet({
                      component: () => (
                        <AccountDeletionConfirmation
                          onDelete={async () => {
                            await logout();
                            _bottomsheet.closeBottomSheet();
                            navigation.goBack();
                          }}
                          onSelectCancel={_bottomsheet.closeBottomSheet}
                        />
                      ),
                      title: formatMessage({ id: 'app.login.delete.confirmation.title' }),
                      key: 'delete-account',
                    });
                  }}
                  style={{
                    marginVertical: theme.space.l,
                    alignSelf: 'flex-start',
                  }}
                >
                  <Text size="s" color="gray">
                    {formatMessage({ id: 'app.login.delete.buttonText' })}
                  </Text>
                </TouchableOpacity>
              </ScreenScrollContainer>
            </View>
          </>
        )}
      </BottomSheetContext.Consumer>
    </BottomSheetProvider>
  );
};
export default Account;
