import { CULTURE_KEY } from '@app/config/StorageKeys';
import AsyncStorage from '@react-native-async-storage/async-storage';
import moment from 'moment-timezone';
import 'moment/locale/nl';
import 'moment/locale/de';
import 'moment/locale/da';
import 'moment/locale/fr';
import React, { useState } from 'react';
import { IntlProvider } from 'react-intl';

import useMetrics from '@app/metrics/useMetrics';

const DEFAULT_LANGUAGE = 'nl-NL';

export const CultureContextUpdater = React.createContext<(cultureCode: LanguageCode) => void>(
  (_: LanguageCode) => {}
);
export const CultureContext = React.createContext<LanguageCode | undefined>(undefined);
import { translationsLandal, translationsHofvansaksen } from '@app/translations';
import DeviceInfo from 'react-native-device-info';
import { languageCode, LanguageCode } from '@app/hooks/useCulture';

export function CultureProvider({ children }: { children: React.ReactNode }) {
  const [cultureCode, setCulture] = useState<LanguageCode>();
  const messages = cultureCode ? getTranslationsForLanguage(cultureCode) : undefined;
  const { setUserProperty } = useMetrics();
  const [locale, setLocale] = useState(DEFAULT_LANGUAGE);

  React.useEffect(() => {
    if (cultureCode) {
      AsyncStorage.setItem(CULTURE_KEY, cultureCode);
      setUserProperty('app_language', cultureCode);
      setLocale(cultureCode);
      moment.locale(languageCode[cultureCode]);
    }
  }, [cultureCode, setUserProperty]);

  return (
    <IntlProvider
      locale={locale}
      messages={messages}
      onError={(err) => {
        if (err.code === 'MISSING_TRANSLATION') {
          console.warn('Missing translation', err.message);
          return;
        }
        throw err;
      }}
    >
      <CultureContext.Provider value={cultureCode ?? DEFAULT_LANGUAGE}>
        <CultureContextUpdater.Provider value={setCulture}>{children}</CultureContextUpdater.Provider>
      </CultureContext.Provider>
    </IntlProvider>
  );
}

export function getTranslationsForLanguage(language: LanguageCode) {
  const bundleId = DeviceInfo.getBundleId();
  const isHofvanSaksen = bundleId.includes('hofvansaksen');
  const landalTranslations = translationsLandal[language]();

  if (isHofvanSaksen) {
    const hofvansaksenTranslations = translationsHofvansaksen[language]();
    return { ...landalTranslations, ...hofvansaksenTranslations };
  }

  return landalTranslations;
}
