import React, { useState } from 'react';
import Text from '@components/Text';
import useTheme from '@app/hooks/useTheme';
import { View } from 'react-native';
import Button from '@app/components/Button';
import { FormattedMessage, useIntl } from 'react-intl';
import SAPLogoutWebview from './SAPLogoutWebview';
import { useBottomSheet } from '@app/navigation/bottom-sheet/BottomSheetProvider';
import useMetrics from '@app/metrics/useMetrics';
import useLogout from '../hooks/useLogout';

interface Props {
  username?: string;
}

const LogoutConfirmationSheet = ({ username }: Props) => {
  const theme = useTheme();
  const { formatMessage } = useIntl();
  const [logoutRequested, setLogoutRequested] = useState(false);
  const { logEvent } = useMetrics();
  const { logout } = useLogout();
  const { closeBottomSheet } = useBottomSheet();

  const onLogout = async () => {
    await logout();
    setLogoutRequested(false);
    closeBottomSheet();
  };

  return (
    <>
      <View style={{ margin: theme.space.l }}>
        <View style={{ flexDirection: 'column', alignItems: 'center', marginBottom: theme.space.l }}>
          <Text style={{ textAlign: 'center', maxWidth: '80%' }} type="body" size="s">
            <FormattedMessage id={'app.login.logout.confirmation.text'} />
            {` `}
            {username}
          </Text>
        </View>
        <Button
          type="primary"
          onPress={() => {
            setLogoutRequested(true);
          }}
        >
          {logoutRequested ? (
            <Button.ActivityIndicator style={{ height: 22 }} />
          ) : (
            <Button.Text>
              <FormattedMessage id={'app.login.logout.confirmation.positive'} />
            </Button.Text>
          )}
        </Button>
        <Button
          type="primary"
          onPress={() => {
            closeBottomSheet();
          }}
          style={{
            backgroundColor: 'transparent',
          }}
        >
          <Button.Text style={{ color: theme.colors.tertiary }}>
            {formatMessage({ id: 'app.general.cancel' })}
          </Button.Text>
        </Button>
      </View>
      {logoutRequested && (
        <View>
          <SAPLogoutWebview
            onSuccess={async () => {
              await onLogout();
              logEvent('LogoutSuccess');
              setLogoutRequested(false);
            }}
            onError={() => {
              logEvent('LogoutError');
              onLogout();
              setLogoutRequested(false);
            }}
          />
        </View>
      )}
    </>
  );
};
export default LogoutConfirmationSheet;
