import React from 'react';
import { useAnimatedScrollHandler, useSharedValue } from 'react-native-reanimated';
import { View } from 'react-native';
import useTheme from '@app/hooks/useTheme';
import TitleAndDescription from '@app/components/DetailScreen/TitleAndDescription';
import Button from '@app/components/Button';
import OpeningHours from '@app/components/DetailScreen/OpeningHours';
import FoodMenuOverview from '../component/FoodMenuOverview';
import { useGetMenus } from '../queries/useGetMenus';
import { useGetFacilities } from '@domains/park/at-the-park/queries/useGetFacilities';
import { useIntl } from 'react-intl';
import ActivityNavigation from '@domains/park/activities/components/ActivityNavigation';
import { useFocusEffect } from '@react-navigation/native';
import useMetrics from '@app/metrics/useMetrics';
import { Openinghour } from 'apis/_generated-park-api/models';
import { NavScreenProp } from '@app/navigation/stacks/Main';
import StickyImageHeader, { useStickyImageHeaderDimensions } from '@app/components/StickyImageHeader';
import ScreenScrollContainer from '@app/components/ScreenScrollContainer';
import StickyBottomBar from '@app/navigation/StickyBottomBar';
import Loader from '@app/components/Loader';

export default function FoodAndDrinksDetail({ route, navigation }: NavScreenProp<'ActivitiesDetail'>) {
  const { logScreenView } = useMetrics();
  const { formatMessage } = useIntl();
  const theme = useTheme();
  const parkCode = route?.params?.parkCode;
  const id = route?.params?.id;
  const { data, isLoading } = useGetFacilities({ parkCodeOverride: parkCode });
  const foodAndDrinks = data?.find((d) => d.id === id);
  const { data: foodMenus } = useGetMenus({ parkCodeOverride: parkCode, restaurantId: foodAndDrinks?.id });

  const translationY = useSharedValue(0);
  const scrollHandler = useAnimatedScrollHandler((event) => {
    translationY.value = event.contentOffset.y;
  });

  useFocusEffect(
    React.useCallback(() => {
      if (foodAndDrinks?.id) {
        logScreenView('FoodAndDrinksDetail', [foodAndDrinks.id], { location: foodAndDrinks?.name ?? '' });
      }
    }, [foodAndDrinks, logScreenView])
  );

  const { EXPANDED_HEADER_HEIGHT, size } = useStickyImageHeaderDimensions('m');

  if (!data || !route?.params?.id) {
    return null;
  }

  if (isLoading) {
    return (
      <View style={{ flex: 1, justifyContent: 'center', alignItems: 'center' }}>
        <Loader />
      </View>
    );
  }

  const hasAction = !!foodAndDrinks?.reservationId;

  return (
    <>
      <StickyImageHeader
        scrollOffset={translationY}
        title={foodAndDrinks?.name}
        images={[foodAndDrinks?.media]}
        size={size}
      />
      <ScreenScrollContainer
        onScroll={scrollHandler}
        animated
        contentContainerStyle={{ paddingTop: EXPANDED_HEADER_HEIGHT }}
        forceBottomInset={!!hasAction}
      >
        <TitleAndDescription name={foodAndDrinks?.name} description={foodAndDrinks?.description} />

        <OpeningHours
          openingHours={foodAndDrinks?.openingHours ? (foodAndDrinks?.openingHours as Openinghour[]) : []}
        />
        {!!foodAndDrinks?.id && <FoodMenuOverview menu={foodMenus} restaurantId={foodAndDrinks?.id} />}
        <ActivityNavigation
          parkCode={parkCode}
          facilityId={foodAndDrinks?.id}
          style={{ marginTop: theme.space.l }}
        />
      </ScreenScrollContainer>

      {hasAction && (
        <StickyBottomBar style={{ padding: theme.space.l, justifyContent: 'center' }}>
          {!!foodAndDrinks?.reservationId && (
            <View style={{ flex: 1 }}>
              <Button
                onPress={() => {
                  navigation.navigate('TableBooking', {
                    parkCode,
                    restaurantId: foodAndDrinks?.reservationId ?? '',
                  });
                }}
                style={{
                  backgroundColor: theme.colors.secondary,
                }}
              >
                <Button.Icon name="food-and-drinks" size="s" color={'white'} />
                <Button.Text color="white">
                  {formatMessage({ id: 'app.foodanddrinks.bookatable' })}
                </Button.Text>
              </Button>
            </View>
          )}
        </StickyBottomBar>
      )}
    </>
  );
}
