import React from 'react';
import { useGetPOI } from '../queries/useGetPoi';
import useTheme from '@app/hooks/useTheme';
import GroupPois from '../data/groupPois';
import useMetrics from '@app/metrics/useMetrics';
import { useFocusEffect } from '@react-navigation/native';
import StickyHeader from '@app/components/StickyHeader';
import { useIntl } from 'react-intl';
import { NavScreenProp } from '@app/navigation/stacks/Main';

const InTheRegion = ({ navigation, route }: NavScreenProp<'InTheRegion'>) => {
  const theme = useTheme();
  const { logScreenView } = useMetrics();
  const { formatMessage } = useIntl();
  const parkCode = route?.params?.parkCode;
  const { data } = useGetPOI({ parkCode });
  const { getPOIsForCategory, uniqueCategories } = GroupPois(data);

  useFocusEffect(
    React.useCallback(() => {
      logScreenView('InTheRegion');
    }, [logScreenView])
  );

  const emptyImage = require('@assets/images/landal_activityEmpty.png');

  return (
    <>
      <StickyHeader
        title={formatMessage({ id: 'app.headers.inTheRegion' })}
        showClose
        navigation={navigation}
      />
    </>
  );
};
export default InTheRegion;
