import React from 'react';
import ActivityNavigation from '../components/ActivityNavigation';
import { useFocusEffect } from '@react-navigation/native';
import useMetrics from '@app/metrics/useMetrics';
import StickyHeader from '@app/components/StickyHeader';
import { useIntl } from 'react-intl';
import { NavScreenProp } from '@app/navigation/stacks/Main';

const Activities = ({ navigation, route }: NavScreenProp<'Activities'>) => {
  const { logScreenView } = useMetrics();
  const { formatMessage } = useIntl();

  const parkCode = route?.params?.parkCode;

  useFocusEffect(
    React.useCallback(() => {
      logScreenView('Activities');
    }, [logScreenView])
  );

  return (
    <>
      <StickyHeader
        title={formatMessage({ id: 'app.park.funAndEntertainment' })}
        showClose
        navigation={navigation}
      />
      <ActivityNavigation style={{ flex: 1 }} parkCode={parkCode} />
    </>
  );
};
export default Activities;
