import useCart from '@app/hooks/useCart';
import { ShopSettings } from 'apis/_generated-park-api/models';
import { AxiosError } from 'axios';
import { gql, GraphQLClient } from 'graphql-request';
import Config from 'react-native-config';
import { UseQueryOptions, useQuery } from 'react-query';

const key = `getshopsettings`;
type ProductDataProps = {
  options?: Omit<UseQueryOptions<ShopSettings, AxiosError>, typeof key>;
};

export function useGetShopSettings(props?: ProductDataProps) {
  const { activeShop: shopId } = useCart();

  return useQuery(
    [key, { shopId }],
    async () => {
      const graphQLClient = new GraphQLClient(Config.PARK_API_URL);
      const query = gql`
        query shopSettings {
          shopSettings(shopId: ${shopId}) {
            hasPickup
            hasDeliver
            deliveryCost
          }
        }
      `;
      const result = await graphQLClient.rawRequest<{ shopSettings: ShopSettings }>(query);
      return result?.data?.shopSettings;
    },
    props?.options
  );
}
