import React from 'react';
import { Platform, StyleSheet, View, ViewStyle } from 'react-native';
import Icon from '../Icon';
import RNPickerSelect, { PickerStyle } from 'react-native-picker-select';
import useTheme from '@app/hooks/useTheme';

const INPUT_HEIGHT = 50;

interface Props {
  value: string | number;
  items: Array<{ label: string; value: string | number }>;
  onValueChange: (value: number) => void;
  style?: ViewStyle;
  border?: boolean;
}

const FormPicker = ({ value, items, onValueChange, style, border }: Props) => {
  const theme = useTheme();
  const pickerSelectStyles: PickerStyle = StyleSheet.create({
    inputIOS: {
      height: INPUT_HEIGHT,
      paddingHorizontal: 16,
      borderRadius: 8,
      paddingRight: 30, // to ensure the text is never behind the icon
      fontFamily: theme.fonts.bodyRegular,
      color: theme.colors.black,
      fontSize: theme.fontSizes.m.fontSize,
    },
    inputAndroid: {
      // Android only
      height: INPUT_HEIGHT,
      paddingHorizontal: 16,
      paddingRight: 30, // to ensure the text is never behind the icon
      fontFamily: theme.fonts.bodyRegular,
      color: theme.colors.black,
      fontSize: theme.fontSizes.m.fontSize,
    },
    viewContainer: {
      flex: 1,
      borderRadius: border ? 8 : 0,
      borderWidth: border ? 1 : 0,
      backgroundColor: border ? 'white' : undefined,
      borderColor: theme.colors.light,
      ...style,
    },
    iconContainer: {
      top: INPUT_HEIGHT / 2 - 8,
      right: theme.space.l,
    },
  });

  return (
    <RNPickerSelect
      onValueChange={onValueChange}
      style={{ ...pickerSelectStyles }}
      value={value}
      items={items}
      placeholder={{}}
      useNativeAndroidPickerStyle
      Icon={() => {
        if (Platform.OS === 'android') return <></>;
        return (
          <View pointerEvents="none">
            <Icon name="chevron-down" size={'s'} color="tertiary" />
          </View>
        );
      }}
    />
  );
};

export default FormPicker;
