import { Route } from '@react-navigation/native';
import React, { useContext, useState } from 'react';
import { RouteNames } from './Navigation';

export const ScreenTrackingContextUpdater = React.createContext<
  React.Dispatch<React.SetStateAction<Route<RouteNames, any> | null>>
>(() => {});
export const ScreenTrackingContext = React.createContext<Route<RouteNames, any> | null>(null);

export const useActiveScreenName = () => {
  const screen = useContext(ScreenTrackingContext);
  return screen?.name;
};

export function ScreenTrackingProvider({ children }: { children: React.ReactNode }) {
  const [screen, setScreen] = useState<Route<RouteNames, any> | null>(null);

  React.useEffect(() => {
    if (screen && __DEV__) {
      console.log(screen.name);
    }
  }, [screen]);

  return (
    <ScreenTrackingContext.Provider value={screen}>
      <ScreenTrackingContextUpdater.Provider value={setScreen}>
        {children}
      </ScreenTrackingContextUpdater.Provider>
    </ScreenTrackingContext.Provider>
  );
}
