import { fontSizes, spaceDefaults } from './sizes';

export type ThemeType = 'hofvansaksen' | 'landal';

export type Theme = {
  type: ThemeType;
  fonts: {
    headingBold: string;
    headingBoldWeight: string;
    headingRegular: string;
    bodyBold: string;
    bodyBoldWeight: string;
    bodyRegular: string;
  };
  infoBoxBorderStyle: string;
  fontSizes: typeof fontSizes;
  space: typeof spaceDefaults;
  mainBackground: string;
  colors: {
    [key: string]: string;
  };
};

const generalPalette = {
  white: '#FFFFFF',
  trueBlack: '#000000',
};

export type AllColors =
  | keyof typeof generalPalette
  | 'primary'
  | 'secondary'
  | 'tertiary'
  | 'gradientPrimary1'
  | 'gradientPrimary2'
  | 'gradientPrimary3'
  | 'gradientDanger1'
  | 'gradientDanger2'
  | 'gradientWarning1'
  | 'gradientWarning2'
  | 'success'
  | 'warning'
  | 'danger'
  | 'attention'
  | 'black'
  | 'trueBlack'
  | 'dark'
  | 'gray'
  | 'light'
  | 'canvas'
  | 'shade'
  | 'white'
  | 'vega';

export default generalPalette;
