import React, { useCallback, useContext, useState, useEffect, useRef } from 'react';
import { ScrollView, TouchableOpacity } from 'react-native';
import useCulture from '@app/hooks/useCulture';
import { useIntl } from 'react-intl';
import useMetrics from '@app/metrics/useMetrics';
import { useFocusEffect } from '@react-navigation/native';
import { getBuildNumber } from 'react-native-device-info';
import { UserContext } from '@app/context/usercontext/UserContextProvider';
import Config from 'react-native-config';
import ListItems from '@app/components/ListItems2/ListItems';
import Text from '@app/components/Text';
import ScreenScrollContainer from '@app/components/ScreenScrollContainer';
import StickyHeader, { RelativeHeaderArea } from '@app/components/StickyHeader';
import { useBottomSheet } from '@app/navigation/bottom-sheet/BottomSheetProvider';
import Login from './Login';
import { useGetProfile } from '../queries/useGetProfile';
import LogoutConfirmationSheet from './LogoutConfirmationSheet';
import FeedbackSheet from '../sheets/FeedbackSheet';
import SelectLanguageSheet from '@domains/onboarding/screens/SelectLanguageSheet';
import { useAnimatedScrollHandler, useSharedValue } from 'react-native-reanimated';
import ScreenContainer from '@app/components/ScreenContainer';
import { NavScreenProp } from '@app/navigation/stacks/Main';
import useTheme from '@app/hooks/useTheme';
import DebugMenu from './DebugMenu';

export default function ProfileScreen({ navigation }: NavScreenProp<'Profile'>) {
  const culture = useCulture();
  const theme = useTheme();
  const { formatMessage } = useIntl();
  const { logScreenView } = useMetrics();
  const { token, demoMode } = useContext(UserContext);
  const [debugTaps, setDebugTaps] = useState(0);
  const { openBottomSheet } = useBottomSheet();
  const { data: profile } = useGetProfile({});

  const scrollRef = useRef<ScrollView>(null);
  const isInFocus = useRef<boolean>();

  useFocusEffect(
    useCallback(() => {
      isInFocus.current = true;
      logScreenView('Settings');

      return () => {
        isInFocus.current = false;
      };
    }, [logScreenView])
  );
  useEffect(() => {
    return navigation.addListener('tabPress', (e) => {
      if (isInFocus.current === true) {
        scrollRef.current?.scrollTo({ y: 0, animated: true });
      }
    });
  }, [navigation]);

  let buildNumberString = `v.${getBuildNumber()}`;
  if (Config.ENVIRONMENT !== 'production') {
    buildNumberString += ' - Acceptance version';
  }
  if (demoMode) {
    buildNumberString += ' - DEMO mode';
  }

  const title = profile?.firstName
    ? `Hi ${profile.firstName}`
    : formatMessage({ id: 'app.onboarding.language.title' });

  const scrollOffset = useSharedValue(0);
  const scrollHandler = useAnimatedScrollHandler((event) => {
    scrollOffset.value = event.contentOffset.y;
  });

  return (
    <ScreenContainer>
      <StickyHeader title={title} bigTitle main scrollOffset={scrollOffset} />
      <ScreenScrollContainer
        animated
        onScroll={scrollHandler}
        scrollEventThrottle={1}
        topPadding={false}
        forceBottomInset
        scrollRef={scrollRef}
      >
        <RelativeHeaderArea />
        <ListItems
          style={{ marginTop: 0 }}
          items={
            token
              ? [
                  {
                    id: 'data',
                    title: formatMessage({ id: 'app.headers.account' }),
                    icon: 'profile-active',
                    navigateTo: 'Account',
                  },
                ]
              : [
                  {
                    id: 'login',
                    title: formatMessage({ id: 'app.login.title' }),
                    icon: 'profile-active',
                    onPress: () => {
                      openBottomSheet({
                        key: 'login',
                        initialHeight: 197,
                        component: () => (
                          <Login
                            onSuccess={() => {
                              navigation.goBack();
                            }}
                            navigation={navigation}
                          />
                        ),
                        title: formatMessage({ id: 'app.login.title' }),
                      });
                    },
                  },
                ]
          }
        />
        <ListItems
          title={formatMessage({ id: 'app.profile.settingsAndPreferences' })}
          items={[
            /* { id: 'one', title: 'Notificaties', icon: 'bell' }, */
            {
              id: 'language',
              title: formatMessage({ id: 'app.profile.language' }),
              subtitle: culture?.languageCode
                ? formatMessage({ id: 'app.onboarding.selectlanguage.' + culture.languageCode.toLowerCase() })
                : undefined,
              icon: 'language',
              bottomSheet: {
                key: 'language',
                initialHeight: theme.type === 'hofvansaksen' ? 350 : 462,
                component: () => <SelectLanguageSheet />,
                title: formatMessage({ id: 'app.profile.language' }),
                backgroundColor: 'canvas',
              },
            },
            /* { id: 'payment-methods', title: 'Betaalmethoden' }, */
          ]}
        />
        <ListItems
          title={formatMessage({ id: 'app.profile.helpAndContact' })}
          items={[
            /* {
              id: 'service',
              title: formatMessage({ id: 'app.profile.serviceAndContact' }),
              icon: 'question-mark-circular',
            }, */
            {
              id: 'feedback',
              title: formatMessage({ id: 'app.feedback.title1' }),
              icon: 'report-damage',
              bottomSheet: {
                key: 'feedback',
                initialHeight: 532,
                component: () => <FeedbackSheet />,
                title: formatMessage({ id: 'app.feedback.title1' }),
                backgroundColor: 'canvas',
                scrollToEnd: true,
              },
            },
            {
              id: 'privacy-notice',
              title: formatMessage({ id: 'app.profile.privacyNotice' }),
              icon: 'lock',
              linkTo: formatMessage({ id: 'app.settings.privacyUrl' }),
              event: 'SettingsPrivacyNotice',
            },
          ]}
        />
        {!!token && (
          <ListItems
            items={[
              {
                id: 'logout',
                title: formatMessage({ id: 'app.login.logout' }),
                titleColor: 'attention',
                icon: 'logout',
                iconColor: 'attention',
                bottomSheet: {
                  component: () => <LogoutConfirmationSheet username={profile?.emailAddress} />,
                  title: formatMessage({ id: 'app.login.logout.confirmation.title' }),
                  key: 'logout',
                },
              },
            ]}
          />
        )}
        {debugTaps > 4 && (
          <ListItems
            items={[
              {
                id: 'debug',
                title: 'Debug menu',
                icon: 'placeholder',
                bottomSheet: {
                  component: () => <DebugMenu />,
                  title: 'Debug menu',
                  key: 'debug',
                },
              },
            ]}
          />
        )}
        <TouchableOpacity
          activeOpacity={0.8}
          onPress={() => {
            setDebugTaps((_debugTaps) => _debugTaps + 1);
          }}
        >
          <Text color="gray">{buildNumberString}</Text>
        </TouchableOpacity>
      </ScreenScrollContainer>
    </ScreenContainer>
  );
}
