import React, { useEffect } from 'react';
import { NativeStackNavigationProp } from '@react-navigation/native-stack';
import Text from '@app/components/Text';
import { useIntl } from 'react-intl';
import { AllRoutes } from '@app/navigation/stacks/Main';
import useMetrics from '@app/metrics/useMetrics';
import StickyHeader from '@app/components/StickyHeader';
import ScreenContainer from '@app/components/ScreenContainer';
import ScreenScrollContainer from '@app/components/ScreenScrollContainer';
import { NavigationContainerRef, RouteProp, useNavigation } from '@react-navigation/native';
import useTheme from '@app/hooks/useTheme';
import { ReadableDayByDate } from 'utils/date';
import { capitalizedFirstLetter } from 'utils/string';
import { NotificationContextUpdater } from '../context/NotificationProvider';
import TitleAndDescription from '@app/components/DetailScreen/TitleAndDescription';
import { NotificationMessage } from '../types/NotificationMessage';
import Table, { TableItem } from '@app/components/Table';
import { View } from 'react-native';
import StickyImageHeader, { useStickyImageHeaderDimensions } from '@app/components/StickyImageHeader';
import { useAnimatedScrollHandler, useSharedValue } from 'react-native-reanimated';
import LandalImage from '@app/components/LandalImage';

interface Props {
  navigation: NativeStackNavigationProp<AllRoutes, keyof AllRoutes>;
  route: RouteProp<AllRoutes>;
}

const NotificationDetail = ({ route }: Props) => {
  const theme = useTheme();
  const { logEvent } = useMetrics();
  const { setNotifications } = React.useContext(NotificationContextUpdater);
  const navigation = useNavigation<NavigationContainerRef<any>>();
  const { formatMessage } = useIntl();
  const message: NotificationMessage = route?.params?.message;
  const { EXPANDED_HEADER_HEIGHT, size } = useStickyImageHeaderDimensions('m');

  useEffect(() => {
    logEvent('NotificationSheetOpen');
  }, [logEvent]);

  // Mark this notification as read
  useEffect(() => {
    setNotifications((prev) => {
      const newArray = [...prev];
      const thisMessageIndex = prev.findIndex(
        (p) => p.text === message.text && p.createdAt === message.createdAt
      );
      if (thisMessageIndex === -1) {
        return prev;
      }

      const thisMessage = prev[thisMessageIndex];
      thisMessage.read = true;

      newArray.splice(thisMessageIndex, 1);
      return [...newArray, thisMessage];
    });
  }, [message.createdAt, message.text, setNotifications]);

  const translationY = useSharedValue(0);
  const scrollHandler = useAnimatedScrollHandler((event) => {
    translationY.value = event.contentOffset.y;
  });

  const hasImage = !!message.image;
  const imageInline = hasImage && message.imageInline;

  return (
    <ScreenContainer>
      {hasImage && !imageInline ? (
        <StickyImageHeader
          scrollOffset={translationY}
          title={formatMessage({ id: 'app.notificationcenter.message' })}
          images={message.image ? [message.image] : []}
          size={size}
        />
      ) : (
        <StickyHeader
          title={formatMessage({ id: 'app.notificationcenter.message' })}
          showClose
          onClose={() => navigation.goBack()}
        />
      )}
      <ScreenScrollContainer
        onScroll={hasImage && !imageInline ? scrollHandler : undefined}
        topPadding={false}
        animated={hasImage && !imageInline}
        contentContainerStyle={{ paddingTop: hasImage && !imageInline ? EXPANDED_HEADER_HEIGHT : undefined }}
        style={{ paddingBottom: theme.space.l }}
      >
        <TitleAndDescription
          name={message.title}
          description={message.text}
          dateLabel={
            message.createdAt
              ? capitalizedFirstLetter(ReadableDayByDate(new Date(message.createdAt), true, true))
              : undefined
          }
          label={message.label}
        >
          {imageInline && (
            <LandalImage
              remoteSource={message.image}
              style={{
                width: '100%',
                height: undefined,
                borderRadius: 8,
                marginTop: theme.space.l,
                aspectRatio: 1.6,
              }}
              animateOnLoad
            />
          )}
        </TitleAndDescription>

        {(!!message.shortDetails || !!message.longDetails) && (
          <Table
            title={formatMessage({ id: 'app.general.details' })}
            items={message.shortDetails as TableItem[]}
            showChildrenDivider
            style={{ top: theme.space.s }}
          >
            {!!message.longDetails &&
              message.longDetails?.map((d) => (
                <View style={{ marginBottom: theme.space.l }}>
                  <Text style={{ lineHeight: 26 }} bold>
                    {d.label}
                  </Text>
                  <Text style={{ lineHeight: 26, fontStyle: 'italic' }} color="dark">
                    {d.value || '-'}
                  </Text>
                </View>
              ))}
            <Text color="gray" style={{ fontStyle: 'italic', marginBottom: theme.space.m }} useAutoLinking>
              {message.subnote}
            </Text>
          </Table>
        )}
      </ScreenScrollContainer>
    </ScreenContainer>
  );
};

export default NotificationDetail;
