import useCart from '@app/hooks/useCart';
import { ShopAvailability } from 'apis/_generated-park-api/models';
import { AxiosError } from 'axios';
import { gql, GraphQLClient } from 'graphql-request';
import Config from 'react-native-config';
import { UseQueryOptions, useQuery } from 'react-query';

const key = `getshopavailability`;
type Props = {
  options?: Omit<UseQueryOptions<ShopAvailability[] | undefined, AxiosError>, typeof key>;
  type: 'pickup' | 'deliver';
};

export function useGetShopAvailability({ type, options }: Props) {
  const { activeShop: shopId } = useCart();

  return useQuery(
    [key, { shopId, type }],
    async () => {
      const graphQLClient = new GraphQLClient(Config.PARK_API_URL);
      const query = gql`
        query shopAvailability {
          shopAvailability(shopId: ${shopId}, type: "${type}") {
            dateTimeFrom
            id
          }
        }
      `;
      const result = await graphQLClient.rawRequest<{ shopAvailability: ShopAvailability[] }>(query);
      return result?.data?.shopAvailability;
    },
    options
  );
}
