import useTheme from '@app/hooks/useTheme';
import React, { useEffect } from 'react';
import { TouchableOpacity, View, ViewProps } from 'react-native';
import Animated, { Easing, useAnimatedStyle, useSharedValue, withTiming } from 'react-native-reanimated';

const DOT_SIZE = 6;
const SPACING = 6;
const MAX_VISIBLE_DOTS = 5;
const MID = Math.floor(MAX_VISIBLE_DOTS / 2);
const DOT_PLUS_SPACING = DOT_SIZE + SPACING;
const SHRINK_PERCENTAGE = '70%';

interface Props extends ViewProps {
  length: number;
  selected: number;
  setSelected: Function;
  loop?: boolean;
  color?: string;
  inactiveOpacity?: number;
}

const getMoveIndex = (length: number, selected: number): number => {
  if (selected < MID) {
    return 0;
  } else if (selected >= length - MID) {
    return MAX_VISIBLE_DOTS - length;
  } else {
    return MAX_VISIBLE_DOTS - selected - MID - 1;
  }
};

export default function PageIndicator({
  length,
  selected = 0,
  setSelected = () => {},
  loop = false,
  style,
  color = 'red',
  inactiveOpacity,
}: Props) {
  const theme = useTheme();
  const innerContainerLeft = useSharedValue(0);

  useEffect(() => {
    if (length <= MAX_VISIBLE_DOTS) {
      return;
    }

    const moveIndex = getMoveIndex(length, selected);

    innerContainerLeft.value = moveIndex * DOT_PLUS_SPACING;
  }, [innerContainerLeft, length, selected]);

  const animatedInnerContainerStyle = useAnimatedStyle(() => {
    return {
      left: withTiming(innerContainerLeft.value, {
        duration: 500,
        easing: Easing.bezier(0.25, 0.1, 0.25, 1),
      }),
    };
  });

  return (
    <Animated.View
      style={[
        {
          position: 'absolute',
          bottom: theme.space.m,
          alignSelf: 'center',
        },
        style,
      ]}
    >
      <View
        style={{
          maxWidth: MAX_VISIBLE_DOTS * DOT_PLUS_SPACING,
          overflow: 'hidden',
        }}
      >
        <Animated.View // This view moves inside the container
          style={[
            {
              flexDirection: 'row',
            },
            animatedInnerContainerStyle,
          ]}
        >
          {[...Array(length)].map((_, index) => {
            const isSelected = index === selected;

            const moveIndex = getMoveIndex(length, selected);

            const shrunk =
              isSelected || index === 0 || index === length - 1
                ? false
                : index <= -moveIndex || index >= MAX_VISIBLE_DOTS - moveIndex - 1;

            return (
              <TouchableOpacity key={index} onPress={() => setSelected(index)}>
                <Animated.View
                  key={index}
                  style={{
                    width: DOT_SIZE,
                    height: DOT_SIZE,
                    borderRadius: DOT_SIZE / 2,
                    marginRight: SPACING,
                    opacity: isSelected ? 0.95 : inactiveOpacity ? inactiveOpacity : 0.6,
                    alignItems: 'center',
                    justifyContent: 'center',
                  }}
                >
                  <View
                    style={{
                      width: shrunk ? SHRINK_PERCENTAGE : '100%',
                      height: shrunk ? SHRINK_PERCENTAGE : '100%',
                      backgroundColor: color,
                      borderRadius: DOT_SIZE / 2,
                    }}
                  />
                </Animated.View>
              </TouchableOpacity>
            );
          })}
        </Animated.View>
      </View>
    </Animated.View>
  );
}
