import { useGetPOI } from '@domains/park/in-the-region/queries/useGetPoi';
import { Location } from 'apis/_generated-park-api/models';

type PoiHighlightType = {
  id?: string;
  title: string;
  ticketsWebsite?: string;
  media?: string;
  location?: Location;
  subtitle?: string;
};

interface Props {
  parkCode: string;
}
export default function usePOIHighlights({ parkCode }: Props): PoiHighlightType[] {
  const { data } = useGetPOI({ parkCode });

  /* const sortedData = useMemo(() => {
    if (!data) return [];

    // POI's with tickets get priority
    return data.sort((a, b) => {
      return b.ticketsWebsite ? 0 : -1;
    });
  }, [data]); */

  const items = data?.slice(0, 6)?.map((i) => ({
    id: i.id,
    title: i.name ?? '',
    ticketsWebsite: i.ticketsWebsite ?? '',
    media: i.media ?? '',
    subtitle: '',
    location: i.location ?? {},
  }));
  return items ?? [];
}
