import React, { useContext, useMemo } from 'react';
import { ListItem } from '@app/components/ListItems2/ListItems';
import { UserContext } from '@app/context/usercontext/UserContextProvider';
import useMetrics from '@app/metrics/useMetrics';
import { useGetReservation } from '@domains/reservations/reservation/queries/useGetReservation';
import { useIntl } from 'react-intl';
import { getBrand } from 'utils/brand';
import useCulture from './useCulture';
import useTheme from './useTheme';
import DigitalCleaningSheet from '@domains/sheets/DigitalCleaningSheet';
import { BookingStatusType } from 'apis/_generated-reservation-api/models';
import CheckoutNowSheet from '@domains/sheets/CheckoutNowSheet';
import useDigitalCleaningChecklist from '@app/context/ChecklistProvider';

interface Props {
  bookingReference?: string;
}

export default function useCheckoutChecklist({ bookingReference }: Props) {
  const { token } = useContext(UserContext);
  const { formatMessage, messages } = useIntl();
  const { cultureCode } = useCulture();
  const theme = useTheme();
  const { logEvent } = useMetrics();

  const { data: reservation } = useGetReservation({
    brand: getBrand(theme),
    token,
    bookingReference,
  });

  const { completed: completedDigitalCleaning } = useDigitalCleaningChecklist(bookingReference);

  const translatedItems = useMemo<string[]>(() => {
    return (
      reservation?.digitalCleaningTasks?.filter((item) => {
        const id = 'app.reservation.digitalCleaning.items.' + item;
        return !!messages[id];
      }) || []
    );
  }, [messages, reservation?.digitalCleaningTasks]);

  let checkoutChecklist = useMemo(() => {
    let list: ListItem[] = [
      {
        id: 'digital-cleaning',
        title: formatMessage({ id: 'app.reservation.digitalCleaning.listTitle' }),
        subtitle: formatMessage({ id: 'app.reservation.digitalCleaning.listSubtitle' }),
        icon: completedDigitalCleaning ? 'cleaning-complete' : 'cleaning-incomplete',
        bottomSheet: {
          key: 'cleanlist-' + 'reservation.unitTypeIndicator',
          component: () => <DigitalCleaningSheet bookingReference={bookingReference || ''} />,
          title: formatMessage({ id: 'app.reservation.digitalCleaning.listTitle' }),
          backgroundColor: 'canvas',
        },
        status: completedDigitalCleaning ? 'complete' : 'incomplete',
        disabled:
          reservation?.status === BookingStatusType.Booked ||
          reservation?.status === BookingStatusType.Provisional ||
          reservation?.status === BookingStatusType.Departed,
      },
      {
        id: 'checkout-now',
        title: formatMessage({
          id: 'app.launcher.checkoutNow',
        }),
        icon: 'check-out-small',
        status: reservation?.status === BookingStatusType.Departed ? 'complete' : 'incomplete',
        bottomSheet: {
          component: () => <CheckoutNowSheet bookingReference={reservation?.bookingReference ?? ''} />,
          title: formatMessage({ id: 'app.launcher.checkoutNow' }),
          key: 'checkout-now',
        },
        disabled: !reservation?.featuresEnabled?.checkoutNow,
      },
    ];

    if ((translatedItems?.length || 0) === 0) list.shift();

    return list;
  }, [
    bookingReference,
    completedDigitalCleaning,
    formatMessage,
    reservation?.bookingReference,
    reservation?.featuresEnabled?.checkoutNow,
    reservation?.status,
    translatedItems?.length,
  ]);

  const numberOfItems = checkoutChecklist.length;
  const numberOfItemsCompleted = checkoutChecklist.filter((c) => c.status === 'complete').length;
  const percentageComplete = Math.round((numberOfItemsCompleted / numberOfItems) * 100);
  const pendingTasks = numberOfItems - numberOfItemsCompleted;

  return { checkoutChecklist };
}
