import React from 'react';
import useTheme from '@app/hooks/useTheme';
import { View } from 'react-native';
import Text from '@app/components/Text';
import ContentContainer from '@app/components/DetailScreen/ContentContainer';
import { useIntl } from 'react-intl';
import Button from '@app/components/Button';
import { format } from 'utils/string';
import { useGetFacilities } from '@domains/park/at-the-park/queries/useGetFacilities';
import { getFormattedDate } from 'utils/date';
import StickyHeader from '@app/components/StickyHeader';
import { NavScreenProp } from '@app/navigation/stacks/Main';

const ActivityBookingSuccessful = ({ navigation, route }: NavScreenProp<'ActivityBookingSuccessful'>) => {
  const theme = useTheme();
  const { formatMessage } = useIntl();

  const activity = route?.params?.activity;
  const amount = route?.params?.amount;
  const routeDate = route?.params?.activity?.time?.date;
  const date = routeDate ? new Date(routeDate) : undefined;

  const { data } = useGetFacilities();
  const facility = data?.find((c) => c.id === activity?.locationId);

  const people =
    amount > 1
      ? formatMessage({ id: 'app.forms.result.person_plural' })
      : formatMessage({ id: 'app.forms.result.person' });

  return (
    <>
      <StickyHeader showClose navigation={navigation} />
      <View style={{ flex: 1, backgroundColor: theme.colors.tertiary }}>
        <ContentContainer style={{ flex: 1, backgroundColor: theme.colors.tertiary }}>
          <Text type="title" size="xl" color="white" style={{ marginBottom: theme.space.l }} bold>
            {formatMessage({ id: 'app.forms.result.title' })}
          </Text>

          <Text type="body" color="white" style={{ marginBottom: theme.space.l }}>
            {format(
              formatMessage({
                id: 'app.forms.activitybooking.result.description',
              }),
              activity.name ?? '',
              amount.toString(),
              people,
              date ? getFormattedDate(date) : '',
              activity.time?.startTime ?? '',
              facility?.name ?? '',
              '\r\n',
              '\r\n',
              activity.name ?? ''
            )}
          </Text>

          <Text type="body" color="white" style={{ marginBottom: theme.space.l }}>
            {formatMessage({ id: 'app.forms.result.messagedescription1' })}
            {facility?.name}
            {formatMessage({ id: 'app.forms.result.messagedescription2' })}
          </Text>
          <Button
            type="primary"
            onPress={() => {
              navigation.goBack();
            }}
            style={{
              marginBottom: 32,
            }}
          >
            <Button.Text>{formatMessage({ id: 'app.forms.result.back' })}</Button.Text>
          </Button>
        </ContentContainer>
      </View>
    </>
  );
};
export default ActivityBookingSuccessful;
