import React, { useContext, useMemo } from 'react';
import { ActionPadButtonType } from '../components/ActionPad';
import useKeyStatus from '@domains/locks/hooks/useKeyStatus';
import { UserContext } from '@app/context/usercontext/UserContextProvider';
import { useGetReservation } from '@domains/reservations/reservation/queries/useGetReservation';
import { getBrand } from 'utils/brand';
import useTheme from '@app/hooks/useTheme';
import { SaltoContext } from '@app/context/SaltoContextProvider';
import LockSheet from '@domains/locks/sheets/LockSheet';
import SaltoLoginWebview from '@domains/profile/screens/SaltoLoginWebview';
import Login from '@domains/profile/screens/Login';
import { NavigationContainerRef, useNavigation } from '@react-navigation/native';
import { useIntl } from 'react-intl';
import { Linking, View } from 'react-native';
import ParkMap from '@app/components/ParkMap';
import useMetrics, { Screens } from '@app/metrics/useMetrics';
import queryString from 'query-string';
import AttachReservation from '@domains/reservations/reservation/screens/AttachReservation';
import { useGetPark } from '@domains/park/contact-and-info/queries/useGetPark';
import useHasOwnApp from '@app/hooks/useHasOwnApp';

interface Props {
  gettingKeyReady?: boolean;
  reservationParkCode?: string;
}
export default function useActionPadButtons({ gettingKeyReady, reservationParkCode }: Props) {
  const theme = useTheme();
  const { saltoUserToken } = React.useContext(SaltoContext);
  const { keyShown, keyReady, keyExpired, accommodationReady } = useKeyStatus({});
  const { token } = useContext(UserContext);
  const navigation = useNavigation<NavigationContainerRef<any>>();
  const { formatMessage } = useIntl();
  const { logEvent } = useMetrics();
  const { data: park } = useGetPark({ parkCodeOverride: reservationParkCode });
  const { hasOwnApp } = useHasOwnApp(park?.id);

  const brand = getBrand(theme);
  const { data: reservation } = useGetReservation({
    brand,
    token,
  });

  return useMemo(() => {
    if (hasOwnApp) {
      return null;
    }

    const viewParkButton: ActionPadButtonType = {
      label: formatMessage({ id: 'app.actionpad.viewParks' }),
      icon: 'park',
      navigateTo: 'ParkTab',
    };
    const bookHolidayButton: ActionPadButtonType = {
      label: formatMessage({ id: 'app.actionpad.bookHoliday' }),
      icon: 'book-holiday',
      onPress: () => {
        try {
          const url = formatMessage({
            id: 'app.general.homepage',
          });

          const query = queryString.stringify({
            utm_source: getBrand(theme) === 'HSN' ? 'Hof-van-Saksen-Park-App' : 'Landal-Park-App',
            utm_medium: 'app',
            utm_campaign: 'Retention', // Self-service | Retention | Upsell
            utm_content: `${Screens.Reservation.nl}` /* |${park?.id}|| */,
          });

          const completeUrl = `${url}?${query}`;
          Linking.openURL(completeUrl);
          logEvent('BookHolidayLead');
        } catch (ex) {
          const message = (ex as { message: string }).message;
          logEvent('BookHolidayError', { message });
        }
      },
    };
    const attachReservationButton: ActionPadButtonType = {
      label: formatMessage({ id: 'app.reservation.add' }),
      icon: 'reservations-active',
      bottomSheet: {
        key: 'attach-reservation',
        component: () => <AttachReservation />,
        title: formatMessage({ id: 'app.reservation.add' }),
      },
    };
    const parkMapButton: ActionPadButtonType = {
      label: formatMessage({ id: 'app.actionpad.parkMap' }),
      icon: 'map',
      bottomSheet: {
        component: () => (
          <View style={{ width: '100%', flex: 1 }}>
            <ParkMap parkCode={reservation?.parkCode} bookingReference={reservation?.bookingReference} />
          </View>
        ),
        title: formatMessage({ id: 'app.actionpad.parkMap' }).replace('\n', ' '),
        fullHeight: true,
        delay: 300,
        panHandleOnly: true,
      },
    };
    const extrasButton: ActionPadButtonType = {
      label: formatMessage({ id: 'app.actionpad.extras' }),
      icon: 'extras',
      navigateTo: 'Products',
    };
    const foodAndDrinksButton: ActionPadButtonType = {
      label: formatMessage({ id: 'app.actionpad.foodAndDrinks' }),
      icon: 'food-and-drinks',
      navigateTo: 'FoodAndDrinks',
      navigateParams: { parkCode: reservation?.parkCode },
    };
    const atTheParkButton: ActionPadButtonType = {
      label: formatMessage({ id: 'app.actionpad.atThePark' }),
      icon: 'park',
      navigateTo: 'AtThePark',
      navigateParams: { parkCode: reservation?.parkCode },
    };
    const funAndEntertainmentButton: ActionPadButtonType = {
      label: formatMessage({ id: 'app.actionpad.funAndEntertainment' }),
      icon: 'fun-and-entertainment',
      navigateTo: 'Activities',
      navigateParams: { parkCode: reservation?.parkCode },
    };
    const keyButton: ActionPadButtonType = {
      label: keyReady
        ? formatMessage({ id: 'app.key.keyUse' }).replace(' ', '\n')
        : formatMessage({ id: 'app.key.keyActivate' }).replace(' ', '\n'),
      icon: keyReady ? 'key' : 'key-inactive',
      state: keyReady ? 'active' : 'inactive',
      processing: gettingKeyReady,
      bottomSheet: {
        component: () => (
          <>
            <LockSheet
              bookingReference={reservation?.bookingReference}
              keyReady={keyReady}
              keyExpired={keyExpired}
              accommodationReady={accommodationReady}
            />
            {!!token && !saltoUserToken && (
              <SaltoLoginWebview
                onSuccess={async () => {
                  //setGettingSaltoReady(false);
                }}
                onReady={() => {}}
                onLoadStart={() => {
                  //setGettingSaltoReady(true);
                }}
                onError={() => {
                  //setGettingSaltoReady(false);
                }}
              />
            )}
          </>
        ),
        key: 'lock-key',
        title: keyReady
          ? formatMessage({ id: 'app.key.keyUse' })
          : formatMessage({ id: 'app.key.keyActivate' }),
      },
    };
    const loginButton: ActionPadButtonType = {
      label: formatMessage({ id: 'app.actionpad.login' }),
      icon: 'login',
      bottomSheet: {
        key: 'login',
        initialHeight: 197,
        component: () => (
          <Login
            navigation={navigation}
            onSuccess={() => {
              //
            }}
          />
        ),
        title: formatMessage({ id: 'app.login.title' }),
      },
    };

    let actionPadButtons: ActionPadButtonType[];
    if (token) {
      if (!reservation) {
        actionPadButtons = [attachReservationButton, viewParkButton, bookHolidayButton];
      } else if (reservation?.combiReference) {
        actionPadButtons = [parkMapButton, foodAndDrinksButton, atTheParkButton, funAndEntertainmentButton];
      } else if (reservation?.featuresEnabled?.extras) {
        actionPadButtons = [
          parkMapButton,
          extrasButton,
          foodAndDrinksButton,
          atTheParkButton,
          funAndEntertainmentButton,
        ];
      } else {
        actionPadButtons = [parkMapButton, foodAndDrinksButton, atTheParkButton, funAndEntertainmentButton];
      }
      if (keyShown) {
        actionPadButtons = [keyButton, ...actionPadButtons];
      }
    } else {
      actionPadButtons = [loginButton, viewParkButton, bookHolidayButton];
    }

    return actionPadButtons;
  }, [
    accommodationReady,
    formatMessage,
    gettingKeyReady,
    hasOwnApp,
    keyExpired,
    keyReady,
    keyShown,
    logEvent,
    navigation,
    reservation,
    saltoUserToken,
    theme,
    token,
  ]);
}
