import { DIGITAL_CLEANING_PERSIST } from '@app/config/StorageKeys';
import { useAsyncStorage } from '@react-native-async-storage/async-storage';
import React, { useCallback, useEffect, useRef, useState } from 'react';

interface DigitalCleaningAcco {
  reference: string;
  checked: string[];
  completed: boolean;
}

interface ContextInterface {
  items: DigitalCleaningAcco[];
  getCheckedItems: (reference: string) => string[];
  getCompleted: (reference: string) => boolean;
  checkItem: (task: string, reference: string) => void;
  checkAll: (tasks: string[], reference: string) => void;
  setCompleted: (reference: string) => void;
}

export const ChecklistContext = React.createContext<Partial<ContextInterface>>({});

interface Props {
  children: React.ReactNode;
}

export default function useChecklist(reference = '') {
  const { items, getCheckedItems, getCompleted, checkItem, checkAll, setCompleted } =
    React.useContext(ChecklistContext);
  const [checkedItems, setCheckedItems] = useState<string[]>([]);
  const [completed, setLocalCompleted] = useState<boolean>(false);
  useEffect(() => {
    setCheckedItems(getCheckedItems?.(reference) ?? []);
    setLocalCompleted(getCompleted?.(reference) ?? false);
  }, [items, reference, getCheckedItems, getCompleted]);
  return {
    items,
    getCheckedItems,
    getCompleted,
    checkItem: (task: string) => checkItem?.(task, reference),
    checkAll: (tasks: string[]) => checkAll?.(tasks, reference),
    checkedItems,
    completed,
    setCompleted: () => setCompleted?.(reference),
  };
}

export const useCompletedChecklists = (references: string[]) => {
  const { items } = React.useContext(ChecklistContext);
  const [completedCount, setCompletedCount] = useState(0);
  useEffect(() => {
    const count = items?.filter((item) => references.includes(item.reference) && item.completed)?.length ?? 0;
    setCompletedCount(count);
  }, [items, references]);
  return completedCount;
};

export function ChecklistProvider({ children }: Props) {
  const [digitalCleaningData, setDigitalCleaningData] = useState<DigitalCleaningAcco[]>([]);

  const { getItem, setItem } = useAsyncStorage(DIGITAL_CLEANING_PERSIST);
  const initialized = useRef(false);

  useEffect(() => {
    const getData = async () => {
      const json = await getItem();
      if (json) {
        const data = JSON.parse(json || '');
        setDigitalCleaningData(data);
      }
    };
    if (!initialized.current) {
      getData();
    }
    initialized.current = true;
  }, [getItem]);

  const getCheckedItems = useCallback(
    (reference: string) => {
      const accoCleaningData = digitalCleaningData.find(
        (item: DigitalCleaningAcco) => item.reference === reference
      );
      return accoCleaningData?.checked ?? [];
    },
    [digitalCleaningData]
  );

  const getCompleted = useCallback(
    (reference: string) => {
      const accoCleaningData = digitalCleaningData.find(
        (item: DigitalCleaningAcco) => item.reference === reference
      );
      return accoCleaningData?.completed ?? false;
    },
    [digitalCleaningData]
  );

  const checkItem = useCallback<(task: string, reference: string) => void>(
    (task, reference) => {
      const checkedItems = getCheckedItems(reference);
      let newCheckedItems: string[] = [];
      if (checkedItems.includes(task)) {
        newCheckedItems = checkedItems.filter((_item) => _item !== task);
      } else {
        newCheckedItems = [...checkedItems, task];
      }
      const other = digitalCleaningData.filter((item) => !!item?.reference && item.reference !== reference);
      const items = [...other, { reference, checked: newCheckedItems, completed: false }];
      setDigitalCleaningData(items);
      setItem(JSON.stringify(items));
    },
    [digitalCleaningData, getCheckedItems, setItem]
  );

  const checkAll = useCallback<(tasks: string[], reference: string) => void>(
    (tasks, reference) => {
      const other = digitalCleaningData.filter((item) => !!item?.reference && item.reference !== reference);
      const items = [...other, { reference, checked: tasks, completed: false }];
      setDigitalCleaningData(items);
      setItem(JSON.stringify(items));
    },
    [digitalCleaningData, setItem]
  );

  const setCompleted = useCallback<(reference: string) => void>(
    (reference) => {
      const other = digitalCleaningData.filter((item) => !!item?.reference && item.reference !== reference);
      const checked = getCheckedItems(reference);
      const items = [...other, { reference, checked, completed: true }];
      setDigitalCleaningData(items);
      setItem(JSON.stringify(items));
    },
    [digitalCleaningData, getCheckedItems, setItem]
  );

  return (
    <ChecklistContext.Provider
      value={{ items: digitalCleaningData, getCheckedItems, getCompleted, checkItem, checkAll, setCompleted }}
    >
      {children}
    </ChecklistContext.Provider>
  );
}
