import ListItems from '@app/components/ListItems2/ListItems';
import useTheme from '@app/hooks/useTheme';
import { NavigationContainerRef, useNavigation } from '@react-navigation/native';
import { FacilityMenu } from 'apis/_generated-park-api/models';
import React from 'react';
import { useIntl } from 'react-intl';
import { View } from 'react-native';

interface Props {
  menu?: FacilityMenu[];
  restaurantId: string;
}
export default function FoodMenuOverview({ menu, restaurantId }: Props) {
  const theme = useTheme();
  const { formatMessage } = useIntl();
  const navigation = useNavigation<NavigationContainerRef<any>>();

  if (!menu || menu.length === 0) {
    return null;
  }

  return (
    <View style={{ marginTop: theme.space.m }}>
      <ListItems
        title={
          menu.length === 1
            ? formatMessage({ id: 'app.foodmenu.overview.title.singular' })
            : formatMessage({ id: 'app.foodmenu.overview.title.plural' })
        }
        items={menu?.map((m, _index) => {
          const title = m.name
            ? m.name
            : formatMessage({ id: 'app.foodmenu.overview.maincategory.' + m.type });
          return {
            id: title,
            title,
            onPress: () => {
              navigation.navigate('FoodMenu', { restaurantId, menu: m, title });
            },
          };
        })}
      />
    </View>
  );
}
