import usePOIHighlights from '@domains/start/hooks/usePOIHighlights';
import React from 'react';
import { useIntl } from 'react-intl';
import PolaroidItems from '../../../../app/components/PolaroidItems/PolaroidItems';
import Text from '../../../../app/components/Text';
import { useGetPark } from '@domains/park/contact-and-info/queries/useGetPark';
import useTheme from '@app/hooks/useTheme';
import { Platform, TouchableOpacity, View } from 'react-native';
import { NavigationContainerRef, useNavigation } from '@react-navigation/native';
import { useGetPOI } from '@domains/park/in-the-region/queries/useGetPoi';
import InTheRegionSVG from '../../../../app/components/PolaroidItems/svg/InTheRegionSvg';
import { getDistanceText } from 'utils/distance';
import Icon from '@app/components/Icon';

interface Props {
  parkCode?: string;
}
export default function PoiHighlights({ parkCode }: Props) {
  const theme = useTheme();
  const { formatMessage } = useIntl();
  const navigation = useNavigation<NavigationContainerRef<any>>();
  const { data: park } = useGetPark({ parkCodeOverride: parkCode ?? undefined });
  let poiHighlights = usePOIHighlights({ parkCode: parkCode ?? park?.id ?? '' });
  const { data: allPoi } = useGetPOI({ parkCode });

  poiHighlights = [
    ...poiHighlights,
    {
      id: 'show-all',
      title: formatMessage({ id: 'app.intheregion.showmore' }),
      subtitle: `${allPoi?.length ?? 0} ${formatMessage({ id: 'app.general.results' })}`,
    },
  ];

  return (
    <View style={{ marginVertical: theme.space.l }}>
      <Text type="title" size="l" bold>
        {formatMessage({ id: 'app.headers.inTheRegion' })}
      </Text>
      <PolaroidItems
        items={poiHighlights.map((h) => {
          const isSeeAllItem = h.id === 'show-all';

          return {
            id: h.id ?? '' + h.title ?? '',
            title: h.title ?? '',
            subtitle: h.subtitle || getDistanceText(park, h.location),
            subtitleIcon: isSeeAllItem ? undefined : 'distance',
            image: isSeeAllItem ? undefined : h.media,
            SvgComponent: isSeeAllItem ? InTheRegionSVG : undefined,
            badgeIcon: h.ticketsWebsite ? 'ticket' : undefined,
            badgeText: h.ticketsWebsite
              ? formatMessage({ id: 'app.poi.components.ticketshop.badge' })
              : undefined,
            onPress: () => {
              const { id } = h;
              if (id === 'show-all') {
                navigation.navigate('InTheRegion', { parkCode });
              } else {
                navigation.navigate('InTheRegionDetail', { id, parkCode });
              }
            },
          };
        })}
        style={{
          marginBottom: -theme.space.m,
        }}
      />
      {(poiHighlights.length || 0) > 2 && (
        <TouchableOpacity
          onPress={() => navigation.navigate('InTheRegion', { parkCode })}
          activeOpacity={0.8}
        >
          <Text color="tertiary" bold style={{ lineHeight: 26 }}>
            {formatMessage({ id: 'app.intheregion.showmore' })}
            <Icon
              name="chevron-right"
              color="tertiary"
              style={{ marginLeft: 3, top: Platform.OS === 'android' ? 6 : 0 }}
            />
          </Text>
        </TouchableOpacity>
      )}
    </View>
  );
}
