import React, { useState } from 'react';
import Text from '@components/Text';
import useTheme from '@app/hooks/useTheme';
import { View } from 'react-native';
import Button from '@app/components/Button';
import { FormattedMessage, FormattedNumber } from 'react-intl';
import { CartContextUpdater } from '@app/context/CartProvider';
import useCart from '@app/hooks/useCart';
import Popup from '@app/components/Popup';
import NumberControl from '@app/components/Form/NumberControl';
import Icon from '@app/components/Icon';
import { LandalPressable } from '@app/components/LandalPressable';
import { NavScreenProp } from '@app/navigation/stacks/Main';

const QuantityPopup = ({ navigation, route }: NavScreenProp<'QuantityPopup'>) => {
  const theme = useTheme();
  const { updateCart } = React.useContext(CartContextUpdater);

  const id = route?.params?.id;
  const attributeComboId = route?.params?.attributeComboId;

  const { cart } = useCart();
  const thisItem = cart.find((item) => item.product?.id === id && item.attributeComboId === attributeComboId);

  const [newQuantity, setNewQuantity] = useState(thisItem?.quantity);

  const updateQuantity = () => {
    updateCart((prev) => {
      const newArray = [...prev];
      const thisItemInCart = prev.find(
        (item) => item.product?.id === id && item.attributeComboId === attributeComboId
      );

      if (!thisItemInCart) {
        // ERROR
        navigation.goBack();
        return [...prev];
      }
      thisItemInCart.quantity = newQuantity ?? 1;
      return [...newArray];
    });
    navigation.goBack();
  };

  const removeProductFromCart = () => {
    updateCart((prev) => {
      const newArray = [...prev];
      const thisItemInCartIndex = prev.findIndex(
        (item) => item.product?.id === id && item.attributeComboId === attributeComboId
      );
      newArray.splice(thisItemInCartIndex, 1);
      return [...newArray];
    });
    navigation.goBack();
  };

  return (
    <Popup>
      <View style={{ alignItems: 'center' }}>
        <Text style={{ marginBottom: theme.space.s }} type="title" bold>
          {thisItem?.product?.name}
        </Text>
        <Text style={{ marginBottom: theme.space.s }} color="gray">
          {thisItem?.options?.map((o) => o.name)?.join(', ')}
        </Text>
        {thisItem?.totalPrice !== undefined && (
          <Text style={{ marginBottom: theme.space.s }} size="s">
            <FormattedNumber
              value={thisItem?.totalPrice}
              style="currency"
              currency={'EUR'}
              currencyDisplay={'symbol'}
            />
          </Text>
        )}
        {newQuantity !== undefined && (
          <NumberControl value={newQuantity} maximum={99} onChange={setNewQuantity} />
        )}
        <View style={{ marginTop: theme.space.l, width: '100%' }}>
          <Button style={{ backgroundColor: theme.colors.tertiary }} onPress={updateQuantity}>
            <Button.Text style={{ color: theme.colors.white }} type="title">
              <FormattedMessage id={'app.general.save'} />
            </Button.Text>
          </Button>
        </View>
      </View>
      <LandalPressable
        onPress={removeProductFromCart}
        style={{ top: theme.space.l, right: theme.space.l, position: 'absolute' }}
      >
        <Icon name="delete" color="gray" />
      </LandalPressable>
    </Popup>
  );
};
export default QuantityPopup;
