import React from 'react';
import { View, ViewProps } from 'react-native';
import useTheme from '@hooks/useTheme';
import Text from '../Text';
import Icon from '../Icon';
import FormInput from './FormInput';
import FormPicker from './FormPicker';
import { useIntl } from 'react-intl';
import { LandalPressable } from '../LandalPressable';

interface Props {
  name?: string;
  types: { value: number; label: string }[];
  selectedType: number;
  canBeRemoved?: boolean;
  onChange: (name: string | undefined, selectedType: number) => void;
  onRemove: () => void;
}

interface Props extends React.PropsWithChildren<ViewProps> {}
export default function PersonControl({
  name,
  types,
  selectedType,
  canBeRemoved,
  onRemove,
  onChange,
}: Props) {
  const { formatMessage } = useIntl();
  const theme = useTheme();

  const type = selectedType || types?.[0]?.value;

  return (
    <View style={{ marginBottom: 8 }}>
      <View
        style={{ flexDirection: 'row', justifyContent: 'space-between', alignItems: 'center', height: 40 }}
      >
        <Text bold size="s" color={'white'}>
          {formatMessage({ id: 'app.forms.labels.name' })}
        </Text>
        {canBeRemoved && (
          <LandalPressable onPress={onRemove}>
            <Icon name="delete" size="s" color={'white'} style={{ padding: 4 }} />
          </LandalPressable>
        )}
      </View>
      <View
        style={{
          flexDirection: 'row',
          justifyContent: 'space-between',
          alignItems: 'center',
        }}
      >
        <FormInput
          value={name}
          isValid={true}
          onChangeText={(text) => {
            onChange(text, type);
          }}
          showIcon={false}
        />
        {types?.length > 1 && (
          <FormPicker
            value={selectedType}
            onValueChange={(newType) => {
              onChange(name, newType);
            }}
            items={types}
            style={{ marginLeft: theme.space.s, flexShrink: 1 }}
          />
        )}
      </View>
    </View>
  );
}
