import React, { useContext, useEffect } from 'react';
import useTheme from '@app/hooks/useTheme';
import { View } from 'react-native';
import { NativeStackNavigationProp } from '@react-navigation/native-stack';
import { useAnimatedScrollHandler, useSharedValue } from 'react-native-reanimated';
import Text from '@app/components/Text';
import { useIntl } from 'react-intl';
import { AllRoutes } from '@app/navigation/stacks/Main';
import useMetrics from '@app/metrics/useMetrics';
import ItemDivider from '@app/components/ListItems2/ItemDivider';
import NotificationsList, { NotificationsListItem } from '@app/components/ListItems2/NotificationsList';
import { getFromNowString } from 'utils/date';
import StickyHeader, { RelativeHeaderArea } from '@app/components/StickyHeader';
import ScreenContainer from '@app/components/ScreenContainer';
import ScreenScrollContainer from '@app/components/ScreenScrollContainer';
import { NavigationContainerRef, useNavigation } from '@react-navigation/native';
import { NotificationContext } from '../context/NotificationProvider';

interface Props {
  navigation: NativeStackNavigationProp<AllRoutes, keyof AllRoutes>;
}

const Notifications = ({}: Props) => {
  const theme = useTheme();
  const notifications = useContext(NotificationContext);
  const { logEvent } = useMetrics();
  const navigation = useNavigation<NavigationContainerRef<any>>();
  const { formatMessage } = useIntl();

  useEffect(() => {
    logEvent('NotificationSheetOpen');
  }, [logEvent]);
  const scrollOffset = useSharedValue(0);
  const scrollHandler = useAnimatedScrollHandler((event) => {
    scrollOffset.value = event.contentOffset.y;
  });

  return (
    <ScreenContainer>
      <StickyHeader
        title={formatMessage({ id: 'app.notificationcenter.messages' })}
        main
        showClose
        onClose={() => navigation.goBack()}
        scrollOffset={scrollOffset}
      />
      <ScreenScrollContainer
        animated
        onScroll={scrollHandler}
        scrollEventThrottle={1}
        topPadding={false}
        forceBottomInset
      >
        <RelativeHeaderArea />
        <NotificationsList>
          {notifications?.map((n, index) => {
            return (
              <>
                <NotificationsListItem
                  title={n.title ?? ''}
                  subtitle={n.text}
                  createdAt={n.createdAt && getFromNowString(n.createdAt)}
                  isRead={n.read}
                  label={n.label}
                  key={n?.createdAt?.toString() ?? '' + n?.text ?? ''}
                  isFirst={index === 0}
                  isLast={index === notifications.length - 1}
                  onPress={() => navigation.navigate('NotificationDetail', { message: n })}
                />
                {index < notifications.length - 1 && <ItemDivider />}
              </>
            );
          })}
        </NotificationsList>
        {notifications?.length === 0 && (
          <View style={{ paddingHorizontal: theme.space.m, paddingVertical: theme.space.xl }}>
            <Text type="title" size="l" style={{ lineHeight: 26 }}>
              {formatMessage({ id: 'app.notificationcenter.empty' })}
            </Text>
          </View>
        )}
      </ScreenScrollContainer>
    </ScreenContainer>
  );
};

export default Notifications;
