import { ThemeType } from '@app/theme/partials/general';
import { Platform } from 'react-native';
import useTheme from './useTheme';

const parksWithOwnApp = [
  {
    parkCode: 'HSN',
    dynamicLink: 'https://landal.page.link/open-hsn-app',
    appName: 'Hof van Saksen',
    appIcon: require('@domains/reservations/assets/images/app-icon-hsn.png'),
  },
  {
    parkCode: 'MWL',
    dynamicLink:
      Platform.OS === 'android'
        ? 'https://play.google.com/store/apps/details?id=com.wemaleisure.leukermeer&hl=nl&gl=US'
        : 'https://apps.apple.com/jm/app/leukermeer/id1616686204',
    appName: 'Leukermeer',
    appIcon: require('@domains/reservations//assets/images/app-icon-mwl.png'),
  },
];
const DYNAMIC_LINK_LGP = 'https://landal.page.link/open-lgp-app';

export const getHasOwnApp = (themeType: ThemeType, parkCode?: string) => {
  if (!parkCode) return { hasOwnApp: false };

  if (themeType === 'hofvansaksen') {
    return {
      hasOwnApp: parkCode !== 'HSN',
      dynamicLink: DYNAMIC_LINK_LGP,
      appName: 'Landal',
      appIcon: require('@domains/reservations/assets/images/app-icon-lgp.png'),
    };
  }
  const ownAppItem = parksWithOwnApp.find((p) => p.parkCode === parkCode);
  return {
    hasOwnApp: !!ownAppItem,
    dynamicLink: ownAppItem?.dynamicLink,
    appName: ownAppItem?.appName,
    appIcon: ownAppItem?.appIcon,
  };
};

export default function useHasOwnApp(parkCode?: string) {
  const theme = useTheme();
  return getHasOwnApp(theme.type, parkCode);
}
