import useTheme from '@app/hooks/useTheme';
import { BASELINE_INSET } from '@app/theme/partials/sizes';
import React, { ReactElement, useState } from 'react';
import { View, ScrollView, useWindowDimensions, ViewStyle, Platform } from 'react-native';
import Icon from '../Icon';
import Shadow from '../Shadow';
import Text from '../Text';
import useBadge, { BADGE_HEIGHT } from '@app/hooks/useBadge';
import { LandalPressable } from '../LandalPressable';
import LandalSkiaImage from '../LandalSkiaImage';

export interface PolaroidItem {
  id: string;
  title: string;
  subtitle?: string;
  subtitleIcon?: string;
  image?: string;
  SvgComponent?: ({ size }: { size: number }) => ReactElement;
  onPress?: () => void;
  badgeIcon?: string;
  badgeText?: string;
}

interface Props {
  items: PolaroidItem[];
  style?: ViewStyle;
}

interface ItemProps {
  item: PolaroidItem;
  isLastItem?: boolean;
}

const Item = ({ item, isLastItem }: ItemProps) => {
  const { title, image, SvgComponent, badgeIcon, badgeText, subtitle, subtitleIcon, onPress } = item;
  const theme = useTheme();
  const { width: screenWidth } = useWindowDimensions();

  const fallbackSource = require('@assets/images/image-not-available.jpg');

  const FACTOR = 0.38;
  const PADDING = 6;

  const width = Math.min(screenWidth * FACTOR, 200);
  const imageSize = width - PADDING * 2;

  const { reactBadge, skiaBadge } = useBadge({
    text: badgeText,
    icon: badgeIcon,
    borderRadius: 6,
    space: 's',
    y: imageSize - theme.space.s - BADGE_HEIGHT,
    fullWidth: true,
    containerSize: imageSize,
  });

  const [showSkia, setShowSkia] = useState(false);

  const hasBadge = !!badgeIcon || !!badgeText;

  return (
    <LandalPressable
      style={{
        backgroundColor: '#FFF',
        marginRight: isLastItem ? 0 : theme.space.s,
        width,
        height: width + theme.space.s * 2 + theme.fontSizes.m.lineHeight * 3 + PADDING * 2,
        borderRadius: 12,
      }}
      activeOpacity={0.8}
      onPress={onPress}
    >
      <View
        style={{
          width: imageSize,
          height: imageSize,
          borderRadius: 8,
          backgroundColor: '#EEE',
          overflow: 'hidden',
          marginHorizontal: PADDING,
          marginTop: PADDING,
          flex: Platform.OS === 'web' ? undefined : 0,
        }}
      >
        <View
          style={{
            width: '100%',
            height: '100%',
          }}
          onLayout={(event) => {
            var { width: layoutWidth } = event.nativeEvent.layout;
            if (layoutWidth <= 0) {
              return;
            }
            // setShowSkia(true);
          }}
        >
          <LandalSkiaImage
            width={imageSize + 2}
            height={imageSize + 2}
            remoteSource={image}
            localSource={!image ? fallbackSource : undefined}
            badge={hasBadge && skiaBadge}
          />

          {hasBadge && reactBadge}
        </View>
        {!image && !!SvgComponent && <SvgComponent size={imageSize + 1} style={{ position: 'absolute' }} />}
      </View>
      <View
        style={{
          flex: 1,
          margin: theme.space.m,
        }}
      >
        <View style={{ flex: 1, marginBottom: theme.space.s }}>
          <Text style={{ flex: 1 }} numberOfLines={2} bold>
            {title}
          </Text>
        </View>
        {!!subtitle && (
          <View style={{ flexDirection: 'row', alignItems: 'center' }}>
            {!!subtitleIcon && <Icon name={subtitleIcon} size="s" color="gray" style={{ marginRight: 4 }} />}
            <Text color="gray">{subtitle}</Text>
          </View>
        )}
      </View>
    </LandalPressable>
  );
};

const PolaroidItems = ({ items, style }: Props) => {
  const theme = useTheme();

  return (
    <ScrollView
      horizontal
      style={[
        style,
        {
          marginTop: theme.space.m,
          paddingBottom: theme.space.xl,
          marginHorizontal: -BASELINE_INSET - theme.space.l,
        },
      ]}
      contentContainerStyle={{ paddingHorizontal: theme.space.l }}
      showsHorizontalScrollIndicator={false}
    >
      {items.map((item, index) => (
        <Shadow key={item.id}>
          <Item item={item} isLastItem={index === items.length - 1} />
        </Shadow>
      ))}
    </ScrollView>
  );
};

export default PolaroidItems;
