import React, { useCallback, useContext, useEffect, useState } from 'react';
import { View } from 'react-native';
import Text from '@app/components/Text';
import useTheme from '@app/hooks/useTheme';
import { useFormik } from 'formik';
import Button from '@app/components/Button';
import { TouchableOpacity } from 'react-native-gesture-handler';
import { UserContext, UserContextUpdater } from '@app/context/usercontext/UserContextProvider';
import { useGetNewToken } from '../queries/useGetNewToken';
import { ParkContextUpdater } from '@app/context/usercontext/ParkProvider';
import { useIntl } from 'react-intl';
import { getBrand } from 'utils/brand';
import { isValidEmail } from 'utils/validation';
import { SAPWebviewInject } from 'utils/webviewCommands';
import useMetrics from '@app/metrics/useMetrics';
import SAPLoginWebview from './SAPLoginWebview';
import WebView from 'react-native-webview';
import Config from 'react-native-config';
import { NativeStackNavigationProp } from '@react-navigation/native-stack';
import { ProfileRoutes } from '@app/navigation/stacks/Profile';
import { useBottomSheet } from '@app/navigation/bottom-sheet/BottomSheetProvider';
import FormControl from '@app/components/Form/FormControl';
import Loader from '@app/components/Loader';
import { useGetReservations } from '@domains/reservations/reservation/queries/useGetReservations';
import { getActiveReservation } from 'utils/reservation';
import { getHasOwnApp } from '@app/hooks/useHasOwnApp';

type FormFields = 'email' | 'password';

interface Props {
  enableCreateAccount?: boolean;
  onSuccess: () => void;
  onNeedsParkSelection?: () => void;
  navigation: NativeStackNavigationProp<ProfileRoutes, keyof ProfileRoutes>;
}

type FormikErrors = {
  name?: string;
  email?: string;
};

const SAP_READY_TIMEOUT = 10000;
const VISIBLE_WEBVIEWS = __DEV__ && false;

const Login = ({ enableCreateAccount = true, navigation }: Props) => {
  const theme = useTheme();
  const { setToken } = useContext(UserContextUpdater);

  const [loginReady, setLoginReady] = useState(false);
  const [processing, setProcessing] = useState(false);
  const [loginError, setLoginError] = useState<string>();
  const [loginErrorMessage, setLoginErrorMessage] = useState<string>();
  const { formatMessage } = useIntl();
  const { logEvent } = useMetrics();
  const changePark = useContext(ParkContextUpdater);
  const sapLoginWebview = React.useRef<WebView>(null);

  const { closeBottomSheet } = useBottomSheet();

  // eslint-disable-next-line no-undef
  const [readyTimer, setReadyTimer] = useState<NodeJS.Timeout | null | undefined>(undefined);

  let validated = false;
  const formik = useFormik<Record<FormFields, any>>({
    initialValues: {
      email: '',
      password: '',
    },
    validateOnMount: true,
    onSubmit: () => {
      validated = true;
    },
    validate: (validatedValues) => {
      const errors: FormikErrors = {};

      if (!validated) {
        return errors;
      }
      if (!validatedValues.password) {
        errors.name = 'no password';
      }
      if (!validatedValues.email || !isValidEmail(validatedValues.email)) {
        errors.email = 'no email';
      }

      return errors;
    },
  });

  const { getToken } = useGetNewToken();
  const { token } = React.useContext(UserContext);

  const {
    data: reservations,
    status,
    failureCount,
  } = useGetReservations({
    brand: getBrand(theme),
    token,
  });

  const loginFailed = useCallback(
    (reason: 'missing_code' | 'missing_token' | 'invalid_credentials' | 'load_error') => {
      setLoginError(reason);

      switch (reason) {
        case 'invalid_credentials': {
          formik.setFieldError('email', 'FOUT');
          formik.setFieldError('password', 'FOUT');
          setLoginErrorMessage('app.login.invalidCredentials');
          break;
        }
        case 'missing_code':
        case 'missing_token': {
          setLoginErrorMessage('app.login.serverError');
          break;
        }
        case 'load_error': {
          setLoginErrorMessage('app.login.serverError');
          break;
        }
        default: {
          setLoginErrorMessage('app.login.serverError');
        }
      }

      logEvent('LoginError', { error_type: reason });
      setProcessing(false);
    },
    [formik, logEvent]
  );

  React.useEffect(() => {
    if (!processing || !token) {
      return;
    }

    if (status === 'success') {
      setProcessing(false);

      const firstReservation = getActiveReservation(reservations);
      if (firstReservation?.parkCode) {
        const { hasOwnApp } = getHasOwnApp(theme.type, firstReservation?.parkCode);
        if (!hasOwnApp) changePark(firstReservation?.parkCode);
      }
      closeBottomSheet();
    } else if (status === 'error' && failureCount > 2) {
      setProcessing(false);
    }
  }, [changePark, closeBottomSheet, failureCount, processing, reservations, status, theme.type, token]);

  const clearTimer = useCallback(() => {
    setReadyTimer(null);
    readyTimer && clearTimeout(readyTimer);
  }, [readyTimer]);

  const initReadyTimer = useCallback(() => {
    if (readyTimer === null) {
      return;
    }
    if (loginReady) {
      clearTimer();
    }
    if (readyTimer === undefined) {
      setReadyTimer(
        setTimeout(() => {
          if (!loginReady) {
            loginFailed?.('load_error');
            clearTimer?.();
          }
          return () => clearTimer?.();
        }, SAP_READY_TIMEOUT)
      );
    }
  }, [clearTimer, loginFailed, loginReady, readyTimer]);

  useEffect(() => {
    initReadyTimer();
  }, [initReadyTimer]);

  const injectLogin = (username: string, password: string) => {
    sapLoginWebview?.current?.injectJavaScript(SAPWebviewInject(username, password));
  };

  const loginButtonDisabled =
    processing || !loginReady || !formik.isValid || !formik.values.email || !formik.values.password;

  const onLoginPress = () => {
    if (processing) {
      return;
    }
    setProcessing(true);
    setLoginError(undefined);
    setLoginErrorMessage(undefined);
    logEvent('Login');

    injectLogin(formik.values.email, formik.values.password);
  };

  return (
    <View>
      {!loginReady && !loginError ? (
        <View
          style={{
            alignItems: 'center',
            justifyContent: 'center',
          }}
        >
          <Text size="s" color={'gray'} style={{ marginVertical: theme.space.m, textAlign: 'center' }}>
            {formatMessage({
              id: 'app.login.loading',
            })}
          </Text>
          <Loader />
        </View>
      ) : loginError !== 'load_error' ? (
        <View
          style={{
            marginHorizontal: theme.space.l,
          }}
        >
          <View style={{ marginTop: theme.space.l }}>
            <FormControl
              label={formatMessage({
                id: 'app.login.emailPlaceholder',
              })}
              value={formik.values.email}
              isValid={!formik.errors.email}
              onChangeText={(text) => {
                formik.setFieldValue('email', text);
              }}
              autoCapitalize="none"
              autoCorrect={false}
              textContentType="emailAddress"
              keyboardType="email-address"
            />
            <FormControl
              label={formatMessage({
                id: 'app.login.passwordPlaceholder',
              })}
              value={formik.values.password}
              isValid={!formik.errors.password}
              onChangeText={(text) => {
                formik.setFieldValue('password', text);
              }}
              autoCorrect={false}
              secureTextEntry
              textContentType="password"
              autoCapitalize="none"
              returnKeyType="send"
              onSubmitEditing={() => !loginButtonDisabled && onLoginPress()}
            />
            {loginError && (
              <Text
                bold
                size="s"
                color="attention"
                style={{
                  textAlign: 'center',
                  marginVertical: theme.space.l,
                  marginHorizontal: theme.space.s,
                }}
              >
                {loginErrorMessage &&
                  formatMessage({
                    id: loginErrorMessage,
                  })}
              </Text>
            )}
            <TouchableOpacity
              activeOpacity={0.8}
              onPress={() => {
                logEvent('RequestPasswordReset');
                closeBottomSheet();
                navigation?.navigate('ForgotPassword', {
                  email: isValidEmail(formik.values.email?.trim()) ? formik.values.email.trim() : '',
                });
              }}
            >
              <Text
                size="s"
                color="tertiary"
                style={{
                  textAlign: 'center',
                  marginTop: 0,
                  marginBottom: theme.space.xl,
                  alignSelf: 'flex-end',
                }}
              >
                {formatMessage({
                  id: 'app.login.forgotPassword',
                })}
              </Text>
            </TouchableOpacity>
            <Button
              onPress={async () => {
                onLoginPress();
              }}
              style={{ backgroundColor: theme.colors.secondary, marginTop: theme.space.s }}
              disabled={loginButtonDisabled}
            >
              {processing ? (
                <Button.ActivityIndicator style={{ height: 22 }} />
              ) : (
                <Button.Text color="white">
                  {formatMessage({
                    id: 'app.login.login',
                  })}
                </Button.Text>
              )}
            </Button>
            {enableCreateAccount && (
              <TouchableOpacity
                activeOpacity={0.8}
                onPress={() => {
                  logEvent('CreateAccountBegin');
                  navigation.navigate('CreateAccount');
                  closeBottomSheet();
                }}
              >
                <Text
                  bold
                  size="m"
                  color="tertiary"
                  style={{ textAlign: 'center', marginVertical: theme.space.l }}
                >
                  <Text color="gray" size="m">
                    {formatMessage({
                      id: 'app.login.noAccount',
                    })}{' '}
                  </Text>
                  {formatMessage({
                    id: 'app.login.createAccount',
                  })}
                </Text>
              </TouchableOpacity>
            )}
          </View>
        </View>
      ) : (
        <View style={{ flex: 1, alignItems: 'center', justifyContent: 'center' }}>
          <Text
            bold
            size="s"
            color="attention"
            style={{
              textAlign: 'center',
              marginVertical: theme.space.l,
              marginHorizontal: theme.space.s,
            }}
          >
            {loginErrorMessage &&
              formatMessage({
                id: loginErrorMessage,
              })}
          </Text>
          <Button
            onPress={async () => {
              setLoginError(undefined);
              setLoginErrorMessage(undefined);
              initReadyTimer();
              sapLoginWebview?.current?.reload();
            }}
            style={{ backgroundColor: theme.colors.secondary, paddingHorizontal: theme.space.l }}
          >
            <Button.Text color="white">
              {formatMessage({
                id: 'app.key.again',
              })}
            </Button.Text>
          </Button>
        </View>
      )}
      <View style={{ width: VISIBLE_WEBVIEWS ? 100 : 1, height: VISIBLE_WEBVIEWS ? 100 : 1 }}>
        <SAPLoginWebview
          reference={sapLoginWebview}
          onSuccess={async (code) => {
            // Force the ready and processing state,
            // for when the browser session still has an active login
            setProcessing(true);
            setLoginReady(true);
            if (!code) {
              loginFailed('missing_code');
              return;
            }
            try {
              const tokenData = await getToken(code, Config.SAP_CDC_REDIRECT_URI, formik.values.email);
              logEvent('LoginSuccess');
              setToken(tokenData?.data?.userToken?.uuid);
            } catch (ex) {
              loginFailed('missing_token');
            }
          }}
          onReady={() => {
            setLoginReady(true);
          }}
          onError={() => {
            loginFailed('invalid_credentials');
          }}
        />
      </View>
    </View>
  );
};
export default Login;
