import React, { useRef, useState, useCallback, PropsWithChildren, useEffect } from 'react';

import { useContext } from 'react';
import AsyncStorage from '@react-native-async-storage/async-storage';
import { View, useWindowDimensions, Platform } from 'react-native';
import { useSafeAreaInsets } from 'react-native-safe-area-context';
import useTheme from '@app/hooks/useTheme';
import { AllColors } from '@app/theme/partials/general';
import useKeyboardHeight from '@app/hooks/useKeyboardHeight';
import Animated, {
  Easing,
  interpolate,
  useAnimatedStyle,
  useSharedValue,
  withTiming,
} from 'react-native-reanimated';
import BottomSheetHandle from './components/BottomSheetHandle';
import { LandalPressable } from '@app/components/LandalPressable';
import Icon from '@app/components/Icon';
import { ScrollView } from 'react-native-gesture-handler';
import Backdrop from './BottomSheetBackdrop';
import Delay from '@app/components/Delay';
import Loader from '@app/components/Loader';
import { BOTTOMSHEET_HEIGHT_CACHE } from '@app/config/StorageKeys';

const ENABLE_CACHE = true;
const TRANSITION_TIME = 400;

const CLOSE_VISUAL_SIZE = 24;
const FAKE_HITSLOP = 20;

interface ContextInterface {
  openBottomSheet: (config: BottomSheetConfigType) => void;
  closeBottomSheet: () => void;
  MAX_HEIGHT_CONTENT: number;
  clearBottomSheetPositionCache: () => void;
  getAmountOfBottomSheetPositionsCached: () => number;
}

export type BottomSheetConfigType = {
  key?: string;
  fullHeight?: boolean; // Please note that an automatic scrollview when content height exceeds screen height is not currently supported when using fullHeight.
  initialHeight?: number;
  component: React.FC | null;
  title?: string;
  panHandleOnly?: boolean;
  backgroundColor?: AllColors;
  scrollToEnd?: boolean;
  onClose?: () => void;
  delay?: number; // ms
  componentContainsScroll?: boolean;
};

const HANDLE_SIZE = 62;

type InternalBottomSheetConfigType = {
  autoHeight?: boolean; // This setting is used implicitly, when no fullHeight boolean is specified
} & BottomSheetConfigType;

type AnimatedGestureHandlerContext = {
  startFall: number;
  startBackdropOpacity: number;
};

export const BottomSheetContext = React.createContext<ContextInterface>({
  openBottomSheet: () => {},
  closeBottomSheet: () => {},
  MAX_HEIGHT_CONTENT: 0,
  clearBottomSheetPositionCache: () => {},
  getAmountOfBottomSheetPositionsCached: () => 0,
});

export const useBottomSheet = () => useContext(BottomSheetContext);
export const DefaultContent = () => <View />;

const defaultBottomSheetConfig: InternalBottomSheetConfigType = {
  component: DefaultContent,
  title: '',
  autoHeight: false,
  panHandleOnly: false,
  backgroundColor: 'white',
  scrollToEnd: false,
  onClose: undefined,
  fullHeight: false,
  delay: undefined,
  componentContainsScroll: false,
};

interface Props {}

const storeCache = async (values: {}) => {
  try {
    const json = JSON.stringify(values);
    await AsyncStorage.setItem(BOTTOMSHEET_HEIGHT_CACHE, json);
  } catch (e) {
    // saving error
  }
};

const BottomSheetProvider = ({ children }: PropsWithChildren<Props>) => {
  const theme = useTheme();
  const [key, setKey] = useState<number>(0);
  const { height: screenHeight, width: screenWidth } = useWindowDimensions();
  const { bottom: bottomInset, top: topInset } = useSafeAreaInsets();

  const scrollViewWrapperRef = useRef<View>(null);
  const scrollViewRef = useRef<ScrollView>(null);

  const [isOpen, setIsOpen] = useState(false);
  const [height, setHeight] = useState(0);

  const layoutIntervalRef = useRef<ReturnType<typeof setInterval>>();
  const requestedHeight = useRef<number>(0);
  const layoutIterator = useRef<number>(0);
  const previousIterator = useRef<number>(0);
  const keyboardIncludedContainerHeightRef = useRef<number | null>(null);

  const MAX_HEIGHT = screenHeight - theme.space.m - topInset;
  const MAX_HEIGHT_CONTENT = MAX_HEIGHT - HANDLE_SIZE;

  const positionsCache = useRef<{ [key: string]: number }>({});

  const clearBottomSheetPositionCache = async () => {
    try {
      await AsyncStorage.removeItem(BOTTOMSHEET_HEIGHT_CACHE);
      positionsCache.current = {};
    } catch (e) {
      // saving error
    }
  };
  const getAmountOfBottomSheetPositionsCached = () => Object.keys(positionsCache.current).length;

  useEffect(() => {
    // get stored cache-component dimensions
    const getCache = async () => {
      const json = await AsyncStorage.getItem(BOTTOMSHEET_HEIGHT_CACHE);
      if (json) {
        positionsCache.current = JSON.parse(json);
      }
    };
    getCache();
  }, []);

  const [keyboardIncludedContainerHeight, setKeyboardIncludedContainerHeight] = useState<number | null>(null);

  const fall = useSharedValue(-1);
  const backdropOpacity = useSharedValue(0);

  const bottomSheetConfig = useRef<InternalBottomSheetConfigType>(defaultBottomSheetConfig);
  const keyboardHeight = useKeyboardHeight();

  useEffect(() => {
    if (!isOpen) {
      return;
    }

    if (keyboardHeight > 0) {
      const definitiveBottomsheetHeight = Math.min(
        MAX_HEIGHT,
        requestedHeight.current + keyboardHeight + HANDLE_SIZE
      );

      const definitiveContainerHeight = definitiveBottomsheetHeight - HANDLE_SIZE - keyboardHeight;

      setKeyboardIncludedContainerHeight(definitiveContainerHeight);
      keyboardIncludedContainerHeightRef.current = definitiveBottomsheetHeight;
      setHeight(definitiveBottomsheetHeight);

      scrollViewWrapperRef.current?.setNativeProps({
        style: { height: definitiveContainerHeight },
      });

      setBottomSheetScroll(definitiveContainerHeight);

      if (bottomSheetConfig.current.scrollToEnd) {
        setTimeout(() => scrollViewRef.current?.scrollToEnd({ animated: true }), 200);
      }
    } else if (requestedHeight.current > 0) {
      setKeyboardIncludedContainerHeight(null);
      keyboardIncludedContainerHeightRef.current = null;
      let totalHeight = requestedHeight.current + HANDLE_SIZE + Math.max(bottomInset, theme.space.xl);
      const definitiveBottomsheetHeight = bottomSheetConfig.current.autoHeight
        ? Math.min(totalHeight, MAX_HEIGHT)
        : MAX_HEIGHT;

      const definitiveContentHeight = Math.min(requestedHeight.current, MAX_HEIGHT_CONTENT);
      setHeight(definitiveBottomsheetHeight);

      scrollViewWrapperRef.current?.setNativeProps({
        style: { height: definitiveContentHeight },
      });

      setBottomSheetScroll(MAX_HEIGHT_CONTENT);
    }
  }, [MAX_HEIGHT, MAX_HEIGHT_CONTENT, bottomInset, isOpen, keyboardHeight, theme.space.xl]);

  const setBottomSheetScroll = (scrollThreshold: number) => {
    if (bottomSheetConfig.current.componentContainsScroll) {
      return;
    }

    scrollViewRef.current?.setNativeProps({
      scrollEnabled: requestedHeight.current > scrollThreshold,
    });
  };

  const openBottomSheet = (config: BottomSheetConfigType) => {
    if (isOpen) {
      reset(); // reset, just to be sure the next attempt will succeed
      return;
    }
    bottomSheetConfig.current = config;
    bottomSheetConfig.current.autoHeight = !bottomSheetConfig.current.fullHeight;

    setKey((prevKey) => prevKey + 1);
    console.log('opened bottomsheet');
    setIsOpen(true);

    if (bottomSheetConfig.current.autoHeight) {
      if (ENABLE_CACHE && !!config.key && !!positionsCache.current?.[config.key]) {
        setHeight(positionsCache.current?.[config.key]);

        layoutIntervalRef.current = setInterval(performLayoutIntervalCheck, 500);
      } else if (config.initialHeight) {
        setHeight(config.initialHeight + bottomInset);

        layoutIntervalRef.current = setInterval(performLayoutIntervalCheck, 200);
      } else {
        layoutIntervalRef.current = setInterval(performLayoutIntervalCheck, 70);
      }
    } else {
      // fullHeight
      setHeight(MAX_HEIGHT);
      requestedHeight.current = MAX_HEIGHT;
    }

    // START ANIMATION
    // animate to 0 (open state)
    fall.value = withTiming(0, { duration: TRANSITION_TIME, easing: Easing.out(Easing.exp) });
    backdropOpacity.value = withTiming(0.7, {
      duration: TRANSITION_TIME * 0.8,
      easing: Easing.bezier(0.15, 0.1, 0.15, 1),
    });
  };

  const performLayoutIntervalCheck = () => {
    if (layoutIterator.current === 0 || requestedHeight.current === 0) {
      return;
    }

    // Wait for the view to be 'stable'; If there was no layout within the last x ms, set the new height.
    // This prevents jittery height animations.
    if (previousIterator.current === layoutIterator.current) {
      if (!keyboardIncludedContainerHeightRef.current) {
        let totalHeight = requestedHeight.current + HANDLE_SIZE + Math.max(bottomInset, theme.space.xl);

        const definitiveHeight = Math.min(totalHeight, MAX_HEIGHT);
        const definitiveContentHeight = Math.min(requestedHeight.current, MAX_HEIGHT_CONTENT);

        scrollViewWrapperRef.current?.setNativeProps({
          style: { height: definitiveContentHeight },
        });

        // Cache the bottomsheet height for this specific sheet content
        if (ENABLE_CACHE) {
          if (!!bottomSheetConfig.current?.key && !positionsCache.current?.[bottomSheetConfig.current.key]) {
            positionsCache.current[bottomSheetConfig.current.key] = definitiveHeight;
            storeCache(positionsCache.current);
          }
        }

        setBottomSheetScroll(MAX_HEIGHT_CONTENT);
        setHeight(definitiveHeight);

        // slow down interval since delay is acceptable for subsequent height changes
        if (layoutIntervalRef.current) {
          clearInterval(layoutIntervalRef.current);
        }
        layoutIntervalRef.current = setInterval(performLayoutIntervalCheck, 350);
      }
      layoutIterator.current = 0;
      previousIterator.current = 0;
      return;
    } else {
      //console.log('iterator', 'interrupted by another layout rendering...');
    }
    previousIterator.current = layoutIterator.current;
    return () => {
      if (layoutIntervalRef.current) {
        clearInterval(layoutIntervalRef.current);
      }
    };
  };

  const reset = useCallback(() => {
    requestedHeight.current = 0;
    layoutIterator.current = 0;
    previousIterator.current = 0;

    fall.value = -1;
    backdropOpacity.value = 0;

    // TODO: this reset of the component is disabled, as it caused some components not rendering again, when they were shown before.
    //bottomSheetConfig.current.component = () => <></>;

    keyboardIncludedContainerHeightRef.current = null;
    if (layoutIntervalRef.current) {
      clearInterval(layoutIntervalRef.current);
    }

    scrollViewWrapperRef.current?.setNativeProps({
      style: { height: undefined },
    });
    scrollViewRef.current?.setNativeProps({
      scrollEnabled: undefined,
    });

    setKeyboardIncludedContainerHeight(null);
    setIsOpen(false);

    setHeight(0);
  }, [backdropOpacity, fall]);

  const closeBottomSheet = () => {
    // animate to -1 (closed state)
    fall.value = withTiming(-1, { duration: TRANSITION_TIME, easing: Easing.out(Easing.exp) });
    backdropOpacity.value = withTiming(0, {
      duration: TRANSITION_TIME * 1.4,
      easing: Easing.out(Easing.exp),
    });
    setTimeout(() => {
      reset();
    }, TRANSITION_TIME);
  };

  const bottomSheetStyle = useAnimatedStyle(() => {
    const heightOverdrag = interpolate(fall.value, [-1, 0, 1], [0, 0, 60]);

    // heightOverdrag should not be within the withTiming, but it is not possible to sum the withTiming and interpolate output numbers.
    const heightWithTiming = withTiming(height + heightOverdrag, {
      duration: TRANSITION_TIME,
      easing: Easing.out(Easing.exp),
    });
    //const heightCombined = heightWithTiming + heightOverdrag;

    return {
      shadowOpacity: interpolate(fall.value, [-1, 0], [0, 0.3]),
      bottom: interpolate(fall.value, [-1, 0], [-height, 0]),
      height: isOpen ? heightWithTiming : 0,
    };
  });

  let Component = bottomSheetConfig.current.component;

  return (
    <BottomSheetContext.Provider
      value={{
        openBottomSheet,
        closeBottomSheet,
        MAX_HEIGHT_CONTENT,
        clearBottomSheetPositionCache,
        getAmountOfBottomSheetPositionsCached,
      }}
    >
      {children}
      <>
        <Backdrop fall={fall} backdropOpacity={backdropOpacity} closeBottomSheet={closeBottomSheet} />
        {/*
          // @ts-ignore: according to spec */}
        <Animated.View
          style={[
            {
              position: 'absolute',
              bottom: Platform.OS === 'web' ? (isOpen ? 0 : -height - 120) : -height,
              height: Platform.OS === 'web' && isOpen ? height : undefined,
              width: screenWidth,
              backgroundColor: bottomSheetConfig.current.backgroundColor
                ? theme.colors[bottomSheetConfig.current.backgroundColor]
                : theme.colors.white,
              zIndex: 21,
              shadowColor: '#000',
              shadowOffset: { width: 0, height: 2 },
              shadowRadius: 10,
              elevation: 15,
              borderTopLeftRadius: 12,
              borderTopRightRadius: 12,
            },
            Platform.OS !== 'web' && bottomSheetStyle,
          ]}
        >
          <>
            <BottomSheetHandle
              title={bottomSheetConfig.current.title}
              height={HANDLE_SIZE}
              backgroundColor={bottomSheetConfig.current.backgroundColor ?? 'white'}
              closeComponent={
                <LandalPressable
                  style={{
                    position: 'absolute',
                    right: theme.space.m,
                    elevation: 16,
                    padding: theme.space.s,
                    width: CLOSE_VISUAL_SIZE + FAKE_HITSLOP,
                    height: CLOSE_VISUAL_SIZE + FAKE_HITSLOP,
                    marginHorizontal: -FAKE_HITSLOP / 2,
                  }}
                  onPress={closeBottomSheet}
                  //hitSlop={{ top: 16, bottom: 16, left: 16, right: 20 }} We disabled this hitlop, because it is not working on Android with our pressable implementation.
                >
                  <Icon name="close" size="m" color="black" />
                </LandalPressable>
              }
            />
            {isOpen && !!Component && (
              <Delay
                key={key}
                ms={bottomSheetConfig.current.delay || TRANSITION_TIME * 0.35}
                placeholder={
                  bottomSheetConfig.current.delay ? (
                    <View style={{ flex: 1, justifyContent: 'center', alignItems: 'center' }}>
                      <Loader />
                    </View>
                  ) : (
                    <></>
                  )
                }
              >
                <View
                  ref={scrollViewWrapperRef}
                  style={{
                    flex: 1,
                    ...(keyboardIncludedContainerHeight && {
                      maxHeight: keyboardIncludedContainerHeight,
                    }),
                    /* height will be set via native props */
                  }}
                >
                  {/* scrollEnabled will be set using setNativeProps */}
                  <ScrollView
                    ref={scrollViewRef}
                    style={{ flex: 1 }}
                    contentContainerStyle={{ flex: bottomSheetConfig.current.autoHeight ? 0 : 1 }}
                    scrollEnabled={false}
                    // onStartShouldSetResponder={() => true}
                    // onStartShouldSetResponderCapture={() => true}
                    // onMoveShouldSetResponderCapture={() => true}
                    // onMoveShouldSetResponder={() => true}
                  >
                    <View
                      style={{
                        flex: bottomSheetConfig.current.autoHeight ? 0 : 1,
                      }}
                      onLayout={(event) => {
                        if (!bottomSheetConfig.current.autoHeight) {
                          return;
                        }
                        var { height: computedHeight } = event.nativeEvent.layout;
                        if (computedHeight <= 0) {
                          return;
                        }

                        requestedHeight.current = computedHeight;
                        layoutIterator.current = layoutIterator.current + 1;
                      }}
                    >
                      <Component />
                    </View>
                  </ScrollView>
                </View>
              </Delay>
            )}
          </>
        </Animated.View>
      </>
    </BottomSheetContext.Provider>
  );
};

export default BottomSheetProvider;
