import { TravelCompanionAgeCategory } from 'apis/_generated-reservation-api/models';
import { IntlFormatters } from 'react-intl';

export const getAgeCategoryText = (
  formatMessage: IntlFormatters['formatMessage'],
  ageCategory?: TravelCompanionAgeCategory,
  minAge?: number,
  maxAge?: number
) => {
  let result = '';
  if (!minAge && !maxAge) return result;

  switch (ageCategory) {
    case 'ADULT':
      result = formatMessage({ id: 'app.general.ageCategory.adult' }) + ` ${minAge}+`;
      return result;
    case 'CHILD':
      result = formatMessage({ id: 'app.general.ageCategory.child' });
      break;
    case 'BABY':
      result = formatMessage({ id: 'app.general.ageCategory.baby' });
      break;
  }
  result += ' ' + minAge + ` ${formatMessage({ id: 'app.general.ageInYears.to' })} ` + maxAge + ' ';
  if ((maxAge || 2) > 1) {
    result += formatMessage({ id: 'app.general.ageInYears.plural' });
  } else {
    result += formatMessage({ id: 'app.general.ageInYears.singular' });
  }
  return result;
};
