import Button from '@app/components/Button';
import Text from '@app/components/Text';
import { UserContext } from '@app/context/usercontext/UserContextProvider';
import useTheme from '@app/hooks/useTheme';
import useMetrics from '@app/metrics/useMetrics';
import { useGetPark } from '@domains/park/contact-and-info/queries/useGetPark';
import { useGetReservation } from '@domains/reservations/reservation/queries/useGetReservation';
import { useFocusEffect } from '@react-navigation/native';
import React, { useContext } from 'react';
import { useIntl } from 'react-intl';
import { Linking, View } from 'react-native';
import { getBrand } from 'utils/brand';
import ScreenScrollContainer from '@app/components/ScreenScrollContainer';
import StickyHeader from '@app/components/StickyHeader';
import ExtrasSlider from '../components/ExtrasSlider';
import Shadow from '@app/components/Shadow';
import { NavScreenProp } from '@app/navigation/stacks/Main';

export default function Products({ navigation, route }: NavScreenProp<'Products'>) {
  const theme = useTheme();
  const { token } = useContext(UserContext);
  const { logScreenView, logEvent } = useMetrics();
  const { formatMessage } = useIntl();

  useFocusEffect(
    React.useCallback(() => {
      logScreenView('Products');
      logEvent('ExtraList');
    }, [logEvent, logScreenView])
  );

  const bookingReference = route?.params?.bookingReference;

  const { data: reservation } = useGetReservation({
    token,
    brand: getBrand(theme),
    bookingReference,
  });

  const { data: reservationPark } = useGetPark({ parkCodeOverride: reservation?.parkCode });

  return (
    <>
      <StickyHeader title={`Extra's & vouchers`} showClose navigation={navigation} />
      <ScreenScrollContainer topPadding={false}>
        <ExtrasSlider bookingReference={bookingReference} />
        <View>
          <View>
            <Text type="title" style={{ marginTop: theme.space.m }} bold size="l">
              {formatMessage({ id: 'app.extra.noExtras' })}
            </Text>
            <View
              style={{
                paddingVertical: theme.space.l,
              }}
            >
              <Text color="dark" style={{ lineHeight: 26 }}>
                {reservation?.holidayStarted
                  ? formatMessage({ id: 'app.extra.addFootnoteDuringHoliday' })
                  : formatMessage({ id: 'app.extra.addFootnoteBeforeHoliday' })}
              </Text>
            </View>
            <View>
              <Shadow>
                <Button
                  onPress={() => {
                    if (reservation?.holidayStarted) {
                      logEvent('ExtraAddExtras', { method: 'phone' });
                      Linking.openURL('tel:' + reservationPark?.phone);
                    } else {
                      if (reservation?.pendingPaymentUrl) {
                        logEvent('ExtraAddExtras', { method: 'web' });
                        Linking.openURL(reservation?.pendingPaymentUrl);
                      }
                    }
                  }}
                  style={{
                    flex: 1,
                    backgroundColor: theme.colors.white,
                  }}
                >
                  <Button.Text color="tertiary">
                    {reservation?.holidayStarted
                      ? formatMessage({ id: 'app.extra.callReception' })
                      : formatMessage({ id: 'app.extra.add' })}
                  </Button.Text>
                  <Button.Icon name="exit-app" color="tertiary" />
                </Button>
              </Shadow>
            </View>
          </View>
        </View>
      </ScreenScrollContainer>
    </>
  );
}
