import Button from '@app/components/Button';
import FormControl from '@app/components/Form/FormControl';
import StickyHeader from '@app/components/StickyHeader';
import Text from '@app/components/Text';
import useCulture from '@app/hooks/useCulture';
import useTheme from '@app/hooks/useTheme';
import useMetrics from '@app/metrics/useMetrics';
import { NavScreenProp } from '@app/navigation/stacks/Main';
import React, { useEffect, useState } from 'react';
import { useIntl } from 'react-intl';
import { Alert, View } from 'react-native';
import { SafeAreaView } from 'react-native-safe-area-context';
import { isValidEmail } from 'utils/validation';
import { useRequestPasswordReset } from '../queries/useRequestPasswordReset';

const ForgotPassword = ({ route, navigation }: NavScreenProp<'ForgotPassword'>) => {
  const theme = useTheme();
  const { formatMessage } = useIntl();
  const [email, setEmail] = useState(route?.params?.email ?? '');
  const [processing, setProcessing] = useState(false);
  const { requestPasswordReset } = useRequestPasswordReset();
  const { cultureCode } = useCulture();
  const { logEvent } = useMetrics();

  const isChange = route?.params?.isChange;

  useEffect(() => {
    if (isChange) {
      logEvent('SettingsChangePasswordBegin');
    }
  }, [isChange, logEvent]);

  return (
    <SafeAreaView style={{ flex: 1, backgroundColor: theme.colors.white }} edges={['bottom']}>
      <StickyHeader
        title={
          isChange
            ? formatMessage({ id: 'app.profile.account.loginData.change' })
            : formatMessage({ id: 'app.login.forgotPassword' })
        }
        showClose
        navigation={navigation}
      />
      <View
        style={{
          flex: 1,
          margin: theme.space.l,
          marginHorizontal: theme.space.xl,
        }}
      >
        <Text style={{ marginBottom: theme.space.l }}>
          {formatMessage({
            id: 'app.login.forgotPassword.text',
          })}
        </Text>
        <FormControl
          label={formatMessage({
            id: 'app.login.emailPlaceholder',
          })}
          value={email}
          isValid={!email}
          onChangeText={(text) => {
            setEmail(text);
          }}
          returnKeyType="next"
          autoCapitalize="none"
          autoCorrect={false}
          textContentType="username"
        />
        <View style={{ width: '100%' }}>
          <Button
            onPress={async () => {
              if (processing) {
                return;
              }
              setProcessing(true);
              try {
                const result = await requestPasswordReset(email.trim(), cultureCode ?? '');
                if (result.status === 200) {
                  Alert.alert(
                    '',
                    formatMessage({
                      id: 'app.login.forgotPassword.success',
                    })
                  );
                  if (isChange) {
                    logEvent('SettingsChangePasswordComplete');
                  } else {
                    logEvent('RequestPasswordResetComplete');
                  }
                  navigation.goBack();
                } else {
                  Alert.alert(
                    '',
                    formatMessage({
                      id: 'app.login.forgotPassword.failed',
                    })
                  );
                  if (isChange) {
                    logEvent('SettingsChangePasswordError', { statusCode: result.status });
                  } else {
                    logEvent('RequestPasswordResetError', { statusCode: result.status });
                  }
                }
              } catch (e) {
                Alert.alert(
                  '',
                  formatMessage({
                    id: 'app.login.forgotPassword.failed',
                  })
                );
                if (isChange) {
                  logEvent('SettingsChangePasswordError');
                } else {
                  logEvent('RequestPasswordResetError');
                }
              }
              setProcessing(false);
            }}
            style={{
              backgroundColor: theme.colors.secondary,
              marginTop: theme.space.s,
            }}
            disabled={processing || !email || !isValidEmail(email?.trim())}
          >
            {processing ? (
              <Button.ActivityIndicator style={{ height: 22 }} />
            ) : (
              <Button.Text color="white">
                {formatMessage({
                  id: 'app.general.send',
                })}
              </Button.Text>
            )}
          </Button>
        </View>
      </View>
    </SafeAreaView>
  );
};
export default ForgotPassword;
