import useCulture from '@app/hooks/useCulture';
import { FacilityBookRequest, FacilityBookResponse } from 'apis/_generated-park-api/models';
import { AxiosError } from 'axios';
import { gql, GraphQLClient } from 'graphql-request';
import Config from 'react-native-config';
import { UseQueryOptions, useMutation } from 'react-query';

type BookFacilityProps = {
  options?: UseQueryOptions<FacilityBookResponse, AxiosError>;
};

export function useBookFacility(props?: BookFacilityProps) {
  const { reserveLanguageCode } = useCulture();
  const facilityBookingMutation = useMutation(async (request: FacilityBookRequest) => {
    const graphQLClient = new GraphQLClient(Config.PARK_API_URL);
    const mutation = gql`
      mutation bookFacility($request: FacilityBookRequest){
        bookFacility(bookRequest: $request, languageCode: ${reserveLanguageCode}) {
          status
          messages
        }
      }
    `;
    const result = await graphQLClient.rawRequest<{ bookFacility: FacilityBookResponse }>(mutation, {
      request,
    });
    return result?.data?.bookFacility;
  }, props?.options);

  const postBooking = async (booking: FacilityBookRequest) => {
    const bookingResult = await facilityBookingMutation.mutateAsync(booking);
    return bookingResult;
  };

  return { postBooking };
}
