import React from 'react';
import { Linking, View } from 'react-native';
import {
  LinkingOptions,
  NavigationContainer,
  NavigationContainerRef,
  RouteProp,
} from '@react-navigation/native';
import { NativeStackNavigationProp } from '@react-navigation/native-stack';
import { UserContext } from '@app/context/usercontext/UserContextProvider';
import AppLoading from '../AppLoading';
import { ScreenTrackingContextUpdater } from '../ScreenTrackingProvider';
import DeviceInfo from 'react-native-device-info';
import { DeeplinkUpdater } from '@app/context/DeeplinkProvider';
import AppBlocked from '@domains/start/screens/AppBlocked';
import AppDeprecated from '@domains/start/screens/AppDeprecated';
import useBlockAppOnDeprecatedVersion from '@app/hooks/useBlockAppOnDeprecatedVersion';
import useBlockAppOnBlockingError from '@app/hooks/useBlockAppOnBlockingError';
import usePushNotifications from '@app/hooks/usePushNotifications';
import { ProfileRoutes, useProfileScreens } from './Profile';
import { ReservationsRoutes, useReservationScreens } from './Reservations';
import { StartRoutes } from './Start';
import 'dayjs/locale/nl';
import BottomSheetProvider from '../bottom-sheet/BottomSheetProvider';
import { ActivitiesRoutes } from './park/Activities';
import { FoodAndDrinksRoutes } from './park/FoodAndDrinks';
import { InTheRegionRoutes } from './park/InTheRegion';
import useRefetchActiveReservationOnAppStateChange from '@app/hooks/useRefetchActiveReservationOnAppStateChange';
import { AtTheParkRoutes, useAtTheParkScreens } from './park/AtThePark';
import { useActivitiesScreens } from './park/Activities';
import { useInTheRegionScreens } from './park/InTheRegion';
import { useFoodAndDrinksScreens } from './park/FoodAndDrinks';
import useTheme from '@app/hooks/useTheme';
import FeedbackConfirmation from '@domains/profile/screens/FeedbackConfirmation';
import CheckoutNowConfirmation from '@domains/sheets/CheckoutNowConfirmation';
import useSurvicate from '@app/hooks/useSurvicate';
import Notifications from '@domains/notification/screens/Notifications';
import NotificationDetail from '@domains/notification/screens/NotificationDetail';
import PopupProvider from '../popup/PopupProvider';
import BottomTabs from '../BottomTabs';
import { useIntl } from 'react-intl';
import { createDrawerNavigator } from '@react-navigation/drawer';

// Types of params passed to route via navigation.navigate('SCREEN_NAME', params)
export type MainRoutes = {
  BottomTabs: undefined;
  AppLoading: undefined;
  Onboarding: { changedPark: boolean; changedCulture: boolean };
  AppBlocked: undefined;
  AppDeprecated: undefined;
  FeedbackConfirmation: undefined;
  CheckoutNowConfirmation: undefined;
};

export type AllRoutes = MainRoutes &
  StartRoutes &
  ReservationsRoutes &
  ProfileRoutes &
  ActivitiesRoutes &
  FoodAndDrinksRoutes &
  AtTheParkRoutes &
  InTheRegionRoutes;

export type RouteNames = keyof AllRoutes;
export interface NavScreenProp<T extends RouteNames> {
  navigation: NativeStackNavigationProp<AllRoutes, T>;
  route: RouteProp<AllRoutes, T>;
}

const MainStack = createDrawerNavigator<MainRoutes>();

export default function Main() {
  const { userState } = React.useContext(UserContext);
  const setDeeplink = React.useContext(DeeplinkUpdater);
  const routeNameRef = React.useRef<string>();
  const navigationRef = React.useRef<NavigationContainerRef<AllRoutes>>(null);
  const updateScreenName = React.useContext(ScreenTrackingContextUpdater);
  useBlockAppOnDeprecatedVersion({ navigationRef });
  useBlockAppOnBlockingError({ navigationRef, routeName: routeNameRef.current });
  usePushNotifications({ navigationRef });
  useRefetchActiveReservationOnAppStateChange();
  const { formatMessage } = useIntl();
  const theme = useTheme();

  let bundleId = DeviceInfo.getBundleId();

  if (__DEV__) {
    bundleId = 'com.landal.parkapp';
  }

  const prefix = `${bundleId}://`;

  const linking: LinkingOptions<any> = {
    prefixes: [prefix],
    config: {
      initialRouteName: 'PaymentConfirmation',
      screens: {
        PaymentConfirmation: {
          path: 'www.landal.com/shop/:shopId?payment=:status', //:status
          parse: {
            shopId: String,
            status: String,
          },
        },
      },
    },
    async getInitialURL() {
      const url = await Linking.getInitialURL();
      if (url) {
        setDeeplink(url);
      }
      return null;
    },
    subscribe(listener) {
      const onReceiveURL = ({ url }: { url: string }) => {
        listener(url);
      };
      Linking.addEventListener('url', onReceiveURL);

      return () => {
        Linking.removeEventListener('url', onReceiveURL);
      };
    },
  };

  const atTheParkScreens = useAtTheParkScreens(MainStack);
  const activitiesScreens = useActivitiesScreens(MainStack);
  const inTheRegionScreens = useInTheRegionScreens(MainStack);
  const foodAndDrinksScreens = useFoodAndDrinksScreens(MainStack);
  const reservationScreens = useReservationScreens(MainStack);
  const profileScreens = useProfileScreens(MainStack);

  const { leaveScreen, enterScreen } = useSurvicate();

  return (
    <NavigationContainer
      ref={navigationRef}
      linking={linking}
      onReady={() => {
        if (navigationRef.current) {
          routeNameRef.current = navigationRef.current.getCurrentRoute()?.name;
        }
      }}
      onStateChange={() => {
        if (navigationRef.current) {
          const previousRouteName = routeNameRef.current;
          const currentRoute = navigationRef.current.getCurrentRoute() as any;
          if (previousRouteName !== currentRoute?.name && currentRoute?.name) {
            updateScreenName(currentRoute);
          }
          leaveScreen(routeNameRef.current || '');
          routeNameRef.current = currentRoute?.name;
          enterScreen((currentRoute?.name || '').toString());
        }
      }}
    >
      <PopupProvider>
        <BottomSheetProvider>
          <View
            style={{
              flex: 1,
            }}
          >
            <MainStack.Navigator
              backBehavior="history"
              screenOptions={{
                headerShown: false,
                orientation: 'portrait_up',
                contentStyle: {
                  overflow: 'hidden',
                  backgroundColor: theme.colors.canvas,
                },
              }}
            >
              {userState === 'initial' && <MainStack.Screen name="AppLoading" component={AppLoading} />}
              <MainStack.Screen
                name="BottomTabs"
                component={BottomTabs}
                options={{
                  headerShown: false,
                  animation: 'fade',
                }}
              />

              {reservationScreens}
              {atTheParkScreens}
              {activitiesScreens}
              {inTheRegionScreens}
              {foodAndDrinksScreens}
              {profileScreens}

              <MainStack.Screen
                name="Notifications"
                component={Notifications}
                options={{ presentation: 'fullScreenModal', animation: 'default' }}
              />
              <MainStack.Screen
                name="NotificationDetail"
                component={NotificationDetail}
                options={{ presentation: 'fullScreenModal', animation: 'default' }}
              />
              <MainStack.Screen
                name="AppBlocked"
                component={AppBlocked}
                options={{ presentation: 'fullScreenModal', animation: 'default' }}
              />
              <MainStack.Screen
                name="AppDeprecated"
                component={AppDeprecated}
                options={{ presentation: 'fullScreenModal', animation: 'default' }}
              />
              <MainStack.Screen
                name="FeedbackConfirmation"
                component={FeedbackConfirmation}
                options={{ presentation: 'fullScreenModal', animation: 'default' }}
              />
              <MainStack.Screen
                name="CheckoutNowConfirmation"
                component={CheckoutNowConfirmation}
                options={{ presentation: 'fullScreenModal', animation: 'default' }}
              />
            </MainStack.Navigator>
          </View>
        </BottomSheetProvider>
      </PopupProvider>
    </NavigationContainer>
  );
}
