import React, { Dispatch, SetStateAction, useState } from 'react';
import { useWindowDimensions, View } from 'react-native';
import Text from '@app/components/Text';
import Icon from '@app/components/Icon';
import useTheme from '@app/hooks/useTheme';
import { spaceDefaults } from '@app/theme/partials/sizes';
import {
  BackdropBlur,
  Fill,
  SkiaValue,
  useComputedValue,
  useSharedValueEffect,
  useValue,
} from '@shopify/react-native-skia';
import { SharedValue, useSharedValue } from 'react-native-reanimated';

export const BADGE_HEIGHT = 26;

interface Props {
  text?: string;
  icon?: string;
  borderRadius?: number; // default = round
  space?: keyof typeof spaceDefaults;
  fullWidth?: boolean;
  y?: number;
  containerSize?: number;
  blur?: SkiaValue<number>;
  opacity?: SkiaValue<number>;
  scrollOffset?: SharedValue<number>;
}

// This hook provides both a react badge, and a Skia badge.
// This is because the badge background is a blurred transparency on the image it overlays, and needs to be drawn in a Skia canvas.
// The react-side of the badge however, which includes the text and icon, should be rendered separately outside the canvas.
export default function useBadge({
  text,
  icon,
  borderRadius,
  space,
  fullWidth,
  y,
  containerSize,
  blur,
  opacity,
  scrollOffset,
}: Props) {
  const theme = useTheme();
  const { width: screenWidth } = useWindowDimensions();
  const [badgeWidth, setBadgeWidth] = useState(0);

  if (!space) space = 'm';
  if (!borderRadius) borderRadius = BADGE_HEIGHT / 2;

  const maxBadgeSize = screenWidth - theme.space[space] * 2;
  const computedBadgeWidth = Math.min(maxBadgeSize, badgeWidth);

  if (computedBadgeWidth === maxBadgeSize) fullWidth = true;
  const alignRight = !!icon && !text;

  const reactBadge = (
    <ReactBadge
      text={text}
      icon={icon}
      badgeWidth={badgeWidth}
      setBadgeWidth={setBadgeWidth}
      borderRadius={borderRadius}
      fullWidth={fullWidth}
      alignRight={alignRight}
      space={space}
      y={y}
    />
  );
  const skiaBadge = (
    <SkiaBadge
      y={y}
      computedBadgeWidth={computedBadgeWidth}
      space={space}
      borderRadius={borderRadius}
      alignRight={alignRight}
      containerSize={containerSize}
      blur={blur}
      opacity={opacity}
      scrollOffset={scrollOffset}
    />
  );

  return {
    reactBadge,
    skiaBadge,
  };
}

interface ReactBadgeProps {
  text?: string;
  icon?: string;
  badgeWidth: number;
  setBadgeWidth: Dispatch<SetStateAction<number>>;
  borderRadius: number;
  space: keyof typeof spaceDefaults;
  fullWidth?: boolean;
  alignRight?: boolean;
  y?: number;
}
export function ReactBadge({
  badgeWidth,
  setBadgeWidth,
  text,
  icon,
  borderRadius,
  space,
  fullWidth,
  alignRight,
  y,
}: ReactBadgeProps) {
  const theme = useTheme();

  return (
    <View
      style={{
        position: 'absolute',
        borderRadius,
        flexDirection: 'row',
        alignItems: 'center',
        justifyContent: 'center',
        left: alignRight ? undefined : theme.space[space],
        right: fullWidth || alignRight ? theme.space[space] : undefined,
        bottom: y ? undefined : theme.space[space],
        top: y ? y : undefined,
        height: BADGE_HEIGHT,
        width: alignRight ? BADGE_HEIGHT : undefined,
        paddingHorizontal: theme.space[space],
      }}
      onLayout={(event) => {
        var { width: layoutWidth } = event.nativeEvent.layout;
        if (badgeWidth || layoutWidth <= 0) return;
        setBadgeWidth(layoutWidth);
      }}
    >
      {!!icon && <Icon name={icon} size="s" color={'white'} />}
      {!!text && (
        <Text
          color="white"
          bold
          size="s"
          numberOfLines={1}
          ellipsizeMode="tail"
          style={{
            marginLeft: theme.space.s,
          }}
        >
          {text}
        </Text>
      )}
    </View>
  );
}

interface SkiaBadgeProps {
  y?: number;
  borderRadius: number;
  computedBadgeWidth: number;
  space: keyof typeof spaceDefaults;
  alignRight: boolean;
  containerSize?: number;
  scrollOffset?: SharedValue<number>;
}

export function SkiaBadge({
  y,
  computedBadgeWidth,
  space,
  borderRadius,
  alignRight,
  containerSize,
  scrollOffset,
}: SkiaBadgeProps) {
  const theme = useTheme();

  const badgeY = useValue(0);
  const valueFallback = useSharedValue(0);

  useSharedValueEffect(() => {
    badgeY.current = scrollOffset?.value ?? 0;
  }, scrollOffset || valueFallback);

  const transform = useComputedValue(() => [{ translateY: Math.max(badgeY?.current / 2, 0) ?? 0 }], [badgeY]);

  const blurr = useComputedValue(
    () => ((badgeY?.current ?? 0) < 0 ? 4 + (badgeY?.current ?? 0) / 4 : 4 - (badgeY?.current ?? 0) / 20),
    [badgeY]
  );

  const opacityy = useComputedValue(
    () =>
      (badgeY?.current ?? 0) < 0 ? 0.2 + (badgeY?.current ?? 0) / 20 : 0.2 - (badgeY?.current ?? 0) / 100,
    [badgeY]
  );

  if (!y || !computedBadgeWidth) return null;

  return (
    <BackdropBlur
      blur={blurr || 4}
      clip={{
        rect: {
          x: alignRight
            ? (containerSize ?? 200) - computedBadgeWidth - theme.space[space] + 1
            : theme.space[space],
          y,
          width: computedBadgeWidth,
          height: BADGE_HEIGHT,
        },
        rx: borderRadius,
        ry: borderRadius,
      }}
      transform={transform}
    >
      <Fill color={theme.colors.shade} opacity={opacityy} />
    </BackdropBlur>
  );
}
