import React from 'react';
import { useAnimatedScrollHandler, useSharedValue } from 'react-native-reanimated';
import useTheme from '@app/hooks/useTheme';
import TitleAndDescription from '@app/components/DetailScreen/TitleAndDescription';
import ActivityNavigation from '@domains/park/activities/components/ActivityNavigation';
import Button from '@app/components/Button';
import OpeningHours from '@app/components/DetailScreen/OpeningHours';
import useMetrics from '@app/metrics/useMetrics';
import { useFocusEffect } from '@react-navigation/native';
import { Openinghour } from 'apis/_generated-park-api/models';
import StickyImageHeader, { useStickyImageHeaderDimensions } from '@app/components/StickyImageHeader';
import ScreenScrollContainer from '@app/components/ScreenScrollContainer';
import StickyBottomBar from '@app/navigation/StickyBottomBar';
import { View } from 'react-native';
import { useIntl } from 'react-intl';
import { NavScreenProp } from '@app/navigation/stacks/Main';

export default function AtTheParkDetail({ navigation, route }: NavScreenProp<'AtTheParkDetail'>) {
  const theme = useTheme();
  const { formatMessage } = useIntl();
  const { logScreenView } = useMetrics();

  const facility = route?.params?.facility;
  const parkCode = route?.params?.parkCode;

  const { EXPANDED_HEADER_HEIGHT, size } = useStickyImageHeaderDimensions('m');
  const translationY = useSharedValue(0);
  const scrollHandler = useAnimatedScrollHandler((event) => {
    translationY.value = event.contentOffset.y;
  });

  useFocusEffect(
    React.useCallback(() => {
      if (facility?.id) {
        logScreenView('AtTheParkDetail', [facility.id], { location: facility.name ?? '' });
      }
    }, [facility, logScreenView])
  );

  return (
    <>
      <StickyImageHeader
        scrollOffset={translationY}
        title={facility?.name}
        images={[facility?.media]}
        size={size}
      />
      <ScreenScrollContainer
        onScroll={scrollHandler}
        animated
        contentContainerStyle={{ paddingTop: EXPANDED_HEADER_HEIGHT }}
      >
        <TitleAndDescription description={facility?.description} />
        <OpeningHours openingHours={(facility?.openingHours ?? []) as Openinghour[]} />
        <ActivityNavigation parkCode={parkCode} facilityId={facility?.id} />
      </ScreenScrollContainer>
      {!!facility?.reservationId && (
        <StickyBottomBar style={{ padding: theme.space.l, justifyContent: 'center' }}>
          <View style={{ flex: 1 }}>
            <Button
              onPress={() => {
                navigation.navigate('FacilityBooking', { reservationId: facility?.reservationId ?? '' });
              }}
              style={{
                backgroundColor: theme.colors.secondary,
              }}
            >
              <Button.Icon name="reserve" size="s" color={'white'} />
              <Button.Text color="white">{formatMessage({ id: 'app.general.book' })}</Button.Text>
            </Button>
          </View>
        </StickyBottomBar>
      )}
    </>
  );
}
