import React, { PropsWithChildren } from 'react';
import { Linking, Text as RNText, TextProps as RNTextProps, TextStyle } from 'react-native';
import useTheme from '@app/hooks/useTheme';
import { fontSizes } from '@app/theme/partials/sizes';
import ParsedText from 'react-native-parsed-text';
import Autolink from 'react-native-autolink';
import { AllColors } from '@app/theme/partials/general';
import useMetrics from '@app/metrics/useMetrics';

export interface TextProps extends PropsWithChildren<RNTextProps> {
  size?: 's' | 'm' | 'l' | 'xl' | 'xxl';
  weight?: 'regular';
  type?: 'body' | 'title';
  color?: AllColors;
  center?: boolean;
  useAutoLinking?: boolean;
  useParser?: boolean;
  styleParsed?: TextStyle;
}

interface Props extends TextProps {
  bold?: boolean;
}
export default function Text({
  style,
  center,
  color = 'black',
  size = 'm',
  type = 'body',
  children,
  useAutoLinking,
  useParser,
  styleParsed,
  bold,
  ...rest
}: Props) {
  const theme = useTheme();
  const { logEvent } = useMetrics();

  const types = {
    body: {
      ...fontSizes.m,
      fontFamily: theme.fonts.bodyRegular,
      fontColor: theme.colors.black,
    },
    title: {
      ...fontSizes.m,
      fontFamily: theme.fonts.headingRegular,
      fontColor: theme.colors.black,
    },
  };

  let fontFamily: string = theme.fonts.bodyRegular;
  let fontColor: string = theme.colors.black;
  let fontSize: number = fontSizes.m.fontSize;
  let lineHeight: number = fontSizes.m.lineHeight;
  let fontWeight = 'normal';

  const allTypes = types;
  if (type && allTypes[type]) {
    fontFamily = allTypes[type].fontFamily;
  }
  if (size) {
    fontSize = fontSizes[size].fontSize;
    lineHeight = fontSizes[size].lineHeight;
  }
  if (bold) {
    switch (type) {
      case 'body':
        if (fontFamily === 'Arial') {
          fontWeight = 'bold';
        } else {
          fontFamily = theme.fonts.bodyBold;
          fontWeight = theme.fonts.bodyBoldWeight;
        }
        break;
      case 'title':
        fontFamily = theme.fonts.headingBold;
        fontWeight = theme.fonts.headingBoldWeight;
        break;
    }
  }
  if (color) {
    fontColor = theme.colors[color];
  }

  const styles = [
    {
      fontSize,
      lineHeight,
      fontFamily,
      fontWeight,
      color: fontColor,
    },
    style,
    center && { textAlign: 'center' },
  ] as TextStyle[];

  if (useAutoLinking) {
    return (
      <Autolink
        text={children?.toString() || ''}
        style={styles}
        linkStyle={{ color: theme.colors.tertiary }}
        phone
        allowFontScaling={false}
        onPress={(url) => {
          try {
            logEvent('ExternalLink', {
              link: url,
              location: 'text',
            });
            Linking.openURL(url);
          } catch {}
        }}
      />
    );
  }
  if (useParser) {
    const renderText = (matchingString: string) => {
      return matchingString.replace(/(\*)/g, '');
    };
    return (
      <ParsedText
        style={styles}
        allowFontScaling={false}
        parse={[
          {
            pattern: /(\*)\w+(\*)/g,
            style: [styles, { fontFamily: theme.fonts.headingBold }, styleParsed],
            renderText,
          },
        ]}
      >
        {children?.toString() || ''}
      </ParsedText>
    );
  }

  return (
    <RNText style={styles} allowFontScaling={false} {...rest}>
      {children}
    </RNText>
  );
}
