import React from 'react';
import { createIconSetFromIcoMoon } from 'react-native-vector-icons';
import icoMoonConfig from '@assets/fonts/selection.json';
import { IconProps as RNIconProps } from 'react-native-vector-icons/Icon';
import { AllColors } from '@app/theme/partials/general';
import useTheme from '@app/hooks/useTheme';
import { View } from 'react-native';
import LinearGradient from 'react-native-linear-gradient';
const Icon = createIconSetFromIcoMoon(icoMoonConfig);

export type IconSize = keyof typeof iconSizes;

export interface IconProps extends Omit<RNIconProps, 'size'> {
  size?: IconSize;
  color?: AllColors;
  showBadge?: boolean;
}

export const iconSizes = {
  xs: 12,
  s: 16,
  m: 20,
  l: 24,
  xl: 28,
  xxl: 36,
  xxxl: 48,
} as const;

const BADGE_BORDER_SIZE = 2;
const READ_DOT_SIZE = 6 + BADGE_BORDER_SIZE * 2;

export default function CustomIcon({
  style,
  size = 'm',
  color = 'black',
  showBadge = false,
  ...props
}: IconProps) {
  const theme = useTheme();

  return (
    <View>
      <Icon style={style} size={iconSizes[size]} color={theme.colors[color]} {...props} />
      {showBadge && (
        <LinearGradient
          colors={[theme.colors.gradientDanger1, theme.colors.gradientDanger2]}
          style={[
            {
              backgroundColor: 'transparent', // required for gradient
              width: READ_DOT_SIZE,
              height: READ_DOT_SIZE,
              borderRadius: READ_DOT_SIZE / 2,
              position: 'absolute',
              right: -1,
              top: -1,
              alignSelf: 'flex-end',
              borderWidth: BADGE_BORDER_SIZE,
              borderColor: theme.colors.white,
            },
            style,
          ]}
          angle={120}
          useAngle
        />
      )}
    </View>
  );
}
