import { AccoGeo } from 'apis/_generated-park-api/models';
import { gql, GraphQLClient } from 'graphql-request';
import Config from 'react-native-config';

type Props = {
  selectedParkCode: string;
};

export function useGetAccommodationGeo({ selectedParkCode }: Props) {
  const getAcco = async (accommodationNumber: string) => {
    const graphQLClient = new GraphQLClient(Config.PARK_API_URL);
    const query = gql`
          query accoGeo {
            accoGeo(parkCode: "${selectedParkCode}", accommodationNumber: "${accommodationNumber}") {
              houseNumber,
              lat,
              lon
            }
          }
        `;
    const result = await graphQLClient.rawRequest(query);
    return result.data?.accoGeo as AccoGeo;
  };
  return getAcco;
}
