export type ImageSize = 's' | 'm' | 'l' | 'xl' | 'xxl';

export const getImageUrlWithWidth = (url?: string, size?: ImageSize) => {
  if (!url) return undefined;
  if (!size) return url;
  let width = 80;

  switch (size) {
    case 's':
      width = 80;
      break;
    case 'm':
      width = 161;
      break;
    case 'l':
      width = 320;
      break;
    case 'xl':
      width = 640;
      break;
    case 'xxl':
      width = 1280;
      break;
  }
  return `${url}?w=${width}`;
};
