import { saltoAPI } from '@app/config/Endpoints';
import type { AxiosResponse } from 'axios';
import { DeviceEventEmitter, NativeEventEmitter, NativeModules, Platform } from 'react-native';
import { generateGuid } from 'utils/random';
import { EventName } from '@app/metrics/useMetrics';
import { Reservation } from 'apis/_generated-reservation-api/models';
const { RNSaltoKS } = NativeModules;

interface MobileKey {
  id: string;
  key_id: string;
  expiry_date: string;
  registration_date: string;
}
interface RegistrationResult {
  certificate: string;
  id: string;
  device_name: string;
  device_uid: string;
  mkey: MobileKey;
}

type OpenDoorEventType =
  | 'none'
  | 'openDoorEventFound'
  | 'openDoorEventSuccess'
  | 'openDoorEventFailed'
  | 'openDoorEventTimeout'
  | 'openDoorEventAlreadyRunning';

export interface OpenDoorEvent {
  eventName: OpenDoorEventType;
  error: String;
}

const openDoorEvent = 'openDoorEvent';

let eventEmitter = new NativeEventEmitter(NativeModules.RNSaltoKS);
function isModuleAvailable() {
  if (!RNSaltoKS) {
    throw new Error('SaltoKS native module not available, did you forget to link the library?');
  }
  return true;
}

export default class SaltoKS {
  static async initialize() {
    if (Platform.OS === 'android') return Promise.resolve(true);

    // explicit initialization of the openDoorDelegate and applePaySuppressor
    // Initialization is also done when an observer is attached.
    await RNSaltoKS.initialize();
  }
  static async suppressApplePay() {
    return await RNSaltoKS.suppressApplePay();
  }
  static async enableApplePay() {
    return await RNSaltoKS.enableApplePay();
  }
  static async getSuppressionStatus() {
    return await RNSaltoKS.getSuppressionStatus();
  }
  static async getPassLibraryAvailable() {
    return await RNSaltoKS.getPassLibraryAvailable();
  }

  static async stopListeningForDoorEvents() {
    if (Platform.OS === 'ios') {
      eventEmitter.removeAllListeners(openDoorEvent);
    } else if (Platform.OS === 'android') {
      DeviceEventEmitter.removeAllListeners(openDoorEvent);
    }
  }

  static async listenForDoorEvents(open: (event: OpenDoorEvent) => void) {
    if (Platform.OS === 'ios') {
      eventEmitter.addListener(openDoorEvent, open);
    } else if (Platform.OS === 'android') {
      DeviceEventEmitter.addListener(openDoorEvent, open);
    }
  }
  static async clearMobileKey() {
    await RNSaltoKS.clearMobileKey();
  }
  static async hasMobileKey() {
    return await RNSaltoKS.hasMobileKey();
  }
  static async activateDevice(
    userToken: string | undefined,
    deviceId: string | undefined,
    generatedId: string | undefined,
    setDeviceId: (id: string) => void,
    setGeneratedId: (id: string) => void,
    logEvent: (event: EventName, params?: { [key: string]: any } | undefined) => void
  ) {
    //no device id found, go for registration, this is done so that every new installment will register
    if (deviceId && generatedId) {
      await RNSaltoKS.getOrFetchMobileKey(userToken, generatedId);
      return { deviceId, generatedId, userToken };
    }

    if (!deviceId) {
      deviceId = generateGuid();
      setDeviceId(deviceId);
    }

    //when registration is requested, clear the current key to make sure we start with a working combination
    await RNSaltoKS.clearMobileKey();

    const publicKey = await RNSaltoKS.getPublicKey(deviceId);
    if (!publicKey) {
      logEvent('LockProviderSDKError', { error_type: 'public-key' });
      return;
    }
    let result: AxiosResponse<RegistrationResult> | undefined;

    if (!generatedId) {
      try {
        result = await saltoAPI.post<RegistrationResult>(
          '/me/devices',
          {
            device_name: deviceId,
            device_uid: deviceId,
            public_key: publicKey,
          },
          {
            headers: {
              Authorization: `Bearer ${userToken}`,
            },
          }
        );
      } catch (ex) {
        __DEV__ && console.log('SALTO registerDevice error', ex);
        logEvent('LockProviderAPIError', { method: '/me/devices' });
      }
      generatedId = result?.data?.id;

      // Disabled because not working yet
      /* if (Platform.OS === 'ios') {
        await RNSaltoKS.showSuppressAlert();
      } */
    }
    if (generatedId) {
      setGeneratedId(generatedId);
      return { deviceId, generatedId, userToken };
    }
  }

  static async openDoor(
    deviceId: string,
    saltoGeneratedDeviceId: string,
    userToken: string,
    logEvent: (
      event: EventName,
      params?:
        | {
            [key: string]: any;
          }
        | undefined,
      onlyOnce?: boolean
    ) => void,
    showRetryActivationAlert: () => void,
    reservation?: Reservation
  ) {
    __DEV__ && console.log('SALTO openDoor start');
    if (isModuleAvailable()) {
      try {
        RNSaltoKS.openDoor(deviceId, saltoGeneratedDeviceId, userToken);
      } catch (ex) {
        __DEV__ && console.log('SALTO openDoor error', ex);
        logEvent('LockKeyOpenError', {
          lock_type: reservation?.lockType,
          unit_code: reservation?.unitCode,
          reason: ex instanceof Error ? ex.message : 'Catched unknown exception on openDoor call',
        });
        showRetryActivationAlert();
      }
    } else {
      __DEV__ && console.log('SALTO openDoor missing native module');
      logEvent('LockKeyOpenError', {
        lock_type: reservation?.lockType,
        unit_code: reservation?.unitCode,
        reason: 'SaltoKS native module not available',
      });
      showRetryActivationAlert();
    }
  }
}
