import useTheme from '@app/hooks/useTheme';
import { NavigationContainerRef, useNavigation } from '@react-navigation/native';
import { Reservation } from 'apis/_generated-reservation-api/models';
import React, { useMemo, useState } from 'react';
import { Platform, TouchableOpacity, useWindowDimensions, View } from 'react-native';
import Text from '@app/components/Text';
import { addHours, formatDayWithoutTime, fromUnixNumber, nightsUntil } from 'utils/date';
import NumberOfNightsBadge from '@app/components/NumberOfNightsBadge';
import { BASELINE_INSET } from '@app/theme/partials/sizes';
import LinearGradient from 'react-native-linear-gradient';
import Shadow from '@app/components/Shadow';
import { Canvas, Image, BackdropBlur, Fill, useImage } from '@shopify/react-native-skia';
import { useGetPark } from '@domains/park/contact-and-info/queries/useGetPark';
import { AllRoutes } from '@app/navigation/stacks/Main';
import { LandalPressable, LandalPressableRefType } from '@app/components/LandalPressable';
import { useExternalImageCache } from 'utils/image-cache/useExternalImageCache';
import ReservationCardAccommodation from './ReservationCardAccommodation';
import { useGetReservation } from '../queries/useGetReservation';
import { getBrand } from 'utils/brand';
import { UserContext } from '@app/context/usercontext/UserContextProvider';
import LandalImage from '@app/components/LandalImage';
import useHasOwnApp from '@app/hooks/useHasOwnApp';

const ASPECT_RATIO = 3 / 2;
const HAS_OWN_APP_ICON_SIZE = 48;

interface Props {
  reservations: Reservation[];
}

export default function ReservationCard({ reservations }: Props) {
  const { token } = React.useContext(UserContext);
  const theme = useTheme();
  const brand = getBrand(theme);

  // reservation variable is the reservation, OR the first reservation in a combi
  const { data: reservation } = useGetReservation({
    token,
    brand,
    bookingReference: reservations?.[0]?.bookingReference,
  });

  const { data: park } = useGetPark({ parkCodeOverride: reservation?.parkCode });
  const { hasOwnApp, appIcon } = useHasOwnApp(park?.id);

  const [showSkia, setShowSkia] = useState(false);
  const navigation = useNavigation<NavigationContainerRef<AllRoutes>>();
  const { width: screenWidth } = useWindowDimensions();

  const parkImage = park?.images?.[0];
  const cachedImage = useExternalImageCache(parkImage ?? '');

  const skiaImage = useImage(cachedImage);

  const openReservation = (bookingReference?: string) => {
    if (!bookingReference) {
      return;
    }
    navigation.navigate('Reservation', { bookingReference });
  };

  const imageWidth = screenWidth - theme.space.xl;
  const imageHeight = imageWidth / ASPECT_RATIO;

  const numberOfNights = nightsUntil(reservation?.arrivalDate ?? 0);

  const itemRefs = useMemo(() => {
    return reservations?.map((r) => ({
      id: r.bookingReference,
      ref: React.createRef<LandalPressableRefType>(),
    }));
  }, [reservations]);

  if (!reservation?.parkCode) {
    return null;
  }

  const shouldNumberOfNightsBeShown =
    nightsUntil(
      reservation?.arrivalDate
        ? addHours(fromUnixNumber(reservation.arrivalDate * 1000), 2).getTime() / 1000 // We add 2 hours to compensate eventual timezone difference from dutch timezone.
        : 0
    ) > 0;

  const hintAllLines = () => {
    // staggered motion
    let timeoutIncrement = 0;
    for (let i = 0; i < itemRefs.length; i++) {
      setTimeout(() => {
        itemRefs?.[i]?.ref?.current?.hint();
      }, timeoutIncrement);
      timeoutIncrement += 100;
    }
  };

  return (
    <Shadow topOffset={-theme.space.l}>
      <LandalPressable
        onPress={() => openReservation(reservation.bookingReference)}
        style={{ marginHorizontal: -BASELINE_INSET, marginBottom: theme.space.l }}
        disabled={(reservations?.length || 0) > 1}
        pointerEvents={(reservations?.length || 0) > 1 ? 'box-none' : 'auto'}
      >
        <View
          key={reservation.bookingReference}
          style={{
            borderRadius: 12,
            overflow: 'hidden',
          }}
        >
          <View
            onLayout={(event) => {
              var { width: layoutWidth } = event.nativeEvent.layout;
              if (layoutWidth <= 0) {
                return;
              }
              setShowSkia(true);
            }}
          >
            {showSkia && Platform.OS !== 'web' ? (
              <Canvas style={{ width: '100%', height: imageHeight, backgroundColor: theme.colors.dark }}>
                {!!skiaImage && (
                  <>
                    <Image
                      x={0}
                      y={0}
                      width={imageWidth}
                      height={imageHeight}
                      image={skiaImage}
                      fit="cover"
                    />
                    {shouldNumberOfNightsBeShown && (
                      <BackdropBlur
                        blur={4}
                        clip={{
                          rect: {
                            x: imageWidth - 74 - theme.space.s,
                            y: theme.space.s,
                            width: 74,
                            height: 74,
                          },
                          rx: 8,
                          ry: 8,
                        }}
                      >
                        <Fill color={theme.colors.shade} />
                      </BackdropBlur>
                    )}
                  </>
                )}
              </Canvas>
            ) : (
              <LandalImage style={{ width: imageWidth, height: imageHeight }} remoteSource={parkImage} />
            )}
            <LinearGradient
              colors={[
                'rgba(0, 0, 0,0.0)',
                'rgba(0, 0, 0,0.0)',
                'rgba(0, 0, 0,0.8)', // Change this gradient to match BG
              ]}
              style={[
                {
                  position: 'absolute',
                  height: imageHeight,
                  width: '100%',
                  backgroundColor: 'transparent', // required for gradient
                },
              ]}
            />
            {shouldNumberOfNightsBeShown && <NumberOfNightsBadge numberOfNights={numberOfNights} />}
            <View
              style={{
                position: 'absolute',
                bottom: theme.space.l,
                left: theme.space.l,
                marginRight: theme.space.l * 2,
                flexDirection: 'row',
              }}
            >
              <View>
                <Text style={{ flex: 1, lineHeight: 26 }} color="white">
                  {reservation?.arrivalDate && reservation?.departureDate
                    ? `${formatDayWithoutTime(
                        fromUnixNumber(reservation.arrivalDate * 1000)
                      )} - ${formatDayWithoutTime(
                        fromUnixNumber(reservation.departureDate * 1000),
                        false,
                        false,
                        true
                      )}`
                    : '-'}
                </Text>
                <Text
                  type="title"
                  size="xl"
                  color="white"
                  bold
                  numberOfLines={1}
                  ellipsizeMode="tail"
                  style={{ paddingRight: theme.space.xl }}
                >
                  {park?.name}
                </Text>
              </View>
              {hasOwnApp && (
                <View
                  style={{
                    justifyContent: 'flex-end',
                    alignItems: 'flex-end',
                    flex: 1,
                  }}
                >
                  <LandalImage
                    localSource={appIcon}
                    style={{ width: HAS_OWN_APP_ICON_SIZE, height: HAS_OWN_APP_ICON_SIZE, borderRadius: 8 }}
                  />
                </View>
              )}
            </View>
          </View>
          {(reservations?.length || 0) > 1 && (
            <TouchableOpacity
              activeOpacity={1}
              onPress={hintAllLines}
              style={{
                position: 'absolute',
                width: '100%',
                height: imageHeight,
                backgroundColor: 'transparent',
              }}
            />
          )}
          {reservations.map((a, index) => {
            const id = a.bookingReference ?? '' + a.accommodationCode ?? '' + a.unitCode ?? '';
            return (
              <ReservationCardAccommodation
                key={id}
                index={index}
                total={reservations.length}
                accommodationCode={a.accommodationCode}
                bookingReference={a.bookingReference}
                id={id}
                unitCode={a.unitCode}
                parkCode={reservation.parkCode}
                ref={itemRefs[index]?.ref}
                isCamping={reservation?.unitTypeIndicator === 'CAMPING'}
              />
            );
          })}
        </View>
      </LandalPressable>
    </Shadow>
  );
}
