import useTheme from '@app/hooks/useTheme';
import { Blur, Canvas, Easing, Image, Rect, useImage, useTiming } from '@shopify/react-native-skia';
import React from 'react';
import { useExternalImageCache } from 'utils/image-cache/useExternalImageCache';
import { getImageUrlWithWidth, ImageSize } from 'utils/imageUrl';
import FastImage, { Source } from 'react-native-fast-image';
import { Platform } from 'react-native';

interface Props {
  remoteSource?: string;
  localSource?: Source;
  size?: ImageSize;
  width: number;
  height: number;
  applyBlur?: boolean;
  showIconMask?: boolean;
  badge?: any;
}
export default function LandalSkiaImage({
  localSource,
  remoteSource,
  size,
  width,
  height,
  applyBlur,
  showIconMask,
  badge,
}: Props) {
  const theme = useTheme();

  const sizedImage = !!size && !!remoteSource ? getImageUrlWithWidth(remoteSource, size) : remoteSource;

  const cachedImage = useExternalImageCache(
    !!sizedImage && (sizedImage?.length || 0) > 20 ? sizedImage : undefined
  );

  const skiaImage = useImage(localSource || cachedImage);

  const opacity = useTiming(skiaImage ? 1 : 0, {
    duration: 800,
    easing: Easing.bezier(0.25, 0.1, 0.25, 1),
  });

  if (Platform.OS === 'web' && !applyBlur) {
    return (
      <FastImage
        source={localSource ? localSource : { uri: sizedImage }}
        /* onError={() => {
        setImageError(true);
        onLoadOrError?.();
      }} */
        style={{ width, height, position: 'absolute' }}
        //resizeMode={resizeMode}
        //onLoadEnd={onLoadOrError ? onLoadOrError : undefined}
      />
    );
  }

  return (
    <Canvas style={{ width, height, position: 'absolute' }}>
      {!!skiaImage && (
        <Image x={0} y={0} width={width} height={height} image={skiaImage} fit="cover" opacity={opacity}>
          {applyBlur && <Blur blur={6} mode="clamp" />}
        </Image>
      )}
      {showIconMask && (
        <Rect x={0} y={0} width={width} height={height} color={theme.colors.black} opacity={0.2} />
      )}
      {badge}
    </Canvas>
  );
}
