import React, { useState } from 'react';
import useTheme from '@app/hooks/useTheme';
import { View, Alert } from 'react-native';
import Text from '@app/components/Text';
import { useFormik } from 'formik';
import FormInput from '@app/components/Form/FormInput';
import { useIntl } from 'react-intl';
import FormCheckBox from '@app/components/Form/FormCheckBox';
import Button from '@app/components/Button';
import PersonControl from '@app/components/Form/PersonControl';
import { useGetActivityDetails } from '../queries/useGetActivityDetails';
import DateTimeLabel from '@app/components/DateTimeLabel';
import { KeyboardAwareScrollView } from 'react-native-keyboard-aware-scroll-view';
import ContentContainer from '@app/components/DetailScreen/ContentContainer';
import { useBookActivity } from '../queries/useBookActivity';
import ReservationBill from '@app/components/ReservationBill';
import { Person } from '@app/interfaces/Person';
import { getAPIDate, getFormattedDate } from 'utils/date';
import { format } from 'utils/string';
import { useFocusEffect } from '@react-navigation/native';
import useMetrics from '@app/metrics/useMetrics';
import { isValidEmail, isValidPhone } from 'utils/validation';
import { ActivityBookRequest, ActivityMetaDepositType } from 'apis/_generated-park-api/models';
import { TextInput } from 'react-native-gesture-handler';
import StickyHeader from '@app/components/StickyHeader';
import { NavScreenProp } from '@app/navigation/stacks/Main';
import { NotificationContextUpdater } from '@domains/notification/context/NotificationProvider';
import { NotificationMessage } from '@domains/notification/types/NotificationMessage';

type FormFields =
  | 'persons'
  | 'name'
  | 'phone'
  | 'email'
  | 'accommodation_number'
  | 'remarks'
  | 'child_may_return_alone'
  | 'agreed_to_terms';

const emptyPersonObject: Person = { label: '', type: 0, quantity: 1, totalPrice: 0 };

export default function ActivitySignup({ navigation, route }: NavScreenProp<'ActivitySignup'>) {
  const theme = useTheme();
  const { formatMessage, formatNumber } = useIntl();
  const [processing, setProcessing] = useState(false);

  const { setNotifications } = React.useContext(NotificationContextUpdater);

  const scrollViewRef = React.useRef(null);
  const phoneInputRef = React.useRef<TextInput>(null);

  const activity = route?.params?.activity;
  const reservationId = activity?.time?.reservationId;
  const { data: activityDetails } = useGetActivityDetails({
    reservationId,
  });

  const { postBooking } = useBookActivity();

  const formik = useFormik<Record<FormFields, any>>({
    initialValues: {
      persons: [emptyPersonObject],
      name: '',
      phone: '',
      email: '',
      accommodation_number: undefined as undefined | number,
      remarks: '',
      child_may_return_alone: false,
      agreed_to_terms: false,
    },
    onSubmit: () => {},
    validate: () => {
      // TODO: These checks should be based on the callback values
      if (!validAccommodationNumber || !validEmail || !validName || !validPhone) {
        return { error: 'invalid_data_provided' };
      }
      return {};
    },
  });

  const { logEvent, logScreenView } = useMetrics();
  useFocusEffect(
    React.useCallback(() => {
      if (activity?.id) {
        logScreenView('ActivitySignup', [activity.id.toString()]);
      }
    }, [activity.id, logScreenView])
  );

  const validName = !!formik.values.name;
  const validEmail = formik.values.email ? isValidEmail(formik.values.email) : false;
  const validPhone = formik.values.phone ? isValidPhone(formik.values.phone) : false;
  const validAccommodationNumber = !isNaN(formik.values.accommodation_number);

  const enteredValidPerson = !!formik.values.persons?.[0]?.name;

  const allPersonNamesEntered =
    formik.values.persons.filter((p: Person) => !!p.name).length >= formik.values.persons.length;

  const limitReached = (activityDetails?.Free || 0) <= formik.values.persons.length;

  const participants = activityDetails?.participants;

  const billPersons = formik.values.persons.reduce((result: Person[], person: Person) => {
    const existingPersonType = result.find((r) => r.type === person.type);
    const participantType = participants?.find((p) => p?.id === person.type);

    // Only count when a name is entered
    if (!person.name) {
      return result;
    }

    const participantAmount = Number(participantType?.price || 0);

    if (!existingPersonType) {
      const newPerson: Person = {
        type: person.type,
        label: participantType?.name || '',
        name: person.name,
        quantity: 1,
        totalPrice: participantAmount,
      };
      result.push(newPerson);
    } else {
      existingPersonType.quantity += 1;
      existingPersonType.totalPrice += participantAmount;
    }
    return result;
  }, []) as Person[];

  return (
    <View style={{ flex: 1 }}>
      <StickyHeader showClose navigation={navigation} />
      <KeyboardAwareScrollView
        style={{ flex: 1, backgroundColor: theme.colors.tertiary }}
        ref={scrollViewRef}
        enableOnAndroid
        extraScrollHeight={120}
        keyboardOpeningTime={Number.MAX_SAFE_INTEGER}
        enableResetScrollToCoords={false}
        keyboardShouldPersistTaps="handled"
      >
        <ContentContainer style={{ flex: 1, backgroundColor: theme.colors.tertiary }}>
          {activity.time && (
            <DateTimeLabel
              dateObject={activity.time}
              showDay
              style={{ marginBottom: theme.space.l }}
              color="white"
            />
          )}
          <Text type="title" size="xl" color="white" style={{ marginBottom: theme.space.xl }} bold>
            {activity.name}
          </Text>
          <View>
            <Text type="title" size="l" color="white" style={{ marginBottom: theme.space.s }} bold>
              {formatMessage({ id: 'app.general.participants' })}
            </Text>
            {formik.values.persons.map((p: Person, index: number) => (
              <PersonControl
                key={index}
                name={p.name}
                types={
                  participants?.map((par) => ({
                    value: Number(par?.id),
                    label: par?.description || '',
                  })) || []
                }
                selectedType={p.type}
                onChange={(name, selectedType) => {
                  const persons = [...formik.values.persons];
                  persons[index] = { name, type: selectedType };
                  formik.setFieldValue('persons', persons);
                }}
                onRemove={() => {
                  const persons = [...formik.values.persons];
                  persons.splice(index, 1);
                  formik.setFieldValue('persons', persons);
                }}
                canBeRemoved={formik.values.persons.length > 1}
              />
            ))}
            <View style={{ opacity: enteredValidPerson ? 1 : 0.4, flex: 2 }}>
              <Button
                type="transparent"
                disabled={!enteredValidPerson || limitReached || !allPersonNamesEntered}
                onPress={() => {
                  const persons = [...formik.values.persons, emptyPersonObject];
                  formik.setFieldValue('persons', persons);
                }}
                style={{ marginTop: 8 }}
              >
                <Button.Icon name="plus" size="xs" color={'white'} />
                <Button.Text color="white" type="body">
                  {formatMessage({ id: 'app.activities.signup.addsomeone' })}
                </Button.Text>
              </Button>
              {limitReached && (
                <Text bold color="white" size="s" style={{ marginTop: 12 }}>
                  {formatMessage({ id: 'app.activities.signup.limitreached' })}
                </Text>
              )}
            </View>
          </View>
          {enteredValidPerson && (
            <View style={{ marginBottom: theme.space.xl, marginTop: theme.space.xl }}>
              <Text type="title" size="l" color="white" style={{ marginBottom: 32 }} bold>
                {formatMessage({ id: 'app.activities.signup.contact' })}
              </Text>
              <Text bold color="white" size="s" style={{ marginBottom: 8 }}>
                {formatMessage({ id: 'app.forms.labels.name' })}
              </Text>
              <FormInput
                value={formik.values.name}
                isValid={validName}
                onChangeText={(text) => {
                  formik.setFieldValue('name', text);
                }}
                onSubmitEditing={() => phoneInputRef.current?.focus()}
              />
            </View>
          )}
          {!!formik.values.name && (
            <View style={{ marginBottom: theme.space.xl }}>
              <Text bold color="white" size="s" style={{ marginBottom: 8 }}>
                {formatMessage({ id: 'app.forms.labels.phone' })}
              </Text>
              <FormInput
                ref={phoneInputRef}
                value={formik.values.phone}
                isValid={validPhone}
                onChangeText={(text) => {
                  formik.setFieldValue('phone', text);
                }}
              />
            </View>
          )}
          {validPhone && (
            <View style={{ marginBottom: theme.space.xl }}>
              <Text
                bold
                color="white"
                size="s"
                style={{ marginBottom: 8, lineHeight: theme.fontSizes.s.lineHeight }}
              >
                {formatMessage({ id: 'app.forms.labels.email' })}{' '}
                <Text
                  bold
                  color="white"
                  size="s"
                  style={{ marginBottom: 8, lineHeight: theme.fontSizes.s.lineHeight }}
                >
                  {formatMessage({ id: 'app.forms.labels.email.explanation' })}
                </Text>
              </Text>
              <FormInput
                value={formik.values.email}
                isValid={validEmail}
                onChangeText={(text) => {
                  formik.setFieldValue('email', text);
                }}
              />
            </View>
          )}
          {validEmail && (
            <>
              <View style={{ marginBottom: theme.space.l }}>
                <Text bold color="white" size="s" style={{ marginBottom: 8 }}>
                  {formatMessage({ id: 'app.forms.labels.accommodationnumber' })}
                </Text>
                <FormInput
                  value={formik.values.accommodation_number}
                  isValid={validAccommodationNumber}
                  onChangeText={(text) => {
                    formik.setFieldValue('accommodation_number', text);
                  }}
                />
              </View>
              {activityDetails?.childMayReturnAlone && (
                <View
                  style={{
                    marginBottom: theme.space.xl,
                    marginTop: theme.space.l,
                    flexDirection: 'row',
                  }}
                >
                  <FormCheckBox
                    value={formik.values.child_may_return_alone}
                    onValueChange={(newValue) => formik.setFieldValue('child_may_return_alone', newValue)}
                  />
                  <Text type="body" color="white" size="s" style={{ marginLeft: 16, flexShrink: 1 }}>
                    {formatMessage({ id: 'app.activities.signup.childmayreturnalone' })}
                  </Text>
                </View>
              )}

              <View style={{ marginBottom: theme.space.xl }}>
                <Text bold color="white" size="s" style={{ marginBottom: 8 }}>
                  {formatMessage({ id: 'app.general.remarks' })}
                </Text>
                <FormInput
                  value={formik.values.remarks}
                  onChangeText={(text) => {
                    formik.setFieldValue('remarks', text);
                  }}
                  multiline
                />
              </View>
            </>
          )}
          {validAccommodationNumber && (
            <>
              <ReservationBill
                orderRows={billPersons?.map((c) => {
                  return {
                    id: c.type.toString(),
                    label: participants?.find((par) => par?.id === c.type)?.name,
                    amount: c.quantity,
                    price: c.totalPrice,
                  };
                })}
                style={{ backgroundColor: 'white' }}
              />
              {!!activityDetails?.deposit && (
                <View>
                  <Text type="body" color="white" style={{ marginTop: theme.space.l }}>
                    {formatMessage({ id: 'app.activities.signup.deposit1' })}
                    {formatNumber(
                      activityDetails?.depositType === ActivityMetaDepositType.Person
                        ? activityDetails?.deposit * billPersons.length
                        : activityDetails?.deposit || 0,
                      {
                        style: 'currency',
                        currency: 'EUR',
                        currencyDisplay: 'symbol',
                      }
                    )}
                    {formatMessage({ id: 'app.activities.signup.deposit2' })}
                    {activityDetails?.depositLocation}
                  </Text>
                </View>
              )}
              {!!activityDetails?.disclaimerRequired && (
                <View>
                  <Text type="body" color="white" style={{ marginTop: theme.space.l }}>
                    {formatMessage({ id: 'app.activities.signup.disclaimer' })}
                  </Text>
                </View>
              )}
              <Button
                type="primary"
                onPress={async () => {
                  setProcessing(true);

                  const totalAmountOfParticipants = billPersons.reduce((acc, curr) => acc + curr.quantity, 0);

                  const bookRequest: ActivityBookRequest = {
                    customer: {
                      email: formik.values.email,
                      lastName: formik.values.name,
                      postalCode: formik.values.accommodation_number,
                      phoneMobile: formik.values.phone,
                    },
                    eventId: reservationId,
                    participants: Object.assign(
                      {},
                      formik.values.persons?.map((b: Person) => ({ id: b.type, lastName: b.name }))
                    ),
                    remarks: `${formik.values.remarks}${
                      formik.values.child_may_return_alone
                        ? `\r\n${formatMessage({ id: 'app.activities.signup.childmayreturnalone' })}`
                        : ''
                    }`,
                  };

                  const result = await postBooking(bookRequest);

                  const reservationDate = activity?.time?.date ? new Date(activity.time.date) : new Date();

                  const billPersonText =
                    totalAmountOfParticipants > 1
                      ? formatMessage({ id: 'app.forms.result.person_plural' })
                      : formatMessage({ id: 'app.forms.result.person' });

                  const ANALYTICS_TYPE = 'activity';
                  const ANALYTICS_RESERVATION = `${
                    activity.name
                  }|${totalAmountOfParticipants} ${billPersonText} ${activityDetails?.location}|${getAPIDate(
                    reservationDate
                  )} ${activity?.time?.startTime}`;

                  if (result.status) {
                    logEvent('GenerateLead', {
                      type: ANALYTICS_TYPE,
                      reservation: ANALYTICS_RESERVATION,
                    });

                    const message: NotificationMessage = {
                      read: false,
                      createdAt: new Date(),
                      shortDetails: [
                        {
                          label: formatMessage({ id: 'app.general.date' }),
                          value: route.params?.activity?.time?.date
                            ? getFormattedDate(new Date(route.params.activity.time.date))
                            : '',
                        },
                        {
                          label: formatMessage({ id: 'app.general.time' }),
                          value: `${activity?.time?.startTime} - ${activity?.time?.endTime}`,
                        },
                      ],
                      text: `${activity?.time?.startTime} - ${activity?.time?.endTime}\n\n${
                        activityDetails?.location?.toString() || ''
                      }\n\n${totalAmountOfParticipants.toString()} ${billPersonText}\n\n${
                        bookRequest.remarks
                      }${format(
                        formatMessage({
                          id: 'app.forms.activitybooking.result.description',
                        }),
                        activity.name || '',
                        billPersons.length.toString(),
                        billPersonText,
                        route.params?.activity?.time?.date
                          ? getFormattedDate(new Date(route.params.activity.time.date))
                          : '',
                        route.params?.activity?.time?.startTime ?? '',
                        activityDetails?.location ?? '',
                        '\r\n',
                        '\r\n',
                        activity?.name ?? ''
                      )}`,
                      image: activity?.media ?? undefined,
                    };

                    setNotifications((prev) => [...prev, message]);

                    navigation.replace('ActivityBookingSuccessful', {
                      activity,
                      amount: totalAmountOfParticipants,
                    });
                    return;
                  } else {
                    logEvent('GenerateLeadError', {
                      type: ANALYTICS_TYPE,
                      type_error: result?.messages?.join('/r/n') ?? '',
                      reservation: ANALYTICS_RESERVATION,
                    });

                    const message = result?.messages
                      ? result.messages.join('/r/n')
                      : formatMessage({ id: 'app.activities.signup.failed' });
                    Alert.alert(message);
                  }
                  setProcessing(false);
                }}
                style={{
                  marginTop: 16,
                  marginBottom: 16,
                  backgroundColor: formik.isValid ? theme.colors.tertiary : theme.colors.light,
                }}
                disabled={!formik.isValid || processing}
              >
                <Button.Text>{formatMessage({ id: 'app.forms.signup' })}</Button.Text>
              </Button>
            </>
          )}
        </ContentContainer>
      </KeyboardAwareScrollView>
    </View>
  );
}
