import Shadow from '@app/components/Shadow';
import useHasOwnApp from '@app/hooks/useHasOwnApp';
import useTheme from '@app/hooks/useTheme';
import { BASELINE_INSET } from '@app/theme/partials/sizes';
import { useGetPark } from '@domains/park/contact-and-info/queries/useGetPark';
import React, { PropsWithChildren } from 'react';
import { useIntl } from 'react-intl';
import { Linking, View } from 'react-native';
import Button from './Button';
import Text from './Text';

interface Props {
  parkCode?: string;
}

const ParkHasOwnApp = ({ parkCode }: Props) => {
  const theme = useTheme();
  const { formatMessage } = useIntl();
  const { data: park } = useGetPark({ parkCodeOverride: parkCode });
  const { hasOwnApp, dynamicLink, appName } = useHasOwnApp(park?.id);

  if (!hasOwnApp) return null;

  return (
    <View>
      <Text size="l" type="title" bold>
        {formatMessage({ id: 'app.parkHasOwnApp.component.title' })}
      </Text>
      <Text style={{ lineHeight: 26, marginTop: theme.space.m, marginBottom: theme.space.l }} color="dark">
        {formatMessage({ id: 'app.parkHasOwnApp.component.text' })}
      </Text>
      <Button
        type="primary"
        onPress={() => !!dynamicLink && Linking.openURL(dynamicLink)}
        style={{ backgroundColor: theme.colors.secondary }}
      >
        <Button.Icon name="exit-app" color={'white'} />
        <Button.Text>
          {formatMessage({ id: 'app.parkHasOwnApp.component.downloadCTA' }).replace('{0}', appName || '')}
        </Button.Text>
      </Button>
    </View>
  );
};

export const ParkHasOwnAppWrapper = ({ children }: PropsWithChildren<{}>) => {
  const theme = useTheme();

  return (
    <View>
      <Shadow>
        <View
          style={{
            backgroundColor: theme.colors.white,
            top: -36,
            marginBottom: -36,
            marginHorizontal: -BASELINE_INSET,
            borderRadius: 12,
            overflow: 'hidden',
            padding: theme.space.l,
          }}
        >
          {children}
        </View>
      </Shadow>
    </View>
  );
};

export default ParkHasOwnApp;
