import Icon from '@app/components/Icon';
import useTheme from '@app/hooks/useTheme';
import { AllColors } from '@app/theme/partials/general';
import React from 'react';
import { ViewStyle } from 'react-native';
import { LandalPressable, LandalPressableProps } from './LandalPressable';

interface Props extends LandalPressableProps {
  onPress: () => void;
  icon: string;
  iconColor?: AllColors;
  style?: ViewStyle;
  showBadge?: boolean;
}
export default function RoundButton({ onPress, icon, iconColor, showBadge, style, ...props }: Props) {
  const theme = useTheme();

  return (
    <LandalPressable
      style={[
        {
          width: 40,
          height: 40,
          borderRadius: 20,
          justifyContent: 'center',
          alignItems: 'center',
          backgroundColor: theme.colors.white,
          elevation: 5,
          shadowRadius: 8,
          shadowOpacity: 0.08,
          shadowColor: 'black',
          shadowOffset: {
            width: 0,
            height: 5,
          },
          zIndex: 6,
        },
        style,
      ]}
      onPress={onPress}
      hitSlop={{ top: 10, bottom: 10, left: 10, right: 10 }}
      {...props}
    >
      <Icon name={icon} size="m" color={iconColor ? iconColor : 'primary'} showBadge={showBadge} />
    </LandalPressable>
  );
}
