import useScreenSize from '@app/hooks/useScreenSize';
import useTheme from '@app/hooks/useTheme';
import React, { useState } from 'react';
import { Modal, Platform, View } from 'react-native';
import FormButton from '../Form/FormButton';
import Text from '../Text';
import DateTimePicker, { Event as DatePickerEvent } from '@react-native-community/datetimepicker';
import { fromUnixNumber, getFormattedDate, isToday, isTomorrow, toUnixNumber } from 'utils/date';
import { useIntl } from 'react-intl';
import Popup from '../Popup';
import Button from '../Button';

interface Props {
  date?: string;
  maximumDate?: number;
  onSelectDate: (value: Date) => void;
}

const ANDROID = Platform.OS === 'android';

//this is a bit of a hack solving versioning issues. there's 13.5 but also 14.4.2 ; 14.4.2 does only resolve as string
//by removing all dots and multiplying it, versions from x.x x.x.x and x.x.x.x will be comparable
const iosVersionNumber = () => {
  let flattenedVersionNumber = +Platform.Version.toString().split('.').join('');

  while (flattenedVersionNumber < 10000) {
    flattenedVersionNumber = flattenedVersionNumber * 10;
  }
  return flattenedVersionNumber;
};

const IOS_WITH_CALENDAR = Platform.OS === 'ios' && iosVersionNumber() > 13500;
const IOS_WITHOUT_CALENDAR = Platform.OS === 'ios' && iosVersionNumber() <= 13500;
export default function DateSelector({ date, maximumDate, onSelectDate }: Props) {
  const theme = useTheme();
  const { width } = useScreenSize();
  const [showDatePicker, setShowDatePicker] = useState(false);
  const { formatMessage } = useIntl();

  const TODAY = !!date && isToday(date);
  const TOMORROW = !!date && isTomorrow(toUnixNumber(date));
  const contentWidth = width - theme.space.l * 2;

  return (
    <View style={{ marginBottom: 16 }}>
      <Text bold size="s" style={{ marginBottom: 8 }}>
        {formatMessage({ id: 'app.forms.labels.when' })}
      </Text>
      <View style={{ flexDirection: 'row', justifyContent: 'space-between', flex: 1 }}>
        <FormButton
          style={{ width: contentWidth / 3 - 5 }}
          text={formatMessage({ id: 'app.general.today' })?.toLowerCase()}
          selected={!showDatePicker && TODAY}
          onPress={() => {
            setShowDatePicker(false);
            let newDate = new Date();
            onSelectDate(newDate);
          }}
        />
        <FormButton
          style={{ width: contentWidth / 3 - 5 }}
          text={formatMessage({ id: 'app.general.tomorrow' })?.toLowerCase()}
          selected={!showDatePicker && TOMORROW}
          onPress={() => {
            setShowDatePicker(false);
            let newDate = new Date();
            newDate.setDate(newDate.getDate() + 1);
            onSelectDate(newDate);
          }}
        />
        <View style={{ width: contentWidth / 3 - 5 }}>
          {ANDROID && showDatePicker && (
            <DateTimePicker
              style={{ flex: 1 }}
              mode="date"
              value={date ? new Date(date) : new Date()}
              minimumDate={new Date()}
              maximumDate={maximumDate ? fromUnixNumber(maximumDate * 1000) : undefined}
              onChange={(_event: DatePickerEvent, selectedDate: Date | undefined) => {
                if (selectedDate) {
                  setShowDatePicker(false);
                  onSelectDate(selectedDate);
                }
              }}
            />
          )}

          {IOS_WITH_CALENDAR && (
            <DateTimePicker
              style={{ flex: 1 }}
              mode="date"
              display={'compact'}
              value={date ? new Date(date) : new Date()}
              minimumDate={new Date()}
              maximumDate={maximumDate ? fromUnixNumber(maximumDate * 1000) : undefined}
              onChange={(_event, selectedDate) => {
                if (selectedDate) {
                  setShowDatePicker(false);
                  onSelectDate(selectedDate);
                }
              }}
            />
          )}
          {IOS_WITHOUT_CALENDAR && showDatePicker && (
            <Modal transparent animationType="fade">
              <Popup>
                <DateTimePicker
                  mode="date"
                  value={date ? new Date(date) : new Date()}
                  minimumDate={new Date()}
                  maximumDate={maximumDate ? fromUnixNumber(maximumDate * 1000) : undefined}
                  onChange={(_event: DatePickerEvent, selectedDate: Date | undefined) => {
                    if (selectedDate) {
                      onSelectDate(selectedDate);
                    }
                  }}
                />
                <Button
                  type="primary"
                  onPress={() => {
                    setShowDatePicker(false);
                  }}
                  style={{ marginBottom: 8 }}
                >
                  <Button.Text>{formatMessage({ id: 'app.general.ok' })}</Button.Text>
                </Button>
              </Popup>
            </Modal>
          )}
          <FormButton
            text={
              !!date && !TODAY && !TOMORROW
                ? getFormattedDate(new Date(date))
                : formatMessage({ id: 'app.general.other' })?.toLowerCase()
            }
            selected={showDatePicker || (!!date && !TODAY && !TOMORROW)}
            pointerEvents={IOS_WITH_CALENDAR ? 'none' : 'auto'}
            style={{ position: 'absolute', width: '100%' }}
            onPress={() => setShowDatePicker(true)}
          />
        </View>
      </View>
    </View>
  );
}
