export const SAPWebviewInject = (username: string, password: string) => {
  return `(function() { 
    var usernameInput = Array.from(document.getElementsByName('username')).find(field => field.attributes["data-screenset-roles"].value == "instance")
    var passwordInput = Array.from(document.getElementsByName('password')).find(field => field.attributes["data-screenset-roles"].value == "instance")
    var submit = Array.from(document.getElementsByClassName('gigya-input-submit')).find(field => field.attributes["data-screenset-roles"].value == "instance")

    usernameInput.value = "${username}";
    passwordInput.value = "${password}";
    submit.click();
  })()`;
};
