import Text from '@app/components/Text';
import useScreenSize from '@app/hooks/useScreenSize';
import useTheme from '@app/hooks/useTheme';
import { BOTTOM_TAB_BAR_HEIGHT, BOTTOM_TAB_BAR_HEIGHT_NO_INSET } from '@app/navigation/BottomTabs';
import { NavigationContainerRef, useNavigation } from '@react-navigation/native';
import {
  Blur,
  Canvas,
  Image as SkiaImage,
  Image,
  useValue,
  useSharedValueEffect,
  Rect,
  runTiming,
  useImage,
  SkImage,
  Group,
} from '@shopify/react-native-skia';
import React, { useCallback, useEffect, useState } from 'react';
import { Platform, View } from 'react-native';
import LinearGradient from 'react-native-linear-gradient';
import Animated, {
  Easing,
  interpolate,
  useAnimatedStyle,
  useSharedValue,
  withTiming,
} from 'react-native-reanimated';
import { useExternalImageCache } from 'utils/image-cache/useExternalImageCache';
import { getImageUrlWithWidth } from 'utils/imageUrl';
import LandalImage from './LandalImage';
import Pager from './Pager';
import RoundButton from './RoundButton';

type sizeType = 'm' | 'l' | 'xl' | 'stretch';

interface Props {
  scrollOffset: Animated.SharedValue<number>;
  title?: string;
  images?: string[];
  titleOverImage?: boolean;
  children?: React.ReactNode;
  withGradient?: boolean;
  pullBack?: boolean;
  size?: sizeType;
  close?: boolean;
  skiaBadge?: any;
  pullBackBackgroundColor?: string;
  gradientToTrueBlack?: boolean;
}

interface Map {
  [key: string]: number | undefined;
}

interface SkiaImageCompProps {
  expandedHeaderHeight: number;
  skiaImageLarge?: SkImage;
  screenWidth: number;
  skiaBadge?: any;
  hideBadge: boolean;
}
const SkiaImageComp = ({
  expandedHeaderHeight,
  skiaImageLarge,
  screenWidth,
  skiaBadge,
  hideBadge,
}: SkiaImageCompProps) => {
  const fade = useValue(0);
  // TODO: useCanvasSize is not always providing a width, so we are not using this yet.
  /* const canvas = useCanvasSize(); */

  const fadeIn = useCallback(() => {
    if (fade.current === 0) {
      runTiming(fade, 1, { duration: 400 });
    }
  }, [fade]);

  /* useEffect(() => {
    if (canvas.current.width > 0) {
      fadeIn();
    }
  }, [canvas, fade, fadeIn]); */

  // This is a fallback, in case we do not receive the canvas size.
  useEffect(() => {
    /* setTimeout(() => { */
    fadeIn();
    /* }, 600); */
  });

  return (
    <Group>
      {!!skiaImageLarge && (
        <Image
          x={0}
          y={0}
          width={screenWidth}
          height={expandedHeaderHeight}
          image={skiaImageLarge}
          fit="cover"
        />
      )}
      {!!skiaBadge && skiaBadge}
    </Group>
  );
};

export const useStickyImageHeaderDimensions = (size: sizeType) => {
  const { width, height: screenHeight, insets } = useScreenSize();

  const SIZES: Map = {
    m: 250,
    l: Math.max(Math.round((width / 2) * 1), 246 + insets.top),
    xl: 378 + insets.top,
    stretch: screenHeight - (insets.bottom ? BOTTOM_TAB_BAR_HEIGHT : BOTTOM_TAB_BAR_HEIGHT_NO_INSET),
  };

  const EXPANDED_HEADER_HEIGHT = size === 'stretch' ? screenHeight - 328 : SIZES[size] || 220;
  // This value is not 100% correct.
  // It should actually use the react-native-screens useHeaderHeight value, but that seems to provide '0' all the time.
  const COLLAPSED_HEADER_HEIGHT = 60 + insets.top;
  const HEADER_SOLID_THRESHOLD = EXPANDED_HEADER_HEIGHT - COLLAPSED_HEADER_HEIGHT; //  + (insets.top <= 24 ? 24 : 0);
  return { COLLAPSED_HEADER_HEIGHT, EXPANDED_HEADER_HEIGHT, HEADER_SOLID_THRESHOLD, size };
};

const StickyImageHeader = ({
  scrollOffset,
  title,
  titleOverImage = true,
  images,
  children,
  withGradient = true,
  pullBack = false, // while scrolling, increase speed of header sliding upwards
  size = 'm',
  close = true,
  skiaBadge,
  pullBackBackgroundColor,
  gradientToTrueBlack,
}: Props) => {
  const { width: screenWidth, insets } = useScreenSize();

  const { EXPANDED_HEADER_HEIGHT, COLLAPSED_HEADER_HEIGHT, HEADER_SOLID_THRESHOLD } =
    useStickyImageHeaderDimensions(size);

  const PULLBACK_OFFSET = 46;
  const theme = useTheme();

  const [hideBadge, setHideBadge] = useState(false);
  const [activeImage, setActiveImage] = useState(0);

  const navigation = useNavigation<NavigationContainerRef<any>>();

  const titleOpacity = useSharedValue(0);
  const contentOpacity = useSharedValue(1);

  const headerContentStyles = useAnimatedStyle(() => {
    return {
      opacity:
        scrollOffset.value > 0
          ? interpolate(scrollOffset.value, [30, 70], [1, 0])
          : interpolate(scrollOffset.value, [-18, -50], [1, 0]),
    };
  });
  const titleStyle = useAnimatedStyle(() => {
    return pullBack
      ? {
          opacity: interpolate(scrollOffset.value, [70, 100], [0, 1]),
        }
      : {
          opacity: withTiming(titleOpacity.value, {
            duration: 250,
            easing: Easing.bezier(0.25, 0.1, 0.25, 1),
          }),
        };
  });

  const x = useValue(1);

  useSharedValueEffect(() => {
    x.current = contentOpacity.value;
    const hide = contentOpacity.value === 0;
    setHideBadge(hide);
  }, contentOpacity); // you can pass other shared values as extra parameters

  const pageIndicatorOpacity = useSharedValue(0);
  const previousScrollOffset = useSharedValue(0);

  const headerAnimatedStyle = useAnimatedStyle(() => {
    if (titleOverImage) {
      if (titleOpacity.value !== 1) {
        titleOpacity.value = 1;
      }
    } else if (pullBack) {
      if (scrollOffset.value < previousScrollOffset.value && scrollOffset.value < 100) {
        if (titleOpacity.value === 1) {
          titleOpacity.value = 0;
        }
      } else if (
        scrollOffset.value > previousScrollOffset.value &&
        scrollOffset.value > 20 &&
        titleOpacity.value === 0
      ) {
        titleOpacity.value = 1;
      } else if (scrollOffset.value < 20 && titleOpacity.value === 1) {
        titleOpacity.value = 0;
      }
      previousScrollOffset.value = scrollOffset.value;
    } else {
      if (scrollOffset.value > (pullBack ? HEADER_SOLID_THRESHOLD - 46 : HEADER_SOLID_THRESHOLD - 10)) {
        if (titleOpacity.value === 0) {
          titleOpacity.value = 1;
        }
      } else if (titleOpacity.value === 1) {
        titleOpacity.value = 0;
      }
    }

    if ((scrollOffset.value > 20 || scrollOffset.value < -20) && contentOpacity.value === 1) {
      contentOpacity.value = 0;
    } else if (scrollOffset.value < 20 && scrollOffset.value > -20 && contentOpacity.value === 0) {
      contentOpacity.value = 1;
    }
    if (scrollOffset.value < 0 || scrollOffset.value > 5) {
      if (pageIndicatorOpacity.value === 1) {
        pageIndicatorOpacity.value = 0;
      }
    } else if (titleOpacity.value === 0) {
      pageIndicatorOpacity.value = 1;
    }
    return {
      opacity:
        size === 'stretch'
          ? 0
          : Math.max(
              0,
              interpolate(
                scrollOffset.value,
                [
                  pullBack ? 120 : 100 + (insets.top <= 24 ? insets.top : 0),
                  pullBack ? HEADER_SOLID_THRESHOLD - PULLBACK_OFFSET : HEADER_SOLID_THRESHOLD,
                ],
                [0, 1]
              )
            ),
      height: Math.max(
        COLLAPSED_HEADER_HEIGHT,
        interpolate(
          scrollOffset.value,
          [0, pullBack ? HEADER_SOLID_THRESHOLD - PULLBACK_OFFSET : HEADER_SOLID_THRESHOLD],
          pullBack
            ? [EXPANDED_HEADER_HEIGHT + 7, COLLAPSED_HEADER_HEIGHT + 7]
            : [EXPANDED_HEADER_HEIGHT, COLLAPSED_HEADER_HEIGHT]
        )
      ),
    };
  });

  const headerImageStyles = useAnimatedStyle(() => {
    return {
      transform: [
        {
          translateY: interpolate(
            scrollOffset.value,
            [0, pullBack ? HEADER_SOLID_THRESHOLD - PULLBACK_OFFSET : HEADER_SOLID_THRESHOLD],
            [
              EXPANDED_HEADER_HEIGHT / -4,
              insets.top > 24 ? -COLLAPSED_HEADER_HEIGHT : -COLLAPSED_HEADER_HEIGHT - insets.top * 2,
            ]
          ),
        },
      ],
    };
  });

  const pageIndicatorStyle = useAnimatedStyle(() => {
    return {
      opacity: withTiming(pageIndicatorOpacity.value, {
        duration: 250,
        easing: Easing.bezier(0.25, 0.1, 0.25, 1),
      }),
    };
  });

  const heroContainerStyles = useAnimatedStyle(() => {
    return {
      height: EXPANDED_HEADER_HEIGHT - scrollOffset.value,
      zIndex: !!images && images.length > 1 && scrollOffset.value < 8 && scrollOffset.value > -8 ? 4 : 0,
      elevation: !!images && images.length > 1 && scrollOffset.value < 8 && scrollOffset.value > -8 ? 4 : 0,
    };
  });

  const heroPositionStyles = useAnimatedStyle(() => {
    return {
      transform: [
        {
          translateY: scrollOffset.value / -2,
        },
      ],
      height: pullBack
        ? EXPANDED_HEADER_HEIGHT - Math.max(scrollOffset.value, 0) * 0.77
        : EXPANDED_HEADER_HEIGHT,
    };
  });

  const heroImagesStyles = useAnimatedStyle(() => {
    return {
      transform: [
        {
          scale: interpolate(-scrollOffset.value, [0, 150], [1, 2]),
        },
      ],
    };
  });

  const fallbackImage = require('@assets/images/image-not-available.jpg');

  const isLocalImage = !!images?.[activeImage] && typeof images[activeImage] === 'number';

  const remoteImage =
    !isLocalImage && !!images?.[activeImage] ? getImageUrlWithWidth(images[activeImage], 'm') : undefined;
  const cachedImage = useExternalImageCache(remoteImage);

  const skiaImage = useImage(
    isLocalImage ? images[activeImage] : remoteImage ? cachedImage || null : fallbackImage
  );

  const remoteImageLarge =
    !isLocalImage && !!images?.[activeImage] ? getImageUrlWithWidth(images[activeImage], 'xl') : undefined;
  const cachedImageLarge = useExternalImageCache(remoteImageLarge);

  const skiaImageLarge = useImage(
    isLocalImage ? images[activeImage] : remoteImageLarge ? cachedImageLarge || null : fallbackImage
  );

  return (
    <>
      <Animated.View
        style={[
          {
            position: 'absolute',
            left: 0,
            right: 0,
            width: screenWidth,
            height: EXPANDED_HEADER_HEIGHT,
            top: 0,
            overflow: 'hidden',
          },
          heroContainerStyles,
        ]}
      >
        <Animated.View
          style={[{ position: 'absolute', top: 0, left: 0, right: 0, bottom: 0 }, heroPositionStyles]}
        >
          <Animated.View
            style={[{ position: 'absolute', top: 0, left: 0, right: 0, bottom: 0 }, heroImagesStyles]}
          >
            {!!images && (images?.length || 0) > 1 ? (
              <Pager
                height={EXPANDED_HEADER_HEIGHT + 5}
                indicatorStyle={pageIndicatorStyle}
                onChange={(index: number) => setActiveImage(index)}
                images={images}
              />
            ) : !!skiaImageLarge && Platform.OS !== 'web' ? (
              <Canvas style={{ width: screenWidth, height: EXPANDED_HEADER_HEIGHT }}>
                <SkiaImageComp
                  skiaImageLarge={skiaImageLarge}
                  expandedHeaderHeight={EXPANDED_HEADER_HEIGHT}
                  screenWidth={screenWidth}
                  skiaBadge={skiaBadge}
                  hideBadge={hideBadge}
                />
              </Canvas>
            ) : (
              <LandalImage
                style={{ width: screenWidth, height: EXPANDED_HEADER_HEIGHT }}
                remoteSource={images?.[activeImage]}
              />
            )}
            {!!withGradient && (
              <LinearGradient
                colors={
                  gradientToTrueBlack
                    ? [
                        'rgba(0, 0, 0, 0.0)',
                        'rgba(0, 0, 0, 0.33)', // Change this gradient to match BG
                        theme.colors.trueBlack,
                      ]
                    : [
                        'rgba(0, 0, 0, 0.0)',
                        'rgba(0, 0, 0, 0.8)', // Change this gradient to match BG
                      ]
                }
                style={[
                  {
                    position: 'absolute',
                    width: screenWidth,
                    height: EXPANDED_HEADER_HEIGHT + 50,
                    top: 0,
                    backgroundColor: 'transparent', // required for gradient
                  },
                ]}
                pointerEvents="none"
              />
            )}
          </Animated.View>
          {pullBack && (
            <View
              style={{
                position: 'absolute',
                bottom: -200,
                width: '100%',
                backgroundColor: pullBackBackgroundColor || theme.colors.canvas,
                height: 200,
              }}
            />
          )}
        </Animated.View>
        <View // This gradient is behind the status bar
          style={{
            position: 'absolute',
            width: screenWidth,
            top: -EXPANDED_HEADER_HEIGHT,
          }}
          pointerEvents="none"
        >
          <View
            style={{
              width: screenWidth,
              height: EXPANDED_HEADER_HEIGHT,
              backgroundColor: 'rgba(0, 0, 0,0.4)',
            }}
            pointerEvents="none"
          />
          <LinearGradient
            colors={[
              'rgba(0, 0, 0,0.4)',
              'rgba(0, 0, 0,0.0)', // Change this gradient to match BG
            ]}
            style={[
              {
                width: screenWidth,
                height: EXPANDED_HEADER_HEIGHT / 2,
                backgroundColor: 'transparent', // required for gradient
              },
            ]}
            pointerEvents="none"
          />
        </View>
        {!!children && (
          <Animated.View style={[{}, headerContentStyles]} pointerEvents="none">
            {children}
          </Animated.View>
        )}
      </Animated.View>
      <Animated.View
        style={[
          headerAnimatedStyle,
          {
            position: 'absolute',
            top: 0,
            width: screenWidth,
            overflow: 'hidden',
            zIndex: 1,
            elevation: 1000,
            maxHeight: COLLAPSED_HEADER_HEIGHT + 36,
          },
        ]}
        pointerEvents="none"
      >
        <Animated.View
          style={[
            {
              position: 'absolute',
              width: screenWidth,
              height: EXPANDED_HEADER_HEIGHT,
              left: 0,
              top: -2,
            },
            headerImageStyles,
          ]}
        >
          <Canvas
            style={{
              width: screenWidth,
              height: EXPANDED_HEADER_HEIGHT,
              backgroundColor: '#DDD',
              position: 'relative',
              top: pullBack ? 36 : PULLBACK_OFFSET,
            }}
          >
            {!!skiaImage && (
              <SkiaImage
                x={0}
                y={0}
                width={screenWidth}
                height={EXPANDED_HEADER_HEIGHT}
                image={skiaImage}
                fit="cover"
              >
                <Blur blur={5} mode="clamp" />
              </SkiaImage>
            )}
            <Rect
              x={0}
              y={0}
              width={screenWidth}
              height={EXPANDED_HEADER_HEIGHT}
              color="black"
              opacity={0.3} // the 'shade' color opacity is usually 0.2, but with blur 0.3 looks more like it.
            />
          </Canvas>
        </Animated.View>
      </Animated.View>
      {close && (
        <RoundButton
          icon="close"
          iconColor="black"
          onPress={() => !!navigation && navigation.goBack()}
          style={{
            position: 'absolute',
            top: insets.top <= 24 ? insets.top + 6 : insets.top,
            left: theme.space.l,
            zIndex: 9,
            elevation: 9,
          }}
        />
      )}
      <Animated.View
        style={[
          {
            position: 'absolute',
            top:
              (insets.top <= 24 ? insets.top + 6 : insets.top) +
              (pullBack ? theme.space.l : theme.space.m) -
              1,
            alignSelf: close ? 'center' : 'flex-start',
            paddingHorizontal: theme.space.l,
            maxWidth: close ? screenWidth - 90 : screenWidth,
            overflow: 'hidden',
            zIndex: 20,
            elevation: 20,
          },
          titleStyle,
        ]}
        pointerEvents="none"
      >
        <Text type="title" size="l" color="white" bold numberOfLines={1} ellipsizeMode="tail">
          {title}
        </Text>
      </Animated.View>
      <View
        style={{
          position: 'absolute',
          left: 0,
          top: 0,
          right: 0,
          height: COLLAPSED_HEADER_HEIGHT,
          backgroundColor: 'rgba(0,0,0,0.0)',
          zIndex: 1,
          elevation: 1,
        }}
      />
    </>
  );
};

export default StickyImageHeader;
