import { gql, GraphQLClient } from 'graphql-request';
import Config from 'react-native-config';
import { UpdateMainTenantRequest, UpdateMainTenantResponse } from 'apis/_generated-reservation-api/models';
import { useMutation, UseQueryOptions } from 'react-query';
import { AxiosError } from 'axios';

export interface ErrorMessage {
  message: string;
  errorCode: number;
}

type Props = {
  options?: UseQueryOptions<UpdateMainTenantResponse, AxiosError>;
};

export function useMutateICE(props?: Props) {
  const performUpdateMainTenant = useMutation(async (request: UpdateMainTenantRequest) => {
    const graphQLClient = new GraphQLClient(Config.RESERVATION_API_URL);

    const query = gql`
      mutation updateMainTenant($request: UpdateMainTenantRequest) {
        updateMainTenant(updateMainTenantRequest: $request) {
          success
          errorMessage
        }
      }
    `;

    const response = await graphQLClient.rawRequest<{ updateMainTenant: UpdateMainTenantResponse }>(query, {
      request,
    });
    if (!response.data.updateMainTenant.success) {
      throw 'updating main tenant failed';
    }
    return response?.data?.updateMainTenant;
  }, props?.options);

  const updateMainTenant = async (request: UpdateMainTenantRequest) => {
    const result = await performUpdateMainTenant.mutateAsync(request);
    return result;
  };

  return { updateMainTenant };
}
