import { DeleteAccountRequest, DeleteAccountResponse } from 'apis/_generated-identity-api/models';
import { AxiosError } from 'axios';
import { gql, GraphQLClient } from 'graphql-request';
import Config from 'react-native-config';
import { UseQueryOptions, useMutation } from 'react-query';

type Props = {
  options?: UseQueryOptions<DeleteAccountResponse, AxiosError>;
};

export function useDeleteAccount(props?: Props) {
  const deleteAccountMutation = useMutation(async (request: DeleteAccountRequest) => {
    const graphQLClient = new GraphQLClient(Config.IDENTITY_API_URL);

    const query = gql`
      mutation deleteAccount($request: DeleteAccountRequest) {
        deleteAccount(request: $request) {
          success
        }
      }
    `;

    const response = await graphQLClient.rawRequest<{ deleteAccount: DeleteAccountResponse }>(query, {
      request,
    });
    return response.data?.deleteAccount;
  }, props?.options);

  const requestAccountDeletion = async (request: DeleteAccountRequest) => {
    const result = await deleteAccountMutation.mutateAsync(request);
    return result;
  };

  return { requestAccountDeletion };
}
