import React, { useCallback, useContext, useEffect, useRef, useState } from 'react';
import { Platform, ScrollView, View } from 'react-native';
import useTheme from '@app/hooks/useTheme';
import { useGetReservation } from '../../reservations/reservation/queries/useGetReservation';
import { useFocusEffect } from '@react-navigation/native';
import { UserContext } from '@app/context/usercontext/UserContextProvider';
import { getBrand } from 'utils/brand';
import useMetrics from '@app/metrics/useMetrics';
import ActionPad from '../components/ActionPad';
import { useGetReservationPark } from '@domains/park/contact-and-info/queries/useGetReservationPark';
import ScreenScrollContainer from '@app/components/ScreenScrollContainer';
import { useAnimatedScrollHandler, useSharedValue } from 'react-native-reanimated';
import PoiHighlights from '@domains/park/in-the-region/components/PoiHighlights';
import RoundButton from '@app/components/RoundButton';
import { useSafeAreaInsets } from 'react-native-safe-area-context';
import ListQuickAndEasy from '../components/ListQuickAndEasy';
import ListPractical from '../components/ListPractical';
import useActionPadButtons from '../hooks/useActionPadButtons';
import usePracticalItems from '@app/hooks/usePracticalItems';
import StickyImageHeader, { useStickyImageHeaderDimensions } from '@app/components/StickyImageHeader';
import useBadge from '@app/hooks/useBadge';
import { formatDayWithoutTime, fromUnixNumber, isTodayUnix } from 'utils/date';
import { useGetPark } from '@domains/park/contact-and-info/queries/useGetPark';
import ScreenContainer from '@app/components/ScreenContainer';
import { getFormattedDate } from 'utils/date';
import HeaderContent from '../components/HeaderContent';
import ExtrasSlider from '@domains/reservations/extras/components/ExtrasSlider';
import useHasUnreadMessages from '../hooks/useHasUnreadMessages';
import SaltoLoginWebview from '@domains/profile/screens/SaltoLoginWebview';
import { SaltoContext } from '@app/context/SaltoContextProvider';
import { BookingStatusType, Reservation } from 'apis/_generated-reservation-api/models';
import DigitalFun from '@domains/park/activities/components/DigitalFun';
import { NavScreenProp } from '@app/navigation/stacks/Main';
import ParkHasOwnApp, { ParkHasOwnAppWrapper } from '@app/components/ParkHasOwnApp';
import useHasOwnApp from '@app/hooks/useHasOwnApp';
import useGoodToKnowItems from '@app/hooks/useGoodToKnowItems';

interface Props extends NavScreenProp<'Start'> {}

export default function Start({ navigation }: Props) {
  const theme = useTheme();
  const { token, demoMode } = useContext(UserContext);

  const { logScreenView, setUserProperty, requestMessagingPermissions } = useMetrics();

  const { data: reservationPark } = useGetReservationPark();
  const insets = useSafeAreaInsets();
  const { saltoUserToken } = React.useContext(SaltoContext);
  const [lockLoginProcessing, setLockLoginProcessing] = useState(!saltoUserToken);

  const hasUnreadMessages = useHasUnreadMessages();
  const scrollRef = useRef<ScrollView>(null);
  const isInFocus = useRef<boolean>();

  const brand = getBrand(theme);
  const { data: reservation } = useGetReservation({
    brand,
    token,
  });
  const actionPadButtons = useActionPadButtons({
    gettingKeyReady: demoMode ? false : lockLoginProcessing,
    reservationParkCode: reservation?.parkCode,
  });
  const { data: park } = useGetPark({ parkCodeOverride: reservation?.parkCode });
  const { hasOwnApp } = useHasOwnApp(park?.id);

  const arrivalDate =
    !!reservation?.arrivalDate && !!reservation?.departureDate
      ? `${formatDayWithoutTime(fromUnixNumber(reservation.arrivalDate * 1000))} - ${formatDayWithoutTime(
          fromUnixNumber(reservation.departureDate * 1000)
        )}`
      : null;

  const { hasGoodToKnowItems } = useGoodToKnowItems(park?.id);

  let requestPracticalItems = [{ id: 'reservation-details' }];
  if (reservation?.holidayStarted) {
    requestPracticalItems = [...requestPracticalItems, { id: 'report-damage' }];
  }
  requestPracticalItems = [...requestPracticalItems, { id: 'contact-reception' }];

  if (hasGoodToKnowItems) {
    requestPracticalItems = [...requestPracticalItems, { id: 'good-to-know' }];
  }

  const practicalItems = usePracticalItems({
    items: requestPracticalItems,
    parkCode: reservation?.parkCode,
  });

  const requestUserPermission = useCallback(async () => {
    await requestMessagingPermissions();
  }, [requestMessagingPermissions]);

  useEffect(() => {
    Platform.OS !== 'web' && requestUserPermission();
  }, [requestUserPermission]);

  useFocusEffect(
    useCallback(() => {
      isInFocus.current = true;
      logScreenView('Start');
      return () => {
        isInFocus.current = false;
      };
    }, [logScreenView])
  );

  useEffect(() => {
    return navigation.addListener('tabPress', (e) => {
      if (isInFocus.current === true) {
        scrollRef.current?.scrollTo({ y: 0, animated: true });
      }
    });
  }, [navigation]);

  const openMessages = () => {
    navigation.navigate('Notifications');
  };

  const scrollOffset = useSharedValue(0);
  const scrollHandler = useAnimatedScrollHandler((event) => {
    scrollOffset.value = event.contentOffset.y;
  });

  const { EXPANDED_HEADER_HEIGHT, size } = useStickyImageHeaderDimensions(
    !reservation || hasOwnApp ? 'stretch' : 'l'
  );

  const parkImage = !!token && !!reservation ? park?.images?.[0] : undefined;

  const headerImage = parkImage
    ? parkImage
    : theme.type === 'hofvansaksen'
    ? require('@assets/images/hsn/start.jpg')
    : require('@assets/images/start.jpg');

  const previousReservation = useRef<Reservation | undefined>();
  useEffect(() => {
    if (
      !!reservation &&
      isTodayUnix(reservation.arrivalDate || 0) &&
      previousReservation.current?.status === BookingStatusType.Arrived
    ) {
      // switched to a new reservation which starts at the same day?
      // previous reservation was 'Arrived'? Current reservation started today, so it's party time!!!
      // alert('WE GAAN NOG NIET NAAR HUIS!!!');
    }

    // Send the properties of this reservation for metrics purposes
    if (previousReservation.current !== reservation) {
      const reservationParkCode = reservation?.parkCode || null;
      const reservationArrivalDay = reservation?.arrivalDate;
      const reservationDepartureDay = reservation?.departureDate;
      const reservationBookingReference = reservation?.bookingReference || null;
      setUserProperty('booking_reference', reservationBookingReference);
      setUserProperty('park_reservation', reservationParkCode);
      setUserProperty(
        'arrival_date',
        reservation?.arrivalDate
          ? getFormattedDate(fromUnixNumber((reservationArrivalDay ?? 0) * 1000))
          : null
      );
      setUserProperty(
        'departure_date',
        reservationDepartureDay
          ? getFormattedDate(fromUnixNumber((reservationDepartureDay ?? 0) * 1000))
          : null
      );
    }

    previousReservation.current = reservation;
  }, [reservation, setUserProperty]);

  const { reactBadge, skiaBadge } = useBadge({
    text: arrivalDate ?? '',
    icon: 'calendar',
    y: insets.top + theme.space.m,
    scrollOffset,
  });

  return (
    <>
      <ScreenContainer
        key={reservation?.bookingReference || '' + reservation?.parkCode || ''}
        style={{ backgroundColor: hasOwnApp ? theme.colors.trueBlack : undefined }}
      >
        <StickyImageHeader
          scrollOffset={scrollOffset}
          title={reservationPark?.name}
          titleOverImage={false}
          images={headerImage ? [headerImage] : []}
          size={size}
          pullBack
          withGradient={true}
          close={false}
          skiaBadge={!!arrivalDate && skiaBadge}
          pullBackBackgroundColor={hasOwnApp ? theme.colors.trueBlack : undefined}
          gradientToTrueBlack={hasOwnApp}
        >
          {!!arrivalDate && reactBadge}
        </StickyImageHeader>

        <RoundButton
          onPress={openMessages}
          icon="notification-inactive"
          style={{
            position: 'absolute',
            right: theme.space.l,
            top: insets.top + theme.space.s,
          }}
          showBadge={hasUnreadMessages}
        />

        <ScreenScrollContainer
          onScroll={scrollHandler}
          scrollEventThrottle={1}
          topPadding={false}
          animated
          forceBottomInset
          scrollRef={scrollRef}
        >
          <View
            style={{
              flexDirection: 'column',
              paddingTop: EXPANDED_HEADER_HEIGHT,
            }}
          >
            <HeaderContent scrollOffset={scrollOffset} />
            {!!actionPadButtons && <ActionPad buttons={actionPadButtons} />}
            {hasOwnApp && (
              <ParkHasOwnAppWrapper>
                <ParkHasOwnApp parkCode={park?.id} />
              </ParkHasOwnAppWrapper>
            )}
            {!!token && !hasOwnApp && (
              <View>
                <ListQuickAndEasy parkCode={reservation?.parkCode} />
                {!!reservation?.parkCode && <PoiHighlights parkCode={reservation?.parkCode} />}
                {!!reservation && <ListPractical items={practicalItems} />}
                {!!token &&
                  !!reservation &&
                  !!reservation?.featuresEnabled?.extras &&
                  !reservation?.combiReference && (
                    <ExtrasSlider showShowAll bookingReference={reservation?.bookingReference} />
                  )}
                {!!reservation?.parkCode && <DigitalFun parkCode={reservation?.parkCode} />}
              </View>
            )}
          </View>
        </ScreenScrollContainer>
      </ScreenContainer>
      {!!token && !saltoUserToken && !demoMode && (
        <View style={{ position: 'absolute', width: 1, height: 1 }}>
          <SaltoLoginWebview
            onSuccess={async () => {
              setLockLoginProcessing(false);
            }}
            onReady={() => {}}
            onError={(_) => {
              setLockLoginProcessing(false);
            }}
            onLoadStart={() => {
              setLockLoginProcessing(true);
            }}
          />
        </View>
      )}
    </>
  );
}
