import React from 'react';
import { createNativeStackNavigator, NativeStackNavigationProp } from '@react-navigation/native-stack';
import FoodAndDrinks from '@domains/park/food-and-drink/screens/FoodAndDrinks';
import FoodAndDrinksDetail from '@domains/park/food-and-drink/screens/FoodAndDrinksDetail';
import TableBooking from '@domains/park/food-and-drink/screens/TableBooking';
import ProductCategories from '@domains/park/shop/screens/ProductCategories';
import ProductOptions from '@domains/park/shop/screens/ProductOptions';
import Cart from '@domains/park/shop/screens/Cart';
import CompleteOrder from '@domains/park/shop/screens/CompleteOrder';
import QuantityPopup from '@domains/park/shop/screens/QuantityPopup';
import TableBookingSuccessful from '@domains/park/food-and-drink/screens/TableBookingSuccessful';
import { RouteProp } from '@react-navigation/native';
import { FacilityMenu } from 'apis/_generated-park-api/models';

// Types of params passed to route via navigation.navigate('SCREEN_NAME', params)
export type FoodAndDrinksRoutes = {
  FoodAndDrinks: { parkCode: string };
  FoodAndDrinksDetail: { id: string };
  TableBooking: { restaurantId: string };
  TableBookingSuccessful: { restaurantName: string; timestamp: number; timestring: string; guests: number };
  FacilityBookingSuccessful: { reservationId: string; date: Date; amount: number };
  FacilityBooking: { reservationId: string };
  ProductCategories: { shopId: number; facilityId: string };
  ProductOptions: { id: number; categoryId: number };
  QuantityPopup: { id: number; attributeComboId?: number };
  Cart: { shopId: number; facilityId: string };
  CompleteOrder: { shopId: number; facilityId: string };
  PaymentConfirmation: { shopId: number; status: string };
  FoodMenu: { title: string; menu: FacilityMenu };
};

type RouteNames = keyof FoodAndDrinksRoutes;
export interface NavScreenProp<T extends RouteNames> {
  navigation: NativeStackNavigationProp<FoodAndDrinksRoutes, T>;
  route: RouteProp<FoodAndDrinksRoutes, T>;
}
const FoodAndDrinksStack = createNativeStackNavigator<FoodAndDrinksRoutes>();

export const useFoodAndDrinksScreens = (stack: any) => {
  return (
    <>
      <stack.Screen name="FoodAndDrinks" component={FoodAndDrinks} />
      <stack.Screen name="FoodAndDrinksDetail" component={FoodAndDrinksDetail} />
      <stack.Screen name="TableBooking" component={TableBooking} />
      <stack.Screen name="TableBookingSuccessful" component={TableBookingSuccessful} />
      {/* <stack.Screen name="FoodMenu" component={FoodMenu} /> */}
      <stack.Screen name="ProductCategories" component={ProductCategories} />
      <stack.Screen name="ProductOptions" component={ProductOptions} />
      <stack.Screen name="Cart" component={Cart} />
      <stack.Screen
        name="QuantityPopup"
        component={QuantityPopup}
        options={{
          presentation: 'transparentModal',
          animation: 'fade',
          contentStyle: {
            backgroundColor: 'transparent',
          },
        }}
      />
      <stack.Screen name="CompleteOrder" component={CompleteOrder} />
    </>
  );
};

export default function FoodAndDrinksNav({}: NavScreenProp<'FoodAndDrinks'>) {
  const screens = useFoodAndDrinksScreens(FoodAndDrinksStack);

  return (
    <FoodAndDrinksStack.Navigator
      backBehavior="history"
      screenOptions={{
        headerShown: false,
        orientation: 'portrait_up',
        presentation: 'fullScreenModal',
        animation: 'default',
      }}
    >
      {screens}
    </FoodAndDrinksStack.Navigator>
  );
}
