import useTheme from '@app/hooks/useTheme';
import { useCallback } from 'react';

const useSurvicate = () => {
  const theme = useTheme();
  const landal = theme.type === 'landal';

  const leaveScreen = useCallback((routeName: string) => {}, []);
  const enterScreen = useCallback((routeName: string) => {}, []);
  const invokeEvent = useCallback((eventName: string) => {}, []);

  return { leaveScreen, enterScreen, invokeEvent };
};

export default useSurvicate;
