import useTheme from '@app/hooks/useTheme';
import { BASELINE_INSET } from '@app/theme/partials/sizes';
import React, { PropsWithChildren } from 'react';
import { View } from 'react-native';
import Shadow from '../Shadow';

interface Props {}

export default function TextContainer({ children }: PropsWithChildren<Props>) {
  const theme = useTheme();

  return (
    <View style={{ marginBottom: theme.space.m }}>
      <Shadow>
        <View
          style={{
            backgroundColor: theme.colors.white,
            marginHorizontal: -BASELINE_INSET - theme.space.m,
            paddingHorizontal: theme.space.l,
            paddingVertical: theme.space.xl,
            borderBottomLeftRadius: 8,
            borderBottomEndRadius: 8,
          }}
        >
          {children}
        </View>
      </Shadow>
    </View>
  );
}
