import React from 'react';
import useTheme from '@hooks/useTheme';
import Svg, { Path, Defs, LinearGradient, Stop } from 'react-native-svg';
import Icon from '../Icon';
import { View } from 'react-native';
import { AllColors } from '@app/theme/partials/general';

export type LockSheetStatus = 'success' | 'failed' | 'attention';

interface Props {
  status: LockSheetStatus;
  size?: number;
  icon: string;
}

export default function LockStatusSVG({ status, icon, size = 90 }: Props) {
  const theme = useTheme();

  let gradient1 = '';
  let gradient2 = '';
  let gradient3 = '';
  let statusColor: AllColors = 'primary';
  let statusIcon = '';

  switch (status) {
    case 'success':
      gradient1 = theme.colors.gradientSuccess1;
      gradient2 = theme.colors.gradientSuccess2;
      gradient3 = theme.colors.gradientSuccess3;
      statusColor = 'primary';
      statusIcon = 'check-mark';
      break;
    case 'failed':
      gradient1 = theme.colors.gradientDanger1;
      gradient2 = theme.colors.gradientDanger2;
      statusColor = 'gradientDanger2';
      statusIcon = 'cross';

      break;
    case 'attention':
      gradient1 = theme.colors.gradientWarning1;
      gradient2 = theme.colors.gradientWarning2;
      statusColor = 'gradientWarning2';
      statusIcon = 'exclamation-mark';
      break;
  }

  const gradientA = gradient3 ? (
    <LinearGradient id="a" x1={0} y1={0} x2={90} y2={90} gradientUnits="userSpaceOnUse">
      <Stop stopColor={gradient1} />
      <Stop offset={0.495} stopColor={gradient2} />
      <Stop offset={1} stopColor={gradient3} />
    </LinearGradient>
  ) : (
    <LinearGradient id="a" x1={0} y1={0} x2={90} y2={90} gradientUnits="userSpaceOnUse">
      <Stop stopColor={gradient1} />
      <Stop offset={1} stopColor={gradient2} />
    </LinearGradient>
  );
  const gradientB = gradient3 ? (
    <LinearGradient id="b" x1={6} y1={6} x2={84} y2={84} gradientUnits="userSpaceOnUse">
      <Stop stopColor={gradient1} />
      <Stop offset={0.495} stopColor={gradient2} />
      <Stop offset={1} stopColor={gradient3} />
    </LinearGradient>
  ) : (
    <LinearGradient id="b" x1={6} y1={6} x2={84} y2={84} gradientUnits="userSpaceOnUse">
      <Stop stopColor={gradient1} />
      <Stop offset={1} stopColor={gradient2} />
    </LinearGradient>
  );

  return (
    <View style={{ width: size, height: size }}>
      <Svg width={size} height={size} fill="none" viewBox="0 0 90 90">
        <Path
          opacity={0.2}
          fillRule="evenodd"
          clipRule="evenodd"
          d="M45 89c24.3 0 44-19.7 44-44S69.3 1 45 1 1 20.7 1 45s19.7 44 44 44Zm0 1c24.853 0 45-20.147 45-45S69.853 0 45 0 0 20.147 0 45s20.147 45 45 45Z"
          fill="url(#a)"
        />
        <Path
          fillRule="evenodd"
          clipRule="evenodd"
          d="M8 45c0 20.434 16.566 37 37 37s37-16.566 37-37S65.434 8 45 8 8 24.566 8 45ZM45 6C23.46 6 6 23.46 6 45s17.46 39 39 39 39-17.46 39-39S66.54 6 45 6Z"
          fill="url(#b)"
        />
        <View style={{ width: size, height: size, alignItems: 'center', justifyContent: 'center' }}>
          <Icon name={icon} size="xxl" color={statusColor} />
        </View>
        <Defs>
          {gradientA}
          {gradientB}
        </Defs>
      </Svg>
      <View
        style={{
          position: 'absolute',
          right: 0,
          width: 30,
          height: 30,
          borderRadius: 15,
          backgroundColor: theme.colors[statusColor],
          alignItems: 'center',
          justifyContent: 'center',
        }}
      >
        <Icon name={statusIcon} size="s" color={'white'} />
      </View>
    </View>
  );
}
