import useCart from '@app/hooks/useCart';
import useCulture from '@app/hooks/useCulture';
import useSelectedPark from '@app/hooks/useSelectedPark';
import { ShopOrderRequest, ShopOrderResponse } from 'apis/_generated-park-api/models';
import { AxiosError } from 'axios';
import { gql, GraphQLClient } from 'graphql-request';
import Config from 'react-native-config';
import { UseQueryOptions, useMutation } from 'react-query';

type Props = {
  options?: UseQueryOptions<ShopOrderResponse, AxiosError>;
};

export function usePostOrder(props?: Props) {
  const { activeShop: shopId } = useCart();
  const { parkCode } = useSelectedPark();
  const { cultureCode } = useCulture();

  const postOrderMutation = useMutation(async (order: ShopOrderRequest) => {
    const graphQLClient = new GraphQLClient(Config.PARK_API_URL);

    const query = gql`
      mutation shopPostOrder($order: ShopOrderRequest) {
        shopPostOrder(order: $order, shopId: "${shopId}", cultureCode: "${cultureCode}", parkCode: "${parkCode}" ) {
          paymentUrl
          orderNumber
        }
      }
    `;

    const response = await graphQLClient.rawRequest<{ shopPostOrder: ShopOrderResponse }>(query, {
      order,
    });
    return response.data?.shopPostOrder;
  }, props?.options);

  const postOrder = async (order: ShopOrderRequest) => {
    const result = await postOrderMutation.mutateAsync(order);
    return result;
  };

  return { postOrder };
}
