import React, { useState } from 'react';

export const BottomTabsContextUpdater = React.createContext<React.Dispatch<React.SetStateAction<number>>>(
  () => {}
);
export const BottomTabsContext = React.createContext<number>(0);

export function BottomTabsProvider({ children }: { children: React.ReactNode }) {
  const [activeTab, setActiveTab] = useState(0);

  return (
    <BottomTabsContext.Provider value={activeTab}>
      <BottomTabsContextUpdater.Provider value={setActiveTab}>{children}</BottomTabsContextUpdater.Provider>
    </BottomTabsContext.Provider>
  );
}
