import Icon from '@app/components/Icon';
import { LandalPressable } from '@app/components/LandalPressable';
import Text from '@app/components/Text';
import useTheme from '@app/hooks/useTheme';
import React, { ReactNode } from 'react';
import { View, ViewStyle } from 'react-native';

interface Props {
  onPress: () => void;
  label: string;
  text: string;
  icon?: string;
  iconSvg?: ReactNode;
  sticky?: boolean;
  style?: ViewStyle;
}
export default function Banner({ onPress, label, text, icon, iconSvg, sticky, style }: Props) {
  const theme = useTheme();

  const stickyStyle = { paddingBottom: theme.space.m * 3, top: theme.space.m * 2 };

  return (
    <LandalPressable
      style={[
        {
          flex: 1,
          backgroundColor: theme.colors.black,
          borderRadius: 12,
          paddingVertical: theme.space.m,
          paddingHorizontal: theme.space.l,
          flexDirection: 'row',
          elevation: 1,
        },
        sticky && stickyStyle,
        style,
      ]}
      onPress={onPress}
    >
      <View style={{ marginRight: theme.space.m, justifyContent: 'center' }}>
        <View
          style={{
            alignItems: 'center',
            justifyContent: 'center',
          }}
        >
          {icon && <Icon name={icon} size={'xxl'} color="primary" />}
          {!!iconSvg && iconSvg}
        </View>
      </View>
      <View style={{ justifyContent: 'space-evenly', flex: 1 }}>
        <Text color="white" bold>
          {label}
        </Text>
        <Text type="body" color="light" numberOfLines={1} ellipsizeMode="tail">
          {text}
        </Text>
      </View>
    </LandalPressable>
  );
}
