import React, { useMemo } from 'react';
import {
  FlatList,
  NativeScrollEvent,
  NativeSyntheticEvent,
  useWindowDimensions,
  View,
  ViewProps,
  ViewStyle,
} from 'react-native';
import useTheme from '@hooks/useTheme';
import Text from '@app/components/Text';
import ActivitiesDate from '../screens/ActivitiesDate';
import { firstToUpper, getAnalyticsFriendlyDate, ReadableDayByDate } from 'utils/date';
import groupActivityByDate from '../data/groupActivityByDate';
import { useGetActivities } from '../queries/useGetActivities';
import Icon from '@app/components/Icon';
import { useFocusEffect, useNavigationState } from '@react-navigation/native';
import useMetrics from '@app/metrics/useMetrics';
import { useIntl } from 'react-intl';
import { useGetPark } from '@domains/park/contact-and-info/queries/useGetPark';
import LandalImage from '@app/components/LandalImage';
import PoiHighlights from '@domains/park/in-the-region/components/PoiHighlights';
import ScreenScrollContainer from '@app/components/ScreenScrollContainer';
import { LandalPressable } from '@app/components/LandalPressable';
import Loader from '@app/components/Loader';
import ListItems, { ListItem } from '@app/components/ListItems2/ListItems';
import { useGetPromos } from '@domains/promo/queries/useGetPromos';
import { getDataBySubCat } from '@domains/promo/helpers/getDataBySubCat';
import DigitalFun from './DigitalFun';

interface Props extends React.PropsWithChildren<ViewProps> {
  parkCode?: string;
  facilityId?: string;
  style?: ViewStyle;
}

export default function ActivityNavigation({ facilityId, style, parkCode }: Props) {
  const theme = useTheme();
  const flatListRef = React.useRef<FlatList>(null);
  const navigationState = useNavigationState((state) => state);
  const { formatMessage } = useIntl();
  const { width: screenWidth } = useWindowDimensions();
  const { logEvent } = useMetrics();

  const AMOUNT_OF_DAYS = 14;
  const FIRST_PAGE = 0;
  const { data, isLoading } = useGetActivities({ parkCodeOverride: parkCode });
  const [page, setPage] = React.useState(FIRST_PAGE);
  const { data: park } = useGetPark({ parkCodeOverride: parkCode });
  const { data: promos } = useGetPromos({ parkCodeOverride: parkCode });

  const isOnFacilityDetailScreen = !!facilityId;
  let activities = data?.sortedData;
  if (isOnFacilityDetailScreen) {
    activities = activities?.filter((act) => act.locationId === facilityId);
  }

  const { getActivitiesForDate, allDates, getNextDateWithActivities } = groupActivityByDate({
    data: activities,
    startDate: data ? data?.date : new Date().toString(),
    amountOfDays: AMOUNT_OF_DAYS,
  });

  useFocusEffect(
    React.useCallback(() => {
      // If we have a stack length of 1, we just switched to another main stack.
      // In that case, reset the pager to the first one.
      if (
        navigationState.routes.length === 1 &&
        !navigationState.routeNames.find((r) => r === 'ActivitiesDetail')
      ) {
        setPage(FIRST_PAGE);
        flatListRef.current?.scrollToIndex({ index: 0, animated: false });
      }
    }, [navigationState])
  );
  useFocusEffect(
    React.useCallback(() => {
      logEvent('ShowActivities', { day: getAnalyticsFriendlyDate(allDates[page]?.date) });
    }, [allDates, logEvent, page])
  );

  const LAST_PAGE = () => {
    return allDates.length - 1;
  };

  const scrollTo = (date: Date) => {
    const pageNumber = allDates.findIndex((d) => d.date === date);
    if (pageNumber >= FIRST_PAGE && pageNumber <= LAST_PAGE()) {
      flatListRef.current?.scrollToIndex({ index: pageNumber });
    }
  };

  const onScroll = (e: NativeSyntheticEvent<NativeScrollEvent>) => {
    let contentOffset = e.nativeEvent.contentOffset;
    let viewSize = e.nativeEvent.layoutMeasurement;

    // Divide the horizontal offset by the width of the view to see which page is visible
    let pageNum = Math.round(contentOffset.x / viewSize.width);
    if (page !== pageNum) {
      setPage(pageNum);
    }
  };

  const reservationOrderItems: ListItem[] = useMemo(() => {
    const items =
      promos
        ?.filter((p) => p.hoofdCat === 'fe' && !['adventure'].includes(p.subCat || ''))
        .map((p, index) => {
          const categoryData = getDataBySubCat(p.subCat || '', formatMessage, p.imageUrl);
          return {
            id: p.shopId || p.name || index,
            title: p.name || categoryData.name,
            subtitle: categoryData.sub,
            image: categoryData.imageUrl,
            localImage: categoryData.imageUrl ? undefined : categoryData.imageLocal,
            linkTo: p.url,
          };
        }) ?? [];
    return items;
  }, [formatMessage, promos]);

  if (isOnFacilityDetailScreen && !allDates.find((a) => a.hasActivities)) {
    return null;
  }

  if (!activities || activities.length === 0) {
    return (
      <ScreenScrollContainer contentContainerStyle={{ alignItems: 'center' }}>
        {isLoading ? (
          <Loader />
        ) : (
          <>
            {reservationOrderItems.length > 0 && (
              <ListItems
                title={formatMessage({ id: 'app.promo.reservations' })}
                items={reservationOrderItems ?? []}
                usePlaceholderImage
                style={{ width: '100%', marginTop: 0, top: -theme.space.l }}
              />
            )}
            <LandalImage
              style={{
                height: screenWidth * 0.4,
                width: screenWidth * 0.4,
                marginBottom: theme.space.xl,
              }}
              localSource={
                theme.type === 'hofvansaksen'
                  ? require('@app/assets/images/hsn/oops.png')
                  : require('@app/assets/images/oops.png')
              }
            />
            <Text type="title" size="xl" bold style={{ marginBottom: theme.space.m, textAlign: 'center' }}>
              {formatMessage({ id: 'app.activities.empty.title' }).replace('{0}', park?.name || '')}
            </Text>
            <Text color="dark" style={{ marginBottom: theme.space.xl, textAlign: 'center' }}>
              {formatMessage({ id: 'app.activities.empty.text' })}
            </Text>
            <View style={{ flex: 1, width: '100%' }}>
              <DigitalFun parkCode={parkCode} />
            </View>
            <View style={{ flex: 1, width: '100%' }}>
              <PoiHighlights parkCode={park?.id} />
            </View>
          </>
        )}
      </ScreenScrollContainer>
    );
  }

  return (
    <View
      style={[
        {
          marginLeft: isOnFacilityDetailScreen ? -theme.space.l : 0,
          marginRight: isOnFacilityDetailScreen ? -theme.space.l : 0,
          backgroundColor: theme.colors.canvas,
        },
        style,
      ]}
    >
      <View
        style={{
          height: 40,
          flexDirection: 'row',
          justifyContent: 'space-between',
          alignItems: 'center',
          paddingHorizontal: theme.space.m,
          backgroundColor: theme.colors.white,
          borderColor: '#E8E8E8',
          borderBottomWidth: isOnFacilityDetailScreen ? 0 : 1,
        }}
      >
        <LandalPressable
          disabled={page === FIRST_PAGE}
          onPress={() => {
            page > FIRST_PAGE && scrollTo(allDates[page - 1]?.date);
          }}
        >
          <Icon name={'back'} size="m" color={page === FIRST_PAGE ? 'light' : 'gray'} />
        </LandalPressable>
        <Text size="s" color={'dark'} bold>
          {firstToUpper(ReadableDayByDate(allDates[page]?.date ?? new Date(), true))}
        </Text>
        <LandalPressable
          disabled={page === LAST_PAGE()}
          onPress={() => {
            page < LAST_PAGE() && scrollTo(allDates[page + 1]?.date);
          }}
        >
          <Icon name={'forward'} size="m" color={page === LAST_PAGE() ? 'light' : 'gray'} />
        </LandalPressable>
      </View>
      <FlatList
        horizontal
        pagingEnabled
        renderToHardwareTextureAndroid
        shouldRasterizeIOS
        data={allDates}
        showsHorizontalScrollIndicator={false}
        renderItem={({ item }: { item: typeof allDates[0] }) => (
          <ActivitiesDate
            activities={activities && getActivitiesForDate(item.date)}
            date={item.date}
            nextDate={getNextDateWithActivities(item.date, allDates)}
            scrollTo={scrollTo}
            facilityId={facilityId}
            isLoading={isLoading}
            parkCode={parkCode}
            promoSectionItems={
              !isOnFacilityDetailScreen && reservationOrderItems.length > 0
                ? reservationOrderItems
                : undefined
            }
            promoSectionTitle={formatMessage({ id: 'app.promo.reservations' })}
          />
        )}
        ref={flatListRef}
        onScroll={onScroll}
        keyExtractor={(item) => item.date.toString()}
        onScrollToIndexFailed={(info) => {
          const wait = new Promise((resolve) => setTimeout(resolve, 500));
          wait.then(() => {
            flatListRef.current?.scrollToIndex({ index: info.index, animated: true });
          });
        }}
        initialNumToRender={1}
        maxToRenderPerBatch={2}
        windowSize={5}
      />
    </View>
  );
}
