import React from 'react';
import { TextInputProps, View } from 'react-native';
import useTheme from '@hooks/useTheme';
import { TextInput } from 'react-native-gesture-handler';
import Icon, { iconSizes } from '../Icon';

const INPUT_HEIGHT = 50;

interface Props extends TextInputProps {
  isValid?: boolean;
  showIcon?: boolean;
  light?: boolean;
}

const FormInput = React.forwardRef<TextInput, Props>(
  ({ style, showIcon = true, light = true, ...props }, ref) => {
    const theme = useTheme();
    const { isValid, multiline } = props;

    return (
      <View>
        <TextInput
          ref={ref}
          style={[
            {
              fontFamily: theme.fonts.bodyBold,
              color: theme.colors.dark,
              fontSize: theme.fontSizes.m.fontSize,
              width: '100%',
              height: multiline ? 120 : INPUT_HEIGHT,
              paddingHorizontal: theme.space.l,
              paddingTop: multiline ? 16 : undefined,
              borderRadius: 8,
              backgroundColor: theme.colors.white,
              borderWidth: 1,
              borderColor: isValid ? theme.colors.light : theme.colors.attention,
              textAlignVertical: multiline ? 'top' : undefined,
            },
            style,
          ]}
          numberOfLines={3}
          selectionColor={theme.colors.gray}
          {...props}
        />
        {!multiline && !isValid && (
          <Icon
            name="exclamation-mark"
            size="s"
            color={'attention'}
            style={{ position: 'absolute', right: 18, bottom: INPUT_HEIGHT / 2 - iconSizes.s / 2 }}
          />
        )}
      </View>
    );
  }
);

export default FormInput;
