import React, { useContext } from 'react';
import useTheme from '@app/hooks/useTheme';
import { BottomTabBarProps, createBottomTabNavigator } from '@react-navigation/bottom-tabs';
import Icon from '@app/components/Icon';
import ReservationsScreen from '@domains/reservations/reservation/screens/Reservations';
import Text from '@app/components/Text';
import { Platform, TouchableOpacity } from 'react-native';
import StickyBannerProvider from '@app/context/StickyBannerProvider';
import { useIntl } from 'react-intl';
import StickyBottomBar from './StickyBottomBar';
import ParkScreen from '@domains/start/screens/Park';
import Start from '@domains/start/screens/Start';
import Profile from '@domains/profile/screens/Profile';
import { BottomTabsContextUpdater, BottomTabsProvider } from './BottomTabsProvider';
import { LabelPosition } from '@react-navigation/bottom-tabs/lib/typescript/src/types';
import useHasUnreadMessages from '@domains/start/hooks/useHasUnreadMessages';

interface TabBarIconProps {
  focused: boolean;
  iconFocused: string;
  iconUnfocused: string;
  showBadge?: boolean;
}
export const TabBarIcon = ({ focused, iconFocused, iconUnfocused, showBadge }: TabBarIconProps) => {
  return (
    <Icon
      name={focused ? iconFocused : iconUnfocused}
      size="l"
      color={focused ? 'primary' : 'gray'}
      showBadge={showBadge}
    />
  );
};

interface TabBarLabelProps {
  focused: boolean;
  label: string | ((props: { focused: boolean; color: string; position: LabelPosition }) => React.ReactNode);
}
const TabBarLabel = ({ focused, label }: TabBarLabelProps) => {
  const theme = useTheme();
  return (
    <Text
      style={{
        fontWeight: focused ? 'bold' : undefined,
        fontSize: 12,
        lineHeight: 16,
        letterSpacing: focused ? (Platform.OS === 'ios' ? -0.2 : 0.1) : 0.2,
        color: focused ? theme.colors.primary : theme.colors.gray,
        paddingTop: theme.space.s,
      }}
      numberOfLines={1}
      ellipsizeMode="clip"
    >
      {label}
    </Text>
  );
};

export const BOTTOM_TAB_BAR_HEIGHT = 114;
export const BOTTOM_TAB_BAR_HEIGHT_NO_INSET = 78;

function CustomTabBar({ state, descriptors, navigation }: BottomTabBarProps) {
  return (
    <StickyBottomBar>
      {state.routes.map((route, index) => {
        const { options } = descriptors[route.key];
        const label =
          options.tabBarLabel !== undefined
            ? options.tabBarLabel
            : options.title !== undefined
            ? options.title
            : route.name;

        const isFocused = state.index === index;

        // @ts-ignore; we only use the focused prop
        const icon = options.tabBarIcon?.({ focused: isFocused });

        const onPress = () => {
          const event = navigation.emit({
            type: 'tabPress',
            target: route.key,
            canPreventDefault: true,
          });

          if (!isFocused && !event.defaultPrevented) {
            // The `merge: true` option makes sure that the params inside the tab screen are preserved
            navigation.navigate({ name: route.name, merge: true });
          }
        };

        const onLongPress = () => {
          navigation.emit({
            type: 'tabLongPress',
            target: route.key,
          });
        };

        return (
          <TouchableOpacity
            key={route.name}
            accessibilityRole="button"
            accessibilityState={isFocused ? { selected: true } : {}}
            accessibilityLabel={options.tabBarAccessibilityLabel}
            testID={options.tabBarTestID}
            onPress={onPress}
            onLongPress={onLongPress}
            style={{
              paddingBottom: 14,
              paddingTop: 12,
              flex: 1,
              alignItems: 'center',
              justifyContent: 'center',
            }}
            activeOpacity={1}
          >
            {icon}
            <TabBarLabel focused={isFocused} label={label} />
          </TouchableOpacity>
        );
      })}
    </StickyBottomBar>
  );
}

export default function BottomTabs() {
  return (
    <BottomTabsProvider>
      <InnerBottomTabs />
    </BottomTabsProvider>
  );
}

const Tab = createBottomTabNavigator();
function InnerBottomTabs() {
  const theme = useTheme();
  const { formatMessage } = useIntl();
  const setActiveTab = useContext(BottomTabsContextUpdater);
  const hasUnreadMessages = useHasUnreadMessages();

  return (
    <StickyBannerProvider>
      <Tab.Navigator
        screenOptions={{
          headerShown: false,
          tabBarInactiveTintColor: theme.colors.gray,
          tabBarActiveTintColor: theme.colors.primary,
        }}
        initialRouteName={'StartTab'}
        tabBar={(props) => <CustomTabBar {...props} />}
        screenListeners={{
          state: (e) => {
            setActiveTab(e.data.state.index);
          },
        }}
      >
        <Tab.Screen
          name="StartTab"
          component={Start}
          options={{
            tabBarLabel: formatMessage({ id: 'app.tabs.start' }),
            tabBarIcon: ({ focused }) => (
              <TabBarIcon
                focused={focused}
                iconFocused={'start-active'}
                iconUnfocused={'start-inactive'}
                showBadge={hasUnreadMessages}
              />
            ),
          }}
        />
        <Tab.Screen
          name="ParkTab"
          component={ParkScreen}
          options={{
            tabBarLabel: formatMessage({ id: 'app.tabs.park' }),
            tabBarIcon: ({ focused }) => (
              <TabBarIcon focused={focused} iconFocused={'park-active'} iconUnfocused={'park-inactive'} />
            ),
          }}
        />
        <Tab.Screen
          name="ReservationsTab"
          component={ReservationsScreen}
          options={{
            tabBarLabel: formatMessage({ id: 'app.tabs.reservations' }),
            tabBarIcon: ({ focused }) => (
              <TabBarIcon
                focused={focused}
                iconFocused={'reservations-active'}
                iconUnfocused={'reservations-inactive'}
              />
            ),
          }}
        />
        <Tab.Screen
          name="ProfileTab"
          component={Profile}
          options={{
            tabBarLabel: formatMessage({ id: 'app.tabs.profile' }),
            tabBarIcon: ({ focused }) => (
              <TabBarIcon
                focused={focused}
                iconFocused={'profile-active'}
                iconUnfocused={'profile-inactive'}
              />
            ),
          }}
        />
      </Tab.Navigator>
    </StickyBannerProvider>
  );
}
