import React, { useCallback, useContext } from 'react';
import { Linking, NativeModules, Platform, View } from 'react-native';
import useTheme from '@app/hooks/useTheme';
import { UserContext, UserContextUpdater } from '@app/context/usercontext/UserContextProvider';
import { ParkContextUpdater } from '@app/context/usercontext/ParkProvider';
import { CultureContextUpdater, getTranslationsForLanguage } from '@app/context/usercontext/CultureProvider';
import AsyncStorage from '@react-native-async-storage/async-storage';
import {
  CHAT_KEY,
  CULTURE_KEY,
  DEVICEID_KEY,
  GENERATEDSALTOID_KEY,
  NOTIFICATION_KEY,
  PARK_KEY,
  SALTO_USER_TOKEN_KEY,
} from '@app/config/StorageKeys';
import { LanguageCode } from '@app/hooks/useCulture';
import { Deeplink, DeeplinkUpdater } from '@app/context/DeeplinkProvider';
import { ChatContextUpdater } from '@app/context/usercontext/ChatProvider';
import { SaltoContextUpdater } from '@app/context/SaltoContextProvider';
import { NotificationContextUpdater } from '@domains/notification/context/NotificationProvider';
import { ChatMessage } from '@app/interfaces/ChatMessage';
import { NotificationMessage } from '@domains/notification/types/NotificationMessage';
import { useIntl } from 'react-intl';

const DEFAULT_LANGUAGE = 'nl-NL';

const AppLoading = () => {
  const theme = useTheme();
  const { formatMessage, messages } = useIntl();
  const { demoMode } = useContext(UserContext);
  const { setUserState } = useContext(UserContextUpdater);
  const setDeeplink = useContext(DeeplinkUpdater);
  const deeplink = useContext(Deeplink);
  const setPark = useContext(ParkContextUpdater);
  const setCulture = useContext(CultureContextUpdater);
  const { setMessages } = useContext(ChatContextUpdater);
  const { setNotifications } = useContext(NotificationContextUpdater);
  const { setGeneratedId, setDeviceId, setSaltoUserToken } = useContext(SaltoContextUpdater);

  const loadMessageCenter = useCallback(
    async (cultureCode: { cultureCode: LanguageCode }) => {
      const chatMessages = await AsyncStorage.getItem(CHAT_KEY); // deprecated
      const notificationMessages = await AsyncStorage.getItem(NOTIFICATION_KEY);

      const storedNotifications: NotificationMessage[] = notificationMessages
        ? JSON.parse(notificationMessages)
        : [];

      let combinedMessages: NotificationMessage[] = storedNotifications;
      if (chatMessages) {
        const storedChatMessages: ChatMessage[] = JSON.parse(chatMessages);
        // turn the old chatMessages to the new Notifications structure
        // We take out everything but reservation confirmations for the 7.0 messages box.
        // By chance, this happens to be all messages without a defined 'action'.
        const notificationsFromChatMessages: NotificationMessage[] = storedChatMessages
          .filter((m) => !m.action)
          .map((m) => {
            return {
              bookingReference: m.bookingReference,
              createdAt: m.createdAt,
              image: m.image,
              read: m.read,
              title: m.text?.match(/^(.*)$/m)?.[0],
              text: m.text,
              label: 'reservation',
            } as NotificationMessage;
          });

        combinedMessages = [...storedNotifications, ...notificationsFromChatMessages];

        setMessages([]);
        AsyncStorage.removeItem(CHAT_KEY);
      }

      // We get the translations without using the intl-provider here, as we found this wasn't always present at this stage.
      // The messages were not properly loaded, leaving the translations in the default message below, empty.
      const tempTranslations = getTranslationsForLanguage(cultureCode);

      if (combinedMessages.length === 0) {
        // add the default message
        combinedMessages = [
          {
            createdAt: new Date(),
            title: tempTranslations['app.notificationcenter.defaultMessage.title'],
            text: tempTranslations['app.notificationcenter.defaultMessage.text'],
          },
        ];
      }
      setNotifications(combinedMessages);
    },
    [setMessages, setNotifications]
  );

  const sync = React.useCallback(async () => {
    const parkCode = await AsyncStorage.getItem(PARK_KEY);
    //const onboardedStatus = await AsyncStorage.getItem(ONBOARDED_KEY);
    let storedCultureCode = (await AsyncStorage.getItem(CULTURE_KEY)) as LanguageCode | undefined;
    const saltoGeneratedId = await AsyncStorage.getItem(GENERATEDSALTOID_KEY);
    const deviceId = await AsyncStorage.getItem(DEVICEID_KEY);
    const saltoUserToken = await AsyncStorage.getItem(SALTO_USER_TOKEN_KEY);

    if (!storedCultureCode) {
      const deviceLanguage: string =
        Platform.OS === 'ios'
          ? NativeModules.SettingsManager.settings.AppleLocale ||
            NativeModules.SettingsManager.settings.AppleLanguages[0] //iOS 13
          : Platform.OS === 'web'
          ? 'en'
          : NativeModules.I18nManager.localeIdentifier;

      const supportedLanguages = ['nl-NL', 'en-US', 'de-DE', 'fr-FR', 'da-DK'];
      const availableLanguage = supportedLanguages.find((s) => s.slice(0, 2) === deviceLanguage.slice(0, 2));
      if (availableLanguage) {
        storedCultureCode = (availableLanguage as LanguageCode) || DEFAULT_LANGUAGE;
      }
    }

    setPark(parkCode || '');
    const cultureCode = (storedCultureCode || DEFAULT_LANGUAGE) as LanguageCode;
    setCulture(cultureCode);

    if (saltoGeneratedId) {
      setGeneratedId(saltoGeneratedId);
    }
    if (deviceId) {
      setDeviceId(deviceId);
    }
    if (saltoUserToken) {
      setSaltoUserToken(saltoUserToken);
    }

    if (deeplink) {
      Linking.openURL(deeplink);
      setDeeplink('');
    }

    await loadMessageCenter(cultureCode);
    setUserState('onboarded');
  }, [
    deeplink,
    loadMessageCenter,
    setCulture,
    setDeeplink,
    setDeviceId,
    setGeneratedId,
    setPark,
    setSaltoUserToken,
    setUserState,
  ]);

  React.useEffect(() => {
    sync();
  }, [sync]);

  return <View style={{ flex: 1, backgroundColor: theme.colors.primary }} />;
};
export default AppLoading;
