import { NOTIFICATION_KEY } from '@app/config/StorageKeys';
import AsyncStorage from '@react-native-async-storage/async-storage';
import React, { useMemo, useState } from 'react';
import { NotificationMessage } from '../types/NotificationMessage';

type NotificationContextUpdaterModel = {
  setNotifications: React.Dispatch<React.SetStateAction<NotificationMessage[]>>;
  clearNotifications: () => void;
};

export const NotificationContextUpdater = React.createContext<NotificationContextUpdaterModel>({
  setNotifications: () => {},
  clearNotifications: () => {},
});
export const NotificationContext = React.createContext<NotificationMessage[]>([]);

export function NotificationProvider({ children }: { children: React.ReactNode }) {
  const [notificationMessages, setNotificationMessages] = useState<NotificationMessage[]>([]);

  React.useEffect(() => {
    // flush the context to storage
    if (notificationMessages && notificationMessages.length > 0) {
      AsyncStorage.setItem(NOTIFICATION_KEY, JSON.stringify(notificationMessages));
    }
  }, [notificationMessages]);

  const clearNotifications = () => {
    setNotificationMessages([]);
    AsyncStorage.removeItem(NOTIFICATION_KEY);
  };

  let sortedNotificationMessages = useMemo(() => {
    return notificationMessages.sort((a, b) => new Date(b.createdAt) - new Date(a.createdAt));
  }, [notificationMessages]);

  return (
    <NotificationContext.Provider value={sortedNotificationMessages}>
      <NotificationContextUpdater.Provider
        value={{ setNotifications: setNotificationMessages, clearNotifications }}
      >
        {children}
      </NotificationContextUpdater.Provider>
    </NotificationContext.Provider>
  );
}
