import { gql, GraphQLClient } from 'graphql-request';
import Config from 'react-native-config';
import { USERNAME_KEY } from '@app/config/StorageKeys';
import AsyncStorage from '@react-native-async-storage/async-storage';

export interface ErrorMessage {
  message: string;
  errorCode: number;
}

export function useGetNewToken() {
  const getToken = async (authCode: string, redirectUrl: string, username: string) => {
    const graphQLClient = new GraphQLClient(Config.IDENTITY_API_URL);
    const query = gql`
        query userToken {
          userToken(authCode: "${authCode}", redirectUrl:"${redirectUrl}") {
            uuid
            expiryDate
          }
        }
      `;
    await AsyncStorage.setItem(USERNAME_KEY, username);
    return await graphQLClient.rawRequest(query);
  };
  return { getToken };
}
