import ListItems, { ListItem } from '@app/components/ListItems2/ListItems';
import useTheme from '@app/hooks/useTheme';
import { useGetPromos } from '@domains/promo/queries/useGetPromos';
import React, { useMemo } from 'react';
import { useIntl } from 'react-intl';

interface Props {
  parkCode?: string;
}
export default function ListQuickAndEasy({ parkCode }: Props) {
  const theme = useTheme();
  const { data: promos } = useGetPromos({ parkCodeOverride: parkCode });
  const { formatMessage } = useIntl();

  const listItems: ListItem[] = useMemo(() => {
    let items = [];
    const groceriesPromo = promos?.find((p) => p.subCat === 'groceries');
    if (groceriesPromo) {
      items.push({
        id: 'groceries',
        title: formatMessage({ id: 'app.quickandeasy.groceries.title' }),
        subtitle: formatMessage({ id: 'app.quickandeasy.groceries.subtitle' }),
        image: groceriesPromo.imageUrl,
        localImage: groceriesPromo.imageUrl
          ? undefined
          : theme.type === 'hofvansaksen'
          ? require('@assets/images/hsn/etenendrinken.png')
          : require('@assets/images/etenendrinken.jpg'),
        linkTo: groceriesPromo.url,
      });
    }
    return items;
  }, [formatMessage, promos, theme.type]);

  /*  [
     {
      id: 'tablebooker',
      title: 'Tafel reserveren',
      image:
        theme.type === 'hofvansaksen'
          ? require('@assets/images/hsn/etenendrinken.png')
          : require('@assets/images/etenendrinken.jpg'),
      subtitle: 'Heerlijk uit eten op het park',
    }, */
  /* {
      id: 'activities',
      title: 'Activiteiten bekijken',
      image:
        theme.type === 'hofvansaksen'
          ? require('@assets/images/hsn/funenentertainment.png')
          : require('@assets/images/funenentertainment.png'),
      subtitle: 'Wat gaan jullie vandaag doen?',
    }, */
  /* {
      id: 'food',
      title: 'Eten bestellen',
      image:
        theme.type === 'hofvansaksen'
          ? require('@assets/images/hsn/etenendrinken.png')
          : require('@assets/images/etenendrinken.jpg'),
      subtitle: 'Heerlijk eten in je accommodatie',
    },
  ] */

  if (!parkCode) {
    return <></>;
  }

  return <ListItems title={formatMessage({ id: 'app.quickandeasy.title' })} items={listItems} />;
}
