import { useGetUnitTypes } from '@domains/park/queries/useGetUnitTypes';
import { Reservation } from 'apis/_generated-reservation-api/models';
import { useIntl } from 'react-intl';

interface Props {
  reservation?: Reservation;
  isDetailPage?: boolean;
}
export default function useAccommodationCodeText({ reservation, isDetailPage }: Props) {
  const { formatMessage } = useIntl();
  const { data: unitTypes } = useGetUnitTypes({ parkCode: reservation?.parkCode });
  const unitType = unitTypes?.find((u) => u.code === reservation?.accommodationCode);
  const isCamping = reservation?.unitTypeIndicator === 'CAMPING';

  let hasNumber = !!reservation?.unitCode;
  if (!reservation) return { number: '', type: '', hasNumber };

  if (reservation.combiReference && !isDetailPage)
    return {
      number: '',
      type: formatMessage({ id: 'app.reservation.multipleAccommodations' }),
      hasNumber: false,
    };

  const campingWithoutPreference = isCamping && !reservation?.unitCodeIsPreference;

  if (campingWithoutPreference && reservation?.accommodationCode) {
    return { number: '', type: reservation?.accommodationCode };
  }

  let text = isCamping
    ? formatMessage({ id: 'app.forms.labels.campingNumber' })
    : formatMessage({ id: 'app.forms.labels.houseNumber' }) + ' ';
  text += reservation?.unitCode
    ? reservation?.unitCode.toString()
    : formatMessage({ id: 'app.reservation.unknownAccoNr' });

  if (
    reservation?.arrivalDate && // There is a reservation
    !reservation?.unitCodeIsPreference && // Not booked with a unit preference
    reservation?.arrivalDate > new Date().getTime() / 1000
  ) {
    // Arrival still to come, Show 'temporary' label
    text += ` (${formatMessage({ id: 'app.reservation.unitCodeIsNotConfirmed' })})`;
  }

  let type: string | undefined;
  if (unitType?.name) type = unitType?.name;
  if (unitType?.code) {
    if (type) {
      type += ' ' + unitType?.code;
    } else {
      type = unitType?.code;
    }
  }

  return { number: text, type, hasNumber };
}
