import React, { forwardRef, Ref, useContext, useImperativeHandle } from 'react';
import { KeyRefType } from '../types/KeyRefType';
import { useBottomSheet } from '@app/navigation/bottom-sheet/BottomSheetProvider';
import LockSheet from '../sheets/LockSheet';
import useKeyStatus from '../hooks/useKeyStatus';
import SaltoLoginWebview from '@domains/profile/screens/SaltoLoginWebview';
import { useGetReservation } from '@domains/reservations/reservation/queries/useGetReservation';
import { getBrand } from 'utils/brand';
import useTheme from '@app/hooks/useTheme';
import { UserContext } from '@app/context/usercontext/UserContextProvider';
import { SaltoContext } from '@app/context/SaltoContextProvider';
import { useIntl } from 'react-intl';
import Button from '@app/components/Button';

interface Props {}

const KeyButton = forwardRef(({}: Props, ref: Ref<KeyRefType>) => {
  const { openBottomSheet } = useBottomSheet();
  const { keyShown, keyReady, keyExpired, accommodationReady } = useKeyStatus({});
  const theme = useTheme();
  const { token } = useContext(UserContext);
  const { saltoUserToken } = React.useContext(SaltoContext);
  const { formatMessage } = useIntl();

  const brand = getBrand(theme);
  const { data: reservation } = useGetReservation({
    brand,
    token,
  });

  useImperativeHandle(ref, () => ({
    isKeyAvailable() {
      return keyShown;
    },
  }));

  return (
    <>
      {keyShown && (
        <Button
          onPress={() => {
            openBottomSheet({
              component: () => (
                <>
                  <LockSheet
                    bookingReference={reservation?.bookingReference}
                    keyReady={keyReady}
                    keyExpired={keyExpired}
                    accommodationReady={accommodationReady}
                  />
                  {!!token && !saltoUserToken && (
                    <SaltoLoginWebview
                      onSuccess={async () => {
                        //setGettingSaltoReady(false);
                      }}
                      onReady={() => {}}
                      onLoadStart={() => {
                        //setGettingSaltoReady(true);
                      }}
                      onError={() => {
                        //setGettingSaltoReady(false);
                      }}
                    />
                  )}
                </>
              ),
              title: keyReady
                ? formatMessage({ id: 'app.key.keyUse' })
                : formatMessage({ id: 'app.key.keyActivate' }),
              key: 'lock-key',
            });
          }}
          style={{
            backgroundColor: theme.colors.secondary,
          }}
        >
          <Button.Icon name="key" size="s" color="white" />
          <Button.Text color="white">{formatMessage({ id: 'app.key.keyUse' })}</Button.Text>
        </Button>
      )}
    </>
  );
});
export default KeyButton;
