import useCart from '@app/hooks/useCart';
import useCulture from '@app/hooks/useCulture';
import { Product } from 'apis/_generated-park-api/models';
import { AxiosError } from 'axios';
import { gql, GraphQLClient } from 'graphql-request';
import Config from 'react-native-config';
import { UseQueryOptions, useQuery } from 'react-query';

const key = `getproducts`;

interface Props {
  options?: Omit<UseQueryOptions<Product[], AxiosError>, typeof key>;
  offset?: number;
  limit?: number;
}

export function useGetProducts({ options }: Props) {
  const { cultureCode } = useCulture();
  const { activeShop: shopId } = useCart();

  return useQuery(
    [key, { cultureCode, shopId }],
    async () => {
      const graphQLClient = new GraphQLClient(Config.PARK_API_URL);

      const getQuery = (offset: number, limit: number) => gql`
      query shopProducts {
        shopProducts(cultureCode: "${cultureCode}", shopId: ${shopId}, offset: ${offset}, limit: ${limit}) {
          id
          name
          productNumber
          shortDescription
          description
          price
          unit
          mainCategory
          subCategory
          productMainPhoto
          alias
        }
      }
    `;
      const BREAK_THRESHOLD = 18;
      const SIZE = 20;
      let offset = 0;
      let pagesQueried = 0;

      const getData = async () => {
        const query = getQuery(offset, SIZE);
        const result = await graphQLClient.rawRequest<{ shopProducts: Product[] }>(query);
        return result?.data?.shopProducts;
      };

      let data = await getData();

      while (true) {
        // fail-safe
        if (pagesQueried > BREAK_THRESHOLD) break;
        pagesQueried++;

        const moreData = await getData();
        if (!moreData || moreData.length <= 0) break;

        offset += SIZE;
        data = [...data, ...moreData];
      }

      return data;
    },
    options
  );
}
