import useCart from '@app/hooks/useCart';
import useCulture from '@app/hooks/useCulture';
import { ProductCombination } from 'apis/_generated-park-api/models';
import { AxiosError } from 'axios';
import { gql, GraphQLClient } from 'graphql-request';
import Config from 'react-native-config';
import { UseQueryOptions, useQuery } from 'react-query';

const key = `getproductcombinations`;
type ProductDataProps = {
  options?: Omit<UseQueryOptions<ProductCombination[], AxiosError>, typeof key>;
  productId: number;
};

export function useGetProductCombinations({ options, productId }: ProductDataProps) {
  const { cultureCode } = useCulture();
  const { activeShop: shopId } = useCart();

  return useQuery(
    [key, { cultureCode, shopId, productId }],
    async () => {
      const graphQLClient = new GraphQLClient(Config.PARK_API_URL);
      const query = gql`
          query shopProductCombinations {
            shopProductCombinations(cultureCode: "${cultureCode}", shopId: ${shopId}, productId: ${productId}) {
              id
              attributes {
                name
              }
            }
          }
        `;
      const result = await graphQLClient.rawRequest<{ shopProductCombinations: ProductCombination[] }>(query);
      return result?.data?.shopProductCombinations;
    },
    options
  );
}
