import React from 'react';
import { useAnimatedScrollHandler, useSharedValue } from 'react-native-reanimated';
import { Alert, View } from 'react-native';
import useTheme from '@app/hooks/useTheme';
import TitleAndDescription from '@app/components/DetailScreen/TitleAndDescription';
import { useGetActivities } from '../queries/useGetActivities';
import ActivityDetails from '../components/ActivityDetails';
import moment from 'moment-timezone';
import { useIntl } from 'react-intl';
import Text from '@app/components/Text';
import Button from '@app/components/Button';
import { useGetActivityDetails } from '../queries/useGetActivityDetails';
import { useFocusEffect } from '@react-navigation/native';
import useMetrics from '@app/metrics/useMetrics';
import StickyImageHeader, { useStickyImageHeaderDimensions } from '@app/components/StickyImageHeader';
import ScreenScrollContainer from '@app/components/ScreenScrollContainer';
import { NavScreenProp } from '@app/navigation/stacks/Main';
import useBadge, { BADGE_HEIGHT } from '@app/hooks/useBadge';
import useOpeningHours from '@app/hooks/useOpeningHours';
import Loader from '@app/components/Loader';
import RNCalendarEvents from 'react-native-calendar-events';
import { useGetFacilities } from '@domains/park/at-the-park/queries/useGetFacilities';
import { getActivityDateTimes } from '../helpers/activityTime';

export default function ActivitiesDetail({ navigation, route }: NavScreenProp<'ActivitiesDetail'>) {
  const { formatMessage } = useIntl();
  const { logScreenView, logEvent } = useMetrics();
  const theme = useTheme();

  const formattedOpeningHours = useOpeningHours();
  const { data: facilities } = useGetFacilities();

  const parkCode = route?.params?.parkCode;
  const date = route?.params?.date;

  const { data, isLoading } = useGetActivities({ parkCodeOverride: parkCode });
  const { EXPANDED_HEADER_HEIGHT, size } = useStickyImageHeaderDimensions('m');

  const translationY = useSharedValue(0);
  const scrollHandler = useAnimatedScrollHandler((event) => {
    translationY.value = event.contentOffset.y;
  });

  const activities = data?.sortedData;

  const activity = activities?.find((d) => d.time?.id?.toString() === route?.params?.id);

  const { data: activityDetails } = useGetActivityDetails({
    reservationId: activity?.time?.reservationId,
  });

  var activityInPresent = !!activityDetails?.DateFrom && moment(activityDetails?.DateFrom) > moment();

  let openingHoursText = formattedOpeningHours(
    activity?.time?.startTime,
    activity?.time?.endTime,
    activity?.time?.displayHint
  );
  const { reactBadge, skiaBadge } = useBadge({
    text: openingHoursText,
    icon: 'clock',
    y: EXPANDED_HEADER_HEIGHT - theme.space.m - BADGE_HEIGHT,
  });

  useFocusEffect(
    React.useCallback(() => {
      if (activity?.id) {
        logScreenView('ActivitiesDetail', [activity.id.toString()], { activity: activity.name || '' });
      }
    }, [activity, logScreenView])
  );

  if (!data || !route?.params?.id || !activity) {
    navigation.goBack();
    return null;
  }

  const reservationText = () => {
    if (activity?.time?.reservationId) {
      if (!activityDetails) {
        return '';
      }
      if (activityDetails?.Free) {
        return `${formatMessage({ id: 'app.activities.signup.openspots1' })}${
          activityDetails.Free
        }${formatMessage({ id: 'app.activities.signup.openspots2' })}`;
      } else if (!activityDetails?.Free) {
        return formatMessage({ id: 'app.activities.signup.nospots' });
      }
    }
  };

  return (
    <>
      <StickyImageHeader
        scrollOffset={translationY}
        title={activity?.name}
        images={activity?.media ? [activity?.media] : []}
        size={size}
        skiaBadge={skiaBadge}
      >
        {reactBadge}
      </StickyImageHeader>
      <ScreenScrollContainer
        onScroll={scrollHandler}
        animated
        contentContainerStyle={{ paddingTop: EXPANDED_HEADER_HEIGHT }}
      >
        {isLoading ? (
          <View style={{ flex: 1, justifyContent: 'center', alignItems: 'center' }}>
            <Loader />
          </View>
        ) : (
          <>
            <TitleAndDescription name={activity.name} description={activity.description}>
              {!!activity && !!activity.time && !!activity.name && (
                <Button
                  onPress={async () => {
                    if (!activity?.time) return; // Condition already checked above to hide the button, but Typescript does not agree.
                    if (!activity.name) return; // Condition already checked above to hide the button, but Typescript does not agree.
                    const thisFacility = facilities?.find((f) => f.id === activity.locationId);

                    const { startDateTime, endDateTime } = getActivityDateTimes(
                      date,
                      activity.time.startTime,
                      activity.time.endTime,
                      activity.time.displayHint
                    );

                    const permission = await RNCalendarEvents.checkPermissions();
                    if (permission !== 'authorized') {
                      const updatedPermission = await RNCalendarEvents.requestPermissions();
                      if (updatedPermission !== 'authorized') {
                        Alert.alert(
                          formatMessage({ id: 'app.general.oops' }),
                          formatMessage({ id: 'app.activities.addToCalendar.errorNoPermissionAfterRequest' })
                        );
                      }
                    }

                    try {
                      const result = await RNCalendarEvents.saveEvent(activity.name, {
                        startDate: startDateTime,
                        endDate: endDateTime,
                        location: thisFacility?.latitude
                          ? thisFacility.latitude + ', ' + thisFacility?.longitude
                          : thisFacility?.name,
                        notes: formatMessage({ id: 'app.general.cost' }) + ': ' + activity.price?.description,
                        allDay: activity.time.displayHint === 'openallday',
                        alarms: [{ date: -15 }], // minutes from the start
                      });
                      if (result) {
                        Alert.alert(
                          formatMessage({ id: 'app.activities.addToCalendar.successTitle' }),
                          formatMessage({ id: 'app.activities.addToCalendar.successText' })
                        );
                        logEvent('AddActivityToCalendar', { parkCode, id: activity.activityId });
                      } else {
                        Alert.alert(
                          formatMessage({ id: 'app.general.oops' }),
                          formatMessage({ id: 'app.activities.addToCalendar.errorUnexpected' })
                        );
                        logEvent('AddActivityToCalendarError', {
                          parkCode,
                          id: activity.activityId,
                          reason: 'RNCalendarEvents.saveEvent result is empty',
                        });
                      }
                    } catch (e) {
                      logEvent('AddActivityToCalendarError', {
                        parkCode,
                        id: activity.activityId,
                        reason: e,
                      });
                      Alert.alert(
                        formatMessage({ id: 'app.general.oops' }),
                        formatMessage({ id: 'app.activities.addToCalendar.errorUnexpected' })
                      );
                    }
                  }}
                  style={{
                    marginTop: theme.space.l,
                    backgroundColor: 'transparent',
                    borderColor: theme.colors.light,
                    borderWidth: 1,
                  }}
                  disabled={activityDetails?.Free === 0}
                >
                  <Button.Icon color="tertiary" name="calendar" />
                  <Button.Text color="tertiary">Toevoegen aan kalender</Button.Text>
                </Button>
              )}
            </TitleAndDescription>
            <ActivityDetails
              location={activity.locationId}
              age={activity.ageCategory}
              price={activity.price}
              signUpNeeded={!!activity.signUpNeeded}
            />
            {!!activity?.time?.reservationId && activityInPresent && (
              <Button
                type="primary"
                onPress={() => {
                  navigation.navigate('ActivitySignup', {
                    activity,
                  });
                }}
                style={{ marginBottom: 8 }}
                disabled={activityDetails?.Free === 0}
              >
                <Button.Text>{formatMessage({ id: 'app.forms.signup' })}</Button.Text>
              </Button>
            )}

            {activityInPresent && (
              <Text size="s" color="gray" style={{ fontStyle: 'italic' }}>
                {reservationText()}
              </Text>
            )}
          </>
        )}
      </ScreenScrollContainer>
    </>
  );
}
