import { EventName } from '@app/metrics/useMetrics';
import React, { useState } from 'react';

interface MetricsContextUpdaterModel {
  setEventsLoggedThisSession: (value: React.SetStateAction<EventName[]>) => void;
}
interface MetricsContextModel {
  eventsLoggedThisSession: EventName[];
}

export const MetricsContextUpdater = React.createContext<MetricsContextUpdaterModel>({
  setEventsLoggedThisSession: () => {},
});
export const MetricsContext = React.createContext<MetricsContextModel>({
  eventsLoggedThisSession: [],
});

export function MetricsContextProvider({ children }: { children: React.ReactNode }) {
  const [eventsLoggedThisSession, setEventsLoggedThisSession] = useState<EventName[]>([]);

  return (
    <MetricsContext.Provider value={{ eventsLoggedThisSession }}>
      <MetricsContextUpdater.Provider value={{ setEventsLoggedThisSession }}>
        {children}
      </MetricsContextUpdater.Provider>
    </MetricsContext.Provider>
  );
}
