import useTheme from '@app/hooks/useTheme';
import {
  add,
  Canvas,
  FitBox,
  Group,
  LinearGradient,
  Path,
  rect,
  RoundedRect,
  vec,
} from '@shopify/react-native-skia';
import React from 'react';
import { View, ViewStyle } from 'react-native';

const SIZE = 24;
const c = vec(12, 12);
const r = SIZE;

interface CheckBoxProps {
  value: boolean;
  style?: ViewStyle;
}

const checkMarkPath =
  'M896 329.984c0-12.57-4.954-25.14-13.875-34.191l-67.383-68.381c-8.917-9.050-21.308-14.079-33.694-14.079s-24.772 5.028-33.694 14.079l-325.036 330.344-145.672-148.328c-8.919-9.050-21.306-14.079-33.693-14.079s-24.774 5.028-33.693 14.079l-67.386 68.384c-8.919 9.050-13.873 21.619-13.873 34.189s4.955 25.139 13.873 34.189l246.751 250.398c8.919 9.054 21.306 14.080 33.693 14.080s24.774-5.026 33.691-14.080l426.116-432.412c8.922-9.050 13.875-21.62 13.875-34.191z';

const CheckBox = ({ value, style }: CheckBoxProps) => {
  const theme = useTheme();
  const checked = value;

  return (
    <View
      style={[
        {
          height: SIZE,
          width: SIZE,
          alignItems: 'center',
          justifyContent: 'center',
        },
        style,
      ]}
    >
      <Canvas
        //key={checked.toString()}
        style={{
          height: SIZE,
          width: SIZE,
          zIndex: 0,
        }}
      >
        <RoundedRect x={0} y={0} width={SIZE} height={SIZE} r={theme.space.s} color={theme.colors.light} />
        <RoundedRect
          x={2}
          y={2}
          width={SIZE - 4}
          height={SIZE - 4}
          r={theme.space.s - 2}
          color={theme.colors.white}
        />
        {checked && (
          <Group>
            <Group>
              <LinearGradient
                transform={[{ rotate: Math.PI / 3 }]}
                origin={c}
                start={add(c, vec(-r, 0))}
                end={add(c, vec(r, 0))}
                colors={[
                  theme.colors.gradientPrimary1,
                  theme.colors.gradientPrimary2,
                  theme.colors.gradientPrimary3,
                ]}
              />
              <RoundedRect x={0} y={0} width={SIZE} height={SIZE} r={theme.space.s} />
            </Group>
            <FitBox src={rect(0, 0, 1024, 1024)} dst={rect(4, 4, 16, 16)}>
              <Path path={checkMarkPath} color="white" />
            </FitBox>
          </Group>
        )}
      </Canvas>
    </View>
  );
};

export default CheckBox;
