import React from 'react';
import useTheme from '@app/hooks/useTheme';
import { View } from 'react-native';
import Text from '@app/components/Text';
import ContentContainer from '@app/components/DetailScreen/ContentContainer';
import { useIntl } from 'react-intl';
import Button from '@app/components/Button';
import { useGetFacilityMetaData } from '../queries/useGetFacilityMetaData';
import { getFormattedDate, getFormattedTime } from 'utils/date';
import StickyHeader from '@app/components/StickyHeader';
import { NavScreenProp } from '@app/navigation/stacks/Main';

const FacilityBookingSuccessful = ({ navigation, route }: NavScreenProp<'FacilityBookingSuccessful'>) => {
  const theme = useTheme();
  const { formatMessage } = useIntl();
  const reservationId = route?.params?.reservationId;
  const amount = route?.params?.amount;
  const date = route?.params?.date;

  const { data } = useGetFacilityMetaData({ reservationId });
  const facilityName = data?.description;

  const objects = amount > 1 ? data?.placesPlural : data?.placesSingle;

  return (
    <>
      <StickyHeader showClose navigation={navigation} />
      <View style={{ flex: 1, backgroundColor: theme.colors.canvas }}>
        <ContentContainer style={{ flex: 1, backgroundColor: theme.colors.canvas }}>
          <Text type="title" size="xl" style={{ marginBottom: theme.space.l }} bold>
            {formatMessage({ id: 'app.forms.result.title' })}
          </Text>

          <Text type="body" style={{ marginBottom: theme.space.l }}>
            {formatMessage({
              id: 'app.forms.facilitybooking.result.description1',
            })}
            {amount} {objects}
            {formatMessage({
              id: 'app.forms.facilitybooking.result.description2',
            })}
            {getFormattedDate(date)} {getFormattedTime(date)}
            {formatMessage({
              id: 'app.forms.facilitybooking.result.description3',
            })}
          </Text>
          <Text type="body" style={{ marginBottom: theme.space.l }}>
            {formatMessage({ id: 'app.forms.result.messagedescription1' })}
            {facilityName}
            {formatMessage({ id: 'app.forms.result.messagedescription2' })}
          </Text>
          <Button
            type="primary"
            onPress={() => {
              navigation.goBack();
            }}
            style={{
              marginBottom: 32,
            }}
          >
            <Button.Text>{formatMessage({ id: 'app.forms.result.back' })}</Button.Text>
          </Button>
        </ContentContainer>
      </View>
    </>
  );
};
export default FacilityBookingSuccessful;
