import React, { useContext, useState } from 'react';
import { Alert, Platform, TouchableOpacity, View } from 'react-native';
import useTheme from '@app/hooks/useTheme';
import Text from '@app/components/Text';
import { useIntl } from 'react-intl';
import useMetrics from '@app/metrics/useMetrics';
import { useFocusEffect } from '@react-navigation/native';
import { useFormik } from 'formik';
import FormControl from '@app/components/Form/FormControl';
import Button from '@app/components/Button';
import { useMutateICE } from '../queries/useMutateICE';
import { UserContext } from '@app/context/usercontext/UserContextProvider';
import { useGetReservation } from '../queries/useGetReservation';
import { getBrand } from 'utils/brand';
import { isValidPhone } from 'utils/validation';
import { useBottomSheet } from '@app/navigation/bottom-sheet/BottomSheetProvider';

type FormFields = 'iceName' | 'icePhoneNumber';

type FormikErrors = {
  iceName?: string;
  icePhoneNumber?: string;
};

interface Props {
  bookingReference?: string;
}

const ICE = ({ bookingReference }: Props) => {
  const { formatMessage } = useIntl();
  const { logScreenView, logEvent } = useMetrics();
  const theme = useTheme();
  const { updateMainTenant } = useMutateICE();
  const [processing, setProcessing] = useState(false);
  const { token } = useContext(UserContext);
  const { closeBottomSheet } = useBottomSheet();

  const { data: reservation, refetch } = useGetReservation({
    brand: getBrand(theme),
    token,
    bookingReference,
  });

  useFocusEffect(
    React.useCallback(() => {
      logScreenView('TravelCompanyMainTenant');
      logEvent('MainTenantView');
    }, [logEvent, logScreenView])
  );

  const formik = useFormik<Record<FormFields, any>>({
    initialValues: {
      iceName: reservation?.iceName,
      icePhoneNumber: reservation?.icePhoneNumber,
    },
    validateOnMount: true,
    validateOnChange: true,
    onSubmit: () => {},
    validate: (values) => {
      const errors: FormikErrors = {};
      if (!values.iceName) {
        errors.iceName = 'no emergency name';
      }
      if (!values.icePhoneNumber) {
        errors.icePhoneNumber = 'no emergency phone number';
      }
      if (!!values.icePhoneNumber && !isValidPhone(values.icePhoneNumber)) {
        errors.icePhoneNumber = 'incorrect phone number';
      }
      return errors;
    },
  });

  return (
    <View style={{ flex: Platform.OS === 'web' ? undefined : 0 }}>
      <View style={{ flex: Platform.OS === 'web' ? undefined : 0 }}>
        <View style={{ paddingHorizontal: theme.space.l, paddingVertical: theme.space.xl }}>
          <FormControl
            label={formatMessage({ id: 'app.forms.labels.name' })}
            value={formik.values.iceName}
            isValid={!formik.errors.iceName}
            onChangeText={(text) => {
              formik.setFieldValue('iceName', text);
            }}
          />
          <FormControl
            label={formatMessage({ id: 'app.forms.labels.phone' })}
            value={formik.values.icePhoneNumber}
            isValid={!formik.errors.icePhoneNumber}
            errorMessage={formatMessage({ id: 'app.forms.error.phone' })}
            placeholder="+31612345678"
            onChangeText={(text) => {
              formik.setFieldValue('icePhoneNumber', text);
            }}
          />
        </View>
      </View>
      <View style={{ paddingHorizontal: theme.space.l, paddingBottom: theme.space.l }}>
        <Button
          onPress={async () => {
            if (processing) {
              return;
            }
            setProcessing(true);

            // Check if this is the first person whose details are being provided.
            // If this is the case, we mark this by sending a 'travel company start' event.
            // This code is also present in TravelCompanyCompanion.tsx!
            const companionsCompleted =
              (reservation?.travelCompanions?.filter((c) => c?.isComplete)?.length || 0) +
              (reservation?.mainTenant?.isComplete ? 1 : 0);
            if (companionsCompleted === 0) {
              logEvent('TravelCompanyBegin');
            }

            logEvent('MainTenantUpdate');

            try {
              await updateMainTenant({
                bookingReference: reservation?.bookingReference,
                uid: token,
                iceName: formik.values.iceName,
                icePhoneNumber: formik.values.icePhoneNumber,
                //idType: formik.values?.idType,
              });

              // Check if this is the LAST person whose details are being provided.
              // If this is the case, we mark this by sending a 'travel company complete' event.
              // This code is also present in TravelCompanyCompanion.tsx!
              // we don't do travelCompanions-1, as maintenant is not included in travelCompanions
              if (companionsCompleted === (reservation?.travelCompanions?.length || 0)) {
                // This was the last one.
                logEvent('TravelCompanyComplete');
              }

              // TODO: this should not be in timeout, but otherwise we sometimes see the bottom sheet not closing
              setTimeout(() => {
                refetch();
              }, 50);
              closeBottomSheet();
            } catch {
              Alert.alert(formatMessage({ id: 'app.forms.tablebooking.error.generic' }));
            }
            setProcessing(false);
          }}
          style={{ backgroundColor: theme.colors.secondary, marginTop: theme.space.s }}
          disabled={processing || !formik.isValid}
        >
          {processing ? (
            <Button.ActivityIndicator style={{ height: 22 }} />
          ) : (
            <Button.Text color="white">
              {formatMessage({
                id: 'app.general.save',
              })}
            </Button.Text>
          )}
        </Button>
        <TouchableOpacity
          activeOpacity={0.8}
          style={{ marginVertical: theme.space.l, alignItems: 'center' }}
          onPress={() => closeBottomSheet()}
        >
          <Text color="gray" bold>
            {formatMessage({
              id: 'app.general.cancel',
            })}
          </Text>
        </TouchableOpacity>
      </View>
    </View>
  );
};
export default ICE;
