import { Linking, Platform, ViewProps } from 'react-native';
import React from 'react';

const appleUrl = 'maps://';
const googleUrl = Platform.OS === 'android' ? 'https://maps.google.com/' : 'https://www.google.com/maps/';

interface Props extends React.PropsWithChildren<ViewProps> {
  street?: string;
  streetNumber?: string;
  zipcode?: string;
  city?: string;
  country?: string;
  locationName?: string;
  latitude?: number;
  longitude?: number;
  name?: string;
}

export const getAppleUrl = (latLong: string, address: string, name?: string) => {
  if (latLong) {
    if (name) {
      return `${appleUrl}?q=${name}@${latLong}`;
    }
    return `${appleUrl}?q=${latLong}`;
  }
  return `${appleUrl}?q=${address}`;
};

export const getGoogleUrl = (latLong: string, address: string, name?: string) => {
  if (latLong) {
    let url = `${googleUrl}?q=${latLong}`;
    if (name) {
      if (Platform.OS === 'ios') {
        url = `${googleUrl}?q=${name}@${latLong}`;
      } else {
        // TODO: Fix marker name. Does not work the way specified by Google.
        // https://developers.google.com/maps/documentation/urls/android-intents#search-for-a-location
        url = `${googleUrl}?q=${latLong}(${name})`;
      }
    }
    if (address) {
      url += `(${address})`;
    }
    return url;
  }
  return `${googleUrl}?q=${address}`;
};

export default async function externalNavigateToLocation(options: Props) {
  const latlngString =
    options.latitude && options.longitude ? `${options.latitude},${options.longitude}` : '';
  let address = '';
  if (options.street) {
    address += options.street;
  }
  if (options.streetNumber) {
    address += ' ' + options.streetNumber;
  }
  if (options.city) {
    address += ' ' + options.city;
  }
  if (options.country) {
    address += ' ' + options.country;
  }
  const encodedAddress = address ?? encodeURIComponent(address);

  const googleURL = getGoogleUrl(latlngString, encodedAddress, options.name);
  const appleURL = getAppleUrl(latlngString, encodedAddress, options.name);

  if (Platform.OS === 'ios') {
    Linking.canOpenURL('comgooglemaps://?center=40.765819,-73.975866').then((googleMapsOnIOS) => {
      if (googleMapsOnIOS) {
        Linking.openURL(googleURL);
      } else {
        Linking.openURL(appleURL);
      }
    });
  } else {
    Linking.openURL(googleURL);
  }
}
