import { ParksPark } from 'apis/_generated-park-api/models';
import { AxiosError } from 'axios';
import { gql, GraphQLClient } from 'graphql-request';
import Config from 'react-native-config';
import { UseQueryOptions, useQuery } from 'react-query';

const key = `getparks`;

type Props = {
  options?: Omit<UseQueryOptions<ParksPark[], AxiosError>, typeof key>;
};

export function useGetParks({ options }: Props) {
  const extendedOptions: typeof options = {
    ...options,
    retryDelay: 8000,
  };

  return useQuery(
    [key],
    async () => {
      const graphQLClient = new GraphQLClient(Config.PARK_API_URL);
      const query = gql`
        query parks {
          parks {
            href
            id
            name
            appEnabled
            address {
              street
              streetNumber
              zipcode
              city
              country
              region
            }
            latitude
            longitude
            images
            appEnabledLanguages
          }
        }
      `;
      const result = await graphQLClient.rawRequest<{ parks: ParksPark[] }>(query);
      return result?.data?.parks;
    },
    extendedOptions
  );
}
