import React from 'react';
import { createNativeStackNavigator, NativeStackNavigationProp } from '@react-navigation/native-stack';
import InTheRegion from '@domains/park/in-the-region/screens/InTheRegion';
import InTheRegionDetail from '@domains/park/in-the-region/screens/InTheRegionDetail';
import InTheRegionEventDetail from '@domains/park/in-the-region/screens/InTheRegionEventDetail';
import { RouteProp } from '@react-navigation/native';
import { Poi } from 'apis/_generated-park-api/models';

// Types of params passed to route via navigation.navigate('SCREEN_NAME', params)
export type InTheRegionRoutes = {
  InTheRegion: { parkCode: string };
  InTheRegionCategory: { parkCode: string; data: Poi[] };
  InTheRegionDetail: { id: string; parkCode: string };
  InTheRegionEventDetail: { id: string };
};

type RouteNames = keyof InTheRegionRoutes;
export interface NavScreenProp<T extends RouteNames> {
  navigation: NativeStackNavigationProp<InTheRegionRoutes, T>;
  route: RouteProp<InTheRegionRoutes, T>;
}

export const useInTheRegionScreens = (stack: any) => {
  return (
    <>
      <stack.Screen name="InTheRegion" component={InTheRegion} />
      <stack.Screen name="InTheRegionDetail" component={InTheRegionDetail} />
      <stack.Screen name="InTheRegionEventDetail" component={InTheRegionEventDetail} />
    </>
  );
};

const InTheRegionStack = createNativeStackNavigator<InTheRegionRoutes>();
export default function InTheRegionNav({ navigation }: NavScreenProp<'InTheRegion'>) {
  const screens = useInTheRegionScreens(InTheRegionStack);

  return (
    <InTheRegionStack.Navigator
      backBehavior="history"
      screenOptions={{
        headerShown: false,
        orientation: 'portrait_up',
        presentation: 'fullScreenModal',
        animation: 'default',
      }}
    >
      {screens}
    </InTheRegionStack.Navigator>
  );
}
