import React, { PropsWithChildren } from 'react';
import useTheme from '@app/hooks/useTheme';
import { useSafeAreaInsets } from 'react-native-safe-area-context';
import { View, ViewStyle } from 'react-native';
import { BOTTOM_TAB_BAR_HEIGHT, BOTTOM_TAB_BAR_HEIGHT_NO_INSET } from './BottomTabs';
import Shadow from '@app/components/Shadow';
import { useStickyBanner } from '@app/context/StickyBannerProvider';

interface Props {
  containerStyle?: ViewStyle;
  style?: ViewStyle;
  autoHeight?: boolean;
  extraHeight?: number;
}

export default function StickyBottomBar({
  children,
  style,
  containerStyle,
  autoHeight,
  extraHeight = 0,
}: PropsWithChildren<Props>) {
  const theme = useTheme();
  const insets = useSafeAreaInsets();

  const { component: banner } = useStickyBanner();

  return (
    <View
      style={[
        {
          width: '100%',
          position: 'absolute',
          bottom: 0,
          left: 0,
          right: 0,
        },
        containerStyle,
      ]}
      pointerEvents="box-none"
    >
      {banner}
      <Shadow
        position="top"
        radius={12}
        style={{
          elevation: 2,
        }}
      >
        <View
          style={[
            {
              flexDirection: 'row',
              backgroundColor: theme.colors.white,
              borderTopLeftRadius: 12,
              borderTopRightRadius: 12,
              borderTopWidth: 0,
              zIndex: 1,
              height: autoHeight
                ? undefined
                : insets.bottom
                ? BOTTOM_TAB_BAR_HEIGHT
                : BOTTOM_TAB_BAR_HEIGHT_NO_INSET,
              paddingBottom: (insets.bottom ?? 0) + extraHeight,
              elevation: 2,
            },
            style,
          ]}
        >
          {children}
        </View>
      </Shadow>
    </View>
  );
}
