import React, { useRef, useState, useCallback, PropsWithChildren, useEffect } from 'react';

import { useContext } from 'react';
import { View, useWindowDimensions, Platform } from 'react-native';
import { useSafeAreaInsets } from 'react-native-safe-area-context';
import useTheme from '@app/hooks/useTheme';
import useKeyboardHeight from '@app/hooks/useKeyboardHeight';
import Animated, { Easing, ZoomIn, ZoomOut } from 'react-native-reanimated';
import { ScrollView } from 'react-native-gesture-handler';
import Backdrop from './PopupBackdrop';

const CLOSE_DELAY = 100;
export const POPUP_ANIMATION_ENTERING_DURATION = 400;
export const POPUP_ANIMATION_EXITING_DURATION = 350;

interface ContextInterface {
  openPopup: (config: PopupConfigType) => void;
  closePopup: () => void;
}

export type PopupConfigType = {
  key?: string;
  component: React.FC | null;
  title?: string;
  onClose?: () => void;
  disableBackdropClose?: boolean;
};

export const PopupContext = React.createContext<ContextInterface>({
  openPopup: () => {},
  closePopup: () => {},
});

export const usePopup = () => useContext(PopupContext);
export const DefaultContent = () => <View />;

const defaultPopupConfig: PopupConfigType = {
  component: DefaultContent,
  title: '',
  onClose: undefined,
  disableBackdropClose: false,
};

interface Props {}
const PopupProvider = ({ children, propValue }: PropsWithChildren<Props>) => {
  const theme = useTheme();
  const { height: screenHeight, width: screenWidth } = useWindowDimensions();
  const { bottom: bottomInset, top: topInset } = useSafeAreaInsets();

  const scrollViewWrapperRef = useRef<View>(null);
  const scrollViewRef = useRef<ScrollView>(null);

  const [isOpen, setIsOpen] = useState(false);

  const requestedHeight = useRef<number>(0);
  const layoutIterator = useRef<number>(0);
  const previousIterator = useRef<number>(0);

  const MAX_HEIGHT = screenHeight - topInset - bottomInset - theme.space.m * 2;

  const [keyboardIncludedContainerHeight, setKeyboardIncludedContainerHeight] = useState<number | null>(null);

  const popupConfig = useRef<PopupConfigType>(defaultPopupConfig);
  const keyboardHeight = useKeyboardHeight();

  useEffect(() => {
    if (!isOpen) {
      return;
    }

    if (keyboardHeight > 0) {
      const popupContainerHeight = screenHeight - keyboardHeight;

      setKeyboardIncludedContainerHeight(popupContainerHeight);

      /* scrollViewWrapperRef.current?.setNativeProps({
        style: { height: definitiveContainerHeight },
      }); */

      /* setPopupScroll(definitiveContainerHeight); */
    } else if (requestedHeight.current > 0) {
      setKeyboardIncludedContainerHeight(null);
      const definitiveContentHeight = Math.min(requestedHeight.current, MAX_HEIGHT);

      scrollViewWrapperRef.current?.setNativeProps({
        style: { height: definitiveContentHeight },
      });

      setPopupScroll(MAX_HEIGHT);
    }
  }, [MAX_HEIGHT, bottomInset, isOpen, keyboardHeight, screenHeight, theme.space.xl]);

  const setPopupScroll = (scrollThreshold: number) => {
    scrollViewRef.current?.setNativeProps({
      scrollEnabled: requestedHeight.current > scrollThreshold,
    });
  };

  const openPopup = (config: PopupConfigType) => {
    if (isOpen) {
      reset(); // reset, just to be sure the next attempt will succeed
      return;
    }
    popupConfig.current = config;

    setIsOpen(true);
  };

  const reset = useCallback(() => {
    requestedHeight.current = 0;
    layoutIterator.current = 0;
    previousIterator.current = 0;

    // TODO: this reset of the component is disabled, as it caused some components not rendering again, when they were shown before.
    //popupConfig.current.component = () => <></>;

    scrollViewWrapperRef.current?.setNativeProps({
      style: { height: undefined },
    });
    scrollViewRef.current?.setNativeProps({
      scrollEnabled: undefined,
    });

    setKeyboardIncludedContainerHeight(null);
    setIsOpen(false);
  }, []);

  const closePopup = () => {
    /* setTimeout(() => { */
    reset();
    /* }, CLOSE_DELAY); */
  };

  let Component = popupConfig.current.component;

  return (
    <PopupContext.Provider
      value={{
        openPopup,
        closePopup,
      }}
    >
      {children}
      <>
        {isOpen && !!Component && (
          <>
            <Backdrop
              closePopup={() => {
                !popupConfig.current.disableBackdropClose && closePopup;
              }}
              style={{
                flex: 1,
                zIndex: 99999,
                elevation: 99999,
                maxHeight: keyboardIncludedContainerHeight ? keyboardIncludedContainerHeight : undefined,
              }}
            >
              <Animated.View
                style={[
                  {
                    position: 'absolute',
                    width: screenWidth - theme.space.m * 2,
                    backgroundColor: theme.colors.white,
                    zIndex: 22,
                    elevation: 16,
                    borderRadius: 18,
                  },
                ]}
                entering={ZoomIn.duration(POPUP_ANIMATION_ENTERING_DURATION).easing(
                  Easing.bezier(0.25, 0.1, 0.25, 1).factory()
                )}
                exiting={ZoomOut.duration(POPUP_ANIMATION_EXITING_DURATION).easing(Easing.ease)}
              >
                <View
                  ref={scrollViewWrapperRef}
                  style={{
                    flex: 1,
                    /* height will be set via native props */
                  }}
                >
                  {/* scrollEnabled will be set using setNativeProps */}
                  <ScrollView
                    ref={scrollViewRef}
                    style={{ flex: 1 }}
                    contentContainerStyle={{ flex: Platform.OS === 'web' ? undefined : 0 }}
                    scrollEnabled={false}
                  >
                    <Animated.View
                      style={{
                        flex: Platform.OS === 'web' ? undefined : 0,
                      }}
                      onLayout={(event) => {
                        var { height: computedHeight } = event.nativeEvent.layout;
                        if (computedHeight <= 0) {
                          return;
                        }

                        layoutIterator.current = layoutIterator.current + 1;
                        requestedHeight.current = computedHeight;
                      }}
                    >
                      <Component />
                    </Animated.View>
                  </ScrollView>
                </View>
              </Animated.View>
            </Backdrop>
          </>
        )}
      </>
    </PopupContext.Provider>
  );
};

export default PopupProvider;
