import useTheme from '@app/hooks/useTheme';
import React, { useCallback, useContext, useEffect, useRef } from 'react';
import { getBrand } from 'utils/brand';
import { useGetReservations } from '../queries/useGetReservations';
import { UserContext } from '@app/context/usercontext/UserContextProvider';
import { NavScreenProp } from '@app/navigation/stacks/Main';
import { useIntl } from 'react-intl';
import ScreenScrollContainer from '@app/components/ScreenScrollContainer';
import StickyHeader, { RelativeHeaderArea } from '@app/components/StickyHeader';
import Button from '@app/components/Button';
import ReservationCard from '../components/ReservationCard';
import ListItems from '@app/components/ListItems2/ListItems';
import { useAnimatedScrollHandler, useSharedValue } from 'react-native-reanimated';
import { useBottomSheet } from '@app/navigation/bottom-sheet/BottomSheetProvider';
import AttachReservation from './AttachReservation';
import Login from '@domains/profile/screens/Login';
import ScreenContainer from '@app/components/ScreenContainer';
import { Reservation } from 'apis/_generated-reservation-api/models';
import { ScrollView } from 'react-native';
import { useFocusEffect } from '@react-navigation/native';

type CardReservation = {
  id?: string;
  partialReservations: Reservation[];
};

export default function Reservations({ navigation }: NavScreenProp<'Reservation'>) {
  const theme = useTheme();
  const { token } = useContext(UserContext);
  const { openBottomSheet } = useBottomSheet();
  const { formatMessage } = useIntl();
  const { data: reservations } = useGetReservations({
    brand: getBrand(theme),
    token,
  });

  const scrollOffset = useSharedValue(0);
  const scrollHandler = useAnimatedScrollHandler((event) => {
    scrollOffset.value = event.contentOffset.y;
  });
  const scrollRef = useRef<ScrollView>(null);
  const isInFocus = useRef<boolean>();

  const combinedReservations =
    reservations?.reduce((mapResult: CardReservation[], r) => {
      if (!r) {
        return mapResult;
      }
      // if there is a result object with the same combireference...
      const combiRes = mapResult.findIndex(
        (mr) =>
          !!mr.partialReservations.find((p) => !!p.combiReference && p.combiReference === r.combiReference)
      );
      if (combiRes >= 0) {
        // join the combi reservations
        mapResult[combiRes].partialReservations = [...mapResult[combiRes].partialReservations, r];
      } else {
        mapResult = [
          ...mapResult,
          {
            id: r.bookingReference,
            partialReservations: [r],
          },
        ];
      }

      return mapResult;
    }, []) ?? [];

  useFocusEffect(
    useCallback(() => {
      isInFocus.current = true;

      return () => {
        isInFocus.current = false;
      };
    }, [])
  );
  useEffect(() => {
    return navigation.addListener('tabPress', (e) => {
      if (isInFocus.current === true) {
        scrollRef.current?.scrollTo({ y: 0, animated: true });
      }
    });
  }, [navigation]);

  return (
    <ScreenContainer>
      <StickyHeader
        title={formatMessage({ id: 'app.tabs.reservations' })}
        bigTitle
        main
        scrollOffset={scrollOffset}
      />
      <ScreenScrollContainer
        animated
        onScroll={scrollHandler}
        scrollEventThrottle={1}
        topPadding={false}
        forceBottomInset
        scrollRef={scrollRef}
      >
        <RelativeHeaderArea />
        {combinedReservations?.map((r) => (
          <ReservationCard reservations={r.partialReservations} key={r.id} />
        ))}

        {(reservations?.length || 0) > 0 ? (
          <Button
            type="primary"
            onPress={() => {
              openBottomSheet({
                key: 'attach-reservation',
                component: () => <AttachReservation />,
                title: formatMessage({ id: 'app.reservation.add' }),
              });
            }}
            style={{
              backgroundColor: 'transparent',
              borderWidth: 1,
              borderColor: theme.colors.light,
            }}
          >
            <Button.Icon name="reservations-active" size="s" color="tertiary" />
            <Button.Text color="tertiary">{formatMessage({ id: 'app.reservation.add' })}</Button.Text>
          </Button>
        ) : (
          <>
            {token ? (
              <ListItems
                style={{ marginTop: 0 }}
                items={[
                  {
                    id: 'add-reservation',
                    title: formatMessage({ id: 'app.reservation.add' }),
                    subtitle: formatMessage({ id: 'app.reservation.add.subtitle' }),
                    icon: 'plus',
                    bottomSheet: {
                      key: 'attach-reservation',
                      component: () => <AttachReservation />,
                      title: formatMessage({ id: 'app.reservation.add' }),
                    },
                  },
                ]}
              />
            ) : (
              <ListItems
                style={{ marginTop: 0 }}
                items={[
                  {
                    id: 'login',
                    title: formatMessage({ id: 'app.login.title' }),
                    icon: 'profile-active',
                    bottomSheet: {
                      key: 'login',
                      initialHeight: 197,
                      component: () => (
                        <Login
                          onSuccess={() => {
                            navigation.goBack();
                          }}
                          navigation={navigation}
                        />
                      ),
                      title: formatMessage({ id: 'app.login.title' }),
                    },
                  },
                ]}
              />
            )}
          </>
        )}
      </ScreenScrollContainer>
    </ScreenContainer>
  );
}
