import React from 'react';
import useTheme from '@app/hooks/useTheme';
import { View } from 'react-native';
import { useGetProducts } from '../queries/useGetProducts';
import Text from '@app/components/Text';
import { useIntl } from 'react-intl';
import Button from '@app/components/Button';
import { CartContextUpdater } from '@app/context/CartProvider';
import { useGetProductOptions } from '../queries/useGetProductOptions';
import { useFormik } from 'formik';
import { useGetProductCombinations } from '../queries/useGetProductCombinations';
import LandalImage from '@app/components/LandalImage';
import { useFocusEffect } from '@react-navigation/native';
import useMetrics from '@app/metrics/useMetrics';
import useCart from '@app/hooks/useCart';
import { ProductCombination } from 'apis/_generated-park-api/models';
import StickyHeader from '@app/components/StickyHeader';
import StickyBottomBar from '@app/navigation/StickyBottomBar';
import RadioList from '@app/components/ListItems2/RadioList';
import ScreenScrollContainer from '@app/components/ScreenScrollContainer';
import { NavScreenProp } from '@app/navigation/stacks/Main';

const TYPE_MANDATORY_VALUE = '4';

export default function ProductOptions({ navigation, route }: NavScreenProp<'ProductOptions'>) {
  const { formatMessage, formatNumber } = useIntl();
  const theme = useTheme();
  const { logScreenView } = useMetrics();
  const { activeShop: shopId } = useCart();

  const id = route.params?.id;
  const categoryId = route.params?.categoryId;
  const { data: products, isFetching: productFetching, isError: productFetchingError } = useGetProducts({});
  const { data: options, isFetching: optionsFetching } = useGetProductOptions({
    productId: id,
    options: {
      onSuccess: () => {
        formik.validateForm();
      },
    },
  });
  const { data: combinations, isFetching: combinationFetching } = useGetProductCombinations({
    productId: id,
    options: {
      onSuccess: () => {
        formik.validateForm();
      },
    },
  });
  const { updateCart } = React.useContext(CartContextUpdater);

  const isFetching = productFetching || optionsFetching || combinationFetching;
  const isError = productFetchingError;

  useFocusEffect(
    React.useCallback(() => {
      if (shopId && id) {
        logScreenView('ProductOptions', [shopId.toString(), categoryId.toString(), id.toString()]);
      }
    }, [shopId, id, logScreenView, categoryId])
  );

  const formik = useFormik<Record<string, any>>({
    initialValues: {},
    onSubmit: (values) => {
      __DEV__ && console.log(values);
    },
    validateOnMount: true,
    validate: (values) => {
      // At least we need response from the options call to continue
      if (!options) {
        return { error: true };
      }
      const mandatoryFieldErrors = options?.filter((o) => {
        return o.type === TYPE_MANDATORY_VALUE && !!o.id && !values[o.id];
      });

      const foundCombination = getCombination(values);

      if ((options?.length || 0) > 0 && !foundCombination) {
        return { error: true };
      }

      if (mandatoryFieldErrors?.length) {
        return { error: true };
      }

      return {};
    },
  });

  const getCombination = (values: Record<string, any>): ProductCombination | null => {
    // Check if the combination is available.
    // TODO: This is a temporary solution, and should be improved when the API's are improved.
    let foundCombination: ProductCombination | null = null;
    combinations?.forEach((c) => {
      const combinationLength = c.attributes?.length;
      let matchingAttributesInCombination = 0;
      // If every selected attribute is within the combination
      Object.values(values).forEach((a) => {
        if (c.attributes?.find((o) => o?.name === a?.name)) {
          matchingAttributesInCombination++;
        }
        if (combinationLength === matchingAttributesInCombination) {
          foundCombination = c;
        }
      });
    });
    return foundCombination;
  };

  const product = products?.find((p) => p.id === id);

  let totalPrice = 0;
  if (product) {
    totalPrice = formik.values
      ? product.price +
        Object.values(formik.values)
          ?.map((v) => v.price)
          ?.reduce((prev: number, next: number) => Number(prev) + Number(next), [])
      : product.price;
  }

  const addProductToCart = () => {
    const selectedOptions = Object.values(formik.values);
    const foundCombination = getCombination(formik.values);
    if (selectedOptions && selectedOptions.length > 0 && !foundCombination) {
      return;
    }

    updateCart((prev) => {
      const newArray = [...prev];
      const thisItemInCart = prev.find(
        (item) => item.product?.id === id && item.attributeComboId === foundCombination?.id
      );

      if (thisItemInCart) {
        thisItemInCart.quantity = thisItemInCart.quantity + 1;
        thisItemInCart.totalPrice = totalPrice;
        const filteredCart = prev.filter((item) => item.product?.id !== id);
        return [...filteredCart, thisItemInCart];
      }

      if (product) {
        const newItem = {
          product,
          quantity: 1,
          options: selectedOptions,
          totalPrice,
          attributeComboId: foundCombination?.id,
        };
        return [...newArray, newItem];
      }
      return prev;
    });
    navigation.goBack();
  };

  const isValid = (!!options && options.length === 0) || formik.isValid;

  return (
    <View style={{ flex: 1 }}>
      <StickyHeader
        title={formatMessage({ id: 'app.forms.labels.pickOptions' })}
        showClose
        navigation={navigation}
      />
      <ScreenScrollContainer
        forceBottomInset
        contentContainerStyle={{ alignItems: 'center', paddingTop: theme.space.l }}
      >
        <View
          style={{
            alignItems: 'center',
          }}
        >
          <View
            style={{
              height: 120,
              width: 120,
              backgroundColor: theme.colors.white,
              borderRadius: 12,
              marginBottom: theme.space.m,
              padding: theme.space.s,
            }}
          >
            {!!product?.productMainPhoto && (
              <LandalImage
                remoteSource={product?.productMainPhoto}
                style={{ flex: 1, borderRadius: 8 }}
                size={'xl'}
                resizeMode="contain"
              />
            )}
          </View>
          <Text type="title" bold style={{ marginBottom: theme.space.s }}>
            {product?.name}
          </Text>
          {product?.price !== undefined && (
            <Text size="s" style={{ marginBottom: theme.space.l }} bold>
              {formatNumber(product?.price, {
                style: 'currency',
                currency: 'EUR',
                currencyDisplay: 'symbol',
              })}
            </Text>
          )}
          <Text
            type="body"
            color="gray"
            size="s"
            style={{ textAlign: 'center', marginBottom: theme.space.l }}
          >
            {product?.shortDescription}
          </Text>
        </View>
        <View style={{ width: '100%' }}>
          {options?.map((a) => {
            return (
              <RadioList
                title={`${a.attributename} - ${formatMessage({ id: 'app.shop.order.makeaselection' })}`}
                items={
                  a?.items?.map((o) => {
                    const isSelected = !!a.id && formik.values[a.id.toString()]?.id === o?.id;
                    return {
                      label: o?.optionsname,
                      labelRight:
                        Number(o?.price) === 0
                          ? '-'
                          : `+ ${formatNumber(o?.price ?? 0, {
                              style: 'currency',
                              currency: 'EUR',
                              currencyDisplay: 'symbol',
                            })}`,
                      value: o?.id,
                    };
                  }) ?? []
                }
                onSelect={(value) => {
                  const fieldId = a.id;
                  if (!fieldId) return;

                  const option = a.items?.find((i) => i?.id === Number(value));

                  const newValue = {
                    id: option?.id,
                    name: option?.optionsname,
                    price: option?.price,
                  };

                  if (formik.values[fieldId.toString()]?.id === option?.id) {
                    formik.setFieldValue(fieldId.toString(), undefined);
                    return;
                  }
                  formik.setFieldValue(fieldId.toString(), newValue);
                  //formik.setFieldValue(a.id.toString(), value);
                }}
              />
            );
          })}
        </View>
      </ScreenScrollContainer>
      <StickyBottomBar style={{ padding: theme.space.l, justifyContent: 'center' }}>
        <View style={{ flex: 1 }}>
          <Button
            onPress={addProductToCart}
            style={{
              backgroundColor: theme.colors.tertiary,
            }}
            disabled={!isValid || isFetching}
          >
            {isFetching ? (
              <Button.ActivityIndicator />
            ) : (
              <Button.Text color="white" style={{ flex: 1 }}>
                {`${formatMessage({ id: 'app.shop.order.add' })} (${formatNumber(totalPrice, {
                  style: 'currency',
                  currency: 'EUR',
                  currencyDisplay: 'symbol',
                })})`}
              </Button.Text>
            )}
          </Button>
        </View>
      </StickyBottomBar>
    </View>
  );
}
