import { ListItem } from '@app/components/ListItems2/ListItems';
import ParkMap from '@app/components/ParkMap';
import { UserContext } from '@app/context/usercontext/UserContextProvider';
import useMetrics from '@app/metrics/useMetrics';
import { useGetPark } from '@domains/park/contact-and-info/queries/useGetPark';
import { useGetReservation } from '@domains/reservations/reservation/queries/useGetReservation';
import CheckoutNowSheet from '@domains/sheets/CheckoutNowSheet';
import DamageReportSheet from '@domains/sheets/DamageReportSheet';
import DepartureTimeSheet from '@domains/sheets/departure-time/DepartureTimeSheet';
import { useGetAccommodationGeo } from '@domains/start/queries/useGetAccommodationGeo';
import { AccoGeo } from 'apis/_generated-park-api/models';
import { BookingStatusType } from 'apis/_generated-reservation-api/models';
import React, { useContext } from 'react';
import { useIntl } from 'react-intl';
import { View } from 'react-native';
import { getBrand } from 'utils/brand';
import {
  formatDayWithoutTime,
  fromUnixNumber,
  getTime,
  ReadableDayByDate,
  useTimeToDaypart,
} from 'utils/date';
import externalNavigateToLocation from 'utils/externalNavigateToLocation';
import useTheme from './useTheme';

interface Props {
  items: ListItem[];
  parkCode?: string;
  bookingReference?: string;
}
export default function usePracticalItems({ items, parkCode, bookingReference }: Props) {
  const theme = useTheme();
  const { logEvent } = useMetrics();
  const { formatMessage } = useIntl();

  const { data: park } = useGetPark({ parkCodeOverride: parkCode });
  const { token } = useContext(UserContext);
  const { data: reservation } = useGetReservation({
    token,
    brand: getBrand(theme),
    bookingReference,
  });
  const timeToDaypart = useTimeToDaypart(reservation?.accommodationShouldLeaveDateTime);

  const getAcco = useGetAccommodationGeo({ selectedParkCode: parkCode ?? '' });

  const navigateToMapsWithPark = async () => {
    if (!park) return;
    if (park.latitude && park.longitude) {
      logEvent('ViewRouteToPark', { park: parkCode ?? '' });
      await externalNavigateToLocation({
        latitude: park.latitude,
        longitude: park.longitude,
        name: park.name,
      });
    }
  };

  const navigateToMapsWithAccommodation = async (acc: AccoGeo, unitCode: string) => {
    if (acc.lat && acc.lon) {
      logEvent('ViewRouteToBungalow', { bungalow_number: unitCode });
      await externalNavigateToLocation({
        latitude: acc.lat,
        longitude: acc.lon,
        name: formatMessage({ id: 'app.launcher.acco' }) + ' ' + unitCode,
      });
    }
  };

  const navigateToAcco = async () => {
    const unitCode = reservation?.unitCode;

    if (!park?.allowAccommodationNavigation || reservation?.unitTypeIndicator === 'CAMPING') {
      navigateToMapsWithPark();
      return;
    }

    if (unitCode) {
      try {
        const accommodation = await getAcco(unitCode);
        if (!accommodation) {
          logEvent('ShowBungalowRouteError', { parkId: park.id, unitCode });
          throw 'oh no';
        }
        navigateToMapsWithAccommodation(accommodation, unitCode);
        return;
      } catch (ex) {
        logEvent('ShowBungalowRouteError', { parkId: park.id, unitCode });
        navigateToMapsWithPark();
        return;
      }
    }
  };

  const practicalItems: ListItem[] = [
    {
      id: 'reservation-details',
      title: formatMessage({ id: 'app.practical.reservation.title' }),
      subtitle: formatMessage({ id: 'app.practical.reservation.subtitle' }),
      localImage: require('@assets/images/blur-images/reserveringsdetails.jpg'),
      blurImage: true,
      icon: 'reservation-details',
      navigateTo: reservation?.combiReference ? 'ReservationsTab' : 'Reservation',
    },
    {
      id: 'report-damage',
      title: formatMessage({ id: 'app.practical.damageReport.title' }),
      subtitle: formatMessage({ id: 'app.practical.damageReport.subtitle' }),
      localImage: require('@assets/images/blur-images/schade-melden.jpg'),
      blurImage: true,
      icon: 'report-damage',
      bottomSheet: {
        component: () => <DamageReportSheet parkCode={parkCode} bookingReference={bookingReference} />,
        title: formatMessage({ id: 'app.practical.damageReport.title' }),
        key: 'report-damage',
        initialHeight: 524,
        //delay: 400,
      },
    },
    {
      id: 'contact-reception',
      title: formatMessage({ id: 'app.practical.reception.title' }),
      subtitle: formatMessage({ id: 'app.practical.reception.subtitle' }),
      localImage: require('@assets/images/blur-images/contact-met-receptie.jpg'),
      blurImage: true,
      icon: 'call',
      navigateTo: 'ContactAndInfo',
      navigateParams: { parkCode },
    },
    {
      id: 'good-to-know',
      title: formatMessage({ id: 'app.practical.goodToKnow.title' }),
      subtitle: formatMessage({ id: 'app.practical.goodToKnow.subtitle' }),
      localImage: require('@assets/images/blur-images/parkplattegrond.jpg'), // TODO: other blur image
      blurImage: true,
      icon: 'information-circular',
      navigateTo: 'AtThePark',
      navigateParams: { parkCode, category: 'goodToKnow' },
    },
    {
      id: 'route-park',
      title: formatMessage({ id: 'app.practical.routePark.title' }),
      subtitle: formatMessage({ id: 'app.practical.routePark.subtitle' }),
      localImage: require('@assets/images/blur-images/route-naar-het-park.jpg'),
      blurImage: true,
      icon: 'navigate',
      onPress: navigateToMapsWithPark,
      external: true,
    },
    {
      id: 'route-acco',
      title: formatMessage({ id: 'app.practical.routeAcco.title' }),
      subtitle: formatMessage({ id: 'app.practical.routeAcco.subtitle' }),
      localImage: require('@assets/images/blur-images/route-naar-accommodatie.jpg'),
      blurImage: true,
      icon: 'navigate',
      onPress: navigateToAcco,
    },
    {
      id: 'parkmap',
      title: formatMessage({ id: 'app.practical.parkMap.title' }),
      subtitle: formatMessage({ id: 'app.practical.parkMap.subtitle' }),
      localImage: require('@assets/images/blur-images/parkplattegrond.jpg'),
      blurImage: true,
      icon: 'map',
      bottomSheet: {
        component: () => (
          <View style={{ width: '100%', flex: 1 }}>
            <ParkMap parkCode={parkCode} bookingReference={reservation?.bookingReference} />
          </View>
        ),
        title: formatMessage({ id: 'app.practical.parkMap.title' }),
        fullHeight: true,
        delay: 300,
        panHandleOnly: true,
      },
    },
    {
      id: 'departure-time',
      title: formatMessage({ id: 'app.reservation.unknownDepartureDate' }),
      subtitle: reservation?.expectedDepartureDate
        ? ReadableDayByDate(fromUnixNumber(reservation?.expectedDepartureDate * 1000), true, false) +
          ' ' +
          timeToDaypart(
            getTime(reservation?.expectedDepartureDate * 1000),
            formatDayWithoutTime(fromUnixNumber(reservation?.expectedDepartureDate * 1000)) !==
              formatDayWithoutTime(fromUnixNumber(reservation?.departureDate * 1000))
          )
        : undefined,
      icon: 'clock',
      localImage: require('@assets/images/blur-images/uitcheckmoment-doorgeven.jpg'),
      blurImage: true,
      bottomSheet: {
        component: () => <DepartureTimeSheet bookingReference={reservation?.bookingReference ?? ''} />,
        title: formatMessage({ id: 'app.reservation.unknownDepartureDate' }),
        backgroundColor: 'canvas',
        key: 'departure-time',
      },
      disabled: reservation?.status === BookingStatusType.Departed,
    },
    {
      id: 'checkout-now',
      title: formatMessage({
        id: 'app.launcher.checkoutNow',
      }),
      icon: 'check-out-small',
      localImage: require('@assets/images/blur-images/nu-uitchecken.jpg'),
      blurImage: true,
      bottomSheet: {
        component: () => <CheckoutNowSheet bookingReference={reservation?.bookingReference ?? ''} />,
        title: formatMessage({ id: 'app.launcher.checkoutNow' }),
        key: 'checkout-now',
      },
    },
  ];

  const mergedItems = items?.reduce<ListItem[]>((groupResult, iterateItem) => {
    if (!iterateItem) return groupResult;
    const staticItem = practicalItems.find((i) => i.id === iterateItem.id);
    if (!staticItem) return groupResult;

    return [...groupResult, { ...staticItem, ...iterateItem }];
  }, []);
  return mergedItems;
}
