import { gql, GraphQLClient } from 'graphql-request';
import Config from 'react-native-config';

export interface AttachReservationResult {
  Success: boolean;
  ResultCode: string;
  ResultMessage: string;
}

export type AttachReservationRequest = {
  uid: string;
  bookingReference: string;
};

export function useAttachReservation() {
  const attachReservation = async (request: AttachReservationRequest) => {
    const graphQLClient = new GraphQLClient(Config.RESERVATION_API_URL);
    const mutation = gql`
      mutation attachReservation($request: AttachReservationRequest) {
        attachReservation(attachReservationRequest: $request) {
          success
          statusCode
          errorMessage
          priceHasChanged
        }
      }
    `;

    let result = await graphQLClient.rawRequest(mutation, { request });
    return result?.data?.attachReservation;
  };

  return { attachReservation };
}
