import { CultureContext } from '@app/context/usercontext/CultureProvider';
import React from 'react';

export type LanguageCode = 'nl-NL' | 'en-US' | 'de-DE' | 'fr-FR' | 'da-DK';

export const reserveLanguageCode = {
  'nl-NL': 'dutch',
  'de-DE': 'german',
  'fr-FR': 'french',
  'da-DK': 'danish',
  'en-US': 'english',
} as const;

export const languageCode = {
  'nl-NL': 'nl',
  'de-DE': 'de',
  'fr-FR': 'fr',
  'da-DK': 'da',
  'en-US': 'en',
} as const;

export const countryIcons = {
  'nl-NL': require('@assets/images/nl.png'),
  'de-DE': require('@assets/images/de.png'),
  'fr-FR': require('@assets/images/fr.png'),
  'da-DK': require('@assets/images/da.png'),
  'en-US': require('@assets/images/en.png'),
} as const;

export default function useCulture() {
  let cultureCode = React.useContext(CultureContext);

  return {
    cultureCode,
    reserveLanguageCode: !!cultureCode && reserveLanguageCode[cultureCode],
    languageCode: !!cultureCode && languageCode[cultureCode],
    countryIcon: !!cultureCode && countryIcons[cultureCode],
    FALLBACK_CULTURE: 'en-US' as LanguageCode,
  };
}
