import React from 'react';
import { Alert, useWindowDimensions, View } from 'react-native';
import Text from '@app/components/Text';
import { fromUnixNumber, getFormattedDate, nightsUntil } from 'utils/date';
import ViewShot, { captureRef } from 'react-native-view-shot';
import QRCode from 'react-native-qrcode-svg';
import Button from '@app/components/Button';
import useTheme from '@app/hooks/useTheme';
import { useIntl } from 'react-intl';
import useMetrics from '@app/metrics/useMetrics';
import { Reservation, Voucher } from 'apis/_generated-reservation-api/models';
import Icon from '@app/components/Icon';
import Share, { ShareOptions } from 'react-native-share';
import Shadow from '@app/components/Shadow';

const QR_FORMAT = 'png';
const QR_RESULT = 'base64';

const ACTIVE_VOUCHER_STATUS = 'CREATED';
const REDEEMED_VOUCHER_STATUS = 'REDEEMED';

interface Props {
  voucher: Voucher;
  reservation?: Reservation;
  extraId?: string;
  voucherId?: string;
  index: number;
  extraName?: string;
  totalVouchers: number;
}

export default function VoucherCard({
  voucher,
  reservation,
  extraId,
  index,
  extraName,
  totalVouchers,
  voucherId,
}: Props) {
  const theme = useTheme();
  const { formatMessage } = useIntl();
  const { logEvent } = useMetrics();

  let qrCodeRef: ViewShot | null = null;

  const voucherAvailable = !!voucher.code && voucher.status === ACTIVE_VOUCHER_STATUS;
  const voucherAvailableLater = !voucher.code;
  const voucherUsed = voucher.status === REDEEMED_VOUCHER_STATUS;

  const { width: screenWidth } = useWindowDimensions();
  const QR_SIZE = screenWidth * 0.6;
  const cardSize = QR_SIZE + theme.space.xxl * 2;

  return (
    <View
      style={{
        width: screenWidth,
        paddingHorizontal: theme.space.xxl,
        marginBottom: theme.space.l, // needed for the shadow
      }}
    >
      <Shadow>
        <View
          style={{
            alignItems: 'center',
            backgroundColor: theme.colors.white,
            borderRadius: 12,
            paddingTop: theme.space.xxl,
            paddingBottom: voucherAvailableLater ? theme.space.xxl : undefined,
          }}
        >
          {voucherAvailable ? (
            <>
              {/* @ts-ignore - incorrect typing as children are allowed (necessary even) */}
              <ViewShot
                ref={(ref) => {
                  qrCodeRef = ref;
                }}
              >
                <QRCode value={voucher.code} size={QR_SIZE} />
              </ViewShot>
              <View
                style={{
                  width: QR_SIZE,
                  justifyContent: 'center',
                  alignItems: 'center',
                  paddingTop: theme.space.l,
                }}
              >
                <Text color="gray">{voucher.code}</Text>
                <View
                  style={{
                    width: cardSize + theme.space.m + 24,
                    alignItems: 'center',
                    flexDirection: 'row',
                  }}
                >
                  <View
                    style={{ width: 24, height: 24, backgroundColor: theme.colors.canvas, borderRadius: 12 }}
                  />
                  <View style={[{ height: 2, overflow: 'hidden', flex: 1 }]}>
                    <View
                      style={[
                        {
                          height: 2,
                          borderWidth: 2,
                          borderColor: theme.colors.canvas,
                          borderStyle: 'dashed',
                        },
                      ]}
                    />
                  </View>
                  <View
                    style={{ width: 24, height: 24, backgroundColor: theme.colors.canvas, borderRadius: 12 }}
                  />
                </View>
                <Text color="gray">
                  {formatMessage({ id: 'app.extra.voucher' })}{' '}
                  {formatMessage({ id: 'app.conversation.companion.indicator' })
                    .replace('{0}', (index + 1).toString())
                    .replace('{1}', totalVouchers.toString())}
                </Text>
                <Text type="title" size="l" bold style={{ marginTop: 2 }}>
                  {extraName}
                </Text>
                <Button
                  onPress={async () => {
                    if (!qrCodeRef) {
                      return;
                    }

                    logEvent('VoucherShare', { extra_id: extraId, voucher_id: voucherId });

                    captureRef(qrCodeRef, { result: QR_RESULT, format: QR_FORMAT }).then(
                      async (data) => {
                        let base64Url = `data:image/${QR_FORMAT};base64,${data}`;

                        const shareOptions: ShareOptions = {
                          title: 'Share file',
                          url: base64Url,
                          failOnCancel: false,
                        };

                        try {
                          await Share.open(shareOptions);
                        } catch (error) {
                          logEvent('VoucherShareError', { error_type: 'share' });
                          Alert.alert('', formatMessage({ id: 'app.forms.tablebooking.error.generic' }));
                        }
                      },
                      (error) => {
                        console.error('Oops, snapshot failed', error);
                        logEvent('VoucherShareError', { error_type: 'snapshot' });
                        Alert.alert('', formatMessage({ id: 'app.forms.tablebooking.error.generic' }));
                      }
                    );
                    return;
                  }}
                  style={{
                    backgroundColor: 'transparent',
                    padding: 0,
                  }}
                >
                  <Button.Text color="tertiary">{formatMessage({ id: 'app.general.share' })}</Button.Text>
                  <Icon
                    name="share"
                    size="s"
                    color="tertiary"
                    style={{
                      marginRight: theme.space.s,
                      marginTop: 3,
                      alignSelf: 'center',
                    }}
                  />
                </Button>
              </View>
            </>
          ) : (
            <View style={{ alignItems: 'center' }}>
              <View style={{ opacity: 0.1 }}>
                <QRCode value={'bollo'} size={QR_SIZE} />
              </View>
              <View
                style={{
                  height: QR_SIZE,
                  maxWidth: QR_SIZE - theme.space.s,
                  marginTop: -QR_SIZE,
                  justifyContent: 'center',
                }}
              >
                <Text
                  color="gray"
                  style={{
                    textAlign: 'center',
                  }}
                >
                  {voucherUsed
                    ? formatMessage({ id: 'app.extra.voucherAlreadyUsed' })
                    : formatMessage({ id: 'app.extra.voucherAvailableLater' })
                        .replace('{0}', nightsUntil(reservation?.arrivalDate ?? 0).toString())
                        .replace(
                          '{1}',
                          getFormattedDate(new Date(fromUnixNumber((reservation?.arrivalDate ?? 0) * 1000)))
                        )}
                </Text>
              </View>
            </View>
          )}
        </View>
      </Shadow>
    </View>
  );
}
