import React, { useEffect, useState } from 'react';
import useTheme from '@app/hooks/useTheme';
import { Alert, View } from 'react-native';
import Text from '@app/components/Text';
import ContentContainer from '@app/components/DetailScreen/ContentContainer';
import NumberControl from '@app/components/Form/NumberControl';
import { useFormik } from 'formik';
import { getAPIDate, getFormattedDate } from 'utils/date';
import { useGetTablebookingAvailability } from '../queries/useGetTablebookingAvailability';
import { useGetRestaurantInfo } from '../queries/useGetRestaurantInfo';
import TimeSlotSelector from '@app/components/Reservation/TimeSlotSelector';
import FormCheckBox from '@app/components/Form/FormCheckBox';
import Button from '@app/components/Button';
import { KeyboardAwareScrollView } from 'react-native-keyboard-aware-scroll-view';
import DateSelector from '@app/components/Reservation/DateSelector';
import { useIntl } from 'react-intl';
import { useBookTable } from '../queries/useBookTable';
import { useGetPark } from '@domains/park/contact-and-info/queries/useGetPark';
import { useFocusEffect } from '@react-navigation/native';
import useMetrics from '@app/metrics/useMetrics';
import Config from 'react-native-config';
import { useGetFacilities } from '@domains/park/at-the-park/queries/useGetFacilities';
import { isValidEmail, isValidPhone } from 'utils/validation';
import { BlockRequest, BookRequest } from 'apis/_generated-park-api/models';
import { TextInput } from 'react-native-gesture-handler';
import StickyHeader from '@app/components/StickyHeader';
import { NavScreenProp } from '@app/navigation/stacks/park/FoodAndDrinks';
import FormControl from '@app/components/Form/FormControl';
import { NotificationContextUpdater } from '@domains/notification/context/NotificationProvider';
import { NotificationMessage } from '@domains/notification/types/NotificationMessage';

type FormFields =
  | 'adults'
  | 'kids_young'
  | 'kids_older'
  | 'kids_chairs'
  | 'date'
  | 'time_of_day'
  | 'time'
  | 'timestamp'
  | 'name'
  | 'phone'
  | 'email'
  | 'allergy_diet'
  | 'allergy'
  | 'diet'
  | 'remarks'
  | 'no_covid_symptoms'
  | 'number_of_households';

type FormikErrors = {
  date?: string;
  email?: string;
  name?: string;
  phone?: string;
  time?: string;
  no_covid_symptoms?: string;
};

const TableBooking = ({ navigation, route }: NavScreenProp<'TableBooking'>) => {
  const formik = useFormik<Record<FormFields, any>>({
    initialValues: {
      adults: 2,
      kids_young: 0,
      kids_older: 0,
      kids_chairs: 0,
      date: undefined,
      time_of_day: undefined,
      time: undefined,
      timestamp: undefined,
      name: '',
      phone: '',
      email: '',
      allergy_diet: false,
      allergy: '',
      diet: '',
      remarks: '',
      no_covid_symptoms: false,
      number_of_households: 1,
    },
    validateOnMount: true,
    onSubmit: () => {},
    validate: (validatedValues) => {
      const errors: FormikErrors = {};

      if (!validatedValues.date) {
        errors.date = 'no date';
      }
      if (!validatedValues.email || !isValidEmail(validatedValues.email)) {
        errors.email = 'no email';
      }

      if (!validatedValues.name) {
        errors.name = 'no name';
      }

      if (!validatedValues.phone || !isValidPhone(validatedValues.phone)) {
        errors.phone = 'no phone';
      }

      if (!validatedValues.time) {
        errors.time = 'no time';
      }

      if (!validatedValues.no_covid_symptoms && park?.covidForm) {
        errors.no_covid_symptoms = 'no covid answer';
      }
      return errors;
    },
  });

  const setFormField = (id: FormFields, value: any) => {
    formik.setFieldValue(id, value);
  };

  const parkCode = route?.params?.parkCode;

  const theme = useTheme();
  const { logEvent, logScreenView } = useMetrics();
  const { data: park } = useGetPark({ parkCodeOverride: parkCode });
  const { makeBooking } = useBookTable();
  const { setNotifications } = React.useContext(NotificationContextUpdater);

  const { formatMessage } = useIntl();
  const [totalLimit, setTotalLimit] = useState(9);
  const [processing, setProcessing] = useState(false);
  const scrollViewRef = React.useRef(null);
  const phoneInputRef = React.useRef<TextInput>(null);

  const restaurantId = Config.ENVIRONMENT === 'staging' ? '16978691' : route.params?.restaurantId;

  const TOTAL_GUESTS = formik.values.adults + formik.values.kids_young + formik.values.kids_older;
  const date = getAPIDate(formik.values.date);
  const { data: availability, isLoading: availabilityLoading } = useGetTablebookingAvailability({
    date,
    guests: TOTAL_GUESTS,
    restaurantId,
  });

  const { data } = useGetFacilities({ parkCodeOverride: parkCode });
  const facility = data?.find((d) => d.reservationId === route.params.restaurantId);

  const { data: restaurantInfo } = useGetRestaurantInfo({
    restaurantId,
  });

  const maxGuests = restaurantInfo?.maxGuests;
  useEffect(() => {
    if (maxGuests) {
      setTotalLimit(maxGuests);
    }
  }, [maxGuests]);

  useFocusEffect(
    React.useCallback(() => {
      if (restaurantId) {
        logScreenView('TableBooking', [restaurantId]);
      }
    }, [restaurantId, logScreenView])
  );

  const LIMIT_REACHED = TOTAL_GUESTS >= totalLimit;
  const ASK_COVID_QUESTION = park?.covidForm;

  const ANALYTICS_TYPE = 'foodAndDrinks';
  const ANALYTICS_RESERVATION = `${facility?.name}|${TOTAL_GUESTS} couverts|${formik.values.date}`;

  const logBookingError = (error: string) => {
    logEvent('GenerateLeadError', {
      type: ANALYTICS_TYPE,
      error_type: error,
      reservation: ANALYTICS_RESERVATION,
    });
  };

  return (
    <>
      <StickyHeader
        showClose
        navigation={navigation}
        title={`${formatMessage({ id: 'app.forms.labels.makeareservationat' })} ${facility?.name}`}
      />
      <KeyboardAwareScrollView
        style={{ flex: 1 }}
        ref={scrollViewRef}
        enableOnAndroid
        extraScrollHeight={120}
        keyboardOpeningTime={Number.MAX_SAFE_INTEGER}
        enableResetScrollToCoords={false}
        keyboardShouldPersistTaps="handled"
      >
        <ContentContainer style={{ flex: 1 }}>
          <View style={{ marginBottom: theme.space.xl }}>
            <NumberControl
              title={formatMessage({ id: 'app.forms.labels.amountofadults' })}
              value={formik.values.adults}
              minimum={1}
              maximum={LIMIT_REACHED ? formik.values.adults : totalLimit}
              onChange={(value) => setFormField('adults', value)}
            />
          </View>
          <View style={{ marginBottom: theme.space.xl }}>
            <NumberControl
              title={formatMessage({ id: 'app.forms.labels.amountofyoungkids' })}
              value={formik.values.kids_young}
              maximum={LIMIT_REACHED ? formik.values.kids_young : totalLimit}
              onChange={(value) => {
                setFormField('kids_young', value);
                if (value < formik.values.kids_chairs) {
                  setFormField('kids_chairs', value);
                }
              }}
            />
          </View>
          {!!formik.values.kids_young && (
            <View style={{ marginBottom: theme.space.xl }}>
              <NumberControl
                title={formatMessage({ id: 'app.forms.labels.amountofkidschairs' })}
                value={formik.values.kids_chairs}
                maximum={formik.values.kids_young}
                onChange={(value) => setFormField('kids_chairs', value)}
              />
            </View>
          )}
          <View style={{ marginBottom: theme.space.xl }}>
            <NumberControl
              title={formatMessage({ id: 'app.forms.labels.amountofolderkids' })}
              value={formik.values.kids_older}
              maximum={LIMIT_REACHED ? formik.values.kids_older : totalLimit}
              onChange={(value) => setFormField('kids_older', value)}
            />
          </View>
          {LIMIT_REACHED ? (
            <>
              <Text>
                {`${formatMessage({
                  id: 'app.forms.validation.peoplelimitreached.part1',
                })}\n\n${formatMessage({
                  id: 'app.forms.validation.peoplelimitreached.part2',
                })} ${totalLimit} ${formatMessage({ id: 'app.forms.validation.peoplelimitreached.part3' })}`}
              </Text>
            </>
          ) : (
            <View>
              <DateSelector
                date={formik.values.date}
                onSelectDate={(newDate) => {
                  // Reset time when another date is selected, as the previous time might not be available this day
                  formik.setValues({
                    ...formik.values,
                    date: newDate,
                    time: undefined,
                  });
                }}
              />
              {!!formik.values.date && !formik.errors.date && (
                <TimeSlotSelector
                  time={formik.values.time}
                  onSelectTime={(label, timestamp) => {
                    formik.setValues({
                      ...formik.values,
                      time: label,
                      timestamp,
                    });
                  }}
                  slotDuration={15}
                  availability={availability}
                  showDayPart={true}
                  dataIsLoading={availabilityLoading}
                />
              )}
              {!!formik.values.time && !formik.errors.time && (
                <View
                  style={{
                    backgroundColor: theme.colors.white,
                    margin: -theme.space.l,
                    padding: theme.space.l,
                  }}
                >
                  <FormControl
                    label={formatMessage({ id: 'app.forms.labels.name' })}
                    value={formik.values.name}
                    isValid={!formik.errors.name}
                    onChangeText={(text) => {
                      setFormField('name', text);
                    }}
                    onSubmitEditing={() => phoneInputRef.current?.focus()}
                    autoFocus
                  />
                  {!!formik.values.name && !formik.errors.name && (
                    <FormControl
                      label={formatMessage({ id: 'app.forms.labels.phone' })}
                      value={formik.values.phone}
                      isValid={!formik.errors.phone}
                      onChangeText={(text) => {
                        setFormField('phone', text);
                      }}
                    />
                  )}
                  {!!formik.values.phone && !formik.errors.phone && (
                    <FormControl
                      label={
                        formatMessage({ id: 'app.forms.labels.email' }) +
                        ' ' +
                        formatMessage({ id: 'app.forms.labels.email.explanation' })
                      }
                      value={formik.values.email}
                      isValid={!formik.errors.email}
                      onChangeText={(text) => {
                        setFormField('email', text);
                      }}
                    />
                  )}
                  {!!formik.values.email && !formik.errors.email && (
                    <>
                      <View style={{ marginBottom: theme.space.xl, flexDirection: 'row' }}>
                        <FormCheckBox
                          value={formik.values.allergy_diet}
                          onValueChange={(newValue) => setFormField('allergy_diet', newValue)}
                        />
                        <Text bold size="s" style={{ marginLeft: 16, flex: 1 }}>
                          {formatMessage({ id: 'app.forms.labels.allergyordiet' })}
                        </Text>
                      </View>
                      {formik.values.allergy_diet && (
                        <>
                          <FormControl
                            label={formatMessage({ id: 'app.forms.labels.allergy' })}
                            value={formik.values.allergy}
                            isValid={true}
                            onChangeText={(text) => {
                              setFormField('allergy', text);
                            }}
                            multiline
                            optional
                          />
                          <FormControl
                            label={formatMessage({ id: 'app.forms.labels.diet' })}
                            value={formik.values.diet}
                            isValid={true}
                            onChangeText={(text) => {
                              setFormField('diet', text);
                            }}
                            multiline
                            optional
                          />
                        </>
                      )}
                      <FormControl
                        label={formatMessage({ id: 'app.general.remarks' })}
                        value={formik.values.remarks}
                        isValid={!formik.errors.remarks}
                        onChangeText={(text) => {
                          setFormField('remarks', text);
                        }}
                        multiline
                        optional
                      />
                      {ASK_COVID_QUESTION && (
                        <>
                          <View style={{ marginBottom: theme.space.xl, flexDirection: 'row' }}>
                            <FormCheckBox
                              value={formik.values.no_covid_symptoms}
                              onValueChange={(newValue) => setFormField('no_covid_symptoms', newValue)}
                            />
                            <Text bold size="s" style={{ flex: 1, marginLeft: theme.space.xl }}>
                              {formatMessage({ id: 'app.forms.labels.covid' })}
                            </Text>
                          </View>
                          <View style={{ marginBottom: theme.space.xl }}>
                            <NumberControl
                              title={formatMessage({ id: 'app.forms.labels.numberofhouseholds' })}
                              value={formik.values.number_of_households}
                              onChange={(value) => setFormField('number_of_households', value)}
                              minimum={1}
                            />
                          </View>
                        </>
                      )}
                      {formik.isValid && (
                        <>
                          <Button
                            onPress={async () => {
                              setProcessing(true);

                              const blockRequest: BlockRequest = {
                                restaurant_id: restaurantId,
                                guests: TOTAL_GUESTS,
                                timeslot: formik.values.timestamp,
                              };

                              let customer_notes = formik.values.remarks;
                              if (ASK_COVID_QUESTION) {
                                customer_notes += `\r\ncov_question: agreed\r\ncov_households: ${formik.values.number_of_households}`;
                              }
                              const bookRequest: BookRequest = {
                                restaurant_id: restaurantId,
                                guests: TOTAL_GUESTS,
                                timeslot: formik.values.timestamp,
                                customer_lastname: formik.values.name,
                                customer_phone: formik.values.phone,
                                customer_email: formik.values.email,
                                customer_notes,
                                additional_data: {
                                  allergens_allergens: formik.values.allergy,
                                  allergens_diet: formik.values.diet,
                                  children_highchair: formik.values.kids_chairs,
                                  children_children: [
                                    { from: 0, to: 3, number: formik.values.kids_young },
                                    { from: 4, to: 12, number: formik.values.kids_older },
                                  ],
                                },
                              };

                              try {
                                const result = await makeBooking(blockRequest, bookRequest);
                                if (!result) {
                                  logBookingError('no block & bookresult');
                                  throw new Error(
                                    formatMessage({ id: 'app.forms.tablebooking.error.generic' })
                                  );
                                }
                                const blockResult = result?.blockResult;
                                const bookResult = result?.bookResult;
                                if (!blockResult || !bookResult) {
                                  logBookingError(!blockResult ? 'no blockresult' : 'no bookresult');
                                  throw new Error(
                                    formatMessage({ id: 'app.forms.tablebooking.error.generic' })
                                  );
                                }

                                switch (blockResult.status) {
                                  case 'success': {
                                    // Silently continue
                                    break;
                                  }
                                  case 'duplicate_reservation': {
                                    logBookingError(blockResult.status);
                                    throw new Error(
                                      formatMessage({ id: 'app.forms.tablebooking.error.full' })
                                    );
                                  }
                                }

                                switch (bookResult.status) {
                                  case 'OK': {
                                    logEvent('GenerateLead', {
                                      type: ANALYTICS_TYPE,
                                      reservation: ANALYTICS_RESERVATION,
                                    });
                                    navigation.replace('TableBookingSuccessful', {
                                      timestamp: formik.values.timestamp,
                                      timestring: formik.values.time,
                                      restaurantName: facility?.name || '',
                                      guests: TOTAL_GUESTS,
                                    });
                                    break;
                                  }
                                  case 'error': {
                                    if (!bookResult.error_code) {
                                      logBookingError('error without error code');
                                      throw new Error(
                                        formatMessage({ id: 'app.forms.tablebooking.error.generic' })
                                      );
                                    }
                                    switch (bookResult.error_code) {
                                      case 'invalid_field': {
                                        switch (bookResult.field) {
                                          case 'customer_phone': {
                                            logBookingError('incorrect phone');
                                            throw new Error(
                                              formatMessage({ id: 'app.forms.tablebooking.error.phone' })
                                            );
                                          }
                                          default: {
                                            logBookingError('invalid field (other than phone)');
                                            throw new Error(
                                              formatMessage({ id: 'app.forms.tablebooking.error.generic' })
                                            );
                                          }
                                        }
                                      }
                                      default: {
                                        logBookingError(`error code: ${bookResult.error_code}`);
                                        throw new Error(
                                          formatMessage({ id: 'app.forms.tablebooking.error.generic' })
                                        );
                                      }
                                    }
                                  }
                                  default: {
                                    logBookingError(`unexpected status: ${bookResult.status}`);
                                    throw new Error(
                                      formatMessage({ id: 'app.forms.tablebooking.error.generic' })
                                    );
                                  }
                                }

                                const message: NotificationMessage = {
                                  createdAt: new Date(),
                                  title:
                                    formatMessage({ id: 'app.messages.reservation' }) + ' ' + facility?.name,
                                  shortDetails: [
                                    {
                                      label: formatMessage({ id: 'app.general.date' }),
                                      value: getFormattedDate(new Date(formik.values.timestamp * 1000)),
                                    },
                                    {
                                      label: formatMessage({ id: 'app.general.time' }),
                                      value: formik.values.time,
                                    },
                                    {
                                      label: formatMessage({ id: 'app.forms.result.person_plural' }),
                                      value:
                                        TOTAL_GUESTS.toString() +
                                        ' ' +
                                        (TOTAL_GUESTS > 1
                                          ? formatMessage({ id: 'app.forms.result.person_plural' })
                                          : formatMessage({ id: 'app.forms.result.person' })),
                                    },
                                    {
                                      label: formatMessage({ id: 'app.forms.labels.amountofkidschairs' }),
                                      value: formik.values.kids_chairs || '-',
                                    },
                                  ],
                                  longDetails: [
                                    {
                                      label: formatMessage({ id: 'app.forms.labels.allergy' }),
                                      value: `${formik.values.allergy ? '\r\n' + formik.values.allergy : ''}${
                                        formik.values.diet ? '\r\n' + formik.values.diet : ''
                                      }`,
                                    },
                                    {
                                      label: formatMessage({ id: 'app.forms.labels.remarks' }),
                                      value: formik.values.remarks,
                                    },
                                  ],
                                  subnote:
                                    formatMessage({
                                      id: 'app.forms.tablebooking.notification.subnote',
                                    }) +
                                    ' ' +
                                    park?.phone,
                                  text: formatMessage({ id: 'app.forms.tablebooking.notification.text' }),
                                  image: facility?.media?.toString() || '',
                                  label: 'reservation',
                                };

                                setNotifications((prev) => [...prev, message]);
                              } catch (ex) {
                                if (ex instanceof Error) {
                                  Alert.alert('', ex.message);
                                } else {
                                  Alert.alert(
                                    '',
                                    formatMessage({ id: 'app.forms.tablebooking.error.generic' })
                                  );
                                }
                              }
                              setProcessing(false);
                            }}
                            style={{
                              marginVertical: theme.space.m,
                              backgroundColor: theme.colors.secondary,
                            }}
                            disabled={processing}
                          >
                            {processing ? (
                              <Button.ActivityIndicator />
                            ) : (
                              <Button.Text>
                                {formatMessage({ id: 'app.forms.action.makeareservation' })}
                              </Button.Text>
                            )}
                          </Button>
                        </>
                      )}
                    </>
                  )}
                </View>
              )}
            </View>
          )}
        </ContentContainer>
      </KeyboardAwareScrollView>
    </>
  );
};
export default TableBooking;
