import { LanguageCode } from './useCulture';
import useSelectedPark from './useSelectedPark';

export default function useCulturesOfSelectedPark() {
  const { park } = useSelectedPark();

  const parkCulture = park?.appEnabledLanguages?.map((culture) => ({
    cultureCode: culture,
    languageCode: culture?.split('-')[0],
  })) as { cultureCode: LanguageCode; languageCode: string }[];

  return parkCulture || [{ cultureCode: 'nl-NL', languageCode: 'nl' }];
}
