import { UserContext } from '@app/context/usercontext/UserContextProvider';
import { Profile } from 'apis/_generated-identity-api/models';
import { AxiosError } from 'axios';
import { gql, GraphQLClient } from 'graphql-request';
import { useContext } from 'react';
import Config from 'react-native-config';
import { UseQueryOptions, useQuery } from 'react-query';

const key = `getProfile`;
type Props = {
  options?: Omit<UseQueryOptions<Profile, AxiosError>, typeof key>;
};

const MOCK: Profile = { emailAddress: 'test@test.nl', firstName: 'Bollo', lastName: 'Tester' };

export function useGetProfile(props: Props) {
  const { token, demoMode } = useContext(UserContext);
  const { options } = props;

  // Belows ''undefined'' is a patch as we saw literal undefined strings triggering this query.
  // TODO: This should be investigated
  const extendedOptions: typeof options = demoMode
    ? {
        ...options,
        enabled: true,
        staleTime: 0,
        cacheTime: 0,
        initialData: MOCK,
        placeholderData: MOCK,
      }
    : {
        ...options,
        enabled: !!token,
      };

  return useQuery(
    [key, { token }],
    async () => {
      if (demoMode) return MOCK;

      const graphQLClient = new GraphQLClient(Config.IDENTITY_API_URL);

      const query = gql`
        query profile {
          profile(uuid: "${token}") {
            gender
            firstName
            middleName
            lastName
            emailAddress
            type
          }
        }
      `;
      const result = await graphQLClient.rawRequest<{ profile: Profile }>(query);
      return result?.data.profile;
    },
    extendedOptions
  );
}
