import React, { useState } from 'react';
import { View, ViewProps } from 'react-native';
import { Canvas, RoundedRect, Shadow as SkiaShadow } from '@shopify/react-native-skia';
import useTheme from '@app/hooks/useTheme';

const HEIGHT = 50;

interface Props extends ViewProps {
  topOffset?: number;
  position?: 'top' | 'bottom';
  radius?: number;
}
export default function Shadow({
  style,
  children,
  topOffset = 0,
  position = 'bottom',
  radius = 8,
}: React.PropsWithChildren<Props>) {
  const [width, setWidth] = useState(0);
  const [isMeasured, setIsMeasured] = useState(false);
  const theme = useTheme();

  const offset = topOffset;

  return (
    <View
      onLayout={(event) => {
        if (isMeasured) return;
        var { width: layoutWidth } = event.nativeEvent.layout;
        if (layoutWidth <= 0) return;

        setWidth(layoutWidth);
        setIsMeasured(true);
      }}
      pointerEvents="box-none"
    >
      {isMeasured && (
        <View
          style={{
            position: 'absolute',
            left: -10,
            right: -10,
            height: HEIGHT,
            bottom: position === 'bottom' ? -28 - offset : undefined,
            top: position === 'top' ? -HEIGHT - offset : undefined,
          }}
          pointerEvents="none"
        >
          <Canvas
            style={{
              height: HEIGHT,
            }}
            pointerEvents="none"
          >
            <RoundedRect
              x={36}
              y={position === 'bottom' ? 2 : HEIGHT + 22}
              width={(width ?? 30) - 50}
              height={30}
              r={radius}
              color={theme.colors.black}
            >
              <SkiaShadow dx={0} dy={-16} blur={12} color={theme.colors.shade} shadowOnly />
            </RoundedRect>
          </Canvas>
        </View>
      )}

      {children}
    </View>
  );
}
