import React from 'react';
import { useAnimatedScrollHandler, useSharedValue } from 'react-native-reanimated';
import { View } from 'react-native';
import useTheme from '@app/hooks/useTheme';
import { useGetPOI } from '../queries/useGetPoi';
import MoreInfo from '@app/components/DetailScreen/MoreInfo';
import TitleAndDescription from '@app/components/DetailScreen/TitleAndDescription';
import ActivityBadge from '@app/components/ActivityBadge';
import _ from 'lodash';
import { useFocusEffect } from '@react-navigation/native';
import useMetrics from '@app/metrics/useMetrics';
import { Activities, ActivityDate, Address } from 'apis/_generated-park-api/models';
import { NavScreenProp } from '@app/navigation/stacks/Main';
import Loader from '@app/components/Loader';
import StickyImageHeader, { useStickyImageHeaderDimensions } from '@app/components/StickyImageHeader';
import ScreenScrollContainer from '@app/components/ScreenScrollContainer';

// deze updaten naar intheregiondetail standaard

interface Event extends Activities {
  address?: Address;
  ticketsAvailable?: boolean;
  website?: string;
  mediaUrl?: string;
}

export default function InTheRegionEventDetail({ route }: NavScreenProp<'InTheRegionEventDetail'>) {
  const theme = useTheme();
  const { logScreenView } = useMetrics();
  const { data, isLoading } = useGetPOI({});
  const translationY = useSharedValue(0);
  const scrollHandler = useAnimatedScrollHandler((event) => {
    translationY.value = event.contentOffset.y;
  });

  const { EXPANDED_HEADER_HEIGHT, size } = useStickyImageHeaderDimensions('m');

  const id = route?.params?.id;

  useFocusEffect(
    React.useCallback(() => {
      if (id) {
        logScreenView('InTheRegionEventDetail', [id]);
      }
    }, [id, logScreenView])
  );

  if (!data || !id) {
    return null;
  }

  //get root activities and activities under poi locations.
  const rootActivity = _.flatMap(data, (poi) => poi?.activities).find(
    (act_in_poi) => act_in_poi?.id === route?.params?.id
  );
  const poi_act = data.find((act) => act.isEvent && act?.id === route?.params?.id);
  const event: Event = {
    name: rootActivity?.name ?? poi_act?.name,
    description: rootActivity?.description ?? poi_act?.description,
    dates: rootActivity?.dates,
    mediaUrl: rootActivity?.media?.[0] ?? poi_act?.media ?? '',
    ticketsAvailable: poi_act?.ticketsAvailable,
    website: poi_act?.website,
    address: poi_act?.address,
  };

  if (isLoading) {
    return (
      <View style={{ flex: 1, justifyContent: 'center', alignItems: 'center' }}>
        <Loader />
      </View>
    );
  }

  return (
    <>
      <StickyImageHeader
        scrollOffset={translationY}
        title={event?.name}
        images={[event?.mediaUrl ?? '']}
        size={size}
      />
      <ScreenScrollContainer
        onScroll={scrollHandler}
        contentContainerStyle={{ paddingTop: EXPANDED_HEADER_HEIGHT }}
        forceBottomInset={false}
        animated
      >
        {/* <FavouriteButton id={poi_act?.id} name={poi_act?.name} favouriteType="poiActivity" /> */}

        {event?.dates?.[0] && (
          <ActivityBadge
            compact={false}
            dateObject={event.dates ? (event.dates as ActivityDate[]) : []}
            style={{ marginBottom: theme.space.xl, marginTop: theme.space.m }}
          />
        )}

        <TitleAndDescription name={event?.name} description={event?.description}>
          {/* {event?.ticketsAvailable && <TicketsAvailable name={event?.name ?? ''} />} */}
          <MoreInfo
            locationName={event?.name}
            street={event?.address?.street ?? ''}
            streetNumber={event?.address?.streetNumber}
            city={event?.address?.city ?? ''}
            zipcode={event?.address?.zipcode ?? ''}
            website={event?.website}
          />
        </TitleAndDescription>
      </ScreenScrollContainer>
    </>
  );
}
