export type ScreenName = keyof typeof Screens;
export type EventName = keyof typeof Events;

export default function useMetrics() {
  const { eventsLoggedThisSession } = useContext(MetricsContext);
  const { setEventsLoggedThisSession } = useContext(MetricsContextUpdater);

  const logEvent = useCallback(
    (event: EventName, params?: { [key: string]: any }, onlyOnce?: boolean) => {
      if (eventsLoggedThisSession.includes(event)) {
        if (onlyOnce) {
          // suppress
          return;
        }
      } else {
        setEventsLoggedThisSession((prev) => [...prev, event]);
      }
    },
    [eventsLoggedThisSession, setEventsLoggedThisSession]
  );

  const logScreenView = useCallback(
    (screenName: ScreenName, params?: string[], extraData?: { [key: string]: string }) => {
      const screenNames = Screens[screenName];
      let screenNameEN: string = screenNames?.en;
      let screenNameNL: string = screenNames?.nl;

      params?.forEach((p) => {
        screenNameEN = screenNameEN.replace('xxx', p);
        screenNameNL = screenNameNL.replace('xxx', p);
      });
    },
    []
  );
  const setUserProperty = useCallback((property: string, value: string | null) => {}, []);
  const setAnalyticsCollectionEnabled = useCallback((enabled: boolean) => {}, []);
  return { logEvent, logScreenView, setUserProperty, setAnalyticsCollectionEnabled };
}
