import useCulture from '@app/hooks/useCulture';
import { ActivityMeta } from 'apis/_generated-park-api/models';
import { AxiosError } from 'axios';
import { gql, GraphQLClient } from 'graphql-request';
import Config from 'react-native-config';
import { UseQueryOptions, useQuery } from 'react-query';

const key = 'getactivitydetails';

type Props = {
  options?: Omit<UseQueryOptions<ActivityMeta, AxiosError>, typeof key>;
  reservationId?: string;
};

export function useGetActivityDetails(props: Props) {
  const { reservationId } = props;
  const { reserveLanguageCode } = useCulture();

  const extendedOptions: typeof props.options = {
    ...props.options,
    enabled: !!reservationId,
  };

  return useQuery(
    [key, { reservationId }],
    async () => {
      const graphQLClient = new GraphQLClient(Config.PARK_API_URL);
      const query = gql`
        query activityMeta {
          activityMeta(reservationId: "${reservationId}", cultureCode: "${reserveLanguageCode}") {
            eventId
            objectDesc
            eventDesc
            DateFrom
            DateTill
            location
            Booked
            Free
            participants
            deposit
            depositType
            depositLocation
            childMayReturnAlone
            disclaimerRequired
          }
        }
      `;
      const result = await graphQLClient.rawRequest<{ activityMeta: ActivityMeta }>(query);
      return result?.data?.activityMeta;
    },
    extendedOptions
  );
}
