import React, { useState, useEffect, createContext, ReactNode, useRef, useCallback, useContext } from 'react';
import sha1 from 'sha1';

interface ItemQueue {
  hash: string;
}

export const CACHE_DIR = '';
interface ContextType {
  getPathByUri: (uri?: string) => Promise<string | undefined>;
  clearCache: () => Promise<void>;
}

export const ImageCacheContext = createContext<ContextType>({
  getPathByUri: async () => '',
  clearCache: async () => {},
});

const ImageCacheProvider = ({ children }: { children: ReactNode }) => {
  const filesCached = useRef<{ [hash: string]: string }>({});

  const clearCache = useCallback(async (): Promise<void> => {}, []);

  const getCacheOrDownload = useCallback(async (uri: string, hash: string): Promise<void> => {}, []);

  const getPathByUri = useCallback(
    async (uri: string | undefined): Promise<string | undefined> => {
      if (!uri) {
        return;
      }
      const hash = sha1(uri);

      try {
        if (!filesCached.current[hash]) {
          await getCacheOrDownload(uri, hash);
        }
        return filesCached.current[hash];
      } catch (e) {
        return uri;
      }
    },
    [getCacheOrDownload]
  );

  return (
    <ImageCacheContext.Provider
      value={{
        getPathByUri,
        clearCache,
      }}
    >
      {children}
    </ImageCacheContext.Provider>
  );
};

export default ImageCacheProvider;

export const useExternalImageCache = (uri: string | undefined) => {
  const { getPathByUri } = useContext(ImageCacheContext);
  const [filePath, setFilePath] = useState('');

  useEffect(() => {
    (async () => {
      if (!uri) {
        return;
      }
      const path = await getPathByUri(uri);
      if (path) {
        setFilePath(path);
      }
    })();
  }, [getPathByUri, uri]);

  return filePath
    ? typeof filePath === 'string' && filePath?.charAt(0) === '/'
      ? 'file://' + filePath
      : filePath
    : null;
};
