import useCart from '@app/hooks/useCart';
import useCulture from '@app/hooks/useCulture';
import { Product } from 'apis/_generated-park-api/models';
import { AxiosError } from 'axios';
import { gql, GraphQLClient } from 'graphql-request';
import Config from 'react-native-config';
import { UseQueryOptions, useQuery } from 'react-query';

const key = `getproductsofcategory`;
type ProductDataProps = {
  options?: Omit<UseQueryOptions<Product[], AxiosError>, typeof key>;
  categoryId: number;
  offset?: number;
  limit?: number;
};

export function useGetProductsOfCategory({ options, categoryId, offset = 0, limit = 100 }: ProductDataProps) {
  const { cultureCode } = useCulture();
  const { activeShop: shopId } = useCart();

  const extendedOptions: typeof options = {
    ...options,
    enabled: shopId !== -1,
  };

  // TODO: fetch more until results < size (for when there are more than 100 products in one category)
  return useQuery(
    [key, { cultureCode, categoryId, shopId, offset, limit }],
    async () => {
      const graphQLClient = new GraphQLClient(Config.PARK_API_URL);
      const query = gql`
        query shopProductsOfCategory {
          shopProductsOfCategory(cultureCode: "${cultureCode}", shopId: ${shopId}, categoryId: ${categoryId}, offset: ${offset}, limit: ${limit}) {
            id
            name
            productNumber
            shortDescription
            description
            price
            unit
            mainCategory
            subCategory
            productMainPhoto
            alias
          }
        }
      `;
      const result = await graphQLClient.rawRequest<{ shopProductsOfCategory: Product[] }>(query);
      return result?.data?.shopProductsOfCategory;
    },
    extendedOptions
  );
}
