import React from 'react';
import useTheme from '@app/hooks/useTheme';
import { View } from 'react-native';
import { NavigationContainerRef, useNavigation } from '@react-navigation/native';
import { useGetProductsOfCategory } from '../queries/useGetProductsOfCategory';
import { useIntl } from 'react-intl';
import LandalImage from '@app/components/LandalImage';
import { AllRoutes } from '@app/navigation/stacks/Main';
import ListItems from '@app/components/ListItems2/ListItems';
import { BASELINE_INSET } from '@app/theme/partials/sizes';
import LinearGradient from 'react-native-linear-gradient';
import Text from '@app/components/Text';
import ScreenScrollContainer from '@app/components/ScreenScrollContainer';

interface Props {
  categoryId: number;
  photoUrl?: string;
  name?: string;
}

export default function ProductList({ categoryId, photoUrl, name }: Props) {
  const { formatNumber } = useIntl();
  const theme = useTheme();
  const { data } = useGetProductsOfCategory({ categoryId });
  const navigation = useNavigation<NavigationContainerRef<AllRoutes>>();

  if (!categoryId || !data) {
    return null;
  }

  return (
    <ScreenScrollContainer
      forceBottomInset
      topPadding={false}
      contentContainerStyle={{ paddingTop: theme.space.l }}
    >
      {!!photoUrl && (
        <View
          style={{
            height: 124,
            marginHorizontal: -BASELINE_INSET,
          }}
        >
          <LandalImage
            remoteSource={photoUrl}
            style={{
              flex: 1,
              borderTopLeftRadius: 8,
              borderTopRightRadius: 8,
            }}
            size={'l'}
            resizeMode="cover"
          />
          <LinearGradient
            colors={[
              'rgba(0, 0, 0,0.0)',
              'rgba(0, 0, 0,0.0)',
              'rgba(0, 0, 0,0.0)',
              'rgba(0, 0, 0,0.4)',
              'rgba(0, 0, 0,0.8)',
            ]}
            style={[
              {
                position: 'absolute',
                top: 0,
                left: 0,
                height: 124,
                width: '100%',
                backgroundColor: 'transparent', // required for gradient
              },
            ]}
            pointerEvents="none"
          />
          <Text
            type="title"
            style={{ position: 'absolute', bottom: theme.space.l, left: theme.space.l }}
            color="white"
            size="l"
            bold
          >
            {name}
          </Text>
        </View>
      )}

      <ListItems
        items={data?.map((i) => ({
          id: i.id?.toString ?? i.name,
          title: i.name,
          subtitle: i.shortDescription,
          rightTitle: formatNumber(i.price, {
            style: 'currency',
            currency: 'EUR',
            currencyDisplay: 'symbol',
          }),
          image: i.productMainPhoto,
          onPress: () => {
            !!i.id && navigation.navigate('ProductOptions', { id: i.id, categoryId });
          },
        }))}
        style={{ marginTop: 0 }}
        useTopRadius={!photoUrl}
        usePlaceholderImage
        subtitleNumberOfLines={1}
      />
      {/* <FlatList
        contentContainerStyle={{
          paddingTop: photoUrl ? 8 : 0,
          paddingBottom: insets.bottom + theme.space.xl * 4 + FOOTER_TEXT_PADDING,
        }}
        data={data}
        renderItem={({ item }) => (
          <ListItemProduct
            item={item}
            onPress={() => {
              !!item.id && navigation.navigate('ProductOptions', { id: item.id, categoryId });
            }}
          />
        )}
        ListFooterComponent={
          <View style={{ padding: theme.space.m }}>
            <Text size="s">{formatMessage({ id: 'app.shop.order.allergyordiet' })}</Text>
          </View>
        }
        keyExtractor={(item) => item.id?.toString() ?? ''}
      /> */}
    </ScreenScrollContainer>
  );
}
