import React from 'react';
import { View, ViewProps } from 'react-native';
import useTheme from '@hooks/useTheme';
import Text from '@app/components/Text';
import { BASELINE_INSET } from '@app/theme/partials/sizes';
import Shadow from '@app/components/Shadow';
import ItemDivider from './ListItems2/ItemDivider';

export interface TableItem {
  label: string;
  value?: string;
  onValuePress?: () => void;
}

interface Props extends React.PropsWithChildren<ViewProps> {
  title?: string;
  items: TableItem[];
  boldLabel?: boolean;
  showChildrenDivider?: boolean;
}
export default function Table({
  title,
  items,
  boldLabel = true,
  showChildrenDivider,
  children,
  style,
}: Props) {
  const theme = useTheme();

  return (
    <View style={[{ marginVertical: theme.space.l }, style]}>
      <Text type="title" size="l" style={{ marginBottom: theme.space.m }} bold>
        {title}
      </Text>

      <Shadow>
        <View
          style={{
            borderRadius: 8,
            borderBottomLeftRadius: children ? 0 : 8,
            borderBottomRightRadius: children ? 0 : 8,
            backgroundColor: theme.colors.white,
            padding: theme.space.l,
            paddingBottom: theme.space.s,
            marginHorizontal: -BASELINE_INSET,
          }}
        >
          {items.map((i) => (
            <View
              style={{ flexDirection: 'row', justifyContent: 'space-between', marginBottom: theme.space.m }}
              key={i.label}
            >
              <Text style={{ lineHeight: 26, flex: 1 }} bold={boldLabel}>
                {i.label}
              </Text>
              <Text
                style={{ lineHeight: 26 }}
                color={i.onValuePress ? 'tertiary' : 'dark'}
                onPress={i.onValuePress}
              >
                {i.value}
              </Text>
            </View>
          ))}
        </View>

        {!!children && showChildrenDivider && <ItemDivider />}
        {!!children && (
          <View
            style={{
              borderBottomLeftRadius: 8,
              borderBottomRightRadius: 8,
              backgroundColor: theme.colors.white,
              padding: theme.space.l,
              paddingBottom: theme.space.s,
              marginHorizontal: -BASELINE_INSET,
            }}
          >
            {children}
          </View>
        )}
      </Shadow>
    </View>
  );
}
