import { PARK_KEY } from '@app/config/StorageKeys';
import useMetrics from '@app/metrics/useMetrics';
import AsyncStorage from '@react-native-async-storage/async-storage';
import React, { useState } from 'react';
import DeviceInfo from 'react-native-device-info';

const bundleId = DeviceInfo.getBundleId();
const defaultPark = bundleId.includes('hofvansaksen') ? 'HSN' : '';

export const ParkContextUpdater = React.createContext<(parkCode: string) => void>((_: string) => {});
export const ParkContext = React.createContext<string>(defaultPark);

export function ParkProvider({ children }: { children: React.ReactNode }) {
  const [parkCode, setPark] = useState<string>(defaultPark);
  const { setUserProperty } = useMetrics();

  React.useEffect(() => {
    if (parkCode) {
      AsyncStorage.setItem(PARK_KEY, parkCode);
      setUserProperty('park', parkCode);
    }
  }, [parkCode, setUserProperty]);

  return (
    <ParkContext.Provider value={parkCode}>
      <ParkContextUpdater.Provider value={setPark}>{children}</ParkContextUpdater.Provider>
    </ParkContext.Provider>
  );
}
