import useTheme from '@app/hooks/useTheme';
import React, { useState, useCallback, useMemo } from 'react';
import { View, StyleProp, StyleSheet, ViewStyle, FlatList } from 'react-native';
import LinearGradient from 'react-native-linear-gradient';
import Animated, { FadeIn } from 'react-native-reanimated';
import { useExternalImageCache } from 'utils/image-cache/useExternalImageCache';
import { getImageUrlWithWidth } from 'utils/imageUrl';
import LandalImage from './LandalImage';
import PageIndicator from './PageIndicator';

interface Props {
  style?: StyleProp<ViewStyle>;
}

export const GalleryItem: React.FC<Props> = ({ style }) => {
  var randomColor = useMemo(() => Math.floor(Math.random() * 16777215).toString(16), []);

  return <View style={[styles.container, { backgroundColor: '#' + randomColor }, style]} />;
};

const styles = StyleSheet.create({
  container: {
    flex: 1,
    justifyContent: 'center',
    alignItems: 'center',
  },
});

const Page = ({ image, width = 200 }: { image?: string; width?: number }) => {
  const remoteImageLarge = image ? getImageUrlWithWidth(image, 'xl') : undefined;
  const cachedImage = useExternalImageCache(remoteImageLarge);
  return cachedImage ? (
    <LandalImage remoteSource={cachedImage} style={{ width, height: '100%' }} animateOnLoad />
  ) : (
    <View />
  );
};

const Pager = ({
  height = 400,
  indicatorStyle,
  style,
  onChange,
  images,
}: {
  height: number | string;
  style?: object;
  indicatorStyle?: {
    opacity: number;
  };
  onChange: Function;
  images: string[];
}) => {
  const ref = React.useRef<FlatList>(null);
  const [width, setWidth] = useState(0);
  const [active, setActive] = useState(0);
  const theme = useTheme();

  const detectActiveSlide = useCallback(
    (xOffset: number) => {
      const index = Math.round(xOffset / width);
      setActive(index);
      onChange?.(index);
    },
    [onChange, width]
  );

  const length = images?.length;

  if (!images || length === 0) {
    return null;
  }

  const go = (index: number) => ref.current?.scrollToIndex({ index });

  return (
    <Animated.View style={[style, { height, width: '100%' }]} entering={FadeIn}>
      <FlatList
        ref={ref}
        onLayout={(event) => setWidth(event.nativeEvent.layout.width)}
        horizontal
        pagingEnabled
        data={images}
        renderItem={({ item }) => <Page key={item} image={item} width={width} />}
        showsHorizontalScrollIndicator={false}
        onScroll={(event) => detectActiveSlide(event.nativeEvent.contentOffset.x)}
        style={{ height, backgroundColor: theme.colors.black }}
        removeClippedSubviews={false}
      />
      <LinearGradient
        colors={['rgba(0, 0, 0,0.0)', 'rgba(0, 0, 0,0.0)', 'rgba(0, 0, 0,0.0)', 'rgba(0, 0, 0,0.4)']}
        style={[
          {
            position: 'absolute',
            top: 0,
            height,
            width: '100%',
            backgroundColor: 'transparent', // required for gradient
          },
        ]}
        pointerEvents="none"
      />
      <PageIndicator
        length={length}
        loop
        selected={active}
        setSelected={go}
        style={indicatorStyle}
        color="white"
      />
    </Animated.View>
  );
};

export default Pager;
