import useCart from '@app/hooks/useCart';
import useCulture from '@app/hooks/useCulture';
import { ProductAttribute } from 'apis/_generated-park-api/models';
import { AxiosError } from 'axios';
import { gql, GraphQLClient } from 'graphql-request';
import Config from 'react-native-config';
import { UseQueryOptions, useQuery } from 'react-query';

const key = `getproductoptions`;
type ProductDataProps = {
  options?: Omit<UseQueryOptions<ProductAttribute[], AxiosError>, typeof key>;
  productId: number;
};

export function useGetProductOptions({ options, productId }: ProductDataProps) {
  const { cultureCode } = useCulture();
  const { activeShop: shopId } = useCart();

  return useQuery(
    [key, { cultureCode, shopId, productId }],
    async () => {
      const graphQLClient = new GraphQLClient(Config.PARK_API_URL);
      const query = gql`
          query shopProductOptions {
            shopProductOptions(cultureCode: "${cultureCode}", shopId: ${shopId}, productId: ${productId}) {
              id
              attributename
              attributeposition
              type
              items{
                href
                id
                optionsname
                price
                enable
                position
              }
            }
          }
        `;
      const result = await graphQLClient.rawRequest<{ shopProductOptions: ProductAttribute[] }>(query);
      return result?.data?.shopProductOptions;
    },
    options
  );
}
