import { IntlFormatters } from 'react-intl';
import { Source } from 'react-native-fast-image';

interface Images {
  background?: string | Source;
  icon?: string;
}
interface SubCatData {
  imageUrl?: string;
  imageLocal: Source;
  name?: string;
  sub?: string;
}
export interface Promo {
  mainCategory?: string;
  subCategory?: string;
  url?: string;
  name?: string;
  images: Images;
  imageUrl?: string;
  shopData?:
    | {
        shopId?: number;
        facilityId?: string;
        publicKey?: string;
        secretKey?: string;
      }
    | undefined;
}
export interface PromoGroup {
  elements: Promo[];
  mainCategory: string;
  images: Images;
  remoteImage: boolean;
  name?: string;
  sub?: string;
}

export const getDataBySubCat = (
  subCat: string,
  formatMessage: IntlFormatters['formatMessage'],
  imageUrl?: string
): SubCatData => {
  return {
    imageUrl,
    imageLocal: null,
    name: formatMessage({ id: 'app.promo.meals' }),
  };
};
