export type Maybe<T> = T | undefined;
export type InputMaybe<T> = T | undefined;
export type Exact<T extends { [key: string]: unknown }> = { [K in keyof T]: T[K] };
export type MakeOptional<T, K extends keyof T> = Omit<T, K> & { [SubKey in K]?: Maybe<T[SubKey]> };
export type MakeMaybe<T, K extends keyof T> = Omit<T, K> & { [SubKey in K]: Maybe<T[SubKey]> };
/** All built-in and custom scalars, mapped to their actual values */
export type Scalars = {
  ID: string;
  String: string;
  Boolean: boolean;
  Int: number;
  Float: number;
};

export type Address = {
  city?: Maybe<Scalars['String']>;
  country?: Maybe<Country>;
  houseNumber?: Maybe<Scalars['Int']>;
  houseNumberExtension?: Maybe<Scalars['String']>;
  postalCode?: Maybe<Scalars['String']>;
  streetName?: Maybe<Scalars['String']>;
};

export type ArrivalTime = {
  prefix?: Maybe<ArrivalTimePrefix>;
  time?: Maybe<Scalars['Int']>;
  timeTill?: Maybe<Scalars['Int']>;
};

export enum ArrivalTimePrefix {
  After = 'AFTER',
  Before = 'BEFORE',
  Nextday = 'NEXTDAY',
  None = 'NONE'
}

export type AttachReservationRequest = {
  bookingReference?: InputMaybe<Scalars['String']>;
  uid?: InputMaybe<Scalars['String']>;
};

export type AttachReservationResponse = {
  errorMessage?: Maybe<Scalars['String']>;
  priceHasChanged?: Maybe<Scalars['Boolean']>;
  statusCode?: Maybe<Scalars['String']>;
  success?: Maybe<Scalars['Boolean']>;
};

export type BaseReservation = {
  accommodationCode?: Maybe<Scalars['String']>;
  arrivalDate?: Maybe<Scalars['Int']>;
  bookingReference?: Maybe<Scalars['String']>;
  combiReference?: Maybe<Scalars['String']>;
  departureDate?: Maybe<Scalars['Int']>;
  parkCode?: Maybe<Scalars['String']>;
  unitCode?: Maybe<Scalars['String']>;
};

/** Booking status */
export enum BookingStatusType {
  Arrived = 'ARRIVED',
  Booked = 'BOOKED',
  Cancelled = 'CANCELLED',
  Departed = 'DEPARTED',
  Provisional = 'PROVISIONAL'
}

/** LGP or HSN */
export enum Brand {
  Hsn = 'HSN',
  Lgp = 'LGP'
}

export enum CheckinTimePrefix {
  After = 'AFTER',
  Before = 'BEFORE',
  Nextday = 'NEXTDAY',
  None = 'NONE'
}

export type CheckinTimeType = {
  prefix?: Maybe<CheckinTimePrefix>;
  selected?: Maybe<Scalars['Boolean']>;
  text?: Maybe<Scalars['String']>;
  time?: Maybe<Scalars['Int']>;
  timeTill?: Maybe<Scalars['Int']>;
};

export type Checklist = {
  needsArrivalTime?: Maybe<Scalars['Boolean']>;
  needsCompanyRegistration?: Maybe<Scalars['Boolean']>;
  needsToPay?: Maybe<Scalars['Boolean']>;
};

export type CheckoutNowRequest = {
  bookingReference?: InputMaybe<Scalars['String']>;
  uid?: InputMaybe<Scalars['String']>;
};

export type CheckoutNowResponse = {
  message?: Maybe<Scalars['String']>;
  success?: Maybe<Scalars['Boolean']>;
};

export type Country = {
  code?: Maybe<Scalars['String']>;
  name?: Maybe<Scalars['String']>;
};

export type Extra = {
  amount?: Maybe<Scalars['Int']>;
  automaticallyCreated?: Maybe<Scalars['Boolean']>;
  extraCode?: Maybe<Scalars['String']>;
  extraDescription?: Maybe<Scalars['String']>;
  extraName?: Maybe<Scalars['String']>;
  image?: Maybe<Scalars['String']>;
  issuedVouchers?: Maybe<Array<Maybe<Voucher>>>;
  key?: Maybe<Scalars['String']>;
  nights?: Maybe<Scalars['Int']>;
  people?: Maybe<Scalars['Int']>;
  price?: Maybe<Scalars['Float']>;
  redeemFacilities?: Maybe<Array<Maybe<Scalars['String']>>>;
  voucherDescription?: Maybe<Scalars['String']>;
  voucherIdentifier?: Maybe<Scalars['String']>;
};

export type FeaturesEnabled = {
  checkoutNow?: Maybe<Scalars['Boolean']>;
  extras?: Maybe<Scalars['Boolean']>;
};

export type InputAddress = {
  city?: InputMaybe<Scalars['String']>;
  country?: InputMaybe<Scalars['String']>;
  houseNumber?: InputMaybe<Scalars['Int']>;
  houseNumberExtension?: InputMaybe<Scalars['String']>;
  postalCode?: InputMaybe<Scalars['String']>;
  streetName?: InputMaybe<Scalars['String']>;
};

export type InputCompanionAddress = {
  city?: InputMaybe<Scalars['String']>;
  countryCode?: InputMaybe<Scalars['String']>;
  houseNumber?: InputMaybe<Scalars['Int']>;
  houseNumberExtension?: InputMaybe<Scalars['String']>;
  postalCode?: InputMaybe<Scalars['String']>;
  street?: InputMaybe<Scalars['String']>;
};

export type InputCompanionGuest = {
  address?: InputMaybe<InputCompanionAddress>;
  birthDate?: InputMaybe<Scalars['Int']>;
  emailAddress?: InputMaybe<Scalars['String']>;
  firstName?: InputMaybe<Scalars['String']>;
  gender?: InputMaybe<Scalars['Int']>;
  lastName?: InputMaybe<Scalars['String']>;
  middleName?: InputMaybe<Scalars['String']>;
  phone?: InputMaybe<Scalars['String']>;
  sequenceNumber?: InputMaybe<Scalars['Int']>;
};

export type InputReservation = {
  bookingReference?: InputMaybe<Scalars['String']>;
  iceName?: InputMaybe<Scalars['String']>;
  icePhoneNumber?: InputMaybe<Scalars['String']>;
  idType?: InputMaybe<Scalars['String']>;
  travelCompanions?: InputMaybe<Array<InputMaybe<InputTravelCompanion>>>;
};

export type InputTravelCompanion = {
  address?: InputMaybe<InputAddress>;
  birthdate?: InputMaybe<Scalars['String']>;
  email?: InputMaybe<Scalars['String']>;
  firstName?: InputMaybe<Scalars['String']>;
  gender?: InputMaybe<Scalars['Int']>;
  lastName?: InputMaybe<Scalars['String']>;
  middleName?: InputMaybe<Scalars['String']>;
  phone?: InputMaybe<Scalars['String']>;
  phone2?: InputMaybe<Scalars['String']>;
};

export type Mutation = {
  attachReservation?: Maybe<AttachReservationResponse>;
  checkoutNow?: Maybe<CheckoutNowResponse>;
  updateArrivalTime?: Maybe<UpdateArrivalTimeResponse>;
  updateDepartureTime?: Maybe<UpdateDepartureTimeResponse>;
  updateMainTenant?: Maybe<UpdateMainTenantResponse>;
  updateTravelCompanion?: Maybe<UpdateTravelCompanionResponse>;
  updateTravelCompanions?: Maybe<UpdateTravelCompanionsResponse>;
};


export type MutationAttachReservationArgs = {
  attachReservationRequest?: InputMaybe<AttachReservationRequest>;
};


export type MutationCheckoutNowArgs = {
  checkoutNowRequest?: InputMaybe<CheckoutNowRequest>;
};


export type MutationUpdateArrivalTimeArgs = {
  updateArrivalTimeRequest?: InputMaybe<UpdateArrivalTimeRequest>;
};


export type MutationUpdateDepartureTimeArgs = {
  updateDepartureTimeRequest?: InputMaybe<UpdateDepartureTimeRequest>;
};


export type MutationUpdateMainTenantArgs = {
  updateMainTenantRequest?: InputMaybe<UpdateMainTenantRequest>;
};


export type MutationUpdateTravelCompanionArgs = {
  updateTravelCompanionRequest?: InputMaybe<UpdateTravelCompanionRequest>;
};


export type MutationUpdateTravelCompanionsArgs = {
  updateTravelCompanionsRequest?: InputMaybe<UpdateTravelCompanionsRequest>;
};

export type Product = {
  description?: Maybe<Scalars['String']>;
  extras?: Maybe<Array<Maybe<Extra>>>;
  id?: Maybe<Scalars['String']>;
  image?: Maybe<Scalars['String']>;
  key?: Maybe<Scalars['String']>;
  name?: Maybe<Scalars['String']>;
};

export type Query = {
  extras?: Maybe<Array<Maybe<Product>>>;
  nextReservation?: Maybe<Reservation>;
  reservation?: Maybe<Reservation>;
  reservations?: Maybe<Array<Maybe<BaseReservation>>>;
};


export type QueryExtrasArgs = {
  cultureCode?: InputMaybe<Scalars['String']>;
  reservationId?: InputMaybe<Scalars['String']>;
  uid?: InputMaybe<Scalars['String']>;
};


export type QueryNextReservationArgs = {
  brand?: InputMaybe<Brand>;
  languageCode?: InputMaybe<Scalars['String']>;
  uuid?: InputMaybe<Scalars['String']>;
};


export type QueryReservationArgs = {
  bookingReference?: InputMaybe<Scalars['String']>;
  brand?: InputMaybe<Brand>;
  languageCode?: InputMaybe<Scalars['String']>;
  uuid?: InputMaybe<Scalars['String']>;
};


export type QueryReservationsArgs = {
  brand?: InputMaybe<Brand>;
  languageCode?: InputMaybe<Scalars['String']>;
  limit?: InputMaybe<Scalars['Int']>;
  requestArrivalTime?: InputMaybe<Scalars['Boolean']>;
  uuid?: InputMaybe<Scalars['String']>;
};

export type Reservation = {
  accommodationCode?: Maybe<Scalars['String']>;
  accommodationLeaveDateTime?: Maybe<Scalars['Int']>;
  accommodationReadyDateTime?: Maybe<Scalars['Int']>;
  accommodationShouldLeaveDateTime?: Maybe<Scalars['Int']>;
  arrivalDate?: Maybe<Scalars['Int']>;
  arrivalTime?: Maybe<ArrivalTime>;
  arrivalTimeUrl?: Maybe<Scalars['String']>;
  bookingFee?: Maybe<Scalars['Float']>;
  bookingReference?: Maybe<Scalars['String']>;
  checkinTimes?: Maybe<Array<Maybe<CheckinTimeType>>>;
  checklist?: Maybe<Checklist>;
  combiReference?: Maybe<Scalars['String']>;
  currency?: Maybe<Scalars['String']>;
  departureDate?: Maybe<Scalars['Int']>;
  digitalCleaningTasks?: Maybe<Array<Maybe<Scalars['String']>>>;
  expectedDepartureDate?: Maybe<Scalars['Int']>;
  featuresEnabled?: Maybe<FeaturesEnabled>;
  holidayStarted?: Maybe<Scalars['Boolean']>;
  iceName?: Maybe<Scalars['String']>;
  icePhoneNumber?: Maybe<Scalars['String']>;
  idType?: Maybe<Scalars['String']>;
  isOwner?: Maybe<Scalars['Boolean']>;
  lockType?: Maybe<Scalars['String']>;
  mainTenant?: Maybe<TravelCompanion>;
  needsCompanyRegistration?: Maybe<Scalars['Boolean']>;
  numberOfNights?: Maybe<Scalars['Int']>;
  parkCode?: Maybe<Scalars['String']>;
  pendingPayment?: Maybe<Scalars['Float']>;
  pendingPaymentUrl?: Maybe<Scalars['String']>;
  seamlessEnabled?: Maybe<Scalars['Boolean']>;
  smartlockAvailability?: Maybe<SmartlockAvailability>;
  smartlockAvailable?: Maybe<Scalars['Boolean']>;
  status?: Maybe<BookingStatusType>;
  totalGuests?: Maybe<Scalars['Int']>;
  travelCompanions?: Maybe<Array<Maybe<TravelCompanion>>>;
  unitCode?: Maybe<Scalars['String']>;
  unitCodeIsPreference?: Maybe<Scalars['Boolean']>;
  unitTypeIndicator?: Maybe<UnitTypeIndicator>;
};

export enum SmartlockAvailability {
  Available = 'available',
  AvailableButAfterDeparture = 'availableButAfterDeparture',
  AvailableButBeforeArrival = 'availableButBeforeArrival',
  AvailableButPendingTasks = 'availableButPendingTasks',
  None = 'none'
}

export type TravelCompanion = {
  address?: Maybe<Address>;
  ageCategory?: Maybe<TravelCompanionAgeCategory>;
  arrivalDate?: Maybe<Scalars['String']>;
  birthdate?: Maybe<Scalars['String']>;
  departureDate?: Maybe<Scalars['String']>;
  email?: Maybe<Scalars['String']>;
  firstName?: Maybe<Scalars['String']>;
  gender?: Maybe<Scalars['Int']>;
  isComplete?: Maybe<Scalars['Boolean']>;
  lastName?: Maybe<Scalars['String']>;
  maxAge?: Maybe<Scalars['Int']>;
  middleName?: Maybe<Scalars['String']>;
  minAge?: Maybe<Scalars['Int']>;
  phone?: Maybe<Scalars['String']>;
  phone2?: Maybe<Scalars['String']>;
  sequenceNumber?: Maybe<Scalars['Int']>;
};

export enum TravelCompanionAgeCategory {
  Adult = 'ADULT',
  Baby = 'BABY',
  Child = 'CHILD',
  Pet = 'PET'
}

/** CAMPING or BUNGALOW */
export enum UnitTypeIndicator {
  Bungalow = 'BUNGALOW',
  Camping = 'CAMPING'
}

