import Button from '@app/components/Button';
import LandalImage from '@app/components/LandalImage';
import Text from '@app/components/Text';
import useTheme from '@app/hooks/useTheme';
import { NavScreenProp } from '@app/navigation/stacks/Main';
import React from 'react';
import { useIntl } from 'react-intl';
import { View } from 'react-native';

const CheckoutNowConfirmation = ({ navigation, route }: NavScreenProp<'CheckoutNowConfirmation'>) => {
  const theme = useTheme();
  const { formatMessage } = useIntl();
  const parkName = route?.params?.parkName;

  return (
    <View style={{ flex: 1, backgroundColor: theme.colors.white }}>
      <View style={{ flex: 1 }}>
        <LandalImage
          localSource={
            theme.type === 'hofvansaksen'
              ? require('@app/assets/images/hsn/hsn-feedback.png')
              : require('@app/assets/images/bollo-feedback.png')
          }
          style={{ flex: 1 }}
          resizeMode="cover"
        />
      </View>
      <View style={{ margin: theme.space.xxl }}>
        <Text type="title" size="xxl" style={{ marginBottom: theme.space.m }} useParser>
          {formatMessage({ id: 'app.reservation.checkoutNow.confirmationTitle' })}
        </Text>
        <Text color="dark" style={{ lineHeight: 26, marginBottom: theme.space.xxl }}>
          {formatMessage({ id: 'app.reservation.checkoutNow.confirmationText' }).replace(
            '{0}',
            parkName.replace('Landal ', '') ?? ''
          )}
        </Text>
        <Button
          type="primary"
          onPress={() => {
            navigation.goBack();
          }}
          style={{
            marginBottom: theme.space.l,
          }}
        >
          <Button.Text>{formatMessage({ id: 'app.general.close' })}</Button.Text>
        </Button>
      </View>
    </View>
  );
};

export default CheckoutNowConfirmation;
