import React from 'react';
import { View, ViewProps, ViewStyle } from 'react-native';
import useTheme from '@hooks/useTheme';
import Text from '../Text';
import Icon from '../Icon';
import { LandalPressable } from '../LandalPressable';

interface Props {
  value: number;
  minimum?: number;
  maximum?: number;
  onChange: (value: number) => void;
  title?: string;
}

interface Props extends React.PropsWithChildren<ViewProps> {}
export default function NumberControl({ value, minimum, maximum, onChange, title }: Props) {
  const theme = useTheme();
  const MINIMUM = minimum ?? 0;
  const MAXIMUM = maximum ?? 8;

  const updateAmount = (increase: boolean) => {
    if (increase && value < MAXIMUM) {
      onChange(value + 1);
    }
    if (!increase && value > MINIMUM) {
      onChange(value - 1);
    }
  };

  const shadow: ViewStyle = {
    elevation: 5,
    shadowRadius: 2,
    shadowOpacity: 0.05,
    shadowColor: 'black',
    shadowOffset: {
      width: 0,
      height: 4,
    },
  };

  return (
    <View
      style={{
        alignItems: 'center',
      }}
    >
      {!!title && (
        <Text bold style={{ marginBottom: theme.space.m }}>
          {title}
        </Text>
      )}
      <View
        style={{
          flexDirection: 'row',
          alignItems: 'center',
        }}
      >
        <LandalPressable
          style={[
            {
              width: 40,
              height: 40,
              borderRadius: 20,
              backgroundColor: theme.colors.white,
              alignItems: 'center',
              justifyContent: 'center',
            },
            shadow,
          ]}
          onPress={() => {
            updateAmount(false);
          }}
          disabled={value <= MINIMUM}
        >
          <Icon
            name="min"
            size="s"
            color={'black'}
            style={{ opacity: value <= MINIMUM ? 0.5 : 1, padding: 4 }}
          />
        </LandalPressable>
        <Text bold color={'black'} size="xl" style={{ marginHorizontal: theme.space.xxl }}>
          {value}
        </Text>
        <LandalPressable
          style={[
            {
              width: 40,
              height: 40,
              borderRadius: 20,
              backgroundColor: theme.colors.white,
              alignItems: 'center',
              justifyContent: 'center',
            },
            shadow,
          ]}
          onPress={() => {
            updateAmount(true);
          }}
          disabled={value >= MAXIMUM}
        >
          <Icon
            name="plus"
            size="s"
            color={'black'}
            style={{ opacity: value >= MAXIMUM ? 0.5 : 1, padding: 4 }}
          />
        </LandalPressable>
      </View>
    </View>
  );
}
