import React, { useCallback, useState } from 'react';
import landal from '@app/theme/landal';
import hofvansaksen from '@app/theme/hofvansaksen';
import DeviceInfo from 'react-native-device-info';
import { Theme } from '@app/theme/partials/general';

export const ThemeContextUpdater = React.createContext<(theme: Theme) => void>((_: Theme) => {});
export const ThemeContext = React.createContext<Theme>(landal);

export function ThemeProvider({ children }: { children: React.ReactNode }) {
  const getThemeFromConfig = (): Theme => {
    const bundleId = DeviceInfo.getBundleId();
    if (bundleId.includes('hofvansaksen')) {
      return hofvansaksen;
    }
    return landal;
  };
  const [theme, setTheme] = useState<Theme>(getThemeFromConfig());

  const changeTheme = useCallback((updatedTheme: Theme) => {
    setTheme(updatedTheme);
  }, []);

  return (
    <ThemeContext.Provider value={theme}>
      <ThemeContextUpdater.Provider value={changeTheme}>{children}</ThemeContextUpdater.Provider>
    </ThemeContext.Provider>
  );
}
