import React, { forwardRef, useImperativeHandle, useState, Ref, useContext, useEffect } from 'react';
import { Linking, TouchableOpacity, View } from 'react-native';
import useTheme from '@app/hooks/useTheme';
import { useFormik } from 'formik';
import Button from '@app/components/Button';
import { UserContext } from '@app/context/usercontext/UserContextProvider';
import { useIntl } from 'react-intl';
import useMetrics, { Screens } from '@app/metrics/useMetrics';
import { useAttachReservation } from '../queries/useAttachReservation';
import { ClientError } from 'graphql-request';
import { AttachReservationRefType } from '../types/AttachReservationRefType';
import FormControl from '@app/components/Form/FormControl';
import { useBottomSheet } from '@app/navigation/bottom-sheet/BottomSheetProvider';
import Text from '@app/components/Text';
import Icon from '@app/components/Icon';
import { getBrand } from 'utils/brand';
import queryString from 'query-string';
import { useGetReservations } from '../queries/useGetReservations';

type FormFields = 'bookingReference' | 'generalError';

interface Props {
  onValidate?: (isValid: boolean) => void;
  setProcessing?: (processing: boolean) => void;
}

type FormikErrors = {
  bookingReference?: string;
  generalError?: string;
};

// current numbers are 9 characters, but we stay flexible for future
const MAX_LENGTH = 11;

const AttachReservation = forwardRef(
  ({ onValidate, setProcessing: setExternalProcessing }: Props, ref: Ref<AttachReservationRefType>) => {
    const theme = useTheme();
    const { formatMessage } = useIntl();
    const { logEvent } = useMetrics();
    const [processing, setProcessing] = useState(false);
    const [attachReservationErrorMessage, setAttachReservationErrorMessage] = useState<string>();
    const { closeBottomSheet } = useBottomSheet();

    const { token } = useContext(UserContext);
    const { refetch } = useGetReservations({
      brand: getBrand(theme),
      token,
    });

    useEffect(() => {
      logEvent('AttachReservationBegin');
    }, []);

    let submitted = false;
    const formik = useFormik<Record<FormFields, any>>({
      initialValues: {
        bookingReference: '',
        generalError: '',
      },
      validateOnMount: true,
      isInitialValid: false,
      onSubmit: () => {
        submitted = true;
      },
      validate: (validatedValues) => {
        const errors: FormikErrors = {};

        const isValid: boolean =
          !processing &&
          validatedValues.bookingReference &&
          validatedValues.bookingReference.length >= 8 &&
          validatedValues.bookingReference.length <= MAX_LENGTH;

        onValidate?.(isValid);

        if (!submitted) {
          if (!isValid) {
            errors.generalError = 'error before submit';
          }
          return errors;
        }

        if (!validatedValues.bookingReference) {
          errors.bookingReference = 'no booking reference';
        }
        if (validatedValues.bookingReference.length < 8) {
          errors.bookingReference = 'booking reference too short';
        }

        return errors;
      },
    });

    const { attachReservation } = useAttachReservation();

    useImperativeHandle(ref, () => ({
      externalPerformAttachReservation() {
        performAttachReservation();
      },
    }));

    const performAttachReservation = async () => {
      if (processing) {
        return;
      }
      if (!formik.values.bookingReference) {
        return;
      }
      if (!token) {
        return;
      }

      setProcessing(true);
      setExternalProcessing?.(true);

      setAttachReservationErrorMessage(undefined);
      try {
        const result = await attachReservation({
          uid: token,
          bookingReference: formik.values.bookingReference,
        });
        if (result.success) {
          logEvent('AttachReservationComplete');
          refetch();
          // TODO: this should not be in timeout, but otherwise we sometimes see the bottom sheet not closing
          setTimeout(() => {
            closeBottomSheet();
          }, 50);
        } else {
          setAttachReservationErrorMessage('app.login.serverError');
        }
      } catch (ex) {
        let { response } = ex as ClientError;
        const message = response?.errors?.[0]?.message;
        switch (message) {
          case 'Q400': // queued reservation
            setAttachReservationErrorMessage('app.attachreservation.error.processing');
            logEvent('AttachReservationError', { reason: 'queued_reservation' });
            break;
          case 'W400': // offline reservation
            setAttachReservationErrorMessage('app.attachreservation.error.processing');
            logEvent('AttachReservationError', { reason: 'offline_reservation' });
            break;
          case 'M400': // combi reservation
            setAttachReservationErrorMessage('app.attachreservation.error.notSupported');
            logEvent('AttachReservationError', { reason: 'combi_reservation' });
            break;
          case '401': // profile not found
            setAttachReservationErrorMessage('app.login.serverError');
            logEvent('AttachReservationError', { reason: 'profile_not_found' });
            break;
          case '403': // reservation connected to another email address
            setAttachReservationErrorMessage('app.attachreservation.error.otherEmail');
            logEvent('AttachReservationError', { reason: 'email_not_matching' });
            break;
          case '404': // reservation not found
            setAttachReservationErrorMessage('app.attachreservation.error.notFound');
            logEvent('AttachReservationError', { reason: 'reservation_not_found' });
            break;
          default:
            setAttachReservationErrorMessage('app.login.serverError');
            logEvent('AttachReservationError', { reason: 'server_error' });
            break;
        }
      }

      setProcessing(false);
      setExternalProcessing?.(false);
    };

    return (
      <View
        style={{
          marginHorizontal: theme.space.xl,
          alignItems: 'center',
        }}
      >
        <View style={{ width: '100%', marginTop: theme.space.l }}>
          <Text color="gray" style={{ textAlign: 'center', marginBottom: theme.space.l, lineHeight: 26 }}>
            {formatMessage({ id: 'app.attachreservation.description' })}
          </Text>
          <FormControl
            label={formatMessage({
              id: 'app.attachreservation.inputTitle',
            })}
            value={formik.values.bookingReference}
            isValid={!formik.errors.bookingReference}
            errorMessage={attachReservationErrorMessage}
            onChangeText={(text) => {
              formik.setFieldValue('bookingReference', text);
            }}
            autoCorrect={false}
            returnKeyType="send"
            onSubmitEditing={performAttachReservation}
            keyboardType="numeric"
            maxLength={MAX_LENGTH}
          />
          {attachReservationErrorMessage && (
            <Text
              color="attention"
              style={{
                textAlign: 'center',
                marginBottom: theme.space.l,
              }}
            >
              {attachReservationErrorMessage &&
                formatMessage({
                  id: attachReservationErrorMessage,
                })}
            </Text>
          )}
          <Text color="gray" style={{ textAlign: 'center' }}>
            {formatMessage({
              id: 'app.attachreservation.noBooking.part1',
            })}{' '}
            <Text
              color="tertiary"
              onPress={() => {
                const url = formatMessage({
                  id: 'app.general.homepage',
                });

                const query = queryString.stringify({
                  utm_source: getBrand(theme) === 'HSN' ? 'Hof-van-Saksen-Park-App' : 'Landal-Park-App',
                  utm_medium: 'app',
                  utm_campaign: 'Retention', // Self-service | Retention | Upsell
                  utm_content: `${Screens.Reservation.nl}` /* |${park?.id}|| */,
                });

                const completeUrl = `${url}?${query}`;
                Linking.openURL(completeUrl);
              }}
              style={{
                lineHeight: 26,
              }}
              bold
            >
              {formatMessage({
                id: 'app.attachreservation.noBooking.part2',
              })}
              <Icon
                name="exit-app"
                color="tertiary"
                style={{
                  marginLeft: 2,
                  lineHeight: 26,
                }}
              />
            </Text>
          </Text>

          <Button
            onPress={performAttachReservation}
            style={{ backgroundColor: theme.colors.secondary, marginTop: theme.space.xl }}
            disabled={!formik.isValid}
          >
            {processing ? (
              <Button.ActivityIndicator style={{ height: 22 }} />
            ) : (
              <Button.Text color="white">
                {formatMessage({
                  id: 'app.attachreservation.cta',
                })}
              </Button.Text>
            )}
          </Button>
          <TouchableOpacity
            onPress={closeBottomSheet}
            style={{ marginTop: theme.space.m, alignSelf: 'center' }}
            hitSlop={{ top: 10, bottom: 10, left: 10, right: 10 }}
          >
            <Text color="gray" bold>
              {formatMessage({ id: 'app.general.cancel' })}
            </Text>
          </TouchableOpacity>
        </View>
      </View>
    );
  }
);
export default AttachReservation;
