import { useEffect, useContext, RefObject } from 'react';
import { UserContext } from '@app/context/usercontext/UserContextProvider';
import { currentBuildSupported } from 'utils/currentBuildSupported';
import { NavigationContainerRef } from '@react-navigation/native';

interface Props {
  navigationRef: RefObject<NavigationContainerRef<any>>;
}

export default function useBlockAppOnDeprecatedVersion({ navigationRef }: Props) {
  const { userState } = useContext(UserContext);

  useEffect(() => {
    // wait until the app is fully loaded
    // NOTE: we don't need this for data, but the app crashes if we catch deprecation too early (other hooks interfering?)
    if (userState === 'initial') {
      return;
    }

    if (!currentBuildSupported()) {
      if (navigationRef.current) {
        navigationRef.current.navigate('AppDeprecated');
      }
    }
  }, [navigationRef, userState]);
}
