import React, { useCallback, useContext, useEffect, useState } from 'react';
import { TouchableOpacity, View } from 'react-native';
import useTheme from '@app/hooks/useTheme';
import Text from '@app/components/Text';
import { useIntl } from 'react-intl';
import { useBottomSheet } from '@app/navigation/bottom-sheet/BottomSheetProvider';
import { Voucher } from 'apis/_generated-reservation-api/models';
import { useGetReservation } from '@domains/reservations/reservation/queries/useGetReservation';
import { getBrand } from 'utils/brand';
import useMetrics from '@app/metrics/useMetrics';
import { UserContext } from '@app/context/usercontext/UserContextProvider';
import VoucherCard from './VoucherCard';
import { FlatList } from 'react-native-gesture-handler';
import PageIndicator from '@app/components/PageIndicator';
import RoundButton from '@app/components/RoundButton';

const ACTIVE_VOUCHER_STATUS = 'CREATED';
const REDEEMED_VOUCHER_STATUS = 'REDEEMED';

interface Props {
  bookingReference?: string;
  vouchers: Voucher[];
  extraId?: string;
  voucherId?: string;
  extraName?: string;
}

export default function VoucherSheet({ bookingReference, vouchers, extraId, voucherId, extraName }: Props) {
  const theme = useTheme();
  const { formatMessage } = useIntl();
  const { logEvent } = useMetrics();
  const { token } = useContext(UserContext);

  const { closeBottomSheet } = useBottomSheet();
  const ref = React.useRef<FlatList>(null);
  const [active, setActive] = useState(0);

  const { data: reservation } = useGetReservation({
    token,
    brand: getBrand(theme),
    bookingReference,
  });

  const getVoucherStatusForAnalytics = useCallback(() => {
    if ((vouchers?.length || 0) <= 0) return 'voucher available after';
    switch (vouchers[0].status) {
      case ACTIVE_VOUCHER_STATUS:
        return 'active';
      case REDEEMED_VOUCHER_STATUS:
        return 'voucher is redeemed';
    }
  }, [vouchers]);

  useEffect(() => {
    logEvent('VoucherView', {
      extra_id: extraId,
      voucher_id: voucherId,
      voucher_amount: Number(vouchers?.length || 0),
      voucher_status: getVoucherStatusForAnalytics(),
    });
  }, [extraId, getVoucherStatusForAnalytics, logEvent, voucherId, vouchers?.length]);

  const go = (index: number) => {
    const forward = index > active;

    if (forward) {
      if (active < entries.length) {
        ref.current?.scrollToIndex({ index });
      }
    } else if (active > 0) {
      ref.current?.scrollToIndex({ index });
    }
  };

  //TODO: remove mock
  /* vouchers = [
    {
      code: 'ABC',
      status: 'CREATED',
    },
    {
      code: 'DEF',
      status: 'CREATED',
    },
    {
      code: 'GHI',
      status: 'CREATED',
    },
  ]; */

  let entries = vouchers;
  if ((entries?.length || 0) === 0) {
    entries = [{ status: 'AVAILABLE_LATER' }];
  }

  const [width, setWidth] = useState(0);

  const detectActiveSlide = useCallback(
    (xOffset: number) => {
      const index = Math.round(xOffset / width);
      setActive(index);
    },
    [width]
  );

  return (
    <View
      style={{
        marginTop: theme.space.l,
      }}
    >
      <FlatList
        ref={ref}
        onLayout={(event) => setWidth(event.nativeEvent.layout.width)}
        horizontal
        pagingEnabled
        data={entries}
        //snapToAlignment={'center'}
        //ItemSeparatorComponent={() => <View style={{ width: 35 }} />}
        //contentInset={{ right: 0, top: 0, left: 40, bottom: 0 }}
        renderItem={({ item, index }) => {
          return (
            <VoucherCard
              reservation={reservation}
              voucher={item}
              extraId={extraId}
              extraName={extraName}
              voucherId={voucherId}
              index={index}
              totalVouchers={entries.length}
            />
          );
        }}
        showsHorizontalScrollIndicator={false}
        //centerContent
        onScroll={(event) => detectActiveSlide(event.nativeEvent.contentOffset.x)}
        //style={{ height, backgroundColor: theme.colors.black }}
        //removeClippedSubviews={false}
      />
      <View
        style={{
          alignItems: 'center',
        }}
      >
        {vouchers.length > 0 && (
          <View
            style={{
              width: '100%',
              justifyContent: 'space-between',
              flexDirection: 'row',
              paddingHorizontal: theme.space.xl,
            }}
          >
            <RoundButton
              icon="back"
              iconColor="black"
              onPress={() => {
                go(active - 1);
              }}
              disabled={active === 0}
              style={{ opacity: active === 0 ? 0.5 : 1 }}
            />
            <View>
              <PageIndicator
                length={entries.length}
                loop
                selected={active}
                setSelected={go}
                color={theme.colors.black}
                inactiveOpacity={0.2}
                style={{ bottom: 16 }}
              />
            </View>
            <RoundButton
              icon="forward"
              iconColor="black"
              onPress={() => {
                go(active + 1);
              }}
              disabled={active === entries.length - 1}
              style={{ opacity: active === entries.length - 1 ? 0.5 : 1 }}
            />
          </View>
        )}
        <TouchableOpacity
          onPress={closeBottomSheet}
          style={{ marginTop: theme.space.m }}
          hitSlop={{ top: 10, bottom: 10, left: 10, right: 10 }}
        >
          <Text color="gray" bold>
            {formatMessage({ id: 'app.general.cancel' })}
          </Text>
        </TouchableOpacity>
      </View>
    </View>
  );
}
