import useTheme from '@app/hooks/useTheme';
import useMetrics from '@app/metrics/useMetrics';
import { useBottomSheet } from '@app/navigation/bottom-sheet/BottomSheetProvider';
import { useNavigateToAcco } from '@domains/reservations/reservation/screens/AccoNumberPopup';
import React, { useEffect } from 'react';
import { useIntl } from 'react-intl';
import { View } from 'react-native';
import { Document, Page, pdfjs } from 'react-pdf';
import { useSafeAreaInsets } from 'react-native-safe-area-context';
import Button from './Button';
import { usePopup } from '@app/navigation/popup/PopupProvider';
import PopupContent from '@app/navigation/popup/PopupContent';

pdfjs.GlobalWorkerOptions.workerSrc = `//cdnjs.cloudflare.com/ajax/libs/pdf.js/${pdfjs.version}/pdf.worker.js`;

interface Props {
  parkCode?: string;
  bookingReference?: string;
}
export default function ParkMap({ parkCode, bookingReference }: Props) {
  const { formatMessage } = useIntl();
  const { logEvent } = useMetrics();
  const theme = useTheme();
  const insets = useSafeAreaInsets();
  const { closeBottomSheet } = useBottomSheet();
  const { openPopup, closePopup } = usePopup();

  const { navigateToAcco, unitCode, isCombiBooking } = useNavigateToAcco({ parkCode, bookingReference });

  useEffect(() => {
    logEvent('ViewMap');
  }, [logEvent]);

  if (!parkCode) {
    return null;
  }

  const pdfURL = `https://www.landal.nl/-/media/Files/${parkCode}/${parkCode}_Plat.pdf`;
  return (
    <View style={{ flex: 1 }}>
      <Document file={pdfURL} onLoadSuccess={() => {}}>
        <Page pageNumber={1} />
      </Document>

      <View style={{ padding: theme.space.l, marginBottom: insets.bottom + theme.space.m }}>
        <Button
          type="primary"
          onPress={() => {
            if (unitCode && !isCombiBooking) {
              navigateToAcco(unitCode);
            } else {
              closeBottomSheet();
              openPopup({
                component: () => (
                  <PopupContent
                    title={formatMessage({ id: 'app.reservation.accoInputTitle' })}
                    text={formatMessage({ id: 'app.forms.labels.accommodationnumber' })}
                    ctaText={formatMessage({ id: 'app.components.detail.moreinfo.address' })}
                    showClose
                    fields={[
                      {
                        id: 'accommodationnumber',
                        value: unitCode || '',
                        width: 74,
                        textAlign: 'center',
                      },
                    ]}
                    onCTA={async (values, setProcessing) => {
                      setProcessing(true);
                      /*
                    await new Promise((resolve, reject) => {
                      setTimeout(() => resolve(), 3000);
                    });
                    */
                      await navigateToAcco(values.accommodationnumber);

                      setProcessing(false);
                      closePopup();
                    }}
                  />
                ),
                disableBackdropClose: true,
              });
            }
          }}
          style={{ marginBottom: 8 }}
        >
          <Button.Text>{formatMessage({ id: 'app.practical.routeAcco.title' })}</Button.Text>
        </Button>
      </View>
    </View>
  );
}
