import useTheme from '@app/hooks/useTheme';
import React, { useEffect, useRef } from 'react';
import { useSafeAreaInsets } from 'react-native-safe-area-context';
import { ScrollView, ScrollViewProps, StatusBar, useWindowDimensions, View } from 'react-native';
import { BOTTOM_TAB_BAR_HEIGHT, BOTTOM_TAB_BAR_HEIGHT_NO_INSET } from '@app/navigation/BottomTabs';
import Animated from 'react-native-reanimated';
import { useStickyBanner } from '@app/context/StickyBannerProvider';
import useKeyboardHeight from '@app/hooks/useKeyboardHeight';
import { useStickyImageHeaderDimensions } from './StickyImageHeader';

interface Props extends React.PropsWithChildren<ScrollViewProps> {
  animated?: boolean;
  topPadding?: boolean;
  forceBottomInset?: boolean;
  scrollRef?: any;
}

const ScreenScrollContainer = ({
  children,
  animated = false,
  topPadding = true,
  forceBottomInset,
  contentContainerStyle,
  scrollRef,
  ...props
}: Props) => {
  const theme = useTheme();
  const insets = useSafeAreaInsets();
  const { bannerShown } = useStickyBanner();
  const scrollViewWrapperRef = useRef<View>(null);
  const keyboardHeight = useKeyboardHeight();
  const { EXPANDED_HEADER_HEIGHT } = useStickyImageHeaderDimensions('m');
  const { height: screenHeight } = useWindowDimensions();

  const hasBottomBar = forceBottomInset;

  const usedInset =
    (hasBottomBar
      ? insets.bottom
        ? BOTTOM_TAB_BAR_HEIGHT
        : BOTTOM_TAB_BAR_HEIGHT_NO_INSET
      : insets.bottom) + (bannerShown ? 64 : 0);

  const innerContentContainerStyle = {
    paddingHorizontal: theme.space.l,
    paddingBottom: usedInset + theme.space.l,
    paddingTop: !topPadding ? 0 : insets.top || theme.space.l,
  };

  const scrollIndicatorInsets = { top: 0, left: 0, bottom: usedInset - insets.bottom, right: 0 };

  useEffect(() => {
    if (keyboardHeight > 0) {
      scrollViewWrapperRef.current?.setNativeProps({
        style: { flex: 1, paddingBottom: keyboardHeight },
      });
    } else {
      scrollViewWrapperRef.current?.setNativeProps({
        style: { flex: 1, paddingBottom: 0 },
      });
    }
  }, [EXPANDED_HEADER_HEIGHT, insets.top, keyboardHeight, screenHeight]);

  return (
    <View ref={scrollViewWrapperRef}>
      {animated ? (
        <Animated.ScrollView
          contentContainerStyle={[innerContentContainerStyle, contentContainerStyle]}
          scrollIndicatorInsets={scrollIndicatorInsets}
          nestedScrollEnabled
          scrollEventThrottle={1}
          ref={scrollRef}
          {...props}
        >
          <StatusBar translucent barStyle={'light-content'} backgroundColor="transparent" />
          {children}
        </Animated.ScrollView>
      ) : (
        <ScrollView
          contentContainerStyle={[innerContentContainerStyle, contentContainerStyle]}
          scrollIndicatorInsets={scrollIndicatorInsets}
          nestedScrollEnabled
          ref={scrollRef}
          {...props}
        >
          <StatusBar translucent barStyle={'light-content'} backgroundColor="transparent" />
          {children}
        </ScrollView>
      )}
    </View>
  );
};
export default ScreenScrollContainer;
