import React, { useMemo } from 'react';
import { useGetFacilities } from '@domains/park/at-the-park/queries/useGetFacilities';
import { useFocusEffect } from '@react-navigation/native';
import useMetrics from '@app/metrics/useMetrics';
import _ from 'lodash';
import StickyHeader from '@app/components/StickyHeader';
import { useIntl } from 'react-intl';
import ScreenScrollContainer from '@app/components/ScreenScrollContainer';
import ListItems, { ListItem } from '@app/components/ListItems2/ListItems';
import { getAPIDate, isToday } from 'utils/date';
import useOpeningHours from '@app/hooks/useOpeningHours';
import { useGetPromos } from '@domains/promo/queries/useGetPromos';
import { NavScreenProp } from '@app/navigation/stacks/park/FoodAndDrinks';
import { getDataBySubCat } from '@domains/promo/helpers/getDataBySubCat';

const FoodAndDrinks = ({ navigation, route }: NavScreenProp<'FoodAndDrinks'>) => {
  const { logScreenView } = useMetrics();

  const { formatMessage } = useIntl();
  const formattedOpeningHours = useOpeningHours();
  const parkCode = route?.params?.parkCode;

  const { data: facilities } = useGetFacilities({ parkCodeOverride: parkCode });
  const foodAndDrinksData = facilities?.filter((f) => f.categories?.find((c) => c === 'foodAndDrink'));

  const mappedData = _.sortBy(foodAndDrinksData, 'name');

  const { data: promos } = useGetPromos({ parkCodeOverride: parkCode });
  const reservationOrderItems: ListItem[] = useMemo(() => {
    const items =
      promos
        ?.filter(
          (p) =>
            p.connectionType === 'CCV' ||
            (['meals', 'breakfast', 'groceries', 'reservations'].includes(p.subCat || '') &&
              p.hoofdCat === 'fb')
        )
        .map((p, index) => {
          const categoryData = getDataBySubCat(p.subCat || '', formatMessage, p.imageUrl);

          let shopData =
            p.connectionType === 'CCV'
              ? {
                  shopId: p.shopId,
                  facilityId: p.facilityId,
                  publicKey: p.publicKey,
                  secretKey: p.secretKey,
                }
              : undefined;

          let linkTo = p.url;
          let title = p.name ?? categoryData.name;
          let subtitle = categoryData.sub ?? formatMessage({ id: 'app.promo.meals' });

          if (p.connectionType === 'CCV') {
            const shop = facilities?.find((f) => f.id === shopData?.facilityId);
            const openingHours = shop?.openingHours;
            const today = openingHours?.find((o) => o?.date && isToday(o.date));

            if (today) {
              subtitle = `${formatMessage({
                id: 'app.shop.takeaway',
              })} ${today.startTime} ${formatMessage({
                id: 'app.general.time.till',
              })} ${today.endTime}`;
            }
            if (shop?.name) {
              title = shop.name;
            }
            linkTo = undefined;
          }

          return {
            id: p.shopId || p.name || index,
            title,
            subtitle,
            image: categoryData.imageUrl,
            localImage: categoryData.imageUrl ? undefined : categoryData.imageLocal,
            linkTo,
            shopData,
            onPress: shopData?.shopId
              ? () => {
                  navigation.navigate('ProductCategories', {
                    shopId: shopData?.shopId,
                    facilityId: shopData?.facilityId ?? '',
                  });
                }
              : undefined,
          };
        }) ?? [];
    return items;
  }, [facilities, formatMessage, navigation, promos]);

  useFocusEffect(
    React.useCallback(() => {
      logScreenView('FoodAndDrinks');
    }, [logScreenView])
  );
  return (
    <>
      <StickyHeader
        title={formatMessage({ id: 'app.park.foodAndDrinks' })}
        showClose
        navigation={navigation}
      />
      <ScreenScrollContainer topPadding={false}>
        {reservationOrderItems.length > 0 && (
          <ListItems
            title={formatMessage({ id: 'app.promo.reserveAndOrder' })}
            items={reservationOrderItems ?? []}
            usePlaceholderImage
          />
        )}

        <ListItems
          title={formatMessage({ id: 'app.park.foodAndDrinks.list.title' })}
          items={
            mappedData.map((a) => {
              const openingHoursToday = a.openingHours?.filter((o) => o?.date === getAPIDate(new Date()));

              let openingHoursSubtitle = openingHoursToday
                ?.map((oht) => formattedOpeningHours(oht?.startTime, oht?.endTime, oht?.displayHint))
                .join('\n');

              return {
                id: a.id ?? '',
                title: a.name ?? '',
                subtitle: openingHoursSubtitle ?? '',
                rightSubtitleIcon: !!a.reservationId && 'food-and-drinks',
                rightSubtitle: !!a.reservationId && formatMessage({ id: 'app.foodanddrinks.bookatable' }),
                image: a.media,
                onPress: () => {
                  navigation.navigate('FoodAndDrinksDetail', { id: a.id ?? '', parkCode });
                },
                // TODO
                /* numberOfActivitiesToday: item.numberOfActivitiesToday,
              categories: item.categories ? (item.categories as string[]) : [],
              description: item.description,
              reservationId: item.reservationId, */
              };
            }) ?? []
          }
          usePlaceholderImage
        />
      </ScreenScrollContainer>
    </>
  );
};
export default FoodAndDrinks;
