import { LanguageCode } from '@app/hooks/useCulture';
import { BookingStatusType, Reservation } from 'apis/_generated-reservation-api/models';
import { QueryStatus } from 'react-query';
import { isTodayUnix } from './date';

const getAmountWithCurrency = (cultureCode?: LanguageCode, reservation?: Reservation) => {
  if (!reservation?.pendingPayment) {
    return '';
  }
  let currency = reservation?.currency ? reservation.currency : 'EUR';
  const formatter = Intl.NumberFormat(cultureCode?.toString(), {
    style: 'currency',
    currency,
  });

  return formatter.format(reservation.pendingPayment);
};

const isComplete = (reservation?: Reservation) => {
  if (!reservation) {
    return false;
  }
  return (
    (reservation.arrivalTime || reservation.holidayStarted) &&
    !reservation.needsCompanyRegistration &&
    Number(reservation.pendingPayment) <= 0
  );
};

const mandatoryTasksCompleted = (reservation?: Reservation) => {
  if (!reservation) {
    return false;
  }
  return !reservation.needsCompanyRegistration && Number(reservation.pendingPayment) <= 0;
};

const hasNoAvailableReservation = (status: QueryStatus, reservation?: Reservation) =>
  !reservation && status === 'success';

const getActiveReservation = (reservations: Reservation[]) => {
  // filter de reserveringen die vandaag (vertrek, aankomst) zijn
  const reservationDepartureToday = reservations.find((reservation) =>
    isTodayUnix(reservation.departureDate)
  );
  const reservationArrivalToday = reservations.find((reservation) => isTodayUnix(reservation.arrivalDate));

  if (!!reservationDepartureToday && !!reservationArrivalToday) {
    return reservationDepartureToday.status === BookingStatusType.Departed
      ? reservationArrivalToday
      : reservationDepartureToday;
  } else {
    return reservations?.[0];
  }
};

export {
  getAmountWithCurrency,
  isComplete,
  hasNoAvailableReservation,
  mandatoryTasksCompleted,
  getActiveReservation,
};
