import React, { useState } from 'react';
import useTheme from '@app/hooks/useTheme';
import ProductList from './ProductList';
import { View } from 'react-native';
import { useGetCategories } from '../queries/useGetCategories';
import { CartContextUpdater } from '@app/context/CartProvider';
import useCart from '@app/hooks/useCart';
import useScreenSize from '@app/hooks/useScreenSize';
import { useFocusEffect } from '@react-navigation/native';
import useMetrics from '@app/metrics/useMetrics';
import { Category } from 'apis/_generated-park-api/models';
import StickyHeader from '@app/components/StickyHeader';
import { useIntl } from 'react-intl';
import StickyBottomBar from '@app/navigation/StickyBottomBar';
import Button from '@app/components/Button';
import { NavScreenProp } from '@app/navigation/stacks/Main';

export default function ProductCategories({ navigation, route }: NavScreenProp<'ProductCategories'>) {
  const SHOP_ID = route?.params?.shopId; //DEV: 168652; //PROD: 187780;

  const theme = useTheme();
  const { logScreenView } = useMetrics();
  const { width } = useScreenSize();
  const { formatMessage, formatNumber } = useIntl();
  const { cart } = useCart();

  const { data: items } = useGetCategories({
    shopId: SHOP_ID,
    options: { notifyOnChangeProps: ['data', 'error'] },
  });
  const { setActiveShop } = React.useContext(CartContextUpdater);

  React.useEffect(() => {
    setActiveShop(SHOP_ID);
  }, [SHOP_ID, setActiveShop]);

  useFocusEffect(
    React.useCallback(() => {
      if (SHOP_ID) {
        logScreenView('ProductCategories', [SHOP_ID.toString()]);
      }
    }, [SHOP_ID, logScreenView])
  );

  const categoryRoutes = React.useMemo(
    () =>
      items?.map((c) => {
        return { key: c.name ?? '', title: c.name ?? '' };
      }),
    [items]
  );
  const sceneMap = React.useMemo(() => {
    const mappedData = items?.reduce(
      (prev, c) => ({
        ...prev,
        [c.name ?? '']: () => (
          <ProductList categoryId={c.id ?? 0} photoUrl={c.photo} key={c.id} name={c.name} />
        ),
      }),
      {}
    );
    return mappedData;
  }, [items]) as {
    [key: string]: React.ComponentType<Category>;
  };

  const [categoryIndex, setCategoryIndex] = useState(0);

  const itemsInCart = cart
    ?.map((p) => p.quantity)
    .reduce((prev: number, next: number) => Number(prev) + Number(next), 0);

  const totalPrice = cart
    ?.map((item) => item.totalPrice * item.quantity)
    .reduce((prev: number, next: number) => Number(prev) + Number(next), 0);

  return (
    <>
      <StickyHeader
        title={formatMessage({ id: 'app.headers.shop.overview' })}
        showClose
        navigation={navigation}
      />

      {!!totalPrice && (
        <StickyBottomBar style={{ padding: theme.space.l, justifyContent: 'center' }}>
          <View style={{ flex: 1 }}>
            <Button
              onPress={() => {
                navigation.navigate('Cart', {
                  facilityId: route?.params?.facilityId,
                  shopId: route?.params?.shopId,
                });
              }}
              style={{
                backgroundColor: theme.colors.tertiary,
              }}
              disabled={!itemsInCart}
            >
              <Button.Icon name="shopping-card" size="s" color="white" showBadge={!!totalPrice} />
              <Button.Text color="white">{`${formatMessage({ id: 'app.shop.goToCart' })} (${formatNumber(
                totalPrice,
                {
                  style: 'currency',
                  currency: 'EUR',
                  currencyDisplay: 'symbol',
                }
              )})`}</Button.Text>
            </Button>
          </View>
        </StickyBottomBar>
      )}
    </>
  );
}
