import React, { useContext, useRef } from 'react';
import { Linking, TouchableOpacity, View } from 'react-native';
import Button from '@app/components/Button';
import useTheme from '@app/hooks/useTheme';
import Text from '@app/components/Text';
import { useIntl } from 'react-intl';
import useMetrics from '@app/metrics/useMetrics';
import { useGetPark } from '@domains/park/contact-and-info/queries/useGetPark';
import Clipboard from '@react-native-clipboard/clipboard';
import { UserContext } from '@app/context/usercontext/UserContextProvider';
import { useGetReservation } from '@domains/reservations/reservation/queries/useGetReservation';
import { getBrand } from 'utils/brand';
import Icon from '@app/components/Icon';
import Tooltip, { TooltipRefType } from '../../app/components/Tooltip';
import { useBottomSheet } from '@app/navigation/bottom-sheet/BottomSheetProvider';
import { LandalPressable } from '@app/components/LandalPressable';

interface Props {
  bookingReference?: string;
  parkCode?: string;
}

export default function DamageReportSheet({ bookingReference, parkCode }: Props) {
  const theme = useTheme();
  const { formatMessage } = useIntl();
  const { logEvent } = useMetrics();
  const { data: park } = useGetPark({ parkCodeOverride: parkCode });
  const { token } = useContext(UserContext);
  const copiedComponentRef = useRef<TooltipRefType>(null);
  const { closeBottomSheet } = useBottomSheet();

  const { data: reservation } = useGetReservation({
    token,
    brand: getBrand(theme),
    bookingReference,
  });

  React.useEffect(() => {
    logEvent('ShowDamageReportContact');
  }, [logEvent]);

  let copyText = `${formatMessage({
    id: 'app.reservation.damageReport.bookingReferenceShort',
  })}: ${reservation?.bookingReference}`;

  if (reservation?.unitCode) {
    copyText += `\r\n${formatMessage({
      id: 'app.reservation.damageReport.accoNumberShort',
    })}: ${reservation?.unitCode}`;
  }

  return (
    <View
      style={{
        marginTop: theme.space.l,
        alignItems: 'center',
      }}
    >
      <View
        style={{
          alignItems: 'center',
          paddingHorizontal: theme.space.m,
        }}
      >
        <Text style={{ marginTop: theme.space.s }} bold>
          {formatMessage({ id: 'app.reservation.damageReportTitle' })}
        </Text>

        <Text
          type="body"
          style={{ textAlign: 'center', marginTop: theme.space.m, marginBottom: theme.space.s }}
          color="gray"
        >
          {formatMessage({ id: 'app.reservation.damageReportText' })}
        </Text>
      </View>
      <View
        style={{
          alignSelf: 'stretch',
        }}
      >
        <Tooltip text={formatMessage({ id: 'app.general.copied' })} ref={copiedComponentRef} />
        <LandalPressable
          style={{
            backgroundColor: theme.colors.canvas,
            borderRadius: 8,
            marginHorizontal: theme.space.l,
            marginVertical: theme.space.l,
            paddingVertical: theme.space.m,
            paddingHorizontal: theme.space.l,
            alignSelf: 'stretch',
            flexDirection: 'row',
          }}
          onPress={() => {
            Clipboard.setString(copyText);
            copiedComponentRef?.current?.show();
          }}
        >
          <Text type="body" style={{ flex: 1, lineHeight: 20 }} color="gray">
            {copyText}
          </Text>
          <View
            style={{
              borderColor: theme.colors.light,
              borderLeftWidth: 1,
              justifyContent: 'center',
              alignItems: 'center',
              paddingLeft: theme.space.l,
              height: '100%',
              flexDirection: 'row',
            }}
          >
            <Icon name="copy" size="m" color="gray" />
          </View>
        </LandalPressable>
      </View>
      <View
        style={{
          marginTop: theme.space.m,
          width: '100%',
          paddingHorizontal: theme.space.l,
        }}
      >
        {!!park?.whatsapp && (
          <Button
            onPress={async () => {
              park?.whatsapp && Linking.openURL(park?.whatsapp);
              return;
            }}
            style={{
              backgroundColor: theme.colors.secondary,
              marginBottom: theme.space.s,
            }}
          >
            <Button.Icon name="whatsapp" color={'white'} />
            <Button.Text color="white" style={{ lineHeight: 20 }}>
              {formatMessage({ id: 'app.reservation.damageReport.openWhatsapp' })}
            </Button.Text>
          </Button>
        )}

        <Button
          onPress={async () => {
            Linking.openURL('tel:' + park?.phone);
            return;
          }}
          style={{
            backgroundColor: park?.whatsapp ? 'transparent' : theme.colors.secondary,
            borderColor: park?.whatsapp ? theme.colors.light : undefined,
            marginBottom: theme.space.s,
          }}
        >
          <Button.Icon name="call" size="s" color={park?.whatsapp ? 'gray' : 'white'} />
          <Button.Text type="title" color={park?.whatsapp ? 'tertiary' : 'white'} style={{ lineHeight: 20 }}>
            {park?.phone}
          </Button.Text>
        </Button>
        <Button
          onPress={async () => {
            Linking.openURL('mailto:' + park?.email);
            return;
          }}
          style={{
            backgroundColor: 'transparent',
            borderColor: theme.colors.light,
            borderWidth: 1,
          }}
        >
          <Button.Icon name="mail" size="s" color={'tertiary'} />
          <Button.Text color="tertiary" style={{ lineHeight: 20 }}>
            {park?.email}
          </Button.Text>
        </Button>
      </View>
      <TouchableOpacity
        onPress={closeBottomSheet}
        style={{ marginTop: theme.space.m }}
        hitSlop={{ top: 10, bottom: 10, left: 10, right: 10 }}
      >
        <Text color="gray" bold>
          {formatMessage({ id: 'app.general.cancel' })}
        </Text>
      </TouchableOpacity>
    </View>
  );
}
