import { ONBOARDED_KEY, TOKEN_KEY } from '@app/config/StorageKeys';
import AsyncStorage from '@react-native-async-storage/async-storage';
import React, { useEffect, useState } from 'react';
import { Platform } from 'react-native';
import Config from 'react-native-config';

export type UserState = 'initial' | 'newUser' | 'onboarded' | 'payment';
type UserContextModel = {
  userState: UserState;
  token?: string;
  demoMode: boolean;
};
type UserContextUpdaterModel = {
  setUserState: (state: UserState) => void;
  setToken: (token: string) => void;
  clearToken: () => void;
};

export const UserContextUpdater = React.createContext<UserContextUpdaterModel>({
  setToken: () => {},
  setUserState: () => {},
  clearToken: () => {},
});

export const UserContext = React.createContext<UserContextModel>({
  userState: 'initial',
  token: undefined,
  demoMode: false,
});

export function UserContextProvider({ children }: { children: React.ReactNode }) {
  const [userState, setUserState] = useState<UserState>('initial');
  const [token, setToken] = useState<string>();
  const [demoMode, setDemoMode] = useState(Platform.OS === 'web' || Config.DEMO_MODE === 'true');

  const userContext: UserContextModel = {
    userState,
    demoMode,
    token: demoMode ? '123' : token,
  };

  useEffect(() => {
    const storeOnboarded = async () => {
      await AsyncStorage.setItem(ONBOARDED_KEY, 'true');
    };

    if (userState === 'onboarded') {
      storeOnboarded();
    }
  }, [userState]);

  const setNewToken = (newToken?: string) => {
    setToken(() => {
      return newToken;
    });
  };

  const clearToken = () => {
    setToken(undefined);
    AsyncStorage.removeItem(TOKEN_KEY);
  };

  React.useEffect(() => {
    // synchronise stored favourites with app context on appstart
    const getToken = async () => {
      const storedToken = await AsyncStorage.getItem(TOKEN_KEY);
      // TODO: Fix strange undefined in string scenario
      if (storedToken && storedToken !== 'undefined') {
        setToken(storedToken);
      }
    };
    getToken();
  }, []);

  React.useEffect(() => {
    if (token) {
      AsyncStorage.setItem(TOKEN_KEY, token);
    }
  }, [token]);

  return (
    <UserContext.Provider value={userContext}>
      <UserContextUpdater.Provider
        value={{
          setUserState,
          setToken: setNewToken,
          clearToken,
        }}
      >
        {children}
      </UserContextUpdater.Provider>
    </UserContext.Provider>
  );
}
