import { gql, GraphQLClient } from 'graphql-request';
import Config from 'react-native-config';

export function useRequestPasswordReset() {
  const requestPasswordReset = async (email: string, cultureCode: string) => {
    const graphQLClient = new GraphQLClient(Config.IDENTITY_API_URL);
    const query = gql`
        query passwordResetRequest {
          passwordResetRequest(email: "${email}", cultureCode:"${cultureCode}") {
            success
          }
        }
      `;
    return await graphQLClient.rawRequest(query);
  };
  return { requestPasswordReset };
}
