import RadioButton from '@app/components/Form/RadioButton';
import Text from '@app/components/Text';
import useTheme from '@app/hooks/useTheme';
import { NavigationContainerRef, useNavigation } from '@react-navigation/native';
import { Address } from 'apis/_generated-reservation-api/models';
import React, { useState } from 'react';
import { useIntl } from 'react-intl';
import { TouchableOpacity, View } from 'react-native';
import { add, Canvas, Group, LinearGradient, RoundedRect, vec } from '@shopify/react-native-skia';
import { AllRoutes } from '@app/navigation/stacks/Main';
import { LandalPressable } from '@app/components/LandalPressable';

interface Props {
  superLine?: string;
  addressLine1: string;
  addressLine2: string;
  selected: boolean;
  onSelect: () => void;
  customAddress?: Address;
  setCustomAddress: (address: Address) => void;
}

export default function AddressOption({
  selected,
  onSelect,
  superLine,
  addressLine1,
  addressLine2,
  customAddress,
  setCustomAddress,
}: Props) {
  const { formatMessage } = useIntl();
  const theme = useTheme();
  const navigation = useNavigation<NavigationContainerRef<AllRoutes>>();
  const [width, setWidth] = useState(0);
  const [height, setHeight] = useState(0);
  const [isMeasured, setIsMeasured] = useState(false);

  const SIZE = 180;
  const c = vec(100, 100);
  const r = SIZE;

  return (
    <LandalPressable
      style={{
        flexDirection: 'row',
        backgroundColor: theme.colors.white,
        paddingHorizontal: theme.space.l,
        paddingVertical: theme.space.l,
        borderRadius: 8,
        borderWidth: 2,
        borderColor: selected ? 'transparent' : theme.colors.light,
        marginBottom: theme.space.m,
      }}
      onPress={onSelect}
      onLayout={(event) => {
        if (isMeasured) return;
        var { width: layoutWidth, height: layoutHeight } = event.nativeEvent.layout;
        if (layoutWidth <= 0) return;

        setWidth(layoutWidth);
        setHeight(layoutHeight);
        setIsMeasured(true);
      }}
    >
      {isMeasured && (
        <Canvas
          key={addressLine1 + addressLine2 + selected}
          style={{
            position: 'absolute',
            width: width + 2,
            height: height + 2,
            left: -2,
            top: -2,
          }}
        >
          {selected && (
            <Group>
              <Group style="stroke" strokeWidth={2}>
                <LinearGradient
                  start={add(c, vec(-r, 0))}
                  end={add(c, vec(r, 0))}
                  colors={[
                    theme.colors.gradientPrimary1,
                    theme.colors.gradientPrimary2,
                    theme.colors.gradientPrimary3,
                  ]}
                  transform={[{ rotate: Math.PI / 3 }]}
                />
                <RoundedRect x={1} y={1} r={8} width={width - 2} height={height} />
              </Group>
            </Group>
          )}
        </Canvas>
      )}
      <View style={{ flex: 1 }}>
        {!!superLine && <Text color="gray">{superLine}</Text>}
        <Text>{addressLine1}</Text>
        <Text>{addressLine2}</Text>
        {!!customAddress && (
          <TouchableOpacity
            activeOpacity={0.8}
            style={{ marginTop: theme.space.s, elevation: 3 }}
            onPress={() => {
              navigation?.navigate('TravelCompanyAddress', {
                setCustomAddress: async (address: Address) => {
                  await setCustomAddress(address);
                  onSelect();
                },
                address: customAddress,
              });
            }}
          >
            <Text color="tertiary" bold size="s">
              {formatMessage({ id: 'app.reservation.companions.changeAddress' })}
            </Text>
          </TouchableOpacity>
        )}
      </View>
      <RadioButton selected={selected} style={{ alignSelf: 'center' }} />
    </LandalPressable>
  );
}
