import { addDays, daysBetween, fromUnixNumber, toUnixNumber } from 'utils/date';

interface Props {
  departureDate?: number;
}

export default function useDateOptions({ departureDate }: Props) {
  const dayBeforeDeparture = addDays(fromUnixNumber(departureDate ? Number(departureDate) * 1000 : 0), -1);
  const departureDay = fromUnixNumber(departureDate ? Number(departureDate) * 1000 : 0);
  const numberOfDaysToCheckout = daysBetween(departureDay, new Date());
  let dayOptions: { date?: number | null; other: boolean }[] = [
    { date: toUnixNumber(departureDay.toUTCString()), other: false },
  ];
  if (numberOfDaysToCheckout > 0) {
    dayOptions = [
      {
        date: toUnixNumber(dayBeforeDeparture.toUTCString()),
        other: false,
      },
      ...dayOptions,
    ];
  }
  if (numberOfDaysToCheckout > 1) {
    dayOptions = [...dayOptions, { date: null, other: true }];
  }

  return dayOptions;
}
