import React, { useMemo } from 'react';
import { View } from 'react-native';
import { useGetFacilities } from '@domains/park/at-the-park/queries/useGetFacilities';
import _ from 'lodash';
import { useFocusEffect } from '@react-navigation/native';
import useMetrics from '@app/metrics/useMetrics';
import StickyHeader from '@app/components/StickyHeader';
import { useIntl } from 'react-intl';
import ScreenScrollContainer from '@app/components/ScreenScrollContainer';
import ListItems, { ListItem } from '@app/components/ListItems2/ListItems';
import { getAPIDate } from 'utils/date';
import useOpeningHours from '@app/hooks/useOpeningHours';
import { useGetPromos } from '@domains/promo/queries/useGetPromos';
import { getDataBySubCat } from '@domains/promo/helpers/getDataBySubCat';
import Loader from '@app/components/Loader';
import { NavScreenProp } from '@app/navigation/stacks/Main';

const AtThePark = ({ navigation, route }: NavScreenProp<'AtThePark'>) => {
  const { formatMessage } = useIntl();
  const { logScreenView } = useMetrics();
  const parkCode = route?.params?.parkCode;
  const category = route?.params?.category || 'onThePark';

  const { data: facilities, isLoading } = useGetFacilities({ parkCodeOverride: parkCode });
  const formattedOpeningHours = useOpeningHours();

  const data = facilities?.filter((f) => f?.categories?.find((c) => c === category));
  const mappedData = _.sortBy(data, 'name');

  const { data: promos } = useGetPromos({ parkCodeOverride: parkCode });
  const reservationOrderItems: ListItem[] = useMemo(() => {
    if (category === 'goodToKnow') return [];

    const items =
      promos
        ?.filter((p) => ['park'].includes(p.hoofdCat || ''))
        .map((p, index) => {
          const categoryData = getDataBySubCat(p.subCat || '', formatMessage, p.imageUrl);
          return {
            id: p.shopId || p.name || index,
            title: p.name || categoryData.name,
            subtitle: categoryData.sub,
            image: categoryData.imageUrl,
            localImage: categoryData.imageUrl ? undefined : categoryData.imageLocal,
            linkTo: p.url,
          };
        }) ?? [];
    return items;
  }, [category, formatMessage, promos]);

  useFocusEffect(
    React.useCallback(() => {
      logScreenView(category === 'onThePark' ? 'AtThePark' : 'GoodToKnow');
    }, [category, logScreenView])
  );

  return (
    <>
      <StickyHeader
        title={formatMessage({
          id: category === 'onThePark' ? 'app.park.facilities' : 'app.park.goodToKnow',
        })}
        showClose
        navigation={navigation}
      />
      {isLoading ? (
        <View style={{ flex: 1, justifyContent: 'center', alignItems: 'center' }}>
          <Loader />
        </View>
      ) : (
        <ScreenScrollContainer topPadding={false}>
          {reservationOrderItems.length > 0 && (
            <ListItems
              title={formatMessage({ id: 'app.promo.reservations' })}
              items={reservationOrderItems ?? []}
              usePlaceholderImage
            />
          )}

          <ListItems
            title={formatMessage({
              id:
                category === 'onThePark'
                  ? 'app.park.facilities.list.title'
                  : 'app.park.goodToKnow.list.title',
            })}
            items={
              mappedData.map((a) => {
                const openingHoursToday = a.openingHours?.filter((o) => o?.date === getAPIDate(new Date()));
                let openingHoursSubtitle = openingHoursToday
                  ?.map((oht) => formattedOpeningHours(oht?.startTime, oht?.endTime, oht?.displayHint))
                  .join('\n');
                return {
                  id: a.id ?? '',
                  title: a.name ?? '',
                  subtitle: category === 'onThePark' ? openingHoursSubtitle : '',
                  image: a.media,
                  rightSubtitle: !!a?.reservationId && formatMessage({ id: 'app.promo.reservations' }),
                  onPress: () => {
                    const facility = facilities?.find((f) => f.id === a.id);
                    navigation.navigate('AtTheParkDetail', { facility, parkCode });
                  },
                };
              }) ?? []
            }
            usePlaceholderImage
            useSkiaImage
          />
        </ScreenScrollContainer>
      )}
    </>
  );
};
export default AtThePark;
