import useCulture from '@app/hooks/useCulture';
import { AxiosError } from 'axios';
import { UseQueryOptions, useMutation } from 'react-query';
import DeviceInfo from 'react-native-device-info';
import { gql, GraphQLClient } from 'graphql-request';
import Config from 'react-native-config';
import { BlockRequest, BlockResponse, BookRequest, BookResponse } from 'apis/_generated-park-api/models';

type Props = {
  blockTimeSlotOptions?: UseQueryOptions<BlockResponse, AxiosError>;
  bookTimeSlotOptions?: UseQueryOptions<BookResponse, AxiosError>;
};

export function useBookTable(props?: Props) {
  const { languageCode } = useCulture();
  const blockTimeSlotMutation = useMutation(async (request: BlockRequest) => {
    request.device_id = DeviceInfo.getUniqueId();
    const graphQLClient = new GraphQLClient(Config.PARK_API_URL);
    const mutation = gql`
      mutation blockTable($request: BlockRequest) {
        blockTable(blockRequest: $request) {
          status
        }
      }
    `;

    const result = await graphQLClient.rawRequest<{ blockTable: BlockResponse }>(mutation, { request });
    return result?.data?.blockTable;
  }, props?.blockTimeSlotOptions);

  const bookTimeSlotMutation = useMutation(async (request: BookRequest) => {
    request.device_id = DeviceInfo.getUniqueId();
    const graphQLClient = new GraphQLClient(Config.PARK_API_URL);
    const mutation = gql`
      mutation bookTable($request: BookRequest){
        bookTable(bookRequest: $request, languageCode: "${languageCode}") {
          status
        }
      }
    `;
    const result = await graphQLClient.rawRequest<{ bookTable: BookResponse }>(mutation, { request });
    return result?.data?.bookTable;
  }, props?.bookTimeSlotOptions);

  const makeBooking = async (blockRequest: BlockRequest, bookRequest: BookRequest) => {
    const blockResult = await blockTimeSlotMutation.mutateAsync(blockRequest);
    if (!blockResult) {
      return null;
    }

    const bookResult = await bookTimeSlotMutation.mutateAsync(bookRequest);
    return { bookResult, blockResult };
  };

  return { makeBooking };
}
