import React, { useCallback, useEffect, useState } from 'react';
import { Alert, Platform, Vibration } from 'react-native';
import useTheme from '@app/hooks/useTheme';
import useMetrics from '@app/metrics/useMetrics';
import { UserContext } from '@app/context/usercontext/UserContextProvider';
import { useGetReservation } from '@domains/reservations/reservation/queries/useGetReservation';
import { getBrand } from 'utils/brand';
import SaltoKS, { OpenDoorEvent } from '../native';
import { SaltoContext, SaltoContextUpdater } from '@app/context/SaltoContextProvider';
import useLogout from '@domains/profile/hooks/useLogout';
import { useBottomSheet } from '@app/navigation/bottom-sheet/BottomSheetProvider';
import { NavigationContainerRef, useNavigation } from '@react-navigation/native';
import { useIntl } from 'react-intl';

interface Props {
  onFailure: () => void;
  onSuccess: () => void;
}

export default function OpenDoorManager({ onFailure, onSuccess }: Props) {
  const theme = useTheme();
  const { logEvent } = useMetrics();
  //const [lockStatus, setLockStatus] = useState('');
  const [processing, setProcessing] = useState(false);
  const [initialized, setInitialized] = useState(false);
  const { logout } = useLogout();
  const { formatMessage } = useIntl();
  const navigation = useNavigation<NavigationContainerRef<any>>();

  const { closeBottomSheet, openBottomSheet } = useBottomSheet();
  const { token, demoMode } = React.useContext(UserContext);
  const { deviceId, generatedId, saltoUserToken } = React.useContext(SaltoContext);
  const { setGeneratedId, setDeviceId } = React.useContext(SaltoContextUpdater);

  const { data: reservation } = useGetReservation({
    brand: getBrand(theme),
    token,
  });

  React.useEffect(() => {
    logEvent('LockKeyOpenStart', {
      lock_type: reservation?.lockType,
      unit_code: reservation?.unitCode,
    });
  }, [logEvent, reservation?.lockType, reservation?.unitCode]);

  const showRetryActivationAlert = useCallback(() => {
    Alert.alert(
      formatMessage({ id: 'app.key.logoutRetryActivation.title' }),
      formatMessage({ id: 'app.key.logoutRetryActivation.text' }),
      [
        {
          text: formatMessage({ id: 'app.general.cancel' }),
          style: 'cancel',
          onPress: () => {
            closeBottomSheet();
          },
        },
        {
          text: formatMessage({ id: 'app.login.logout' }),
          style: 'destructive',
          onPress: async () => {
            await logout();
            closeBottomSheet();
          },
        },
      ]
    );
  }, [closeBottomSheet, formatMessage, logout]);

  const openDoor = useCallback(async () => {
    if (!processing) {
      setProcessing(true);
    }

    const deviceKeys = (await SaltoKS.activateDevice(
      saltoUserToken,
      deviceId,
      generatedId,
      setDeviceId,
      setGeneratedId,
      logEvent
    )) ?? {
      deviceId: '',
      generatedId: '',
      userToken: '',
    };

    if (deviceKeys?.generatedId && deviceKeys?.deviceId && deviceKeys?.userToken) {
      SaltoKS.openDoor(
        deviceKeys.deviceId,
        deviceKeys.generatedId,
        deviceKeys?.userToken,
        logEvent,
        showRetryActivationAlert,
        reservation
      );
    } else {
      logEvent('LockKeyOpenError', {
        lock_type: reservation?.lockType,
        unit_code: reservation?.unitCode,
        reason: 'Required smart lock properties missing. Probably key activation failed.',
      });
      showRetryActivationAlert();
    }
  }, [
    deviceId,
    generatedId,
    logEvent,
    processing,
    reservation,
    saltoUserToken,
    setDeviceId,
    setGeneratedId,
    showRetryActivationAlert,
  ]);

  const onOpenDoorChanged = useCallback(
    (event: OpenDoorEvent) => {
      switch (event.eventName) {
        case 'openDoorEventFound': {
          if (Platform.OS === 'android') {
            Vibration.vibrate(400);
          } else {
            Vibration.vibrate([1]);
          }
          //setLockStatus('Slot gevonden');
          break;
        }
        case 'openDoorEventFailed': {
          SaltoKS.stopListeningForDoorEvents();
          processing && setProcessing(false);
          logEvent('LockKeyOpenError', {
            lock_type: reservation?.lockType,
            unit_code: reservation?.unitCode,
            reason: event.error,
          });
          onFailure();
          break;
        }
        case 'openDoorEventAlreadyRunning': {
          //just ignore
          break;
        }
        case 'openDoorEventSuccess': {
          SaltoKS.stopListeningForDoorEvents();
          processing && setProcessing(false);
          onSuccess();
          break;
        }
        case 'openDoorEventTimeout': {
          openDoor();
          break;
        }
      }
    },
    [logEvent, onFailure, onSuccess, openDoor, processing, reservation?.lockType, reservation?.unitCode]
  );

  useEffect(() => {
    if (demoMode) {
      Alert.alert('Demo modus', 'Deze actie is geblokkeerd in de demo modus'); // TODO: translation
      return;
    }
    if (!initialized) {
      SaltoKS.listenForDoorEvents(onOpenDoorChanged);
      openDoor();
      setInitialized(true);
    }
  }, [demoMode, initialized, onOpenDoorChanged, openDoor]);
  return null;
}
