import useCulture from '@app/hooks/useCulture';
import { gql, GraphQLClient } from 'graphql-request';
import Config from 'react-native-config';
import { UseQueryOptions, useQuery } from 'react-query';
import { Maybe, Reservation, Scalars } from 'apis/_generated-reservation-api/models';
import { UserContext } from '@app/context/usercontext/UserContextProvider';
import { useContext } from 'react';
import { AxiosError } from 'axios';
import { MOCK_RES } from './demo-data';

const key = 'getreservations';

type Props = {
  options?: Omit<UseQueryOptions<Reservation[], AxiosError>, typeof key>;
  token?: string;
  brand: 'LGP' | 'HSN';
  staleTime?: number;
};

export type ReservationMeta = {
  bookingReference?: Maybe<Scalars['String']>;
  combiReference?: Maybe<Scalars['String']>;
  parkCode?: Maybe<Scalars['String']>;
  accommodationCode?: Maybe<Scalars['String']>;
  unitCode?: Maybe<Scalars['String']>;
};

export function useGetReservations({ token, brand, options }: Props) {
  const { languageCode } = useCulture();
  const { demoMode } = useContext(UserContext);

  // Belows ''undefined'' is a patch as we saw literal undefined strings triggering this query.
  // TODO: This should be investigated
  const extendedOptions: typeof options = demoMode
    ? {
        ...options,
        enabled: true,
        staleTime: 0,
        cacheTime: 0,
        initialData: MOCK_RES,
        placeholderData: MOCK_RES,
      }
    : {
        ...options,
        enabled: !!token && token !== 'undefined' && !!brand,
        cacheTime: 1000 * 60 * 60 * 72, // 3 days,
      };

  return useQuery(
    [key, { token, brand, languageCode }],
    async () => {
      if (demoMode) return MOCK_RES;
      const graphQLClient = new GraphQLClient(Config.RESERVATION_API_URL);
      const query = gql`
        query {
          reservations(uuid: "${token}",brand:${brand
        ?.toString()
        .toUpperCase()},languageCode:"${languageCode.toString()}") {
            parkCode
            bookingReference
            combiReference
            accommodationCode
            unitCode
          }
        }`;
      try {
        const result = await graphQLClient.rawRequest<{ reservations: ReservationMeta[]; errors: any[] }>(
          query
        );

        if ((result?.data?.errors?.length || 0) > 0) {
          throw new Error('something went wrong');
        }

        return result?.data?.reservations;
      } catch (ex) {
        throw 'something went wrong';
      }
    },
    extendedOptions
  );
}
