import Icon from '@app/components/Icon';
import Text from '@app/components/Text';
import { UserContext } from '@app/context/usercontext/UserContextProvider';
import useTheme from '@app/hooks/useTheme';
import useMetrics from '@app/metrics/useMetrics';
import { NavScreenProp } from '@app/navigation/stacks/Main';
import { useGetReservation } from '@domains/reservations/reservation/queries/useGetReservation';
import { useFocusEffect } from '@react-navigation/native';
import { BookingStatusType } from 'apis/_generated-reservation-api/models';
import React, { useContext, useEffect, useRef, useState } from 'react';
import { useIntl } from 'react-intl';
import { ActivityIndicator, AppState, View } from 'react-native';
import { getBrand } from 'utils/brand';
import { formatDayWithoutTime, fromUnixNumber } from 'utils/date';
import useAccommodationCodeText from '@app/hooks/useAccommodationCodeText';
import ScreenScrollContainer from '@app/components/ScreenScrollContainer';
import StickyImageHeader, { useStickyImageHeaderDimensions } from '@app/components/StickyImageHeader';
import { useAnimatedScrollHandler, useSharedValue } from 'react-native-reanimated';
import TextContainer from '@app/components/DetailScreen/TextContainer';
import { useGetParks } from '@domains/onboarding/queries/useGetParks';
import ListItems from '@app/components/ListItems2/ListItems';
import { useGetUnitTypes } from '@domains/park/queries/useGetUnitTypes';
import ListPractical from '@domains/start/components/ListPractical';
import usePracticalItems from '@app/hooks/usePracticalItems';
import useChecklist from '@app/hooks/useChecklist';
import Button from '@app/components/Button';
import useKeyStatus from '@domains/locks/hooks/useKeyStatus';
import BottomSheetProvider, { useBottomSheet } from '@app/navigation/bottom-sheet/BottomSheetProvider';
import LockSheet from '@domains/locks/sheets/LockSheet';
import SaltoLoginWebview from '@domains/profile/screens/SaltoLoginWebview';
import { SaltoContext } from '@app/context/SaltoContextProvider';
import ExtrasSlider from '@domains/reservations/extras/components/ExtrasSlider';
import { useIsCurrentReservation } from '../hooks/useIsCurrentReservation';
import useCheckoutChecklist from '@app/hooks/useCheckoutChecklist';
import { useGetPark } from '@domains/park/contact-and-info/queries/useGetPark';
import StickyBottomBar from '@app/navigation/StickyBottomBar';
import ParkHasOwnApp from '@app/components/ParkHasOwnApp';
import useHasOwnApp from '@app/hooks/useHasOwnApp';

interface Props extends NavScreenProp<'Reservation'> {}

export default function Reservation({ route, navigation }: Props) {
  return (
    <BottomSheetProvider>
      <InnerReservation route={route} navigation={navigation} />
    </BottomSheetProvider>
  );
}

function InnerReservation({ route }: Props) {
  const theme = useTheme();
  const { token } = useContext(UserContext);
  const { logScreenView, logEvent } = useMetrics();
  const { formatMessage } = useIntl();
  const appState = useRef(AppState.currentState);
  const bookingReference = route?.params?.bookingReference;
  const { checklist } = useChecklist({ bookingReference });
  const { openBottomSheet } = useBottomSheet();
  const { saltoUserToken } = React.useContext(SaltoContext);
  const [gettingSaltoReady, setGettingSaltoReady] = useState(false);

  useFocusEffect(
    React.useCallback(() => {
      logScreenView('Reservation');
    }, [logScreenView])
  );

  const {
    data: reservation,
    refetch,
    status,
  } = useGetReservation({
    token,
    brand: getBrand(theme),
    bookingReference,
  });

  const { EXPANDED_HEADER_HEIGHT, size } = useStickyImageHeaderDimensions('m');
  const { keyShown, keyReady, keyExpired, accommodationReady } = useKeyStatus({ bookingReference });

  let requestPracticalItems = [{ id: 'contact-reception' }];
  if (reservation?.unitCode) {
    requestPracticalItems = [{ id: 'route-acco' }, ...requestPracticalItems];
  } else {
    requestPracticalItems = [{ id: 'route-park' }, ...requestPracticalItems];
  }
  if (reservation) {
    requestPracticalItems = [{ id: 'departure-time' }, ...requestPracticalItems];
  }
  if (reservation?.holidayStarted) {
    requestPracticalItems = [{ id: 'report-damage' }, ...requestPracticalItems];
  }

  const { checkoutChecklist } = useCheckoutChecklist({ bookingReference });

  const allPracticalItems = usePracticalItems({
    items: requestPracticalItems,
    parkCode: reservation?.parkCode,
    bookingReference,
  });

  const practicalItems = allPracticalItems.filter(
    (item) => !['checkout-now', 'departure-time'].includes(item.id.toString())
  );

  const departureTimeItem = allPracticalItems.find((item) => item.id.toString() === 'departure-time');
  const updatedDepartureTimeItem = departureTimeItem
    ? [
        {
          ...departureTimeItem,
          status: reservation?.expectedDepartureDate ? 'complete' : 'incomplete',
          localImage: undefined,
        },
      ]
    : [];

  const checkoutItems = [...updatedDepartureTimeItem, ...checkoutChecklist];

  const { data: unitTypes } = useGetUnitTypes({ parkCode: reservation?.parkCode });
  const unitType = unitTypes?.find((u) => u.code === reservation?.accommodationCode);

  const { data: park } = useGetPark({ parkCodeOverride: reservation?.parkCode });

  const { data: parks } = useGetParks({});
  const { number, type } = useAccommodationCodeText({ reservation, isDetailPage: true });

  const translationY = useSharedValue(0);
  const scrollHandler = useAnimatedScrollHandler((event) => {
    translationY.value = event.contentOffset.y;
  });

  const isCurrent = useIsCurrentReservation({ bookingReference });

  const { hasOwnApp } = useHasOwnApp(park?.id);

  useEffect(() => {
    logEvent('ReservationShow', {
      bookingReference,
      isCurrent: isCurrent ? 'yes' : 'no',
    });
  }, [bookingReference, isCurrent, logEvent]);

  // Refetch reservation on foreground
  useEffect(() => {
    const subscription = AppState.addEventListener('change', (nextAppState) => {
      if (appState.current.match(/inactive|background/) && nextAppState === 'active') {
        refetch();
      }
      appState.current = nextAppState;
    });

    return () => {
      subscription.remove();
    };
  }, [refetch]);

  if (status === 'loading') {
    return (
      <View style={{ flex: 1, justifyContent: 'center', alignItems: 'center' }}>
        <ActivityIndicator size="large" color={theme.colors.primary} />
      </View>
    );
  }

  const parkName = parks?.find((p) => p.id === reservation?.parkCode)?.name;
  const keyText = keyReady
    ? formatMessage({ id: 'app.key.keyUse' })
    : formatMessage({ id: 'app.key.keyActivate' });

  return (
    <>
      <StickyImageHeader
        scrollOffset={translationY}
        title={parkName}
        titleOverImage={false}
        images={unitType?.images || []}
        size={size}
        withGradient={false}
      >
        {/* no header content, just the pager */}
      </StickyImageHeader>

      <ScreenScrollContainer
        onScroll={scrollHandler}
        animated
        scrollIndicatorInsets={{ bottom: 0 }}
        topPadding={false}
        contentContainerStyle={{ paddingTop: EXPANDED_HEADER_HEIGHT }}
      >
        <TextContainer>
          <Text style={{ flex: 1, lineHeight: 26 }} color="gray">
            {reservation?.arrivalDate && reservation?.departureDate
              ? `${formatDayWithoutTime(
                  fromUnixNumber(reservation.arrivalDate * 1000)
                )} - ${formatDayWithoutTime(
                  fromUnixNumber(reservation.departureDate * 1000),
                  false,
                  false,
                  true
                )}`
              : '-'}
          </Text>
          <Text type="title" size="xl" bold>
            {parkName}
          </Text>
          <View>
            <View
              style={{
                flexDirection: 'row',
                paddingTop: theme.space.l,
              }}
            >
              <Icon
                name="accommodation"
                size="m"
                color="primary"
                style={{
                  marginRight: theme.space.m,
                }}
              />
              <Text
                color="dark"
                style={{
                  flex: 1,
                }}
              >
                {!!number && (
                  <Text color="dark" bold>
                    {number}
                  </Text>
                )}
                <Text color={number ? 'gray' : 'dark'}>{`${number ? '\n' : ''}${type}`}</Text>
              </Text>
            </View>
            <View
              style={{
                flexDirection: 'row',
                paddingTop: theme.space.l,
              }}
            >
              <Icon
                name="reservation-details"
                size="m"
                color="primary"
                style={{
                  marginRight: theme.space.m,
                }}
              />
              <Text color="dark">
                {`${formatMessage({ id: 'app.reservation.reservationNr' })}: ${
                  reservation?.bookingReference
                }`}
              </Text>
            </View>
            <View
              style={{
                flexDirection: 'row',
                paddingTop: theme.space.l,
              }}
            >
              <Icon
                name="travel-group"
                size="m"
                color="primary"
                style={{
                  marginRight: theme.space.m,
                }}
              />
              <Text color="dark">{`${reservation?.totalGuests || 1} ${
                (reservation?.totalGuests ?? 0) > 1
                  ? formatMessage({ id: 'app.forms.result.person_plural' })
                  : formatMessage({ id: 'app.forms.result.person' })
              }`}</Text>
            </View>
          </View>
          {!!keyShown && !hasOwnApp && (
            <Button
              onPress={() => {
                openBottomSheet({
                  component: () => (
                    <>
                      <LockSheet
                        bookingReference={reservation?.bookingReference}
                        keyReady={keyReady}
                        keyExpired={keyExpired}
                        accommodationReady={accommodationReady}
                      />
                      {!!token && !saltoUserToken && (
                        <SaltoLoginWebview
                          onSuccess={async () => {
                            setGettingSaltoReady(false);
                          }}
                          onReady={() => {}}
                          onLoadStart={() => {}}
                          onError={() => {
                            setGettingSaltoReady(false);
                          }}
                        />
                      )}
                    </>
                  ),
                  key: 'lock-key',
                  title: keyReady
                    ? formatMessage({ id: 'app.key.keyUse' })
                    : formatMessage({ id: 'app.key.keyActivate' }),
                });
              }}
              style={{
                backgroundColor: theme.colors.secondary,
                marginTop: theme.space.l,
              }}
              disabled={gettingSaltoReady}
            >
              <Button.Icon name="key" size="s" color={'white'} />
              {gettingSaltoReady ? (
                <Button.ActivityIndicator style={{ marginLeft: theme.space.s }} />
              ) : (
                <Button.Text color="white">{keyText}</Button.Text>
              )}
            </Button>
          )}
        </TextContainer>
        {!hasOwnApp && (
          <>
            {reservation?.status === BookingStatusType.Arrived && (
              <ListItems
                title={formatMessage({ id: 'app.reservation.checkoutChecklist' })}
                items={checkoutItems}
              />
            )}
            <ListItems title={formatMessage({ id: 'app.reservation.checkinChecklist' })} items={checklist} />
            {reservation?.status !== BookingStatusType.Arrived && (
              <ListItems
                title={formatMessage({ id: 'app.reservation.checkoutChecklist' })}
                items={checkoutItems}
              />
            )}

            {!!token && !!reservation && !!reservation?.featuresEnabled?.extras && (
              <ExtrasSlider showShowAll bookingReference={bookingReference} />
            )}
            <ListPractical items={practicalItems} />
          </>
        )}
      </ScreenScrollContainer>
      {!!hasOwnApp && (
        <StickyBottomBar
          style={{
            paddingVertical: theme.space.l,
            paddingHorizontal: theme.space.m,
            justifyContent: 'center',
          }}
          autoHeight
          extraHeight={theme.space.l}
        >
          <ParkHasOwnApp parkCode={reservation?.parkCode} />
        </StickyBottomBar>
      )}
    </>
  );
}
