import useCulture from '@app/hooks/useCulture';
import { ActivityBookRequest, ActivityBookResponse } from 'apis/_generated-park-api/models';
import { AxiosError } from 'axios';
import { gql, GraphQLClient } from 'graphql-request';
import Config from 'react-native-config';
import { UseQueryOptions, useMutation } from 'react-query';

type Props = {
  options?: UseQueryOptions<ActivityBookResponse, AxiosError>;
};

export function useBookActivity(props?: Props) {
  const { reserveLanguageCode } = useCulture();
  const bookingMutation = useMutation(async (request: ActivityBookRequest) => {
    const graphQLClient = new GraphQLClient(Config.PARK_API_URL);
    const mutation = gql`
      mutation bookFacility($request: ActivityBookRequest){
        bookActivity(bookRequest: $request, languageCode: ${reserveLanguageCode}){
          status
          messages
          bookingId
        }
      }
    `;
    const result = await graphQLClient.rawRequest<{ bookActivity: ActivityBookResponse }>(mutation, {
      request,
    });
    return result?.data?.bookActivity;
  }, props?.options);

  const postBooking = async (request: ActivityBookRequest) => {
    const bookingResult = await bookingMutation.mutateAsync(request);
    return bookingResult;
  };

  return { postBooking };
}
