import React, { useContext } from 'react';
import { View } from 'react-native';
import useTheme from '@app/hooks/useTheme';
import { useGetReservation } from '../../reservations/reservation/queries/useGetReservation';
import { UserContext } from '@app/context/usercontext/UserContextProvider';
import { getBrand } from 'utils/brand';
import Text from '@app/components/Text';
import { useGetReservationPark } from '@domains/park/contact-and-info/queries/useGetReservationPark';
import useAccommodationCodeText from '@app/hooks/useAccommodationCodeText';
import MiniButton from '@app/components/MiniButton';
import Animated, { interpolate, useAnimatedStyle } from 'react-native-reanimated';
import usePracticalItems from '@app/hooks/usePracticalItems';
import { useIntl } from 'react-intl';

interface Props {
  scrollOffset: Animated.SharedValue<number>;
}
export default function HeaderContent({ scrollOffset }: Props) {
  const theme = useTheme();
  const { token } = useContext(UserContext);
  const { formatMessage } = useIntl();
  const { data: reservationPark } = useGetReservationPark();

  const brand = getBrand(theme);
  const { data: reservation } = useGetReservation({
    brand,
    token,
  });

  const openRouteToPark = usePracticalItems({
    items: [{ id: 'route-park' }],
    parkCode: reservation?.parkCode,
  })[0]?.onPress as () => void;

  const openRouteToAcco = usePracticalItems({
    items: [{ id: 'route-acco' }],
    parkCode: reservation?.parkCode,
  })[0]?.onPress as () => void;

  const { number, type, hasNumber } = useAccommodationCodeText({ reservation });

  const headerContentStyles = useAnimatedStyle(() => {
    return {
      opacity:
        scrollOffset.value > 0
          ? interpolate(scrollOffset.value, [30, 70], [1, 0])
          : interpolate(scrollOffset.value, [-18, -50], [1, 0]),
    };
  });

  return (
    <View style={{ width: '100%' }}>
      <Animated.View
        style={[{ position: 'absolute', bottom: 36 + theme.space.l, width: '100%' }, headerContentStyles]}
      >
        {reservation ? (
          <View>
            <Text color="white">{type}</Text>
            <View style={{ flexDirection: 'row' }}>
              <Text type="title" size="xl" color="white" bold>
                {reservationPark?.name}{' '}
              </Text>
              {!hasNumber && <MiniButton icon="navigate" onPress={openRouteToPark} style={{ top: 4 }} />}
            </View>
            {hasNumber && (
              <View style={{ flexDirection: 'row', alignItems: 'center' }}>
                <Text type="title" size="xl" color="white" bold style={{ marginRight: theme.space.s }}>
                  {number}
                </Text>
                <MiniButton
                  icon="navigate"
                  onPress={reservation?.combiReference ? openRouteToPark : openRouteToAcco}
                />
              </View>
            )}
          </View>
        ) : (
          <View style={{ top: -theme.space.xl }}>
            {brand === 'HSN' ? (
              <>
                <Text type="title" size="l" color="white" style={{ textAlign: 'center' }} useParser>
                  {formatMessage({ id: 'app.start.intro.title' }).split('\n')[0]}
                </Text>
                <Text type="title" size="xxl" color="white" style={{ textAlign: 'center' }} useParser>
                  {formatMessage({ id: 'app.start.intro.title' }).split('\n')[1]}
                </Text>
              </>
            ) : (
              <Text type="title" size="xxl" color="white" style={{ textAlign: 'center' }} useParser>
                {formatMessage({ id: 'app.start.intro.title' })}
              </Text>
            )}
          </View>
        )}
      </Animated.View>
    </View>
  );
}
