import React from 'react';
import { useAnimatedScrollHandler, useSharedValue } from 'react-native-reanimated';
import { Linking, TouchableOpacity, View } from 'react-native';
import useTheme from '@app/hooks/useTheme';
import { useGetPOI } from '../queries/useGetPoi';
import MoreInfo from '@app/components/DetailScreen/MoreInfo';
import TitleAndDescription from '@app/components/DetailScreen/TitleAndDescription';
import ActivityBadge from '@app/components/ActivityBadge';
import Text from '@app/components/Text';
import { BASELINE_INSET, spaceDefaults } from '@app/theme/partials/sizes';
import useMetrics from '@app/metrics/useMetrics';
import { useFocusEffect } from '@react-navigation/native';
import { ActivityDate } from 'apis/_generated-park-api/models';
import ScreenScrollContainer from '@app/components/ScreenScrollContainer';
import StickyImageHeader, { useStickyImageHeaderDimensions } from '@app/components/StickyImageHeader';
import { useIntl } from 'react-intl';
import StickyBottomBar from '@app/navigation/StickyBottomBar';
import Button from '@app/components/Button';
import { NavScreenProp } from '@app/navigation/stacks/park/InTheRegion';
import Loader from '@app/components/Loader';
import Icon from '@app/components/Icon';
import { LandalPressable } from '@app/components/LandalPressable';

export default function InTheRegionDetail({ navigation, route }: NavScreenProp<'InTheRegionDetail'>) {
  const theme = useTheme();
  const { logScreenView, logEvent } = useMetrics();
  const parkCode = route?.params?.parkCode;

  const { data, isLoading } = useGetPOI({ parkCode });
  const { formatMessage } = useIntl();
  const { EXPANDED_HEADER_HEIGHT, size } = useStickyImageHeaderDimensions('m');

  const translationY = useSharedValue(0);
  const scrollHandler = useAnimatedScrollHandler((event) => {
    translationY.value = event.contentOffset.y;
  });

  const id = route?.params?.id;
  const poi = data?.find((d) => d.id === id);

  const showBottomBar = !!poi?.ticketsWebsite;
  const showBadge = !!poi?.ticketsWebsite;

  useFocusEffect(
    React.useCallback(() => {
      if (id) {
        logScreenView('InTheRegionDetail', [id], { location: poi?.name ?? '' });
      }
    }, [id, logScreenView, poi])
  );

  if (!data || !id || !poi || poi.isEvent) {
    return null;
  }
  if (isLoading) {
    return (
      <View style={{ flex: 1, justifyContent: 'center', alignItems: 'center' }}>
        <Loader />
      </View>
    );
  }

  const buyTickets = () => {
    if (!poi.ticketsWebsite) return;
    logEvent('POITicketLead');
    Linking.openURL(poi.ticketsWebsite);
  };

  return (
    <>
      <StickyImageHeader
        scrollOffset={translationY}
        title={poi?.name}
        images={[poi?.media ?? '']}
        size={size}
      />
      <ScreenScrollContainer
        onScroll={scrollHandler}
        contentContainerStyle={{ paddingTop: EXPANDED_HEADER_HEIGHT }}
        forceBottomInset={!!showBottomBar}
        animated
      >
        {poi.isEvent && (
          <ActivityBadge
            compact={false}
            dateObject={poi.activities?.[0]?.dates ? (poi.activities[0].dates as ActivityDate[]) : []}
            style={{ marginBottom: 8 }}
          />
        )}
        {showBadge && (
          <View
            style={{
              marginHorizontal: -BASELINE_INSET - theme.space.m,
              paddingHorizontal: theme.space.l,
              paddingTop: theme.space.xl,
              backgroundColor: theme.colors.white,
            }}
          >
            <LandalPressable onPress={buyTickets}>
              <View
                style={{
                  borderWidth: 1,
                  borderRadius: 8,
                  borderColor: theme.colors.primary,
                  flexDirection: 'row',
                  alignItems: 'center',
                  paddingHorizontal: theme.space.s,
                }}
              >
                <Icon name="ticket" color="primary" style={{ marginHorizontal: theme.space.s }} />
                <Text
                  color="primary"
                  size="s"
                  bold
                  style={{
                    marginVertical: theme.space.s,
                    flex: 1,
                  }}
                >
                  {formatMessage({ id: 'app.poi.components.ticketshop.infoBox' })}
                </Text>
              </View>
            </LandalPressable>
          </View>
        )}
        <TitleAndDescription name={poi?.name} description={poi?.description}>
          <MoreInfo
            street={poi?.address?.street ?? ''}
            streetNumber={poi?.address?.streetNumber}
            zipcode={poi?.address?.zipcode ?? ''}
            city={poi?.address?.city ?? ''}
            title={formatMessage({ id: 'app.components.detail.moreinfo.title' })}
            website={poi?.website}
          />
        </TitleAndDescription>

        {poi.isLocationWithEvents && (
          <View
            style={{
              backgroundColor: theme.colors.canvas,
              padding: spaceDefaults.l,
              marginBottom: spaceDefaults.xl,
              marginLeft: -theme.space.l,
              marginRight: -theme.space.l,
            }}
          >
            {poi.activities?.map((activity) => {
              return (
                <TouchableOpacity
                  onPress={() => {
                    !!activity?.id && navigation.navigate('InTheRegionEventDetail', { id: activity.id });
                  }}
                >
                  <View
                    style={{
                      borderTopWidth: 1,
                      borderColor: theme.colors.canvas,
                      backgroundColor: 'white',
                      width: '100%',
                      flexDirection: 'row',
                      paddingLeft: theme.space.m,
                      paddingRight: theme.space.m,
                      paddingTop: spaceDefaults.m,
                      paddingBottom: spaceDefaults.s,
                    }}
                  >
                    {activity?.dates?.[0] && (
                      <ActivityBadge
                        compact={true}
                        dateObject={activity.dates ? (activity.dates as ActivityDate[]) : []}
                        style={{ marginBottom: 8, alignSelf: 'center' }}
                      />
                    )}
                    <View style={{ flexDirection: 'column', flex: 1, marginLeft: spaceDefaults.l }}>
                      <Text size="s" color="gray">
                        {activity?.name}
                      </Text>
                    </View>
                  </View>
                </TouchableOpacity>
              );
            })}
          </View>
        )}
      </ScreenScrollContainer>
      {showBottomBar && (
        <StickyBottomBar style={{ padding: theme.space.l, justifyContent: 'center' }}>
          <View style={{ flex: 1 }}>
            <Button
              onPress={buyTickets}
              style={{
                backgroundColor: theme.colors.secondary,
              }}
            >
              <Button.Icon name="ticket" size="s" color={'white'} />
              <Button.Text color="white">
                {formatMessage({ id: 'app.poi.components.ticketshop.badge' })}
              </Button.Text>
            </Button>
          </View>
        </StickyBottomBar>
      )}
    </>
  );
}
