import { Timeslot } from 'apis/_generated-park-api/models';
import { AxiosError } from 'axios';
import { gql, GraphQLClient } from 'graphql-request';
import Config from 'react-native-config';
import { UseQueryOptions, useQuery } from 'react-query';
import { addDays, getAPIDate } from 'utils/date';

export interface FacilityBookingTimeslot extends Timeslot {
  amountOfAssets: number;
}

const key = `getfacilitybookingavailability`;
type FacilitybookingAvailabilityDataProps = {
  options?: Omit<UseQueryOptions<FacilityBookingTimeslot[], AxiosError>, typeof key>;
  reservationId: string;
  date: Date;
};

export default function useGetFacilityBookingAvailability(props: FacilitybookingAvailabilityDataProps) {
  const { reservationId, date, options } = props;

  const dateFrom = getAPIDate(date);
  const dateTill = getAPIDate(addDays(date, 1));

  const extendedOptions: typeof options = {
    ...options,
    enabled: !!date,
  };

  return useQuery(
    [key, { reservationId, date }],
    async () => {
      const graphQLClient = new GraphQLClient(Config.PARK_API_URL);
      const query = gql`
        query facilityBookingAvailability {
          facilityBookingAvailability(reservationId: "${reservationId}", dateFrom: "${dateFrom}", dateTill: "${dateTill}") {
            timeslot
            amountOfAssets
          }
        }
      `;
      const result = await graphQLClient.rawRequest<{
        facilityBookingAvailability: FacilityBookingTimeslot[];
      }>(query);

      const availability = result?.data?.facilityBookingAvailability;
      if (!availability) {
        return [];
      }

      return availability;
    },
    extendedOptions
  );
}
