import useCulture from '@app/hooks/useCulture';
import { RestaurantInfo } from 'apis/_generated-park-api/models';
import { AxiosError } from 'axios';
import { gql, GraphQLClient } from 'graphql-request';
import Config from 'react-native-config';
import { UseQueryOptions, useQuery } from 'react-query';

const key = `getrestaurantinfo`;
interface Props {
  options?: Omit<UseQueryOptions<RestaurantInfo, AxiosError>, typeof key>;
  restaurantId: string;
}

export function useGetRestaurantInfo(props: Props) {
  const { cultureCode } = useCulture();
  const { restaurantId, options } = props;

  return useQuery(
    [key, { restaurantId, cultureCode }],
    async () => {
      const graphQLClient = new GraphQLClient(Config.PARK_API_URL);
      const query = gql`
        query restaurantInfo {
          restaurantInfo(restaurantId: "${restaurantId}", cultureCode: "${cultureCode}") {
            maxGuests
          }
        }
      `;
      const result = await graphQLClient.rawRequest<{ restaurantInfo: RestaurantInfo }>(query);
      return result?.data?.restaurantInfo;
    },
    options
  );
}
