import { UserContext } from '@app/context/usercontext/UserContextProvider';
import { useGetReservation } from '@domains/reservations/reservation/queries/useGetReservation';
import { useContext, useRef, useEffect } from 'react';
import { AppState, AppStateStatus } from 'react-native';
import { getBrand } from 'utils/brand';
import useTheme from './useTheme';

export default function useRefetchActiveReservationOnAppStateChange() {
  const { token } = useContext(UserContext);
  const appState = useRef(AppState.currentState);
  const theme = useTheme();
  const { refetch } = useGetReservation({
    brand: getBrand(theme),
    token,
  });

  // This effect is triggered everytime the app is pulled from background, and refetches the reservation
  useEffect(() => {
    const _handleAppStateChange = (nextAppState: AppStateStatus) => {
      if (appState.current.match(/inactive|background/) && nextAppState === 'active') {
        refetch();
      }
      appState.current = nextAppState;
    };

    const listener = AppState.addEventListener('change', _handleAppStateChange);

    return () => {
      listener.remove();
    };
  }, [refetch]);
}
