import React from 'react';
import { createNativeStackNavigator, NativeStackNavigationProp } from '@react-navigation/native-stack';
import Activities from '@domains/park/activities/screens/Activities';
import ActivitiesDetail from '@domains/park/activities/screens/ActivitiesDetail';
import ActivitySignup from '@domains/park/activities/screens/ActivitySignup';
import ActivityBookingSuccessful from '@domains/park/activities/screens/ActivityBookingSuccessful';
import { Activity } from 'apis/_generated-park-api/models';
import { RouteProp } from '@react-navigation/native';

// Types of params passed to route via navigation.navigate('SCREEN_NAME', params)
export type ActivitiesRoutes = {
  Activities: undefined;
  ActivitiesDetail: { id: string };
  ActivitySignup: { activity: Activity };
  ActivityBookingSuccessful: {
    activity: Activity;
    amount: number;
  };
};

type RouteNames = keyof ActivitiesRoutes;
interface NavScreenProp<T extends RouteNames> {
  navigation: NativeStackNavigationProp<ActivitiesRoutes, T>;
  route: RouteProp<ActivitiesRoutes, T>;
}

export const useActivitiesScreens = (stack: any) => {
  return (
    <>
      <stack.Screen name="Activities" component={Activities} />
      <stack.Screen name="ActivitiesDetail" component={ActivitiesDetail} />
      <stack.Screen name="ActivitySignup" component={ActivitySignup} />
      <stack.Screen name="ActivityBookingSuccessful" component={ActivityBookingSuccessful} />
    </>
  );
};

const ActivitiesStack = createNativeStackNavigator<ActivitiesRoutes>();
export default function ActivitiesNav({ navigation }: NavScreenProp<'Activities'>) {
  const screens = useActivitiesScreens(ActivitiesStack);

  return (
    <ActivitiesStack.Navigator
      backBehavior="history"
      screenOptions={{
        headerShown: false,
        orientation: 'portrait_up',
        presentation: 'fullScreenModal',
        animation: 'default',
      }}
    >
      {screens}
    </ActivitiesStack.Navigator>
  );
}
