import React from 'react';
import { View } from 'react-native';
import useTheme from '@app/hooks/useTheme';
import Text from '@app/components/Text';
import { AllColors } from '@app/theme/partials/general';

interface HandleProps {
  title?: string;
  closeComponent?: any;
  height?: number;
  backgroundColor: AllColors;
}

const BottomSheetHandle: React.FC<HandleProps> = ({ height, title, closeComponent, backgroundColor }) => {
  const theme = useTheme();

  return (
    <View
      style={{
        alignContent: 'center',
        alignItems: 'center',
        justifyContent: 'center',
        paddingVertical: 14,
        borderBottomColor: backgroundColor === 'white' ? theme.colors.canvas : theme.colors.light,
        borderBottomWidth: 1,
        height,
      }}
    >
      <View
        style={{
          position: 'absolute',
          width: 36,
          height: 4,
          top: 6,
          borderRadius: 20,
          backgroundColor: theme.colors.light,
        }}
      />
      <View
        style={{
          flexDirection: 'row',
          width: '100%',
          justifyContent: 'center',
          paddingTop: theme.space.s,
        }}
      >
        {!!title && (
          <Text type="title" size="l" bold>
            {title}
          </Text>
        )}
        {closeComponent}
      </View>
    </View>
  );
};

export default BottomSheetHandle;
