import { UserContext } from '@app/context/usercontext/UserContextProvider';
import useTheme from '@app/hooks/useTheme';
import { UpdateArrivalTimeRequest, UpdateArrivalTimeResponse } from 'apis/_generated-reservation-api/models';

import { AxiosError } from 'axios';
import { gql, GraphQLClient } from 'graphql-request';
import { useContext } from 'react';
import Config from 'react-native-config';
import { useMutation, UseQueryOptions } from 'react-query';
import { getBrand } from 'utils/brand';
import { useGetReservation } from './useGetReservation';

type Props = {
  options?: UseQueryOptions<UpdateArrivalTimeResponse, AxiosError>;
  bookingReference?: string;
};

export function useMutateExpectedArrivalTime(props?: Props) {
  const theme = useTheme();
  const { token } = useContext(UserContext);
  const { data: reservation } = useGetReservation({
    brand: getBrand(theme),
    token,
    bookingReference: props?.bookingReference,
  });

  const performUpdateExpectedArrivalTime = useMutation(async (request: UpdateArrivalTimeRequest) => {
    const graphQLClient = new GraphQLClient(Config.RESERVATION_API_URL);

    if (!reservation?.bookingReference) {
      throw 'no active reservation found';
    }

    const query = gql`
      mutation {
         updateArrivalTime(updateArrivalTimeRequest: { uid: "${token}", bookingReference: "${reservation?.bookingReference}", arrivalTime: ${request.arrivalTime} }) {
          success
          message
        }
      }
    `;

    const response = await graphQLClient.rawRequest<{
      updateArrivalTime: UpdateArrivalTimeResponse;
    }>(query, {
      request,
    });
    return response?.data?.updateArrivalTime;
  }, props?.options);

  const updateExpectedArrivalTime = async (request: UpdateArrivalTimeRequest) => {
    const result = await performUpdateExpectedArrivalTime.mutateAsync(request);
    return result;
  };

  return { updateExpectedArrivalTime };
}
