import React, { useCallback, useContext, useEffect, useMemo, useRef } from 'react';
import useTheme from '@app/hooks/useTheme';
import { getBrand } from 'utils/brand';
import { ScrollView, useWindowDimensions, View } from 'react-native';
import { useIntl } from 'react-intl';
import { NavigationContainerRef, useFocusEffect, useNavigation, useRoute } from '@react-navigation/native';
import { useGetPark } from '@domains/park/contact-and-info/queries/useGetPark';
import ScreenScrollContainer from '@app/components/ScreenScrollContainer';
import { Brand } from 'apis/_generated-reservation-api/models';
import Pager from '@app/components/Pager';
import ListItems, { ListItem } from '@app/components/ListItems2/ListItems';
import { BASELINE_INSET } from '@app/theme/partials/sizes';
import PoiHighlights from '@domains/park/in-the-region/components/PoiHighlights';
import { useGetCountryItems } from '@domains/reservations/reservation/hooks/useGetCountryItems';
import StickyHeader, { RelativeHeaderArea } from '@app/components/StickyHeader';
import MiniButton from '@app/components/MiniButton';
import { useBottomSheet } from '@app/navigation/bottom-sheet/BottomSheetProvider';
import SelectParkModal from '@domains/onboarding/screens/SelectParkModal';
import Animated, {
  FadeInDown,
  FadeOutDown,
  useAnimatedScrollHandler,
  useSharedValue,
} from 'react-native-reanimated';
import ListPractical from '../components/ListPractical';
import usePracticalItems from '@app/hooks/usePracticalItems';
import ScreenContainer from '@app/components/ScreenContainer';
import { UserContext } from '@app/context/usercontext/UserContextProvider';
import { useGetReservation } from '@domains/reservations/reservation/queries/useGetReservation';
import useSurvicate from '@app/hooks/useSurvicate';
import { AllRoutes } from '@app/navigation/stacks/Main';
import useGoodToKnowItems from '@app/hooks/useGoodToKnowItems';

const ASPECT_RATIO = 3 / 2;

export default function ParkScreen() {
  const navigation = useNavigation<NavigationContainerRef<AllRoutes>>();
  const route = useRoute();

  const theme = useTheme();
  const { data: park, isLoading } = useGetPark();
  const { formatMessage } = useIntl();
  const { width: screenWidth } = useWindowDimensions();
  const countryItems = useGetCountryItems();
  const { openBottomSheet } = useBottomSheet();
  const countryName = countryItems.find((c) => c.value === park?.address?.country)?.label;

  const { token } = useContext(UserContext);
  const brand = getBrand(theme);
  const { data: reservation } = useGetReservation({
    brand,
    token,
  });

  const scrollRef = useRef<ScrollView>(null);
  const isInFocus = useRef<boolean>();

  const hasPark = !!park?.id;

  const parkName = park?.name ? park?.name : brand === Brand.Lgp ? 'Landal' : 'Hof van Saksen';

  const parkLocation = useMemo(() => {
    let loc = [];

    if (park?.address?.city) {
      loc.push(park?.address?.city);
    }
    if (park?.address?.region) {
      loc.push(park?.address?.region);
    }
    if (countryName) {
      loc.push(countryName);
    }

    return loc.join(', ');
  }, [countryName, park?.address?.city, park?.address?.region]);

  let pagerImages = park?.images ?? [];

  const { hasGoodToKnowItems } = useGoodToKnowItems(park?.id);

  // decide which practical items to show
  const practicalItemsToRequest = useMemo(() => {
    let items: ListItem[] = [{ id: 'route-park' }, { id: 'parkmap' }];
    if (hasGoodToKnowItems) {
      items = [...items, { id: 'good-to-know' }];
    }
    items = [...items, { id: 'contact-reception' }];
    return items;
  }, [hasGoodToKnowItems]);

  const practicalItems = usePracticalItems({
    items: practicalItemsToRequest,
    parkCode: park?.id,
    bookingReference: park?.id === reservation?.parkCode ? reservation?.bookingReference : undefined,
  });

  useEffect(() => {
    scrollRef.current?.scrollTo({ y: 0, animated: true });
  }, [park?.id]);

  const openSelectPark = () => {
    openBottomSheet({
      component: () => <SelectParkModal navigation={navigation} route={route} />,
      fullHeight: true,
      title: formatMessage({ id: 'app.park.select' }),
      backgroundColor: 'canvas',
      delay: 400,
      componentContainsScroll: true,
    });
  };

  const scrollOffset = useSharedValue(0);
  const scrollHandler = useAnimatedScrollHandler((event) => {
    scrollOffset.value = event.contentOffset.y;
  });

  const { invokeEvent } = useSurvicate();

  useFocusEffect(
    useCallback(() => {
      isInFocus.current = true;
      if (park?.id) {
        invokeEvent('park_' + park.id);
      }

      return () => {
        isInFocus.current = false;
      };
    }, [invokeEvent, park?.id])
  );
  useEffect(() => {
    return navigation.addListener('tabPress', (e) => {
      if (isInFocus.current === true) {
        scrollRef.current?.scrollTo({ y: 0, animated: true });
      }
    });
  }, [navigation]);

  return (
    <ScreenContainer key={parkName || '' + parkLocation || ''}>
      <StickyHeader
        title={parkName}
        subTitle={parkLocation}
        bigTitle
        right={
          hasPark
            ? () => theme.type !== 'hofvansaksen' && <MiniButton icon="switch" onPress={openSelectPark} />
            : undefined
        }
        scrollOffset={scrollOffset}
        main
      />
      <ScreenScrollContainer
        animated
        onScroll={scrollHandler}
        scrollEventThrottle={1}
        topPadding={false}
        forceBottomInset
        scrollRef={scrollRef}
      >
        <RelativeHeaderArea additionalHeight={!!parkLocation} />
        {hasPark ? (
          <Animated.View
            key={park?.id + isLoading.toString()}
            entering={FadeInDown.duration(600).springify().stiffness(40).damping(12)}
            exiting={FadeOutDown.duration(600)}
          >
            <View
              style={{
                marginHorizontal: -BASELINE_INSET,
              }}
            >
              <View
                style={{
                  alignItems: 'center',
                  borderTopLeftRadius: 12,
                  borderTopRightRadius: 12,
                  overflow: 'hidden',
                  height: (screenWidth - theme.space.l) / ASPECT_RATIO,
                  backgroundColor: theme.colors.dark,
                }}
              >
                <Pager height={(screenWidth - theme.space.l) / ASPECT_RATIO} images={pagerImages} />
              </View>
              <ListItems
                items={[
                  {
                    id: 'foodAndDrinks',
                    title: formatMessage({
                      id: 'app.park.foodAndDrinks',
                    }),
                    localImage:
                      theme.type === 'hofvansaksen'
                        ? require('@assets/images/hsn/etenendrinken.png')
                        : require('@assets/images/etenendrinken.jpg'),
                    subtitle: formatMessage({
                      id: 'app.park.foodAndDrinks.subtitle',
                    }),
                    onPress: () => {
                      navigation.navigate('FoodAndDrinks', { parkCode: park?.id });
                    },
                  },
                  {
                    id: 'facilities',
                    title: formatMessage({ id: 'app.park.facilities' }),
                    localImage:
                      theme.type === 'hofvansaksen'
                        ? require('@assets/images/hsn/ophetresort.png')
                        : require('@assets/images/ophetpark.jpg'),
                    subtitle: formatMessage({ id: 'app.park.facilities.subtitle' }),
                    onPress: () => {
                      navigation.navigate('AtThePark', { parkCode: park?.id });
                    },
                  },
                  {
                    id: 'funAndEntertainment',
                    title: formatMessage({ id: 'app.park.funAndEntertainment' }),
                    localImage:
                      theme.type === 'hofvansaksen'
                        ? require('@assets/images/hsn/funenentertainment.png')
                        : require('@assets/images/funenentertainment.png'),
                    subtitle: formatMessage({ id: 'app.park.funAndEntertainment.subtitle' }),
                    onPress: () => {
                      navigation.navigate('Activities');
                    },
                  },
                ]}
                style={{
                  marginTop: 0,
                }}
                useBaseLineInset={false}
                useTopRadius={false}
              />
            </View>
            <PoiHighlights parkCode={park?.id} />
            <ListPractical items={practicalItems} />
          </Animated.View>
        ) : (
          <ListItems
            items={[
              {
                id: 'select-park',
                title: formatMessage({ id: 'app.park.select' }),
                //subtitle: '',
                icon: 'switch',
                onPress: openSelectPark,
              },
            ]}
            style={{ marginTop: 0 }}
          />
        )}
      </ScreenScrollContainer>
    </ScreenContainer>
  );
}
