import { UserContext } from '@app/context/usercontext/UserContextProvider';
import useTheme from '@app/hooks/useTheme';
import { useGetReservation } from '@domains/reservations/reservation/queries/useGetReservation';
import React, { useCallback, useContext, useEffect, useRef, useState } from 'react';
import { getBrand } from 'utils/brand';
import useMetrics from '@app/metrics/useMetrics';
import SaltoKS from '@domains/locks/native';
import { SaltoContext, SaltoContextUpdater } from '@app/context/SaltoContextProvider';
import AsyncStorage from '@react-native-async-storage/async-storage';
import { useFocusEffect } from '@react-navigation/native';
import BluetoothStateManager from 'react-native-bluetooth-state-manager';
import { SmartlockAvailability } from 'apis/_generated-reservation-api/models';
import { Platform } from 'react-native';

interface Props {
  bookingReference?: string;
}

export default function useKeyStatus({ bookingReference }: Props) {
  const theme = useTheme();
  const { token, demoMode } = useContext(UserContext);
  const { logEvent } = useMetrics();
  const { deviceId, generatedId, saltoUserToken } = React.useContext(SaltoContext);
  const { setGeneratedId, setDeviceId } = React.useContext(SaltoContextUpdater);
  const [fetchedInitialBluetoothState, setFetchedInitialBluetoothState] = useState(false);
  const [doorSuccessfullyOpenedBefore, setDoorSuccessfullyOpenedBefore] = useState(false);
  const [gettingSaltoReady, setGettingSaltoReady] = useState(true);

  // eslint-disable-next-line no-undef
  let timer = useRef<NodeJS.Timeout | null>(null);

  const activateDevice = useCallback(async () => {
    if (demoMode) {
      return;
    }
    await SaltoKS.activateDevice(
      saltoUserToken,
      deviceId,
      generatedId,
      setDeviceId,
      setGeneratedId,
      logEvent
    );
  }, [demoMode, deviceId, generatedId, logEvent, saltoUserToken, setDeviceId, setGeneratedId]);

  useEffect(() => {
    // TODO: improve this. This should not be a timeout, but a decent check to complete the Salto registration.
    timer.current = setTimeout(() => {
      setGettingSaltoReady?.(false);
      return () => !!timer?.current && clearTimeout(timer.current);
    }, 10000);
  }, []);

  useEffect(() => {
    if (!gettingSaltoReady && !!timer?.current) {
      clearTimeout(timer.current);
    }
  }, [gettingSaltoReady]);

  useEffect(() => {
    if (saltoUserToken) {
      setGettingSaltoReady(false);
      activateDevice();
    }
  }, [activateDevice, saltoUserToken]);

  const brand = getBrand(theme);
  const { data: reservation } = useGetReservation({
    brand,
    token,
    bookingReference,
  });

  // Get the door opened status, and write to state
  const getOpenStatus = useCallback(async () => {
    if (!bookingReference) {
      return true;
    }
    const storedDoorSuccessfullyOpenedBefore =
      (await AsyncStorage.getItem(bookingReference + '_opened')) === 'true';
    setDoorSuccessfullyOpenedBefore(storedDoorSuccessfullyOpenedBefore);
  }, [bookingReference]);

  useEffect(() => {
    getOpenStatus();
  }, [getOpenStatus]);

  const dateNow = Math.round(new Date().getTime() / 1000);

  const keyExpired = !!(
    reservation?.accommodationLeaveDateTime && dateNow > reservation.accommodationLeaveDateTime
  );
  const accommodationReady = !!(
    reservation?.accommodationReadyDateTime && dateNow > reservation.accommodationReadyDateTime
  );
  const keyShown =
    doorSuccessfullyOpenedBefore ||
    (!!token &&
      !!reservation &&
      reservation.smartlockAvailability !== SmartlockAvailability.None &&
      !!reservation.lockType);
  const keyReady = !!(doorSuccessfullyOpenedBefore || (!!keyShown && accommodationReady && !keyExpired));

  useFocusEffect(
    useCallback(() => {
      if (keyShown && !fetchedInitialBluetoothState) {
        // This function is called because of an issue with BluetoothStateManager, where the first getState might result in an 'unknown' state.
        const getBluetoothState = async () => {
          await BluetoothStateManager.getState();
        };
        Platform.OS !== 'web' && getBluetoothState();
        setFetchedInitialBluetoothState(true);
      }
    }, [fetchedInitialBluetoothState, keyShown])
  );

  return {
    keyExpired,
    keyShown,
    keyReady,
    accommodationReady,
  };
}
