import { UserContext } from '@app/context/usercontext/UserContextProvider';
import React, { useContext } from 'react';
import Config from 'react-native-config';
import { WebView } from 'react-native-webview';

const VISIBLE_WEBVIEWS = __DEV__ && false;

interface Props {
  reference?: React.RefObject<WebView<{}>>;
  onError: () => void;
  onSuccess: () => void;
  onLoadStart?: () => void;
}

const SAPLogoutWebview = ({ reference, onError, onSuccess, onLoadStart }: Props) => {
  const { token, demoMode } = useContext(UserContext);

  if (!token || demoMode) {
    onSuccess();
  }

  return (
    <WebView
      source={{ uri: Config.SAP_CDC_LOGOUT_URI }}
      style={{ width: VISIBLE_WEBVIEWS ? 100 : 1, height: VISIBLE_WEBVIEWS ? 100 : 1 }}
      originWhitelist={['https://*', 'git://*', 'http://*']}
      ref={reference}
      androidLayerType="hardware"
      onMessage={(event: any) => {
        const { data } = event.nativeEvent;
        switch (data) {
          case 'logoutSuccess':
            onSuccess();
            break;
          case 'logoutError':
            onError();
            break;
        }
      }}
      onError={onError}
      onHttpError={onError}
      onLoadStart={onLoadStart}
      domStorageEnabled
      sharedCookiesEnabled
      androidHardwareAccelerationDisabled
    />
  );
};

export default SAPLogoutWebview;
