import { USE_MOCK_KEY } from '@app/config/StorageKeys';
import AsyncStorage from '@react-native-async-storage/async-storage';
import React, { useState } from 'react';

export const MockContextUpdater = React.createContext<(enableMock: boolean) => void>((_: boolean) => {});
export const MockContext = React.createContext<boolean>(false);

export function MockProvider({ children }: { children: React.ReactNode }) {
  const [enableMock, setEnabledMock] = useState<boolean>(false);

  React.useEffect(() => {
    AsyncStorage.setItem(USE_MOCK_KEY, enableMock.toString());
  }, [enableMock]);

  return (
    <MockContext.Provider value={enableMock}>
      <MockContextUpdater.Provider value={setEnabledMock}>{children}</MockContextUpdater.Provider>
    </MockContext.Provider>
  );
}
