import useCulture from '@app/hooks/useCulture';
import useSelectedPark from '@app/hooks/useSelectedPark';
import { ServerPromo } from 'apis/_generated-park-api/models';
import { AxiosError } from 'axios';
import { gql, GraphQLClient } from 'graphql-request';
import Config from 'react-native-config';
import { Source } from 'react-native-fast-image';
import { UseQueryOptions, useQuery } from 'react-query';

interface Images {
  background?: string | Source;
  icon?: string;
}
interface SubCatData {
  images: Images;
  name?: string;
  sub?: string;
}
export interface Promo {
  mainCategory?: string;
  subCategory?: string;
  url?: string;
  name?: string;
  images: Images;
  imageUrl?: string;
  shopData?:
    | {
        shopId?: number;
        facilityId?: string;
        publicKey?: string;
        secretKey?: string;
      }
    | undefined;
}
export interface PromoGroup {
  elements: Promo[];
  mainCategory: string;
  images: Images;
  remoteImage: boolean;
  name?: string;
  sub?: string;
}

const key = `getpromos2`;

type Props = {
  options?: Omit<UseQueryOptions<ServerPromo[], AxiosError>, typeof key>;
  parkCodeOverride?: string;
};

export function useGetPromos(props?: Props) {
  const { parkCode: selectedParkCode } = useSelectedPark();
  const { cultureCode } = useCulture();

  const parkCode = props?.parkCodeOverride ?? selectedParkCode;

  return useQuery(
    [key, { parkCode, cultureCode }],
    async () => {
      const graphQLClient = new GraphQLClient(Config.PARK_API_URL);
      const query = gql`
        query promos {
          promos(parkCode: "${parkCode}", cultureCode: "${cultureCode}") {
            shopId
            parkId
            facilityId
            publicKey
            secretKey
            url
            deliveryCost
            hoofdCat
            subCat
            connectionType
            name
            imageUrl
          }
        }
      `;
      const result = await graphQLClient.rawRequest<{ promos: ServerPromo[] }>(query);
      return result.data?.promos;
    },
    props?.options
  );
}
