import { IntlFormatters } from 'react-intl';
import { Extra } from '../../../../apis/_generated-reservation-api/models';

export const getQuantityText = (extra: Extra, formatMessage: IntlFormatters['formatMessage']) => {
  const people = extra.people || 0;
  const nights = extra.nights || 0;
  const amount = extra.amount || 0;

  const peopleText = `${people || 1} ${
    people > 1
      ? formatMessage({ id: 'app.forms.result.person_plural' })
      : formatMessage({ id: 'app.forms.result.person' })
  }`;
  const nightsText = `${nights || 1} ${
    nights > 1
      ? formatMessage({ id: 'app.general.night_plural' })
      : formatMessage({ id: 'app.general.night' })
  }`;
  const amountText = `${amount || 1} ${formatMessage({ id: 'app.general.times' })}`;

  if (nights === 1 && people === 1) {
    return amountText;
  }
  if (people > 0 && nights > 1 && amount > 1) {
    return `${peopleText}, ${nightsText}, ${amountText}`;
  }
  if (people > 0 && nights > 1) {
    return `${peopleText}, ${nightsText}`;
  }
  if (people > 0 && amount > 1) {
    return `${peopleText}, ${amountText}`;
  }
  if (nights > 1 && amount > 1) {
    return `${nightsText}, ${amountText}`;
  }
  if (nights > 1) {
    return nightsText;
  }
  if (amount > 1) {
    return amountText;
  }
  return peopleText;
};
