import moment from 'moment-timezone';
import { useIntl } from 'react-intl';

const isToday = (date?: string) => {
  if (!date) return false;
  return (
    new Date(date).getDate() === new Date().getDate() &&
    new Date(date).getMonth() === new Date().getMonth() &&
    new Date(date).getFullYear() === new Date().getFullYear()
  );
};

const isTodayUnix = (unixNumber: number | undefined | null) => {
  return !unixNumber
    ? false
    : moment().format('YYYY-MM-DD') === moment(unixNumber * 1000).format('YYYY-MM-DD');
};

const isTomorrow = (unixNumber: number) => {
  return moment().add(1, 'days').format('YYYY-MM-DD') === moment(unixNumber * 1000).format('YYYY-MM-DD');
};

const addMinutes = (date: Date, minutes: number) => {
  return new Date(date.getTime() + minutes * 60000);
};
const addHours = (date: Date, hours: number) => {
  return new Date(date.getTime() + hours * 3600000);
};
const addDays = (date: Date, days: number) => {
  if (!date) {
    return date;
  }
  return moment(date).add(days, 'days').toDate();
};
const addYears = (date: Date, years: number) => {
  if (!date) {
    return date;
  }
  return moment(date).add(years, 'years').toDate();
};

const toUnixNumber = (date: string) => {
  return moment(date).unix();
};

const fromUnixNumber = (unixTimestamp: number) => {
  return moment(unixTimestamp).toDate();
};

const getFromNowString = (date: string | number | Date) => {
  return moment(date).fromNow();
};

const getFormattedDate = (date: Date) => {
  if (!date) {
    return '';
  }
  var dd = String(date.getDate()).padStart(2, '0');
  var MM = String(date.getMonth() + 1).padStart(2, '0'); //January is 0!
  var yyyy = date.getFullYear();
  return dd + '-' + MM + '-' + yyyy;
};
const getAPIDate = (date: Date) => {
  if (!date) {
    return '';
  }
  var dd = String(date.getDate()).padStart(2, '0');
  var MM = String(date.getMonth() + 1).padStart(2, '0'); //January is 0!
  var yyyy = date.getFullYear();
  return yyyy + '-' + MM + '-' + dd;
};
const getFormattedTime = (date: Date) => {
  if (!date) {
    return '';
  }
  return moment(date).format('LT');
};
const getFormattedTimeByTime = (time: string): string => {
  if (!time) {
    return '';
  }
  return moment(moment().format('YYYY-MM-DD') + ' ' + time).format('LT');
};
const getAge = (birthdate: number) => {
  return moment().diff(birthdate * 1000, 'years');
};

const formatDayWithoutTime = (
  date: string | number | Date,
  longDay?: boolean,
  longMonth?: boolean,
  showYear?: boolean
) =>
  moment(date)
    .format(`ddd${longDay ? 'd' : ''} DD MMM${longMonth ? 'M' : ''}${showYear ? ' YYYY' : ''}`)
    .replace(/\./g, '');

const formatDayWithTime = (date: string | Date, long?: boolean) =>
  moment(date)
    .format(long ? 'dddd D MMM, HH:mm' : 'ddd D MMM, HH:mm')
    .replace(/\./g, '');

const formatTime = (time: number | undefined) => (time ? moment.unix(time).format('HH:mm') : '');

const getCurrentTime = () => {
  return moment().format('HH:mm');
};

const formatShortDayWithoutTime = (date: string | Date, long?: boolean) =>
  moment(date)
    .format(long ? 'dd D MMM' : 'ddd D MMM')
    .replace(/\./g, '');

const ReadableDayByDate = (
  date: Date | null | undefined,
  fullDay: boolean = false,
  addTime: boolean = false
) => {
  const { formatMessage } = useIntl();
  if (!date) {
    return '';
  }

  const time = addTime ? ' ' + moment(date).format('HH:mm') : '';

  if (isToday(date.toString())) {
    return formatMessage({ id: 'app.general.today' }) + time;
  } else if (isTomorrow(date.getTime() / 1000)) {
    return formatMessage({ id: 'app.general.tomorrow' }) + time;
  }
  return formatDayWithoutTime(date, fullDay) + time;
};

const getAnalyticsFriendlyDate = (date: Date) => {
  if (!date) {
    return '';
  }
  if (isToday(date.toString())) {
    return 'today';
  } else if (isTomorrow(date.getTime() / 1000)) {
    return 'tomorrow';
  }
  return `${daysBetween(date, new Date())} days ahead`;
};

const firstToUpper = (dateString: string) => dateString.charAt(0).toUpperCase() + dateString.slice(1);

const daysBetween = (date1: Date, date2: Date) => {
  var b = moment(date2);
  var a = moment(date1);
  return a.diff(b, 'days');
};

const minutesBetween = (date1: Date, date2: Date) => {
  var b = moment(date2);
  var a = moment(date1);
  return a.diff(b, 'minutes');
};

const timeIsBetween = ({ start, end, checkTime }: { start?: string; end?: string; checkTime: string }) => {
  if (!start || !end) return false;
  return moment(checkTime, 'h:mm a').isBetween(moment(start, 'h:mm a'), moment(end, 'h:mm a'));
};
const dateIsBetween = (date1: Date, date2: Date, checkDate: Date) => {
  return moment(checkDate).isBetween(date1, date2);
};
const daysUntil = (unixNumber: number) => {
  return daysBetween(fromUnixNumber(unixNumber * 1000), new Date()) + 1;
};
const nightsUntil = (unixNumber: number) => {
  return daysBetween(
    fromUnixNumber(unixNumber * 1000),
    fromUnixNumber(moment(moment.now()).startOf('day').unix() * 1000)
  );
};

const getTime = (date: number) => {
  return moment(date).format('HH:mm');
};

const getTimeSlotOptions = (accommodationShouldLeaveDateTime?: number) => {
  const options = [
    {
      label: '{before} 7:30',
      value: '7:29',
    },
    { label: '7:30 - 8:30', value: '8:29' },
    { label: '8:30 - 9:30', value: '9:29' },
  ];

  const leaveTime = accommodationShouldLeaveDateTime
    ? getTime(accommodationShouldLeaveDateTime * 1000)
    : '10:30';
  const leaveTimeValue = accommodationShouldLeaveDateTime
    ? getTime(accommodationShouldLeaveDateTime * 1000 - 60 * 1000)
    : '10:29'; // The value should be one minute earlier

  const lastOptionDependingOnMaxDeparture = { label: '9:30 - ' + leaveTime, value: leaveTimeValue };
  return [...options, lastOptionDependingOnMaxDeparture];
};

const dayPartOptions: { label: string; value: string }[] = [
  { label: 'app.general.morning', value: '11:59' },
  { label: 'app.general.afternoon', value: '17:59' },
  { label: 'app.general.evening', value: '23:59' },
];

const useTimeToDaypart = (accommodationShouldLeaveDateTime?: number) => {
  const { formatMessage } = useIntl();

  const timeToDaypart = (time: string, useDaypart: boolean = true) => {
    if (useDaypart) {
      const dayPart = dayPartOptions.find((entry) => entry.value === time)?.label;
      return dayPart ? formatMessage({ id: dayPart }) : time;
    } else {
      const timeSlot = getTimeSlotOptions(accommodationShouldLeaveDateTime).find(
        (entry) => entry.value === time.replace(/^0+/, '')
      )?.label;
      return timeSlot ? timeSlot.replace('{before}', formatMessage({ id: 'app.times.before' })) : time;
    }
  };
  return timeToDaypart;
};
const concatenateDateAndTime = (date: number, time: string) => {
  const momentDate = moment(date);
  const timeParts = time.split(':');
  if ((timeParts?.length || 0) < 2) return ''; // Time should at least include hours and minutes

  momentDate.set({
    hour: parseInt(timeParts[0]),
    minute: parseInt(timeParts[1]),
    second: timeParts?.[2] ? parseInt(timeParts[2]) : undefined,
    millisecond: timeParts?.[3] ? parseInt(timeParts[3]) : undefined,
  });
  return momentDate.toISOString();
};

// This function takes a moment-formatted datetime string, adds hours and/or minutes, and formats back to its original format
const addTimeToFormattedDateTime = (dateTime: string, hours: number, minutes: number = 0) => {
  const m = moment(dateTime).add(hours, 'hours').add(minutes, 'minutes');
  return m.toISOString();
};

export {
  isToday,
  isTodayUnix,
  isTomorrow,
  formatDayWithoutTime,
  formatDayWithTime,
  firstToUpper,
  getFormattedDate,
  getAPIDate,
  getFormattedTime,
  getFormattedTimeByTime,
  getFromNowString,
  getAnalyticsFriendlyDate,
  toUnixNumber,
  fromUnixNumber,
  addMinutes,
  addHours,
  addDays,
  addYears,
  formatTime,
  ReadableDayByDate,
  daysBetween,
  minutesBetween,
  timeIsBetween,
  dateIsBetween,
  formatShortDayWithoutTime,
  getCurrentTime,
  daysUntil,
  nightsUntil,
  useTimeToDaypart,
  getTime,
  getTimeSlotOptions,
  getAge,
  concatenateDateAndTime,
  addTimeToFormattedDateTime,
};
