import React from 'react';
import useTheme from '@hooks/useTheme';
import Svg, { Defs, Stop, LinearGradient, Circle } from 'react-native-svg';
import Text from '../Text';
import { Platform, View } from 'react-native';

const SIZE = 40;
const CIRCLE_RADIUS = 18;

interface Props {
  progressPercentage: number;
}

export default function ProgressSVG({ progressPercentage }: Props) {
  const theme = useTheme();

  const roundRadius = CIRCLE_RADIUS;
  const roundCircum = 2 * roundRadius * Math.PI;
  const roundDraw = (progressPercentage * roundCircum) / 100;

  // TODO: the transform below also rotates the LinearGradient being applied, though this should stay in place (not rotated)

  return (
    <Svg width={SIZE} height={SIZE} fill="none" viewBox="0 0 40 40">
      <Circle cx={20} cy={20} r={CIRCLE_RADIUS} stroke="#485962" strokeWidth={1.5} />
      {progressPercentage > 0 && (
        <Circle
          cx={20}
          cy={20}
          r={CIRCLE_RADIUS}
          stroke="url(#a)"
          strokeWidth={3}
          strokeDasharray={roundDraw + ' 999'}
          transform={
            Platform.OS === 'web' ? undefined : { rotation: 270, originX: SIZE / 2, originY: SIZE / 2 }
          }
        />
      )}
      <View style={{ width: SIZE, height: SIZE, alignItems: 'center', justifyContent: 'center' }}>
        <Text style={{ alignSelf: 'center' }} color="white" size="s" bold>
          {progressPercentage}%
        </Text>
      </View>
      <Defs>
        {/* @ts-ignore - actually works */}
        <LinearGradient id="a" x1={38} y1={38} x2={2} y2={2} gradientUnits="userSpaceOnUse">
          <Stop stopColor={theme.colors.gradientSuccess1} />
          <Stop offset={0.495} stopColor={theme.colors.gradientSuccess2} />
          <Stop offset={1} stopColor={theme.colors.gradientSuccess3} />
        </LinearGradient>
      </Defs>
    </Svg>
  );
}
