import { ListItem } from '@app/components/ListItems2/ListItems';
import { UserContext } from '@app/context/usercontext/UserContextProvider';
import useMetrics from '@app/metrics/useMetrics';
import { useGetReservation } from '@domains/reservations/reservation/queries/useGetReservation';
import ICE from '@domains/reservations/reservation/screens/ICE';
import ExpectedArrivalTimeSheet from '@domains/sheets/expected-arrival-time/ExpectedArrivalTimeSheet';
import { ArrivalTime, ArrivalTimePrefix, BookingStatusType } from 'apis/_generated-reservation-api/models';
import React, { useContext, useMemo } from 'react';
import { useIntl } from 'react-intl';
import { Linking } from 'react-native';
import { getBrand } from 'utils/brand';
import { formatTime } from 'utils/date';
import { getAmountWithCurrency } from 'utils/reservation';
import useCulture from './useCulture';
import useTheme from './useTheme';

interface Props {
  bookingReference?: string;
}

export default function useChecklist(props: Props) {
  const { token } = useContext(UserContext);
  const { formatMessage } = useIntl();
  const { cultureCode } = useCulture();
  const theme = useTheme();
  const { logEvent } = useMetrics();
  const bookingReference = props?.bookingReference;

  const { data: reservation } = useGetReservation({
    brand: getBrand(theme),
    token,
    bookingReference,
  });

  const getArrivalTime = (arrivalTime: ArrivalTime) => {
    if (!arrivalTime?.time) {
      return '';
    }
    switch (arrivalTime.prefix) {
      case ArrivalTimePrefix.Before:
        return `${formatMessage({ id: 'app.times.from' })} ${formatTime(arrivalTime.time)}`;
      case ArrivalTimePrefix.None:
        return `${formatMessage({ id: 'app.times.from' })} ${formatTime(arrivalTime.time)} - ${formatTime(
          arrivalTime.timeTill
        )}`;
      case ArrivalTimePrefix.After:
        return `${formatMessage({ id: 'app.times.from' })} ${formatTime(arrivalTime.time)}`;
      case ArrivalTimePrefix.Nextday:
        return `${formatMessage({ id: 'app.times.nextDay' })}, ${formatMessage({
          id: 'app.times.from',
        })} ${formatTime(arrivalTime.time)}`;
    }
    return '';
  };

  const arrivalTimeText = !reservation?.arrivalTime?.time
    ? formatMessage({ id: 'app.reservation.unknownArrivalDate' })
    : `${formatMessage({ id: 'app.reservation.arrivalTime' })}: ${getArrivalTime(reservation?.arrivalTime)}`;

  const paymentText = !reservation?.checklist?.needsToPay
    ? formatMessage({ id: 'app.conversation.paid' })
    : formatMessage({ id: 'app.conversation.unpaid' }).replace(
        '{0}',
        reservation?.pendingPayment ? `${getAmountWithCurrency(cultureCode, reservation)}` : ''
      );

  const isCombiReservation = !!reservation?.combiReference;

  let checklist = useMemo(() => {
    let list: ListItem[] = [
      {
        id: 'two',
        title: formatMessage({ id: 'app.checklist.travelCompany.title' }),
        subtitle: formatMessage({ id: 'app.checklist.travelCompany.subtitle' }),
        icon: reservation?.checklist?.needsCompanyRegistration
          ? 'travel-group-incomplete'
          : 'travel-group-complete',
        navigateTo: isCombiReservation ? undefined : 'TravelCompany',
        navigateParams: { bookingReference },
        status: reservation?.checklist?.needsCompanyRegistration ? 'incomplete' : 'complete',
        linkTo: isCombiReservation
          ? formatMessage({
              id: 'app.website.mylandal',
            })
          : undefined,
      },
      {
        id: 'ice',
        title: formatMessage({ id: 'app.checklist.ICE.title' }),
        subtitle: reservation?.iceName || formatMessage({ id: 'app.checklist.ICE.subtitle' }),
        icon: !reservation?.iceName ? 'ice-incomplete' : 'ice-complete',
        status: !reservation?.iceName ? 'incomplete' : 'complete',
        blocking: false,
        bottomSheet: {
          key: 'ICE',
          title: formatMessage({ id: 'app.checklist.ICE.title' }),
          backgroundColor: 'canvas',
          component: () => <ICE bookingReference={bookingReference} />,
        },
      },
      {
        id: 'arrival-time',
        title: formatMessage({ id: 'app.checklist.arrivalTime.title' }),
        subtitle: arrivalTimeText,
        icon: reservation?.checklist?.needsArrivalTime ? 'arrival-time-incomplete' : 'arrival-time-complete',
        status: reservation?.checklist?.needsArrivalTime ? 'incomplete' : 'complete',
        bottomSheet: {
          component: () => (
            <ExpectedArrivalTimeSheet bookingReference={reservation?.bookingReference ?? ''} />
          ),
          title: formatMessage({ id: 'app.checklist.arrivalTime.title' }),
          backgroundColor: 'canvas',
          key: 'arrival-time',
        },
        disabled: reservation?.status === BookingStatusType.Departed,
      },
    ];
    if (!reservation?.isOwner) {
      list = [
        ...list,
        {
          id: 'payment',
          title: formatMessage({ id: 'app.checklist.payment.title' }),
          subtitle: paymentText,
          icon: reservation?.checklist?.needsToPay ? 'payments-incomplete' : 'payments-complete',
          status: reservation?.checklist?.needsToPay ? 'incomplete' : 'complete',
          onPress: () => {
            if (reservation?.pendingPaymentUrl) {
              logEvent('PaymentBegin');
              Linking.openURL(reservation?.pendingPaymentUrl);
            }
          },
          external: true,
        },
      ];
    }
    return list?.sort((a, b) => {
      if (a?.status === 'complete') return 1;
      if (a?.id === 'payment') return -1;
    });
  }, [
    arrivalTimeText,
    bookingReference,
    formatMessage,
    isCombiReservation,
    logEvent,
    paymentText,
    reservation?.bookingReference,
    reservation?.checklist?.needsArrivalTime,
    reservation?.checklist?.needsCompanyRegistration,
    reservation?.checklist?.needsToPay,
    reservation?.iceName,
    reservation?.isOwner,
    reservation?.pendingPaymentUrl,
    reservation?.status,
  ]);

  const numberOfItems = checklist.length;
  const numberOfItemsCompleted = checklist.filter((c) => c.status === 'complete').length;
  const numberOfItemsCompletedOrNonBlocking = checklist.filter(
    (c) => c.status === 'complete' || c.blocking === false
  ).length;
  const percentageComplete = Math.round((numberOfItemsCompleted / numberOfItems) * 100);
  const pendingTasks = numberOfItems - numberOfItemsCompleted;
  const blockingTasks = numberOfItems - numberOfItemsCompletedOrNonBlocking;

  return { percentageComplete, pendingTasks, blockingTasks, checklist };
}
