import useCulture from '@app/hooks/useCulture';
import { gql, GraphQLClient } from 'graphql-request';
import Config from 'react-native-config';
import { UseQueryOptions, useQuery } from 'react-query';
import { Reservation } from '../../../../apis/_generated-reservation-api/models';
import { UserContext } from '@app/context/usercontext/UserContextProvider';
import { useContext } from 'react';
import { AxiosError } from 'axios';
import { MOCK_RES } from './demo-data';
import { useGetReservations } from './useGetReservations';
import { getBrand } from 'utils/brand';
import useTheme from '@app/hooks/useTheme';
import { getActiveReservation } from 'utils/reservation';

const key = 'getreservation';

type Props = {
  options?: Omit<UseQueryOptions<Reservation, AxiosError>, typeof key>;
  token?: string;
  brand: 'LGP' | 'HSN';
  staleTime?: number;
  bookingReference?: string;
};

export function useGetReservation({ token, brand, bookingReference, options }: Props) {
  const theme = useTheme();
  const { languageCode } = useCulture();
  const { demoMode } = useContext(UserContext);

  const { data: reservations } = useGetReservations({
    brand: getBrand(theme),
    token,
  });

  if (!bookingReference && !!reservations) {
    // find the first reservation, where you haven't yet departed
    bookingReference = getActiveReservation(reservations)?.bookingReference;
  }

  const LONG_STALE_TIME: number = Number(Config.RESERVATION_STALE_TIME_DEFAULT_LONG);
  const SHORT_STALE_TIME: number = Number(Config.RESERVATION_STALE_TIME_DEFAULT_SHORT);

  const isUpcomingReservation =
    !!reservations && bookingReference === getActiveReservation(reservations)?.bookingReference;

  const staleTime = isUpcomingReservation ? SHORT_STALE_TIME : LONG_STALE_TIME;

  // Belows ''undefined'' is a patch as we saw literal undefined strings triggering this query.
  // TODO: This should be investigated
  const extendedOptions: typeof options = demoMode
    ? {
        ...options,
        enabled: true,
        staleTime: 0,
        cacheTime: 0,
        initialData: MOCK_RES.find((r) => r.bookingReference === bookingReference) || MOCK_RES[0],
        placeholderData: MOCK_RES.find((r) => r.bookingReference === bookingReference) || MOCK_RES[0],
      }
    : {
        ...options,
        enabled: !!token && token !== 'undefined' && !!brand && !!bookingReference,
        staleTime,
        cacheTime: 1000 * 60 * 60 * 72, // 3 days,
      };
  return useQuery(
    [key, { token, brand, languageCode, bookingReference }],
    async () => {
      if (demoMode) {
        return MOCK_RES.find((r) => r.bookingReference === bookingReference) || MOCK_RES[0];
      }

      const graphQLClient = new GraphQLClient(Config.RESERVATION_API_URL);
      const query = gql`
        query {
          reservation(uuid: "${token}",brand:${brand
        ?.toString()
        .toUpperCase()},languageCode:"${languageCode.toString()}", bookingReference: "${bookingReference}") {
            bookingReference
            combiReference
            arrivalDate
            arrivalTime {
              time
              timeTill
              prefix
            }
            accommodationReadyDateTime
            accommodationShouldLeaveDateTime
            accommodationLeaveDateTime
            arrivalTimeUrl
            pendingPayment
            pendingPaymentUrl
            departureDate
            expectedDepartureDate
            holidayStarted
            unitTypeIndicator
            lockType
            numberOfNights
            featuresEnabled {
              extras
              checkoutNow
            }
            totalGuests
            parkCode
            accommodationCode
            smartlockAvailability
            unitCode
            lockType
            checklist {
              needsToPay
              needsCompanyRegistration
              needsArrivalTime
            }
            unitCodeIsPreference
            currency
            bookingFee
            needsCompanyRegistration
            seamlessEnabled
            status
            travelCompanions {
              isComplete
              sequenceNumber
              arrivalDate
              departureDate
              gender
              birthdate
              firstName
              middleName
              lastName
              address {
                postalCode
                houseNumber
                houseNumberExtension
                streetName
                city
                country {
                  code
                  name
                }
              }
              email
              phone
              phone2
              ageCategory
              minAge
              maxAge
            }
            mainTenant {
              isComplete
              sequenceNumber
              arrivalDate
              departureDate
              gender
              birthdate
              firstName
              middleName
              lastName
              address {
                postalCode
                houseNumber
                houseNumberExtension
                streetName
                city
                country {
                  code
                  name
                }
              }
              email
              phone
              phone2
            }
            icePhoneNumber
            iceName
            idType
            isOwner
            digitalCleaningTasks
            checkinTimes {
              time
              timeTill
              selected
              prefix
              text
            }
          }
        }`;
      try {
        const result = await graphQLClient.rawRequest<{ reservation: Reservation; errors: any[] }>(query);

        if ((result?.data?.errors?.length || 0) > 0) {
          throw new Error('something went wrong');
        }
        return result?.data?.reservation;
      } catch (ex) {
        throw 'something went wrong';
      }
    },
    extendedOptions
  );
}
