import Text from '@app/components/Text';
import useTheme from '@app/hooks/useTheme';
import { AllRoutes } from '@app/navigation/stacks/Main';
import { NavigationContainerRef, useNavigation } from '@react-navigation/native';
import { Address } from 'apis/_generated-reservation-api/models';
import React from 'react';
import { useIntl } from 'react-intl';
import { TouchableOpacity, View } from 'react-native';
import AddressOption from './AddressOption';

interface Props {
  selected: number;
  addresses: {
    address: Address;
    fullName?: string;
    custom?: boolean;
  }[];
  onSelect: (value: number) => void;
  setCustomAddress: (address: Address) => void;
  bookingReference: string;
}

export default function AddressSelector({
  addresses,
  selected,
  onSelect,
  setCustomAddress,
  bookingReference,
}: Props) {
  const theme = useTheme();
  const { formatMessage } = useIntl();
  const navigation = useNavigation<NavigationContainerRef<AllRoutes>>();

  return (
    <View style={{ marginBottom: theme.space.l }}>
      <Text
        style={{
          paddingBottom: theme.space.s,
        }}
        bold
      >
        {formatMessage({ id: 'app.forms.labels.address' })}
      </Text>
      <View>
        {addresses?.map((a, index) => (
          <AddressOption
            key={a.address?.city ?? '' + a?.fullName ?? ''}
            superLine={
              !a.custom
                ? `${formatMessage({ id: 'app.reservation.companions.sameAddress' })} ${a.fullName}`
                : undefined
            }
            addressLine1={`${a.address.streetName} ${a.address.houseNumber}${
              a.address.houseNumberExtension ? ` ${a.address.houseNumberExtension}` : ''
            }`}
            addressLine2={`${a.address.postalCode} ${a.address.city} ${a.address.country?.code}`}
            selected={selected === index}
            onSelect={() => {
              onSelect?.(index);
            }}
            customAddress={a.custom ? a.address : undefined}
            setCustomAddress={setCustomAddress}
          />
        ))}
        {!addresses?.find((a) => a.custom) && (
          <TouchableOpacity
            activeOpacity={0.8}
            style={{ marginVertical: theme.space.s }}
            onPress={() => {
              navigation?.navigate('TravelCompanyAddress', {
                setCustomAddress: async (address: Address) => {
                  await setCustomAddress(address);
                },
                bookingReference,
              });
            }}
          >
            <Text color="tertiary" bold size="s">
              {formatMessage({ id: 'app.reservation.companions.addAddress' })}
            </Text>
          </TouchableOpacity>
        )}
      </View>
    </View>
  );
}
