import useCulture from '@app/hooks/useCulture';
import { Category } from 'apis/_generated-park-api/models';
import { AxiosError } from 'axios';
import { gql, GraphQLClient } from 'graphql-request';
import Config from 'react-native-config';
import { UseQueryOptions, useQuery } from 'react-query';

const key = `getcategories`;

type ProductDataProps = {
  options?: Omit<UseQueryOptions<Category[], AxiosError>, typeof key>;
  shopId: number;
};

export function useGetCategories({ options, shopId }: ProductDataProps) {
  const { cultureCode } = useCulture();

  return useQuery(
    [key, { cultureCode, shopId }],
    async () => {
      const graphQLClient = new GraphQLClient(Config.PARK_API_URL);
      const query = gql`
          query shopCategories {
            shopCategories(cultureCode: "${cultureCode}", shopId: "${shopId}") {
              id
              name
              description
              photo
            }
          }
        `;
      const result = await graphQLClient.rawRequest(query);
      console.log('RESULT', result);
      return result?.data?.shopCategories;
    },
    options
  );
}
