import React, { useContext, useState } from 'react';
import useTheme from '@app/hooks/useTheme';
import { useIntl } from 'react-intl';
import useMetrics from '@app/metrics/useMetrics';
import { UserContext } from '@app/context/usercontext/UserContextProvider';
import { useGetReservation } from '@domains/reservations/reservation/queries/useGetReservation';
import { getBrand } from 'utils/brand';
import { Alert, TouchableOpacity, View } from 'react-native';
import Text from '@app/components/Text';
import { useBottomSheet } from '@app/navigation/bottom-sheet/BottomSheetProvider';
import Button from '@app/components/Button';
import RadioList from '@app/components/ListItems2/RadioList';
import { useMutateExpectedArrivalTime } from '@domains/reservations/reservation/queries/useMutateExpectedArrivalTime';

interface Props {
  bookingReference: string;
}

export default function ExpectedArrivalTimeSheet({ bookingReference }: Props) {
  const theme = useTheme();
  const { closeBottomSheet } = useBottomSheet();
  const { formatMessage } = useIntl();
  const { logEvent } = useMetrics();
  const { token } = useContext(UserContext);
  const { data: reservation, refetch } = useGetReservation({
    token,
    brand: getBrand(theme),
    bookingReference,
  });
  const [processing, setProcessing] = useState(false);
  const [selectedArrivalTime, setSelectedArrivalTime] = useState(0);

  React.useEffect(() => {
    logEvent('ArrivalTimeBegin');
  }, [logEvent]);

  const { updateExpectedArrivalTime } = useMutateExpectedArrivalTime({ bookingReference });
  const selectedArrivalTimeIndex = reservation?.checkinTimes
    ? reservation?.checkinTimes?.findIndex((c) => c?.selected)
    : -1;

  return (
    <View
      style={{
        margin: theme.space.l,
      }}
    >
      <Text style={{ textAlign: 'center', marginBottom: theme.space.m }} bold>
        {formatMessage({ id: 'app.reservation.unknownDepartureDateTitle' }).replace(
          '{0}',
          reservation?.mainTenant?.firstName || ''
        )}
      </Text>
      <Text
        type="body"
        style={{ textAlign: 'center', lineHeight: 26, marginBottom: theme.space.xl }}
        color="dark"
      >
        {formatMessage({ id: 'app.reservation.unknownExpectedArrivalDateText' })}
      </Text>
      <RadioList
        title={formatMessage({ id: 'app.reservation.expectedArrivalText' })}
        centerTitle
        items={
          reservation?.checkinTimes?.map((t) => ({
            label: t?.text || '',
            value: t?.time || 0,
          })) || []
        }
        initialPosition={selectedArrivalTimeIndex > -1 ? selectedArrivalTimeIndex + 1 : undefined}
        onSelect={(value) => {
          if (!value) {
            return;
          }

          setSelectedArrivalTime(value as number);
        }}
      />
      <Button
        disabled={processing || !selectedArrivalTime}
        onPress={async () => {
          if (!selectedArrivalTime) return;
          try {
            setProcessing(true);
            const response = await updateExpectedArrivalTime({
              bookingReference: reservation?.bookingReference,
              uid: token,
              arrivalTime: selectedArrivalTime,
            });
            if (!response.success) {
              !!response.message && Alert.alert(response.message);
            } else {
              logEvent('ArrivalTimeComplete');
            }
            await refetch();
            closeBottomSheet();
            setProcessing(false);
          } catch (error) {
            Alert.alert(formatMessage({ id: 'app.forms.tablebooking.error.generic' }));
            logEvent('ArrivalTimeError', { reason: error });
            setProcessing(false);
          }
        }}
        type="primary"
        style={{ backgroundColor: theme.colors.secondary }}
      >
        {processing ? (
          <Button.ActivityIndicator />
        ) : (
          <Button.Text color="white" style={{ lineHeight: 20 }}>
            {formatMessage({ id: 'app.reservation.expectedCheckoutSubmit' })}
          </Button.Text>
        )}
      </Button>
      <TouchableOpacity
        activeOpacity={0.8}
        onPress={closeBottomSheet}
        style={{ marginTop: theme.space.l, marginBottom: theme.space.xl, alignItems: 'center' }}
        hitSlop={{ top: 10, bottom: 10, left: 10, right: 10 }}
      >
        <Text color="gray" bold>
          {formatMessage({ id: 'app.general.cancel' })}
        </Text>
      </TouchableOpacity>
    </View>
  );
}
