import { AxiosError } from 'axios';
import { UseQueryOptions, useMutation } from 'react-query';
import { EventName } from '@app/metrics/useMetrics';
import { useGetPark } from '@domains/park/contact-and-info/queries/useGetPark';
import { useContext } from 'react';
import { useGetReservation } from '@domains/reservations/reservation/queries/useGetReservation';
import { getBrand } from 'utils/brand';
import { UserContext } from '@app/context/usercontext/UserContextProvider';
import useTheme from '@app/hooks/useTheme';
import DeviceInfo from 'react-native-device-info';
import { Platform } from 'react-native';
import { gql, GraphQLClient } from 'graphql-request';
import Config from 'react-native-config';
import { PostFeedbackResponse } from '../../../apis/_generated-park-api/models';

type Props = {
  createConversationOptions?: UseQueryOptions<PostFeedbackResponse, AxiosError>;
};

export function useSendFeedback(props?: Props) {
  const theme = useTheme();
  const { data: park } = useGetPark();
  const { token } = useContext(UserContext);
  const { data: reservation } = useGetReservation({
    brand: getBrand(theme),
    token,
  });

  const createConversationMutation = useMutation(
    async ({ text, event, rating }: { text: string; event: EventName; rating: string }) => {
      const request = {
        actionType: event,
        message: text,
        deviceId: DeviceInfo.getUniqueId(),
        appVersion: `v.${DeviceInfo.getBuildNumber()}`,
        platform: Platform.OS,
        osVersion: Platform.Version.toString(),
        isLoggedIn: !!token,
        parkSelected: park?.id ?? '',
        parkReservation: reservation?.parkCode ?? '',
        rating,
      };
      // TODO: GraphQL mutation
      const graphQLClient = new GraphQLClient(Config.PARK_API_URL);

      const query = gql`
        mutation postFeedback($request: PostFeedbackRequest) {
          postFeedback(request: $request) {
            success
          }
        }
      `;

      const response = await graphQLClient.rawRequest<{ postFeedback: PostFeedbackResponse }>(query, {
        request,
      });
      return response.data?.postFeedback;
    },
    props?.createConversationOptions
  );

  const sendFeedback = async (text: string, event: EventName, rating: string) => {
    const createConversationResult = await createConversationMutation.mutateAsync({ text, event, rating });
    if (!createConversationResult) {
      return null;
    }
    return { createConversationResult };
  };

  return { sendFeedback };
}
