import { UserContext } from '@app/context/usercontext/UserContextProvider';
import useCulture from '@app/hooks/useCulture';
import useTheme from '@app/hooks/useTheme';
import { useGetReservation } from '@domains/reservations/reservation/queries/useGetReservation';
import { Product } from 'apis/_generated-reservation-api/models';
import { AxiosError } from 'axios';
import { gql, GraphQLClient } from 'graphql-request';
import { useContext } from 'react';
import Config from 'react-native-config';
import { UseQueryOptions, useQuery } from 'react-query';
import { getBrand } from 'utils/brand';
import { MOCK_EXTRAS } from './demo-data';

const key = 'getExtras';

type Props = {
  options?: Omit<UseQueryOptions<Product[] | undefined, AxiosError>, typeof key>;
  bookingReference?: string;
};

export function useGetExtras({ options, bookingReference }: Props) {
  const theme = useTheme();
  const { cultureCode } = useCulture();
  const { token, demoMode } = useContext(UserContext);
  const { data: reservation } = useGetReservation({
    token,
    brand: getBrand(theme),
    bookingReference,
  });

  const usedBookingReference = reservation?.bookingReference;

  const extendedOptions: typeof options = demoMode
    ? {
        ...options,
        enabled: true,
        staleTime: 0,
        cacheTime: 0,
        initialData: MOCK_EXTRAS,
        placeholderData: MOCK_EXTRAS,
      }
    : {
        ...options,
        enabled: !!usedBookingReference,
      };

  return useQuery(
    [key, { usedBookingReference, cultureCode }],
    async () => {
      if (demoMode) return MOCK_EXTRAS;
      const graphQLClient = new GraphQLClient(Config.RESERVATION_API_URL);
      const query = gql`
      query{
        extras(uid: "${token}", reservationId: "${usedBookingReference}", cultureCode: "${cultureCode}"){
          id
          name
          description
          image
          key
          extras{
            extraCode
            extraName
            extraDescription
            automaticallyCreated
            amount
            nights
            people
            price
            voucherIdentifier
            voucherDescription
            image
            key
            redeemFacilities
            issuedVouchers{
              code
              status
            }
          }
        }
      }`;

      try {
        const result = await graphQLClient.rawRequest<{ extras: Product[] }>(query);
        const extras = result?.data?.extras;
        return extras;
      } catch (ex) {
        throw 'something went wrong';
      }
    },
    extendedOptions
  );
}
