import { MONITOR_RESERVATION_KEY } from '@app/config/StorageKeys';
import { SaltoContextUpdater } from '@app/context/SaltoContextProvider';
import { UserContext, UserContextUpdater } from '@app/context/usercontext/UserContextProvider';
import useTheme from '@app/hooks/useTheme';
import SaltoKS from '@domains/locks/native';
import { useGetReservation } from '@domains/reservations/reservation/queries/useGetReservation';
import { useGetReservations } from '@domains/reservations/reservation/queries/useGetReservations';
import AsyncStorage from '@react-native-async-storage/async-storage';
import React, { useCallback, useContext } from 'react';
import { Alert } from 'react-native';
import { getBrand } from 'utils/brand';

export default function useLogout() {
  const theme = useTheme();
  const { clearToken } = useContext(UserContextUpdater);
  const { clearDeviceReservationIds } = React.useContext(SaltoContextUpdater);
  const { token, demoMode } = useContext(UserContext);
  const { remove: removeReservationCache } = useGetReservation({
    brand: getBrand(theme),
    token,
  });
  const { remove: removeReservationsCache } = useGetReservations({
    brand: getBrand(theme),
    token,
  });

  const logout = useCallback(async () => {
    if (demoMode) {
      Alert.alert('Demo modus', 'Deze actie is geblokkeerd in de demo modus'); // TODO: translation
      return;
    }
    await AsyncStorage.removeItem(MONITOR_RESERVATION_KEY);
    clearToken();
    await SaltoKS.clearMobileKey();
    clearDeviceReservationIds();
    removeReservationCache();
    removeReservationsCache();
  }, [clearDeviceReservationIds, clearToken, demoMode, removeReservationCache, removeReservationsCache]);
  return { logout };
}
