import useTheme from '@app/hooks/useTheme';
import NotificationLabel from '@domains/notification/components/NotificationLabel';
import { NotificationMessageLabel } from '@domains/notification/types/NotificationMessage';

import React from 'react';
import { View, ViewStyle } from 'react-native';
import LinearGradient from 'react-native-linear-gradient';
import { LandalPressable } from '../LandalPressable';
import Shadow from '../Shadow';
import Text from '../Text';

const ITEM_HEIGHT = 60;
const READ_DOT_SIZE = 6;

export interface NotificationsListItemProps {
  title: string;
  subtitle?: string;
  label?: NotificationMessageLabel;
  createdAt?: string;
  isFirst?: boolean;
  isLast?: boolean;
  isRead?: boolean;
  style?: ViewStyle;
  onPress: () => void;
}

interface NotificationsListContainerProps {
  children?: React.ReactNode;
}

export const NotificationsListItem = ({
  title,
  subtitle,
  label,
  createdAt,
  isRead,
  isFirst,
  isLast,
  style,
  onPress,
}: NotificationsListItemProps) => {
  const theme = useTheme();

  const margin = theme.space.s;

  return (
    <LandalPressable
      style={[
        {
          backgroundColor: '#FFF',
          minHeight: ITEM_HEIGHT + margin * 2,
          paddingVertical: theme.space.l,
          paddingHorizontal: theme.space.s,
        },
        isFirst && {
          borderTopLeftRadius: 12,
          borderTopRightRadius: 12,
        },
        isLast && {
          borderBottomLeftRadius: 12,
          borderBottomRightRadius: 12,
        },
        style,
      ]}
      onPress={onPress}
    >
      <View
        style={{
          flexDirection: 'row',
          paddingHorizontal: theme.space.m,

          marginTop: -theme.space.s,
          alignItems: 'center',
          marginBottom: theme.space.s / 2,
        }}
      >
        {!!label && (
          <NotificationLabel
            label={label}
            style={{
              marginRight: theme.space.s,
            }}
          />
        )}
        <Text color="gray" size="s" numberOfLines={1}>
          {createdAt}
        </Text>
        {!isRead && (
          <View style={{ flex: 1 }}>
            <LinearGradient
              colors={[theme.colors.gradientDanger1, theme.colors.gradientDanger2]}
              style={[
                {
                  backgroundColor: 'transparent', // required for gradient
                  width: READ_DOT_SIZE,
                  height: READ_DOT_SIZE,
                  borderRadius: READ_DOT_SIZE / 2,
                  alignSelf: 'flex-end',
                },
                style,
              ]}
              angle={120}
              useAngle
            />
          </View>
        )}
      </View>
      <Text
        bold
        color={isRead ? 'gray' : 'black'}
        style={{ paddingHorizontal: theme.space.m }}
        numberOfLines={2}
      >
        {title}
      </Text>
      <Text color="gray" style={{ paddingHorizontal: theme.space.m }} numberOfLines={1}>
        {subtitle}
      </Text>
    </LandalPressable>
  );
};

const NotificationsList = ({ children }: NotificationsListContainerProps) => {
  return <Shadow>{children}</Shadow>;
};

export default NotificationsList;
