export const isValidEmail = (email: string) => {
  return (
    email
      .toLowerCase()
      .match(
        `^[a-z0-9!#$%&'*+/=?^_\`{|}~-]+(?:\\.[a-z0-9!#$%&'*+/=?^_\`{|}~-]+)*@(?:[a-z0-9](?:[a-z0-9-]*[a-z0-9])?\\.)+[a-z0-9](?:[a-z0-9-]*[a-z0-9])?$`
      )?.length === 1
  );
};

export const isValidPhone = (phone: string) => {
  return phone.match(`^$|^[0-9 ()+-]+$`)?.length === 1;
};
