import React from 'react';
import { useAnimatedScrollHandler, useSharedValue } from 'react-native-reanimated';
import { View } from 'react-native';
import useTheme from '@app/hooks/useTheme';
import { useFocusEffect } from '@react-navigation/native';
import useMetrics from '@app/metrics/useMetrics';
import { useGetExtras } from '../queries/useGetExtras';
import Text from '@app/components/Text';
import ScreenScrollContainer from '@app/components/ScreenScrollContainer';
import StickyImageHeader, { useStickyImageHeaderDimensions } from '@app/components/StickyImageHeader';
import ListItems from '@app/components/ListItems2/ListItems';
import { NavScreenProp } from '@app/navigation/stacks/Main';

export default function ProductDetail({ route, navigation }: NavScreenProp<'ProductDetail'>) {
  const { logScreenView } = useMetrics();
  const theme = useTheme();
  const { EXPANDED_HEADER_HEIGHT, size } = useStickyImageHeaderDimensions('m');

  const translationY = useSharedValue(0);
  const scrollHandler = useAnimatedScrollHandler((event) => {
    translationY.value = event.contentOffset.y;
  });

  const bookingReference = route?.params?.bookingReference;
  const productId = route?.params?.productId;

  const { data: products } = useGetExtras({ bookingReference });
  const product = products?.find((p) => p.id === productId);

  useFocusEffect(
    React.useCallback(() => {
      logScreenView('ProductDetail', [productId ?? '']);
      /*  logEvent('ProductDetail', {
        payment_complete: Boolean(paymentCompleted),
      }); */
    }, [logScreenView, productId])
  );

  if (!product) {
    return null;
  }

  const image = product.image ?? product.extras?.[0]?.image;

  return (
    <>
      <StickyImageHeader
        scrollOffset={translationY}
        title={product?.name}
        images={image ? [image] : []}
        size={size}
      />
      <ScreenScrollContainer
        onScroll={scrollHandler}
        animated
        contentContainerStyle={{ paddingTop: EXPANDED_HEADER_HEIGHT }}
      >
        <View style={{ flex: 1, paddingTop: theme.space.l }}>
          {(!!product?.name || !!product?.description) && (
            <Text type="title" style={{ marginVertical: theme.space.s }}>
              {product?.description || product?.name}
            </Text>
          )}
          <ListItems
            items={
              product?.extras?.map((e) => {
                return {
                  id: e?.key ?? '',
                  title: e?.extraName ?? '',
                  image: e?.image,
                  onPress: () => {
                    !!productId &&
                      !!e?.extraCode &&
                      navigation?.navigate('ExtraDetail', {
                        productId,
                        extraKey: e?.key,
                        bookingReference,
                      });
                  },
                  rightIcon: !!e?.voucherIdentifier && 'voucher',
                };
              }) ?? []
            }
          />
        </View>
      </ScreenScrollContainer>
    </>
  );
}

