import Button from '@app/components/Button';
import FormCheckBox from '@app/components/Form/FormCheckBox';
import FormControl from '@app/components/Form/FormControl';
import StickyHeader from '@app/components/StickyHeader';
import Text from '@app/components/Text';
import useScreenSize from '@app/hooks/useScreenSize';
import useTheme from '@app/hooks/useTheme';
import useMetrics from '@app/metrics/useMetrics';
import { NavScreenProp } from '@app/navigation/stacks/Main';
import GenderSelector from '@domains/reservations/reservation/components/GenderSelector';
import { CreateAccountRequest } from 'apis/_generated-identity-api/models';
import { useFormik } from 'formik';
import React, { useState } from 'react';
import { useIntl } from 'react-intl';
import { Alert, Linking, TouchableOpacity, View } from 'react-native';
import { KeyboardAwareScrollView } from 'react-native-keyboard-aware-scroll-view';
import { isValidEmail } from 'utils/validation';
import { useCreateAccount } from '../queries/useCreateAccount';

const CreateAccount = ({ route, navigation }: NavScreenProp<'ForgotPassword'>) => {
  const theme = useTheme();
  const { formatMessage } = useIntl();
  const [processing, setProcessing] = useState(false);
  const { createAccount } = useCreateAccount();
  const { insets } = useScreenSize();
  const { logEvent } = useMetrics();

  type FormFields =
    | 'email'
    | 'gender'
    | 'firstName'
    | 'middleName'
    | 'lastName'
    | 'password'
    | 'passwordConfirmation'
    | 'agreedToTerms';

  type FormikErrors = {
    gender?: string;
    firstName?: string;
    middleName?: string;
    lastName?: string;
    email?: string;
    password?: string;
    passwordConfirmation?: string;
    agreedToTerms?: string;
  };

  const formik = useFormik<Record<FormFields, any>>({
    initialValues: {
      email: '',
      gender: 0,
      firstName: '',
      middleName: '',
      lastName: '',
      password: '',
      passwordConfirmation: '',
      agreedToTerms: false,
    },
    validateOnMount: true,
    validateOnChange: true,
    onSubmit: () => {},
    validate: (values) => {
      const errors: FormikErrors = {};

      if (!values.firstName) {
        errors.firstName = 'no first name';
      }
      if (!values.lastName) {
        errors.lastName = 'no last name';
      }
      if (!values.password || values.password.length < 6) {
        errors.password = 'no password or invalid password';
      }
      if (!values.passwordConfirmation) {
        errors.passwordConfirmation = 'no password confirmation';
      }
      if (
        !!values.passwordConfirmation &&
        !!values.password &&
        values.passwordConfirmation !== values.password
      ) {
        errors.passwordConfirmation = 'password fields not equal';
      }
      if (!values.email) {
        errors.email = 'no email address';
      }
      if (!!values.email && !isValidEmail(values.email)) {
        errors.email = 'incorrect email address';
      }
      if (!values.agreedToTerms) {
        errors.agreedToTerms = 'not accepted terms';
      }
      return errors;
    },
  });

  return (
    <>
      <StickyHeader
        title={formatMessage({ id: 'app.login.createAccount' })}
        showClose
        navigation={navigation}
      />
      <KeyboardAwareScrollView
        style={{ flex: 1, paddingBottom: insets.bottom, backgroundColor: theme.colors.white }}
        enableOnAndroid
        keyboardOpeningTime={Number.MAX_SAFE_INTEGER}
      >
        <View
          style={{
            flex: 1,
            margin: theme.space.l,
          }}
        >
          <GenderSelector
            selected={formik.values.gender}
            onSelect={(value) => {
              formik.setFieldValue('gender', value);
            }}
            showOther={true}
          />
          {/* {!!formik.errors.gender && formik.dirty && (
            <Text color={'attention'} style={{ marginTop: -theme.space.s, marginBottom: theme.space.m }}>
              {formatMessage({ id: 'app.forms.error.gender' })}
            </Text>
          )} */}
          <View style={{ flex: 1, flexDirection: 'row' }}>
            <FormControl
              label={formatMessage({ id: 'app.forms.labels.firstName' })}
              value={formik.values.firstName}
              isValid={true}
              onChangeText={(text) => {
                formik.setFieldValue('firstName', text);
              }}
              style={{ marginRight: theme.space.s }}
              flex={5}
            />
            <FormControl
              label={formatMessage({ id: 'app.forms.labels.middleNameShort' })}
              value={formik.values.middleName}
              isValid={true}
              onChangeText={(text) => {
                formik.setFieldValue('middleName', text);
              }}
              flex={2}
              optional
            />
          </View>
          <FormControl
            label={formatMessage({ id: 'app.forms.labels.lastName' })}
            value={formik.values.lastName}
            isValid={true}
            onChangeText={(text) => {
              formik.setFieldValue('lastName', text);
            }}
          />
          <FormControl
            label={formatMessage({ id: 'app.forms.labels.email' })}
            value={formik.values.email}
            isValid={!formik.errors.email}
            errorMessage={formatMessage({ id: 'app.forms.error.emailaddress' })}
            placeholder={formatMessage({ id: 'app.reservation.companions.emailaddress.placeholder' })}
            onChangeText={(text) => {
              formik.setFieldValue('email', text);
            }}
            textContentType="emailAddress"
          />
          <FormControl
            label={formatMessage({ id: 'app.forms.labels.password' })}
            value={formik.values.password}
            isValid={!formik.errors.password}
            errorMessage={formatMessage({ id: 'app.forms.error.password' })}
            onChangeText={(text) => {
              formik.setFieldValue('password', text);
            }}
            secureTextEntry
            textContentType="newPassword"
          />
          <FormControl
            label={formatMessage({ id: 'app.forms.labels.passwordConfirmation' })}
            value={formik.values.passwordConfirmation}
            isValid={!formik.errors.passwordConfirmation}
            errorMessage={formatMessage({ id: 'app.forms.error.passwordConfirmation' })}
            onChangeText={(text) => {
              formik.setFieldValue('passwordConfirmation', text);
            }}
            secureTextEntry
            textContentType="newPassword"
          />
          <View style={{ marginVertical: theme.space.s, flexDirection: 'row' }}>
            <FormCheckBox
              value={formik.values.agreedToTerms}
              onValueChange={(newValue) => formik.setFieldValue('agreedToTerms', newValue)}
              onFillColor={theme.colors.primary}
              tintColor={theme.colors.light}
              onCheckColor={theme.colors.white}
              tintColors={{ true: theme.colors.primary, false: theme.colors.primary }}
              onTintColor={theme.colors.primary}
              lineWidth={1}
            />
            <Text bold size="s" color="gray" style={{ flex: 1, marginLeft: theme.space.m }}>
              {formatMessage({ id: 'app.login.createAccount.agreeTermsPrivacy' })}
            </Text>
          </View>

          <View style={{ flexDirection: 'row' }}>
            <TouchableOpacity
              style={{ marginBottom: theme.space.m }}
              onPress={() => {
                const url = formatMessage({ id: 'app.settings.termsUrl' });
                Linking.openURL(url);
              }}
              activeOpacity={0.8}
            >
              <Text color="gray" style={{ textDecorationLine: 'underline' }}>
                {formatMessage({ id: 'app.forms.privacy.terms.generalTerms' })}
              </Text>
            </TouchableOpacity>
            <Text style={{ marginHorizontal: theme.space.s }}>|</Text>
            <TouchableOpacity
              style={{ marginBottom: theme.space.m }}
              onPress={() => {
                const url = formatMessage({ id: 'app.settings.privacyUrl' });
                Linking.openURL(url);
              }}
              activeOpacity={0.8}
            >
              <Text color="gray" style={{ textDecorationLine: 'underline' }}>
                {formatMessage({ id: 'app.forms.privacy.terms.privacystatement' })}
              </Text>
            </TouchableOpacity>
          </View>

          <Button
            onPress={async () => {
              if (processing) {
                return;
              }
              setProcessing(true);
              try {
                const request: CreateAccountRequest = {
                  email: formik.values.email,
                  firstName: formik.values.firstName,
                  middleName: formik.values.middleName,
                  lastName: formik.values.lastName,
                  gender: formik.values.gender,
                  password: formik.values.password,
                };
                const result = await createAccount(request);
                if (result.success) {
                  Alert.alert(
                    '',
                    formatMessage({
                      id: 'app.login.createAccount.success',
                    })
                  );
                  logEvent('CreateAccountComplete');
                  navigation.goBack();
                } else {
                  Alert.alert(
                    '',
                    formatMessage({
                      id: 'app.login.serverError', // TODO: specific app.login.createAccount.failed
                    })
                  );
                  logEvent('CreateAccountError', { reason: 'unsuccessful' });
                }
              } catch (e) {
                if (
                  e?.response?.errors?.find(
                    (error: { errorCode: number; message: string }) => error.errorCode === 5
                  )
                ) {
                  Alert.alert(
                    '',
                    formatMessage({
                      id: 'app.login.createAccount.alreadyExists',
                    })
                  );
                  logEvent('CreateAccountError', { reason: 'account_already_exists' });
                } else {
                  Alert.alert(
                    '',
                    formatMessage({
                      id: 'app.login.serverError', // TODO: specific app.login.createAccount.failed
                    })
                  );
                  logEvent('CreateAccountError', { reason: 'server_error' });
                }
              }
              setProcessing(false);
            }}
            style={{
              backgroundColor: theme.colors.secondary,
              marginTop: theme.space.s,
              marginBottom: theme.space.m,
            }}
            disabled={!formik.isValid}
          >
            {processing ? (
              <Button.ActivityIndicator style={{ height: 22 }} />
            ) : (
              <Button.Text color="white">
                {formatMessage({
                  id: 'app.general.send',
                })}
              </Button.Text>
            )}
          </Button>
        </View>
      </KeyboardAwareScrollView>
    </>
  );
};
export default CreateAccount;
