import Button from '@app/components/Button';
import Text from '@app/components/Text';
import Tooltip, { TooltipRefType } from '@app/components/Tooltip';
import useTheme from '@app/hooks/useTheme';
import SaltoKS from '@domains/locks/native';
import Clipboard from '@react-native-clipboard/clipboard';
import React, { useCallback, useContext, useEffect, useRef, useState } from 'react';
import BluetoothStateManager from 'react-native-bluetooth-state-manager';
import { useIntl } from 'react-intl';
import { Alert, AppState, PermissionsAndroid, Platform, View } from 'react-native';
import { CACHE_DIR, ImageCacheContext } from 'utils/image-cache/useExternalImageCache';
import { SaltoContext } from '@app/context/SaltoContextProvider';
import { isLocationEnabled } from 'react-native-device-info';
import useKeyStatus from '@domains/locks/hooks/useKeyStatus';
import { useGetReservation } from '@domains/reservations/reservation/queries/useGetReservation';
import { getBrand } from 'utils/brand';
import { UserContext } from '@app/context/usercontext/UserContextProvider';
import RNFetchBlob from 'react-native-blob-util';
import { BottomSheetContext } from '@app/navigation/bottom-sheet/BottomSheetProvider';
import AsyncStorage from '@react-native-async-storage/async-storage';
import {
  NotificationContext,
  NotificationContextUpdater,
} from '@domains/notification/context/NotificationProvider';

export default function DebugMenu() {
  const theme = useTheme();
  const { formatMessage } = useIntl();
  const { token } = useContext(UserContext);
  const [fcmToken, setFcmToken] = useState('');
  const [suppressionStatus, setSuppressionStatus] = useState(false);
  const [passLibraryStatus, setPassLibraryStatus] = useState(false);
  const [hasMobileKey, setHasMobileKey] = useState(false);
  const [locationCoarsePermission, setLocationCoarsePermission] = useState(false);
  const [locationFinePermission, setLocationFinePermission] = useState(false);
  const [locationEnabled, setLocationEnabled] = useState(false);
  const [bluetoothState, setBluetoothState] = useState('');
  const [numberOfImagesCached, setNumberOfImagesCached] = useState(0);
  const [doorSuccessfullyOpenedBefore, setDoorSuccessfullyOpenedBefore] = useState(false);
  const appState = useRef(AppState.currentState);
  const copiedComponentRef = useRef<TooltipRefType>(null);
  const { deviceId, generatedId, saltoUserToken } = React.useContext(SaltoContext);
  const [numberOfBottomSheetPositionsCached, setNumberOfBottomSheetPositionsCached] = useState(0);
  const { clearBottomSheetPositionCache, getAmountOfBottomSheetPositionsCached } =
    useContext(BottomSheetContext);
  const { clearNotifications } = useContext(NotificationContextUpdater);
  const notifications = useContext(NotificationContext);

  const { data: reservation } = useGetReservation({
    brand: getBrand(theme),
    token,
  });
  const { keyShown, keyReady, keyExpired, accommodationReady } = useKeyStatus({
    bookingReference: reservation?.bookingReference,
  });

  const { clearCache } = useContext(ImageCacheContext);

  const isIOS = Platform.OS === 'ios';

  const refreshSuppressionStatus = async () => {
    const newSuppressionStatus = await SaltoKS.getSuppressionStatus();
    setSuppressionStatus(newSuppressionStatus);
  };
  const refreshPassLibraryStatus = async () => {
    const newPassLibraryStatus = await SaltoKS.getPassLibraryAvailable();
    setPassLibraryStatus(newPassLibraryStatus);
  };
  const refreshHasMobileKey = async () => {
    const newHasMobileKey = await SaltoKS.hasMobileKey();
    setHasMobileKey(newHasMobileKey);
  };
  const refreshNumberOfImagesCached = useCallback(async () => {
    const cacheDir = await RNFetchBlob.fs.exists(CACHE_DIR);
    if (cacheDir) {
      RNFetchBlob.fs
        .ls(CACHE_DIR)
        .then((files) => {
          setNumberOfImagesCached(files.length);
        })
        .catch((error) => {
          console.log(error);
        });
    } else {
      // cache was cleared
      setNumberOfImagesCached(0);
    }
  }, []);

  const refreshNumberOfBottomSheetPositionsCached = useCallback(() => {
    setNumberOfBottomSheetPositionsCached(getAmountOfBottomSheetPositionsCached());
  }, [getAmountOfBottomSheetPositionsCached]);

  const refreshLocationPermission = async () => {
    const _coarsePermission = await PermissionsAndroid.check(
      PermissionsAndroid.PERMISSIONS.ACCESS_COARSE_LOCATION
    );
    const _finePermission = await PermissionsAndroid.check(
      PermissionsAndroid.PERMISSIONS.ACCESS_FINE_LOCATION
    );
    setLocationCoarsePermission(_coarsePermission);
    setLocationFinePermission(_finePermission);
  };
  const refreshLocationEnabled = async () => {
    const _locationEnabled = await isLocationEnabled().then((enabled) => enabled);
    setLocationEnabled(_locationEnabled);
  };

  const refreshBluetoothState = () => {
    BluetoothStateManager.getState().then((_bluetoothState: string) => {
      setBluetoothState(_bluetoothState);
    });
  };

  const refreshDoorSuccessfullyOpenedBeforeState = useCallback(async () => {
    if (!reservation?.bookingReference) {
      return true;
    }
    const storedDoorSuccessfullyOpenedBefore =
      (await AsyncStorage.getItem(reservation?.bookingReference + '_opened')) === 'true';
    setDoorSuccessfullyOpenedBefore(storedDoorSuccessfullyOpenedBefore);
  }, [reservation?.bookingReference]);

  const refreshAllData = useCallback(() => {
    if (isIOS) {
      // iOS only
      refreshSuppressionStatus();
      refreshPassLibraryStatus();
    }
    if (!isIOS) {
      // android only
      refreshLocationPermission();
      refreshLocationEnabled();
    }
    refreshHasMobileKey();
    refreshNumberOfImagesCached();
    refreshNumberOfBottomSheetPositionsCached();
    refreshBluetoothState();
    refreshDoorSuccessfullyOpenedBeforeState();
  }, [
    isIOS,
    refreshDoorSuccessfullyOpenedBeforeState,
    refreshNumberOfBottomSheetPositionsCached,
    refreshNumberOfImagesCached,
  ]);

  // Refetch reservation on foreground
  useEffect(() => {
    const subscription = AppState.addEventListener('change', (nextAppState) => {
      if (appState.current.match(/inactive|background/) && nextAppState === 'active') {
        refreshAllData();
      }
      appState.current = nextAppState;
    });
    refreshAllData();

    return () => {
      subscription.remove();
    };
  }, [refreshAllData]);

  return (
    <View style={{ margin: theme.space.m, marginBottom: theme.space.xxl }}>
      <Text style={{ marginBottom: theme.space.xl }}>
        {
          "You found the secret debug menu! Well done!\nWe use these buttons and data for debugging purposes. There's probably nothing here for you to see, so you'd probably want to close this sheet. Don't worry, the app contains a lot of exciting content. Enjoy your stay!"
        }
      </Text>
      {isIOS && (
        <View>
          <Text type="title" bold>
            Apple pay
          </Text>
          <Text>Pass library status: {passLibraryStatus ? 'available' : 'unavailable'}</Text>
          <Text>Suppression status: {suppressionStatus ? 'suppressed' : 'not suppressed'}</Text>
          <Button
            type="primary"
            onPress={async () => {
              await SaltoKS.suppressApplePay();
              setTimeout(() => {
                refreshSuppressionStatus();
              }, 100);
            }}
            style={{
              marginVertical: theme.space.s,
            }}
          >
            <Button.Text>Suppress Apple Pay</Button.Text>
          </Button>
          <Button
            type="primary"
            onPress={async () => {
              await SaltoKS.enableApplePay();
              setTimeout(() => {
                refreshSuppressionStatus();
              }, 100);
            }}
            style={{
              marginBottom: theme.space.xl,
            }}
          >
            <Button.Text>Enable Apple Pay</Button.Text>
          </Button>
        </View>
      )}
      <Text type="title" bold>
        Images
      </Text>
      <Text>Images cached: {numberOfImagesCached}</Text>
      <Button
        type="primary"
        onPress={async () => {
          await clearCache();
          refreshNumberOfImagesCached();
        }}
        style={{
          marginTop: theme.space.s,
          marginBottom: theme.space.xl,
        }}
      >
        <Button.Text>Clear image cache</Button.Text>
      </Button>

      <Text type="title" bold>
        Bottom sheet
      </Text>
      <Text>Bottom sheet positions cached: {numberOfBottomSheetPositionsCached}</Text>
      <Button
        type="primary"
        onPress={async () => {
          await clearBottomSheetPositionCache();
          refreshNumberOfBottomSheetPositionsCached();
        }}
        style={{
          marginTop: theme.space.s,
          marginBottom: theme.space.xl,
        }}
      >
        <Button.Text>Clear positions cache</Button.Text>
      </Button>
      <Text type="title" bold>
        Notifications
      </Text>
      <Text>Number of notifications on this device: {notifications.length}</Text>
      <Button
        type="primary"
        onPress={async () => {
          Alert.alert(
            'Are you sure to delete all notifications on this device? ',
            "These are all the messages behind the 'bell' button on the start tab.",
            [
              { style: 'cancel', text: formatMessage({ id: 'app.general.cancel' }) },
              {
                style: 'destructive',
                text: formatMessage({ id: 'app.login.delete.confirmation.positive' }),
                onPress: () => {
                  clearNotifications();
                },
              },
            ]
          );
        }}
        style={{
          marginTop: theme.space.s,
          marginBottom: theme.space.xl,
        }}
      >
        <Button.Text>Delete all notifications</Button.Text>
      </Button>
      <Text type="title" bold>
        Firebase
      </Text>
      <View>
        <Tooltip
          text={'FCM token: \n' + formatMessage({ id: 'app.general.copied' })}
          ref={copiedComponentRef}
          additionalArrowOffset={16}
          tooltipVerticalOffset={-48}
        />
        <Button
          type="primary"
          onPress={() => {
            Clipboard.setString(fcmToken);
            copiedComponentRef?.current?.show();
          }}
          style={{
            marginTop: theme.space.s,
            marginBottom: theme.space.xl,
          }}
        >
          <Button.Text>Copy FCM token</Button.Text>
        </Button>
      </View>
      <Text type="title" bold>
        Smart locks
      </Text>
      <Text>deviceId: {deviceId || '-'}</Text>
      <Text>generatedId: {generatedId || '-'}</Text>
      <Text>saltoUserToken: {saltoUserToken ? saltoUserToken?.slice(0, 30) + '...' : '-'}</Text>
      <Text>has mobile key: {hasMobileKey ? 'YES' : 'NO'}</Text>
      <Text />
      <Text>Bluetooth state: {bluetoothState || '-'}</Text>
      {!isIOS && (
        <>
          <Text>Location enabled: {locationEnabled || '-'}</Text>
          <Text>Location coarse permission: {locationCoarsePermission || '-'}</Text>
          <Text>Location fine permission: {locationFinePermission || '-'}</Text>
        </>
      )}
      <Text style={{ marginTop: theme.space.l }}>
        Upcoming reservation: {reservation?.bookingReference || '-'}
      </Text>
      {!!reservation?.bookingReference && (
        <>
          <Text>KeyShown: {keyShown ? 'YES' : 'NO'} (is there gonna be a key)</Text>
          <Text>KeyReady: {keyReady ? 'YES' : 'NO'} (is the key ready to be used)</Text>
          <Text>AccommodationReady: {accommodationReady ? 'YES' : 'NO'} (within stay)</Text>
          <Text>KeyExpired: {keyExpired ? 'YES' : 'NO'} (after stay)</Text>
          <Text>Door successfully opened before: {doorSuccessfullyOpenedBefore ? 'YES' : 'NO'}</Text>
        </>
      )}
    </View>
  );
}
