import React from 'react';
import useTheme from '@hooks/useTheme';
import Text from './Text';
import { useIntl } from 'react-intl';
import { Platform, View } from 'react-native';

interface Props {
  numberOfNights: number;
}

export default function NumberOfNightsBadge({ numberOfNights }: Props) {
  const theme = useTheme();
  const { formatMessage } = useIntl();

  const message = formatMessage({ id: 'app.reservations.numberOfNights' });

  const nightsText = numberOfNights > 9 ? numberOfNights.toString() : '0' + numberOfNights.toString();

  const upperText = message.indexOf('*{0}*') === 0 ? null : message.split('\n*{0}*\n')[0];
  const lowerText = message
    .split('*{0}*\n')[1]
    ?.replace(
      '{1}',
      numberOfNights > 1
        ? formatMessage({ id: 'app.general.night_plural' })
        : formatMessage({ id: 'app.general.night' })
    );

  return (
    <View
      style={{
        position: 'absolute',
        top: theme.space.s,
        right: theme.space.s,
        paddingVertical: theme.space.s,
        borderRadius: 8,
        alignItems: 'center',
        width: 74,
        height: 74,
        display: 'flex',
        flexDirection: 'column',
        justifyContent: 'space-between',
      }}
    >
      {!!upperText && (
        <Text
          color="white"
          size="s"
          style={{ textAlign: 'center' }}
          styleParsed={{
            fontSize: theme.fontSizes.xl.fontSize,
            textAlignVertical: 'bottom',
            fontVariant: ['lining-nums'],
          }}
        >
          {upperText}
        </Text>
      )}

      <Text
        color="white"
        size="xl"
        bold
        type="title"
        style={{
          textAlign: 'center',
          textAlignVertical: 'bottom',
          marginTop: upperText ? (Platform.OS === 'android' ? -8 : -2) : 0,
          marginBottom: -8,
        }}
      >
        {nightsText}
      </Text>

      <Text color="white" size="s" style={{ textAlign: 'center', marginBottom: upperText ? 0 : 2 }}>
        {lowerText}
      </Text>
    </View>
  );
}
