import React from 'react';
import { View, ViewProps } from 'react-native';
import useTheme from '@hooks/useTheme';
import Text from './Text';
import { firstToUpper, formatDayWithoutTime } from 'utils/date';
import { useIntl } from 'react-intl';
import { ActivityDate } from 'apis/_generated-park-api/models';

const PoiDateType = {
  Unknown: 0,
  At: 1,
  From: 2,
  Till: 3,
} as const;

interface Props extends React.PropsWithChildren<ViewProps> {
  dateObject: ActivityDate[];
  compact?: boolean;
}

export default function ActivityBadge({ style, dateObject, compact }: Props) {
  const theme = useTheme();
  const { formatMessage } = useIntl();

  const getText = () => {
    if (!dateObject) {
      return null;
    }
    if (dateObject.length === 1) {
      return !!dateObject[0]?.date && firstToUpper(formatDayWithoutTime(dateObject[0].date));
    }
    const firstDate = dateObject[0];
    const secondDate = dateObject[1];

    if (!firstDate) {
      return null;
    }
    const compactChar = compact ? '\r\n' : ' ';

    switch (firstDate.type) {
      case PoiDateType.From:
      case PoiDateType.Till:
        return firstToUpper(
          `${firstDate.date ? formatDayWithoutTime(firstDate.date) : ''}${compactChar}${formatMessage({
            id: 'app.general.time.till',
          })}${compactChar}${secondDate.date ? formatDayWithoutTime(secondDate.date) : ''}`
        );
      case PoiDateType.At:
        return firstToUpper(
          `${formatMessage({
            id: 'app.general.time.at',
          })} ${firstDate.date ? formatDayWithoutTime(firstDate.date) : ''}`
        );
    }

    return '';
  };

  return (
    <View
      style={[
        {
          borderRadius: 4,
          backgroundColor: theme.colors.light,
          alignSelf: 'baseline',
          paddingHorizontal: 8,
          paddingVertical: 4,
          minWidth: 80,
        },
        style,
      ]}
    >
      <Text style={{ textAlign: 'center' }} type="title" bold color="white" size="s">
        {getText()}
      </Text>
    </View>
  );
}
