import React from 'react';
import { useAnimatedScrollHandler, useSharedValue } from 'react-native-reanimated';
import { Linking, View } from 'react-native';
import TitleAndDescription from '@app/components/DetailScreen/TitleAndDescription';
import { useGetPark } from '../queries/useGetPark';
import useTheme from '@app/hooks/useTheme';
import OpeningHours from '@app/components/DetailScreen/OpeningHours';
import MoreInfo from '@app/components/DetailScreen/MoreInfo';
import { useIntl } from 'react-intl';
import { useGetFacilities } from '@domains/park/at-the-park/queries/useGetFacilities';
import useMetrics from '@app/metrics/useMetrics';
import { useFocusEffect } from '@react-navigation/native';
import { Openinghour } from 'apis/_generated-park-api/models';
import { NavScreenProp } from '@app/navigation/stacks/Main';
import StickyImageHeader, { useStickyImageHeaderDimensions } from '@app/components/StickyImageHeader';
import ScreenScrollContainer from '@app/components/ScreenScrollContainer';
import ListItems, { ListItem } from '@app/components/ListItems2/ListItems';
import Table from '@app/components/Table';
import Text from '@app/components/Text';
import { capitalizedFirstLetter } from 'utils/string';

const ContactAndInfo = ({ navigation, route }: NavScreenProp<'ContactAndInfo'>) => {
  const { formatMessage } = useIntl();
  const { logScreenView, logEvent } = useMetrics();
  const theme = useTheme();
  const parkCode = route?.params?.parkCode;
  const { data: park } = useGetPark({ parkCodeOverride: parkCode });
  const { data: facilities } = useGetFacilities({ parkCodeOverride: parkCode });
  const { EXPANDED_HEADER_HEIGHT, size } = useStickyImageHeaderDimensions('m');

  const translationY = useSharedValue(0);
  const scrollHandler = useAnimatedScrollHandler((event) => {
    translationY.value = event.contentOffset.y;
  });

  useFocusEffect(
    React.useCallback(() => {
      logScreenView('ContactAndInfo');
    }, [logScreenView])
  );

  const wifi = facilities?.find((f) => f.categories?.find((c) => c === 'wifi'));
  const reception = facilities?.find((f) => f.categories?.find((c) => c === 'contactAndInfo'));

  const openingHours = reception?.openingHours;

  if (!park) {
    return null;
  }

  const title = park?.name;

  let contactOptions: ListItem[] = [
    {
      id: 'mail',
      title: formatMessage({ id: 'app.contactandinfo.mail' }),
      subtitle: park?.email,
      localImage: require('@assets/images/blur-images/mail-de-receptie.jpg'),
      blurImage: true,
      icon: 'mail',
      onPress: () => {
        logEvent('ContactMail');
        Linking.openURL('mailto:' + park.email);
      },
    },
    {
      id: 'call',
      title: formatMessage({ id: 'app.contactandinfo.phone' }),
      subtitle: park?.phone,
      localImage: require('@assets/images/blur-images/bel-de-receptie.jpg'),
      blurImage: true,
      icon: 'call',
      onPress: () => {
        logEvent('ContactCall');
        Linking.openURL('tel:' + park.phone);
      },
    },
  ];
  if (park?.whatsapp) {
    contactOptions = [
      {
        id: 'whatsapp',
        title: formatMessage({ id: 'app.contactandinfo.whatsapp' }),
        subtitle: formatMessage({ id: 'app.contactandinfo.whatsapp.sub' }),
        localImage: require('@assets/images/blur-images/whatsapp-de-receptie.jpg'),
        blurImage: true,
        icon: 'whatsapp',
        onPress: () => {
          logEvent('ContactWhatsapp');
          park.whatsapp && Linking.openURL(park.whatsapp);
        },
      },
      ...contactOptions,
    ];
  }

  return (
    <>
      <StickyImageHeader
        scrollOffset={translationY}
        title={formatMessage({ id: 'app.practical.reception.title' })}
        images={reception?.media ? [reception.media] : []}
        size={size}
      />
      <ScreenScrollContainer
        onScroll={scrollHandler}
        animated
        contentContainerStyle={{ paddingTop: EXPANDED_HEADER_HEIGHT }}
      >
        <TitleAndDescription name={title} description={reception?.description}>
          <MoreInfo
            street={park.address?.street ?? ''}
            streetNumber={park.address?.streetNumber}
            zipcode={park.address?.zipcode ?? ''}
            city={park.address?.city ?? ''}
            title={formatMessage({ id: 'app.contactandinfo.address' })}
          />
        </TitleAndDescription>
        {!!openingHours && (
          <OpeningHours openingHours={openingHours ? (openingHours as Openinghour[]) : []} />
        )}
        <ListItems title="Contact opnemen" items={contactOptions} />

        {!!wifi && (
          <View style={{ marginVertical: theme.space.xl }}>
            <Text type="title" size="l" bold>
              {capitalizedFirstLetter(wifi?.name || '')}
            </Text>
            <Text color="dark" style={{ lineHeight: 26 }}>
              {wifi.description}
            </Text>
          </View>
        )}

        {(park.otherPhones?.length || 0) > 0 && (
          <Table
            title={formatMessage({ id: 'app.contactandinfo.importantphonenumbers' })}
            boldLabel={false}
            items={park.otherPhones?.map((phone, _index) => ({
              label: phone?.description ?? '',
              value: phone?.number,
              onValuePress: () => {
                logEvent('CallEmergencyNumber', { emergency_name: phone?.description });
                Linking.openURL('tel:' + phone?.number);
              },
            }))}
          />
        )}
      </ScreenScrollContainer>
    </>
  );
};
export default ContactAndInfo;
