import React from 'react';
import { useIntl } from 'react-intl';
import PolaroidItems, { PolaroidItem } from '../../../../app/components/PolaroidItems/PolaroidItems';
import Text from '../../../../app/components/Text';
import useTheme from '@app/hooks/useTheme';
import { Platform, TouchableOpacity, View } from 'react-native';
import { NavigationContainerRef, useNavigation } from '@react-navigation/native';
import { useGetExtras } from '../queries/useGetExtras';
import { getQuantityText } from '../utils/quantity';
import Icon from '@app/components/Icon';
import ExtrasSVG from '@app/components/PolaroidItems/svg/ExtrasSvg';

interface Props {
  bookingReference?: string;
  parkCode?: string;
  showShowAll?: boolean;
}
export default function ExtrasSlider({ bookingReference, parkCode, showShowAll }: Props) {
  const theme = useTheme();
  const { formatMessage } = useIntl();
  const navigation = useNavigation<NavigationContainerRef<any>>();
  const { data: products } = useGetExtras({ bookingReference });

  let items: PolaroidItem[] =
    products?.map((p) => {
      const singleExtraInProduct = (p?.extras?.length || 0) === 1;
      const quantityText = singleExtraInProduct
        ? p?.extras?.[0] && getQuantityText(p.extras[0], formatMessage)
        : undefined;
      const hasVouchers = !!p.extras?.find((e) => !!e?.voucherIdentifier);

      let title = p.name ?? '';
      if ((p.extras?.length || 0) === 1) {
        title = p.extras?.[0]?.extraName || p.name || '';
      }

      return {
        id: p.id ?? '',
        title,
        image: p.image ?? p.extras?.[0]?.image,
        subtitle: quantityText,
        badgeIcon: hasVouchers ? 'voucher' : undefined,
        onPress: () => {
          const { id } = p;
          if (singleExtraInProduct) {
            navigation.navigate('ExtraDetail', {
              productId: id,
              extraKey: p?.extras?.[0]?.key ?? '',
              bookingReference,
            });
          } else {
            navigation.navigate('ProductDetail', { productId: id, bookingReference });
          }
        },
      };
    }) ?? [];
  if (showShowAll) {
    items = [
      ...items,
      {
        id: 'show-all',
        title: formatMessage({ id: 'app.extra.showmore' }),
        subtitle: '',
        onPress: () => navigation.navigate('Products', { parkCode, bookingReference }),
      },
    ];
  }

  return (
    <View style={{ marginVertical: theme.space.l }}>
      <Text type="title" size="l" bold>
        {formatMessage({ id: 'app.extra.title' })}
      </Text>
      <PolaroidItems
        items={items.map((h, _index) => {
          const isSeeAllItem = h.id === 'show-all';

          return {
            id: h.id ?? '' + h.title ?? '',
            title: h.title ?? '',
            subtitle: h.subtitle,
            image: h.image,
            badgeIcon: h.badgeIcon,
            SvgComponent: isSeeAllItem ? ExtrasSVG : undefined,
            onPress: h.onPress,
          };
        })}
      />
      {showShowAll && (
        <TouchableOpacity
          onPress={() => navigation.navigate('Products', { parkCode, bookingReference })}
          style={{ marginTop: -theme.space.m }} // this is a negative margin to move over de drop shadow of the polaroids
        >
          <Text type="title" color="tertiary" style={{ lineHeight: 26 }} bold>
            {formatMessage({ id: 'app.extra.showmore' })}
            <Icon
              name="chevron-right"
              color="tertiary"
              style={{ marginLeft: 3, top: Platform.OS === 'android' ? 6 : 0 }}
            />
          </Text>
        </TouchableOpacity>
      )}
    </View>
  );
}
