import React, { forwardRef } from 'react';
import { ListItemSingle } from '@app/components/ListItems2/ListItemSingle';
import ItemDivider from '@app/components/ListItems2/ItemDivider';
import { ListItem } from '@app/components/ListItems2/ListItems';
import { useGetUnitTypes } from '@domains/park/queries/useGetUnitTypes';
import PendingTasks from './PendingTasks';
import useChecklist from '@app/hooks/useChecklist';
import { useIntl } from 'react-intl';
import { NavigationContainerRef, useNavigation } from '@react-navigation/native';
import { AllRoutes } from '@app/navigation/stacks/Main';
import { LandalPressableRefType } from '@app/components/LandalPressable';

interface Props {
  index: number;
  total: number;
  parkCode?: string;
  id: string;
  accommodationCode?: string;
  unitCode?: string;
  bookingReference?: string;
  isCamping?: boolean;
}

const ReservationCardAccommodation = forwardRef(
  (
    { index, total, parkCode, id, unitCode, accommodationCode, bookingReference, isCamping }: Props,
    ref: React.Ref<LandalPressableRefType>
  ) => {
    const { formatMessage } = useIntl();
    const navigation = useNavigation<NavigationContainerRef<AllRoutes>>();

    const { data: unitTypes } = useGetUnitTypes({ parkCode });
    const unitType = unitTypes?.find((u) => u.code === accommodationCode);
    const { pendingTasks } = useChecklist({ bookingReference });

    const openReservation = () => {
      if (!bookingReference) return;
      navigation.navigate('Reservation', { bookingReference });
    };

    const item: ListItem = {
      id,
      title: unitCode
        ? (isCamping
            ? formatMessage({ id: 'app.forms.labels.campingNumber' })
            : formatMessage({ id: 'app.forms.labels.houseNumber' })) +
          ' ' +
          unitCode
        : formatMessage({ id: 'app.forms.labels.houseNumberUnknown' }),
      onPress: total > 1 ? openReservation : undefined,
      bottom: pendingTasks ? <PendingTasks pendingTasks={pendingTasks} /> : undefined,
      subtitle: unitType?.code ? unitType?.name + ' ' + unitType?.code : undefined,
      image: unitType?.images?.[0],
    };

    return (
      <>
        <ListItemSingle
          item={item}
          usePlaceholderImage={true}
          useSkiaImage={true}
          useTopRadius={false}
          useBottomRadius={true}
          isFirst={index === 0}
          isLast={index === total}
          ref={ref}
        />
        {index < total - 1 && <ItemDivider />}
      </>
    );
  }
);

export default ReservationCardAccommodation;
