import React, { useCallback, useContext, useEffect, useState } from 'react';
import { UserContext } from '@app/context/usercontext/UserContextProvider';
import { useGetReservation } from '@domains/reservations/reservation/queries/useGetReservation';
import Banner from '@domains/start/components/Banner';
import { NavigationContainerRef, useNavigation } from '@react-navigation/native';
import { useIntl } from 'react-intl';
import { getBrand } from 'utils/brand';
import { getTime, isTomorrow } from 'utils/date';
import useTheme from '../hooks/useTheme';
import ProgressSVG from '@app/components/svg/ProgressSvg';
import { useBottomSheet } from '@app/navigation/bottom-sheet/BottomSheetProvider';
import CheckoutNowSheet from '@domains/sheets/CheckoutNowSheet';

import { BookingStatusType, Reservation } from 'apis/_generated-reservation-api/models';
import useMetrics from '@app/metrics/useMetrics';
import { useGetReservations } from '@domains/reservations/reservation/queries/useGetReservations';
import {
  useCompletedChecklists,
  default as useDigitalCleaningChecklist,
} from '@app/context/ChecklistProvider';
import CheckoutNowSVG from '@app/components/svg/CheckoutNowSVG';
import { useDigitalCleaningItems } from '@domains/sheets/DigitalCleaningSheet';

interface StatusItem {
  bookingReference: string;
  expectedDepartureDate?: number | null | undefined;
  status?: BookingStatusType;
}

export const CheckoutCombiBanner = ({
  combiReference,
  reservation,
}: {
  combiReference: string;
  reservation: Reservation;
}) => {
  const navigation = useNavigation<NavigationContainerRef<any>>();

  const { token } = useContext(UserContext);
  const { formatMessage } = useIntl();

  const theme = useTheme();
  const { logEvent } = useMetrics();
  const { data: reservations } = useGetReservations({
    brand: getBrand(theme),
    token,
  });

  const isDayBeforeDepartureDay =
    !!reservations?.[0].departureDate && isTomorrow(reservations?.[0].departureDate);

  const [items, setItems] = useState<StatusItem[]>([]);

  const digitalCleaningCount = useCompletedChecklists(items.map((i) => i.bookingReference));

  useEffect(() => {
    const items = reservations
      ?.filter((res) => res.combiReference === combiReference)
      .map((res) => ({
        bookingReference: res.bookingReference || '',
      }));
    if (items) setItems(items);
  }, [combiReference, reservations]);

  // Get status for every acco, write to state using callback function
  const statusCallback = useCallback((data: StatusItem) => {
    setItems((i) =>
      i.map((item) => (item.bookingReference === data.bookingReference ? { ...item, ...data } : item))
    );
  }, []);

  const expectedCompleted = items.reduce((acc, i) => (i.expectedDepartureDate ? acc + 1 : acc), 0);

  const departed = items.reduce((acc, i) => (i.status === BookingStatusType.Departed ? acc + 1 : acc), 0);

  const totalCompleted = expectedCompleted + digitalCleaningCount + departed;

  const departureOnly =
    expectedCompleted === items.length && digitalCleaningCount === items.length && items.length > 0;

  // digital cleaning might not be enabled, account for that.
  // check if digital cleaning is active:
  const translatedItems = useDigitalCleaningItems(reservation);
  const taskCountPerAcco = translatedItems?.length > 0 ? 3 : 2;

  const percentageComplete = Math.round((100 / ((items.length || 1) * taskCountPerAcco)) * totalCompleted);

  if (!reservation) return null;

  return (
    <>
      {items.map((item) => (
        <BookingCheckoutStatus
          key={item.bookingReference}
          bookingReference={item.bookingReference}
          statusCallback={statusCallback}
        />
      ))}
      <Banner
        label={formatMessage({
          id: departureOnly
            ? isDayBeforeDepartureDay
              ? 'app.launcher.checkoutNowText.dayBefore'
              : 'app.launcher.checkoutNowText'
            : 'app.reservation.checkoutChecklist.bannerTitle',
        })}
        text={
          departureOnly
            ? formatMessage({
                id: 'app.launcher.checkoutNowText.combi',
              })
            : formatMessage({
                id: 'app.reservation.checkoutChecklist.bannerSubtitle',
              })
        }
        iconSvg={<ProgressSVG progressPercentage={percentageComplete} />}
        onPress={() => {
          logEvent('BannerCheckoutList');
          navigation?.navigate('ReservationsTab');
        }}
        sticky
      />
    </>
  );
};

const BookingCheckoutStatus = ({
  bookingReference,
  statusCallback,
}: {
  bookingReference: string;
  statusCallback: Function;
}) => {
  const { token } = useContext(UserContext);
  const theme = useTheme();
  const { data: reservation } = useGetReservation({
    brand: getBrand(theme),
    token,
    bookingReference,
  });

  useEffect(() => {
    statusCallback({
      bookingReference,
      expectedDepartureDate: reservation?.expectedDepartureDate,
      status: reservation?.status,
    });
  }, [reservation, bookingReference, statusCallback]);

  return null;
};

export const CheckoutBanner = ({ reservation }: { reservation: Reservation }) => {
  const { formatMessage } = useIntl();
  const { logEvent } = useMetrics();
  const { openBottomSheet } = useBottomSheet();
  const navigation = useNavigation<NavigationContainerRef<any>>();

  const isDayBeforeDepartureDay = !!reservation?.departureDate && isTomorrow(reservation?.departureDate);
  const { completed: digitalCleaningCompleted } = useDigitalCleaningChecklist(
    reservation?.bookingReference || ''
  );

  const totalTasks = 3;

  let tasksCompleted = 0;
  if (reservation?.expectedDepartureDate) {
    tasksCompleted++;
  }
  if (digitalCleaningCompleted) {
    tasksCompleted++;
  }

  const percentageComplete = Math.round((100 / totalTasks) * tasksCompleted);

  const title = formatMessage({
    id:
      tasksCompleted === 0
        ? 'app.reservation.checkoutChecklist.bannerTitle'
        : !reservation?.expectedDepartureDate
        ? 'app.reservation.unknownDepartureDate'
        : 'app.launcher.checkoutNow',
  });

  const text =
    tasksCompleted === 0
      ? formatMessage({
          id: 'app.reservation.checkoutChecklist.bannerSubtitle',
        })
      : formatMessage({
          id: isDayBeforeDepartureDay
            ? 'app.launcher.checkoutNowText.dayBefore'
            : 'app.launcher.checkoutNowText',
        }).replace(
          '{0}',
          reservation?.accommodationShouldLeaveDateTime
            ? getTime(reservation?.accommodationShouldLeaveDateTime * 1000)
            : ''
        );

  return (
    <Banner
      label={title}
      text={text}
      onPress={
        tasksCompleted === 2
          ? () => {
              logEvent('BannerCheckoutNow');
              openBottomSheet({
                component: () => <CheckoutNowSheet bookingReference={reservation?.bookingReference ?? ''} />,
                title: formatMessage({ id: 'app.launcher.checkoutNow' }),
                key: 'checkout-now',
              });
            }
          : () => {
              logEvent('BannerCheckoutList');
              navigation?.navigate('Reservation', { bookingReference: reservation?.bookingReference });
            }
      }
      iconSvg={
        tasksCompleted === 2 ? <CheckoutNowSVG /> : <ProgressSVG progressPercentage={percentageComplete} />
      }
      sticky
    />
  );
};
