import React, { useContext, useState } from 'react';
import useTheme from '@app/hooks/useTheme';
import { useIntl } from 'react-intl';
import useMetrics from '@app/metrics/useMetrics';
import { UserContext } from '@app/context/usercontext/UserContextProvider';
import { useGetReservation } from '@domains/reservations/reservation/queries/useGetReservation';
import { getBrand } from 'utils/brand';
import { useMutateExpectedCheckout } from '@domains/reservations/reservation/queries/useMutateExpectedCheckout';
import { Alert, TouchableOpacity, View } from 'react-native';
import Text from '@app/components/Text';
import { useBottomSheet } from '@app/navigation/bottom-sheet/BottomSheetProvider';
import Button from '@app/components/Button';
import { useFormik } from 'formik';
import RadioList, { RadioListRefType } from '@app/components/ListItems2/RadioList';
import useDateOptions from './options/useDateOptions';
import { fromUnixNumber, ReadableDayByDate } from 'utils/date';
import { capitalizedFirstLetter } from 'utils/string';
import ScreenScrollContainer from '@app/components/ScreenScrollContainer';
import moment from 'moment-timezone';
import useTimeSlotOptions from './options/useTimeSlotOptions';
import useTimeOfDayOptions from './options/useTimeOfDayOptions';
import CustomDatePicker from '@app/components/CustomDatePicker';

type FormFields = 'selectedDate' | 'selectedTimeOfDay' | 'selectedTimeSlot';

interface Props {
  bookingReference: string;
}

export default function DepartureTimeSheet({ bookingReference }: Props) {
  const theme = useTheme();
  const { closeBottomSheet } = useBottomSheet();
  const { formatMessage } = useIntl();
  const { logEvent } = useMetrics();
  const { token } = useContext(UserContext);
  const { data: reservation, refetch } = useGetReservation({
    token,
    brand: getBrand(theme),
    bookingReference,
  });
  const [processing, setProcessing] = useState(false);
  const [showDatePicker, setShowDatePicker] = useState(false);

  const radioListRef = React.useRef<RadioListRefType>(null);

  const formik = useFormik<Record<FormFields, any>>({
    initialValues: {
      selectedDate: undefined,
      selectedTimeOfDay: undefined,
      selectedTimeSlot: undefined,
    },
    validateOnMount: true,
    onSubmit: () => {},
    validate: ({ selectedDate, selectedTimeOfDay, selectedTimeSlot }) => {
      if (!selectedDate) {
        return { error: 'no-date' };
      }
      if (!selectedTimeOfDay && !selectedTimeSlot) {
        return { error: 'no-timeslot-or-time' };
      }
      return {};
    },
  });
  const dateOptions = useDateOptions({
    departureDate: reservation?.departureDate,
  });
  const timeSlotOptions = useTimeSlotOptions(reservation?.accommodationShouldLeaveDateTime);
  const timeOfDayOptions = useTimeOfDayOptions({
    departureDate: reservation?.departureDate,
    selectedDepartureDate: formik.values.selectedDate,
  });

  const timeSlotUsed =
    !!formik.values.selectedDate && formik.values.selectedDate === reservation?.departureDate;

  React.useEffect(() => {
    logEvent('DepartureTimeBegin');
  }, [logEvent]);

  const { updateExpectedCheckout } = useMutateExpectedCheckout({ bookingReference });

  const otherDateSelected =
    formik.values.selectedDate && !dateOptions.find((d) => d.date === formik.values.selectedDate);

  return (
    <ScreenScrollContainer
      contentContainerStyle={{
        marginTop: theme.space.l,
        alignItems: 'center',
      }}
      topPadding={false}
    >
      <View style={{}}>
        <Text style={{ textAlign: 'center', marginBottom: theme.space.m }} bold>
          {formatMessage({ id: 'app.reservation.unknownDepartureDateTitle' }).replace(
            '{0}',
            reservation?.mainTenant?.firstName || ''
          )}
        </Text>
        <Text
          type="body"
          style={{ textAlign: 'center', lineHeight: 26, marginBottom: theme.space.xl }}
          color="dark"
        >
          {formatMessage({ id: 'app.reservation.unknownDepartureDateText' })}
        </Text>
        <RadioList
          ref={radioListRef}
          title={formatMessage({ id: 'app.reservation.expectedText' })}
          centerTitle
          items={dateOptions.map((o) => ({
            label: o.other
              ? `${capitalizedFirstLetter(formatMessage({ id: 'app.general.other' }))}${
                  otherDateSelected
                    ? ': ' +
                      capitalizedFirstLetter(
                        ReadableDayByDate(fromUnixNumber(formik.values.selectedDate * 1000), true, false)
                      )
                    : ''
                }`
              : capitalizedFirstLetter(ReadableDayByDate(fromUnixNumber(o.date * 1000), true, false)),
            value: o.date,
          }))}
          onSelect={(value) => {
            if (!value) {
              setShowDatePicker(true);
              return;
            }

            formik.setFieldValue('selectedDate', value);
          }}
        />
        {showDatePicker && (
          <CustomDatePicker
            minimumDate={
              reservation?.arrivalDate ? fromUnixNumber(reservation.arrivalDate * 1000) : undefined
            }
            maximumDate={
              reservation?.departureDate ? fromUnixNumber(reservation.departureDate * 1000) : undefined
            }
            initialValue={
              formik.values.selectedDate ? formik.values.selectedDate : reservation?.departureDate
            }
            onChange={(selectedDate) => {
              setShowDatePicker(false);
              if (selectedDate) {
                formik.setFieldValue('selectedDate', selectedDate);

                const existingOption = dateOptions.findIndex((d) => d.date === selectedDate);
                if (existingOption > -1) {
                  radioListRef?.current?.setSelected(existingOption);
                }
              }
            }}
            onClose={() => {
              setShowDatePicker(false);
            }}
          />
        )}
        {!!formik.values.selectedDate && (
          <>
            {timeSlotUsed ? (
              <RadioList
                title={formatMessage({ id: 'app.reservation.expectedText' })}
                centerTitle
                items={timeSlotOptions.map((o) => ({
                  label: o.label,
                  value: o.value,
                }))}
                onSelect={(value) => formik.setFieldValue('selectedTimeSlot', value)}
              />
            ) : (
              <RadioList
                title={formatMessage({ id: 'app.reservation.expectedText' })}
                centerTitle
                items={timeOfDayOptions.map((o) => ({
                  label: capitalizedFirstLetter(o.label),
                  value: o.value,
                }))}
                onSelect={(value) => formik.setFieldValue('selectedTimeOfDay', value)}
              />
            )}
          </>
        )}
      </View>
      <View
        style={{
          width: '100%',
        }}
      >
        <Button
          disabled={processing || !formik.isValid}
          onPress={async () => {
            const date = moment(formik.values.selectedDate * 1000).format('DD/MM/YYYY');
            const time = timeSlotUsed ? formik.values.selectedTimeSlot : formik.values.selectedTimeOfDay;
            const departureDateTime = moment(date + ' ' + time, 'DD/MM/YYYY HH:mm').unix();

            if (!formik.values.selectedDate) return false;
            try {
              setProcessing(true);
              const response = await updateExpectedCheckout({
                bookingReference: reservation?.bookingReference,
                uid: token,
                departureTime: departureDateTime,
              });
              if (!response.success) {
                !!response.message && Alert.alert(response.message);
              } else {
                logEvent('DepartureTimeComplete');
              }
              await refetch();
              closeBottomSheet();
              setProcessing(false);
            } catch (error) {
              Alert.alert(formatMessage({ id: 'app.forms.tablebooking.error.generic' }));
              logEvent('DepartureTimeError', { reason: error });
              setProcessing(false);
            }
          }}
          type="primary"
          style={{ backgroundColor: theme.colors.secondary }}
        >
          {processing ? (
            <Button.ActivityIndicator />
          ) : (
            <Button.Text color="white" style={{ lineHeight: 20 }}>
              {formatMessage({ id: 'app.reservation.expectedCheckoutSubmit' })}
            </Button.Text>
          )}
        </Button>
      </View>
      <TouchableOpacity
        activeOpacity={0.8}
        onPress={closeBottomSheet}
        style={{ marginTop: theme.space.m }}
        hitSlop={{ top: 10, bottom: 10, left: 10, right: 10 }}
      >
        <Text color="gray" bold>
          {formatMessage({ id: 'app.general.cancel' })}
        </Text>
      </TouchableOpacity>
    </ScreenScrollContainer>
  );
}
