import React, { useState } from 'react';
import Text from '../../../../app/components/Text';
import useTheme from '@app/hooks/useTheme';
import { useWindowDimensions, View } from 'react-native';
import { LandalPressable } from '@app/components/LandalPressable';
import { Canvas, Image, LinearGradient, Rect, useImage, vec } from '@shopify/react-native-skia';
import { useExternalImageCache } from 'utils/image-cache/useExternalImageCache';
import { Source } from 'react-native-fast-image';
import useBadge from '@app/hooks/useBadge';

interface Props {
  title?: string;
  subtitle?: string;
  onPress: () => void;
  localImage?: Source;
  image?: string;
}
export default function DigitalFunItem({ title, subtitle, onPress, image, localImage }: Props) {
  const [showSkia, setShowSkia] = useState(false);
  const { width: screenWidth } = useWindowDimensions();
  const [computedWidth, setComputedWidth] = useState(0);
  const theme = useTheme();

  const imageSize = computedWidth;

  const cachedImage = useExternalImageCache(image);
  const fallbackImage = require('@assets/images/image-not-available.jpg');

  const skiaImage = useImage(cachedImage ?? localImage ?? fallbackImage);

  const { reactBadge, skiaBadge } = useBadge({
    icon: 'exit-app',
    y: imageSize - 32,
    borderRadius: 8,
    space: 's',
    containerSize: imageSize,
  });

  return (
    <LandalPressable
      style={{
        height: imageSize,
        borderRadius: 12,
        marginRight: theme.space.s,
        flex: 1,
        maxWidth: screenWidth / 2 - theme.space.s * 2,
      }}
      onPress={onPress}
    >
      <View
        style={{
          width: '100%',
          height: imageSize,
          borderRadius: 8,
          overflow: 'hidden',
        }}
      >
        <View
          onLayout={(event) => {
            var { width: layoutWidth } = event.nativeEvent.layout;
            if (layoutWidth <= 0) return;
            setComputedWidth(layoutWidth);
            setShowSkia(true);
          }}
        >
          {showSkia && (
            <>
              <Canvas style={{ width: imageSize + 2, height: imageSize + 2 }}>
                {!!skiaImage && (
                  <>
                    <Image
                      x={0}
                      y={0}
                      width={imageSize + 2}
                      height={imageSize + 2}
                      image={skiaImage}
                      fit="cover"
                    />
                    <Rect x={0} y={0} width={imageSize + 2} height={imageSize + 2}>
                      <LinearGradient
                        start={vec(0, 0)}
                        end={vec(0, imageSize)}
                        colors={['rgba(0,0,0,0)', 'rgba(0,0,0,0)', 'rgba(0,0,0,0.6)']}
                      />
                    </Rect>
                    {!!skiaBadge && skiaBadge}
                  </>
                )}
              </Canvas>
            </>
          )}
          {!!reactBadge && reactBadge}
        </View>
      </View>
      <View
        style={{
          position: 'absolute',
          bottom: 0,
          margin: theme.space.m,
          marginBottom: theme.space.m - 2, // optical line-height correction to align with the icon
        }}
      >
        <Text bold color="white">
          {title}
        </Text>
        <Text color="white">{subtitle}</Text>
      </View>
    </LandalPressable>
  );
}
