import useCulture from '@app/hooks/useCulture';
import { FacilityMeta } from 'apis/_generated-park-api/models';
import { AxiosError } from 'axios';
import { gql, GraphQLClient } from 'graphql-request';
import Config from 'react-native-config';
import { UseQueryOptions, useQuery } from 'react-query';

const key = 'getfacilitymetadata';
type FacilitybookingMetaDataProps = {
  options?: Omit<UseQueryOptions<FacilityMeta, AxiosError>, typeof key>;
  reservationId: string;
};

export function useGetFacilityMetaData(props: FacilitybookingMetaDataProps) {
  const { reservationId, options } = props;
  const { reserveLanguageCode } = useCulture();

  return useQuery(
    [key, { reservationId }],
    async () => {
      const graphQLClient = new GraphQLClient(Config.PARK_API_URL);
      const query = gql`
        query facilityMeta {
          facilityMeta(reservationId: "${reservationId}", cultureCode: "${reserveLanguageCode}") {
            amount
            amountMaximum
            amountMinimum
            description
            placesSingle
            placesPlural
            daysInAdvanceMax
          }
        }
      `;
      const result = await graphQLClient.rawRequest<{ facilityMeta: FacilityMeta }>(query);
      return result?.data?.facilityMeta;
    },
    options
  );
}
