import { Location, Park } from 'apis/_generated-park-api/models';
import { getDistance } from 'geolib';

export const getDistanceText = (park?: Park, location?: Location) => {
  if (!park?.latitude || !park?.longitude || !location?.latitude || !location?.longitude) return '';

  return (
    (Math.round(
      getDistance(
        { latitude: park?.latitude, longitude: park?.longitude },
        { latitude: location?.latitude, longitude: location?.longitude }
      ) / 1000
    ) || 0) + ' km'
  );
};
