import moment from 'moment-timezone';
import { useCallback, useMemo } from 'react';
import { getAPIDate } from 'utils/date';
import { Activity } from 'apis/_generated-park-api/models';

const GroupActivityByDate = ({
  data,
  startDate,
  amountOfDays,
}: {
  data: Activity[] | undefined;
  startDate: string;
  amountOfDays: number;
}) => {
  const getActivitiesForDate = useCallback(
    (date: Date) => {
      return data?.filter((d) => d.time?.date === getAPIDate(date));
    },
    [data]
  );

  const getNextDateWithActivities = (date: Date, dates: { date: Date; hasActivities: boolean }[]) => {
    const index = dates?.map((d) => d.date).indexOf(date);
    for (let i = index; i < dates.length; i++) {
      if (dates[i].hasActivities) {
        return dates[i].date;
      }
    }
    return null;
  };

  const allDates = useMemo(() => {
    let result: { date: Date; hasActivities: boolean }[] = [];
    let foundLastActivity = false;

    for (let reverse = amountOfDays - 1; reverse >= 0; reverse--) {
      const date = moment.utc(startDate).add(reverse, 'day').toDate();

      const hasActivities = !!getActivitiesForDate(date)?.[0];

      if (!!hasActivities && !foundLastActivity) {
        foundLastActivity = true;
      }

      if (foundLastActivity) {
        result.unshift({ date, hasActivities });
      }
    }

    if (result.length === 0) {
      result = [{ date: new Date(), hasActivities: false }];
    }
    return result;
  }, [amountOfDays, getActivitiesForDate, startDate]);

  return { allDates, getActivitiesForDate, getNextDateWithActivities };
};

export default GroupActivityByDate;
