import { useGetFacilities } from '@domains/park/at-the-park/queries/useGetFacilities';

const useGoodToKnowItems = (parkCode?: string) => {
  const { data: facilities, isLoading } = useGetFacilities({ parkCodeOverride: parkCode });
  const hasGoodToKnowItems = !!facilities?.find((f) => f?.categories?.find((c) => c === 'goodToKnow'));
  return { hasGoodToKnowItems };
};

export default useGoodToKnowItems;
