import React, { PropsWithChildren } from 'react';
import Text, { TextProps } from '@components/Text';
import { ActivityIndicator, ViewStyle, View, TouchableOpacity, Platform } from 'react-native';
import useTheme from '@app/hooks/useTheme';
import Icon, { IconProps } from './Icon';
import { GenericTouchableProps } from 'react-native-gesture-handler/lib/typescript/components/touchables/GenericTouchable';
import { LandalPressable } from './LandalPressable';

type ButtonType = 'primary' | 'secondary' | 'tertiary' | 'transparent' | 'transparentLogin';
interface Props extends PropsWithChildren<GenericTouchableProps> {
  color?: string;
  type?: ButtonType;
  isInModal?: boolean;
  activeOpacity?: number;
}

const ButtonContext = React.createContext<ButtonType>('primary');

function useButtonContext() {
  const context = React.useContext(ButtonContext);
  if (!context) {
    throw new Error('Button compound components cannot be rendered outside the Button component');
  }
  return context;
}

export default function Button({
  children,
  type = 'primary',
  style,
  isInModal,
  activeOpacity,
  ...props
}: Props) {
  const theme = useTheme();

  // FIX: On Android, the react-native-gesture-handler TouchableOpacity does not work in a modal.
  const Touchable = isInModal ? TouchableOpacity : LandalPressable;

  const primaryButtonStyle: ViewStyle = {
    backgroundColor: theme.colors.tertiary,
    justifyContent: 'center',
  };
  const tertiaryButtonStyle: ViewStyle = {
    borderColor: theme.colors.light,
    borderWidth: 1,
    marginRight: 12,
    paddingVertical: 8,
    paddingHorizontal: 16,
    flexDirection: 'column',
  };
  const transparentButtonStyle: ViewStyle = {
    backgroundColor: 'transparent',
    borderColor: theme.colors.white,
    borderWidth: 1,
    paddingVertical: 8,
    paddingHorizontal: 16,
    alignItems: 'center',
  };

  const transparentLoginButtonStyle: ViewStyle = {
    backgroundColor: '#00000030',
    borderColor: theme.colors.white,
    borderWidth: 1,
    alignItems: 'center',
  };

  return (
    <ButtonContext.Provider value={type}>
      <Touchable activeOpacity={activeOpacity} {...props}>
        <View
          style={[
            {
              backgroundColor: theme.colors.white,
              flexDirection: 'row',
              height: 50,
              alignItems: 'center',
              borderRadius: 8,
              opacity: props.disabled ? 0.4 : 1,
            },
            type === 'primary' && primaryButtonStyle,
            type === 'tertiary' && tertiaryButtonStyle,
            type === 'transparent' && transparentButtonStyle,
            type === 'transparentLogin' && transparentLoginButtonStyle,
            style,
          ]}
        >
          {children}
        </View>
      </Touchable>
    </ButtonContext.Provider>
  );
}

function ButtonText({ children, style, ...rest }: TextProps) {
  const type = useButtonContext();
  let color = 'body' as TextProps['color'];

  let flex = 0;
  if (type === 'secondary' || type === 'transparentLogin') {
    color = 'gray';
    flex = 1;
  }
  if (type === 'primary') {
    color = 'white';
  }

  return (
    <Text
      bold
      color={color}
      style={[
        {
          paddingHorizontal: 4,
          paddingVertical: 2,
          flex: flex || (Platform.OS === 'web' ? undefined : 0),
          textAlign: 'center',
          lineHeight: 26,
        },
        style,
      ]}
      {...rest}
    >
      {children}
    </Text>
  );
}

function ButtonIcon({ style, size, ...rest }: IconProps) {
  return (
    <Icon
      size={size || 's'}
      color={'black'}
      style={[style, { paddingHorizontal: 4, paddingVertical: 4 }]}
      {...rest}
    />
  );
}

function ButtonActivityIndicator({ style }: { style?: ViewStyle }) {
  // TODO: new loader without the pulse
  return <ActivityIndicator size="small" color={'white'} style={style} />;
}

Button.Text = ButtonText;
Button.Icon = ButtonIcon;
Button.ActivityIndicator = ButtonActivityIndicator;
