import Button from '@app/components/Button';
import FormControl from '@app/components/Form/FormControl';
import Text from '@app/components/Text';
import useTheme from '@app/hooks/useTheme';
import React, { PropsWithChildren, useState } from 'react';
import { useIntl } from 'react-intl';
import { TouchableOpacity, View } from 'react-native';
import { usePopup } from './PopupProvider';

interface Props {
  title: string;
  text: string;
  onClose?: () => void;
  onCTA?: (values: {}, setProcessing: Function) => void;
  ctaText?: string;
  closeOnCTA?: boolean;
  showClose?: boolean;
  fields?: any[];
}

const PopupContent = ({
  title,
  text,
  onClose,
  onCTA,
  ctaText,
  closeOnCTA,
  showClose,
  fields,
  children,
}: PropsWithChildren<Props>) => {
  const { formatMessage } = useIntl();
  const theme = useTheme();
  const { closePopup } = usePopup();
  const [processing, setProcessing] = useState<boolean>(false);

  const [fieldValues, setFieldValues] = useState<{ [key: string]: string }>(
    fields?.reduce((acc, { id, value }) => ({ ...acc, [id]: value }), {}) || {}
  );

  const updateFieldValue = (id: string, value: string) => {
    setFieldValues((values) => ({ ...values, [id]: value }));
  };

  return (
    <View
      style={{
        marginVertical: theme.space.l,
        alignItems: 'center',
      }}
    >
      <>
        <View
          style={{
            paddingHorizontal: theme.space.l,
          }}
        >
          <Text style={{ textAlign: 'center', marginBottom: theme.space.m }} size="l" bold>
            {title}
          </Text>
          <Text type="body" style={{ textAlign: 'center', lineHeight: 26 }} color="dark" useParser>
            {text}
          </Text>
        </View>
        <View style={{ width: '100%', paddingHorizontal: theme.space.l, alignItems: 'center' }}>
          {fields?.map((field) => {
            return (
              <FormControl
                key={field.id}
                value={fieldValues[field.id] || ''}
                isValid={true}
                onChangeText={(value) => {
                  updateFieldValue(field.id, value);
                }}
                autoCorrect={false}
                textContentType="none"
                autoCapitalize="none"
                returnKeyType="send"
                style={{
                  marginTop: theme.space.m,
                  width: field.width || '100%',
                  marginHorizontal: 'auto',
                }}
                optional
                textAlign={field.textAlign || 'left'}
              />
            );
          })}
        </View>

        {children}

        {!!ctaText && (
          <View
            style={{
              width: '100%',
              paddingHorizontal: theme.space.l,
              marginTop: theme.space.l,
            }}
          >
            <Button
              onPress={() => {
                onCTA?.(fieldValues, setProcessing);
                closeOnCTA && closePopup();
              }}
              type="primary"
              style={{ backgroundColor: theme.colors.secondary }}
            >
              {processing ? (
                <Button.ActivityIndicator />
              ) : (
                <Button.Text color="white" style={{ lineHeight: 20 }}>
                  {ctaText}
                </Button.Text>
              )}
            </Button>
          </View>
        )}
        {showClose && (
          <TouchableOpacity
            activeOpacity={0.8}
            onPress={() => {
              closePopup();
              onClose?.();
            }}
            style={{ marginTop: theme.space.m }}
            hitSlop={{ top: 10, bottom: 10, left: 10, right: 10 }}
          >
            <Text color="gray" bold>
              {formatMessage({ id: 'app.general.cancel' })}
            </Text>
          </TouchableOpacity>
        )}
      </>
    </View>
  );
};
export default PopupContent;
