import React, { Fragment, useContext, useMemo, useState } from 'react';
import { Platform, TouchableOpacity, View } from 'react-native';
import Button from '@app/components/Button';
import useTheme from '@app/hooks/useTheme';
import Text from '@app/components/Text';
import { useIntl } from 'react-intl';
import useMetrics from '@app/metrics/useMetrics';
import { useGetReservation } from '@domains/reservations/reservation/queries/useGetReservation';
import { getBrand } from 'utils/brand';
import { UserContext } from '@app/context/usercontext/UserContextProvider';
import { useBottomSheet } from '@app/navigation/bottom-sheet/BottomSheetProvider';
import CheckList, { CheckListItem } from '@app/components/ListItems2/CheckList';
import ItemDivider from '@app/components/ListItems2/ItemDivider';
import LandalImage from '@app/components/LandalImage';
import Video from 'react-native-video';
import useChecklist from '@app/context/ChecklistProvider';
import { Reservation } from 'apis/_generated-reservation-api/models';

// cleaning steps: save with 'bookingReference as key
interface Props {
  bookingReference: string;
}

export const useDigitalCleaningItems = (reservation?: Reservation) => {
  const { formatMessage, messages } = useIntl();

  const translatedItems = useMemo<string[]>(() => {
    return (
      reservation?.digitalCleaningTasks?.filter((item) => {
        const id = 'app.reservation.digitalCleaning.items.' + item;
        return !!messages[id];
      }) || []
    );
  }, [messages, reservation?.digitalCleaningTasks]);
  return translatedItems;
};

export default function DigitalCleaningSheet({ bookingReference }: Props) {
  const theme = useTheme();
  const { formatMessage, messages } = useIntl();
  const { closeBottomSheet } = useBottomSheet();
  const { logEvent } = useMetrics();
  const { token } = useContext(UserContext);
  const { data: reservation } = useGetReservation({
    token,
    brand: getBrand(theme),
    bookingReference,
  });

  const translatedItems = useDigitalCleaningItems(reservation);

  const [completedSheet, showCompletedSheet] = useState(false);

  const { checkedItems, completed, checkItem, checkAll, setCompleted } = useChecklist(bookingReference);

  const complete = () => {
    setCompleted();
    showCompletedSheet(true);
  };

  return (
    <View
      style={{
        paddingHorizontal: theme.space.l,
        paddingTop: theme.space.l,
        paddingBottom: theme.space.l,
      }}
    >
      {completedSheet ? (
        <>
          <View
            style={{
              width: 200,
              height: 200,
              borderRadius: 100,
              alignSelf: 'center',
              overflow: 'hidden',
              marginTop: theme.space.l,
            }}
          >
            {theme.type !== 'hofvansaksen' ? (
              <Video
                source={require('@app/assets/videos/bollo-confetti.mp4')}
                style={{ width: 200, height: 200 }}
                resizeMode="cover"
                repeat
              />
            ) : (
              <LandalImage
                style={{
                  width: 200,
                  height: 200,
                }}
                localSource={require('@app/assets/images/hsn/hsn-feedback.png')}
              />
            )}
          </View>

          <View style={{ flex: Platform.OS === 'web' ? undefined : 0 }}>
            <Text type="title" bold size="xl" style={{ marginTop: theme.space.l, textAlign: 'center' }}>
              {formatMessage({
                id: 'app.reservation.digitalCleaning.thankYou',
              })}
            </Text>
            <Text
              type="body"
              color="dark"
              style={{
                lineHeight: theme.fontSizes.m.lineHeight,
                marginTop: theme.space.l,
                textAlign: 'center',
              }}
            >
              {formatMessage({
                id: 'app.reservation.digitalCleaning.thankYouText',
              })}
            </Text>
            <Text
              type="body"
              color="dark"
              style={{
                lineHeight: theme.fontSizes.m.lineHeight,
                fontStyle: 'italic',
                marginVertical: theme.space.xl,
                textAlign: 'center',
              }}
            >
              {formatMessage({
                id: 'app.reservation.digitalCleaning.noWorries',
              })}
            </Text>
          </View>
          <Button
            style={{
              backgroundColor: theme.colors.tertiary,
              marginBottom: 0,
            }}
            onPress={closeBottomSheet}
          >
            <Button.Text color="white" style={{ lineHeight: 20 }}>
              {formatMessage({
                id: !completed ? 'app.reservation.digitalCleaning.completed' : 'app.general.close',
              })}
            </Button.Text>
          </Button>
        </>
      ) : (
        <>
          <Text
            type="body"
            style={{ textAlign: 'center', lineHeight: 26, marginBottom: theme.space.xl }}
            color="dark"
          >
            {formatMessage({ id: 'app.reservation.digitalCleaning.text' })}
          </Text>
          <CheckList>
            {translatedItems.map((item, index, items) => (
              <Fragment>
                <CheckListItem
                  item={item}
                  key={index}
                  isFirst={index === 0}
                  isLast={index === items.length - 1}
                  checked={checkedItems.includes(item)}
                  onChange={() => checkItem(item)}
                  disabled={completed}
                />

                {index < items.length - 1 && <ItemDivider />}
              </Fragment>
            ))}
          </CheckList>
          {!completed && (
            <Button
              type="primary"
              onPress={() => checkAll(translatedItems)}
              style={{
                backgroundColor: 'transparent',
              }}
              disabled={translatedItems.length === checkedItems.length}
            >
              <Button.Icon name="check-mark-circular" size="s" color="tertiary" />
              <Button.Text color="tertiary">
                {formatMessage({ id: 'app.reservation.digitalCleaning.checkAll' })}
              </Button.Text>
            </Button>
          )}

          <Button
            style={{
              backgroundColor: completed ? theme.colors.tertiary : theme.colors.secondary,
              marginTop: completed ? theme.space.xl : theme.space.l,
              marginBottom: completed ? theme.space.l : 0,
            }}
            disabled={translatedItems.length !== checkedItems.length}
            onPress={completed ? closeBottomSheet : complete}
          >
            <Button.Text color="white" style={{ lineHeight: 20 }}>
              {formatMessage({
                id: !completed ? 'app.reservation.digitalCleaning.completed' : 'app.general.close',
              })}
            </Button.Text>
          </Button>
          {!completed && (
            <TouchableOpacity
              activeOpacity={0.8}
              onPress={closeBottomSheet}
              style={{ marginTop: theme.space.m, alignItems: 'center', paddingBottom: theme.space.l }}
              hitSlop={{ top: 10, bottom: 10, left: 10, right: 10 }}
            >
              <Text color="gray" bold>
                {formatMessage({ id: 'app.general.cancel' })}
              </Text>
            </TouchableOpacity>
          )}
        </>
      )}
    </View>
  );
}
