import React, { useState } from 'react';
import { Modal, View } from 'react-native';
import DatePicker from 'react-native-date-picker';
import { fromUnixNumber, toUnixNumber } from 'utils/date';
import { useIntl } from 'react-intl';
import Button from './Button';
import useTheme from '@app/hooks/useTheme';
import useCulture from '@app/hooks/useCulture';
import Popup from './Popup';

interface Props {
  onChange?: (value: number, index?: number) => void; // unix number
  onClose: () => void;
  initialValue?: number;
  minimumDate?: Date;
  maximumDate?: Date;
}

export default function CustomDatePicker({
  initialValue,
  onChange,
  onClose,
  minimumDate,
  maximumDate,
}: Props) {
  const { formatMessage } = useIntl();
  const theme = useTheme();
  const { cultureCode } = useCulture();
  const [selectedDate, setSelectedDate] = useState<number>(
    initialValue || toUnixNumber(new Date().toString())
  );

  return (
    <Modal transparent animationType="fade">
      <Popup>
        <View
          style={{
            marginHorizontal: theme.space.s,
            alignItems: 'center',
          }}
        >
          <DatePicker
            mode="date"
            minimumDate={minimumDate}
            maximumDate={maximumDate ?? new Date()}
            date={fromUnixNumber(selectedDate * 1000)}
            locale={cultureCode ?? 'nl-NL'}
            onDateChange={(newSelectedDate) => {
              if (newSelectedDate) {
                setSelectedDate(toUnixNumber(newSelectedDate.toUTCString()));
              }
            }}
          />
        </View>
        <View
          style={{
            flexDirection: 'row',
            marginHorizontal: theme.space.m,
            justifyContent: 'space-between',
          }}
        >
          <Button
            onPress={async () => {
              onClose();
            }}
            style={{
              backgroundColor: 'transparent',
              borderColor: theme.colors.light,
              borderWidth: 1,
              borderRadius: 40,
              paddingHorizontal: theme.space.xl,
            }}
            isInModal
          >
            <Button.Text type="title" color="gray">
              {formatMessage({ id: 'app.general.cancel' })}
            </Button.Text>
          </Button>
          <Button
            onPress={async () => {
              if (selectedDate) {
                onChange?.(selectedDate);
              }
            }}
            style={{
              backgroundColor: theme.colors.secondary,
              borderRadius: 40,
              paddingHorizontal: theme.space.xl,
            }}
            isInModal
            disabled={!selectedDate}
          >
            <Button.Text type="title" color="white">
              {formatMessage({ id: 'app.forms.labels.pick' })}
            </Button.Text>
          </Button>
        </View>
      </Popup>
    </Modal>
  );
}
