import useTheme from '@app/hooks/useTheme';
import React from 'react';
import { View } from 'react-native';
import Icon from './Icon';
import { LandalPressable, LandalPressableProps } from './LandalPressable';

interface Props extends LandalPressableProps {
  icon?: string;
}

const VISUAL_SIZE = 24;
const FAKE_HITSLOP = 20;

export default function MiniButton({ icon, style, ...props }: Props) {
  const theme = useTheme();

  return (
    <LandalPressable
      style={[
        {
          width: VISUAL_SIZE + FAKE_HITSLOP,
          height: VISUAL_SIZE + FAKE_HITSLOP,
          alignItems: 'center',
          justifyContent: 'center',
          //backgroundColor: 'rgba(255,0,0,0.5)', debug: to see the hitslop
          margin: -FAKE_HITSLOP / 2,
        },
        style,
      ]}
      //hitSlop={{ top: 20, bottom: 20, left: 20, right: 20 }} We disabled this hitlop, because it is not working on Android with our pressable implementation.
      {...props}
    >
      <View
        style={{
          width: VISUAL_SIZE,
          height: VISUAL_SIZE,
          backgroundColor: theme.colors.shade,
          alignItems: 'center',
          justifyContent: 'center',
          borderRadius: 8,
        }}
      >
        {!!icon && <Icon name={icon} size="s" color="white" />}
      </View>
    </LandalPressable>
  );
}
