import React, { PropsWithChildren } from 'react';
import { ViewStyle } from 'react-native';
import { TouchableWithoutFeedback } from 'react-native-gesture-handler';
import Animated, { Easing, FadeIn, FadeOut } from 'react-native-reanimated';
import { POPUP_ANIMATION_ENTERING_DURATION, POPUP_ANIMATION_EXITING_DURATION } from './PopupProvider';

interface Props {
  closePopup: () => void;
  style?: ViewStyle;
}

const Backdrop = ({ closePopup, children, style }: PropsWithChildren<Props>) => {
  return (
    <Animated.View
      style={[
        {
          position: 'absolute',
          width: '100%',
          height: '100%',
          zIndex: 20,
          backgroundColor: 'rgba(0, 0, 0, 0.4)',
        },
        style,
      ]}
      entering={FadeIn.duration(POPUP_ANIMATION_ENTERING_DURATION).easing(
        Easing.bezier(0.25, 0.1, 0.25, 1).factory()
      )}
      exiting={FadeOut.duration(POPUP_ANIMATION_EXITING_DURATION).easing(Easing.ease)}
    >
      <TouchableWithoutFeedback
        onPress={closePopup}
        style={{ width: '100%', height: '100%', alignItems: 'center', justifyContent: 'center' }}
      >
        {children}
      </TouchableWithoutFeedback>
    </Animated.View>
  );
};
export default Backdrop;
