import { useIntl } from 'react-intl';
import { daysBetween, fromUnixNumber } from 'utils/date';

interface Props {
  departureDate?: number;
  selectedDepartureDate: Date;
}

export default function useTimeOfDayCard({ selectedDepartureDate, departureDate }: Props) {
  const { formatMessage } = useIntl();

  const departureDay = fromUnixNumber(departureDate ? Number(departureDate) * 1000 : 0);
  const isDepartureDay = daysBetween(departureDay, selectedDepartureDate) <= 0;
  let timeOfDayOptions: { label: string; value: string }[] = [
    {
      label: formatMessage({
        id: 'app.general.morning',
      }),
      value: '11:59',
    },
  ];
  if (!isDepartureDay) {
    timeOfDayOptions = [
      ...timeOfDayOptions,
      {
        label: formatMessage({
          id: 'app.general.afternoon',
        }),
        value: '17:59',
      },
      {
        label: formatMessage({
          id: 'app.general.evening',
        }),
        value: '23:59',
      },
    ];
  }

  return timeOfDayOptions;
}
