import { UserContext } from '@app/context/usercontext/UserContextProvider';
import { useGetParks } from '@domains/onboarding/queries/useGetParks';
import { NavigationContainerRef } from '@react-navigation/native';
import { RefObject, useContext, useEffect } from 'react';
import { Platform } from 'react-native';

interface Props {
  navigationRef: RefObject<NavigationContainerRef<any>>;
  routeName?: string;
}

export default function useBlockAppOnBlockingError({ navigationRef, routeName }: Props) {
  const { userState } = useContext(UserContext);
  const { data: parks, isError, isFetched } = useGetParks({});

  useEffect(() => {
    if (Platform.OS === 'web') {
      return;
    }
    // wait until the app is fully loaded
    if (userState === 'initial') {
      return;
    }

    // When it is impossible to retrieve the Landal parks, the app cannot function, so we block it.
    const failedToRetrieveParks = (!isFetched && isError) || (isFetched && (parks?.length || 0) <= 0);
    const fromErrorScreen = routeName === 'AppBlocked';

    if (fromErrorScreen) {
      if (!failedToRetrieveParks) {
        // We're good to go. Return to where we left off.
        if (navigationRef.current) {
          navigationRef.current.goBack();
        }
        return;
      }
    } else {
      if (failedToRetrieveParks) {
        // Block the app.
        if (navigationRef.current) {
          navigationRef.current.navigate('AppBlocked');
        }
      }
    }
  }, [isError, isFetched, navigationRef, parks, routeName, userState]);
}
