import React, { useContext, useState } from 'react';
import useTheme from '@app/hooks/useTheme';
import { TextInput, View } from 'react-native';
import { NavScreenProp } from '@app/navigation/stacks/Main';
import Popup from '@app/components/Popup';
import ButtonSetHorizontal from '@app/components/ButtonSetHorizontal';
import { useIntl } from 'react-intl';
import Text from '@app/components/Text';
import { useGetPark } from '@domains/park/contact-and-info/queries/useGetPark';
import { useGetReservation } from '../queries/useGetReservation';
import { getBrand } from 'utils/brand';
import { UserContext } from '@app/context/usercontext/UserContextProvider';
import { useGetAccommodationGeo } from '@domains/start/queries/useGetAccommodationGeo';
import externalNavigateToLocation from 'utils/externalNavigateToLocation';
import useMetrics from '@app/metrics/useMetrics';
import { AccoGeo } from 'apis/_generated-park-api/models';

const INPUT_HEIGHT = 50;

export const useNavigateToAcco = ({
  bookingReference,
  parkCode,
}: {
  bookingReference?: string;
  parkCode: string | undefined;
}) => {
  const { logEvent } = useMetrics();
  const { formatMessage } = useIntl();
  const theme = useTheme();

  const { data: park } = useGetPark({ parkCodeOverride: parkCode });
  const { token } = useContext(UserContext);
  const { data: reservation } = useGetReservation({
    token,
    brand: getBrand(theme),
    bookingReference,
  });

  const getAcco = useGetAccommodationGeo({ selectedParkCode: parkCode ?? '' });

  const navigateToMapsWithPark = async () => {
    if (!park) return;
    if (park.latitude && park.longitude) {
      logEvent('ViewRouteToPark', { park: parkCode ?? '' });
      await externalNavigateToLocation({
        latitude: park.latitude,
        longitude: park.longitude,
        name: park.name,
      });
    }
  };

  const navigateToMapsWithAccommodation = async (acc: AccoGeo, unitCode: string) => {
    if (acc.lat && acc.lon) {
      logEvent('ViewRouteToBungalow', { bungalow_number: unitCode });
      await externalNavigateToLocation({
        latitude: acc.lat,
        longitude: acc.lon,
        name: formatMessage({ id: 'app.launcher.acco' }) + ' ' + unitCode,
      });
    }
  };

  const navigateToAcco = async (unitCode?: string) => {
    if (!park) {
      return;
    }

    if (!park.allowAccommodationNavigation || reservation?.unitTypeIndicator === 'CAMPING') {
      navigateToMapsWithPark();
      return;
    }

    if (unitCode) {
      try {
        const accommodation = await getAcco(unitCode);
        if (!accommodation) {
          logEvent('ShowBungalowRouteError', { parkId: park.id, unitCode });
          throw 'oh no';
        }
        navigateToMapsWithAccommodation(accommodation, unitCode);
        return;
      } catch (ex) {
        logEvent('ShowBungalowRouteError', { parkId: park.id, unitCode });
        navigateToMapsWithPark();
        return;
      }
    } else {
      navigateToMapsWithPark();
      return;
    }
  };

  return {
    navigateToAcco,
    unitCode: bookingReference ? reservation?.unitCode : undefined,
    isCombiBooking: !!reservation?.combiReference,
  };
};

const AccoNumberPopup = ({ navigation, route }: NavScreenProp<'AccoNumberPopup'>) => {
  const { formatMessage } = useIntl();
  const theme = useTheme();

  const bookingReference = route?.params?.bookingReference;
  const parkCode = route?.params?.parkCode;

  const { navigateToAcco, unitCode } = useNavigateToAcco({ bookingReference, parkCode });

  const [acco, setAcco] = useState<string>(unitCode || '');
  const value = !acco || acco === '' ? '' : acco?.toString();

  const [processing, setProcessing] = useState(false);

  return (
    <Popup>
      <View
        style={{
          alignItems: 'center',
        }}
      >
        <Text style={{ marginBottom: theme.space.s }} type="title" bold>
          {formatMessage({ id: 'app.reservation.accoInputTitle' })}
        </Text>
        <Text style={{ marginBottom: theme.space.l }} color="gray">
          {formatMessage({ id: 'app.forms.labels.accommodationnumber' })}
        </Text>
        <View
          style={{
            width: '100%',
            paddingBottom: theme.space.l,
          }}
        >
          <TextInput
            style={{
              fontFamily: theme.fonts.bodyBold,
              color: theme.colors.gray,
              fontSize: theme.fontSizes.m.fontSize,
              width: '100%',
              height: INPUT_HEIGHT,
              paddingHorizontal: theme.space.l,
              borderRadius: 8,
              backgroundColor: theme.colors.white,
              borderWidth: 1,
              borderColor: theme.colors.light,
              textAlign: 'center',
            }}
            value={value}
            placeholder={formatMessage({ id: 'app.reservation.accoPlaceholder' })}
            onChangeText={(text) => {
              setAcco(text);
            }}
            keyboardType={'number-pad'}
            numberOfLines={1}
            maxLength={4}
            placeholderTextColor={theme.colors.gray}
          />
        </View>
        <ButtonSetHorizontal
          onAbort={() => navigation.goBack()}
          ctaTextId="app.components.detail.moreinfo.address"
          onPress={async () => {
            setProcessing(true);
            await navigateToAcco(acco);
            setProcessing(false);
          }}
          processing={processing}
        />
      </View>
    </Popup>
  );
};
export default AccoNumberPopup;
