import { GENERATEDSALTOID_KEY, DEVICEID_KEY, SALTO_USER_TOKEN_KEY } from '@app/config/StorageKeys';
import AsyncStorage from '@react-native-async-storage/async-storage';
import React, { useEffect, useState } from 'react';

type SaltoContextModel = {
  generatedId?: string;
  deviceId?: string;
  saltoUserToken?: string;
};
type SaltoContextUpdaterModel = {
  setGeneratedId: (id: string) => void;
  setDeviceId: (id: string) => void;
  setSaltoUserToken: (id: string) => void;
  clearDeviceReservationIds: () => void;
};

export const SaltoContextUpdater = React.createContext<SaltoContextUpdaterModel>({
  setGeneratedId: () => {},
  setDeviceId: () => {},
  setSaltoUserToken: () => {},
  clearDeviceReservationIds: () => {},
});

export const SaltoContext = React.createContext<SaltoContextModel>({
  generatedId: undefined,
  deviceId: undefined,
  saltoUserToken: undefined,
});

export function SaltoContextProvider({ children }: { children: React.ReactNode }) {
  const [generatedId, setGeneratedId] = useState<string>();
  const [deviceId, setDeviceId] = useState<string>();
  const [saltoUserToken, setSaltoUserToken] = useState<string>();

  const saltoContext: SaltoContextModel = {
    generatedId,
    deviceId,
    saltoUserToken,
  };

  useEffect(() => {
    const storeGeneratedId = async (genId: string) => {
      await AsyncStorage.setItem(GENERATEDSALTOID_KEY, genId);
    };

    if (generatedId !== undefined) {
      storeGeneratedId(generatedId);
    }
  }, [generatedId]);

  useEffect(() => {
    const storeDeviceId = async (devId: string) => {
      await AsyncStorage.setItem(DEVICEID_KEY, devId);
    };

    if (deviceId !== undefined) {
      storeDeviceId(deviceId);
    }
  }, [deviceId]);

  useEffect(() => {
    const storeSaltoUserToken = async (SUT: string) => {
      await AsyncStorage.setItem(SALTO_USER_TOKEN_KEY, SUT);
    };

    if (saltoUserToken !== undefined) {
      storeSaltoUserToken(saltoUserToken);
    }
  }, [saltoUserToken]);

  const clearIds = () => {
    setGenId(undefined);
    setDevId(undefined);
    setSaltoUserToken(undefined);
  };

  const setGenId = (genId?: string) => {
    setGeneratedId(() => {
      return genId;
    });
  };

  const setDevId = (devId?: string) => {
    setDeviceId(() => {
      return devId;
    });
  };

  const setSUT = (SUT?: string) => {
    setSaltoUserToken(() => {
      return SUT;
    });
  };

  return (
    <SaltoContext.Provider value={saltoContext}>
      <SaltoContextUpdater.Provider
        value={{
          setDeviceId: setDevId,
          setSaltoUserToken: setSUT,
          setGeneratedId: setGenId,
          clearDeviceReservationIds: clearIds,
        }}
      >
        {children}
      </SaltoContextUpdater.Provider>
    </SaltoContext.Provider>
  );
}
