import useCulture from '@app/hooks/useCulture';
import useSelectedPark from '@app/hooks/useSelectedPark';
import useTheme from '@app/hooks/useTheme';
import { UnitType } from 'apis/_generated-park-api/models';
import { AxiosError } from 'axios';
import { gql, GraphQLClient } from 'graphql-request';
import Config from 'react-native-config';
import { UseQueryOptions, useQuery } from 'react-query';
import { getBrand } from 'utils/brand';

const key = `getUnitTypes`;
type Props = {
  options?: Omit<UseQueryOptions<UnitType[], AxiosError>, typeof key>;
  parkCode?: string;
};

export function useGetUnitTypes(props: Props) {
  const { parkCode: selectedParkCode } = useSelectedPark();
  const { cultureCode } = useCulture();
  const theme = useTheme();
  const brand = getBrand(theme);

  const parkCode = props?.parkCode ?? selectedParkCode;

  const extendedOptions: typeof props.options = {
    ...props.options,
    enabled: !!parkCode,
  };

  return useQuery(
    [key, { parkCode, cultureCode, brand }],
    async () => {
      const graphQLClient = new GraphQLClient(Config.PARK_API_URL);

      const query = gql`
        query unitTypes {
          unitTypes(parkCode: "${parkCode}", cultureCode: "${cultureCode}", brand: ${brand}) {
            code
            name
            area
            images
            indoorMaps
            maxPeople
            maxAdults
            maxChildren
            maxBabycots
            defaultChildChairs
            numberOfBedrooms
            numberOfBathrooms
          }
        }
      `;
      const result = await graphQLClient.rawRequest<{ unitTypes: UnitType[] }>(query);
      return result?.data.unitTypes;
    },
    extendedOptions
  );
}
