import useCulture from '@app/hooks/useCulture';
import useSelectedLanguageAvailableForPark from '@app/hooks/useSelectedLanguageAvailableForPark';
import useSelectedPark from '@app/hooks/useSelectedPark';
import { Poi } from 'apis/_generated-park-api/models';
import { AxiosError } from 'axios';
import { gql, GraphQLClient } from 'graphql-request';
import Config from 'react-native-config';
import { UseQueryOptions, useQuery } from 'react-query';

const key = `getpois`;
type Props = {
  parkCode?: string;
  options?: Omit<UseQueryOptions<Poi[], AxiosError>, typeof key>;
};

export function useGetPOI(props: Props) {
  const { parkCode } = useSelectedPark();
  let { cultureCode, FALLBACK_CULTURE } = useCulture();
  const selectedLanguageAvailableForPark = useSelectedLanguageAvailableForPark();
  if (!selectedLanguageAvailableForPark) {
    cultureCode = FALLBACK_CULTURE;
  }

  const extendedOptions: typeof props.options = {
    ...props.options,
  };

  const usedParkCode = props.parkCode ?? parkCode;

  return useQuery(
    [key, { usedParkCode, cultureCode }],
    async () => {
      const graphQLClient = new GraphQLClient(Config.PARK_API_URL);
      const query = gql`
        query {
          poi(parkCode: "${usedParkCode}", cultureCode: "${cultureCode}") {
            isEvent
            isLocationWithEvents
            id
            name
            description
            website
            ticketsWebsite
            ticketsAvailable
            address{
                street
                streetNumber
                zipcode
                city
            }
            location{
                latitude
                longitude
            }
            categories
            media
            activities{
                id
                description
                dates{
                    type
                    date
                }
                categories
                name
                media
            }
            dates{
              type
              date
            }
          }
        }
      `;
      const result = await graphQLClient.rawRequest<{ poi: Poi[] }>(query);
      return result?.data?.poi;
    },
    extendedOptions
  );
}
