import React, { useState } from 'react';
import useTheme from '@app/hooks/useTheme';
import { View, Alert } from 'react-native';
import ContentContainer from '@app/components/DetailScreen/ContentContainer';
import { useFormik } from 'formik';
import Button from '@app/components/Button';
import { KeyboardAwareScrollView } from 'react-native-keyboard-aware-scroll-view';
import { useIntl } from 'react-intl';
import { useGetFacilityMetaData } from '../queries/useGetFacilityMetaData';
import NumberControl from '@app/components/Form/NumberControl';
import DateSelector from '@app/components/Reservation/DateSelector';
import TimeSlotSelector from '@app/components/Reservation/TimeSlotSelector';
import useGetFacilitybookingAvailability from '../queries/useGetFacilityBookingAvailability';
import { useBookFacility } from '../queries/useBookFacility';
import { addDays, addMinutes, getFormattedDate, getFormattedTime, toUnixNumber } from 'utils/date';
import useMetrics from '@app/metrics/useMetrics';
import { isValidEmail, isValidPhone } from 'utils/validation';
import { useGetFacilities } from '../queries/useGetFacilities';
import { FacilityBookRequest, FacilityBookResponse } from 'apis/_generated-park-api/models';
import { TextInput } from 'react-native-gesture-handler';
import StickyHeader from '@app/components/StickyHeader';
import FormControl from '@app/components/Form/FormControl';
import { NavScreenProp } from '@app/navigation/stacks/Main';
import { NotificationContextUpdater } from '@domains/notification/context/NotificationProvider';
import { NotificationMessage } from '@domains/notification/types/NotificationMessage';
import Text from '@app/components/Text';

type FormFields =
  | 'amount'
  | 'date'
  | 'time_of_day'
  | 'time'
  | 'selected_time_date'
  | 'name'
  | 'phone'
  | 'email'
  | 'remarks';

type FormikErrors = {
  date?: string;
  email?: string;
  name?: string;
  phone?: string;
  time?: string;
};

const FacilityBooking = ({ navigation, route }: NavScreenProp<'FacilityBooking'>) => {
  const formik = useFormik<Record<FormFields, any>>({
    initialValues: {
      amount: 1,
      date: undefined,
      time_of_day: undefined,
      time: undefined,
      selected_time_date: undefined,
      name: '',
      phone: '',
      email: '',
      remarks: '',
    },
    validateOnMount: true,
    onSubmit: () => {},
    validate: (validatedValues) => {
      const errors: FormikErrors = {};

      if (!validatedValues.date) {
        errors.date = 'no date';
      }
      if (!validatedValues.email || !isValidEmail(validatedValues.email)) {
        errors.email = 'no email';
      }

      if (!validatedValues.name) {
        errors.name = 'no name';
      }

      if (!validatedValues.phone || !isValidPhone(validatedValues.phone)) {
        errors.phone = 'no phone';
      }
      if (!validatedValues.time) {
        errors.time = 'no time to waste';
      }
      return errors;
    },
  });
  const setFormField = (id: FormFields, value: any) => {
    formik.setFieldValue(id, value);
  };

  const [processing, setProcessing] = useState(false);

  const theme = useTheme();
  const { logEvent } = useMetrics();
  const { formatMessage } = useIntl();
  const scrollViewRef = React.useRef(null);
  const reservationDuration = 60;

  const { setNotifications } = React.useContext(NotificationContextUpdater);

  const reservationId = route.params?.reservationId;

  const { data: facilityMeta, isLoading: facilityMetaLoading } = useGetFacilityMetaData({ reservationId });
  const { data: availability, isLoading: availabilityLoading } = useGetFacilitybookingAvailability({
    reservationId,
    date: formik.values.date,
  });
  const { postBooking } = useBookFacility();
  const phoneInputRef = React.useRef<TextInput>(null);

  const { data: facilities } = useGetFacilities();
  const facility = facilities?.find((f) => f.reservationId === reservationId);

  return (
    <>
      <StickyHeader
        showClose
        navigation={navigation}
        title={`${formatMessage({ id: 'app.forms.labels.makeareservationat' })} ${facility?.name}`}
      />
      <KeyboardAwareScrollView
        style={{ flex: 1 }}
        ref={scrollViewRef}
        enableOnAndroid
        extraScrollHeight={120}
        keyboardOpeningTime={Number.MAX_SAFE_INTEGER}
        enableResetScrollToCoords={false}
        keyboardShouldPersistTaps="handled"
      >
        <ContentContainer style={{ flex: 1 }}>
          <View style={{ marginBottom: theme.space.xl }}>
            {facilityMetaLoading ? (
              <Text> </Text>
            ) : facilityMeta?.amountMaximum === 1 ? (
              <Text>
                {formatMessage({ id: 'app.forms.facilitybooking.maxUnits' })
                  .replace('{0}', facility?.name ? facility.name : '')
                  .replace('{1}', facilityMeta?.amountMaximum ? facilityMeta.amountMaximum.toString() : '1')
                  .replace('{2}', facilityMeta?.placesSingle ? facilityMeta.placesSingle : '')}
              </Text>
            ) : (
              <NumberControl
                title={`${formatMessage({ id: 'app.forms.labels.amountof' })}${
                  facilityMeta?.placesPlural ?? ''
                } ?`}
                value={formik.values.amount}
                minimum={facilityMeta?.amountMinimum ?? 1}
                maximum={facilityMeta?.amountMaximum ?? 2}
                onChange={(value) => setFormField('amount', value)}
              />
            )}
          </View>
          <DateSelector
            date={formik.values.date}
            maximumDate={
              facilityMeta?.daysInAdvanceMax > 0
                ? toUnixNumber(addDays(new Date(), facilityMeta.daysInAdvanceMax).toUTCString())
                : undefined
            }
            onSelectDate={(newDate) => {
              // Reset time when another date is selected, as the previous time might not be available this day
              formik.setValues({
                ...formik.values,
                date: newDate,
                time: undefined,
              });
            }}
          />
          {!!formik.values.date && !formik.errors.date && (
            <TimeSlotSelector
              time={formik.values.time}
              onSelectTime={(label, timestamp) => {
                formik.setValues({
                  ...formik.values,
                  time: label,
                  selected_time_date: new Date(timestamp * 1000),
                });
              }}
              slotDuration={reservationDuration}
              availability={availability}
              showDayPart={false}
              dataIsLoading={availabilityLoading}
            />
          )}
          {!!formik.values.time && !formik.errors.time && (
            <View style={{ marginBottom: theme.space.xl }}>
              <FormControl
                label={formatMessage({ id: 'app.forms.labels.name' })}
                value={formik.values.name}
                isValid={!formik.errors.name}
                onChangeText={(text) => {
                  setFormField('name', text);
                }}
                returnKeyType="next"
                onSubmitEditing={() => phoneInputRef.current?.focus()}
                autoFocus
              />
            </View>
          )}
          {!!formik.values.name && !formik.errors.name && (
            <View style={{ marginBottom: theme.space.xl }}>
              <FormControl
                label={formatMessage({ id: 'app.forms.labels.phone' })}
                ref={phoneInputRef}
                value={formik.values.phone}
                isValid={!formik.errors.phone}
                onChangeText={(text) => {
                  setFormField('phone', text);
                }}
                returnKeyType="next"
              />
            </View>
          )}
          {!!formik.values.phone && !formik.errors.phone && (
            <View style={{ marginBottom: theme.space.xl }}>
              <FormControl
                label={`${formatMessage({ id: 'app.forms.labels.email' })} ${formatMessage({
                  id: 'app.forms.labels.email.explanation',
                })}`}
                value={formik.values.email}
                isValid={!formik.errors.email}
                onChangeText={(text) => {
                  setFormField('email', text);
                }}
                returnKeyType="next"
              />
            </View>
          )}
          {!!formik.values.email && !formik.errors.email && (
            <View style={{ marginBottom: theme.space.xl }}>
              <FormControl
                label={formatMessage({ id: 'app.forms.labels.remarks' })}
                value={formik.values.remarks}
                isValid={true}
                onChangeText={(text) => {
                  setFormField('remarks', text);
                }}
                returnKeyType="next"
                multiline
                optional
              />
            </View>
          )}
          {formik.isValid && (
            <View>
              <Button
                onPress={async () => {
                  setProcessing(true);
                  const bookRequest: FacilityBookRequest = {
                    reservationId,
                    fromDate: toUnixNumber(formik.values.selected_time_date),
                    tillDate: toUnixNumber(
                      addMinutes(formik.values.selected_time_date, reservationDuration).toString()
                    ),
                    bookerName: formik.values.name,
                    remarks: formik.values.remarks,
                    email: formik.values.email,
                    phoneNumber: formik.values.phone,
                  };

                  let result: FacilityBookResponse;
                  try {
                    result = await postBooking(bookRequest);
                  } catch (ex) {
                    Alert.alert(formatMessage({ id: 'app.forms.tablebooking.error.generic' }));
                    return;
                  }

                  const ANALYTICS_TYPE = 'facility';
                  const ANALYTICS_RESERVATION = `${facilityMeta?.description}|${formik.values.amount}|${formik.values.selected_time_date}`;

                  if (result.status) {
                    logEvent('GenerateLead', {
                      type: ANALYTICS_TYPE,
                      reservation: ANALYTICS_RESERVATION,
                    });

                    const quantityText =
                      formik.values.amount.length > 1
                        ? facilityMeta?.placesPlural
                        : facilityMeta?.placesSingle;

                    const message: NotificationMessage = {
                      createdAt: new Date(),
                      title:
                        formatMessage({ id: 'app.messages.reservation' }) + ' ' + facilityMeta?.description,
                      text: `${formatMessage({
                        id: 'app.forms.facilitybooking.result.description1',
                      })}${formik.values.amount} ${quantityText} ${formatMessage({
                        id: 'app.forms.facilitybooking.result.description2',
                      })} ${getFormattedDate(formik.values.selected_time_date)} ${getFormattedTime(
                        formik.values.selected_time_date
                      )}${formatMessage({
                        id: 'app.forms.facilitybooking.result.description3',
                      })}`,
                      shortDetails: [
                        {
                          label: formatMessage({ id: 'app.general.date' }),
                          value: getFormattedDate(formik.values.selected_time_date),
                        },
                        {
                          label: formatMessage({ id: 'app.general.time' }),
                          value: getFormattedTime(formik.values.selected_time_date),
                        },
                      ],
                      image: facility?.media,
                      label: 'reservation',
                    };

                    setNotifications((prev) => [...prev, message]);

                    navigation.replace('FacilityBookingSuccessful', {
                      reservationId,
                      amount: formik.values.amount,
                      date: formik.values.selected_time_date,
                    });
                    return;
                  } else {
                    logEvent('GenerateLeadError', {
                      type: ANALYTICS_TYPE,
                      error_type: result.messages?.join('/r/n'),
                      reservation: ANALYTICS_RESERVATION,
                    });

                    const message = result.messages
                      ? result.messages.join('/r/n')
                      : formatMessage({ id: 'app.shop.payment.failed.title' });
                    Alert.alert(message);
                  }
                  setProcessing(false);
                }}
                style={{
                  marginVertical: theme.space.m,
                  backgroundColor: theme.colors.secondary,
                }}
                disabled={processing}
              >
                {processing ? (
                  <Button.ActivityIndicator />
                ) : (
                  <Button.Text>{formatMessage({ id: 'app.forms.action.makeareservation' })}</Button.Text>
                )}
              </Button>
            </View>
          )}
        </ContentContainer>
      </KeyboardAwareScrollView>
    </>
  );
};
export default FacilityBooking;
