import React from 'react';
import { View, ViewProps, ViewStyle } from 'react-native';
import Text from './Text';
import Icon from './Icon';
import { useIntl } from 'react-intl';
import { firstToUpper, ReadableDayByDate } from 'utils/date';
import { AllColors } from '@app/theme/partials/general';

interface Props extends React.PropsWithChildren<ViewProps> {
  showDay?: boolean;
  dateObject: any;
  color?: AllColors;
  style?: ViewStyle;
}
export default function DateTimeLabel({ dateObject, showDay, color, style }: Props) {
  const { formatMessage } = useIntl();
  return (
    <View style={[{ flexDirection: 'row', marginBottom: 8, alignItems: 'center' }, style]}>
      <Icon name={'time'} size="xs" color={color ? color : 'gray'} style={{ marginRight: 4 }} />
      {showDay && !!dateObject.date && (
        <Text size="s" color={color ? color : 'gray'}>
          {`${firstToUpper(ReadableDayByDate(new Date(dateObject.date), true))} `}
        </Text>
      )}
      <Text size="s" color={color ? color : 'gray'}>
        {dateObject.displayHint === 'FromTo' && `${dateObject.startTime} - ${dateObject.endTime}`}
        {dateObject.displayHint === 'From' &&
          `${formatMessage({ id: 'app.times.from' })} ${dateObject.startTime}`}
        {dateObject.displayHint === 'At' &&
          `${formatMessage({ id: 'app.times.at' })} ${dateObject.startTime}`}
      </Text>
    </View>
  );
}
