import useCulture from '@app/hooks/useCulture';
import useTheme from '@app/hooks/useTheme';
import { CreateAccountRequest, CreateAccountResponse } from 'apis/_generated-identity-api/models';
import { AxiosError } from 'axios';
import { gql, GraphQLClient } from 'graphql-request';
import Config from 'react-native-config';
import { UseQueryOptions, useMutation } from 'react-query';
import { getBrand } from 'utils/brand';

type Props = {
  options?: UseQueryOptions<CreateAccountResponse, AxiosError>;
};

export function useCreateAccount(props?: Props) {
  const { cultureCode } = useCulture();
  const theme = useTheme();

  const createAccountMutation = useMutation(async (request: CreateAccountRequest) => {
    const graphQLClient = new GraphQLClient(Config.IDENTITY_API_URL);

    const brand = getBrand(theme);

    const query = gql`
      mutation createAccount($request: CreateAccountRequest) {
        createAccount(request: $request, brand: ${brand}, cultureCode:"${cultureCode}") {
          success
        }
      }
    `;

    const response = await graphQLClient.rawRequest<{ createAccount: CreateAccountResponse }>(query, {
      request,
    });
    return response.data?.createAccount;
  }, props?.options);

  const createAccount = async (request: CreateAccountRequest) => {
    const result = await createAccountMutation.mutateAsync(request);
    return result;
  };

  return { createAccount };
}
