import { CHAT_KEY } from '@app/config/StorageKeys';
import { ChatMessage } from '@app/interfaces/ChatMessage';
import AsyncStorage from '@react-native-async-storage/async-storage';
import React, { useMemo, useState } from 'react';

type ChatContextUpdaterModel = {
  setMessages: React.Dispatch<React.SetStateAction<ChatMessage[]>>;
  setBottomSheetOpened: (opened: boolean) => void;
  clearMessages: () => void;
};

export const ChatContextUpdater = React.createContext<ChatContextUpdaterModel>({
  setMessages: () => {},
  clearMessages: () => {},
  setBottomSheetOpened: () => {},
});
export const ChatContext = React.createContext<ChatMessage[]>([]);

export function ChatProvider({ children }: { children: React.ReactNode }) {
  const [chatMessages, setChatMessages] = useState<ChatMessage[]>([]);
  const [bottomSheetOpened, setBottomSheetOpened] = useState(false);

  React.useEffect(() => {
    if (chatMessages && chatMessages.length > 0) {
      AsyncStorage.setItem(CHAT_KEY, JSON.stringify(chatMessages));
    }
  }, [chatMessages]);

  const clearMessages = () => {
    setChatMessages([]);
    AsyncStorage.removeItem(CHAT_KEY);
  };

  const markMessagesAsRead = (message: ChatMessage) => ({ ...message, read: true });

  type FunctionalChatMessageArg = (prev: ChatMessage[]) => ChatMessage[];

  const markFunctionalAsRead =
    (functionalArgCaller: FunctionalChatMessageArg): ((c: ChatMessage[]) => ChatMessage[]) =>
    (messages: ChatMessage[]) =>
      functionalArgCaller(messages).map(markMessagesAsRead);

  const setMessages = (newChatMessages: ChatMessage[] | FunctionalChatMessageArg) => {
    if (!bottomSheetOpened) {
      setChatMessages(newChatMessages);
      return;
    }
    const isFunctional = typeof newChatMessages === 'function';
    const input = isFunctional
      ? markFunctionalAsRead(newChatMessages)
      : newChatMessages.map(markMessagesAsRead);

    setChatMessages(input);
  };

  let filteredChatMessages = useMemo(() => {
    // We take out everything but reservation confirmations for the 7.0 messages box.
    // By chance, this happens to be all messages without a defined 'action'.
    return chatMessages
      .filter((m) => !m.action)
      .sort((a, b) => new Date(b.createdAt) - new Date(a.createdAt));
  }, [chatMessages]);

  return (
    <ChatContext.Provider value={filteredChatMessages}>
      <ChatContextUpdater.Provider value={{ setMessages, clearMessages, setBottomSheetOpened }}>
        {children}
      </ChatContextUpdater.Provider>
    </ChatContext.Provider>
  );
}
