import { addTimeToFormattedDateTime, concatenateDateAndTime } from 'utils/date';

export const getActivityDateTimes = (
  date: number,
  startTime?: string,
  endTime?: string,
  displayHint?: string
) => {
  let startDateTime = '';
  let endDateTime = '';
  let openAllDay = false;

  if (!displayHint) {
    return { startDateTime, endDateTime };
  }
  switch (displayHint.toLowerCase()) {
    case 'fromto': {
      if (!startTime || !endTime) {
        break;
      }
      startDateTime = concatenateDateAndTime(date, startTime);
      endDateTime = concatenateDateAndTime(date, endTime);
      break;
    }
    case 'from': {
      if (!startTime) {
        break;
      }
      startDateTime = concatenateDateAndTime(date, startTime);
      endDateTime = addTimeToFormattedDateTime(concatenateDateAndTime(date, startTime), 1);
      break;
    }
    case 'to': {
      if (!endTime) {
        break;
      }
      startDateTime = addTimeToFormattedDateTime(concatenateDateAndTime(date, endTime), -1); // TODO: verify this assumption
      endDateTime = concatenateDateAndTime(date, endTime);
      break;
    }
    case 'at': {
      if (!startTime) {
        break;
      }
      startDateTime = concatenateDateAndTime(date, startTime);
      endDateTime = addTimeToFormattedDateTime(concatenateDateAndTime(date, startTime), 1);
      break;
    }
    case 'openallday':
      openAllDay = true;
      break;
    case 'closedallday':
      break;
    case 'onrequest':
      break;
    default:
      break;
  }

  return { startDateTime, endDateTime, openAllDay };
};
