import { FAVOURITES_KEY } from '@app/config/StorageKeys';
import AsyncStorage from '@react-native-async-storage/async-storage';
import React, { useState } from 'react';

export type FavouriteType = 'poi' | 'poiActivity' | 'activity' | 'facility' | 'foodAndDrinks';
export type Favourite = {
  id: string;
  favouriteType: FavouriteType;
  parkCode: string;
};

export const FavouritesContextUpdater = React.createContext<
  React.Dispatch<React.SetStateAction<Favourite[]>>
>(() => {});
export const FavouritesContext = React.createContext<Favourite[]>([]);

export function FavouritesProvider({ children }: { children: React.ReactNode }) {
  const [favourites, setFavourites] = useState<Favourite[]>([]);

  React.useEffect(() => {
    // synchronise stored favourites with app context on appstart
    const getFavourites = async () => {
      const data = await AsyncStorage.getItem(FAVOURITES_KEY);
      if (data) {
        setFavourites(JSON.parse(data));
      }
    };
    getFavourites();
  }, []);

  React.useEffect(() => {
    AsyncStorage.setItem(FAVOURITES_KEY, JSON.stringify(favourites));
  }, [favourites]);

  return (
    <FavouritesContext.Provider value={favourites}>
      <FavouritesContextUpdater.Provider value={setFavourites}>{children}</FavouritesContextUpdater.Provider>
    </FavouritesContext.Provider>
  );
}
