import RadioButton from '@app/components/Form/RadioButton';
import { LandalPressable } from '@app/components/LandalPressable';
import Text from '@app/components/Text';
import useTheme from '@app/hooks/useTheme';
import React, { useMemo } from 'react';
import { useIntl } from 'react-intl';
import { View } from 'react-native';

const MANDATORY_DOT_SIZE = 4;

interface Props {
  selected: number;
  onSelect: (value: number) => void;
  showOther?: boolean;
  optional?: boolean;
}

export default function GenderSelector({ selected, onSelect, showOther = true, optional }: Props) {
  const theme = useTheme();
  const { formatMessage } = useIntl();

  const GENDER = useMemo(() => {
    const maleFemale = [
      {
        value: 1,
        label: formatMessage({ id: 'app.forms.labels.gender.man' }),
      },
      {
        value: 2,
        label: formatMessage({ id: 'app.forms.labels.gender.woman' }),
      },
    ];

    if (showOther) {
      return [
        ...maleFemale,
        {
          value: 0,
          label: formatMessage({ id: 'app.forms.labels.gender.other' }),
        },
      ];
    }
    return maleFemale;
  }, [formatMessage, showOther]);

  return (
    <View style={{ marginBottom: theme.space.xl }}>
      <View style={{ flexDirection: 'row' }}>
        <Text bold style={{ marginBottom: theme.space.s }}>
          {formatMessage({ id: 'app.forms.labels.gender' })}
        </Text>
        {!optional && (
          <View
            style={{
              top: 4,
              width: MANDATORY_DOT_SIZE,
              height: MANDATORY_DOT_SIZE,
              borderRadius: MANDATORY_DOT_SIZE / 2,
              backgroundColor: theme.colors.attention,
              marginLeft: 2,
            }}
          />
        )}
      </View>
      <View style={{ flex: 1, flexDirection: 'row' }}>
        {GENDER.map((g) => {
          return (
            <LandalPressable
              key={g.value}
              style={{ flex: 1, flexDirection: 'row' }}
              activeOpacity={0.8}
              onPress={() => onSelect(g.value)}
            >
              <RadioButton selected={selected === g.value} style={{ marginRight: theme.space.s }} />
              <Text type="body" color="dark" style={{ lineHeight: 24 }}>
                {g.label}
              </Text>
            </LandalPressable>
          );
        })}
      </View>
    </View>
  );
}
