import useTheme from '@app/hooks/useTheme';
import { add, Canvas, Circle, Group, LinearGradient, vec } from '@shopify/react-native-skia';
import React from 'react';
import { View, ViewStyle } from 'react-native';

const SIZE = 24;
const c = vec(12, 12);
const r = SIZE;

interface Props {
  selected: boolean;
  style?: ViewStyle;
}
export default function RadioButton({ selected, style }: Props) {
  const theme = useTheme();

  return (
    <View
      style={[
        {
          height: SIZE,
          width: SIZE,
          borderRadius: SIZE / 2,
          borderWidth: 1,
          borderColor: selected ? theme.colors.primary : theme.colors.light,
          alignItems: 'center',
          justifyContent: 'center',
        },
        style,
      ]}
    >
      <Canvas
        key={selected.toString()}
        style={{
          height: SIZE,
          width: SIZE,
        }}
      >
        {selected && (
          <Group>
            <Group transform={[{ rotate: Math.PI / 3 }]} origin={c}>
              <LinearGradient
                start={add(c, vec(-r, 0))}
                end={add(c, vec(r, 0))}
                colors={[
                  theme.colors.gradientPrimary1,
                  theme.colors.gradientPrimary2,
                  theme.colors.gradientPrimary3,
                ]}
              />
              <Circle c={c} r={r / 2} />
            </Group>
          </Group>
        )}
        <Circle cx={SIZE / 2} cy={SIZE / 2} r={SIZE / 4} color={theme.colors.white} />
      </Canvas>
    </View>
  );
}
