import React from 'react';
import useTheme from '@app/hooks/useTheme';
import { View } from 'react-native';
import Text from '@app/components/Text';
import ContentContainer from '@app/components/DetailScreen/ContentContainer';
import { useIntl } from 'react-intl';
import Button from '@app/components/Button';
import { getFormattedDate } from 'utils/date';
import { format } from 'utils/string';
import StickyHeader from '@app/components/StickyHeader';
import { NavScreenProp } from '@app/navigation/stacks/Main';

const TableBookingSuccessful = ({ navigation, route }: NavScreenProp<'TableBookingSuccessful'>) => {
  const theme = useTheme();
  const { formatMessage } = useIntl();
  const restaurantName = route?.params?.restaurantName;
  const timestamp = route?.params?.timestamp;
  const timestring = route?.params?.timestring;
  const guests = route?.params?.guests ?? 0;

  return (
    <>
      <StickyHeader showClose navigation={navigation} />
      <View style={{ flex: 1 }}>
        <ContentContainer style={{ flex: 1 }}>
          <Text type="title" size="xl" style={{ marginBottom: theme.space.l }} bold>
            {formatMessage({ id: 'app.forms.result.title' })}
          </Text>
          <Text type="body" style={{ marginBottom: theme.space.l }}>
            {format(
              formatMessage({
                id: 'app.forms.tablebooking.result.descriptionformat',
              }),
              getFormattedDate(new Date(timestamp * 1000)),
              guests.toString(),
              guests > 1
                ? formatMessage({ id: 'app.forms.result.person' })
                : formatMessage({ id: 'app.forms.result.person_plural' }),
              timestring
            )}
          </Text>
          <Text type="body" style={{ marginBottom: 32 }}>
            {formatMessage({ id: 'app.forms.result.messagedescription1' })}
            {restaurantName}
            {formatMessage({ id: 'app.forms.result.messagedescription2' })}
          </Text>
          <Button
            type="primary"
            onPress={() => {
              navigation.goBack();
            }}
            style={{
              marginBottom: 32,
            }}
          >
            <Button.Text>{formatMessage({ id: 'app.forms.result.back' })}</Button.Text>
          </Button>
        </ContentContainer>
      </View>
    </>
  );
};
export default TableBookingSuccessful;
