import { saltoIdentityAPI } from '@app/config/Endpoints';
import { SaltoContextUpdater } from '@app/context/SaltoContextProvider';
import { UserContext } from '@app/context/usercontext/UserContextProvider';
import useMetrics from '@app/metrics/useMetrics';
import React, { useContext, useMemo } from 'react';
import Config from 'react-native-config';
import { WebView } from 'react-native-webview';
import { getParameterByName } from 'utils/url';
import { generateCodeChallenge } from '../helpers/utils';

const VISIBLE_WEBVIEWS = __DEV__ && false;

interface Props {
  reference?: React.RefObject<WebView<{}>>;
  onError: (
    reason: 'missing_code' | 'missing_token' | 'invalid_credentials' | 'load_error' | 'salto_user_not_found'
  ) => void;
  onSuccess: () => void;
  onReady: () => void;
  onLoadStart: () => void;
}

// TODO: incomplete type
interface SaltoTokenResult {
  access_token: string;
}

const SaltoLoginWebview = ({ reference, onError, onSuccess, onReady, onLoadStart }: Props) => {
  const { setSaltoUserToken } = useContext(SaltoContextUpdater);
  const { demoMode } = useContext(UserContext);
  const { logEvent } = useMetrics();

  const { verifier, challenge } = useMemo(() => generateCodeChallenge(), []);

  const getTokenByCode = async (code: string) => {
    try {
      const params = new URLSearchParams();
      params.append('grant_type', 'authorization_code');
      params.append('code', code);
      params.append('redirect_uri', Config.SALTO_REDIRECT_URI);
      params.append('code_verifier', verifier);
      params.append('client_id', Config.SALTO_CLIENT_ID);

      const result = await saltoIdentityAPI.post<SaltoTokenResult>('/token', params, {
        headers: {
          'Content-Type': 'application/x-www-form-urlencoded',
        },
      });

      if (result?.data?.access_token) {
        setSaltoUserToken(result?.data?.access_token);
        onSuccess();
      } else {
        logEvent('LockProviderIdentityError', { method: '/token', status: result.status });
        onError('missing_token');
      }
    } catch (ex) {
      logEvent('LockProviderIdentityError', { method: 'exception occurred while sending request.' });
      onError('missing_token');
    }
  };

  if (demoMode) {
    onSuccess();
    return <></>;
  }

  const uri = `${Config.SALTO_IDENTITY_BASE_URL}/authorize?scope=${Config.SALTO_SCOPE}&code_challenge=${challenge}&client_id=${Config.SALTO_CLIENT_ID}&response_type=${Config.SALTO_RESPONSE_TYPE}&code_challenge_method=${Config.SALTO_CODE_CHALLENGE_METHOD}&response_mode=${Config.SALTO_RESPONSE_MODE}&redirect_uri=${Config.SALTO_REDIRECT_URI}`;

  return (
    <WebView
      source={{ uri }}
      style={{ width: VISIBLE_WEBVIEWS ? 100 : 1, height: VISIBLE_WEBVIEWS ? 100 : 1 }}
      originWhitelist={['https://*', 'git://*', 'http://*']}
      androidLayerType="hardware"
      onNavigationStateChange={({ url, loading }) => {
        if (loading) {
          return;
        }

        // OIDC success page
        if (url.includes('?code=')) {
          const code = getParameterByName('code', url);
          code && getTokenByCode(code);
          return;
        }

        // OIDC error page
        if (url.includes('error.html?mode=error')) {
          const message = getParameterByName('message', url);
          if (message) {
            logEvent('LockProviderOIDCError', { error_type: 'oidc-provider-error-page' });
            onError('load_error');

            // alert('Lock OIDC login error: ' + message.replace(/[+]/g, ' '));
          }
        }

        // Salto error page
        if (url.includes('/Error?error=')) {
          const message = getParameterByName('error', url);
          if (message) {
            const textMessage = message.replace(/[ ]/g, ' ');

            switch (textMessage) {
              case 'External login failed': {
                logEvent('LockProviderOIDCError', { error_type: 'no-user-in-locks-identity-provider' });
                onError('salto_user_not_found');

                // Ignore for now.
                // alert('Lock Salto login error: No user found');
                break;
              }
              default: {
                logEvent('LockProviderOIDCError', {
                  error_type: 'lock-provider-error-page',
                  reason: textMessage,
                });
                onError('load_error');
                // alert('Lock Salto login error: ' + textMessage);
              }
            }
          }
        }
      }}
      ref={reference}
      onMessage={(event: any) => {
        const { data } = event.nativeEvent;
        switch (data) {
          case 'ready':
            onReady();
            break;
          case 'error':
            onError('invalid_credentials');
            break;
        }
      }}
      onLoadStart={onLoadStart}
      onError={() => {
        logEvent('LockProviderOIDCError', { error_type: 'oidc_load_error' });
        onError('load_error');
      }}
      onHttpError={() => {
        logEvent('LockProviderOIDCError', { error_type: 'oidc_load_error' });
        onError('load_error');
      }}
      domStorageEnabled
      sharedCookiesEnabled
      androidHardwareAccelerationDisabled
    />
  );
};

export default SaltoLoginWebview;
