import React, { useState } from 'react';

export const DeeplinkUpdater = React.createContext<(url: string) => void>((_: string) => {});
export const Deeplink = React.createContext<string>('');

export function DeeplinkProvider({ children }: { children: React.ReactNode }) {
  const [url, setDeeplink] = useState<string>('');

  return (
    <Deeplink.Provider value={url}>
      <DeeplinkUpdater.Provider value={setDeeplink}>{children}</DeeplinkUpdater.Provider>
    </Deeplink.Provider>
  );
}
