import { CART_KEY, ORDER_KEY } from '@app/config/StorageKeys';
import AsyncStorage from '@react-native-async-storage/async-storage';
import { Product } from 'apis/_generated-park-api/models';
import React, { useState } from 'react';

export interface CartProduct {
  product: Product;
  quantity: number;
  options: { id: number; name: string; price: number }[];
  attributeComboId?: number;
  totalPrice: number;
}

export interface OrderMeta {
  facilityId: string;
  pickupDeliveryDate: Date;
  orderNumber: number;
  pickup: boolean;
}

export const CartContextUpdater = React.createContext<{
  updateCart: React.Dispatch<React.SetStateAction<CartProduct[]>>;
  setActiveShop: React.Dispatch<React.SetStateAction<number>>;
  updateOrderMeta: React.Dispatch<React.SetStateAction<OrderMeta | undefined>>;
}>({ updateCart: () => {}, setActiveShop: () => {}, updateOrderMeta: () => {} });

export const CartContext = React.createContext<{
  cart: CartProduct[];
  activeShop: number;
  orderMeta?: OrderMeta;
}>({
  cart: [],
  activeShop: 0,
  orderMeta: undefined,
});

interface Props {
  children: React.ReactNode;
}

export function CartProvider({ children }: Props) {
  const [cart, updateCart] = useState<CartProduct[]>([]);
  const [activeShop, setActiveShop] = useState<number>(-1);
  const [orderMeta, updateOrderMeta] = useState<OrderMeta | undefined>(undefined);
  const SHOP_CART_KEY = CART_KEY + `_${activeShop}`;
  const ORDER_OF_CART_KEY = ORDER_KEY + `_${activeShop}`;

  React.useEffect(() => {
    if (activeShop !== -1) {
      AsyncStorage.setItem(SHOP_CART_KEY, JSON.stringify(cart));
      AsyncStorage.setItem(ORDER_OF_CART_KEY, JSON.stringify(orderMeta));
    }
  }, [ORDER_OF_CART_KEY, SHOP_CART_KEY, activeShop, cart, orderMeta]);

  return (
    <CartContext.Provider value={{ cart, activeShop, orderMeta }}>
      <CartContextUpdater.Provider value={{ updateCart, setActiveShop, updateOrderMeta }}>
        {children}
      </CartContextUpdater.Provider>
    </CartContext.Provider>
  );
}
