import React, { useContext } from 'react';

import { CultureContextUpdater } from '@app/context/usercontext/CultureProvider';
import { useBottomSheet } from '@app/navigation/bottom-sheet/BottomSheetProvider';
import ListItems from '@app/components/ListItems2/ListItems';
import { useIntl } from 'react-intl';
import { countryIcons, LanguageCode } from '@app/hooks/useCulture';
import LandalImage from '@app/components/LandalImage';
import useTheme from '@app/hooks/useTheme';
import { View } from 'react-native';
import useMetrics from '@app/metrics/useMetrics';

const SelectLanguageSheet = () => {
  const changeCulture = useContext(CultureContextUpdater);
  const { logEvent } = useMetrics();
  const { formatMessage } = useIntl();
  const theme = useTheme();
  const isHofVanSaksen = theme.type === 'hofvansaksen';

  let languageListItems: { cultureCode: LanguageCode; languageCode: string }[] = [
    { cultureCode: 'en-US', languageCode: 'en' },
    { cultureCode: 'nl-NL', languageCode: 'nl' },
    { cultureCode: 'de-DE', languageCode: 'de' },
  ];

  if (!isHofVanSaksen) {
    languageListItems = [
      ...languageListItems,
      { cultureCode: 'da-DK', languageCode: 'da' },
      { cultureCode: 'fr-FR', languageCode: 'fr' },
    ];
  }

  const { closeBottomSheet } = useBottomSheet();

  return (
    <View style={{ paddingHorizontal: theme.space.l }}>
      <ListItems
        items={languageListItems.map((i) => {
          const name = formatMessage({
            id: 'app.onboarding.selectlanguage.' + i.languageCode.toLowerCase(),
          });
          return {
            id: i.cultureCode,
            title: name,
            onPress: () => {
              logEvent('SettingsLanguageUpdate', { language: i.cultureCode });
              changeCulture(i.cultureCode);
              closeBottomSheet();
            },
            left: () => (
              <LandalImage
                localSource={countryIcons[i.cultureCode]}
                style={{ alignSelf: 'center', width: 24, height: 16, marginLeft: theme.space.l }}
              />
            ),
          };
        })}
      />
    </View>
  );
};
export default SelectLanguageSheet;
