import React from 'react';
import Reservation from '@domains/reservations/reservation/screens/Reservation';
import TravelCompany from '@domains/reservations/reservation/screens/TravelCompany';
import TravelCompanyCompanion from '@domains/reservations/reservation/screens/TravelCompanyCompanion';
import TravelCompanyMainTenant from '@domains/reservations/reservation/screens/TravelCompanyMainTenant';
import TravelCompanyAddress from '@domains/reservations/reservation/screens/TravelCompanyAddress';
import Products from '@domains/reservations/extras/screens/Products';
import ProductDetail from '@domains/reservations/extras/screens/ProductDetail';
import ExtraDetail from '@domains/reservations/extras/screens/ExtraDetail';
import { Address } from 'apis/_generated-reservation-api/models';

// Types of params passed to route via navigation.navigate('SCREEN_NAME', params)
export type ReservationsRoutes = {
  Reservations: undefined;
  Reservation: { screen?: string; initial?: boolean; bookingReference?: string };
  TravelCompany: undefined;
  TravelCompanyMainTenant: { title: string };
  TravelCompanyCompanion: { title: string; index: number; totalCompanions: number };
  TravelCompanyAddress: { setCustomAddress: (address: Address) => void; address?: Address };
  Products: undefined;
  ProductDetail: { productId: string };
  ExtraDetail: { productId: string; extraKey?: string };
};

export const useReservationScreens = (Stack: any) => {
  return (
    <>
      <Stack.Screen name="Reservation" component={Reservation} />
      <Stack.Screen name="TravelCompany" component={TravelCompany} />
      <Stack.Screen name="TravelCompanyCompanion" component={TravelCompanyCompanion} />
      <Stack.Screen name="TravelCompanyMainTenant" component={TravelCompanyMainTenant} />
      <Stack.Screen name="TravelCompanyAddress" component={TravelCompanyAddress} />
      <Stack.Screen name="Products" component={Products} />
      <Stack.Screen name="ProductDetail" component={ProductDetail} />
      <Stack.Screen name="ExtraDetail" component={ExtraDetail} />
    </>
  );
};
