import React from 'react';
import useTheme from '@app/hooks/useTheme';
import { Dimensions, View } from 'react-native';
import Text from '@app/components/Text';
import Button from '@app/components/Button';
import { useIntl } from 'react-intl';
import useCart from '@app/hooks/useCart';
import useMetrics from '@app/metrics/useMetrics';
import { useFocusEffect } from '@react-navigation/native';
import StickyHeader from '@app/components/StickyHeader';
import StickyBottomBar from '@app/navigation/StickyBottomBar';
import ListItems from '@app/components/ListItems2/ListItems';
import ScreenScrollContainer from '@app/components/ScreenScrollContainer';
import { NavScreenProp } from '@app/navigation/stacks/Main';

export default function Cart({ navigation, route }: NavScreenProp<'Cart'>) {
  const theme = useTheme();
  const { formatMessage, formatNumber } = useIntl();
  const { logScreenView } = useMetrics();
  const { cart } = useCart();
  const { activeShop: shopId } = useCart();

  useFocusEffect(
    React.useCallback(() => {
      if (shopId) {
        logScreenView('Cart', [shopId.toString()]);
      }
    }, [logScreenView, shopId])
  );

  const totalPrice = cart
    ?.map((item) => item.totalPrice * item.quantity)
    .reduce((prev: number, next: number) => Number(prev) + Number(next), 0);

  const hasItems = cart?.length !== 0;
  return (
    <>
      <StickyHeader
        title={formatMessage({ id: 'app.headers.shop.cart' })}
        showClose
        navigation={navigation}
      />
      <View style={{ flex: 1 }}>
        <ScreenScrollContainer topPadding={false} contentContainerStyle={{ flex: 1 }}>
          <View>
            <ListItems
              items={cart.map((i, _index) => ({
                id: i.product?.id,
                title: i.product?.name,
                subtitle: i?.options?.map((o) => o.name)?.join(', '),
                rightTitle: formatNumber(i.totalPrice, {
                  style: 'currency',
                  currency: 'EUR',
                  currencyDisplay: 'symbol',
                }),
                right: () => (
                  <View
                    style={{
                      width: 40,
                      height: 40,
                      borderRadius: 20,
                      backgroundColor: theme.colors.white,
                      borderWidth: 1,
                      borderColor: theme.colors.light,
                      alignItems: 'center',
                      justifyContent: 'center',
                      marginLeft: 20,
                    }}
                  >
                    <Text color="tertiary" bold>
                      {i.quantity}
                    </Text>
                  </View>
                ),
                onPress: () =>
                  !!i.product?.id &&
                  navigation.navigate('QuantityPopup', {
                    id: i.product.id,
                    attributeComboId: i.attributeComboId,
                  }),
              }))}
            />
            {!hasItems && (
              <View style={{ flex: 1, alignItems: 'center', marginTop: theme.space.l * 2 }}>
                <Text
                  type="title"
                  color="sub"
                  style={{
                    width: Dimensions.get('screen').width * 0.6,
                    textAlign: 'center',
                    marginBottom: theme.space.l,
                  }}
                >
                  {formatMessage({ id: 'app.shop.cart.noProducts' })}
                </Text>
              </View>
            )}
          </View>
          {hasItems && (
            <View style={{ flexDirection: 'row', alignItems: 'center', width: '100%' }}>
              <Text color="dark" size="s" style={{ flex: 1 }}>
                {formatMessage({ id: 'app.shop.cart.total' })}
              </Text>
              <Text type="title" bold size="xl">
                {formatNumber(totalPrice, {
                  style: 'currency',
                  currency: 'EUR',
                  currencyDisplay: 'symbol',
                })}
              </Text>
            </View>
          )}
        </ScreenScrollContainer>
        <StickyBottomBar style={{ padding: theme.space.l, justifyContent: 'center' }}>
          <View style={{ flex: 1 }}>
            <Button
              onPress={() => {
                navigation.navigate('CompleteOrder', {
                  facilityId: route?.params?.facilityId,
                  shopId: route?.params?.shopId,
                });
              }}
              style={{
                backgroundColor: theme.colors.tertiary,
              }}
              disabled={!hasItems}
            >
              <Button.Text color="white">{formatMessage({ id: 'app.shop.cart.completeorder' })}</Button.Text>
            </Button>
          </View>
        </StickyBottomBar>
      </View>
    </>
  );
}
