export const format = function (input: string, ...params: string[]) {
  for (let k = 0; k < params.length; k++) {
    input = input.replace('{' + k + '}', params[k]);
  }
  return input;
};

export const capitalizedFirstLetter = (text?: string) => {
  if (!text) return '';
  return text.charAt(0).toUpperCase() + text.slice(1);
};
