import useTheme from '@app/hooks/useTheme';

import React from 'react';
import { useIntl } from 'react-intl';
import { ViewStyle } from 'react-native';
import CheckBox from '../CheckBox';
import { LandalPressable } from '../LandalPressable';
import Shadow from '../Shadow';
import Text from '../Text';

const ITEM_HEIGHT = 60;

interface CheckListItemProps {
  item: string;
  isFirst?: boolean;
  isLast?: boolean;
  style?: ViewStyle;
  checked: boolean;
  onChange: Function;
  disabled?: boolean;
}

interface CheckListContainerProps {
  children?: React.ReactNode;
}

export const CheckListItem = ({
  item,
  isFirst,
  isLast,
  style,
  checked,
  onChange,
  disabled = false,
}: CheckListItemProps) => {
  const theme = useTheme();
  const { formatMessage } = useIntl();

  const margin = theme.space.s;

  return (
    <LandalPressable
      style={[
        {
          backgroundColor: '#FFF',
          flexDirection: 'row',
          minHeight: ITEM_HEIGHT + margin * 2,
          alignItems: 'center',
          paddingHorizontal: theme.space.l,
        },
        isFirst && {
          borderTopLeftRadius: 12,
          borderTopRightRadius: 12,
        },
        isLast && {
          borderBottomLeftRadius: 12,
          borderBottomRightRadius: 12,
        },
        style,
      ]}
      onPress={() => {
        onChange(item);
      }}
      disabled={disabled}
    >
      <CheckBox value={checked} />
      <Text
        bold
        color={checked ? 'gray' : 'black'}
        style={{
          paddingLeft: theme.space.m,
          paddingRight: theme.space.l,
          textDecorationLine: checked ? 'line-through' : 'none',
        }}
      >
        {formatMessage({ id: 'app.reservation.digitalCleaning.items.' + item })}
      </Text>
    </LandalPressable>
  );
};

const CheckList = ({ children }: CheckListContainerProps) => {
  return <Shadow>{children}</Shadow>;
};

export default CheckList;
