import React, { useContext, useState } from 'react';
import { useAnimatedScrollHandler, useSharedValue } from 'react-native-reanimated';
import { Linking, Platform, TouchableOpacity, View } from 'react-native';
import useTheme from '@app/hooks/useTheme';
import Button from '@app/components/Button';
import { useIntl } from 'react-intl';
import { useFocusEffect } from '@react-navigation/native';
import useMetrics from '@app/metrics/useMetrics';
import { useGetExtras } from '../queries/useGetExtras';
import { UserContext } from '@app/context/usercontext/UserContextProvider';
import Text from '@app/components/Text';
import OpeningHours from '@app/components/DetailScreen/OpeningHours';
import { useGetFacilities } from '@domains/park/at-the-park/queries/useGetFacilities';
import { formatDayWithoutTime, fromUnixNumber, getAPIDate, getCurrentTime } from 'utils/date';
import Icon from '@app/components/Icon';
import { useGetReservation } from '@domains/reservations/reservation/queries/useGetReservation';
import { getBrand } from 'utils/brand';
import { getQuantityText } from '../utils/quantity';
import { Openinghour } from 'apis/_generated-park-api/models';
import ScreenScrollContainer from '@app/components/ScreenScrollContainer';
import StickyImageHeader, { useStickyImageHeaderDimensions } from '@app/components/StickyImageHeader';
import StickyBottomBar from '@app/navigation/StickyBottomBar';
import TitleAndDescription from '@app/components/DetailScreen/TitleAndDescription';
import VoucherSheet from '../sheets/VoucherSheet';
import BottomSheetProvider, { BottomSheetContext } from '@app/navigation/bottom-sheet/BottomSheetProvider';
import { Voucher } from 'apis/_generated-reservation-api/models';
import { capitalizedFirstLetter } from 'utils/string';
import ScreenBrightness from 'react-native-screen-brightness';
import { NavScreenProp } from '@app/navigation/stacks/Main';

const ACTIVE_VOUCHER_STATUS = 'CREATED';

export default function ExtraDetail({ route }: NavScreenProp<'ExtraDetail'>) {
  const { logScreenView, logEvent } = useMetrics();
  const theme = useTheme();
  const { EXPANDED_HEADER_HEIGHT, size } = useStickyImageHeaderDimensions('m');
  const [initialBrightness, setInitialBrightness] = useState(1);

  const translationY = useSharedValue(0);
  const { formatMessage } = useIntl();
  const scrollHandler = useAnimatedScrollHandler((event) => {
    translationY.value = event.contentOffset.y;
  });

  const bookingReference = route?.params?.bookingReference;

  const { token } = useContext(UserContext);
  const { data: reservation } = useGetReservation({
    brand: getBrand(theme),
    token,
    bookingReference,
  });
  const productId = route?.params?.productId;
  const extraKey = route?.params?.extraKey;

  const { data: products } = useGetExtras({ bookingReference });
  const product = products?.find((p) => p.id === productId);
  const extra = product?.extras?.find((e) => e?.key === extraKey);
  const { data: facilities } = useGetFacilities({ parkCodeOverride: reservation?.parkCode });
  const pickupFacilities = facilities?.filter((f) => extra?.redeemFacilities?.includes(f.id));
  const paymentCompleted = Number(reservation?.pendingPayment) <= 0;

  const willHaveVouchers = !!extra?.voucherIdentifier;
  const hasVouchers = !!extra?.issuedVouchers && (extra.issuedVouchers.length || 0) > 0;

  useFocusEffect(
    React.useCallback(() => {
      logScreenView('ExtraDetail', [productId ?? '', extraKey ?? '']);
      logEvent('ExtraDetail', {
        payment_complete: Boolean(paymentCompleted),
        has_voucher: hasVouchers,
        voucher_visible: !!extra?.issuedVouchers?.find((v) => v?.status === ACTIVE_VOUCHER_STATUS),
      });
    }, [extra?.issuedVouchers, extraKey, hasVouchers, logEvent, logScreenView, paymentCompleted, productId])
  );

  if (!extra) {
    return null;
  }

  const fullDescription =
    !!extra?.extraDescription && !!extra?.voucherDescription
      ? extra?.extraDescription + '\n\n' + extra?.voucherDescription
      : extra?.extraDescription
      ? extra?.extraDescription
      : extra?.voucherDescription;

  const image = extra.image ?? product?.image;

  const increaseScreenBrightness = async () => {
    let hasPerm = Platform.OS === 'ios' || (await ScreenBrightness.hasPermission());
    if (hasPerm) {
      ScreenBrightness.getBrightness().then((brightness) => {
        setInitialBrightness(brightness);
        ScreenBrightness.setBrightness(1);
      });
    }
  };

  return (
    <BottomSheetProvider>
      <BottomSheetContext.Consumer>
        {(bottomsheet) => (
          <>
            <StickyImageHeader
              scrollOffset={translationY}
              title={extra?.extraName}
              images={image ? [image] : []}
              size={size}
            />
            <ScreenScrollContainer
              onScroll={scrollHandler}
              animated
              contentContainerStyle={{ paddingTop: EXPANDED_HEADER_HEIGHT }}
              forceBottomInset={willHaveVouchers}
            >
              <View style={{ flex: 1 }}>
                <TitleAndDescription name={extra?.extraName} description={fullDescription}>
                  <View>
                    {willHaveVouchers && (
                      <>
                        <View
                          style={{
                            flexDirection: 'row',
                            paddingTop: theme.space.l,
                          }}
                        >
                          <Icon
                            name="calendar"
                            size="m"
                            color="primary"
                            style={{
                              marginRight: theme.space.m,
                            }}
                          />
                          <Text color="dark">
                            {`${formatMessage({
                              id: 'app.extra.validFrom',
                            })} ${
                              reservation?.arrivalDate &&
                              formatDayWithoutTime(fromUnixNumber(reservation.arrivalDate * 1000))
                            } ${formatMessage({
                              id: 'app.general.time.till',
                            })} ${
                              reservation?.departureDate &&
                              formatDayWithoutTime(fromUnixNumber(reservation.departureDate * 1000))
                            }`}
                          </Text>
                        </View>
                        {hasVouchers && (
                          <View
                            style={{
                              flexDirection: 'row',
                              paddingTop: theme.space.l,
                            }}
                          >
                            <Icon
                              name="voucher"
                              size="m"
                              color="primary"
                              style={{
                                marginRight: theme.space.m,
                              }}
                            />
                            <Text
                              color="dark"
                              style={{
                                textTransform: 'lowercase',
                              }}
                            >
                              {extra.issuedVouchers?.length +
                                ' ' +
                                ((extra.issuedVouchers?.length || 0) > 1
                                  ? formatMessage({ id: 'app.extra.vouchers' })
                                  : formatMessage({ id: 'app.extra.voucher' }))}
                            </Text>
                          </View>
                        )}
                      </>
                    )}
                    <View
                      style={{
                        flexDirection: 'row',
                        paddingTop: theme.space.l,
                      }}
                    >
                      <Icon
                        name="travel-group"
                        size="m"
                        color="primary"
                        style={{
                          marginRight: theme.space.m,
                        }}
                      />
                      <Text color="dark">{getQuantityText(extra, formatMessage)}</Text>
                    </View>
                    <TouchableOpacity
                      style={{
                        flexDirection: 'row',
                        paddingTop: theme.space.l,
                      }}
                      onPress={() => {
                        if (reservation?.pendingPaymentUrl) {
                          Linking.openURL(reservation?.pendingPaymentUrl);
                        }
                      }}
                      disabled={paymentCompleted}
                    >
                      <Icon
                        name="payments"
                        size="m"
                        color={paymentCompleted ? 'primary' : 'attention'}
                        style={{
                          marginRight: theme.space.m,
                        }}
                      />
                      <Text color="dark">
                        {paymentCompleted
                          ? formatMessage({ id: 'app.conversation.paid' })
                          : formatMessage({ id: 'app.extra.pendingPayment' })}
                      </Text>
                    </TouchableOpacity>
                  </View>
                </TitleAndDescription>
                {(pickupFacilities?.length || 0) > 0 && (
                  <View>
                    <Text type="title" bold size="l" style={{ marginVertical: theme.space.m }}>
                      {formatMessage({ id: 'app.extra.pickupLocations' })}
                    </Text>
                    <View>
                      {pickupFacilities?.map((p) => {
                        const openingHoursToday = p?.openingHours?.filter(
                          (o) => o?.date === getAPIDate(new Date())
                        );
                        const currentTime = getCurrentTime();
                        const isOpen = openingHoursToday?.find(
                          (o) =>
                            o?.startTime &&
                            o?.endTime &&
                            o.startTime <= currentTime &&
                            o.endTime > currentTime
                        );

                        let title = p?.name;
                        if (isOpen) {
                          title += ` (${formatMessage({ id: 'app.extra.pickupLocationNowOpened' })})`;
                        }

                        return (
                          <View style={{ marginBottom: -theme.space.m }} key={p?.name}>
                            {!!p?.openingHours && (
                              <>
                                <Text bold style={{ marginBottom: theme.space.s }}>
                                  {title}
                                </Text>
                                <View style={{ marginTop: -theme.space.l }}>
                                  <OpeningHours
                                    openingHours={p.openingHours as Openinghour[]}
                                    icon="marker"
                                    iconColor={theme.colors.tertiary}
                                    hideTitle
                                    showWithoutOpeningHours
                                  />
                                </View>
                              </>
                            )}
                          </View>
                        );
                      })}
                    </View>
                  </View>
                )}
              </View>
            </ScreenScrollContainer>
            {willHaveVouchers && (
              <StickyBottomBar style={{ padding: theme.space.l, justifyContent: 'center' }}>
                <View style={{ flex: 1 }}>
                  <Button
                    onPress={() => {
                      //setVoucherOpened(true);
                      bottomsheet.openBottomSheet({
                        component: () => (
                          <VoucherSheet
                            bookingReference={reservation?.bookingReference}
                            vouchers={extra.issuedVouchers as Voucher[]}
                            extraId={extra.extraCode}
                            voucherId={extra.voucherIdentifier}
                            extraName={extra?.extraName}
                          />
                        ),
                        title: capitalizedFirstLetter(formatMessage({ id: 'app.extra.vouchers' })),
                        backgroundColor: 'canvas',
                        onClose: async () => {
                          let hasPerm = Platform.OS === 'ios' || (await ScreenBrightness.hasPermission());
                          if (hasPerm) {
                            ScreenBrightness.setBrightness(initialBrightness);
                          }
                        },
                        key: 'vouchers',
                      });
                      increaseScreenBrightness();
                    }}
                    style={{
                      backgroundColor: theme.colors.secondary,
                    }}
                  >
                    <Button.Icon name="voucher" size="s" color={'white'} />
                    <Button.Text color={'white'}>
                      {(extra?.issuedVouchers?.length || 0) > 1
                        ? formatMessage({ id: 'app.extra.showVouchers' })
                        : formatMessage({ id: 'app.extra.showVoucher' })}
                    </Button.Text>
                  </Button>
                </View>
              </StickyBottomBar>
            )}
          </>
        )}
      </BottomSheetContext.Consumer>
    </BottomSheetProvider>
  );
}
