import AsyncStorage from '@react-native-async-storage/async-storage';
import { QueryClient } from 'react-query';
import { hydrate, dehydrate, DehydratedState } from 'react-query/hydration';

async function hydrateStoredCache(client: QueryClient, cacheStorage: string, storageKey: string) {
  const cache: { timestamp: number; cacheState: DehydratedState } = JSON.parse(cacheStorage);
  if (cache.timestamp) {
    //  7 days max age of stored cache
    const maxAge = 1000 * 60 * 60 * 24 * 7;
    const expired = Date.now() - cache.timestamp > maxAge;
    if (expired) {
      await AsyncStorage.removeItem(storageKey);
    } else {
      hydrate(client, cache.cacheState);
    }
  } else {
    await AsyncStorage.removeItem(storageKey);
  }
}

function subscribeStorageToCache(client: QueryClient, storageKey: string) {
  const saveCache = throttle(async () => {
    const cacheState = dehydrate(client, { shouldDehydrateQuery: () => true });
    const storageCache = { timestamp: Date.now(), cacheState };
    await AsyncStorage.setItem(storageKey, JSON.stringify(storageCache));
  }, 1000);

  // Subscribe to cache changes
  client.getQueryCache().subscribe(saveCache);
}

export default async function persistQueryCache(client: QueryClient) {
  const storageKey = `REACT_QUERY_OFFLINE_CACHE`;

  // Attempt restore
  const cacheStorage = await AsyncStorage.getItem(storageKey);
  if (!cacheStorage) {
    subscribeStorageToCache(client, storageKey);
    return;
  }

  await hydrateStoredCache(client, cacheStorage, storageKey);
  subscribeStorageToCache(client, storageKey);
}

function throttle(func: (...args: any[]) => any, wait: number) {
  // eslint-disable-next-line no-undef
  let timer: NodeJS.Timeout | null = null;
  return function (...args: any[]) {
    if (timer === null) {
      timer = setTimeout(() => {
        func(...args);
        timer = null;
      }, wait);
    }
  };
}
