import React from 'react';
import { TouchableWithoutFeedback } from 'react-native-gesture-handler';
import Animated, { SharedValue, useAnimatedStyle, useDerivedValue } from 'react-native-reanimated';

interface Props {
  fall: SharedValue<number>;
  backdropOpacity: SharedValue<number>;
  closeBottomSheet: () => void;
}

const Backdrop = ({ fall, backdropOpacity, closeBottomSheet }: Props) => {
  const backdropAnimatedStyle = useAnimatedStyle(() => {
    return {
      opacity: backdropOpacity.value,
    };
  });
  const backdropPointerEvents = useDerivedValue(() => {
    return fall.value < -0.5 ? 'none' : 'auto';
  }, [fall.value]);
  const backdropDisabled = useDerivedValue(() => {
    return fall.value < -0.5 ? true : false;
  }, [fall.value]);
  return (
    <Animated.View
      pointerEvents={backdropPointerEvents.value}
      style={[
        {
          position: 'absolute',
          width: '100%',
          height: '100%',
          zIndex: 20,
          backgroundColor: 'rgba(0, 0, 0, 0.6)',
        },
        backdropAnimatedStyle,
      ]}
    >
      <TouchableWithoutFeedback
        onPress={closeBottomSheet}
        style={{ backgroundColor: 'black', width: '100%', height: '100%', opacity: 0 }}
        disabled={backdropDisabled.value}
      />
    </Animated.View>
  );
};
export default Backdrop;
