import React, { ReactNode } from 'react';
import { Platform, useWindowDimensions, View, ViewProps, ViewStyle } from 'react-native';
import useTheme from '@hooks/useTheme';
import { useSafeAreaInsets } from 'react-native-safe-area-context';
import Text from './Text';
import LinearGradient from 'react-native-linear-gradient';
import RoundButton from './RoundButton';
import Animated, { interpolate, useAnimatedStyle } from 'react-native-reanimated';

const MENUSPACING = 60;
const SUBTITLE_ADDITIONAL_HEIGHT = 18;
const MAIN_ADDITIONAL_HEIGHT = 58;

interface BeforeClose {
  (proceedFN: Function): boolean | void;
}

interface Props extends React.PropsWithChildren<ViewProps> {
  title?: string;
  subTitle?: string;
  right?: () => ReactNode;
  navigation?: any;
  showClose?: boolean;
  onClose?: () => void;
  bigTitle?: boolean;
  scrollOffset?: Animated.SharedValue<number>;
  main?: boolean;
  beforeClose?: BeforeClose;
}

interface GradientProps {
  style?: ViewStyle;
}
const Gradient = ({ style }: GradientProps) => {
  const theme = useTheme();
  return (
    <LinearGradient
      colors={[theme.colors.gradientPrimary1, theme.colors.gradientPrimary2, theme.colors.gradientPrimary3]}
      style={[
        {
          position: 'absolute',
          backgroundColor: 'transparent', // required for gradient
          height: 400,
          left: -theme.space.l,
          right: -theme.space.l,
        },
        style,
      ]}
      angle={315}
      useAngle
    />
  );
};

const useGetHeaderHeight = (additionalHeight = false, main = false, fixedHeaderHeight): number => {
  const insets = useSafeAreaInsets();
  if (!main) {
    return fixedHeaderHeight + (insets.top <= 24 ? insets.top + 8 : insets.top);
  }
  // TODO: check various platforms/notches
  const TOTAL_HEADER_HEIGHT =
    fixedHeaderHeight +
    insets.top +
    (main ? MAIN_ADDITIONAL_HEIGHT : 0) +
    (additionalHeight ? SUBTITLE_ADDITIONAL_HEIGHT : 0);
  return TOTAL_HEADER_HEIGHT;
};

export const RelativeHeaderArea = ({ additionalHeight = false }) => {
  const theme = useTheme();

  const TOTAL_HEIGHT = MAIN_ADDITIONAL_HEIGHT + (additionalHeight ? SUBTITLE_ADDITIONAL_HEIGHT : 0);
  return (
    <View
      style={{
        marginHorizontal: -theme.space.l,
        height: TOTAL_HEIGHT,
        marginBottom: -40,
      }}
    >
      <Gradient style={{ top: -400 + TOTAL_HEIGHT }} />
    </View>
  );
};

const StickyHeader = ({
  title,
  subTitle,
  right,
  showClose,
  navigation,
  bigTitle,
  onClose,
  scrollOffset,
  main = false,
  beforeClose,
}: Props) => {
  const theme = useTheme();
  const insets = useSafeAreaInsets();
  const { width: screenWidth } = useWindowDimensions();

  const additionalHeight = !!subTitle;

  const FIXED_HEADER_HEIGHT = bigTitle ? 60 : 50;
  const TOTAL_HEADER_HEIGHT = useGetHeaderHeight(additionalHeight, main, FIXED_HEADER_HEIGHT);

  const MIN_HEADER_HEIGHT =
    FIXED_HEADER_HEIGHT + (insets.top <= 24 ? insets.top + (main ? 0 : 8) : insets.top);

  const subtitleStyles = useAnimatedStyle(() => {
    return {
      opacity: interpolate(scrollOffset ? scrollOffset.value : 0, [0, 16], [1, 0]),
    };
  });

  const gradientStyles = useAnimatedStyle(() => {
    return {
      top: scrollOffset ? Math.max(-scrollOffset.value, MIN_HEADER_HEIGHT - TOTAL_HEADER_HEIGHT) : 0,
    };
  });

  return (
    <>
      <Animated.View style={[{ position: 'absolute', top: 0, width: screenWidth }, gradientStyles]}>
        <Gradient style={{ top: -400 + TOTAL_HEADER_HEIGHT }} />
      </Animated.View>
      <Animated.View
        style={[
          {
            width: screenWidth,
            paddingTop: bigTitle
              ? insets.top + theme.space.xl
              : insets.top > 24
              ? insets.top
              : insets.top + 8,
            paddingHorizontal: theme.space.l,
            height: MIN_HEADER_HEIGHT,
          },
        ]}
      >
        <View
          style={{
            flexDirection: 'row',
            alignItems: 'center',
          }}
        >
          {showClose && (
            <View style={{ width: MENUSPACING }}>
              <RoundButton
                icon="close"
                iconColor="black"
                onPress={() => {
                  if (beforeClose) {
                    const proceed = beforeClose(() => {
                      !!onClose && onClose();
                      !!navigation && navigation.goBack();
                    });
                    if (!proceed) {
                      return;
                    }
                  }
                  !!onClose && onClose();
                  !!navigation && navigation.goBack();
                }}
              />
            </View>
          )}
          <Text
            type="title"
            color="white"
            size={bigTitle ? 'xl' : 'l'}
            bold
            numberOfLines={1}
            ellipsizeMode="tail"
            style={{
              flex: Platform.OS === 'web' ? 1 : bigTitle ? 0 : 1,
              textAlign: bigTitle ? 'left' : 'center',
            }}
          >
            {title}
          </Text>
          <View>
            {showClose && !right && <View style={{ width: MENUSPACING }} />}
            {!!right && <View style={{ marginLeft: 8 }}>{right?.()}</View>}
          </View>
        </View>
      </Animated.View>
      {!!subTitle && (
        <Animated.View
          pointerEvents="none"
          style={[
            {
              position: 'absolute',
              top: bigTitle
                ? insets.top + theme.space.xl + theme.fontSizes.m.lineHeight + theme.space.s
                : insets.top <= 24
                ? 58
                : insets.top + 32,
              left: theme.space.l,
              zIndex: 3,
              elevation: 3,
            },
            subtitleStyles,
          ]}
        >
          <Text color="white">{subTitle}</Text>
        </Animated.View>
      )}
    </>
  );
};

export default StickyHeader;
