import { gql, GraphQLClient } from 'graphql-request';
import Config from 'react-native-config';
import {
  UpdateTravelCompanionRequest,
  UpdateTravelCompanionResponse,
} from 'apis/_generated-reservation-api/models';
import { useMutation, UseQueryOptions } from 'react-query';
import { AxiosError } from 'axios';

export interface ErrorMessage {
  message: string;
  errorCode: number;
}

type Props = {
  options?: UseQueryOptions<UpdateTravelCompanionResponse, AxiosError>;
};

export function useMutateTravelCompanion(props?: Props) {
  const performUpdateTravelCompanion = useMutation(async (request: UpdateTravelCompanionRequest) => {
    const graphQLClient = new GraphQLClient(Config.RESERVATION_API_URL);

    const query = gql`
      mutation updateTravelCompanion($request: UpdateTravelCompanionRequest) {
        updateTravelCompanion(updateTravelCompanionRequest: $request) {
          success
          sequenceNumber
          errorMessage
          differenceInPrice
        }
      }
    `;

    const response = await graphQLClient.rawRequest<{ updateTravelCompanion: UpdateTravelCompanionResponse }>(
      query,
      { request }
    );

    if (!response.data.updateTravelCompanion.success) {
      throw 'updating companion failed';
    }
    return response?.data?.updateTravelCompanion;
  }, props?.options);

  const updateTravelCompanion = async (request: UpdateTravelCompanionRequest) => {
    const result = await performUpdateTravelCompanion.mutateAsync(request);
    return result;
  };

  return { updateTravelCompanion };
}
