import { Poi } from 'apis/_generated-park-api/models';
import { useIntl } from 'react-intl';

export function GroupPois(data?: Poi[]) {
  const { formatMessage } = useIntl();

  // Get the distinct categories
  let allCategories: string[] = [];

  data?.forEach((poi) => {
    poi?.categories?.forEach((cat) => {
      if (cat) allCategories.push(cat);
    });
  });

  const uniqueCategories = Array.from(new Set(allCategories))?.map((catId) => {
    let id = Number(catId);
    if (
      isNaN(id) ||
      (id !== 10 &&
        id !== 20 &&
        id !== 30 &&
        id !== 40 &&
        id !== 50 &&
        id !== 60 &&
        id !== 70 &&
        id !== 80 &&
        id !== 90)
    ) {
      id = 100;
    }
    return {
      id: catId,
      name: formatMessage({ id: `app.poi.overview.category.${catId}` }),
    };
  });

  const getPOIsForCategory = (category: string) => {
    const filteredData = data?.filter((poi) => poi.categories?.find((c) => c === category));
    return filteredData;
  };

  return { uniqueCategories, getPOIsForCategory };
}

export default GroupPois;
