import React, { useContext, useEffect, useRef, useState } from 'react';
import useTheme from '@hooks/useTheme';
import { Keyboard, Linking, Platform, useWindowDimensions, View } from 'react-native';
import { FlatList, ScrollView, TextInput } from 'react-native-gesture-handler';
import { useGetParks } from '../queries/useGetParks';
import { ParkContextUpdater } from '@app/context/usercontext/ParkProvider';
import { UserContextUpdater } from '@app/context/usercontext/UserContextProvider';
import { ParksPark } from 'apis/_generated-park-api/models';
import { ITEM_HEIGHT, ListItem } from '@app/components/ListItems2/ListItems';
import Icon from '@app/components/Icon';
import { useBottomSheet } from '@app/navigation/bottom-sheet/BottomSheetProvider';
import Text from '@app/components/Text';
import { useIntl } from 'react-intl';
import { useSafeAreaInsets } from 'react-native-safe-area-context';
import { ListItemSingle } from '@app/components/ListItems2/ListItemSingle';
import useMetrics from '@app/metrics/useMetrics';
import { AllRoutes, NavScreenProp } from '@app/navigation/stacks/Main';
import { getHasOwnApp } from '@app/hooks/useHasOwnApp';
import { usePopup } from '@app/navigation/popup/PopupProvider';
import PopupContent from '@app/navigation/popup/PopupContent';

function remapInternationalCharToAscii(c: string) {
  if ('a'.indexOf(c) !== -1) return 'a';
  else if ('e'.indexOf(c) !== -1) return 'e';
  else if ('i'.indexOf(c) !== -1) return 'i';
  else if ('o'.indexOf(c) !== -1) return 'o';
  else if ('uu'.indexOf(c) !== -1) return 'u';
  else if ('ccc'.indexOf(c) !== -1) return 'c';
  else if ('zz'.indexOf(c) !== -1) return 'z';
  else if ('sss'.indexOf(c) !== -1) return 's';
  else if ('n'.indexOf(c) !== -1) return 'n';
  else if (''.indexOf(c) !== -1) return 'y';
  else if ('gg'.indexOf(c) !== -1) return 'g';
  else if (c === 'r') return 'r';
  else if (c === 'l') return 'l';
  else if (c === 'd') return 'd';
  else if (c === '') return 'ae';
  else if (c === '') return 'oe';
  else if (c === '') return 'ue';
  else if (c === '') return 'ss';
  else if (c === '') return 'th';
  else if (c === 'h') return 'h';
  else if (c === 'j') return 'j';
  else return c;
}

function parseStringToNonDiacriticsString(s: string) {
  let string = '';

  for (let i = 0; i < s.length; i++) {
    string += remapInternationalCharToAscii(s[i]);
  }
  return string;
}

function parkMatchesSearch(input: string, park: ParksPark) {
  const sanitizedInput = parseStringToNonDiacriticsString((input ?? '').toLowerCase());
  const sanitizedParkName = parseStringToNonDiacriticsString((park?.name ?? '').toLowerCase());
  const sanitizedParkCity = parseStringToNonDiacriticsString((park?.address?.city ?? '').toLowerCase());
  if (sanitizedInput.length <= 2) {
    if (sanitizedParkName.indexOf(sanitizedInput) === 0 || sanitizedParkCity.indexOf(sanitizedInput) === 0) {
      return true;
    }
  } else {
    if (sanitizedParkName.includes(sanitizedInput) || sanitizedParkCity.includes(sanitizedInput)) {
      return true;
    }
  }

  return false;
}

const SelectParkModal = ({ navigation, route }: NavScreenProp<'SelectParkModal'>) => {
  const excludeParks: string[] = [];
  const { height: screenHeight } = useWindowDimensions();

  const INITIAL_ITEM_COUNT_TO_RENDER = Math.ceil(screenHeight / ITEM_HEIGHT) + 4;
  const [searchString, onChangeSearchString] = React.useState('');
  const refFlatList = useRef<typeof FlatList | undefined>();
  const refScrollView = useRef<typeof ScrollView | undefined>();
  const theme = useTheme();
  const { logEvent } = useMetrics();
  const { formatMessage } = useIntl();
  const insets = useSafeAreaInsets();

  const { closeBottomSheet } = useBottomSheet();
  const { openPopup, closePopup } = usePopup();

  const changePark = useContext(ParkContextUpdater);
  const { setUserState } = useContext(UserContextUpdater);
  const [keyboardOpened, setKeyboardOpened] = useState(false);

  const [action, setAction] = React.useState<string | undefined>(undefined);

  const previousScreenName = route?.params?.previousScreenName as keyof AllRoutes;

  const { data } = useGetParks({});

  let filteredData = data?.filter((p) => !!p.name && !excludeParks.includes(p.name) && p.appEnabled) || [];
  if (searchString) {
    filteredData = filteredData.filter((p) => parkMatchesSearch(searchString, p));
  }

  const items: ListItem[] =
    filteredData.map((f) => {
      const { hasOwnApp, dynamicLink } = getHasOwnApp(theme.type, f.id);
      return {
        id: f.id ?? '',
        title: f.name ?? '',
        subtitle: f.address?.city,
        image: f.images?.[0],
        isSizableImage: true,
        external: hasOwnApp,
        onPress: () => {
          if (hasOwnApp) {
            //closeBottomSheet();
            openPopup({
              component: () => (
                <PopupContent
                  title={formatMessage({ id: 'app.parkHasOwnApp.popup.title' })}
                  text={formatMessage({ id: 'app.parkHasOwnApp.popup.text' })}
                  ctaText={formatMessage({ id: 'app.parkHasOwnApp.popup.downloadCTA' })}
                  showClose
                  onCTA={async () => {
                    !!dynamicLink && Linking.openURL(dynamicLink);
                  }}
                />
              ),
            });

            return;
          }
          setValue(f);

          logEvent('ParkChangePark');
          closeBottomSheet();
        },
      };
    }) ?? [];

  //NOTE, this is done to prevent the bottomsheet to be up halfway for android. It will make sure the keyboard is disposed before navigating any further
  useEffect(() => {
    let hack: ReturnType<typeof setTimeout>;
    Keyboard.addListener('keyboardDidShow', () => {
      setKeyboardOpened(true);
    });
    Keyboard.addListener('keyboardDidHide', () => {
      hack = setTimeout(
        () => {
          setKeyboardOpened?.(false);
          return () => clearTimeout(hack);
        },
        Platform.OS === 'android' ? 300 : 0
      );
    });

    return () => {
      if (hack) clearTimeout(hack);
    };
  }, [keyboardOpened]);

  useEffect(() => {
    if (action && keyboardOpened) {
      Keyboard.dismiss();
    }

    if (action && !keyboardOpened) {
      if (previousScreenName) {
        if (previousScreenName === 'Onboarding') {
          navigation.navigate(previousScreenName, {
            changedPark: action === 'back' ? false : true,
            changedCulture: false,
          });
        } else {
          navigation.navigate(previousScreenName);
        }
      }
    }
  }, [keyboardOpened, navigation, previousScreenName, setUserState, action]);

  const setValue = (selectedPark: ParksPark) => {
    if (selectedPark?.id) {
      changePark(selectedPark.id);
      setAction(selectedPark.id);
    }
  };

  const [renderAll, setRenderAll] = useState(false);

  // delay loading all for iOS:
  useEffect(() => {
    // eslint-disable-next-line no-undef
    let timeout: NodeJS.Timeout | null = null;
    if (Platform.OS === 'ios') {
      timeout = setTimeout(() => {
        setRenderAll(true);
      }, 600);
    }
    return () => {
      if (timeout) clearTimeout(timeout);
    };
  }, []);

  return (
    <View style={{ flex: 1 }}>
      <View
        style={{
          backgroundColor: 'white',
          height: 50,
          margin: theme.space.l,
          marginBottom: theme.space.m,
          alignItems: 'center',
          flexDirection: 'row',
          borderRadius: 8,
          borderWidth: 1,
          borderColor: theme.colors.light,
        }}
      >
        <TextInput
          onChangeText={(text) => {
            onChangeSearchString(text);
            refFlatList.current?.scrollToOffset({ offset: 0, animated: false });
            refScrollView.current?.scrollTo({ y: 0, animated: false });
          }}
          value={searchString}
          autoFocus={false}
          selectionColor={theme.colors.gray}
          autoCorrect={false}
          placeholder={formatMessage({ id: 'app.park.selector.search' })}
          placeholderTextColor={theme.colors.gray}
          style={{
            marginLeft: 4,
            padding: 12,
            flex: 1,
            fontFamily: theme.fonts.bodyRegular,
            color: theme.colors.black,
          }}
        />
        <Icon name="search" size="m" color="primary" style={{ right: theme.space.l }} />
      </View>
      {Platform.OS === 'android' ? (
        <FlatList
          ref={refFlatList}
          style={{ backgroundColor: theme.colors.canvas, flex: 1 }}
          contentContainerStyle={{
            paddingHorizontal: theme.space.l,
            marginBottom: theme.space.l,
            paddingBottom: theme.space.l + (keyboardOpened ? 0 : insets.bottom),
          }}
          data={items}
          ListEmptyComponent={
            <Text type="body" style={{ margin: theme.space.l }}>
              {formatMessage({ id: 'app.onboarding.park.noresults' })}
            </Text>
          }
          ItemSeparatorComponent={() => (
            <View
              style={{
                height: 1,
                width: '100%',
              }}
            />
          )}
          renderItem={({ item, index }) => {
            const lastItemId = items?.[items.length - 1]?.id;
            const isLastItem = lastItemId === item.id;

            return (
              <ListItemSingle
                item={item}
                usePlaceholderImage
                useSkiaImage={false}
                useTopRadius={index === 0}
                useBottomRadius={isLastItem}
                isFirst={index === 0}
                isLast={index === items.length - 1}
                subtitleNumberOfLines={1}
                style={{ height: ITEM_HEIGHT }}
              />
            );
          }}
          keyExtractor={(item) => item.id?.toString() || item.title || ''}
          removeClippedSubviews={true}
          renderToHardwareTextureAndroid
          shouldRasterizeIOS
        />
      ) : (
        <ScrollView
          ref={refScrollView}
          style={{ backgroundColor: theme.colors.canvas, flex: 1 }}
          contentContainerStyle={{
            paddingHorizontal: theme.space.l,
            marginBottom: theme.space.l,
            paddingBottom: theme.space.l + (keyboardOpened ? 0 : insets.bottom),
          }}
        >
          {items.map((item, index) => {
            const lastItemId = items?.[items.length - 1]?.id;
            const isLastItem = lastItemId === item.id;

            return renderAll === true || index <= INITIAL_ITEM_COUNT_TO_RENDER ? (
              <ListItemSingle
                key={item.title}
                item={item}
                usePlaceholderImage
                useSkiaImage={false}
                useTopRadius={index === 0}
                useBottomRadius={isLastItem}
                isFirst={index === 0}
                isLast={index === items.length - 1}
                subtitleNumberOfLines={1}
                style={{ height: ITEM_HEIGHT }}
              />
            ) : null;
          })}
        </ScrollView>
      )}
    </View>
  );
};
export default SelectParkModal;
