import { UserContext } from '@app/context/usercontext/UserContextProvider';
import useTheme from '@app/hooks/useTheme';
import { CheckoutNowRequest, CheckoutNowResponse } from 'apis/_generated-reservation-api/models';
import { AxiosError } from 'axios';
import { gql, GraphQLClient } from 'graphql-request';
import { useContext } from 'react';
import Config from 'react-native-config';
import { useMutation, UseQueryOptions } from 'react-query';
import { getBrand } from 'utils/brand';
import { useGetReservation } from './useGetReservation';

type Props = {
  options?: UseQueryOptions<CheckoutNowResponse, AxiosError>;
};

export function useCheckoutNow(props?: Props) {
  const theme = useTheme();
  const { token } = useContext(UserContext);
  const { data: reservation } = useGetReservation({
    brand: getBrand(theme),
    token,
  });

  const performCheckoutNow = useMutation(async (request: CheckoutNowRequest) => {
    const graphQLClient = new GraphQLClient(Config.RESERVATION_API_URL);

    if (!reservation?.bookingReference) {
      throw 'no active reservation found';
    }

    const query = gql`
      mutation {
        checkoutNow(checkoutNowRequest: { uid: "${token}", bookingReference: "${reservation?.bookingReference}" }) {
          success
          message
        }
      }
    `;

    const response = await graphQLClient.rawRequest<{ checkoutNow: CheckoutNowResponse }>(query, {
      request,
    });
    return response?.data.checkoutNow;
  }, props?.options);

  const checkoutNow = async (request: CheckoutNowRequest) => {
    const result = await performCheckoutNow.mutateAsync(request);
    return result;
  };

  return { checkoutNow };
}
