import { Timeslot } from 'apis/_generated-park-api/models';
import { AxiosError } from 'axios';
import { gql, GraphQLClient } from 'graphql-request';
import Config from 'react-native-config';
import { UseQueryOptions, useQuery } from 'react-query';

const key = `gettableavailability`;
type TablebookingAvailabilityDataProps = {
  options?: Omit<UseQueryOptions<Timeslot[], AxiosError>, typeof key>;
  restaurantId: string;
  date: string;
  guests: number;
};

export function useGetTablebookingAvailability(props: TablebookingAvailabilityDataProps) {
  const { restaurantId, date, guests, options } = props;

  const extendedOptions: typeof options = {
    ...options,
    enabled: !!date,
  };

  return useQuery(
    [key, { restaurantId, date, guests }],
    async () => {
      const graphQLClient = new GraphQLClient(Config.PARK_API_URL);
      const query = gql`
        query tableAvailability {
          tableAvailability(restaurantId: "${restaurantId}", date: "${date}", guests: ${guests}) {
            timeslot
          }
        }
      `;
      const result = await graphQLClient.rawRequest<{ tableAvailability: Timeslot[] }>(query);
      return result?.data?.tableAvailability;
    },
    extendedOptions
  );
}
