import React, { useState } from 'react';
import Text from '@components/Text';
import useTheme from '@app/hooks/useTheme';
import { Alert, Platform, View } from 'react-native';
import Button from '@app/components/Button';
import { FormattedMessage, useIntl } from 'react-intl';
import FormControl from '@app/components/Form/FormControl';
import { useFormik } from 'formik';
import SAPLogoutWebview from '../screens/SAPLogoutWebview';
import useMetrics from '@app/metrics/useMetrics';
import { useDeleteAccount } from '../queries/useDeleteAccount';
import { useGetProfile } from '../queries/useGetProfile';
import { ClientError } from 'graphql-request/dist/types';

type FormFields = 'password';
type FormikErrors = {
  password?: string;
};

interface Props {
  onDelete: () => void;
  onSelectCancel: () => void;
}

const AccountDeletionConfirmation = ({ onDelete, onSelectCancel }: Props) => {
  const theme = useTheme();
  const { formatMessage } = useIntl();
  const { logEvent } = useMetrics();
  const { requestAccountDeletion } = useDeleteAccount();
  const { data: profile } = useGetProfile({});

  const [logoutRequested, setLogoutRequested] = useState(false);
  const [deleteRequested, setDeleteRequested] = useState(false);

  const formik = useFormik<Record<FormFields, any>>({
    initialValues: {
      password: '',
    },
    validateOnMount: true,
    onSubmit: () => {},
    validate: (validatedValues) => {
      const errors: FormikErrors = {};
      if (!validatedValues.password) {
        errors.password = 'no password';
      }
      return errors;
    },
  });

  return (
    <>
      <View
        style={{
          margin: theme.space.l,
          marginBottom: 0,
        }}
      >
        <View
          style={{
            flexDirection: 'column',
            alignItems: 'center',
            marginBottom: theme.space.m,
          }}
        >
          <Text style={{ textAlign: 'center', maxWidth: '80%' }} type="body" size="s">
            <FormattedMessage id={'app.login.delete.confirmation.explanation'} />
            {'\n\n'}
            <FormattedMessage id={'app.login.delete.confirmation.pleaseConfirm'} />
          </Text>
          <FormControl
            style={{ width: '100%', height: 60, flex: Platform.OS === 'web' ? undefined : 0 }}
            label={''}
            value={formik.values.password}
            isValid={!formik.errors.password}
            onChangeText={(text) => {
              formik.setFieldValue('password', text);
            }}
            autoCorrect={false}
            secureTextEntry
            textContentType="password"
            autoCapitalize="none"
            returnKeyType="send"
            optional
          />
        </View>
        <Button
          type="primary"
          onPress={async () => {
            setDeleteRequested(true);

            if (!profile) {
              logEvent('DeleteRequestFailed', { reason: 'email address missing in profile data' });

              Alert.alert(
                formatMessage({ id: 'app.general.oops' }),
                formatMessage({ id: 'app.login.delete.errorMissingEmailAddressText' })
              );
              return;
            }

            // first, request deletion
            try {
              const result = await requestAccountDeletion({
                emailAddress: profile.emailAddress,
                password: formik.values.password,
              });
              if (result.success) {
                logEvent('DeleteRequestSuccess');
                Alert.alert(
                  formatMessage({ id: 'app.general.success' }),
                  formatMessage({ id: 'app.login.delete.successText' })
                );
              } else {
                logEvent('DeleteRequestFailed', { reason: 'unexpected error' });

                Alert.alert(
                  formatMessage({ id: 'app.general.oops' }),
                  formatMessage({ id: 'app.login.delete.errorUnexpectedText' })
                );

                setDeleteRequested(false);
                return;
              }
            } catch (e) {
              if (
                (e as ClientError)?.response?.errors?.find((error) => error.message === 'invalid credentials')
              ) {
                logEvent('DeleteRequestFailed', { reason: 'password incorrect' });

                Alert.alert(
                  formatMessage({ id: 'app.general.oops' }),
                  formatMessage({ id: 'app.login.delete.errorIncorrectPasswordText' })
                );
              } else {
                logEvent('DeleteRequestFailed', { reason: 'unexpected error' });

                Alert.alert(
                  formatMessage({ id: 'app.general.oops' }),
                  formatMessage({ id: 'app.login.delete.errorUnexpectedText' })
                );
              }

              setDeleteRequested(false);
              return;
            }

            // then, logout the user. We do this last, as we won't know upfront is the password is entered correctly.
            setLogoutRequested(true);
          }}
          disabled={!formik.isValid}
          style={{
            backgroundColor: theme.colors.attention,
          }}
        >
          {logoutRequested || deleteRequested ? (
            <Button.ActivityIndicator style={{ height: 22 }} />
          ) : (
            <Button.Text>
              <FormattedMessage id={'app.login.delete.confirmation.positive'} />
            </Button.Text>
          )}
        </Button>
        <Button
          type="primary"
          onPress={() => {
            onSelectCancel();
          }}
          style={{
            backgroundColor: 'transparent',
          }}
        >
          <Button.Text style={{ color: theme.colors.tertiary }}>
            {formatMessage({ id: 'app.general.cancel' })}
          </Button.Text>
        </Button>
      </View>
      {logoutRequested && (
        <View>
          <SAPLogoutWebview
            onSuccess={async () => {
              logEvent('DeleteRequestLogoutSuccess');
              setLogoutRequested(false);
              await onDelete();
            }}
            onError={async () => {
              logEvent('DeleteRequestLogoutError');
              setLogoutRequested(false);
              await onDelete();
            }}
          />
        </View>
      )}
    </>
  );
};
export default AccountDeletionConfirmation;
