import { useGetParks } from '@domains/onboarding/queries/useGetParks';
import { ParkContext } from '@app/context/usercontext/ParkProvider';
import React from 'react';

export default function useSelectedPark() {
  const parkCode = React.useContext(ParkContext);
  const { data } = useGetParks({});

  if (!parkCode || !data) {
    return { parkCode: null, park: null };
  }

  const park = data?.find?.((p) => p.id === parkCode);

  return { parkCode, park };
}
