import useTheme from '@app/hooks/useTheme';
import React from 'react';
import { FormattedNumber, useIntl } from 'react-intl';
import { View, ViewStyle } from 'react-native';
import Text from './Text';
import { MessageOrderRow } from '@app/interfaces/Message';

interface Props {
  orderRows: MessageOrderRow[];
  style?: ViewStyle;
}

export default function ReservationBill({ orderRows, style }: Props) {
  const { formatMessage } = useIntl();
  const theme = useTheme();

  return (
    <View style={[{ borderRadius: 4, padding: theme.space.l }, style]}>
      <Text
        type="title"
        size={'l'}
        style={{
          marginBottom: theme.space.l,
        }}
      >
        {formatMessage({ id: 'app.activities.signup.overview' })}
      </Text>
      <View
        style={{
          paddingBottom: theme.space.l,
          marginBottom: theme.space.l,
          borderBottomWidth: 1,
        }}
      >
        {orderRows?.map((p) => (
          <View
            style={{
              flexDirection: 'row',
              justifyContent: 'space-between',
              marginBottom: theme.space.l * 0.5,
            }}
            key={`${p.label}${p.price}${p.amount}`}
          >
            <Text size="s" color="gray" style={{ flexShrink: 1 }}>
              {`${p.amount} x ${p.label}`}
            </Text>
            <Text size="s" type="title" style={{ textAlign: 'right' }}>
              <FormattedNumber
                value={p.price}
                style="currency"
                currency={'EUR'}
                currencyDisplay={'symbol'}
                minimumFractionDigits={2}
              />
            </Text>
          </View>
        ))}
      </View>
      <View
        style={{
          flexDirection: 'row',
          justifyContent: 'space-between',
        }}
      >
        <Text type="title">{formatMessage({ id: 'app.forms.summary.total' })}</Text>

        <Text type="title" bold>
          <FormattedNumber
            value={orderRows
              ?.map((p) => p.price)
              .reduce((prev: number, next: number) => Number(prev) + Number(next), 0)}
            style="currency"
            currency={'EUR'}
            currencyDisplay={'symbol'}
          />
        </Text>
      </View>
    </View>
  );
}
