import React, { useContext, useMemo } from 'react';
import useScreenSize from '@hooks/useScreenSize';
import { Linking, View, Platform } from 'react-native';
import useTheme from '@app/hooks/useTheme';
import { useIntl } from 'react-intl';
import useMetrics from '@app/metrics/useMetrics';
import { useFocusEffect } from '@react-navigation/native';
import { NavScreenProp } from '@app/navigation/stacks/Main';
import { UserContext } from '@app/context/usercontext/UserContextProvider';
import { getBrand } from 'utils/brand';
import { useGetReservation } from '../queries/useGetReservation';
import { getFullName } from '../../../../utils/name';
import BottomSheetProvider, { BottomSheetContext } from '@app/navigation/bottom-sheet/BottomSheetProvider';
import ListItems from '@app/components/ListItems2/ListItems';
import ScreenScrollContainer from '@app/components/ScreenScrollContainer';
import StickyHeader from '@app/components/StickyHeader';
import TravelCompanyMainTenant from './TravelCompanyMainTenant';
import Text from '@app/components/Text';
import Icon from '@app/components/Icon';
import { useGetPark } from '@domains/park/contact-and-info/queries/useGetPark';
import { formatDayWithoutTime } from 'utils/date';
import { getAgeCategoryText } from '../utils/age';

const TravelCompany = ({ navigation, route }: NavScreenProp<'TravelCompany'>) => {
  const { insets } = useScreenSize();
  const { formatMessage } = useIntl();
  const { logScreenView, logEvent } = useMetrics();
  const theme = useTheme();

  const { token } = useContext(UserContext);

  const bookingReference = route?.params?.bookingReference;
  const { data: reservation } = useGetReservation({
    brand: getBrand(theme),
    token,
    bookingReference,
  });
  const { data: park } = useGetPark({
    parkCodeOverride: reservation?.parkCode,
  });

  useFocusEffect(
    React.useCallback(() => {
      logScreenView('TravelCompany');
    }, [logScreenView])
  );

  const mainTenantTitle =
    getFullName(
      reservation?.mainTenant?.firstName,
      reservation?.mainTenant?.middleName,
      reservation?.mainTenant?.lastName
    ) || '';

  const travelCompanionsList = useMemo(() => {
    if (!reservation) return [];

    // making sure it won't crash: there was an issue with overnight visitors not being added to totalGuests, but were being returned in the guests array.
    // the reservation api now adds the amount of overnight visitors to totalGuests, so it won't crash any longer, did add this as a safeguard.
    return [...Array(reservation?.totalGuests ? Math.max(0, reservation?.totalGuests - 1) : 0)].map(
      (_, index) =>
        reservation?.travelCompanions?.find((companion) => companion?.sequenceNumber === index) || {
          sequenceNumber: index,
        }
    );
  }, [reservation]);

  return (
    <BottomSheetProvider>
      <BottomSheetContext.Consumer>
        {(_bottomsheet) => (
          <>
            <StickyHeader
              title={formatMessage({ id: 'app.reservation.companions' })}
              showClose
              navigation={navigation}
            />
            <ScreenScrollContainer topPadding={false} contentContainerStyle={{ paddingTop: theme.space.xl }}>
              <View style={{ flex: 1 }}>
                <Text type="title" size="l" bold style={{ marginBottom: theme.space.m }}>
                  {formatMessage({ id: 'app.reservation.companions.myData' })}
                </Text>
                <Text type="body" color="dark" style={{ marginBottom: theme.space.s, lineHeight: 26 }}>
                  {park?.address?.country === 'DE' || park?.address?.country === 'CZ'
                    ? formatMessage({ id: 'app.conversation.companion.main.id.infoTextGerman' })
                    : formatMessage({ id: 'app.conversation.companion.main.id.infoTextNonGerman' })}
                </Text>
                <ListItems
                  items={[
                    {
                      id: 'mainTenant',
                      title: mainTenantTitle,
                      subtitle: formatMessage({ id: 'app.reservation.companions.mainTenant' }),
                      icon: reservation?.mainTenant?.isComplete
                        ? 'travel-companion-complete'
                        : 'travel-companion-incomplete',
                      status: reservation?.mainTenant?.isComplete ? 'complete' : 'incomplete',
                      bottomSheet: {
                        component: () => <TravelCompanyMainTenant bookingReference={bookingReference} />,
                        backgroundColor: 'canvas',
                        title: formatMessage({ id: 'app.reservation.companions.mainTenant' }),
                        key: 'travel-company-main-tenant-2',
                      },
                    },
                  ]}
                />
                <ListItems
                  title={formatMessage({ id: 'app.reservation.companions.myCompanions' })}
                  items={
                    travelCompanionsList.map((t, index) => {
                      const title =
                        getFullName(t?.firstName, t?.middleName, t?.lastName) ||
                        `${formatMessage({ id: 'app.conversation.companion.companion.title' })} ${index + 1}`;
                      return {
                        id: t?.sequenceNumber,
                        title,
                        subtitle: getAgeCategoryText(formatMessage, t?.ageCategory, t?.minAge, t?.maxAge),
                        bottom:
                          !!t?.arrivalDate &&
                          !!t?.departureDate &&
                          !!reservation?.arrivalDate &&
                          !!reservation?.departureDate &&
                          (Number(t.arrivalDate) !== reservation.arrivalDate ||
                            Number(t.departureDate) !== reservation.departureDate) ? (
                            <Text color="gray" style={{ fontStyle: 'italic' }} size="s">
                              {`${formatMessage({ id: 'app.general.arrival' })}: ${formatDayWithoutTime(
                                Number(t.arrivalDate) * 1000,
                                false,
                                true,
                                true
                              )}\n${formatMessage({ id: 'app.general.departure' })}: ${formatDayWithoutTime(
                                Number(t.departureDate) * 1000,
                                false,
                                true,
                                true
                              )}`}
                            </Text>
                          ) : undefined,
                        icon: t?.isComplete ? 'travel-companion-complete' : 'travel-companion-incomplete',
                        status: t?.isComplete ? 'complete' : 'incomplete',
                        onPress: () => {
                          navigation.navigate('TravelCompanyCompanion', {
                            title,
                            index: t?.sequenceNumber,
                            totalCompanions: travelCompanionsList.length,
                            bookingReference,
                          });
                        },
                      };
                    }) || []
                  }
                />
              </View>
            </ScreenScrollContainer>
            <View
              style={{
                padding: theme.space.l,
                paddingBottom: theme.space.l + insets.bottom,
              }}
            >
              <Text type="title" size="l" bold style={{ marginBottom: theme.space.m }}>
                {formatMessage({
                  id: 'app.reservation.companions.add.title',
                })}
              </Text>
              <Text style={{ flexDirection: 'row', flexWrap: 'wrap' }}>
                <Text style={{ lineHeight: 26 }} color="dark">
                  {formatMessage({
                    id: 'app.reservation.companions.add.part1',
                  })}
                </Text>
                <Text> </Text>
                <Text>
                  <Text
                    style={{ lineHeight: 26 }}
                    color="tertiary"
                    bold
                    onPress={() => {
                      logEvent('TravelCompanionAddOrRemove');
                      const url = formatMessage({
                        id: 'app.website.mylandal',
                      });
                      Linking.openURL(url);
                    }}
                  >
                    {formatMessage({
                      id: 'app.reservation.companions.add.part2',
                    })}
                  </Text>
                  <Icon
                    name="exit-app"
                    color="tertiary"
                    size="m"
                    style={{ lineHeight: 26, top: Platform.OS === 'android' ? 8 : 2 }}
                  />
                </Text>
              </Text>
            </View>
          </>
        )}
      </BottomSheetContext.Consumer>
    </BottomSheetProvider>
  );
};
export default TravelCompany;
