import React, { useContext, useState } from 'react';
import { Linking, Platform, TouchableOpacity, View } from 'react-native';
import useTheme from '@app/hooks/useTheme';
import { useIntl } from 'react-intl';
import useMetrics from '@app/metrics/useMetrics';
import { useFocusEffect } from '@react-navigation/native';
import { useMutateICE } from '../queries/useMutateICE';
import { UserContext } from '@app/context/usercontext/UserContextProvider';
import { useGetReservation } from '../queries/useGetReservation';
import { getBrand } from 'utils/brand';
import { useBottomSheet } from '@app/navigation/bottom-sheet/BottomSheetProvider';
import ListItems from '@app/components/ListItems2/ListItems';
import { getFullName } from 'utils/name';
import Icon from '@app/components/Icon';
import Text from '@app/components/Text';

type FormFields = 'iceName' | 'icePhoneNumber' | 'idType';

type FormikErrors = {
  iceName?: string;
  icePhoneNumber?: string;
  idType?: string;
};

interface Props {
  bookingReference?: string;
}

const TravelCompanyMainTenant = ({ bookingReference }: Props) => {
  const { formatMessage } = useIntl();
  const { logScreenView, logEvent } = useMetrics();
  const theme = useTheme();
  const { updateMainTenant } = useMutateICE();
  const [processing, setProcessing] = useState(false);
  const { token } = useContext(UserContext);
  const { closeBottomSheet } = useBottomSheet();

  const { data: reservation, refetch } = useGetReservation({
    brand: getBrand(theme),
    token,
    bookingReference,
  });

  useFocusEffect(
    React.useCallback(() => {
      logScreenView('TravelCompanyMainTenant');
      logEvent('MainTenantView');
    }, [logEvent, logScreenView])
  );

  const mainTenantTitle =
    getFullName(
      reservation?.mainTenant?.firstName,
      reservation?.mainTenant?.middleName,
      reservation?.mainTenant?.lastName
    ) || '';

  let gender = '';
  switch (reservation?.mainTenant?.gender) {
    case 1:
      gender = formatMessage({ id: 'app.forms.labels.gender.man' });
      break;
    case 2:
      gender = formatMessage({ id: 'app.forms.labels.gender.woman' });
      break;
  }

  return (
    <View style={{ flex: Platform.OS === 'web' ? undefined : 0, paddingHorizontal: theme.space.l }}>
      <ListItems
        title={formatMessage({ id: 'app.profile.account.personalData' })}
        items={[
          {
            id: 'saluation',
            title: formatMessage({ id: 'app.profile.account.personalData.salutation' }),
            subtitle: gender,
          },
          {
            id: 'name',
            title: formatMessage({ id: 'app.profile.account.personalData.name' }),
            subtitle: mainTenantTitle,
          },
          {
            id: 'email',
            title: formatMessage({ id: 'app.profile.account.personalData.email' }),
            subtitle: reservation?.mainTenant?.email,
          },
        ]}
      />
      <TouchableOpacity
        activeOpacity={0.8}
        style={{ flexDirection: 'row', marginBottom: theme.space.xl }}
        onPress={() => {
          logEvent('SettingsDetailsUpdate');
          Linking.openURL(formatMessage({ id: 'app.website.mylandal' }));
          closeBottomSheet();
        }}
      >
        <Icon color="tertiary" name="edit" size="s" style={{ marginRight: theme.space.s, top: 1 }} />
        <Text color="tertiary" bold>
          {formatMessage({ id: 'app.profile.account.personalData.change' })}
        </Text>
      </TouchableOpacity>
    </View>
  );
};
export default TravelCompanyMainTenant;
