import React from 'react';
import { ViewProps } from 'react-native';
import useTheme from '@hooks/useTheme';
import { useGetFacilities } from '@domains/park/at-the-park/queries/useGetFacilities';
import { useIntl } from 'react-intl';
import { Age, Price } from 'apis/_generated-park-api/models';
import Table from '@app/components/Table';
import Text from '@app/components/Text';

interface Props extends React.PropsWithChildren<ViewProps> {
  location?: string;
  age?: Age;
  price?: Price;
  signUpNeeded: boolean;
}
export default function ActivityDetails({ location, age, price, signUpNeeded }: Props) {
  const theme = useTheme();
  const { formatMessage } = useIntl();
  const { data: facilities } = useGetFacilities();
  const thisFacility = facilities?.find((f) => f.id === location);

  return (
    <Table
      title="Details"
      items={[
        { label: formatMessage({ id: 'app.general.location' }), value: thisFacility?.name },
        { label: formatMessage({ id: 'app.general.age' }), value: age?.description },
        { label: formatMessage({ id: 'app.general.cost' }), value: price?.description },
      ]}
    >
      {signUpNeeded && (
        <Text size="s" color="gray" style={{ fontStyle: 'italic', marginBottom: theme.space.m }}>
          {formatMessage({ id: 'app.activities.signupneeded.long' })}
        </Text>
      )}
    </Table>
  );
}
